import { GpuWorldState } from "./State";
import { objectSetKeys } from "../Util";
import { StepsPerSecond } from "../Time";
import { fract } from "../Math/Math";


export interface GameLoopStats {
  stepsPerSecond: number;
  msPerFrame: number;
  runningTime: number;
}
export function emptyGameLoopStats(): GameLoopStats {
  return {
    stepsPerSecond: 0,
    msPerFrame: 0,
    runningTime: 0
  }
}

export class GameLoop {
  constructor(private state: GpuWorldState) {
    state.gameLoop = this;
    this.loopRunning = true;
    requestAnimationFrame(this.loop);
  }
  public destroy() {
    this.loopRunning = false;
    this.state.setData({ simulationPaused: true });
  }

  private loopRunning = false;
  private prevTime = window.performance.now();

  private statsNFrames = 0;
  private statsNSteps = 0;
  private statsStepsStartTime = window.performance.now();
  private statsFramesStartTime = window.performance.now();
  private statsRunningTime = 0;

  private tryDumpStats() {
    const time = window.performance.now();
    let stats = this.state.data.gameLoopStats;
    const stepsDeltaTime = time - this.statsStepsStartTime;
    if (stepsDeltaTime >= 1000 || this.statsNSteps >= 1000) {
      stats = objectSetKeys(stats, {
        stepsPerSecond: this.statsNSteps / (stepsDeltaTime / 1000),
        runningTime: this.statsRunningTime
      });
      this.statsStepsStartTime = time;
      this.statsNSteps = 0;
    }
    const framesDeltaTime = time - this.statsFramesStartTime;
    if (framesDeltaTime >= 1000 || this.statsNFrames >= 400) {
      stats = objectSetKeys(stats, {
        msPerFrame: framesDeltaTime / this.statsNFrames,
        runningTime: this.statsRunningTime
      });
      this.statsFramesStartTime = time;
      this.statsNFrames = 0;
    }
    if (stats !== this.state.data.gameLoopStats) {
      this.state.setData({
        gameLoopStats: stats
      });
    }
  }

  private loop = () => {
    if (!this.loopRunning) {
      return;
    }
    requestAnimationFrame(this.loop);
    const time = window.performance.now();
    const delta = time - this.prevTime;
    this.prevTime = time;

    this.state.events.camera.dispatch({ type: 'Camera', delta });
    if (this.state.derived.simulationRunning) {
      const stepsStart = window.performance.now();
      let substep = this.state.data.turboMode ? 100
        : (this.state.data.substep + delta * this.state.data.simulationSpeed * StepsPerSecond / 1000);
      while (substep > 1 && this.state.derived.simulationRunning && (window.performance.now() - stepsStart) < 100) {
        this.state.events.step.dispatch();
        this.statsNSteps++;
        substep--;
      }
      if (substep > 1) {
        substep = fract(substep);
      }

      this.statsRunningTime += delta;

      this.state.setData({
        substep,
        ensureRenderNextLoop: false
      });
      if (this.state.data.renderWorld) {
        this.state.events.render.dispatch();
        this.statsNFrames++;
      }
    } else if (this.state.data.ensureRenderNextLoop) {
      this.state.setData({
        ensureRenderNextLoop: false
      });
      if (this.state.data.renderWorld) {
        this.state.events.render.dispatch();
        this.statsNFrames++;
      }
    }
    this.tryDumpStats();
  }

  public step() {
    if (!this.state.derived.simulationRunning) {
      this.state.events.step.dispatch();
      this.state.events.render.dispatch();
    }
  }

}
