import { GpuBuffer, ECSConsts, GpuComponent, Component, componentFormatChannels } from ".";
import { shaderModule, uniformArrayDef, uniformDef, funcDef } from "../ShaderModule";
import { ArrowTransformSM } from "../SM2Utils";

function gpuBufferReadSM(buffer: GpuBuffer) {
  return shaderModule(buffer.name, [ArrowTransformSM], {
    archetypeDataRowOffset: uniformArrayDef('int[]', ECSConsts.MaxArchetypes, d => d.ecs.gpuBuffers[buffer.name].archetypeToDataRowOffset, false),
    sampler: uniformDef('sampler2DArray', d => d.ecs.gpuBuffers[buffer.name].texture),
    dataCell: funcDef('ivec2', [['ivec2', 'entityCell']], `
      const int chunkSize = ${ECSConsts.ChunkSize};
      return ivec2(
        entityCell.x % chunkSize,
        entityCell.x / chunkSize + Self.archetypeDataRowOffset[entityCell.y]
      );
    `),
    get: funcDef(buffer.format, [['ivec2', 'entityCell'], ['int', 'componentLayer']], `
      return texelFetch(Self.sampler, ivec3(
        Self.dataCell(entityCell),
        componentLayer
      ), 0)${buffer.format === 'float' ? '.x' : ''};
    `),
    size: uniformDef('ivec2', d => d.ecs.gpuBuffers[buffer.name].sizeVec2),
    dataArrow: funcDef('mat4', [['ivec2', 'entityCell']], `
      return ${ArrowTransformSM.cellArrowTransform}(Self.size, Self.dataCell(entityCell));
    `)
  });
}

function vec4To(format: GpuComponent['format']) {
  switch (format) {
    case 'float': return '.x';
    case 'int': return '.x';
    case 'bool': return '.x';
    case 'ivec2': return '.xy';
    case 'vec2': return '.xy';
    case 'vec3': return '.xyz';
    case 'ivec3': return '.xyz';
    case 'vec4': return '';
    case 'ivec4': return '';
    case 'mat4': return '';
  }
}

export function gpuComponentReadSM(component: GpuComponent) {
  const buffer = gpuBufferReadSM(component.buffer);
  const layerOffset = uniformDef('int', d => d.ecs.gpuComponentInfo(component)?.layerOffset ?? 10000);

  let args: Array<[string, string]> = [['ivec2', 'entityCell']];
  if (component.depth !== undefined) {
    args = [...args, ['int', 'layer']];
  }
  const dataArrow = funcDef('mat4', [['ivec2', 'entityCell']], `
    return ${buffer.dataArrow}(entityCell);
  `)
  switch (component.buffer.format) {
    case 'float': return shaderModule(component.name, [buffer], {
      layerOffset,
      value: funcDef(component.format, args, `
        return ${component.format}(
          ${new Array(componentFormatChannels(component.format)).fill(0).map((_, i) =>
            `${buffer.get}(entityCell, Self.layerOffset ${component.depth === undefined ? '' : `+ layer * ${componentFormatChannels(component.format)}`} + ${i})`
          ).join(',\n')}
        );
      `),
      dataArrow
    });
    case 'vec4': {
      if (component.format === 'mat4') {
        return shaderModule(component.name, [buffer], {
          layerOffset,
          value: funcDef(component.format, args, `
            return mat4(
              ${new Array(4).fill(0).map((_, i) =>
                `${buffer.get}(entityCell, Self.layerOffset ${component.depth === undefined ? '' : `+ layer * 4`} + ${i})`
              ).join(',\n')}
            );
          `),
          dataArrow
        });
      } else {
        return shaderModule(component.name, [buffer], {
          layerOffset,
          value: funcDef(component.format, args, `
            return ${component.format}(${buffer.get}(entityCell, Self.layerOffset ${component.depth === undefined ? '' : `+ layer`})${vec4To(component.format)});
          `),
          dataArrow
        });
      }
    }
  }
}
export type GpuComponentReader = ReturnType<typeof gpuComponentReadSM>;

export function hasComponentSM(component: Component) {
  return shaderModule(component.name + 'HasComponent', [], {
    componentId: uniformDef('int', d => d.ecs.components[component.name].index),
    archetypeHasComponentTexture: uniformDef('usampler2D', d => d.ecs.archetypeHasComponentTexture),
    value: funcDef('bool', [['ivec2', 'entityCell']], `
      return bool(texelFetch(Self.archetypeHasComponentTexture, ivec2(Self.componentId, entityCell.y), 0).x);
    `)
  })
}


function vec4Pad(format: GpuComponent['format'], data: string, slice: number) {
  switch (format) {
    case 'float': return `vec4(${data})`;
    case 'int': return `vec4(${data})`;
    case 'bool': return `vec4(${data})`;
    case 'ivec2': return `vec4(${data}, 0, 0)`;
    case 'vec2': return `vec4(${data}, 0, 0)`;
    case 'vec3': return `vec4(${data}, 0)`;
    case 'ivec3': return `vec4(${data}, 0)`;
    case 'vec4': return data;
    case 'ivec4': return data;
    case 'mat4': return `data[${slice}]`;
  }
}


// This expects `const int ComponentName_outOffset;` to be defined, as well as `out XX outValues;`
export function gpuComponentBufferWriterSM(component: GpuComponent) {
  const channels = componentFormatChannels(component.format);
  const depth = component.depth ?? 1;
  const args: Array<[string, string]> = [[`${component.format}${component.depth === undefined ? '' : `[${component.depth}]`}`, 'data']];
  switch (component.buffer.format) {
    case 'float': return shaderModule(component.name + 'Writer', [], {
      write: funcDef('void', args, `
        ${new Array(depth).fill(0).map((_, i) =>
          new Array(channels).fill(0).map((_, l) =>
            `outValues[${component.name}_outOffset + ${i * channels} + ${l}] = vec4(data${component.depth === undefined ? '' : `[${i}]`}${channels === 1 ? '' : `[${l}]`});`
          ).join('\n')
        ).join('\n')}
      `)
    })
    case 'vec4': {
      const slices = Math.ceil(channels / 4);
      return shaderModule(component.name + 'Writer', [], {
        write: funcDef('void', args, `
          ${new Array(depth).fill(0).map((_, i) =>
            new Array(slices).fill(0).map((_, l) =>
              `outValues[${component.name}_outOffset + ${i * slices} + ${l}] = ${vec4Pad(component.format, 'data' + (component.depth === undefined ? '' : `[${i}]`), l)};`
            ).join('\n')
          ).join('\n')}
        `)
      })
    }
  }
}
