import * as Math2 from './Math';
import * as Vector3 from './Vector3';

export interface SphericalCoordinate {
  theta: number;
  phi: number; // Around Z axis
  radius: number;
}

export function mix(a: SphericalCoordinate, b: SphericalCoordinate, p: number) {
  return {
    theta: Math2.mix(a.theta, b.theta, p),
    phi: Math2.mix(a.phi, b.phi, p),
    radius: Math2.mix(a.radius, b.radius, p),
  }
}

// This is the cartesian distance
export function distance(a: SphericalCoordinate, b: SphericalCoordinate): number {
  return Vector3.distance(Vector3.fromSphericalCoordinates(a), Vector3.fromSphericalCoordinates(b));
}

export function isEqual(a: SphericalCoordinate, b: SphericalCoordinate): boolean {
  return a.theta === b.theta && a.phi === b.phi && a.radius === b.radius;
}