import * as React from 'react';
import './Editors.css';
import { EditorTypedefResolver, EditorTypedefConvertible } from './EditorTypedefs';
import { EditorProps, EditorDispatch } from './EditorProps';
import { CheckboxEditor, EnumEditor } from './PrimitiveEditors';
import { EnumStructEditor } from './EnumStructEditor';
import { ObnumEditor } from './ObnumEditor';
import { StructEditor, StructEditorChange } from './StructEditor';
import { ListEditor } from './ListEditor';
import { TupleEditor } from './TupleEditor';
import { KeyValueEditor } from './KeyValueEditor';
import { ColorEditor } from './ColorEditor';
import { editorTypedefConvertibleToTypedef } from './EditorTypedefConvertible';
import { FileEditor } from './FileEditor';
import { TableEditor } from './TableEditor';
import { NumberEditor, NumberEditorType, PercentageEditor, SliderEditor } from './NumberEditor';
import { PixlingTimespanDefEditor } from './PixlingTimespanDefEditor';
import { StringEditor } from '.';

export interface AnyEditorProps extends EditorProps<any> {
  typedef: EditorTypedefConvertible;

  className?: string;
  typedefResolver?: EditorTypedefResolver;
}

export function AnyEditor({ className, onChange, readOnly, typedef, typedefResolver, value }: AnyEditorProps & EditorDispatch<any>): any {
  typedef = editorTypedefConvertibleToTypedef(typedef, typedefResolver);
  switch (typedef.metatype) {
    case 'boolean':
      if (typedef.asDropDown) {
        return <EnumEditor
          className={className}
          value={value}
          readOnly={readOnly}
          variants={[{ value: true, displayName: typedef.asDropDown.true }, { value: false, displayName: typedef.asDropDown.false }]}
          onChange={onChange}
          />;
      } else {
        return <CheckboxEditor className={className} readOnly={readOnly} value={value || false} onChange={onChange} />;
      }
    case 'float':
      return <NumberEditor className={className} readOnly={readOnly} value={value} onChange={onChange} type={NumberEditorType.FLOAT} {...typedef} />;
    case 'int':
      return <NumberEditor className={className} readOnly={readOnly} value={value} onChange={onChange} type={NumberEditorType.INT} {...typedef} />;
    case 'slider':
      return <SliderEditor className={className} readOnly={readOnly} value={value} onChange={onChange} {...typedef} />;
    case 'percentage':
      return <PercentageEditor className={className} readOnly={readOnly} value={value} onChange={onChange} type={NumberEditorType.FLOAT} min={typedef.min} max={typedef.max} step={typedef.step} slider={typedef.slider} />;
    case 'string':
      return <StringEditor className={className} readOnly={readOnly || typedef.readOnly} value={value} onChange={onChange} multiline={!!typedef.multiline} widthMode="fit-content" />;
    case 'enum': {
      return <EnumEditor className={className} readOnly={readOnly} value={value} onChange={onChange} defaultValue={typedef.default} variants={typedef.variants} />;
    }
    case 'enumstruct':
      return (
        <EnumStructEditor
          className={className}
          readOnly={readOnly}
          value={value}
          variants={typedef.variants}
          onChange={onChange}
          typedefResolver={typedefResolver}
          />
      );
    case 'obnum':
      return (
        <ObnumEditor className={className} readOnly={readOnly} value={value} onChange={onChange} variants={typedef.variants} typedefResolver={typedefResolver} />
      );
    case 'struct':
      return (
        <StructEditor
          className={className}
          readOnly={readOnly}
          value={value}
          properties={typedef.properties}
          onChange={(change: StructEditorChange) => onChange({...value, [change.property]: change.value})}
          typedefResolver={typedefResolver}
          layout={typedef.layout}
          />
      );
    case 'list': {
      const itemType = typedef.itemType;
      return <ListEditor
        className={className}
        readOnly={readOnly || typedef.readOnly}
        value={value}
        onChange={onChange}
        itemFactory={typedef.newItem}
        keyFromItem={typedef.keyFromItem}
        acceptItemDrop={typedef.acceptItemDrop}
        itemRenderer={props => (
          <AnyEditor {...props} readOnly={readOnly} typedef={itemType} typedefResolver={typedefResolver} />
        )}
        />;
    }
    case 'table':
      return <TableEditor
        value={value}
        onChange={onChange}
        itemProperties={typedef.itemProperties}
        itemPropertiesLabels={typedef.itemPropertiesLabels}
        newItem={typedef.newItem}
        />;
    case 'tuple':
      return <TupleEditor className={className} readOnly={readOnly} value={value} onChange={onChange} itemTypes={typedef.itemTypes} typedefResolver={typedefResolver} />;
    case 'keyvalue':
      return <KeyValueEditor className={className} readOnly={readOnly} value={value} onChange={onChange} valueType={typedef.valueType} valueDefault={typedef.valueDefault} typedefResolver={typedefResolver} />;
    case 'select':
      const key = typedef.propertyKey;
      return (
        <AnyEditor
          className={className}
          readOnly={readOnly}
          typedef={typedef.propertyType}
          value={value !== undefined ? value[key] : undefined}
          onChange={(newValue: any) => onChange({...value, [key]: newValue })}
          />
      );
    case 'map':
      return (
        <AnyEditor
          className={className}
          readOnly={readOnly}
          typedef={typedef.mappedType}
          value={typedef.map(value)}
          onChange={onChange}
          />
      );
    case 'color':
      return <ColorEditor className={className} readOnly={readOnly} value={value} onChange={onChange}  />;
    case 'file':
      return <FileEditor onChange={onChange}  />;
    case 'pixlingTimespanDef':
      return <PixlingTimespanDefEditor value={value} onChange={onChange} />;
    default: {
      return <i>Unrecognized meta type</i>;
    }
  }
}
