import { GpuComponentModule } from "../../GpuUtil/ECS/ComponentModules";
import { UnitBuffer } from "../ECS/UnitComponents";
import { GlState } from "../../GpuUtil/GlState";
import { GpuComponent, ECSConsts } from "../../GpuUtil/ECS";
import { gpuComponentReadSM } from "../../GpuUtil/ECS/GpuComponentSM";
import { CompiledShaderModule, shaderModule, outDef, uniformDef, uniformArrayDef, funcDef } from "../../GpuUtil/ShaderModule";
import { RandomSM, RandomBoxMullerSM } from "../../GpuUtil/SM2Utils";
import { ArchetypesQuery } from "../../GpuUtil/ECS/Query";
import { RenderTarget } from "../../GpuUtil/RenderTarget";
import { Program } from "../../GpuUtil/Program";
import { GpuWorldState } from "../State";
import { createBatches } from "../../GpuUtil/Computation";
import { HiddenLayer, MemoryLayer, OutputLayer } from "../Brain/Components";

export const CopyMutateParent = new GpuComponentModule('CopyMutateParent', UnitBuffer.buffer, 'ivec2');
export const MutationRate = new GpuComponentModule('MutationRate', UnitBuffer.buffer, 'float');

export class CopyAndMutateBrains {
  constructor(private gls: GlState) {}

  private matrices = [...WeightComponents, ...BiasComponents].map(x =>
    new CopyAndMutateMatrix(this.gls, x)
  );

  run(state: GpuWorldState) {
    for (const m of this.matrices) {
      m.run(state);
    }
  }
}

const WeightComponents = [
  HiddenLayer.weights.component,
  MemoryLayer.weights.component,
  OutputLayer.weights.component,
]
const BiasComponents = [
  HiddenLayer.biases.component,
  MemoryLayer.biases.component,
  OutputLayer.biases.component,
]

class CopyAndMutateMatrix {
  constructor(private gls: GlState, private weightsComponent: GpuComponent) {}

  private weightsSM = gpuComponentReadSM(this.weightsComponent);
  private shader = new CompiledShaderModule(shaderModule([
    this.weightsSM,
    RandomSM,
    RandomBoxMullerSM,
    CopyMutateParent.sm,
    MutationRate.sm,
  ], {
    outValues: outDef('vec4[8]'),
    outArchetype: uniformDef('int'),
    randomSeeds: uniformArrayDef('vec2[]', 8),
    weightsOffset: uniformDef('int'),
    renormalization: uniformDef('float', d => d.data.trainingConfig.weightsRenormalizationRate),
    main: funcDef('void', [], `
      ivec2 outDataCell = ivec2(gl_FragCoord);
      ivec2 outEntityCell = ivec2(
        outDataCell.y * ${ECSConsts.ChunkSize} + outDataCell.x,
        outArchetype
      );

      float mutationRate = ${MutationRate.sm.value}(outEntityCell);
      ivec2 readCell = ${CopyMutateParent.sm.value}(outEntityCell);
      if (readCell.x < 0) {
        readCell = outEntityCell;
      }

      vec4 values[8];
      for (int i=0; i < 8; i++) {
        vec4 weight = vec4(${this.weightsSM.value}(readCell, weightsOffset + i));

        if (mutationRate > 0.) {
          vec2 s = vec2(outEntityCell) / 1024.0 + randomSeeds[i];
          vec4 r = vec4(
            ${RandomBoxMullerSM.sample}(s + 0.0, s + 0.1),
            ${RandomBoxMullerSM.sample}(s + 0.2, s + 0.3),
            ${RandomBoxMullerSM.sample}(s + 0.4, s + 0.5),
            ${RandomBoxMullerSM.sample}(s + 0.6, s + 0.7)
          );

          weight += r * mutationRate;
          weight *= (1. - mutationRate * renormalization);
        }
        values[i] = weight;
      }
      outValues = values;
    `)
  }));
  private renderTarget = new RenderTarget(this.gls);
  private program = new Program(this.gls, this.shader.source);
  private copy = this.gls.getCopyTexture();
  run(state: GpuWorldState) {
    this.shader.updateUniformValues(state);
    for (const block of new ArchetypesQuery([this.weightsComponent]).gpuComponentBlocks(this.weightsComponent).get(state)) {
      for (const { l, count } of createBatches(block.slices.layers.length, 8)) {
        this.program.prepare();
        this.shader.writeUniforms(this.program);
        const tmp = this.gls.getTmpTextureSlice(
          block.slices.texture.internalFormat,
          block.slices.rect, count);
        this.renderTarget.set(tmp);
        this.gls.prepareForComputation(tmp.rect);
        this.program.setUniform('randomSeeds', 'vec2[]', new Array(8)
          .fill(0).map(() => ({ x: Math.random(), y: Math.random() })));
        this.program.setUniform('weightsOffset', 'int', l);
        this.program.setUniform('outArchetype', 'int', block.archetype.index);
        this.gls.drawQuad(this.program);
        this.copy.copy(block.slices.sliceRange(l, count), tmp);
      }
    }
  }
}
