import { GpuWorldState } from "./State";
import { EntityInit, EntitiesRange } from "../GpuUtil/ECS";
import { RotationZ, OffsetLocalToWorld, LocalToWorld, Position, TransformParent, LocalToParentOrderComponents, LocalToParent } from "./ECS/Transform";
import { Unit, Hitpoints, Mana, ManaRegenComponent, HitpointsRegenComponent, Creature, Team, TailAttachment, HandLeftAttachment, HandRightAttachment, CpuTeamComponent, CpuPlayerUnitComponent, MaxHitpoints, MaxMana, HeadAttachment, BackAttachment, HitpointsTrailing, ProjectileAttachment, WorthPoints, PlayerUnit, AnimatableUnitComponent, UnitMovement, UnitMovementSpeed, UnitRotationSpeed, UnitSoundMaterialComponent, Focuser, Focusable, UnitSoundsComponent, PreviousHitpoints, DiedStep, Velocity, IsOnGround, Force, WalkingSpeed, Gold, PreviousGold, UnitStats, DuckAI, CrabAI, Stuck, LastPushedBy, IsTakingFallDamage, BountyComponents, ModelCreatedComponent, GymIndex } from "./ECS/UnitComponents";
import { Dazeable } from "./Buffs/Daze";
import { IronBubbable, HeliumBubbable } from "./Buffs/Bubbles";
import { ThreeModel } from "../GpuUtil/Model";
import * as Mat4 from '../Math/Mat4';
import { Teams, UnitAnimations, TeamPoints, StatuePoints } from "./StateLayout";
import { UnitAbilities, Abilities, createAbility } from "./Abilities/Abilities";
import { Casting } from "./Abilities/Abilities";
import { Items, AttachmentItem, BountyDefs, UnitBounties } from "db";
import { AnimationControllerComponent, AnimationBinderComponent } from "./ECS/Animation";
import { EventDispatcher, rgba255ToVec401, rgbFromHex, SimpleProfiler } from "../Util";
import UnitMarkerGlb from '../Models/UnitMarker.glb';
import { HiddenLayer, OutputLayer, Senses, MemoryLayer, GymExtraSenses } from "./Brain/Components";
import { ArenaInstance, ArenaMemberIndex, VisibleComponent } from "./ECS/Components";
import firebase from 'firebase/app';
import { UnitDef } from "./UnitDef";
import { DecisionsCMs } from "./Brain/Decisions";
import { Crippleable } from "./Buffs/Cripple";
import { MutationRate, CopyMutateParent } from "./Tools/CopyAndMutate";

const UnitMarkerModel = ThreeModel.fromPath(UnitMarkerGlb);

export class CreatePlayerUnitOptions {
  createAbilities = true;
  createBrain = false;
  bounties?: Partial<UnitBounties>;
  profiler?: SimpleProfiler;
}

const emptyBounties = BountyDefs.reduce((p, x) => ({...p, [x.key]: x.emptyValue}), {});

function getAbilityForItem(item: string | undefined): Abilities {
  if (item) {
    const it = Items[item] as AttachmentItem;
    return Abilities[it.ability] ?? Abilities.None;
  }
  return Abilities.None;
}
function getWeights(weights: number[] | firebase.firestore.Blob) {
  if (!Array.isArray(weights)) {
    return new Float32Array(weights.toUint8Array().buffer);
  } else {
    return new Float32Array(weights);
  }
}

export interface CreateUnitDef<U extends UnitDef = UnitDef> {
  unit: U;
  arenaIndex?: number;
  arenaMemberIndex?: number;
  team?: Teams;
  gymIndex?: [number, number];
  uploadBrain?: boolean;
}

export async function createPlayerUnits(state: GpuWorldState, units: CreateUnitDef[], opts: CreatePlayerUnitOptions = new CreatePlayerUnitOptions()) {
  const unitType = units[0].unit.type;
  opts.profiler?.enter(`createUnits(${unitType}, ${units.length})`);
  const count = units.length;

  let entityInit = new EntityInit()
    // Static
    .setComponent(CpuPlayerUnitComponent, { unit: units[0].unit })
    .setComponent(Unit.component)
    .setComponents(ArenaInstance.components)
    .setComponents(ArenaMemberIndex.components)
    .setComponent(AnimatableUnitComponent, { stepLength: 2.1 })
    .setComponent(ManaRegenComponent)
    .setComponent(HitpointsRegenComponent)
    .setComponent(Team.component)
    .setComponent(CpuTeamComponent, Teams.HomeTeam)
    .setComponent(UnitMovement.component)
    .setComponent(UnitMovementSpeed.component, [12])
    .setComponent(UnitRotationSpeed.component, [0.15])
    .setComponent(UnitSoundMaterialComponent, { value: 'flesh' })
    .setComponent(WorthPoints.component)

    // Model stuff. These are "filled in" in CreateUnitModel.ts
    .setComponent(LocalToWorld.gpuComponent)
    .setComponent(AnimationBinderComponent, { entityLookup: {}, clipNames: {} })
    .setComponent(AnimationControllerComponent, {
      actions: [{
        clip: UnitAnimations[UnitAnimations.Idle1],
        positionType: 'startTime',
        positionValue: 0,
        looping: true
      }]
    })
    .setComponent(VisibleComponent, false)
    .setComponent(ModelCreatedComponent, false)

    // Reset every round
    .setComponent(Position.gpuComponent, [new Float32Array([0, 0, 0, 0])])
    .setComponent(RotationZ.component, [0])
    .setComponent(Focuser.component, [-1, -1])
    .setComponent(Focusable.component, [1])
    .setComponent(UnitSoundsComponent, { playing: {} })
    .setComponent(Hitpoints.component, [MaxHitpoints])
    .setComponent(Mana.component, [MaxMana])
    .setComponent(HitpointsTrailing.component, [MaxHitpoints])
    .setComponent(PreviousHitpoints.component, [MaxHitpoints])
    .setComponent(DiedStep.component, [-1])
    .setComponent(LastPushedBy.component, [-1, -1])
    .setComponent(IsTakingFallDamage.component)
    .setComponent(Dazeable.component)
    .setComponent(IronBubbable.component)
    .setComponent(HeliumBubbable.component)
    .setComponent(Crippleable.component)
    .setComponent(Velocity.component, [new Float32Array([0, 0, 0, 0])])
    .setComponent(IsOnGround.component, [new Float32Array([1, 0, 0, 0])])
    .setComponent(Force.component, [new Float32Array([0, 0, 0, 0])])
    .setComponent(Stuck.component)
    .setComponent(WalkingSpeed.component)
    .setComponent(Gold.component)
    .setComponent(PreviousGold.component)

  for (const decision of DecisionsCMs) {
    entityInit = entityInit.setComponent(decision.component);
  }
  for (const key of Object.keys(UnitStats)) {
    entityInit = entityInit.setComponent(UnitStats[key].component);
  }
  for (const key of Object.keys(Casting)) {
    entityInit = entityInit.setComponent(Casting[key].component);
  }

  const onUnitsCreated = new EventDispatcher<[EntitiesRange]>();
  let entities: EntitiesRange;

  switch (unitType) {
    case 'creature': {

      entityInit = entityInit
        // Static
        .setComponent(Creature.component)
        .setComponent(PlayerUnit.component)
        .setComponent(GymIndex.component)

        .setComponent(UnitAbilities.component)
        .setComponent(WalkingSpeed.component)
        .setComponent(Senses.component)

      if (state.data.gameInstance.type === 'gym') {
        entityInit = entityInit.setComponent(GymExtraSenses.component);
      }

      if (opts.createBrain) {
        // Weights & brain
        entityInit = entityInit.setComponent(CopyMutateParent.component)
          .setComponent(MutationRate.component)
          .setComponent(HiddenLayer.weights.component)
          .setComponent(HiddenLayer.biases.component)
          .setComponent(HiddenLayer.res.component)
          .setComponent(MemoryLayer.weights.component)
          .setComponent(MemoryLayer.biases.component)
          .setComponent(MemoryLayer.res.component)
          .setComponent(OutputLayer.weights.component)
          .setComponent(OutputLayer.biases.component)
          .setComponent(OutputLayer.res.component);
      }

      for (const key of Object.keys(BountyComponents)) {
        entityInit = entityInit.setComponent(BountyComponents[key].component, [opts.bounties?.[key] ?? emptyBounties[key]]);
      }

      entities = state.ecs.createEntities(entityInit
        .setComponent(OffsetLocalToWorld.component, Mat4.toRows(Mat4.mul(
          Mat4.rotationZ(Math.PI / 2)
        )))
        .setComponent(TailAttachment.component)
        .setComponent(BackAttachment.component)
        .setComponent(HeadAttachment.component)
        .setComponent(HandLeftAttachment.component)
        .setComponent(HandRightAttachment.component)
        .setComponent(ProjectileAttachment.component),
        count);


      const brainComps = [
        HiddenLayer.weights.component,
        HiddenLayer.biases.component,
        MemoryLayer.weights.component,
        MemoryLayer.biases.component,
        OutputLayer.weights.component,
        OutputLayer.biases.component,
      ]
      for (let i=0; i < units.length; i++) {
        const creature = units[i].unit;
        if (creature.type !== 'creature') {
          throw new Error('All units in batch need to be of the same type');
        }
        if (units[i].uploadBrain) {
          const weights = [
            creature.brain?.hiddenLayerWeights,
            creature.brain?.hiddenLayerBiases,
            creature.brain?.memoryLayerWeights,
            creature.brain?.memoryLayerBiases,
            creature.brain?.outputLayerWeights,
            creature.brain?.outputLayerBiases,
          ]
          for (let l = 0; l < brainComps.length; l++) {
            if (weights[l]) {
              state.ecs.setComponentData(brainComps[l], entities.getIndex(i), getWeights(weights[l]));
            }
          }
        }
      }

      if (opts.createAbilities) {
        opts.profiler?.enter('createAbilities');
        for (let i=0; i < units.length; i++) {
          const creature = units[i].unit;
          if (creature.type !== 'creature') {
            throw new Error('All units in batch need to be of the same type');
          }
          const abilities = new Array(3).fill(0).map((_, i) => getAbilityForItem(creature.slots![i]));
          state.ecs.setComponentData(UnitAbilities.component, entities.getIndex(i), abilities);
          for (let slotIndex=0; slotIndex < 3; slotIndex++) {
            if (creature.slots![slotIndex] && abilities[slotIndex] !== Abilities.None) {
              const item = Items[creature.slots![slotIndex]];
              if (item) {
                await createAbility(state, entities.getIndex(i), units[i].arenaIndex ?? 0, abilities[slotIndex],
                  onUnitsCreated.transform(entities => [entities.getIndex(i)]));
              }
            }
          }
        }
        opts.profiler?.exit();
      }
      opts.profiler?.enter('setBounties');
      for (let i=0; i < units.length; i++) {
        state.ecs.setComponentData(GymIndex.component, entities.getIndex(i), units[i].gymIndex ?? [-1, -1]);
        const creature = units[i].unit;
        if (creature.type !== 'creature') {
          throw new Error('All units in batch need to be of the same type');
        }
        const bounties = creature.bounties;
        if (bounties) {
          for (const key of Object.keys(bounties)) {
            state.ecs.setComponentData(BountyComponents[key].component, entities.getIndex(i), [bounties[key]]);
          }
        }
      }
      opts.profiler?.exit();

      break;
    }
    case 'statue': {
      entityInit = entityInit
        .setComponent(OffsetLocalToWorld.component, Mat4.toRows(Mat4.mul(
          Mat4.rotationZ(Math.PI / 2)
        )))
        .removeComponent(WalkingSpeed.component)
        .removeComponent(Focuser.component)
        .removeComponent(Velocity.component)
        .removeComponent(UnitMovement.component)
        .removeComponent(Gold.component)
        .removeComponent(PreviousGold.component)
        .setComponent(UnitSoundMaterialComponent, { value: 'statue' });

      for (const key of Object.keys(Casting)) {
        entityInit = entityInit.removeComponent(Casting[key].component);
      }
      entities = state.ecs.createEntities(entityInit, count);
      break;
    }
    default: {
      entityInit = entityInit
        .setComponent(PlayerUnit.component);

      if (unitType === 'scarecrow') {
        entityInit = entityInit
          .removeComponent(WalkingSpeed.component)
          .removeComponent(Focuser.component)
          .removeComponent(Velocity.component)
          .removeComponent(UnitMovement.component)
          .setComponent(UnitSoundMaterialComponent, { value: 'wood' });

        for (const key of Object.keys(Casting)) {
          entityInit = entityInit.removeComponent(Casting[key].component);
        }
      } else {
        entityInit = entityInit
          .setComponent(OffsetLocalToWorld.component, Mat4.toRows(Mat4.mul(
            Mat4.rotationZ(Math.PI / 2)
          )));
        if (unitType === 'duck') {
          entityInit = entityInit.setComponent(DuckAI.component);
        } else if (unitType === 'turtle') {
          entityInit = entityInit
            .setComponent(CrabAI.component)
            .setComponent(UnitAbilities.component, [Abilities.Pistol, Abilities.None, Abilities.None])
            .setComponent(AnimatableUnitComponent, { stepLength: 0.7 })
            .setComponent(UnitMovementSpeed.component, [1.5])
            .setComponent(UnitRotationSpeed.component, [0.03]);
        } else {
          entityInit = entityInit
            .setComponent(CrabAI.component)
            .setComponent(UnitAbilities.component, [Abilities.Talons, Abilities.None, Abilities.None])
            .setComponent(UnitRotationSpeed.component, [0.07]);
        }
      }
      entities = state.ecs.createEntities(entityInit
        .setComponent(ProjectileAttachment.component), count);
      if (unitType === 'turtle') {
        for (let i=0; i < units.length; i++) {
          await createAbility(state, entities.getIndex(i), units[i].arenaIndex ?? 0, Abilities.Pistol, onUnitsCreated.transform(entities => [entities.getIndex(i)]));
        }
      } else if (unitType === 'crab') {
        for (let i=0; i < units.length; i++) {
          await createAbility(state, entities.getIndex(i), units[i].arenaIndex ?? 0, Abilities.Talons, onUnitsCreated.transform(entities => [entities.getIndex(i)]));
        }
      }
    }
  }
  opts.profiler?.enter('setProps');
  for (let i=0; i < count; i++) {
    state.ecs.setComponentData(CpuPlayerUnitComponent, entities.getIndex(i), { unit: units[i].unit });
    state.ecs.setComponentsData(ArenaInstance.components, entities.getIndex(i), new Float32Array([units[i].arenaIndex ?? 0]));
    state.ecs.setComponentsData(ArenaMemberIndex.components, entities.getIndex(i), new Float32Array([units[i].arenaMemberIndex ?? 0]));
    const team = units[i].team ?? Teams.HomeTeam;
    state.ecs.setComponentData(Team.component, entities.getIndex(i), [team]);
    state.ecs.setComponentData(CpuTeamComponent, entities.getIndex(i), team);
    const sideUnitCount = team === Teams.HomeTeam ? state.data.teams.home.unitCount : state.data.teams.away.unitCount;
    state.ecs.setComponentData(WorthPoints.component, entities.getIndex(i), [unitType === 'statue' ? StatuePoints : (TeamPoints / (sideUnitCount - 1))])
  }
  opts.profiler?.exit();

  opts.profiler?.enter('createUnitMarker');
  const unitMarker = await UnitMarkerModel;
  opts.profiler?.enter('createModels');
  const unitMarkers = unitMarker.createInstances(state, {
    count,
    rootEntityInit: new EntityInit()
      .setComponent(LocalToParent.component, Mat4.toRows(Mat4.identity()))
      .setComponent(TransformParent.component)
      .setComponent(LocalToParentOrderComponents[0])
      .setComponents(ArenaInstance.components, new Float32Array([-1])),
    localToParentStartOrder: 1,
    onMaterial: (mat, index) => {
      const a = 0.4;
      const unit = units[index].unit;
      if (mat.name === 'UnitMaterial') {
        return rgba255ToVec401(unit.type === 'creature' ?
          {...rgbFromHex(unit.primaryColor!), a }
          : unit.type === 'duck' ? { r: 255, g: 255, b: 255, a }
          : unit.type === 'crab' ? { r: 255, g: 0, b: 0, a }
          : unit.type === 'turtle' ? { r: 0, g: 255, b: 0, a }
          : unit.type === 'scarecrow' ? { r: 217, g: 203, b: 98, a }
          : unit.type === 'statue' ? { r: 200, g: 200, b: 200, a }
          : { r: 0, g: 0, b: 0, a })
      } else {
        return units[index].team === Teams.HomeTeam ? { x: 90/255, y: 110/255, z: 83/255, w: a }
          : { x: 163/255, y: 84/255, z: 73/255, w: a };
      }
    }
  });
  opts.profiler?.exit();
  opts.profiler?.enter('setTransformParent');
  for (let i=0; i < count; i++) {
    const entityCell = entities.getIndex(i);
    state.ecs.setComponentData(TransformParent.component, unitMarkers.roots.getIndex(i), [{ ...entityCell, z: 0, w: 0 }]);
  }
  opts.profiler?.exit();
  opts.profiler?.exit();

  onUnitsCreated.dispatch(entities);
  opts.profiler?.exit();
  return entities;
}
