import { base62uuid, getBrowser, isMobile } from "../Util";
import firebase from 'firebase/app';
import { Db, full, DbSession, CurrentSeason } from "db";
import { version } from "../version";

export class SessionRecorder {
  constructor(public user: firebase.User) {
    if (user) {
      this.doc.set(full<DbSession>({
        id: this.id,
        season: CurrentSeason,
        userId: user.uid,

        started: firebase.firestore.Timestamp.now(),
        lastActive: firebase.firestore.Timestamp.now(),
        minutesInteracting: 0,

        appVersion: version.build,
        browser: getBrowser(),
        screenSize: { width: window.screen.width, height: window.screen.height },
        isMobile: isMobile(),

        battles: 0,
        trainings: 0,
      }));
    }
  }
  static Current?: SessionRecorder;
  static init() {
    if (!version.isDevelopment) {
      firebase.auth().onAuthStateChanged(user => {
        SessionRecorder.Current = user ? new SessionRecorder(user) : undefined;
      });
    }
    document.addEventListener('click', () => SessionRecorder.Current?.onDocumentClick());
    window.setInterval(() => SessionRecorder.Current?.onMinuteTicker(), 60*1000);
  }
  id = base62uuid();
  private doc = firebase.firestore().collection(Db.sessions).doc(this.id);
  private clicksLastMinute = 0;
  onDocumentClick() {
    this.clicksLastMinute++;
  }
  onMinuteTicker() {
    const wasActive = this.clicksLastMinute > 0;
    if (wasActive) {
      this.doc.update({
        lastActive: firebase.firestore.Timestamp.now(),
        minutesInteracting: firebase.firestore.FieldValue.increment(1)
      });
    }
    this.clicksLastMinute = 0;
  }
  onClickTrain() {
    this.doc.update({
      trainings: firebase.firestore.FieldValue.increment(1)
    });
  }
  onClickBattle() {
    this.doc.update({
      battles: firebase.firestore.FieldValue.increment(1)
    });
  }
}
