import { GlState } from "../../GpuUtil/GlState";
import { ArchetypesQuery } from "../../GpuUtil/ECS/Query";
import { shaderModule, funcDef } from "../../GpuUtil/ShaderModule";
import { Position, RotationZ } from "./Transform";
import { Hitpoints, MaxHitpoints, DuckAI } from "./UnitComponents";
import { AngleLerpSM, MathConstsSM, RandomUtilSM } from "../../GpuUtil/SM2Utils";
import { Decisions } from "../Brain/Decisions";
import { glslFloat } from "../../GpuUtil";
import { Seeds } from "../../Util";

export function UpdateDuckAI(gls: GlState) {
  return new ArchetypesQuery([DuckAI.component])
    .updateGpu(gls, [Decisions.MoveX.component, Decisions.Rotate.component], shaderModule([
      Decisions.MoveX.writer, Decisions.Rotate.writer,
      Position.sm, Hitpoints.sm, RotationZ.sm, AngleLerpSM, MathConstsSM,
      RandomUtilSM,
    ], {
      update: funcDef('void', [['ivec2', 'entityCell']], `
        float moveX = 0.;
        float rotation = ${RotationZ.sm.value}(entityCell);
        float hitpoints = ${Hitpoints.sm.value}(entityCell);
        float rotate;
        if (hitpoints > 0.) {
          if (hitpoints >= ${glslFloat(MaxHitpoints)}) {
            // Roaming
            float mc = ${RandomUtilSM.entityRandom}(entityCell, ${Seeds.get('duckMoveChange1')}, ${Seeds.get('duckMoveChange2')});
            if (mc > 0.9) {
              moveX = moveX > 0. ? 0. : 0.1;
            }
            float rc = ${RandomUtilSM.entityRandom}(entityCell, ${Seeds.get('duckRotateChange1')}, ${Seeds.get('duckRotateChange2')});
            if (rc > 0.9) {
              float rr = ${RandomUtilSM.entityRandom}(entityCell, ${Seeds.get('duckRotate1')}, ${Seeds.get('duckRotate2')});
              rotate = (rr * 2. - 1.);
            }
          } else {
            // Fleeing
            vec3 position = ${Position.sm.value}(entityCell);
            moveX = 1.;
            float targetRotation = position.x < position.y
              ? (${MathConstsSM.PI} * 3. / 4.)
              : (${MathConstsSM.PI} * 7. / 4.);
            float rot = ${AngleLerpSM.shortestAngleDist}(rotation, targetRotation);
            rotate = rot < 0. ? -1. : 1.;
          }
        }
        ${Decisions.MoveX.writer.write}(moveX);
        ${Decisions.Rotate.writer.write}(rotate);
      `)
    }))
}