import { Focuser, Team } from "../ECS/UnitComponents";
import { GlState } from "../../GpuUtil/GlState";
import { ArchetypesQuery } from "../../GpuUtil/ECS/Query";
import { shaderModule, funcDef } from "../../GpuUtil/ShaderModule";
import { CastingAbilityHelpers, Abilities } from "./Abilities";
import { UnitsInArena } from "../ECS/ArenaStats";
import { SideSize, Teams } from "../StateLayout";
import { ArenaInstance } from "../ECS/Components";
import { GpuWorldState } from "../State";

export class UpdateTrombone {
  constructor(private gls: GlState) {}

  query = new ArchetypesQuery([Focuser.component])
    .mapGpu(this.gls, Focuser.component, shaderModule([
      CastingAbilityHelpers,
      UnitsInArena,
      Team.sm,
      ArenaInstance.sm,
    ], {
      map: funcDef('ivec2', [['ivec2', 'entityCell'], ['ivec2', 'value']], `
        int unitsOffset = ${Team.sm.value}(entityCell) == ${Teams.HomeTeam} ? ${SideSize} : 0;
        int arenaIndex = ${ArenaInstance.sm.value}(entityCell);
        for (int i=0; i < ${SideSize}; i++) {
          ivec2 unit = ${UnitsInArena.unit}(arenaIndex, i + unitsOffset);
          if (${CastingAbilityHelpers.isPerforming}(unit, ${Abilities.Trombone})) {
            return unit;
          }
        }
        return value;
      `),
    }));

  run(state: GpuWorldState) {
    this.query.run(state);
  }
}