import * as React from 'react';
import './index.css';
import { Popdown } from '../Popdown';
import { notNullOrUndefined } from '../../Util';

export function Menu({ children }: { children?: any }) {
  return <div className="Menu">{children}</div>;
}
export interface MenuItemProps {
  title: string;
  checked?: boolean;
  highlighted?: boolean;
  disabled?: boolean;
  onClick?: () => void;
  onMouseDown?: () => void;
  className?: string;
}
export function MenuItem({ onClick, onMouseDown, title, checked, highlighted, disabled, className }: MenuItemProps) {
  return <div
    className={`MenuItem ${onClick ? 'MenuItemClickable' : ''} ${disabled === true ? 'MenuItemDisabled' : ''} ${highlighted === true ? 'MenuItemHighlighted' : ''} ${className ?? ''}`}
    onClick={() => {
      if (!disabled && onClick) {
        onClick();
      }
    }}
    onMouseDown={() => {
      if (!disabled && onMouseDown) {
        onMouseDown();
      }
    }}
    >
    {checked !== undefined && <span><i className={`far fa-${checked === true ? 'check-' : ''}square`} />&nbsp;</span>}{title}
  </div>
}

export type ActionMenuItem = MenuItemProps;

export function ActionMenu({ actions, disabled, children }: { actions: Array<ActionMenuItem | undefined> | (() => Array<ActionMenuItem | undefined>), disabled?: boolean, children?: any }) {
  const [showMenu, setShowMenu] = React.useState<boolean>(false);
  if (disabled) {
    return children || null;
  }
  return <Popdown
    className="ActionMenu"
    showPopdown={showMenu}
    onClickFader={() => setShowMenu(false)}
    popdown={() => {
      const acts = (typeof actions === 'function' ? actions() : actions).filter(notNullOrUndefined);
      return <Menu>
        {acts.map(({ onClick, ...rest}, i) =>
          <MenuItem key={i} onClick={onClick ? () => {
            onClick();
            setShowMenu(false);
          } : undefined} {...rest} />
          )}
      </Menu>
    }}
  >
    <div className="ActionMenuAction" onClick={() => setShowMenu(true)}>
      {children || <i className="material-icons">more_vert</i>}
    </div>
  </Popdown>
}
