import { GlState } from "../../GpuUtil/GlState";
import { GpuWorldState } from "../State";
import { StepsPerSecond } from "../../Time";
import { CpuPlayerUnitComponent, CpuTeamComponent, Hitpoints, MaxHitpoints, PlayerUnit } from "../ECS/UnitComponents";
import { shaderModule, funcDef } from "../../GpuUtil/ShaderModule";
import { Position } from "../ECS/Transform";
import { EntitiesQuery } from "../../GpuUtil/ECS/Query";
import * as Vector4 from '../../Math/Vector4';
import * as Vector2 from '../../Math/Vector2';
import { Teams, SideSize } from "../StateLayout";
import { ArenaWinner } from "../ECS/ArenaStats";
import { ArenaInstance, ArenaMemberIndex } from "../ECS/Components";
import lodash from 'lodash';
import { UnitDef } from "../UnitDef";
import { Gold } from "../ECS/UnitComponents";

interface ArenaUnitInfo {
  position: Vector2.Vector2;
  hitpoints: number;
  unit: UnitDef;
  gold: number;
}
export class ArenaInfo {
  winner = ArenaWinner.Undecided;
  homeUnits: ArenaUnitInfo[] = [];
  awayUnits: ArenaUnitInfo[] = [];
  homeGold = 0;
  awayGold = 0;
}

export const ArenaInfosLimit = 128;

export class UpdateArenaInfos {
  constructor(private gls: GlState) {}

  units = new EntitiesQuery([PlayerUnit.component])
    .filterValue(ArenaInstance.cpuComponent, v => v[0] < ArenaInfosLimit, d => []);
  unitDatas = this.units
    .toGpu(this.gls)
    .mapToTemp(shaderModule([Position.sm, Hitpoints.sm, Gold.sm], {
      map: funcDef('vec4', [['ivec2', 'entityCell']], `
        return vec4(${Position.sm.value}(entityCell).xy, ${Hitpoints.sm.value}(entityCell), ${Gold.sm.value}(entityCell));
      `)
    }))
    .toCpuQueuing(state => state.data.round);

  updateArenaInfos(state: GpuWorldState) {
    const arenaInfos = [...state.data.arenaInfos];
    for (let i=0; i < arenaInfos.length; i++) {
      arenaInfos[i] = { ...arenaInfos[i], homeUnits: [], awayUnits: []};
    }
    const units = this.units.get(state);
    const readResults = this.unitDatas.queueRead(state);
    const unitData = lodash.last(readResults());

    for (let i=0; i < units.length; i++) {
      const entityCell = units[i];
      const arenaIndex = state.ecs.getComponentData(ArenaInstance.cpuComponent, entityCell)[0];
      const arenaMemberIndex = state.ecs.getComponentData(ArenaMemberIndex.cpuComponent, entityCell)[0];
      const team = state.ecs.getComponentData(CpuTeamComponent, entityCell);
      const playUnit = state.ecs.getComponentData(CpuPlayerUnitComponent, entityCell)!;
      const unitD = unitData ? unitData[i] : undefined;
      const arenaUnitInfo: ArenaUnitInfo = {
        position: unitD ? Vector4.xy(unitD.value[0]) : Vector2.create(0),
        hitpoints: unitD ? unitD.value[0].z : MaxHitpoints,
        unit: playUnit.unit,
        gold: unitD ? unitD.value[0].w : 0,
      }
      if (team === Teams.HomeTeam) {
        arenaInfos[arenaIndex].homeUnits[arenaMemberIndex] = arenaUnitInfo;
      } else {
        arenaInfos[arenaIndex].awayUnits[arenaMemberIndex - SideSize] = arenaUnitInfo;
      }
    }
    for (let i=0; i < arenaInfos.length; i++) {
      arenaInfos[i] = {
        ...arenaInfos[i],
        homeGold: arenaInfos[i].homeUnits.reduce((p, x) => p + x.gold, 0),
        awayGold: arenaInfos[i].awayUnits.reduce((p, x) => p + x.gold, 0)
      };
    }
    state.setData({ arenaInfos });
  }

  run(state: GpuWorldState) {
    if (state.data.stepCounter % StepsPerSecond === 0 && !state.data.turboMode) {
      this.updateArenaInfos(state);
    }
  }
}
