import { EditorTypedef } from "../Components/Editors/EditorTypedefs";
import * as Vector3 from "./Vector3";
import * as Vector2 from "./Vector2";
import { vec4, quat } from 'gl-matrix';
import * as Math2 from './Math';

export interface Vector4 {
  x: number;
  y: number;
  z: number;
  w: number;
}

export const Vector4Typedef: EditorTypedef = {
  metatype: 'struct',
  properties: {
    x: { metatype: 'float' },
    y: { metatype: 'float' },
    z: { metatype: 'float' },
    w: { metatype: 'float' },
  }
}

export function create(xyzw: Vector4): Vector4;
export function create(xyzw: number): Vector4;

export function create(xyz: Vector3.Vector3, w: number): Vector4;
export function create(xy: Vector2.Vector2, zw: Vector2.Vector2): Vector4;
// tslint:disable-next-line: unified-signatures
export function create(x: number, yzw: Vector3.Vector3): Vector4;

export function create(xy: Vector2.Vector2, z: number, w: number): Vector4;
export function create(x: number, yz: Vector2.Vector2, w: number): Vector4;
export function create(x: number, y: number, zw: Vector2.Vector2): Vector4;

export function create(x: number, y: number, z: number, w: number): Vector4;
export function create(...args: any[]): Vector4 {
  if (args.length === 1) {
    if (typeof args[0] === 'number') {
      return { x: args[0], y: args[0], z: args[0], w: args[0] };
    } else {
      return {...args[0]};
    }
  } else if (args.length === 2) {
    if (typeof args[0] === 'number') {
      return { x: args[0], y: args[1].x, z: args[1].y, w: args[1].z };
    } else if (typeof args[1] === 'number') {
      return {...args[0], w: args[1] };
    } else {
      return {...args[0], z: args[1].x, w: args[1].y };
    }
  } else if (args.length === 3) {
    if (typeof args[0] === 'object') {
      return { x: args[0].x, y: args[0].y, z: args[1], w: args[2] };
    } else if (args[1] === 'object') {
      return { x: args[0], y: args[1].x, z: args[1].y, w: args[2] };
    } else {
      return { x: args[0], y: args[1], z: args[2].x, w: args[2].y };
    }
  } else {
    return { x: args[0], y: args[1], z: args[2], w: args[3] };
  }
}

export function xy(v: Vector4): Vector2.Vector2 {
  return { x: v.x, y: v.y };
}
export function yz(v: Vector4): Vector2.Vector2 {
  return { x: v.y, y: v.z };
}
export function zw(v: Vector4): Vector2.Vector2 {
  return { x: v.z, y: v.w };
}
export function xyz(v: Vector4): Vector3.Vector3 {
  return { x: v.x, y: v.y, z: v.z };
}
export function yzw(v: Vector4): Vector3.Vector3 {
  return { x: v.y, y: v.z, z: v.w };
}

export function fromNumbers(numbers: number[] | Float32Array): Vector4 {
  return { x: numbers[0], y: numbers[1], z: numbers[2], w: numbers[3] };
}

export function toGlVec4({ x, y, z, w }: Vector4): vec4 {
  return vec4.fromValues(x, y, z, w);
}
export function fromGlVec4(vec: vec4): Vector4 {
  return { x: vec[0], y: vec[1], z: vec[2], w: vec[3] };
}
export function toArray({ x, y, z, w }: Vector4): [number, number, number, number] {
  return [x, y, z, w];
}
export function toGlQuat({ x, y, z, w }: Vector4): quat {
  return quat.fromValues(x, y, z, w);
}


export function add(a: Vector4, b: Vector4): Vector4 {
  return { x: a.x + b.x, y: a.y + b.y, z: a.z + b.z, w: a.w + b.w };
}

export function sub(a: Vector4, b: Vector4): Vector4 {
  return { x: a.x - b.x, y: a.y - b.y, z: a.z - b.z, w: a.w - b.w };
}

export function scale(vec: Vector4, f: number): Vector4 {
  return { x: vec.x * f, y: vec.y * f, z: vec.z * f, w: vec.w * f };
}

export function mul(a: Vector4, b: Vector4): Vector4 {
  return { x: a.x * b.x, y: a.y * b.y, z: a.z * b.z, w: a.w * b.w };
}
export function div(a: Vector4, b: Vector4): Vector4 {
  return { x: a.x / b.x, y: a.y / b.y, z: a.z / b.z, w: a.w / b.w };
}

export function interpolate(value: Vector4, min: Vector4, max: Vector4, valueMin: Vector4, valueMax: Vector4): Vector4 {
  const p = div(sub(value, min), sub(max, min));
  return add(valueMin, mul(p, sub(valueMax, valueMin)));
}

export function withoutNaNInf(v: Vector4): Vector4 {
  return {
    x: Math2.withoutNaNInf(v.x),
    y: Math2.withoutNaNInf(v.y),
    z: Math2.withoutNaNInf(v.z),
    w: Math2.withoutNaNInf(v.w),
  }
}
