import { GpuWorldState } from "../State";
import { EntitiesQuery } from "../../GpuUtil/ECS/Query";
import { GlState } from "../../GpuUtil/GlState";
import { shaderModule, funcDef, uniformDef } from "../../GpuUtil/ShaderModule";
import { ArenaStats, ArenaWinner } from "../ECS/ArenaStats";
import { Arena } from "../ECS/Components";

export class RoundStats {
  wins: number[] = [];
  losses: number[] = [];
  ties: number[] = [];
  homeAlive: number[] = [];
  awayAlive: number[] = [];
}

export class UpdateRoundStats {
  constructor(private gls: GlState) {}

  arenaStats = new EntitiesQuery([Arena.gpuComponent])
    .toGpu(this.gls)
    .mapToTemp(shaderModule([ArenaStats, Arena.sm], {
      homeCount: uniformDef('int', d => d.data.teams.home.unitCount),
      awayCount: uniformDef('int', d => d.data.teams.away.unitCount),
      map: funcDef('vec4[2]', [['ivec2', 'entityCell']], `
        int arenaIndex = ${Arena.sm.value}(entityCell);
        ${ArenaStats.Stats} stats = ${ArenaStats.stats}(arenaIndex);
        return vec4[](
          vec4(
            float(stats.winner == ${ArenaWinner.Home}),
            float(stats.winner == ${ArenaWinner.Away}),
            float(stats.winner == ${ArenaWinner.Tie}),
            0
          ),
          vec4(
            stats.home.alive,
            stats.away.alive,
            0,
            0
          )
        );
      `)
    }))
    .reduceSum();

  run(state: GpuWorldState) {
    const arenaStats = this.arenaStats.get(state);
    state.setData({
      roundStats: {
        wins: [...state.data.roundStats.wins, arenaStats.data[0]],
        losses: [...state.data.roundStats.losses, arenaStats.data[1]],
        ties: [...state.data.roundStats.ties, arenaStats.data[2]],
        homeAlive: [...state.data.roundStats.homeAlive, arenaStats.data[4] / state.layout.arenas],
        awayAlive: [...state.data.roundStats.awayAlive, arenaStats.data[5] / state.layout.arenas],
      }
    });
  }
}
