import { GpuBufferModule, GpuComponentModule } from "../../GpuUtil/ECS/ComponentModules";
import { BrainOutputsKeys } from "../StateLayout";
import { keysOfEnum } from "../../Util";
import { Sense, GymExtraSense } from "./SensesDef";

function createLayerComponents(name: string, conf: { weightsSlices: number, biasesSlices: number, outputSlices: number }) {
  const weightsBuffer = new GpuBufferModule({
    name: name + 'LayerWeightsBuffer',
    format: 'vec4'
  });
  const biasesBuffer = new GpuBufferModule({
    name: name + 'LayerBiasesBuffer',
    format: 'float'
  });
  const resBuffer = new GpuBufferModule({
    name: name + 'LayerResBuffer',
    format: 'vec4'
  });
  return {
    weights: new GpuComponentModule(
      name + 'LayerWeights',
      weightsBuffer.buffer,
      'vec4',
      conf.weightsSlices
    ),
    biases: new GpuComponentModule(
      name + 'LayerBiases',
      biasesBuffer.buffer,
      'float',
      conf.biasesSlices
    ),
    res: new GpuComponentModule(
      name + 'LayerRes',
      resBuffer.buffer,
      'vec4',
      conf.outputSlices
    )
  };
}


export const SensesSlices = Math.ceil(keysOfEnum(Sense).length / 4)
export const MemorySlices = 8;
export const HiddenAndMemoryInputSlices = SensesSlices + MemorySlices;

export const HiddenLayerLayout = new class {
  inputSlices = HiddenAndMemoryInputSlices;
  outputSlices = 8;
  outputs = this.outputSlices * 4;
  weightsSlices = this.inputSlices * this.outputs;
  weights = this.weightsSlices * 4;
  biasesSlices = this.outputs;
}();

export const MemoryLayerLayout = new class {
  inputSlices = HiddenAndMemoryInputSlices;
  outputSlices = MemorySlices;
  outputs = this.outputSlices * 4;
  weightsSlices = this.inputSlices * this.outputs;
  weights = this.weightsSlices * 4;
  biasesSlices = this.outputs;
}();

export const OutputLayerLayout = new class {
  // Only use half of the hidden state as input (half is in the memory layer)
  inputSlices = HiddenLayerLayout.outputSlices;
  outputSlices = Math.ceil(BrainOutputsKeys.length / 4);
  outputs = this.outputSlices * 4;
  weightsSlices = this.inputSlices * this.outputs;
  weights = this.weightsSlices * 4;
  biasesSlices = this.outputs;
}();

export const MemoryLayer = createLayerComponents('Memory', MemoryLayerLayout);
export const HiddenLayer = createLayerComponents('Hidden', HiddenLayerLayout);
export const OutputLayer = createLayerComponents('Output', OutputLayerLayout);

const SensesBuffer = new GpuBufferModule({
  name: 'SensesBuffer',
  format: 'vec4'
});
export const Senses = new GpuComponentModule(
  'Senses',
  SensesBuffer,
  'vec4',
  SensesSlices,
);
export const GymExtraSensesSlices = Math.ceil(keysOfEnum(GymExtraSense).length / 4)
export const GymExtraSenses = new GpuComponentModule(
  'GymExtraSenses',
  SensesBuffer,
  'vec4',
  GymExtraSensesSlices,
);
