interface ArenaRegion {
  sides: 'home' | 'away' | 'both';
  start_arena: number;
  n_arenas: number;
}
type DerkAgentHostConfig = { uri: string, regions: ArenaRegion[] };

export interface GymConfig {
  agentHosts: DerkAgentHostConfig[]
  home?: Array<Partial<UnitDef>>;
  away?: Array<Partial<UnitDef>>;
  rewardFunction: Partial<UnitBounties>;
  nArenas: number;
  substeps: number;
  randomSeed: string;
  turboMode: boolean;
  interleaved: boolean;
  debugNoObservations: boolean;
  debugNoActionsWrite: boolean;
  webWorkerSocket: boolean;
  aiCrowdLogo: boolean;
  gameState: boolean;
}

const DecisionKeys = Object.keys(Decisions);
const NAgentsPerTeam = 3;

enum GymTeamStats {
  Reward,
  OpponentReward,
  Hitpoints,
  AliveTime,
  CumulativeHitpoints,
}

class RemoteClosedError extends Error {}

class RemoteHost {
  constructor(public host: string, public regions: ArenaRegion[], public totalNArenas: number, public runInWebWorker: boolean) {

    this.teamRects = regions.map(({ sides, start_arena, n_arenas }) => ({
      x: (start_arena ?? 0),
      y: sides === 'away' ? 1 : 0,
      width: ((n_arenas !== undefined && n_arenas >= 0) ? n_arenas : totalNArenas),
      height: sides === 'both' ? 2 : 1
    }));
    this.agentRects = this.teamRects.map(rect => ({
      x: rect.x * NAgentsPerTeam,
      y: rect.y,
      width: rect.width * NAgentsPerTeam,
      height: rect.height
    }));
    this.nTeams = this.teamRects.reduce((p, x) => p + x.width * x.height, 0);

    this.socket.binaryType = 'arraybuffer';
    this.socket.addEventListener('message', event => {
      // console.log('Got message', event.data.length, event.data);
      if (this.waiting) {
        this.waiting.resolve(event.data);
        this.waiting = undefined;
      } else {
        this.messageQueue.push(event.data);
      }
    });
    this.socket.addEventListener('close', () => {
      if (this.waiting) {
        this.waiting.reject(new RemoteClosedError());
        this.waiting = undefined;
      }
    });
  }
  send(data: string | ArrayBuffer) {
    if (this.socket.readyState === WebSocket.CLOSED || this.socket.readyState === WebSocket.CLOSING) {
      throw new RemoteClosedError();
    }
    this.socket.send(data);
  }
  close() {
    this.socket.close();
  }
  agentRects: Rect[];
  teamRects: Rect[];
  nTeams: number;
  private socket = this.runInWebWorker ? new WebSocketAsync(this.host) : new WebSocket(this.host);
  connected = new Promise<void>(resolve => {
    const handle = () => {
      resolve();
      this.socket.removeEventListener('open', handle);
    }
    this.socket.addEventListener('open', handle);
  });
  error = new Promise<void>(resolve => {
    const handle = () => {
      resolve();
      this.socket.removeEventListener('error', handle);
    }
    this.socket.addEventListener('error', handle);
  });
  private messageQueue: any[] = [];
  private waiting?: { resolve: (value: any) => void, reject: (error: any) => void };
  async message(): Promise<any> {
    if (this.socket.readyState === WebSocket.CLOSED || this.socket.readyState === WebSocket.CLOSING) {
      throw new RemoteClosedError();
    }
    if (this.messageQueue.length > 0) {
      return this.messageQueue.shift();
    } else {
      return await new Promise((resolve, reject) => { this.waiting = { resolve, reject } });
    }
  }
}

function getRectsData(image: Image, rects: Rect[]): Float32Array {
  const res = new Float32Array(rects.reduce((p, x) => p + x.width * x.height * image.size.depth * image.channels, 0));
  let offset = 0;
  for (const rect of rects) {
    const size = rect.width * rect.height * image.size.depth * image.channels;
    image.select(rect).toArray(res.subarray(offset, offset + size));
    offset += size;
  }
  return res;
}

function padSenses(keys: string[], padKey: string) {
  keys = [...keys];
  while (keys.length % 4 !== 0) {
    keys.push(padKey + keys.length);
  }
  return keys;
}
const AllObservationKeys = [
  ...padSenses(keysOfEnum(Sense), 'UnusedSense'),
  ...padSenses(keysOfEnum(GymExtraSense), 'UnusedExtraSense')
];

class GymTools {
  constructor(private gls: GlState) {}
  readRewards = new ReadRewards(this.gls);
  readSenses = new ReadSenses(this.gls);
  readTeamStats = new ReadTeamStats(this.gls);
  writeActions = new WriteActions(this.gls);
}

enum RemoteConnectionState {
  Connected,
  Error
}

function defaultRewardFunction(): Partial<UnitBounties> {
  return {
    killEnemyStatue: 4,
    killEnemyUnit: 1,
    timeScaling: 1
  }
}

class DerkSession {
  constructor(private program: GpuWorldProgram, public appState: Store<GymState>, public tools: GymTools, config: Partial<GymConfig>) {
    const rewardFunction = {...defaultRewardFunction(), ...config.rewardFunction };
    this.config = {
      agentHosts: config.agentHosts!,
      home: config.home,
      away: config.away,
      rewardFunction,
      nArenas: config.nArenas ?? 1,
      substeps: config.substeps ?? 8,
      randomSeed: config.randomSeed ?? base62uuid(),
      turboMode: config.turboMode ?? false,
      interleaved: config.interleaved ?? true,
      debugNoObservations: config.debugNoObservations ?? false,
      debugNoActionsWrite: config.debugNoActionsWrite ?? false,
      webWorkerSocket: config.webWorkerSocket ?? false,
      aiCrowdLogo: config.aiCrowdLogo ?? false,
      gameState: config.gameState ?? false,
    };
    this.creaturesTexture = this.creaturesTexture = Texture.create(this.program.gls, GL.RG32I, {
      width: this.config.nArenas * NAgentsPerTeam,
      height: 2
    }).init();
    this.teamStatsTexture = Texture.create(this.program.gls, GL.R32F,
      { width: this.config.nArenas, height: 2 }, keysOfEnum(GymTeamStats).length).init();
    console.log(`[DerkSession] Created with config:`, this.config);
  }
  destroy() {
    this.disconnectAllRemotes();
    this.destroyWorldState();
    this.creaturesTexture.destroy();
    this.teamStatsTexture.destroy();
    this.actionsTexture?.destroy();
    this.destroyedResolve!();
  }
  disconnectAllRemotes() {
    for (const remoteHost of this.remoteHosts) {
      remoteHost.close();
    }
    this.remoteHosts = [];
  }
  destroyWorldState() {
    this.state?.gameLoop?.destroy();
    this.state?.destroy();
  }

  config: GymConfig;
  destroyedResolve?: () => void;
  destroyed = new Promise<void>((resolve) => { this.destroyedResolve = resolve; });
  state?: GpuWorldState;
  lastReward?: Image;
  pendingStep?: Promise<boolean>;

  creaturesTexture: Texture;
  teamStatsTexture: Texture;

  actionsTexture?: Texture;
  profiler = new SimpleProfiler();
  remoteHosts: RemoteHost[] = [];

  setRewardFunction(reward: UnitBounties) {
    this.config.rewardFunction = {...defaultRewardFunction(), ...reward };
  }

  async connectToAgentHosts() {
    console.log(`[DerkSession.connectToAgentHosts] Connecting to ${this.config.agentHosts.length} hosts`);
    const res = await Promise.all(this.config.agentHosts.map(async ({ uri: host, regions }, i): Promise<RemoteConnectionState> => {
      if (this.remoteHosts[i]) {
        console.log(`[DerkSession.connectToAgentHosts] ${host} already connected`);
        return RemoteConnectionState.Connected;
      } else {
        console.log(`[DerkSession.connectToAgentHosts] Connecting to ${host}`);
        const remoteHost = new RemoteHost(host, regions, this.config.nArenas, this.config.webWorkerSocket);
        const res = await Promise.race([
          remoteHost.connected.then(() => RemoteConnectionState.Connected),
          remoteHost.error.then(() => RemoteConnectionState.Error)
        ]);
        console.log(`[DerkSession.connectToAgentHosts] ${host} connection:`, RemoteConnectionState[res]);
        if (res === RemoteConnectionState.Connected) {
          console.log(`[DerkSession.connectToAgentHosts] Sending config to ${host}`);
          remoteHost.send(JSON.stringify({
            nTeams: remoteHost.nTeams,
          }));
          this.remoteHosts[i] = remoteHost;
        }
        return res;
      }
    }));
    console.log(`[DerkSession.connectToAgentHosts] Done`);
    return res.reduce((p, x) => p && (x === RemoteConnectionState.Connected), true);
  }

  async reset() {
    this.profiler.enter('reset');
    await wait(0);
    // Let any running step finish
    if (this.pendingStep) {
      await this.pendingStep;
      this.pendingStep = undefined;
    }
    await this.createNewWorldState(this.config);

    if (this.actionsTexture) {
      this.actionsTexture.destroy();
    }
    this.actionsTexture = Texture.create(this.program.gls, GL.R32F, {
      width: this.config.nArenas * 3 * DecisionKeys.length,
      height: 2
    }, 1).init();

    this.profiler.enter('resetForNextRound');
    this.program.resetForNextRound(this.state!, true);
    this.profiler.exit();
    const round = this.state!.data.round + 1;
    this.state!.setData({
      simulationEnded: false,
      battleStartStep: this.state!.data.stepCounter,
      round,
    });
    this.profiler.enter('getRewardAndObservations');

    let [reward, observations] = await Promise.all([
      this.readTotalReward(this.creaturesTexture),
      this.getObservations(this.creaturesTexture),
    ]);
    this.profiler.exit();
    this.lastReward = reward!;
    for (const remoteHost of this.remoteHosts) {
      console.log(`[reset] Sending observations to ${remoteHost.host}`);
      remoteHost.send(getRectsData(observations, remoteHost.agentRects).buffer);
    }
    if (this.config!.interleaved) {
      this.pendingStep = this.stepInternal();
    }

    this.profiler.exit();
  }

  normalizeUnit(unit: any) {
    const base = newRandomCreature();
    return {
      type: 'creature',
      slots: randomCreatureItems(),
      ...createDefStyleFromBase(base),
      bounties: unit.rewardFunction,
      ...unit,
    }
  }


  async createNewWorldState(config: GymConfig) {
    this.profiler.enter('createNewWorldState');
    this.destroyWorldState();
    this.state = await GpuWorldState.createWithMap(this.program.gls, { arenaCount: config.nArenas, });
    this.state.setData({
      renderGazes: true,
      battleground: DbBattleground.Team,
      gameInstance: { type: 'gym' },
      simulationPaused: false,
      aiCrowdLogo: config.aiCrowdLogo
    });
    this.state.random = new RandomSeedable(config.randomSeed);
    this.state.setData({ randomSeed: this.state.random.random() });
    setCanvasSizeToElementSize(this.state);
    const homeUnits = config.home ?? [];
    const awayUnits = config.away ?? [];
    const homeUnitCount = 4;
    const awayUnitCount = 4;
    this.state.updateTeam('home', { unitCount: homeUnitCount, battleUnitsAlive: homeUnitCount - 1, maxPossiblePoints: StatuePoints + (awayUnitCount > 1 ? TeamPoints : 0) });
    this.state.updateTeam('away', { unitCount: awayUnitCount, battleUnitsAlive: awayUnitCount - 1, maxPossiblePoints: StatuePoints + (homeUnitCount > 1 ? TeamPoints : 0) });

    await wait(0);
    const toCreate: { [type: string]: CreateUnitDef[] } = {};
    for (let a=0; a < config.nArenas; a++) {
      for (let i=0; i < homeUnitCount; i++) {
        const gymIndex: [number, number] = [a * 3 + i - 1, 0];
        const unit = i === 0 ? { type: 'statue' } : this.normalizeUnit(homeUnits[gymIndex[0] % homeUnits.length] ?? {});
        if (!toCreate[unit.type]) {
          toCreate[unit.type] = [];
        }
        toCreate[unit.type].push({
          unit,
          gymIndex,
          team: Teams.HomeTeam,
          arenaIndex: a,
          arenaMemberIndex: i
        });
      }
    }
    for (let a=0; a < config.nArenas; a++) {
      for (let i=0; i < awayUnitCount; i++) {
        const gymIndex: [number, number] = [a * 3 + i - 1, 1];
        const unit = i === 0 ? { type: 'statue' } : this.normalizeUnit(awayUnits[gymIndex[0] % awayUnits.length] ?? {});
        if (!toCreate[unit.type]) {
          toCreate[unit.type] = [];
        }
        toCreate[unit.type].push({
          unit,
          gymIndex,
          team: Teams.AwayTeam,
          arenaIndex: a,
          arenaMemberIndex: SideSize + i
        });
      }
    }
    this.profiler.enter('createUnits');
    const creatures: EntityRef[] = [];
    for (const type of Object.keys(toCreate)) {
      const entities = await createPlayerUnits(this.state, toCreate[type], {
        createAbilities: true, createBrain: false, profiler: this.profiler, bounties: this.config?.rewardFunction });
      for (let i=0; i < entities.count; i++) {
        const unit = toCreate[type][i];
        if (unit.unit.type !== 'statue') {
          creatures.push(entities.getIndex(i));
        }
      }
    }
    this.profiler.exit();
    this.creaturesTexture.sliceAll().upload(new Int32Array(lodash.flatMap(creatures, v => [v.x, v.y])));
    this.profiler.enter('createModels');
    await wait(0);
    createUnitModelsForArena(this.state, this.state.data.renderArena);
    this.profiler.exit();

    this.appState.update({
      worldState: this.state
    });
    this.profiler.exit();
  }

  playPromise() {
    return new Promise<void>(resolve => {
      const handler = (event: GpuWorldStateEventDataChanged) => {
        if (!event.after.simulationPaused) {
          this.state?.events.dataChanged.detach(handler);
          resolve();
        }
      };
      this.state?.events.dataChanged.attach(handler);
    })
  }

  async step() {
    if (this.state?.data.simulationPaused) {
      await this.playPromise();
    }
    if (this.config.interleaved) {
      console.log('[step] Running interleaved');
      const prevStep = this.pendingStep!;
      const actions = await this.readStepActions();
      this.pendingStep = prevStep.then(done => {
        if (!done) {
          return this.stepInternal(actions);
        } else {
          return done;
        }
      });
      const res = await prevStep;
      console.log('[step] Done interleaved');
      return res;
    } else {
      console.log('[step] Running');
      const actions = await this.readStepActions();
      const res = await this.stepInternal(actions);
      console.log('[step] Done');
      return res;
    }
  }

  async readStepActions() {
    console.log(`[readStepActions] Waiting for away actions from ${this.remoteHosts.length} hosts`);
    const actions = await Promise.all(this.remoteHosts.map(async host => {
      const m = await host.message();
      const actions = new Float32Array(m);
      return { actions, rects: host.agentRects }
    }));
    if (!this.config?.debugNoActionsWrite) {
      this.tools.writeActions.writeActionsToTexture(actions, this.actionsTexture!.sliceAll());
    }
    console.log(`[readStepActions] Got actions ${actions.length}`);
    return this.actionsTexture!
  }

  private async stepInternal(actions?: Texture): Promise<boolean> {

    if (actions) {
      console.log(`[stepInternal] Write actions`);
      this.tools.writeActions.write(this.state!, actions);
    }

    let nextAnimFrame = waitAnimationFrame();
    for (let i=0; i < this.config!.substeps; i++) {
      if (i > 0 && !this.config?.turboMode) {
        await nextAnimFrame;
        nextAnimFrame = waitAnimationFrame();
        this.program.render(this.state!);
      }

      await this.program.step(this.state!);
    }
    this.program.render(this.state!);
    let [reward, observations] = await Promise.all([
      this.readTotalReward(this.creaturesTexture),
      this.getObservations(this.creaturesTexture),
    ]);
    let rewardDiff = reward!.map((r, p) => r - this.lastReward!.read(p.x, p.y, p.z, p.w));
    this.lastReward = reward!;

    let teamStats: Image | undefined;
    if (this.state!.data.simulationEnded) {
      this.tools.readTeamStats.run(this.state!, this.teamStatsTexture.sliceAll());
      teamStats = await this.teamStatsTexture.readAsync();
    }

    for (const remoteHost of this.remoteHosts) {
      remoteHost.send(getRectsData(observations, remoteHost.agentRects).buffer);
      remoteHost.send(getRectsData(rewardDiff, remoteHost.agentRects).buffer);
      remoteHost.send(JSON.stringify({
        done: [this.state!.data.simulationEnded],
        info: {
          gameState: this.config.gameState ? this.state!.ecs.dumpJSON(this.state!) : undefined
        }
      }));
      if (teamStats) {
        remoteHost.send(getRectsData(teamStats, remoteHost.teamRects).buffer);
      }
    }

    return this.state!.data.simulationEnded;
  }

  private readTotalReward(entities: Texture) {
    return this.tools.readRewards.run(this.state!, entities);
  }

  public async readTeamStatsBase64() {
    return arrayBufferToBase64((await this.teamStatsTexture.readAsync()).data.buffer);
  }

  private async getObservations(entities: Texture) {
    this.program.updateNeuralNetwork.senses.run(this.state!);
    this.program.updateNeuralNetwork.gymExtraSenses.run(this.state!);

    let observations = (await this.tools.readSenses.run(this.state!, entities));

    if (this.config?.debugNoObservations) {
      observations = Image.zeroesF32({ width: 0, height: 0, depth: 0 }, 1);
    }

    console.log(`[getObservations] Returning observations (${observations.data.length}) ${Array.from(observations.data.slice(0, 5))}...`);

    return observations;
  }

}

export class DerkGymAPI {
  constructor(public program: GpuWorldProgram, public appState: Store<GymState>) {
    this.getLatestVersion();
  }

  async getLatestVersion() {
    const res = await (await fetch('http://pypi.org/pypi/gym_derk/json')).json();
    const latestVersion = res.info.version;
    console.log(`Got gym_derk latest version: ${latestVersion}`);
    if (semver.gt(latestVersion, version.version)) {
      console.warn(`New gym_derk version available: ${latestVersion}`);
      this.appState.update({ newVersion: latestVersion });
    }
  }

  tools = new GymTools(this.program.gls);
  session?: DerkSession;

  async createSession(config: GymConfig) {
    if (this.session) {
      this.session.destroy();
    }
    this.session = new DerkSession(this.program, this.appState, this.tools, config);
  }

  public getEnums() {
    return {
      observationKeys: AllObservationKeys,
      teamStatsKeys: keysOfEnum(GymTeamStats)
    }
  }

  public getWebGLRenderer() {
    const gl = this.program.gls.gl;
    var dbgRenderInfo = gl.getExtension('WEBGL_debug_renderer_info');
    if (dbgRenderInfo) {
      const renderer = gl.getParameter(dbgRenderInfo.UNMASKED_RENDERER_WEBGL);
      const vendor = gl.getParameter(dbgRenderInfo.UNMASKED_VENDOR_WEBGL);
      return `Renderer=${renderer} Vendor=${vendor}`;
    } else {
      return 'Failed to get extension WEBGL_debug_renderer_info';
    }
  }

}

function waitAnimationFrame() {
  return new Promise(resolve => {
    requestAnimationFrame(resolve);
  })
}

class CopyEntityComponents<F extends GpuBufferFormats> {
  constructor(private gls: GlState, private component: GpuComponentModule<F>,
    private outSlices: number) {}
  shader = new CompiledShaderModule(shaderModule([this.component.sm], {
    entities: uniformDef('isampler2D'),
    outValues: outDef(`${this.component.component.format}[${this.outSlices}]`),
    main: funcDef('void', [], `
      ivec2 entity = texelFetch(Self.entities, ivec2(gl_FragCoord), 0).xy;
      ${
        new Array(this.outSlices).fill(0).map((x, i) => `
          outValues[${i}] = ${this.component.component.depth !== undefined ? `${this.component.sm.value}(entity, ${i})` : `${this.component.sm.value}(entity)`};
        `).join('\n')
      }
    `)
  }));
  program = new Program(this.gls, this.shader.source);
  renderTarget = new RenderTarget(this.gls);
  run(state: GpuWorldState, entities: Texture, target: TextureSlicing) {
    this.shader.updateUniformValues(state);
    this.program.prepare();
    this.shader.writeUniforms(this.program);
    this.program.setUniform('entities', 'isampler2D', entities);
    this.renderTarget.set(target);
    this.gls.prepareForComputation(target.rect);
    this.gls.drawQuad(this.program);
  }
}
class ReadSenses {
  constructor(private gls: GlState) {}
  senses = new CopyEntityComponents(this.gls, Senses, SensesSlices);
  gymExtraSenses = new CopyEntityComponents(this.gls, GymExtraSenses, GymExtraSensesSlices);
  run(state: GpuWorldState, entities: Texture) {
    const tmp = this.gls.getTmpTextureSlice(GL.RGBA32F, entities.size, SensesSlices + GymExtraSensesSlices);
    this.senses.run(state, entities, tmp.sliceRange(0, SensesSlices));
    this.gymExtraSenses.run(state, entities, tmp.sliceRange(SensesSlices, GymExtraSensesSlices));
    return tmp.readAsync();
  }
}
class ReadRewards {
  constructor(private gls: GlState) {}
  gold = new CopyEntityComponents(this.gls, Gold, 1);
  run(state: GpuWorldState, entities: Texture) {
    const tmp = this.gls.getTmpTextureSlice(GL.R32F, entities.size, 1);
    this.gold.run(state, entities, tmp);
    return tmp.readAsync();
  }
}
class ReadTeamStats {
  constructor(private gls: GlState) {}
  shader = new CompiledShaderModule(shaderModule([TeamStats], {
    outValues: outDef(`float[${keysOfEnum(GymTeamStats).length}]`),
    main: funcDef('void', [], `
      ivec2 cell = ivec2(gl_FragCoord.xy);
      TeamStats_Stats selfStats = ${TeamStats.stats}(cell.x, cell.y == 0 ? ${Teams.HomeTeam} : ${Teams.AwayTeam});
      TeamStats_Stats opponentStats = ${TeamStats.stats}(cell.x, cell.y == 0 ? ${Teams.AwayTeam} : ${Teams.HomeTeam});
      outValues[${GymTeamStats.Reward}] = float(selfStats.gold);
      outValues[${GymTeamStats.OpponentReward}] = float(opponentStats.gold);
      outValues[${GymTeamStats.Hitpoints}] = selfStats.hitpoints;
      outValues[${GymTeamStats.AliveTime}] = selfStats.aliveTime;
      outValues[${GymTeamStats.CumulativeHitpoints}] = selfStats.cumulativeHitpoints;
    `)
  }));
  program = new Program(this.gls, this.shader.source);
  renderTarget = new RenderTarget(this.gls);
  run(state: GpuWorldState, target: TextureSlicing) {
    this.shader.updateUniformValues(state);
    this.program.prepare();
    this.shader.writeUniforms(this.program);
    this.renderTarget.set(target);
    this.gls.prepareForComputation(target.rect);
    this.gls.drawQuad(this.program);
  }
}
class WriteActions {
  constructor(private gls: GlState) {}

  query = new ArchetypesQuery([Creature.component])
    .updateGpu(this.gls, DecisionsCMs.map(x => x.component), shaderModule(
      [
        ...DecisionsCMs.map(x => x.writer),
        GymIndex.sm
      ],
      {
        actions: uniformDef('sampler2DArray'),
        update: funcDef('void', [['ivec2', 'entityCell']], `
          ivec2 index = ${GymIndex.sm.value}(entityCell) * ivec2(${DecisionsCMs.length}, 1);
          ${Decisions.MoveX.writer.write}(          texelFetch(actions, ivec3(index + ivec2(0, 0), 0), 0).x);
          ${Decisions.Rotate.writer.write}(         texelFetch(actions, ivec3(index + ivec2(1, 0), 0), 0).x);
          ${Decisions.ChaseFocus.writer.write}(     texelFetch(actions, ivec3(index + ivec2(2, 0), 0), 0).x);
          ${Decisions.CastingSlot.writer.write}(int(texelFetch(actions, ivec3(index + ivec2(3, 0), 0), 0).x));
          ${Decisions.ChangeFocus.writer.write}(int(texelFetch(actions, ivec3(index + ivec2(4, 0), 0), 0).x));
        `)
      }
    ));

  writeActionsToTexture(actions: Array<{ actions: Float32Array, rects: Rect[] }>, texture: TextureSlicing) {
    texture.clear();
    for (const acts of actions) {
      let offset = 0;
      for (const rect of acts.rects) {
        const size = rect.width * DecisionKeys.length * rect.height;
        const target = texture.selectAbsolute({
          x: rect.x * DecisionKeys.length,
          y: rect.y,
          width: rect.width * DecisionKeys.length,
          height: rect.height
        });
        target.upload(acts.actions.subarray(offset, offset + size));
        offset += size;
      }
    }
  }

  write(state: GpuWorldState, actions: Texture) {
    this.query.run(state, [{ name: 'actions', type: 'sampler2DArray', value: actions }]);
  }
}

const ArmItems = ItemKeys.filter(key => { const it = Items[key]; return it?.type === 'attachment' && it.slot === 'arms' });
const TailItems = ItemKeys.filter(key => { const it = Items[key]; return it?.type === 'attachment' && it.slot === 'tail' });
const MiscItems = ItemKeys.filter(key => { const it = Items[key]; return it?.type === 'attachment' && it.slot === 'misc' });
function randomCreatureItems(): string[] {
  const items: string[] = [];
  if (Math.random() > 0.7) {
    items[0] = ArmItems[Math.floor(Math.random() * ArmItems.length)];
  }
  if (Math.random() > 0.7) {
    items[1] = TailItems[Math.floor(Math.random() * TailItems.length)];
  }
  if (Math.random() > 0.7) {
    items[2] = MiscItems[Math.floor(Math.random() * MiscItems.length)];
  }
  return items;
}
