import React from 'react';
import './index.css';
import { useWorldDataSelect } from "../WorldDataConsumer";
import { Block } from "../../Components/Layout";
import { wait, rgbToHex, vec301ToRgba255 } from '../../Util';

export function BattleScoreChangeFlash() {
  const { teams, homeHealthColor, awayHealthColor } = useWorldDataSelect('teams', 'homeHealthColor', 'awayHealthColor');
  const [text, setText] = React.useState('');
  const homePoints = teams?.home.battlePoints;
  const awayPoints = teams?.away.battlePoints;
  React.useEffect(() => {
    (async () => {
      setText(`${homePoints} - ${awayPoints}`);
      await wait(1000);
      setText('');
    })();
  }, [homePoints, awayPoints]);
  if (!text || !teams || !homeHealthColor || !awayHealthColor) {
    return null;
  }
  const color = teams.home.battlePoints > teams.away.battlePoints ? rgbToHex(vec301ToRgba255(homeHealthColor))
    : teams.home.battlePoints < teams.away.battlePoints ? rgbToHex(vec301ToRgba255(awayHealthColor))
    : 'rgb(133, 133, 133)';
  return <div className={`BattleScoreChangeFlash`} style={{ color }}>
    <div className="zoomIn animated">{text}</div>
  </div>;
}

export function BattleScore() {
  const { teams, homeHealthColor, awayHealthColor } = useWorldDataSelect('teams', 'homeHealthColor', 'awayHealthColor');
  if (!teams || !homeHealthColor || !awayHealthColor) {
    return null;
  }
  const color = teams.home.battlePoints > teams.away.battlePoints ? rgbToHex(vec301ToRgba255(homeHealthColor))
    : teams.home.battlePoints < teams.away.battlePoints ? rgbToHex(vec301ToRgba255(awayHealthColor))
    : 'rgb(133, 133, 133)';
  return <Block className={`BattleHUDScore`} style={{ color }}>
    {teams.home.battlePoints}
    &nbsp;-&nbsp;
    {teams.away.battlePoints}
  </Block>
}
