import { DecisionsCMs } from "../Brain/Decisions";
import { GlState } from "../../GpuUtil/GlState";
import { CompiledShaderModule, shaderModule, funcDef, outDef, uniformDef } from "../../GpuUtil/ShaderModule";
import { Program } from "../../GpuUtil/Program";
import { RenderTarget } from "../../GpuUtil/RenderTarget";
import { GpuWorldState } from "../State";
import { UnitsInArena } from "../ECS/ArenaStats";
import { keysOfEnum } from "../../Util";
import { Position } from "../ECS/Transform";
import { Hitpoints, Unit } from "../ECS/UnitComponents";
import { ArchetypesQuery } from "../../GpuUtil/ECS/Query";
import { ArenaMemberIndex } from "../ECS/Components";
import { Db, full, DbReplay, DbReplayContent } from "db";
import firebase from 'firebase/app';

enum UnitReplayChannels {
  PositionX,
  PositionY,
  PositionZ,
  Hitpoints
}
const UnitReplayChannelsCount = keysOfEnum(UnitReplayChannels).length + DecisionsCMs.length;
export const UnitReplayChannelsSlices = Math.ceil(UnitReplayChannelsCount / 4);

export class CaptureReplay {
  constructor(private gls: GlState) {}

  shader = new CompiledShaderModule(shaderModule([
    ...DecisionsCMs.map(c => c.sm),
    UnitsInArena,
    Position.sm,
    Hitpoints.sm,
  ], {
    outValues: outDef(`vec4[${UnitReplayChannelsSlices}]`),
    renderArena: uniformDef('int', d => d.data.renderArena),
    main: funcDef('void', [], `
      int arenaMemberIndex = int(gl_FragCoord.y);
      ivec2 entityCell = ${UnitsInArena.unit}(Self.renderArena, arenaMemberIndex);
      vec3 position = ${Position.sm.value}(entityCell);
      outValues[${Math.floor(UnitReplayChannels.PositionX / 4)}][${UnitReplayChannels.PositionX % 4}] = position.x;
      outValues[${Math.floor(UnitReplayChannels.PositionY / 4)}][${UnitReplayChannels.PositionY % 4}] = position.y;
      outValues[${Math.floor(UnitReplayChannels.PositionZ / 4)}][${UnitReplayChannels.PositionZ % 4}] = position.z;
      outValues[${Math.floor(UnitReplayChannels.Hitpoints / 4)}][${UnitReplayChannels.Hitpoints % 4}] = ${Hitpoints.sm.value}(entityCell);
      ${DecisionsCMs.map((c, i) => {
        const l = i + keysOfEnum(UnitReplayChannels).length;
        return `outValues[${Math.floor(l / 4)}][${l % 4}] = float(${c.sm.value}(entityCell));`;
      }).join('\n')}
    `)
  }));
  program = new Program(this.gls, this.shader.source);
  renderTarget = new RenderTarget(this.gls);

  run(state: GpuWorldState) {
    if (state.data.gameInstance.type !== 'battle' && state.data.gameInstance.type !== 'train') {
      return;
    }
    this.shader.updateUniformValues(state);

    const slice = state.textures.unitsReplay.sliceAll().selectAbsolute({
      x: state.data.stepCounter - state.data.battleStartStep,
      y: 0,
      width: 1,
      height: state.textures.unitsReplay.size.height
    });
    this.renderTarget.set(slice.back);
    this.program.prepare();
    this.shader.writeUniforms(this.program);
    this.gls.prepareForComputation(slice.rect);
    this.gls.drawQuad(this.program);
  }
}

export async function saveReplay(state: GpuWorldState, title: string) {
  let instance;
  if (state.data.gameInstance.type === 'battle') {
    instance = {
      type: 'battle',
      battleId: state.data.battleId,
      homeTeamId: state.data.teams.home.id,
      awayTeamId: state.data.teams.away.id,
    };
  } else if (state.data.gameInstance.type === 'train') {
    instance = { type: 'train' };
  } else {
    throw Error(`Invalid replay instance: ${state.data.gameInstance.type}`);
  }
  const replay = await firebase.firestore().collection(Db.replays).add(full<DbReplay>({
    userId: state.appState?.state.loggedInUserId ?? '',
    created: firebase.firestore.Timestamp.now(),
    title,
    battleground: state.data.battleground,
    instance,
  }));
  const unitsReplay = await state.textures.unitsReplay.readAsync();
  await firebase.firestore().collection(Db.replayContents).doc(replay.id).set(full<DbReplayContent>({
    userId: state.appState?.state.loggedInUserId ?? '',
    homeUnits: JSON.stringify(state.data.teams.home.units.map(x => ({...x, brain: undefined })), null, 2),
    awayUnits: JSON.stringify(state.data.teams.away.units.map(x => ({...x, brain: undefined })), null, 2),
    unitsReplay: firebase.firestore.Blob.fromUint8Array(new Uint8Array(unitsReplay.data.buffer))
  }))
}

export class PlaybackReplayDecisions {
  constructor(private gls: GlState) {}

  decisions = new ArchetypesQuery([Unit.component])
    .updateGpu(this.gls, [...DecisionsCMs.map(c => c.component)], shaderModule([
      ArenaMemberIndex.sm, ...DecisionsCMs.map(c => c.writer)
    ], {
      stepCounter: uniformDef('int', d => d.data.stepCounter - d.data.battleStartStep),
      replay: uniformDef('sampler2DArray', d => d.textures.unitsReplay),
      update: funcDef('void', [['ivec2', 'entityCell']], `
        int arenaMemberIndex = ${ArenaMemberIndex.sm.value}(entityCell);
        ${DecisionsCMs.map((c, i) => {
            const l = i + keysOfEnum(UnitReplayChannels).length;
            return `${c.writer.write}(${c.component.format}(texelFetch(Self.replay, ivec3(Self.stepCounter, arenaMemberIndex, ${Math.floor(l / 4)}), 0)[${l % 4}]));`;
          }).join('\n')}
      `)
    }));

  run(state: GpuWorldState) {
    if (state.data.gameInstance.type !== 'replay') {
      return;
    }
    this.decisions.run(state);
  }
}

export class PlaybackReplayPositionHitpoints {
  constructor(private gls: GlState) {}

  common = new ArchetypesQuery([Unit.component])
    .updateGpu(this.gls, [Position.gpuComponent, Hitpoints.component], shaderModule([
      ArenaMemberIndex.sm, Position.writer, Hitpoints.writer
    ], {
      stepCounter: uniformDef('int', d => d.data.stepCounter - d.data.battleStartStep),
      replay: uniformDef('sampler2DArray', d => d.textures.unitsReplay),
      update: funcDef('void', [['ivec2', 'entityCell']], `
        int arenaMemberIndex = ${ArenaMemberIndex.sm.value}(entityCell);
        vec4 slice0 = texelFetch(Self.replay, ivec3(Self.stepCounter, arenaMemberIndex, 0), 0);
        ${Position.writer.write}(slice0.xyz);
        ${Hitpoints.writer.write}(slice0.w);
      `)
    }));

  run(state: GpuWorldState) {
    if (state.data.gameInstance.type !== 'replay') {
      return;
    }
    this.common.run(state);
  }
}
