import * as React from 'react';
import { WorldCanvasContext, WorldProgramContext } from '../../Components/WorldCanvas';
import { hotkeyIgnoreElement } from '../../Components/Hotkeys';
import { WorldStateContext } from '../../GameComponents/WorldDataConsumer';
import * as Vector3 from '../../Math/Vector3';
import * as Vector2 from '../../Math/Vector2';
import { Animatable, Vec2Anim, NumberAnim, Vec3Anim } from '../../Math/Animatable';
import { cameraDistance, eyeOfCamera, PerspectiveProjection, LookatView } from '../../GpuUtil/Camera';
import { screenRay, rayPlaneIntersection, clamp } from '../../Math/Math';
import { objectSetKeys } from '../../Util';
import { mat4 } from 'gl-matrix';
import { Position, Quaternion, Scale } from '../../GpuWorld/ECS/Transform';
import { Camera } from '../../GpuWorld/ECS/Camera';

interface RTSCameraState {
  camera: { view: LookatView, projection: PerspectiveProjection };
  panning?: {
    originalMouseWorldPlaneIntersection: Vector3.Vector3,
    originalInvViewProjection: mat4,
    originalCameraPosition: Vector2.Vector2,
  };
  isPanning: boolean;
  position: Animatable<Vector2.Vector2>;
  elevation: Animatable<number>;
  eyeMeasuredHeight: number;
  positionMeasuredHeight: number;
  navigationModeTilt: Animatable<Vector3.Vector3>;
  slideVelocity: Vector2.Vector2;
  slideTargetPosition: Vector2.Vector2 | null;
  screenOffset: Animatable<Vector2.Vector2>;
  zoom: number;
  selectedEntityTranslation?: Vector3.Vector3;
}

export function defaultRTSCameraConf() {
  return {
    fovy: (27.8 / 360) * Math.PI * 2,
    angle: ((90 - 56) / 360) * Math.PI * 2,
    zrot: -Math.PI / 2,
    keyboardPanSpeed: 0.001,
    panningFriction: 0.05,
    panningStopSpeed: 0.0002,
    rotationSpeed: 0.01,
  };
}

export function RTSCameraController({ conf = defaultRTSCameraConf() }: { conf?: ReturnType<typeof defaultRTSCameraConf> }) {
  const worldState = React.useContext(WorldStateContext);
  const worldProgram = React.useContext(WorldProgramContext);
  const worldCanvas = React.useContext(WorldCanvasContext);

  React.useEffect(() => {
    if (!worldState || !worldProgram) {
      return;
    }
    const { worldSize } = worldState.data;

    const DefaultZoom = 70;
    const WorldCenter = { x: worldSize.width / 2, y: worldSize.height / 2 };
    const CameraFreedom = 10;
    const CameraMinPosition = Vector2.sub(WorldCenter, CameraFreedom);
    const CameraMaxPosition = Vector2.add(WorldCenter, CameraFreedom);
    const GameCameraNavigationMinGroundHeight = 5;

    const navigationModeTilt = (zoom: number) => {
      return Vector3.fromSphericalCoordinates({ theta: conf.angle, phi: conf.zrot, radius: zoom });
    }

    let cameraState: RTSCameraState = {
      camera: {
        view: {
          type: 'LookatView',
          lookAt: { x: 0, y: 0, z: 0 },
          eye: { x: 10, y: 10, z: 10 },
          up: { x: 0, y: 0, z: 1 },
          screenOffset: { x: 0, y: 0 }
        },
        projection: {
          type: 'Perspective',
          fovy: 1,
          near: 0.1,
          far: 30,
          aspectRatio: 1,
        }
      },
      isPanning: false,
      position: Vec2Anim.create({ x: worldSize.width / 2, y: worldSize.height / 2 }),
      elevation: NumberAnim.create(0),
      eyeMeasuredHeight: 1,
      positionMeasuredHeight: 1,
      navigationModeTilt: Vec3Anim.create(navigationModeTilt(DefaultZoom)),
      slideVelocity: Vector3.zero(),
      slideTargetPosition: null,
      screenOffset: Vec2Anim.create({ x: 0, y: 0 }),
      zoom: DefaultZoom,
    }

    let previousMousePosition: Vector2.Vector2;
    let mousePosition: Vector2.Vector2;
    let previousDocumentMouseMoveEvent: MouseEvent;


    const startPan = () => {
      if (cameraState.isPanning || !mousePosition) {
        return;
      }
      const originalInvViewProjection = mat4.clone(worldState.derived.cameraInvProjectionView);
      const originalMouseWorldPlaneIntersection = intersectScreenRayWorldPlane(mousePosition, 0, originalInvViewProjection, worldState.data.canvasRect);
      const originalCameraPosition = cameraState.position.current;

      if (originalMouseWorldPlaneIntersection) {
        cameraState = {
          ...cameraState,
          panning: {
            originalInvViewProjection,
            originalMouseWorldPlaneIntersection,
            originalCameraPosition
          },
          slideTargetPosition: originalCameraPosition,
        }
        return true;
      } else {
        return false;
      }
    }

    const stopPan = () => {
      cameraState = {
        ...cameraState,
        slideTargetPosition: null,
        panning: undefined,
      }
      setTimeout(() => {
        cameraState = {
          ...cameraState,
          isPanning: false
        }
      }, 10);
    }


    const handleDocumentMouseMove = (event: MouseEvent) => {
      if (previousDocumentMouseMoveEvent) {
        previousMousePosition = mousePosition;
        mousePosition = { x: event.clientX, y: event.clientY };

        if (!previousDocumentMouseMoveEvent.ctrlKey && event.ctrlKey) {
          startPan();
        } else if (previousDocumentMouseMoveEvent.ctrlKey && !event.ctrlKey) {
          stopPan();
        }
        const { panning } = cameraState;
        if (panning) {
          const mouseWorldPlanePosition = intersectScreenRayWorldPlane(mousePosition, 0, panning.originalInvViewProjection, worldState.data.canvasRect);
          const isPanning = cameraState.isPanning || Vector2.length(Vector2.sub(previousMousePosition, mousePosition)) > 3;
          if (mouseWorldPlanePosition) {
            cameraState = {
              ...cameraState,
              isPanning,
              slideTargetPosition: Vector2.clamp(Vector2.add(panning.originalCameraPosition,
                Vector2.sub(panning.originalMouseWorldPlaneIntersection, mouseWorldPlanePosition)),
                CameraMinPosition, CameraMaxPosition),
            };
          }
        }

        worldState.ensureRendered();
      }
      previousDocumentMouseMoveEvent = event;
    }

    const handleWorldMouseDown = (event: MouseEvent) => {
      if (event.button !== 0) {
        return;
      }
      const panStart = startPan();
      const onMouseUp = () => {
        document.removeEventListener('mouseup', onMouseUp);
        if (panStart) {
          stopPan();
        }
      };
      document.addEventListener('mouseup', onMouseUp);
    }

    const handleWorldMouseWheel = (event: WheelEvent) => {
      event.preventDefault();
      event.stopImmediatePropagation();
      previousMousePosition = mousePosition;
      mousePosition = { x: event.clientX, y: event.clientY };
      const delta = -(window.navigator.platform === 'Win32' ? (event.deltaY < 0 ? -1 : 1) * -0.2 : event.deltaY * -0.005) * worldState.derived.userSettingsOrFallback.mouseWheelSensitivity;

      const zoom = clamp(cameraState.zoom * (1 + delta), 30, DefaultZoom);

      cameraState = {
        ...cameraState,
        navigationModeTilt: {
          current: navigationModeTilt(zoom),
        },
        zoom,
      }
    }

    const updateFrame = (deltaTime: number, gameCamera: RTSCameraState): RTSCameraState => {
      let newCamera = gameCamera.camera;

      gameCamera = objectSetKeys(gameCamera, {
        elevation: NumberAnim.easeTo(gameCamera.elevation, { target: gameCamera.positionMeasuredHeight })
      });

      // Update all animatables
      gameCamera = objectSetKeys(gameCamera, {
        position: Vec2Anim.nextValue(gameCamera.position, deltaTime),
        navigationModeTilt: Vec3Anim.nextValue(gameCamera.navigationModeTilt, deltaTime),
        screenOffset: Vec2Anim.nextValue(gameCamera.screenOffset, deltaTime),
        elevation: NumberAnim.nextValue(gameCamera.elevation, deltaTime),
      });

      if (gameCamera.slideTargetPosition) {
        // While we are dragging we make the velocity so that it perfectly moves with the cursor
        const d = Vector2.sub(gameCamera.slideTargetPosition, gameCamera.position.current);
        gameCamera = {...gameCamera, slideVelocity: Vector2.div(d, deltaTime) };
      }
      const sliding = Vector2.length(gameCamera.slideVelocity) > conf.panningStopSpeed;
      if (sliding) {
        gameCamera = {
          ...gameCamera,
          position: { current: Vector2.add(gameCamera.position.current, Vector2.mul(gameCamera.slideVelocity, deltaTime)) },
          slideVelocity: Vector2.mul(gameCamera.slideVelocity, 1 - conf.panningFriction)
        };

        gameCamera = {
          ...gameCamera,
          position: { current: Vector2.clamp(gameCamera.position.current,
            CameraMinPosition,
            CameraMaxPosition
          ) }
        };
      }

      const tilt = gameCamera.navigationModeTilt.current;

      const lookAt = objectSetKeys(newCamera.view.lookAt, {...gameCamera.position.current, z: gameCamera.elevation.current });
      const eye = objectSetKeys(newCamera.view.eye, Vector3.add(lookAt, tilt));

      newCamera = objectSetKeys(newCamera, {
        view: objectSetKeys(newCamera.view, {
          type: 'LookatView',
          lookAt,
          eye,
          up: objectSetKeys(newCamera.view.up, { x: 0, y: 0, z: 1 }),
          screenOffset: objectSetKeys(newCamera.view.screenOffset, gameCamera.screenOffset.current)
        })
      });

      const minEyeHeight = gameCamera.eyeMeasuredHeight + GameCameraNavigationMinGroundHeight;
      if (newCamera.view.eye.z < minEyeHeight) {
        newCamera = objectSetKeys(newCamera, {
          view: objectSetKeys(newCamera.view, {
            eye: objectSetKeys(newCamera.view.eye, {
              z: minEyeHeight
            })
          })
        });
      }

      newCamera = objectSetKeys(newCamera, {
        projection: objectSetKeys(newCamera.projection, {
          type: 'Perspective',
          fovy: conf.fovy,
          near: 0.1,
          far: 450,
          aspectRatio: worldState.data.canvasRect.width / worldState.data.canvasRect.height
        })
      });

      return objectSetKeys(gameCamera, {
        camera: newCamera
      });
    }
    const updateCameraFrame = (event: { delta: number }) => {
      let nextState = updateFrame(event.delta, cameraState);

      const cameraChanged = nextState !== cameraState;
      if (cameraChanged) {
        const writeCamera = worldState.data.camera!;
        const pos = worldState.ecs.getCpuComponentDataView(Position.cpuComponent, writeCamera);
        const quat = worldState.ecs.getCpuComponentDataView(Quaternion.cpuComponent, writeCamera);
        const scale = worldState.ecs.getCpuComponentDataView(Scale.cpuComponent, writeCamera);
        const tmpMat = mat4.create();
        mat4.lookAt(tmpMat, Vector3.toGlVec3(nextState.camera.view.eye), Vector3.toGlVec3(nextState.camera.view.lookAt), Vector3.toGlVec3(nextState.camera.view.up));
        mat4.invert(tmpMat, tmpMat);
        mat4.getTranslation(pos!, tmpMat);
        mat4.getRotation(quat!, tmpMat);
        mat4.getScaling(scale!, tmpMat);
        worldState.ecs.setComponentData(Camera.Fovy, writeCamera, new Float32Array([nextState.camera.projection.fovy]));
        worldState.ecs.setComponentData(Camera.Near, writeCamera, new Float32Array([nextState.camera.projection.near]));
        worldState.ecs.setComponentData(Camera.Far, writeCamera, new Float32Array([nextState.camera.projection.far]));
        cameraState = nextState;
        worldState.ensureRendered();
        // if (this.audio) {
        //   this.audio.updateCamera(nextState.camera);
        // }
      }
    }

    const stepPanCamera = (step: Vector2.Vector2, slide = true) => {
      const zoom = cameraDistance(cameraState.camera);
      const velocity = conf.keyboardPanSpeed * zoom;
      const pos = {...eyeOfCamera(cameraState.camera)};
      pos.z = 0;
      const lookat = {...cameraState.camera.view.lookAt};
      lookat.z = 0;
      const vertical = Vector2.normalize(Vector2.sub(pos, lookat));
      const horizontal = Vector2.perpendicular(vertical);
      const delta = Vector2.add(cameraState.slideVelocity, Vector2.add(
        Vector2.mul(horizontal, velocity * step.x),
        Vector2.mul(vertical, velocity * step.y))
      );
      cameraState = slide ? {
        ...cameraState,
        slideVelocity: delta
      } : {
        ...cameraState,
        position: Vec2Anim.create(Vector2.add(cameraState.position.current, delta))
      };
    }

    const handleKeydown = (event: KeyboardEvent) => {
      if (hotkeyIgnoreElement(event.target)) {
        return;
      }
      switch (event.code) {
        case 'ArrowLeft': { stepPanCamera({ x: 1, y: 0 }); break }
        case 'ArrowRight': { stepPanCamera({ x: -1, y: 0 }); break }
        case 'ArrowUp': { stepPanCamera({ x: 0, y: -1 }); break }
        case 'ArrowDown': { stepPanCamera({ x: 0, y: 1 }); break }
      }
    };

    worldState.events.camera.attach(updateCameraFrame);
    document.addEventListener('mousemove', handleDocumentMouseMove);
    document.addEventListener('mouseleave', handleDocumentMouseMove);
    document.addEventListener('keydown', handleKeydown);
    if (worldCanvas) {
      worldCanvas.addEventListener('mousedown', handleWorldMouseDown);
      worldCanvas.addEventListener('wheel', handleWorldMouseWheel);
    }

    updateCameraFrame({ delta: 0 });

    return () => {
      worldState.events.camera.detach(updateCameraFrame);
      document.removeEventListener('mousemove', handleDocumentMouseMove);
      document.removeEventListener('mouseleave', handleDocumentMouseMove);
      document.removeEventListener('keydown', handleKeydown);
      if (worldCanvas) {
        worldCanvas.removeEventListener('mousedown', handleWorldMouseDown);
        worldCanvas.removeEventListener('wheel', handleWorldMouseWheel);
      }
    }
  }, [worldState, worldProgram, worldCanvas]);

  return null;
}

function intersectScreenRayWorldPlane(screenPosition: Vector2.Vector2, planeZ: number, invViewProjection: mat4, canvasClientRect: ClientRect): Vector3.Vector3 | null {
  if (!invViewProjection) {
    return null;
  }
  const position = { x: screenPosition.x - canvasClientRect.left, y: screenPosition.y - canvasClientRect.top };
  const viewport = { x: 0, y: 0, width: canvasClientRect.width, height: canvasClientRect.height };
  const ray = screenRay(invViewProjection, viewport, position);
  return rayPlaneIntersection(ray, { normal: { x: 0, y: 0, z: 1 }, distance: -planeZ });
}
