import { TextureSlicing, Texture } from "./Texture";
import { GlState, Destructible } from "./GlState";
import { Rect } from "../Math/Math";

export class RenderTarget implements Destructible {
  constructor(private gls: GlState) {}
  destroy() {
    this.gls.gl.deleteFramebuffer(this.buffer);
    this.buffer = null;
    this.destroyed = true;
  }
  destroyed = false;
  private buffer = this.gls.gl.createFramebuffer();
  private cachedTexture: Texture | null = null;
  private cachedLayers: number[] = [];
  private cachedOutputCount = 0;

  bind() {
    if (this.destroyed) {
      throw new Error(`Trying to bind destroyed buffer`);
    }
    this.gls.framebuffer = this.buffer;
  }

  private setOutputCount(value: number) {
    if (value === this.cachedOutputCount) {
      return;
    }
    if (value > this.gls.maxColorAttachements) {
      throw new Error(`Outputs can't be larger than maxColorAttachements (${this.gls.maxColorAttachements})`);
    }
    const gl = this.gls.gl;
    gl.drawBuffers(new Array(value).fill(0).map((_, i) => gl.COLOR_ATTACHMENT0 + i));
    if (this.cachedOutputCount > value) {
      for (let i=value; i < this.cachedOutputCount; i++) {
        gl.framebufferTexture2D(gl.FRAMEBUFFER, gl.COLOR_ATTACHMENT0 + i, gl.TEXTURE_2D, null, 0);
      }
    }
    this.cachedOutputCount = value;
  }

  set({ texture, layers }: TextureSlicing) {
    this.bind();
    const gl = this.gls.gl;
    if (texture.destroyed || !texture.inited) {
      throw new Error(`Trying to bind destroyed or uninited texture`);
    }
    if (!texture.is2dArray) {
      this.setOutputCount(1);
      if (this.cachedTexture !== texture) {
        gl.framebufferTexture2D(gl.FRAMEBUFFER, gl.COLOR_ATTACHMENT0, gl.TEXTURE_2D, texture.texture, 0);
        this.cachedTexture = texture;
      }
    } else {
      if (layers === undefined) {
        throw new Error(`Trying to set array texture but no layer defined`);
      }
      this.setOutputCount(layers.length);
      for (let index = 0; index < layers.length; index++) {
        const layer = layers[index];
        if (this.cachedTexture !== texture || this.cachedLayers[index] !== layer) {
          if (layer < 0 || layer >= texture.depth!) {
            throw new Error(`Layer out of range`);
          }
          gl.framebufferTextureLayer(gl.FRAMEBUFFER, gl.COLOR_ATTACHMENT0 + index, texture.texture, 0, layer);
        }
      }
      this.cachedTexture = texture;
      this.cachedLayers = layers;
    }

    // const status = gl.checkFramebufferStatus(gl.FRAMEBUFFER);
    // if (status !== gl.FRAMEBUFFER_COMPLETE) {
    //   throw new Error(`Framebuffer failed to create: ${status} ${getGlConstantName(gl, status)}`);
    // }
  }

  clear(color: number[] | Float32Array, index: number = 0, rect?: Rect) {
    this.bind();
    if (!this.cachedTexture) {
      throw new Error(`No texture bound to render target`);
    }
    const gl = this.gls.gl;
    if (rect) {
      this.gls.enable(gl.SCISSOR_TEST);
      gl.scissor(rect.x, rect.y, rect.width, rect.height);
    }
    if (this.cachedTexture.formatType === 'float') {
      gl.clearBufferfv(gl.COLOR, index, new Float32Array(color));
    } else if (this.cachedTexture.formatType === 'int') {
      gl.clearBufferiv(gl.COLOR, index, new Int32Array(color));
    } else if (this.cachedTexture.formatType === 'uint') {
      gl.clearBufferuiv(gl.COLOR, index, new Uint32Array(color));
    } else {
      throw new Error('Not implemented yet');
    }
    if (rect) {
      this.gls.disable(gl.SCISSOR_TEST);
    }
  }
}
