
const WorkerSource = `
  let socket;

  self.onmessage = (e) => {
    console.log('webworker', e);
    switch (e.data.type) {
      case 'init': {
        socket = new WebSocket(e.data.host);
        socket.binaryType = 'arraybuffer';
        socket.addEventListener('open', () => {
          postMessage({ type: 'open' })
        });
        socket.addEventListener('close', () => {
          postMessage({ type: 'close' })
        });
        socket.addEventListener('message', (msg) => {
          if (msg.data instanceof ArrayBuffer) {
            postMessage({ type: 'message', data: msg.data }, [msg.data]);
          } else {
            postMessage({ type: 'message', data: msg.data });
          }
        });
        break;
      };
      case 'send': {
        socket.send(e.data.data);
        break;
      }
      case 'close': {
        socket.close();
        break;
      }
    }
  }
`;
let code = WorkerSource; // .toString();
// code = code.substring(code.indexOf("{")+1, code.lastIndexOf("}"));
const blob = new Blob([code], {type: "application/javascript"});

export class WebSocketAsync extends EventTarget {
  constructor(host: string) {
    super();
    this.worker.addEventListener('message', event => {
      switch (event.data.type) {
        case 'open': {
          this.readyState = WebSocket.OPEN;
          this.dispatchEvent(new Event('open'));
          return;
        }
        case 'close': {
          this.readyState = WebSocket.CLOSED;
          this.dispatchEvent(new CloseEvent('close'));
          return;
        }
        case 'message': {
          this.dispatchEvent(new MessageEvent('message', { data: event.data.data }));
          return;
        }
      }
    });
    this.worker.postMessage({ type: 'init', host });
  }
  readyState = WebSocket.CONNECTING;
  set binaryType(value: WebSocket['binaryType']) {
    if (value !== 'arraybuffer') {
      throw new Error(`Only arraybuffer supported`);
    }
  }
  worker = new Worker(URL.createObjectURL(blob));
  send(data: string | ArrayBuffer) {
    if (data instanceof ArrayBuffer) {
      this.worker.postMessage({ type: 'send', data }, [data]);
    } else {
      this.worker.postMessage({ type: 'send', data });
    }
  }
  close() {
    this.worker.postMessage({ type: 'close' });
  }
}
export interface WebSocketAsync {
  addEventListener<K extends keyof WebSocketEventMap>(type: K, listener: (this: WebSocket, ev: WebSocketEventMap[K]) => any, options?: boolean | AddEventListenerOptions): void;
  addEventListener(type: string, listener: EventListenerOrEventListenerObject, options?: boolean | AddEventListenerOptions): void;
  removeEventListener<K extends keyof WebSocketEventMap>(type: K, listener: (this: WebSocket, ev: WebSocketEventMap[K]) => any, options?: boolean | EventListenerOptions): void;
  removeEventListener(type: string, listener: EventListenerOrEventListenerObject, options?: boolean | EventListenerOptions): void;
}
