import * as Vector2 from '../Math/Vector2';
import * as Vector3 from '../Math/Vector3';
import * as Vector4 from '../Math/Vector4';
import { addLineNumbersToSource, ShaderError } from './ShaderCache';
import lodash from 'lodash';
import { mat4 } from 'gl-matrix';
import { isErr } from '../Result';
import { ITexture } from './Texture';
import { GlState, TextureFiltering, Destructible } from './GlState';
import { GLSLType, TextureRef } from './GLSLTypes';
import * as Mat4 from '../Math/Mat4';

export const quadVertexShader = `#version 300 es
in vec3 Vertex_position;
out vec2 texcoord;

void main() {
  texcoord = (Vertex_position.xy + 1.0) / 2.0;
  gl_Position = vec4(Vertex_position, 1.0);
}
`;

export class Sampler {
  constructor(public texture: ITexture, public filtering: TextureFiltering) {}
  get is2dArray() { return this.texture.is2dArray; }
}

export interface UniformTypeMappings {
  'float': number,
  'int': number,
  'uint': number,
  'bool': boolean,

  'float[]': number[] | Float32Array,
  'int[]': number[] | Int32Array,
  'uint[]': number[] | Uint32Array,
  'bool[]': boolean[] | Int32Array,

  'vec2': Vector2.Vector2 | Float32Array,
  'ivec2': Vector2.Vector2 | Int32Array,
  'vec3': Vector3.Vector3 | Float32Array,
  'ivec3': Vector3.Vector3 | Int32Array,
  'vec4': Vector4.Vector4 | Float32Array,
  'ivec4': Vector4.Vector4 | Int32Array,
  'mat4': Mat4.Mat4 | Float32Array,

  'vec2[]': Vector2.Vector2[] | Float32Array,
  'ivec2[]': Vector2.Vector2[] | Int32Array,
  'vec3[]': Vector3.Vector3[] | Float32Array,
  'ivec3[]': Vector3.Vector3[] | Int32Array,
  'vec4[]': Vector4.Vector4[] | Float32Array,
  'ivec4[]': Vector4.Vector4[] | Int32Array,
  'mat4[]': Mat4.Mat4[] | Float32Array,

  'sampler2D': TextureRef,
  'isampler2D': TextureRef,
  'usampler2D': TextureRef,
  'sampler2DArray': TextureRef,
  'isampler2DArray': TextureRef,
  'usampler2DArray': TextureRef,

  'sampler2D[]': TextureRef[],
  'isampler2D[]': TextureRef[],
  'usampler2D[]': TextureRef[],
  'sampler2DArray[]': TextureRef[],
  'isampler2DArray[]': TextureRef[],
  'usampler2DArray[]': TextureRef[],
}

export interface UniformValue<T extends GLSLType = GLSLType> {
  value: UniformTypeMappings[T];
  type: GLSLType;
}
export interface UniformValues {
  [key: string]: UniformValue
}

export function mergeUniformValues(...values: UniformValues[]): UniformValues {
  return Object.assign({}, ...values);
}

class CachingUniformWriter<T> {
  constructor(private renderer: Program,
    private glSet: (ul: WebGLUniformLocation, value: T) => void,
    private isEqual: (a: T, b: T) => boolean) {}

  values: { [key: string]: T } = {};

  write(key: string, value: T, throwIfNotPresent: boolean = true) {
    if (this.isEqual(this.values[key], value)) {
      return;
    }
    this.values[key] = value;
    const ul = this.renderer.getUniform(key, throwIfNotPresent);
    if (ul) {
      this.glSet(ul, value);
    }
  }
}

function vecArrayEquals<T>(a: T[] | Float32Array | Int32Array, b: T[] | Float32Array | Int32Array) {
  return a === b;
}

function numberArrayEquals<T>(a: T[] | Float32Array | Int32Array, b: T[] | Float32Array | Int32Array) {
  return a === b;
}

export class Program implements Destructible {
  constructor(public gls: GlState, public fragmentShaderSource: string,
      public vertexShaderSource = quadVertexShader, { logErrors = true }: { logErrors?: boolean } = {}) {
    const program = gls.shaderCache.createProgram(vertexShaderSource, fragmentShaderSource, { logErrors });
    if (isErr(program)) {
      this.error = program.error;
    } else {
      this.program = program.value;
    }
    Program.nAlivePrograms++;
  }
  destroy() {
    this.gl.deleteProgram(this.program);
    Program.nAlivePrograms--;
  }
  get gl() {
    return this.gls.gl;
  }
  shader?: WebGLShader;
  program: WebGLProgram | null = null;
  error?: ShaderError;
  public static nAlivePrograms = 0;

  private uniforms: { [key: string]: WebGLUniformLocation | null } = {};
  getUniform(key: string, required: boolean): WebGLUniformLocation | null {
    if (!this.program) {
      return null;
    }
    const gl = this.gl;
    let uniform: WebGLUniformLocation | null = this.uniforms[key];
    if (!uniform) {
      uniform = gl.getUniformLocation(this.program, key);
      if (!uniform && required) {
        // tslint:disable-next-line:no-console
        console.error('Vertex shader:');
        // tslint:disable-next-line:no-console
        console.error(addLineNumbersToSource(this.vertexShaderSource));
        // tslint:disable-next-line:no-console
        console.error('Fragment shader:');
        // tslint:disable-next-line:no-console
        console.error(addLineNumbersToSource(this.fragmentShaderSource));
        throw new Error('No such uniform: ' + key);
      }
      this.uniforms[key] = uniform;
    }
    return uniform;
  }
  private attributes: { [name: string]: number } = {};
  getAttribute(name: string) {
    if (!this.program) {
      return -1;
    }
    let loc = this.attributes[name];
    if (loc !== undefined) {
      return loc;
    } else {
      return this.attributes[name] = this.gl.getAttribLocation(this.program, name);
    }
  }

  private writers = {
    vec2:  new CachingUniformWriter<Vector2.Vector2>(this, (ul, value) => this.gl.uniform2f(ul, value.x, value.y), lodash.isEqual),
    ivec2: new CachingUniformWriter<Vector2.Vector2>(this, (ul, value) => this.gl.uniform2i(ul, value.x, value.y), lodash.isEqual),
    vec3:  new CachingUniformWriter<Vector3.Vector3>(this, (ul, value) => this.gl.uniform3f(ul, value.x, value.y, value.z), lodash.isEqual),
    ivec3: new CachingUniformWriter<Vector3.Vector3>(this, (ul, value) => this.gl.uniform3i(ul, value.x, value.y, value.z), lodash.isEqual),
    vec4:  new CachingUniformWriter<Vector4.Vector4>(this, (ul, value) => this.gl.uniform4f(ul, value.x, value.y, value.z, value.w), lodash.isEqual),
    ivec4: new CachingUniformWriter<Vector4.Vector4>(this, (ul, value) => this.gl.uniform4i(ul, value.x, value.y, value.z, value.w), lodash.isEqual),
    'vec2[]':  new CachingUniformWriter<Vector2.Vector2[] | Float32Array>(this, (ul, value) => { if (value.length === 0) { return; } this.gl.uniform2fv(ul, value instanceof Float32Array ? value : lodash.flatMap(value, v => [v.x, v.y])); }, vecArrayEquals),
    'ivec2[]': new CachingUniformWriter<Vector2.Vector2[] | Int32Array  >(this, (ul, value) => { if (value.length === 0) { return; } this.gl.uniform2iv(ul, value instanceof Int32Array   ? value : lodash.flatMap(value, v => [v.x, v.y])); }, vecArrayEquals),
    'vec3[]':  new CachingUniformWriter<Vector3.Vector3[] | Float32Array>(this, (ul, value) => { if (value.length === 0) { return; } this.gl.uniform3fv(ul, value instanceof Float32Array ? value : lodash.flatMap(value, v => [v.x, v.y, v.z])); }, vecArrayEquals),
    'ivec3[]': new CachingUniformWriter<Vector3.Vector3[] | Int32Array  >(this, (ul, value) => { if (value.length === 0) { return; } this.gl.uniform3iv(ul, value instanceof Int32Array   ? value : lodash.flatMap(value, v => [v.x, v.y, v.z])); }, vecArrayEquals),
    'vec4[]':  new CachingUniformWriter<Vector4.Vector4[] | Float32Array>(this, (ul, value) => { if (value.length === 0) { return; } this.gl.uniform4fv(ul, value instanceof Float32Array ? value : lodash.flatMap(value, v => [v.x, v.y, v.z, v.w])); }, vecArrayEquals),
    'ivec4[]': new CachingUniformWriter<Vector4.Vector4[] | Int32Array  >(this, (ul, value) => { if (value.length === 0) { return; } this.gl.uniform4iv(ul, value instanceof Int32Array   ? value : lodash.flatMap(value, v => [v.x, v.y, v.z, v.w])); }, vecArrayEquals),

    mat4: new CachingUniformWriter<mat4>(this, (ul, value) => this.gl.uniformMatrix4fv(ul, false, value), (a, b) => a === b),
    int: new CachingUniformWriter<number>(this, (ul, value) => this.gl.uniform1i(ul, value), (a, b) => a === b),
    uint: new CachingUniformWriter<number>(this, (ul, value) => this.gl.uniform1ui(ul, value), (a, b) => a === b),
    float: new CachingUniformWriter<number>(this, (ul, value) => this.gl.uniform1f(ul, value), (a, b) => a === b),
    bool: new CachingUniformWriter<boolean>(this, (ul, value) => this.gl.uniform1i(ul, value ? 1 : 0), (a, b) => a === b),

    'float[]': new CachingUniformWriter<number[] | Float32Array>(this, (ul, value) => { if (value.length === 0) { return; } this.gl.uniform1fv(ul, value); }, numberArrayEquals),
    'int[]': new CachingUniformWriter<number[] | Int32Array  >(this, (ul, value) => { if (value.length === 0) { return; } this.gl.uniform1iv(ul, value); }, numberArrayEquals),
    'bool[]': new CachingUniformWriter<boolean[] >(this, (ul, value) => { if (value.length === 0) { return; } this.gl.uniform1iv(ul, value.map(x => x ? 1 : 0)); }, numberArrayEquals),
  }

  private textureUnitOffset = 0;
  prepare() {
    this.textureUnitOffset = 0;
    this.gls.program = this.program;
  }

  private setTexture(key: string, value: ITexture | Sampler | undefined, expect2dArray: boolean, required?: boolean) {
    if (!value) {
      this.writers.int.write(key, -1, required);
      return;
    }

    const sampler = value;
    const tex = sampler instanceof Sampler ? sampler.texture : sampler;
    if (sampler.is2dArray !== expect2dArray) {
      throw new Error(`Expected texture "${key}" ${expect2dArray ? 'to be' : 'to not be'} 2d array, but it ${sampler.is2dArray ? 'is' : 'is not'} 2d array`);
    }
    if (!tex.inited) {
      throw new Error(`Texture has not been initialized`);
    }
    const index = this.textureUnitOffset++;
    this.gls.setActiveTexture(index, tex.isDoubleBuffered ? tex.front : tex);
    const filtering = sampler instanceof Sampler ? sampler.filtering : TextureFiltering.Nearest;
    this.gls.setSampler(index, filtering);

    this.writers.int.write(key, index, required);
  }

  private setTextureArray(key: string, values: Array<ITexture | Sampler>, expect2dArray: boolean, required?: boolean) {
    const inputTextures: number[] = [];
    for (const sampler of values) {
      const tex = sampler instanceof Sampler ? sampler.texture : sampler;
      if (sampler.is2dArray !== expect2dArray) {
        throw new Error(`Expected texture "${key}" to ${expect2dArray ? '' : 'not'} be 2d array, but it ${expect2dArray ? 'is' : 'is not'}`);
      }
      if (!tex.inited) {
        throw new Error(`Texture has not been initialized`);
      }
      // const index = this.common.activeTextures.bind(target, sampler);
      const index = this.textureUnitOffset++;
      this.gls.setActiveTexture(index, tex.isDoubleBuffered ? tex.front : tex);
      const filtering = sampler instanceof Sampler ? sampler.filtering : TextureFiltering.Nearest;
      this.gls.setSampler(index, filtering);
      inputTextures.push(index);
    }

    this.writers['int[]'].write(key, inputTextures, required);
  }

  setUniform<T extends GLSLType>(key: string, type: T, value: UniformTypeMappings[T], required?: boolean) {
    this.gls.program = this.program;
    const isNormalSampler = type === 'isampler2D' || type === 'usampler2D' || type === 'sampler2D';
    const isArraySampler = type === 'isampler2DArray' || type === 'usampler2DArray' || type === 'sampler2DArray';

    const isNormalMultiSampler = type === 'isampler2D[]' || type === 'usampler2D[]' || type === 'sampler2D[]';
    const isArrayMultiSampler = type === 'isampler2DArray[]' || type === 'usampler2DArray[]' || type === 'sampler2DArray[]';

    if (isNormalSampler || isArraySampler) {
      this.setTexture(key, value as (Sampler | undefined), isArraySampler, required);
    } else if (isNormalMultiSampler || isArrayMultiSampler) {
      this.setTextureArray(key, value as Sampler[], isArrayMultiSampler, required);
    } else {
      this.writers[type as any].write(key, value, required);
    }
  }

  setUniforms(uniformValues: UniformValues, uniformIgnoreIfNotPresent: string[] = [], textureUnitOffset = 0) {
    this.textureUnitOffset = textureUnitOffset;
    this.gls.program = this.program;
    for (const key of Object.keys(uniformValues)) {
      const v = uniformValues[key];
      this.setUniform(key, v.type, v.value, uniformIgnoreIfNotPresent.indexOf(key) === -1);
    }
    return this.textureUnitOffset;
  }

}
