import * as React from 'react';
import { Store, useStoreSelect, useStore } from '../Components/Store';
import { FirebaseStreamQueryResult, FirebaseStreamDocumentResult } from '../Components/Firebase';
import { DbCreature, DbItemInstance, DbUserSettings } from 'db';
import { GameInstance } from '../GpuWorld/StateData';
import { ChangelogEntry } from './App';

export class AppState {
  loggedInUserId?: string;
  userSettings: FirebaseStreamDocumentResult<DbUserSettings> = { status: 'loading' };
  draggingUrl = '';
  hideUI = false;
  creatures: FirebaseStreamQueryResult<DbCreature> = { status: 'loading' };
  items: FirebaseStreamQueryResult<DbItemInstance> = { status: 'loading' };
  gameInstance: GameInstance = { type: 'none' };
  scheduledTrainingIndex = -1;
  scheduledEnd = -1;
  changelog: ChangelogEntry[] = [];
}
export const AppStateContext = React.createContext(new Store(new AppState()));
export function useAppState<T>(map: (state: AppState) => T): T {
  return useStore(AppStateContext, map);
}
export function useAppStateSelect(...keys: Array<keyof AppState>): AppState {
  return useStoreSelect(AppStateContext, ...keys);
}
