import { GpuWorldState } from "./State";
import { vec2FromSize } from "../Math/Math";
import * as Vector3 from '../Math/Vector3';
import { mat4 } from "gl-matrix";
import { InvLocalToWorld } from "./ECS/Transform";
import { Camera } from "./ECS/Camera";
import { emptyTrainingSetup, defaultDbUserSettings } from "db";
import firebase from 'firebase/app';


export class GpuWorldStateDerived {
  constructor(private state: GpuWorldState) {}
  get worldSize() { return vec2FromSize(this.state.data.worldSize); }
  get groundSize() { return vec2FromSize(this.state.data.groundSize); }

  get cameraView(): mat4 {
    return this.state.ecs.getComponentData(InvLocalToWorld.cpuComponent, this.state.data.camera!);
  }
  get cameraProjection(): mat4 {
    return this.state.ecs.getComponentData(Camera.Projection, this.state.data.camera!);
  }
  get cameraProjectionView(): mat4 {
    return this.state.ecs.getComponentData(Camera.ProjectionView, this.state.data.camera!);
  }
  get cameraInvProjectionView(): mat4 {
    return this.state.ecs.getComponentData(Camera.InvProjectionView, this.state.data.camera!);
  }

  maxGroundHeight = 0;

  // groundCell * generateGroundCellScaling gives us a vec2 in the range of KM
  get generateGroundCellScaling() {
    // const groundCell = ;
    // const worldCoord = groundCell * (1 / groundSize);
    // const worldPosM = worldCoord * worldSize;
    // const worldPosKM = worldPosM / 1024;
    // const worldPosKM = (worldCoord * worldSize) / 1024;
    // const worldPosKM = groundCell * (1 / groundSize) * worldSize / 1024;
    return (1 / this.groundSize.x) * this.worldSize.x / 1024;
  }

  get sunlightDirection() {
    // const sun = this.state.data.modules.sun;
    // if (!sun.enabled) {
    //   return Vector3.normalize({ x: -1, y: -1, z: -1 });
    // }
    // const r = sunDirectionOnPlanet(sun.latitude, this.timeOfDayPercentage,
    //   sun.planetAxialTilt, this.timeOfYearPercentage);
    // return r.sunDirection;
    // return Vector3.normalize({ x: 1, y: 1, z: 0.5 });
    return Vector3.normalize(this.state.data.rendererConfig.lightDirection);
  }
  get renderLightDirection() {
    return this.state.data.rendererConfig.lightDirection;
    // return this.sunlightDirection.z > 0 ? this.sunlightDirection : Vector3.invert(this.sunlightDirection);
  }

  get simulationRunning() {
    return !this.state.data.simulationPaused && !this.state.data.simulationEnded;
  }

  get userSettingsOrFallback() {
    const userSettings = this.state.appState?.state.userSettings;
    if (userSettings?.status === 'loaded') {
      return userSettings.value;
    } else {
      return defaultDbUserSettings(firebase.firestore.Timestamp.now());
    }
  }
  get trainingSetup() {
    const userSettings = this.state.appState?.state.userSettings;
    if (userSettings?.status === 'loaded') {
      const index = this.state.data.gameInstance.type === 'train' ? this.state.data.gameInstance.setupIndex : 0;
      return userSettings.value.trainingSetups[index] ?? emptyTrainingSetup();
    } else {
      return emptyTrainingSetup();
    }
  }
}