import { GpuWorldState } from "../State";
import { GlState } from "../../GpuUtil/GlState";
import { CastingAbilityHelpers, SleepDartProjectileRef, Casting, UnitRef } from "./Abilities";
import { Abilities, abilitiesData, GunProjectileRef } from "./Abilities";
import { ArchetypesQuery } from "../../GpuUtil/ECS/Query";
import { shaderModule, funcDef, uniformDef } from "../../GpuUtil/ShaderModule";
import { Position, LocalToWorld } from "../ECS/Transform";
import { SeekingProjectileTarget, DamageProjectile, ProjectileSpeed, DazeProjectile, KnockbackProjectile } from "../Projectiles/Projectiles";
import { StepsPerSecond } from "../../Time";
import { MatrixSM } from "../../GpuUtil/SM2Utils";
import { glslVec3 } from "../../GpuUtil";

const ProjectileOffset = { x: 0, y: 1.237, z: 1.1705 };
const TailAttackOffset = { x: 0, y: 0.3068, z: 2.1917 };

export class UpdateSleepDart {
  constructor(private gls: GlState) {}

  update = new ArchetypesQuery([abilitiesData.component[Abilities.SleepDart].gpuComponent])
    .updateGpu(this.gls, [
      Position.gpuComponent,
      SeekingProjectileTarget.component,
      ProjectileSpeed.component,
      DazeProjectile.component,
    ], shaderModule([
      CastingAbilityHelpers,
      Casting.Target.sm,
      abilitiesData.component[Abilities.SleepDart].sm,
      SeekingProjectileTarget.writer,
      Position.writer,
      DazeProjectile.writer,
      SleepDartProjectileRef.sm,
      Position.sm,
      ProjectileSpeed.writer,
      MatrixSM,
      LocalToWorld.sm,
      UnitRef.sm,
    ], {
      sleepDuration: uniformDef('float', () => abilitiesData.get(Abilities.SleepDart, 'Settings').sleepDuration.value * StepsPerSecond),
      update: funcDef('ivec2', [['ivec2', 'entityCell']], `
        ivec2 unit = ${UnitRef.sm.value}(entityCell);
        if (!${CastingAbilityHelpers.isPerforming}(unit, ${Abilities.SleepDart})) {
          return ivec2(-1);
        }
        vec3 projectileOffset = ${glslVec3(TailAttackOffset)};
        vec3 projectileStartPosition = ${MatrixSM.transformPoint}(${LocalToWorld.sm.value}(unit), projectileOffset);
        ${Position.writer.write}(projectileStartPosition);
        ${SeekingProjectileTarget.writer.write}(${Casting.Target.sm.value}(unit));
        ${ProjectileSpeed.writer.write}(1.0);
        ${DazeProjectile.writer.write}(Self.sleepDuration);
        return ${SleepDartProjectileRef.sm.value}(entityCell);
      `)
    }));

  run(state: GpuWorldState) {
    this.update.run(state);
  }
}

