import * as React from 'react';
import './Editors.css';
import { EditorTypedefResolver, EditorEnumStructVariants } from './EditorTypedefs';
import { EditorProps, EditorDispatch } from './EditorProps';
import { StructEditor, StructEditorChange } from './StructEditor';
import { EnumEditor } from './PrimitiveEditors';

interface EnumStructEditorProps extends EditorProps<any> {
  variants: EditorEnumStructVariants;
  typedefResolver?: EditorTypedefResolver;
}

export function EnumStructEditor({ className, onChange, readOnly, value, variants }: EnumStructEditorProps & EditorDispatch<any>): any {
  const typeVariants = Object.keys(variants).map(typeName => 
    ({ value: typeName, displayName: variants[typeName].displayName || typeName, description: variants[typeName].description }));
  return (
    <div className={'EnumStructEditor ' + (className || '')}>
      <EnumEditor
        variants={typeVariants}
        readOnly={readOnly}
        value={value ? value.type : undefined}
        onChange={type => {
          if (!type) {
            onChange(null);
          } else {
            const obj = variants[type].default(type);
            onChange({ ...obj, ...value, type });
          }
        }}
        />
      <div className="EnumStructEditorProperties">
        {value && value.type && variants[value.type] && (
          <StructEditor
            properties={variants[value.type].properties}
            readOnly={readOnly}
            value={value}
            onChange={(change: StructEditorChange) => {
              onChange({...value, [change.property]: change.value });
            }}
            />
        )}
      </div>
    </div>
  );
}
