import { Image, ImageDataType } from './Image';
import { Texture, DoubleBufferedTexture, TextureSlicing, DoubleBufferedTextureSlicing } from './Texture';
import lodash from 'lodash';
import { GlState, Destructible } from './GlState';
import { createBatches } from './Computation';
import { getGlConstantName } from '.';
import { GlUnitTests } from '../SteamApp/Test/GlUnitTests';

export interface ResolvedReadRect {
  x: number;
  y: number;
  width: number;
  height: number;
}
export type ReadRect = Partial<ResolvedReadRect>;
function resolveReadRectForTexture(texture: Texture | DoubleBufferedTexture, readRect: ReadRect): ResolvedReadRect {
  return {
    x: readRect.x !== undefined ? readRect.x : 0,
    y: readRect.y !== undefined ? readRect.y : 0,
    width: readRect.width !== undefined ? readRect.width : texture.size!.width,
    height: readRect.height !== undefined ? readRect.height : texture.size!.height,
  };
}

export class TextureReadPollOptions {
  checkPeriod = 16;
  maxNPeriods = 4000; // After this it will just try to wait for the read
}

interface TextureReaderConfig {
  reuseBuffer?: boolean;
}

interface ITextureReader {
  destroy(): void;
  read<T extends ImageDataType = Float32Array>(mode: 'poll', readRect: ReadRect, pollOpts?: TextureReadPollOptions): Promise<Image<T>>;
  read<T extends ImageDataType = Float32Array>(mode: 'block', readRect: ReadRect): Image<T>;
  read<T extends ImageDataType = Float32Array>(mode: 'background', readRect: ReadRect): () => Image<T>;
}

export type TextureReadMode = 'block' | 'poll' | 'background';

// A buffered texture reader; can be used multiple times, but the same image buffer is reused
export class TextureReader implements ITextureReader, Destructible {
  constructor(private textureSlicing: TextureSlicing, private config: TextureReaderConfig = {}) {
    const texture = textureSlicing.texture;
    const slices = textureSlicing.layers || [0];
    const gl = texture.gls.gl;
    if (texture.internalFormat === gl.R32F) {
      // For some reason RED/FLOAT doesn't work, so we have to read it to a larger image first and then downsample that image
      this.format = gl.RGBA;
      this.type = gl.FLOAT;
      this.packAlignment = 4;
    } else if (texture.internalFormat === gl.RGBA32F) {
      this.format = gl.RGBA;
      this.type = gl.FLOAT;
      this.packAlignment = 4;
    } else if (texture.internalFormat === gl.R32I) {
      // Same problem as R32F, but only on ubuntu
      this.format = gl.RGBA_INTEGER;
      this.type = gl.INT;
      this.packAlignment = 4;
    } else if (texture.internalFormat === gl.RGBA32I) {
      this.format = gl.RGBA_INTEGER;
      this.type = gl.INT;
      this.packAlignment = 4;
    } else if (texture.internalFormat === gl.R8UI) {
      this.format = gl.RED_INTEGER;
      this.type = gl.UNSIGNED_BYTE;
      this.packAlignment = 1;
    } else if (texture.internalFormat === gl.RGBA8UI) {
      this.format = gl.RGBA_INTEGER;
      this.type = gl.UNSIGNED_BYTE;
      this.packAlignment = 4;
    } else {
      throw new Error('Not implemented yet');
    }

    this.batches = Array.from(createBatches(slices.length, this.gls.maxColorAttachements));
    this.fbs = this.batches.map(() => this.gls.gl.createFramebuffer());

    for (let l = 0; l < this.fbs.length; l++) {
      this.texture.gls.framebuffer = this.fbs[l];
      const batch = this.batches[l];
      for (let i=0; i < batch.count; i++) {
        if (this.texture.is2dArray) {
          const layer = slices[batch.l + i];
          if (layer < 0 || layer >= this.texture.depth!) {
            throw new Error(`Layer out of range`);
          }
          gl.framebufferTextureLayer(gl.READ_FRAMEBUFFER, gl.COLOR_ATTACHMENT0 + i, this.texture.texture, 0, layer);
        } else {
          gl.framebufferTexture2D(gl.FRAMEBUFFER, gl.COLOR_ATTACHMENT0 + i, gl.TEXTURE_2D, this.texture.texture, 0);
        }
      }
    }

  }
  destroy() {
    for (const fb of this.fbs) {
      this.gls.gl.deleteFramebuffer(fb);
    }
  }
  private texture = this.textureSlicing.texture;
  private gls = this.texture.gls;
  private batches: Array<{ l: number, count: number }>;
  private fbs: Array<WebGLFramebuffer | null>;
  private format: number;
  private type: number;
  private packAlignment: number;
  private img?: Image<Float32Array> | Image<Int32Array> | Image<Uint8Array>;
  private imgOutBuffer?: Image<Float32Array> | Image<Int32Array> | Image<Uint8Array>;

  private createImgBuffer(r: ResolvedReadRect) {
    const gl = this.gls.gl;
    if (this.texture.internalFormat === gl.R32F) {
      // For some reason RED/FLOAT doesn't work, so we have to read it to a larger image first and then downsample that image
      this.img = Image.zeroesF32({ width: r.width, height: r.height, depth: this.textureSlicing.count }, 4);
      this.imgOutBuffer = Image.zeroesF32({ width: r.width, height: r.height, depth: this.textureSlicing.count }, 1);
    } else if (this.texture.internalFormat === gl.RGBA32F) {
      this.img = Image.zeroesF32({ width: r.width, height: r.height, depth: this.textureSlicing.count }, 4);
    } else if (this.texture.internalFormat === gl.R32I) {
      // Same problem as R32F, but only on ubuntu
      this.img = Image.zeroesI32({ width: r.width, height: r.height, depth: this.textureSlicing.count }, 4);
      this.imgOutBuffer = Image.zeroesI32({ width: r.width, height: r.height, depth: this.textureSlicing.count }, 1);
    } else if (this.texture.internalFormat === gl.RGBA32I) {
      this.img = Image.zeroesI32({ width: r.width, height: r.height, depth: this.textureSlicing.count }, 4);
    } else if (this.texture.internalFormat === gl.R8UI) {
      this.img = Image.zeroesU8({ width: r.width, height: r.height, depth: this.textureSlicing.count }, 1);
    } else if (this.texture.internalFormat === gl.RGBA8UI) {
      this.img = Image.zeroesU8({ width: r.width, height: r.height, depth: this.textureSlicing.count }, 4);
    } else {
      throw new Error('Not implemented yet');
    }
  }

  readAndDestroy<T extends ImageDataType = Float32Array>(mode: 'poll', readRect: ReadRect, pollOpts?: TextureReadPollOptions): Promise<Image<T>>;
  readAndDestroy<T extends ImageDataType = Float32Array>(mode: 'block', readRect: ReadRect): Image<T>;
  readAndDestroy<T extends ImageDataType = Float32Array>(mode: 'background', readRect: ReadRect): () => Image<T>;
  readAndDestroy(mode: TextureReadMode, readRect: ReadRect, asyncOpts?: TextureReadPollOptions): any {
    if (mode === 'poll') {
      return this.read('poll', readRect, asyncOpts).then(res => {
        this.destroy();
        return res;
      });
    } else if (mode === 'background') {
      const r = this.read('background', readRect);
      return () => {
        const img = r();
        this.destroy();
        return img;
      }
    } else {
      const res = this.read('block', readRect);
      this.destroy();
      return res;
    }
  }

  read<T extends ImageDataType = Float32Array>(mode: 'poll', readRect: ReadRect, pollOpts?: TextureReadPollOptions): Promise<Image<T>>;
  read<T extends ImageDataType = Float32Array>(mode: 'block', readRect: ReadRect): Image<T>;
  read<T extends ImageDataType = Float32Array>(mode: 'background', readRect: ReadRect): () => Image<T>;
  read(mode: TextureReadMode, readRect: ReadRect, pollOpts: TextureReadPollOptions = new TextureReadPollOptions()): any {
    const gl = this.texture.gls.gl;
    const r = resolveReadRectForTexture(this.texture, readRect);
    if (!this.config.reuseBuffer || !this.img || this.img.size.width !== r.width || this.img.size.height !== r.height) {
      this.createImgBuffer(r);
    }
    const img = this.img!;
    const imgOutBuffer = this.imgOutBuffer;

    let buff;
    if (mode !== 'block') {
      buff = gl.createBuffer();
      gl.bindBuffer(gl.PIXEL_PACK_BUFFER, buff);
      gl.bufferData(gl.PIXEL_PACK_BUFFER, img.sizeInBytes, gl.STREAM_READ);
    }

    gl.pixelStorei(gl.PACK_ALIGNMENT, this.packAlignment);

    const layerSize = img.sizeInBytes / img.size.depth;

    for (let l=0; l < this.batches.length; l++) {
      this.texture.gls.framebuffer = this.fbs[l];
      const batch = this.batches[l];
      for (let x=0; x < batch.count; x++) {
        gl.readBuffer(gl.COLOR_ATTACHMENT0 + x);
        const i = batch.l + x;
        if (mode !== 'block') {
          gl.readPixels(r.x, r.y, r.width, r.height, this.format, this.type, i * layerSize);
        } else {
          gl.readPixels(r.x, r.y, r.width, r.height, this.format, this.type, img.data, i * layerSize / img.channelSize);
        }
      }
    }

    let syncObject;
    if (mode !== 'block') {
      syncObject = gl.fenceSync(gl.SYNC_GPU_COMMANDS_COMPLETE, 0);
    }

    const textureFormat = this.texture.internalFormat;

    const resolveImage = () => {
      const gl = this.gls.gl;
      if (textureFormat === gl.R32F || textureFormat === gl.R32I) {
        // Because we can't use gl.RED above..
        const res = imgOutBuffer!;
        for (let i=0; i < res.data.length; i++) {
          res.data[i] = img.data[i * 4];
        }
        return res;
      } else {
        return img;
      }
    }

    if (mode === 'poll' || mode === 'background') {
      gl.bindBuffer(gl.PIXEL_PACK_BUFFER, null);

      const check = (timeout = 0) => {
        const s = gl.clientWaitSync(syncObject, 0, timeout);
        if (s === gl.ALREADY_SIGNALED || s === gl.CONDITION_SATISFIED) {
          gl.bindBuffer(gl.PIXEL_PACK_BUFFER, buff);
          gl.getBufferSubData(gl.PIXEL_PACK_BUFFER, 0, img.data);
          gl.bindBuffer(gl.PIXEL_PACK_BUFFER, null);
          gl.deleteSync(syncObject);
          return resolveImage();
        } else {
          return null;
        }
      }

      if (mode === 'background') {
        return check;
      } else {
        return new Promise((resolve, reject) => {
          const checksCounter = { n: 0 };
          const checker = window.setInterval(() => {
            checksCounter.n++;
            const isMaxPeriods = checksCounter.n >= pollOpts.maxNPeriods;
            if (isMaxPeriods) {
              console.warn(`Reached max check periods (${pollOpts.maxNPeriods}) while trying to read texture (${this.texture.name})`);
            }
            if (this.gls.contextLost) {
              console.warn(`Silently failing texture read due to context lost`);
              window.clearInterval(checker);
              return;
            }
            const img = check(isMaxPeriods ? gl.MAX_CLIENT_WAIT_TIMEOUT_WEBGL : 0);
            if (img) {
              resolve(img);
              window.clearInterval(checker);
            } else if (isMaxPeriods) {
              window.clearInterval(checker);
              reject('Texture read timeout');
            }
          }, pollOpts.checkPeriod);
        });
      }
    } else {
      return resolveImage();
    }
  }
}

export class DoubleBufferedTextureReader implements ITextureReader {
  constructor(private textureSlice: DoubleBufferedTextureSlicing, private config?: TextureReaderConfig) {
  }
  destroy() {
    this.read0.destroy();
    this.read1.destroy();
  }
  private read0 = new TextureReader(this.textureSlice.texture0, this.config);
  private read1 = new TextureReader(this.textureSlice.texture1, this.config);

  read<T extends ImageDataType = Float32Array>(mode: 'poll', readRect: ReadRect, pollOpts?: TextureReadPollOptions): Promise<Image<T>>;
  read<T extends ImageDataType = Float32Array>(mode: 'block', readRect: ReadRect): Image<T>;
  read<T extends ImageDataType = Float32Array>(mode: 'background', readRect: ReadRect): () => Image<T>;
  read(mode: TextureReadMode, readRect: ReadRect, pollOpts?: TextureReadPollOptions): any {
    if (this.textureSlice.texture.swapped) {
      return this.read0.read(mode as any, readRect, pollOpts);
    } else {
      return this.read1.read(mode as any, readRect, pollOpts);
    }
  }
}

export class LazyTextureReader implements ITextureReader {
  constructor(private createReader: () => ITextureReader) {
  }
  destroy() {
    if (this.reader) {
      this.reader.destroy();
    }
  }
  private reader?: ITextureReader;

  read<T extends ImageDataType = Float32Array>(mode: 'poll', readRect: ReadRect, pollOpts?: TextureReadPollOptions): Promise<Image<T>>;
  read<T extends ImageDataType = Float32Array>(mode: 'block', readRect: ReadRect): Image<T>;
  read<T extends ImageDataType = Float32Array>(mode: 'background', readRect: ReadRect): () => Image<T>;
  read(mode: TextureReadMode, readRect: ReadRect, pollOpts?: TextureReadPollOptions): any {
    if (!this.reader) {
      this.reader = this.createReader();
    }
    return this.reader.read(mode as any, readRect, pollOpts);
  }
}

const textureReaderTest = async (gls: GlState, original: Image<Float32Array> | Image<Int32Array> | Image<Uint8Array>, async: boolean) => {
  const tex = original.toGl(gls);
  const testName = `${async ? 'async' : 'sync'} ${getGlConstantName(gls.gl, tex.internalFormat)} ${tex.is2dArray ? '2darray' : '2d'} depth=${original.size.depth}`;
  // tslint:disable-next-line:no-console
  console.log(`Testing image read: ${testName}...`);
  const slice = tex.sliceRange(0, original.size.depth);
  const downloaded = async ? await Image.readTextureAsync(slice) : Image.readTextureSync(slice);
  if (!lodash.isEqual(original.data, downloaded.data)) {
    // tslint:disable-next-line:no-console
    console.log('Original');
    original.dump();
    // tslint:disable-next-line:no-console
    console.log('Downloaded');
    downloaded.dump();
    throw new Error(`Image reads differ for ${testName}`);
  }
}

for (const depth of [1, 3, 11]) {
  for (const channels of [1, 4]) {
    const size = { width: 3, height: 3, depth };
    for (const asyncr of [true, false]) {
      GlUnitTests.push({
        name: `TextureReader depth=${depth} channels=${channels} async=${asyncr} f32`,
        test: gls => textureReaderTest(gls, Image.zeroesF32(size, channels).randomize(), asyncr)
      });
      GlUnitTests.push({
        name: `TextureReader depth=${depth} channels=${channels} async=${asyncr} i32`,
        test: gls => textureReaderTest(gls, Image.zeroesI32(size, channels).randomize(0, 6000), asyncr)
      });
      GlUnitTests.push({
        name: `TextureReader depth=${depth} channels=${channels} async=${asyncr} u8`,
        test: gls => textureReaderTest(gls, Image.zeroesU8(size, channels).randomize(0, 255), asyncr)
      });
    }
  }
}
