import { GpuWorldState } from "../State";
import { EntitiesQuery } from "../../GpuUtil/ECS/Query";
import { ArenaInstance, VisibleComponent, VisibilityFromComponent } from "./Components";

export function visibleCacheKeys(state: GpuWorldState) {
  return [state.data.renderArena, state.data.renderAllArenas]
}

export class UpdateVisible {
  visibleQuery = new EntitiesQuery([VisibleComponent, ArenaInstance.cpuComponent])
    .updateCpu((state, entities) => {
      for (const entityCell of entities) {
        const arenaInstance = state.ecs.getComponentData(ArenaInstance.cpuComponent, entityCell)[0];
        state.ecs.setComponentData(VisibleComponent, entityCell, arenaInstance === state.data.renderArena ||
          (state.data.renderAllArenas && arenaInstance === -1));
      }
    }, visibleCacheKeys);

  visibleFromQuery = new EntitiesQuery([VisibleComponent, VisibilityFromComponent])
    .updateCpu((state, entities) => {
      for (const entityCell of entities) {
        const from = state.ecs.getComponentData(VisibilityFromComponent, entityCell)!;
        const visible = state.ecs.getComponentData(VisibleComponent, from.value)!;
        state.ecs.setComponentData(VisibleComponent, entityCell, visible);
      }
    }, visibleCacheKeys);

  run(state: GpuWorldState) {
    this.visibleQuery.run(state);
    this.visibleFromQuery.run(state);
  }
}
