import { EditorTypedef, editorStructTypedef } from "../../Components/Editors/EditorTypedefs";
import { RGBA } from "../../Util";
import * as Vector3 from "../../Math/Vector3";

export interface GpuWorldRendererOptions {
  lightExponent: number;
  lightDirection: Vector3.Vector3;
  diffuseColor: RGBA;
  diffuseStrength: number;
  ambientColor: RGBA;
  ambientStrength: number;

  worldPlaneColor: RGBA;
  skyColor: RGBA;
  fogColor: RGBA;
  sunColor: RGBA;
  sunFlareColor: RGBA;
  sunSize: number;
  sunFlareSize: number;
  sunFlareExponent: number;
  sunBorder: number;
  fogDensity: number;
  fogOffset: number;
  skybox: boolean;
  skyboxFogExponent: number;
  skyboxFogHeight: number;
  horizon: number;
  overheadBars: boolean;
  clearColor: RGBA;
}

export function defaultRenderOptions(): GpuWorldRendererOptions {
  return {
    "lightExponent": 10,
    lightDirection: Vector3.normalize({ x: -0.1, y: 1, z: 0.45 }),
    "diffuseColor": {
      "r": 30,
      "g": 101,
      "b": 168,
      "a": 1
    },
    "diffuseStrength": 1,
    "ambientColor": {
      "r": 105,
      "g": 127,
      "b": 185,
      "a": 1
    },
    "ambientStrength": 1,
    "worldPlaneColor": {
      "r": 0.701 * 255,
      "g": 0.584 * 255,
      "b": 0.242 * 255,
      "a": 1
    },
    "skyColor": {
      "r": 68,
      "g": 112,
      "b": 155,
      "a": 1
    },
    "fogColor": {
      r: 245,
      g: 244,
      b: 235,
      "a": 1
    },
    "sunColor": {
      "r": 255,
      "g": 255,
      "b": 255,
      "a": 1
    },
    "sunFlareColor": {
      "r": 255,
      "g": 99,
      "b": 48,
      "a": 1
    },
    "sunSize": 0.001,
    "sunFlareSize": 0.09,
    "sunFlareExponent": 10,
    "sunBorder": 0.001,
    "skybox": false,
    "skyboxFogExponent": 2,
    skyboxFogHeight: 1,
    horizon: 1.45,
    "overheadBars": true,
    "clearColor": {
      r: 245,
      g: 244,
      b: 235,
      // "r": 239,
      // "g": 209,
      // "b": 181,
      "a": 1
    },
    "fogDensity": 0.003,
    fogOffset: 80,
  }
}

export const GpuWorldRendererOptionsTypedef = (): EditorTypedef => editorStructTypedef<GpuWorldRendererOptions>({
  properties: {
    lightExponent: { metatype: 'float' },
    lightDirection: Vector3.Vector3Typedef,
    diffuseColor: { metatype: 'color' },
    diffuseStrength: { metatype: 'float' },
    ambientColor: { metatype: 'color' },
    ambientStrength: { metatype: 'float' },
    worldPlaneColor: { metatype: 'color' },
    skyColor: { metatype: 'color' },
    fogColor: { metatype: 'color' },
    sunColor: { metatype: 'color' },
    sunFlareColor: { metatype: 'color' },
    sunSize: { metatype: 'float' },
    sunFlareSize: { metatype: 'float' },
    sunFlareExponent: { metatype: 'float' },
    sunBorder: { metatype: 'float' },
    skybox: { metatype: 'boolean' },
    skyboxFogExponent: { metatype: 'float' },
    skyboxFogHeight: { metatype: 'float' },
    horizon: { metatype: 'float' },
    overheadBars: { metatype: 'boolean' },
    clearColor: { metatype: 'color' },
    fogDensity: { metatype: 'float' },
    fogOffset: { metatype: 'float' },
  }
});
