import { RandomSeedable } from "../Util";
import { vec3, mat4, quat } from 'gl-matrix';
import * as Math2 from './Math';
import { EditorTypedef } from "../Components/Editors/EditorTypedefs";
import { SphericalCoordinate } from "./SphericalCoordinate";
import * as Vector2 from "./Vector2";

export interface Vector3 {
  x: number;
  y: number;
  z: number;
}
export const Vector3Typedef: EditorTypedef = {
  metatype: 'struct',
  properties: {
    x: { metatype: 'float' },
    y: { metatype: 'float' },
    z: { metatype: 'float' },
  }
}


export function create(x: number, y: number, z: number): Vector3;
export function create(v: Vector2.Vector2, z: number): Vector3;
export function create(v: number | Vector3): Vector3;
export function create(...args: any[]): Vector3 {
  if (args.length === 1) {
    if (typeof args[0] === 'number') {
      return { x: args[0], y: args[0], z: args[0] };
    } else {
      return {...args[0]};
    }
  } else if (args.length === 2) {
    return { ...args[0], z: args[1] };
  } else {
    return { x: args[0], y: args[1], z: args[2] };
  }
}

export function xy(v: Vector3): Vector2.Vector2 {
  return { x: v.x, y: v.y };
}

export function zero(): Vector3 {
  return { x: 0, y: 0, z: 0 };
}

export function random(rand: RandomSeedable): Vector3 {
  return { x: rand.random(), y: rand.random(), z: rand.random() };
}

export function fromSphericalCoordinates(theta: number, phi: number, radius: number): Vector3;
export function fromSphericalCoordinates({ theta, phi, radius }: SphericalCoordinate): Vector3;
export function fromSphericalCoordinates(...args: any[]): Vector3 {
  if (typeof args[0] === 'object') {
    return fromSphericalCoordinates(args[0].theta, args[0].phi, args[0].radius);
  } else {
    const theta = args[0];
    const phi = args[1];
    const radius = args[2];
    return {
      x: Math.sin(theta) * Math.cos(phi) * radius,
      y: Math.sin(theta) * Math.sin(phi) * radius,
      z: Math.cos(theta) * radius
    };
  }
}

export function toSphericalCoordinates({ x, y, z }: Vector3): SphericalCoordinate {
  const radius = Math.sqrt(x*x + y*y + z*z);
  const theta = Math.acos(z / radius);
  const phi = Math.atan2(y, x);
  return {
    radius,
    theta,
    phi
  };
}

export function length2(vec: Vector3): number {
  return vec.x * vec.x + vec.y * vec.y + vec.z * vec.z;
}

export function length(vec: Vector3): number {
  return Math.sqrt(length2(vec));
}
export function distance(a: Vector3, b: Vector3): number {
  return length(sub(a, b));
}

export function normalize(vec: Vector3): Vector3 {
  const l = length(vec);
  return { x: vec.x / l, y: vec.y / l, z: vec.z / l };
}

export function invert(vec: Vector3): Vector3 {
  return { x: -vec.x, y: -vec.y, z: -vec.z };
}

export function add(a: Vector3, b: Vector3 | number): Vector3 {
  if (typeof b === 'number') {
    return { x: a.x + b, y: a.y + b, z: a.z + b };
  } else {
    return { x: a.x + b.x, y: a.y + b.y, z: a.z + b.z };
  }
}

export function sub(a: Vector3, b: Vector3): Vector3 {
  return { x: a.x - b.x, y: a.y - b.y, z: a.z - b.z };
}

export function abs(a: Vector3): Vector3 {
  return { x: Math.abs(a.x), y: Math.abs(a.y), z: Math.abs(a.z) };
}

export function fract(a: Vector3): Vector3 {
  return { x: Math2.fract(a.x), y: Math2.fract(a.y), z: Math2.fract(a.z) };
}

export function mul(a: Vector3, b: number | Vector3): Vector3 {
  if (typeof b === 'number') {
    return { x: a.x * b, y: a.y * b, z: a.z * b };
  } else {
    return { x: a.x * b.x, y: a.y * b.y, z: a.z * b.z };
  }
}
export function div(vec: Vector3, f: number | Vector3): Vector3 {
  if (typeof f === 'number') {
    return mul(vec, 1 / f);
  } else {
    return { x: vec.x / f.x, y: vec.y / f.y, z: vec.z / f.z };
  }
}

export function floor(vec: Vector3): Vector3 {
  return { x: Math.floor(vec.x), y: Math.floor(vec.y), z: Math.floor(vec.z) };
}

export function toGlVec3({ x, y, z }: Vector3): vec3 {
  return vec3.fromValues(x, y, z);
}
export function fromGlVec3(vec: vec3): Vector3 {
  return { x: vec[0], y: vec[1], z: vec[2] };
}

export function transform(vec: Vector3, mat: mat4): Vector3 {
  return fromGlVec3(vec3.transformMat4(vec3.create(), toGlVec3(vec), mat));
}
export function transformQuat(vec: Vector3, q: quat): Vector3 {
  return fromGlVec3(vec3.transformQuat(vec3.create(), toGlVec3(vec), q));
}

export function dot(a: Vector3, b: Vector3) {
  return a.x * b.x + a.y * b.y + a.z * b.z;
}
export function cross(a: Vector3, b: Vector3): Vector3 {
  return { x: a.y * b.z - a.z * b.y, y: a.z * b.x - a.x * b.z, z: a.x * b.y - a.y * b.x };
}

export function clamp({ x, y, z }: Vector3, min: number, max: number): Vector3 {
  return {
    x: Math2.clamp(x, min, max),
    y: Math2.clamp(y, min, max),
    z: Math2.clamp(z, min, max)
  };
}

export function mix(a: Vector3, b: Vector3, p: number | Vector3): Vector3 {
  if (typeof p === 'number') {
    return {
      x: Math2.mix(a.x, b.x, p),
      y: Math2.mix(a.y, b.y, p),
      z: Math2.mix(a.z, b.z, p),
    }
  } else {
    return {
      x: Math2.mix(a.x, b.x, p.x),
      y: Math2.mix(a.y, b.y, p.y),
      z: Math2.mix(a.z, b.z, p.z),
    }
  }
}

export function interpolate(x: number, x0: number, x1: number, y0: Vector3, y1: Vector3): Vector3;
export function interpolate(x: Vector3, x0: number, x1: number, y0: number, y1: number): Vector3;
export function interpolate(x: Vector3, x0: Vector3, x1: Vector3, y0: Vector3, y1: Vector3): Vector3;
export function interpolate(x: any, x0: any, x1: any, y0: any, y1: any): Vector3 {
  if (typeof x === 'number') {
    const p = (x - x0) / (x1 - x0);
    return mix(y0, y1, p);
  } else {
    if (typeof x0 === 'number') {
      return {
        x: Math2.interpolate(x.x, x0, x1, y0, y1),
        y: Math2.interpolate(x.y, x0, x1, y0, y1),
        z: Math2.interpolate(x.z, x0, x1, y0, y1),
      };
    } else {
      const p = div(sub(x, x0), sub(x1, x0));
      return mix(y0, y1, p);
    }
  }
}

export function interpolateClamped(x: number, x0: number, x1: number, y0: Vector3, y1: Vector3): Vector3;
export function interpolateClamped(x: Vector3, x0: Vector3, x1: Vector3, y0: Vector3, y1: Vector3): Vector3;
export function interpolateClamped(x: any, x0: any, x1: any, y0: Vector3, y1: Vector3): Vector3 {
  if (typeof x === 'number') {
    const p = Math2.clamp((x - x0) / (x1 - x0), 0, 1);
    return mix(y0, y1, p);
  } else {
    const p = clamp(div(sub(x, x0), sub(x1, x0)), 0, 1);
    return mix(y0, y1, p);
  }
}

export function isNaNorInf({ x, y, z }: Vector3): boolean {
  return isNaN(x) || isNaN(y) || isNaN(z) || !isFinite(x) || !isFinite(y) || !isFinite(z);
}

export function isEqual(a: Vector3, b: Vector3): boolean {
  return a.x === b.x && a.y === b.y && a.z === b.z;
}

export function boxVolume(box: Vector3) {
  return box.x * box.y * box.z;
}

export function boxSurfaceArea(box: Vector3) {
  return 2 * box.x * box.y + 2 * box.y * box.z + 2 * box.z * box.x;
}

export function triangleWave(vec: Vector3) {
  return {
    x: Math2.triangleWave(vec.x),
    y: Math2.triangleWave(vec.y),
    z: Math2.triangleWave(vec.z),
  }
}