import { computationShaderHeader } from "..";
import { Program } from "../Program";
import { createBatches } from "../Computation";
import { TextureSlicing, DoubleBufferedTextureSlicing } from "../Texture";
import { GlState, BlendMode } from "../GlState";
import { RenderTarget } from "../RenderTarget";

export type CopyType = 'set' | 'sum';

class CopyTextureForFormat {
  constructor(private gls: GlState, private format: 'float' | 'int' | 'uint') {}
  private renderTarget = new RenderTarget(this.gls);
  private pre = this.format === 'int' ? 'i' : this.format === 'uint' ? 'u' : '';
  private nLayers = this.gls.maxColorAttachements;
  private multi = new Program(this.gls, computationShaderHeader + `
    out ${this.pre}vec4 out_values[${this.nLayers}];

    uniform int layers[${this.nLayers}];
    uniform ${this.pre}sampler2DArray source;
    uniform ivec2 sourceOffset;

    void main() {
      ${
        new Array(this.nLayers).fill(0).map((x, i) => `
          out_values[${i}] = texelFetch(source, ivec3(cellPos + sourceOffset, layers[${i}]), 0);
        `).join('\n')
      }
    }
  `);
  private single = new Program(this.gls, computationShaderHeader + `
    out ${this.pre}vec4 out_value;

    uniform ${this.pre}sampler2D source;
    uniform ivec2 sourceOffset;

    void main() {
      out_value = texelFetch(source, cellPos + sourceOffset, 0);
    }
  `);
  copy(target: TextureSlicing, source: TextureSlicing, copyType: CopyType = 'set') {
    this.gls.prepareForComputation(target.rect, copyType === 'set' ? undefined : BlendMode.Sum);
    const sourceOffset = {
      x: source.rect.x - target.rect.x,
      y: source.rect.y - target.rect.y,
    }
    if (source.texture.is2dArray) {
      for (const { l, count } of createBatches(target.layers.length, this.nLayers)) {
        this.renderTarget.set(target.sliceRange(l, count));
        this.multi.setUniforms({
          source: {
            type: this.format === 'int' ? 'isampler2DArray' : this.format === 'uint' ? 'usampler2DArray' : 'sampler2DArray',
            value: source.texture
          },
          layers: {
            type: 'int[]',
            value: source.layers.slice(l, l + count)
          },
          sourceOffset: {
            type: 'ivec2',
            value: sourceOffset
          }
        });
        this.gls.drawQuad(this.multi);
      }
    } else {
      this.renderTarget.set(target);
      this.single.setUniforms({
        source: {
          type: this.format === 'int' ? 'isampler2D' : this.format === 'uint' ? 'usampler2D' : 'sampler2D',
          value: source.texture
        },
        sourceOffset: {
          type: 'ivec2',
          value: sourceOffset
        }
      });
      this.gls.drawQuad(this.single);
    }
  }
  copyBackToFront(texture: DoubleBufferedTextureSlicing) {
    this.copy(texture.front, texture.back);
  }
}
export class CopyTexture {
  constructor(private gls: GlState) {}
  formats = {
    float: new CopyTextureForFormat(this.gls, 'float'),
    int: new CopyTextureForFormat(this.gls, 'int'),
    uint: new CopyTextureForFormat(this.gls, 'uint')
  }
  copy(target: TextureSlicing, source: TextureSlicing, copyType?: CopyType) {
    this.formats[target.texture.formatType].copy(target, source, copyType);
  }
  copyBackToFront(texture: DoubleBufferedTextureSlicing) {
    this.formats[texture.texture.formatType].copyBackToFront(texture);
  }
}
