import * as React from 'react';
import { EditorDispatch, EditorProps, NonNullEditorDispatch } from './EditorProps';
import { LineListLayout, LineListLayoutItem } from './LineListLayout';
import './Editors.css';
import { DragAndDropList } from '../DragAndDropList';

interface ListEditorItemProps<T> extends EditorProps<T>, NonNullEditorDispatch<T> {
  selected: boolean;
}

interface ListEditorProps<T> extends EditorProps<T[]> {
  itemFactory: () => (T | Promise<T>);
  itemRenderer: (props: ListEditorItemProps<T>) => React.ReactNode;
  keyFromItem?: (item: T) => any;
  filter?: (item: T) => boolean;

  className?: string;
  onSelect?: (selected: number | undefined, totalCount: number) => void;
  selected?: number;
  renderReverse?: boolean;
  showInsertTop?: boolean;
  showInsertBottom?: boolean;
  acceptItemDrop?: (item: any) => boolean;
}


function ReverseListEditor<T>({ value = [], onChange, selected, onSelect, ...rest }: ListEditorProps<T> & EditorDispatch<T[]>) {
  return <ListEditor<T>
    value={[...value].reverse()}
    onChange={newValue => onChange(newValue !== undefined ? newValue.reverse() : undefined)}
    selected={selected !== undefined ? value.length - 1 - selected : undefined}
    onSelect={onSelect && ((index, totalCount) => onSelect(index !== undefined ? totalCount - 1 - index : undefined, totalCount))}
    {...rest}
    />
}

export class ListEditor<T> extends React.Component<ListEditorProps<T> & EditorDispatch<T[]>> {
  render() {
    const { onChange, readOnly, selected, renderReverse, showInsertTop = false, showInsertBottom = true } = this.props;
    if (renderReverse) {
      const { renderReverse: rr, ...props } = this.props;
      return <ReverseListEditor  {...props} />;
    }
    const list = this.props.value || [];
    return (
      <LineListLayout className={this.props.className}>
        {!readOnly && showInsertTop && <LineListLayoutItem
          isFirst={true}
          action={<button
            className="EditorActionButton ListEditorItemAction"
            onClick={async e => {
              e.preventDefault();
              const item = await this.props.itemFactory();
              if (!item) {
                return;
              }
              this.insertItems(0, true, item);
            }}>
            <i className="material-icons">add</i>
          </button>}
          />}
        <DragAndDropList
          value={this.props.value || []}
          onChange={onChange}
          keyFromItem={this.props.keyFromItem}
          onItemRemoved={this.handleItemRemoved}
          onItemInserted={this.handleItemInserted}
          acceptItem={this.props.acceptItemDrop}
          filter={this.props.filter}
          itemRenderer={(item, onItemChange, i) => (
            <LineListLayoutItem
              isFirst={!(!readOnly && showInsertTop) && i === 0}
              isLast={!(!readOnly && showInsertBottom) && i === list.length - 1}
              action={null}
              item={this.props.itemRenderer({
                readOnly,
                value: item,
                onChange: onItemChange,
                selected: selected === i,
              })}
              />
          )}
          />
        {!readOnly && showInsertBottom && <LineListLayoutItem
          isLast={true}
          action={<button
            className="EditorActionButton ListEditorItemAction"
            onClick={async e => {
              e.preventDefault();
              const item = await this.props.itemFactory();
              if (!item) {
                return;
              }
              this.insertItems(-1, true, item);
            }}>
            <i className="material-icons">add</i>
          </button>}
          />}
      </LineListLayout>
    );
  }

  handleItemRemoved = (index: number) => {
    const { onSelect, selected, value = [] } = this.props;
    if (selected && onSelect) {
      if (selected === index) {
        onSelect(undefined, value.length - 1);
      } else if (selected > index) {
        onSelect(selected - 1, value.length - 1);
      }
    }
  }

  handleItemInserted = (index: number) => {
    const { onSelect, selected, value = [] } = this.props;
    if (selected && onSelect) {
      if (selected >= index) {
        onSelect(selected + 1, value.length + 1);
      }
    }
  }

  private insertItems(i: number, selectNew: boolean, ...items: T[]) {
    if (items.length === 0) {
      return;
    }
    const list = this.props.value || [];
    if (i < 0) {
      i = list.length;
    }
    const newList = [...list.slice(0, i), ...items, ...list.slice(i)];
    this.props.onChange(newList);
    if (!this.props.onSelect) {
      return;
    }
    if (selectNew) {
      this.props.onSelect(i, newList.length);
    } else {
      this.handleItemInserted(i);
    }
  }

}
