import * as React from 'react';
import './Editors.css';
import { EditorTypedefResolver, EditorObnumVariants } from './EditorTypedefs';
import { EditorProps, EditorDispatch } from './EditorProps';
import { AnyEditor } from './AnyEditor';
import { EnumEditor } from './PrimitiveEditors';

interface ObnumEditorProps extends EditorProps<any> {
  variants: EditorObnumVariants;
  typedefResolver?: EditorTypedefResolver;
}

export function ObnumEditor({ className, onChange, readOnly, typedefResolver, value, variants }: ObnumEditorProps & EditorDispatch<any>): any {
  const key = value ? Object.keys(value)[0] : undefined;
  return (
    <div className={'ObnumEditor ' + (className || '')}>
      <EnumEditor
        readOnly={readOnly}
        value={key}
        variants={Object.keys(variants)}
        onChange={newKey => onChange({ [newKey]: variants[newKey].default })} />
      {value && key && variants[key] && (
        <AnyEditor
          readOnly={readOnly}
          value={value[key]}
          onChange={(v: any) => onChange({ [key]: v })}
          typedef={variants[key].itemType}
          typedefResolver={typedefResolver}
          />
      )}
    </div>
  );
}
