import { shaderModule, funcDef, uniformDef, ShaderModule, FunctionDef } from "../../GpuUtil/ShaderModule";
import { IronBubbable } from "../Buffs/Bubbles";
import { Abilities, CastingAbilityHelpers, abilitiesData } from "../Abilities/Abilities";
import { ArchetypesQuery } from "../../GpuUtil/ECS/Query";
import { Hitpoints, MaxHitpoints, UnitCategory, ImpactOwner, PreviousHitpoints, DiedStep, Gold, BountyComponents } from "./UnitComponents";
import { GlState } from "../../GpuUtil/GlState";
import { GpuWorldState } from "../State";
import { glslFloat } from "../../GpuUtil";
import { ThreeModel } from "../../GpuUtil/Model";
import ImpactGlb from '../../Models/Impact.glb';
import { EntityInit, Component, EntitiesRange } from "../../GpuUtil/ECS";
import { LocalToWorld, Position } from "./Transform";
import { MatrixSM, InterpolateSM } from "../../GpuUtil/SM2Utils";
import { ArenaInstance, ArenaMemberIndex, VisibilityFromComponent } from "./Components";
import { SideSize } from "../StateLayout";
import { UnitsInArena } from "./ArenaStats";

export const ImpactModel = ThreeModel.fromPath(ImpactGlb);

export async function createImpactEffects(state: GpuWorldState, owners: EntitiesRange) {
  const res = (await ImpactModel).createInstances(state, {
    count: owners.count,
    rootEntityInit: new EntityInit()
      .setComponent(ImpactOwner.component)
      .setComponent(VisibilityFromComponent, { value: { x: -1, y: -1 } }),
    visible: true,
  });
  for (let i=0; i < owners.count; i++) {
    const owner = owners.getIndex(i);
    state.ecs.setComponentData(ImpactOwner.component, res.roots.getIndex(i), [owner.x, owner.y]);
    state.ecs.setComponentData(VisibilityFromComponent, res.roots.getIndex(i), { value: owner });
  }
}

export const DamageCalcHelpers = () => shaderModule([
  IronBubbable.sm,
  CastingAbilityHelpers,
], {
  shellArmorBonus: uniformDef('float', () => abilitiesData.get(Abilities.Shell, 'Settings').activeArmor.value),
  ironBubbleArmorBonus: uniformDef('float', () => abilitiesData.get(Abilities.IronBubblegum, 'Settings').armor.value),
  armor: funcDef('float', [['ivec2', 'entityCell']], `
    return 1.0 +
      (${IronBubbable.sm.value}(entityCell) > 0 ? Self.ironBubbleArmorBonus : 0.0) +
      (${CastingAbilityHelpers.isPerforming}(entityCell, ${Abilities.Shell}) ? Self.shellArmorBonus : 0.0);
  `)
})

export class UpdateDestructible {
  constructor(private gls: GlState) {}

  damageTakenBounty = new ArchetypesQuery([Gold.component])
    .updateGpu(this.gls, [Gold.component], shaderModule([
      PreviousHitpoints.sm, Gold.writer, Gold.sm, Hitpoints.sm, UnitsInArena, ArenaInstance.sm, ArenaMemberIndex.sm,
      BountyComponents.damageTaken.sm, BountyComponents.statueDamageTaken.sm
    ], {
      update: funcDef('void', [['ivec2', 'entityCell']], `
        float hitpoints = ${Hitpoints.sm.value}(entityCell);
        float previousHitpoints = ${PreviousHitpoints.sm.value}(entityCell);
        float gold = ${Gold.sm.value}(entityCell);
        hitpoints = clamp(hitpoints, 0.0, ${glslFloat(MaxHitpoints)});
        int arenaMemberIndex = ${ArenaMemberIndex.sm.value}(entityCell);
        float hpLoss = max(0., previousHitpoints - hitpoints);
        gold += ${BountyComponents.damageTaken.sm.value}(entityCell) * hpLoss;
        int arenaIndex = ${ArenaInstance.sm.value}(entityCell);
        ivec2 statue = ${UnitsInArena.unit}(arenaIndex, int(arenaMemberIndex / ${SideSize}) * ${SideSize});
        float statueHp = ${Hitpoints.sm.value}(statue);
        float statuePreviousHp = ${PreviousHitpoints.sm.value}(statue);
        float statueHpLoss = max(0., statuePreviousHp - statueHp);
        gold += ${BountyComponents.statueDamageTaken.sm.value}(entityCell) * statueHpLoss;
        ${Gold.writer.write}(gold);
      `)
    }));

  query = new ArchetypesQuery([Hitpoints.component, PreviousHitpoints.component])
    .updateGpu(this.gls, [Hitpoints.component, PreviousHitpoints.component, DiedStep.component], shaderModule([
      PreviousHitpoints.sm,
      Hitpoints.writer,
      PreviousHitpoints.writer,
      DiedStep.writer,
      Hitpoints.sm,
      DiedStep.sm,
    ], {
      time: uniformDef('int', d => d.data.stepCounter),
      update: funcDef('void', [['ivec2', 'entityCell']], `
        float hitpoints = ${Hitpoints.sm.value}(entityCell);
        float previousHitpoints = ${PreviousHitpoints.sm.value}(entityCell);
        int diedStep = ${DiedStep.sm.value}(entityCell);
        if (hitpoints <= 0. && previousHitpoints > 0.) {
          diedStep = time;
        }
        hitpoints = clamp(hitpoints, 0.0, ${glslFloat(MaxHitpoints)});
        ${Hitpoints.writer.write}(hitpoints);
        ${PreviousHitpoints.writer.write}(hitpoints);
        ${DiedStep.writer.write}(diedStep);
      `)
    }));

  run(state: GpuWorldState) {
    this.damageTakenBounty.run(state);
    this.query.run(state);
  }
}

export function UpdateImpactEffect(gls: GlState) {
  return new ArchetypesQuery([ImpactOwner.component])
    .updateGpu(gls, [LocalToWorld.gpuComponent], shaderModule([
      ImpactOwner.sm, Hitpoints.sm, PreviousHitpoints.sm, Position.sm, LocalToWorld.writer, LocalToWorld.sm, InterpolateSM
    ], {
      update: funcDef('void', [['ivec2', 'entityCell']], `
        ivec2 owner = ${ImpactOwner.sm.value}(entityCell);
        float hitpoints = ${Hitpoints.sm.value}(owner);
        float previousHitpoints = ${PreviousHitpoints.sm.value}(owner);
        float ownerIncomingDamage = max(0., previousHitpoints - hitpoints);
        if (ownerIncomingDamage > 0.) {
          vec3 ownerPosition = ${Position.sm.value}(owner);
          ${LocalToWorld.writer.write}(${MatrixSM.translateScale}(
            ownerPosition + vec3(0, 0, 1),
            vec3(${InterpolateSM.interpolateClamped}(ownerIncomingDamage, 0., 100., 1., 7.))
          ));
        } else {
          mat4 localToWorld = ${LocalToWorld.sm.value}(entityCell);
          localToWorld[0][0] /= 1.4;
          localToWorld[1][1] /= 1.4;
          localToWorld[2][2] /= 1.4;
          ${LocalToWorld.writer.write}(localToWorld);
        }
      `)
    }))
}

export const DamageBounties = shaderModule('DamageBounties', [
  BountyComponents.damageEnemyStatue.sm, BountyComponents.damageEnemyUnit.sm,
  BountyComponents.friendlyFire.sm,
], {
  get: funcDef('float', [['ivec2', 'selfEntityCell'], ['int', 'selfArenaMemberIndex'], ['int', 'targetArenaMemberIndex']], `
    int selfTeam = selfArenaMemberIndex / ${SideSize};
    int targetTeam = targetArenaMemberIndex / ${SideSize};
    if (selfTeam != targetTeam) {
      bool targetIsStatue = targetArenaMemberIndex == 0 || targetArenaMemberIndex == ${SideSize};
      if (targetIsStatue) {
        return ${BountyComponents.damageEnemyStatue.sm.value}(selfEntityCell);
      } else {
        return ${BountyComponents.damageEnemyUnit.sm.value}(selfEntityCell);
      }
    } else {
      return ${BountyComponents.friendlyFire.sm.value}(selfEntityCell);
    }
  `)
});

export const KillBounties = shaderModule('KillBounties', [
  BountyComponents.killEnemyStatue.sm, BountyComponents.killEnemyUnit.sm
], {
  get: funcDef('float', [['ivec2', 'selfEntityCell'], ['int', 'selfArenaMemberIndex'], ['int', 'targetArenaMemberIndex']], `
    int selfTeam = selfArenaMemberIndex / ${SideSize};
    int targetTeam = targetArenaMemberIndex / ${SideSize};
    if (selfTeam != targetTeam) {
      bool targetIsStatue = targetArenaMemberIndex == 0 || targetArenaMemberIndex == ${SideSize};
      if (targetIsStatue) {
        return ${BountyComponents.killEnemyStatue.sm.value}(selfEntityCell);
      } else {
        return ${BountyComponents.killEnemyUnit.sm.value}(selfEntityCell);
      }
    } else {
      return 0.;
    }
  `)
});

export const HealBounties = shaderModule('HealBounties', [
  BountyComponents.healFriendlyStatue.sm, BountyComponents.healTeammate1.sm, BountyComponents.healTeammate2.sm,
  BountyComponents.healEnemy.sm,
], {
  get: funcDef('float', [['ivec2', 'selfEntityCell'], ['int', 'selfArenaMemberIndex'], ['int', 'targetArenaMemberIndex']], `
    int selfTeam = selfArenaMemberIndex / ${SideSize};
    int targetTeam = targetArenaMemberIndex / ${SideSize};
    if (selfTeam == targetTeam) {
      int selfIndex = selfTeam == 0 ? selfArenaMemberIndex : (selfArenaMemberIndex - ${SideSize});
      int targetIndex = targetTeam == 0 ? targetArenaMemberIndex : (targetArenaMemberIndex - ${SideSize});
      if (targetIndex > selfIndex) {
        targetIndex--;
      }
      return targetIndex == 0 ? ${BountyComponents.healFriendlyStatue.sm.value}(selfEntityCell) :
        targetIndex == 1 ? ${BountyComponents.healTeammate1.sm.value}(selfEntityCell) :
        targetIndex == 2 ? ${BountyComponents.healTeammate2.sm.value}(selfEntityCell) :
        0.;
    } else {
      return ${BountyComponents.healEnemy.sm.value}(selfEntityCell);
    }
  `)
})

export class SingleTargetDamage {
  constructor(private gls: GlState,
    private fromComponents: Component[],
    private sm: ShaderModule<{
      originator: FunctionDef<'ivec2', [['ivec2', 'entityCell']]>,
      getTarget: FunctionDef<'ivec2', [['ivec2', 'entityCell']]>,
      baseDamage: FunctionDef<'float', [['ivec2', 'entityCell']]>,
    }>) {}

  gold = new ArchetypesQuery(this.fromComponents)
    .updateGpu(this.gls, [Gold.component], shaderModule([
      this.sm,
      Gold.writer,
      DamageCalcHelpers(),
      Hitpoints.sm,
      UnitCategory,
      DamageBounties,
      KillBounties,
      ArenaMemberIndex.sm,
    ], {
      update: funcDef('ivec2', [['ivec2', 'entityCell']], `
        ivec2 target = ${this.sm.getTarget}(entityCell);
        if (target.x < 0) {
          return ivec2(-1);
        }
        float damage = ${this.sm.baseDamage}(entityCell) / ${DamageCalcHelpers().armor}(target);
        ivec2 originator = ${this.sm.originator}(entityCell);
        int originatorArenaMemberIndex = ${ArenaMemberIndex.sm.value}(originator);
        int targetArenaMemberIndex = ${ArenaMemberIndex.sm.value}(target);
        float damageBounty = ${DamageBounties.get}(originator, originatorArenaMemberIndex, targetArenaMemberIndex) * damage;
        float targetHitpoints = ${Hitpoints.sm.value}(target);
        float killBounty = (targetHitpoints - damage < 0.) ?
          ${KillBounties.get}(originator, originatorArenaMemberIndex, targetArenaMemberIndex) : 0.;
        ${Gold.writer.write}(damageBounty + killBounty);
        return originator;
      `)
    }), 'sum');

  damage = new ArchetypesQuery(this.fromComponents)
    .updateGpu(this.gls, [Hitpoints.component], shaderModule([
      this.sm,
      Hitpoints.sm,
      Hitpoints.writer,
      DamageCalcHelpers(),
    ], {
      update: funcDef('ivec2', [['ivec2', 'entityCell']], `
        ivec2 target = ${this.sm.getTarget}(entityCell);
        if (target.x < 0) {
          return ivec2(-1);
        }
        float damage = ${this.sm.baseDamage}(entityCell) / ${DamageCalcHelpers().armor}(target);
        ${Hitpoints.writer.write}(-damage);
        return target;
      `)
    }), 'sum');

  run(state: GpuWorldState) {
    this.gold.run(state);
    this.damage.run(state);
  }
}

export class SingleTargetHeal {
  constructor(private gls: GlState,
    private fromComponents: Component[],
    private sm: ShaderModule<{
      originator: FunctionDef<'ivec2', [['ivec2', 'entityCell']]>,
      getTarget: FunctionDef<'ivec2', [['ivec2', 'entityCell']]>,
      hitpoints: FunctionDef<'float', [['ivec2', 'entityCell']]>,
    }>) {}

  gold = new ArchetypesQuery(this.fromComponents)
    .updateGpu(this.gls, [Gold.component], shaderModule([
      this.sm,
      Gold.writer,
      Hitpoints.sm,
      UnitCategory,
      ArenaMemberIndex.sm,
      HealBounties,
    ], {
      update: funcDef('ivec2', [['ivec2', 'entityCell']], `
        ivec2 target = ${this.sm.getTarget}(entityCell);
        if (target.x < 0) {
          return ivec2(-1);
        }
        float heal = ${this.sm.hitpoints}(entityCell);
        float targetHitpoints = ${Hitpoints.sm.value}(target);
        float newTargetHitpoints = clamp(targetHitpoints + heal, 0., ${glslFloat(MaxHitpoints)});
        float healClamped = newTargetHitpoints - targetHitpoints;
        ivec2 originator = ${this.sm.originator}(entityCell);
        int originatorArenaMemberIndex = ${ArenaMemberIndex.sm.value}(originator);
        int targetArenaMemberIndex = ${ArenaMemberIndex.sm.value}(target);
        float bounty = ${HealBounties.get}(originator, originatorArenaMemberIndex, targetArenaMemberIndex) * healClamped;
        ${Gold.writer.write}(bounty);
        return originator;
      `)
    }), 'sum');

  heal = new ArchetypesQuery(this.fromComponents)
    .updateGpu(this.gls, [Hitpoints.component], shaderModule([
      this.sm,
      Hitpoints.sm,
      Hitpoints.writer,
    ], {
      update: funcDef('ivec2', [['ivec2', 'entityCell']], `
        ivec2 target = ${this.sm.getTarget}(entityCell);
        if (target.x < 0) {
          return ivec2(-1);
        }
        float heal = ${this.sm.hitpoints}(entityCell);
        ${Hitpoints.writer.write}(heal);
        return target;
      `)
    }), 'sum');

  run(state: GpuWorldState) {
    this.gold.run(state);
    this.heal.run(state);
  }
}