export function UserNameComponent({ userId, children }: { userId: string, children?: (name) => any }) {
  const user = useFirebaseStreamDocument<DbUserProfile>(Db.userProfiles, userId);
  if (user.status === 'loaded') {
      return <span>{user.value.cpu && <span><i className="fas fa-desktop" title="Cpu player"/>&nbsp;</span>}{user.value.displayName}</span>
  } else {
    return null;
  }
}


