export function BattleHistoryShort({ homeTeamId, awayTeamId, homeTeamName, awayTeamName }: { homeTeamId: string, awayTeamId: string, homeTeamName: string, awayTeamName: string }) {
  const homeBattles = useFirebaseStreamQuery<DbBattle>({
    collection: Db.battles,
    where: [['homeTeamId', '==', homeTeamId], ['awayTeamId', '==', awayTeamId]],
  });
  const awayBattles = useFirebaseStreamQuery<DbBattle>({
    collection: Db.battles,
    where: [['homeTeamId', '==', awayTeamId], ['awayTeamId', '==', homeTeamId]],
  });
  if (homeBattles.status !== 'loaded' || awayBattles.status !== 'loaded') {
    return <FirebaseLoading pending={[homeBattles, awayBattles]} />;
  }
  const homeHistory = homeBattles.values.map(x => ({
    result: x.winner === BattleWinner.HomeWin ? 'Win' :
      x.winner === BattleWinner.AwayWin ? 'Loss' :
      x.winner === BattleWinner.Tie ? 'Tie' :
      'Undecided',
    created: x.created.toDate()
  }));
  const awayHistory = awayBattles.values.map(x => ({
    result: x.winner === BattleWinner.AwayWin ? 'Win' :
      x.winner === BattleWinner.HomeWin ? 'Loss' :
      x.winner === BattleWinner.Tie ? 'Tie' :
      'Undecided',
    created: x.created.toDate()
  }));
  const history = [...homeHistory, ...awayHistory].sort((a, b) => a.created > b.created ? -1 : 1);
  const homeWins = homeBattles.values.reduce((p, x) => p + (x.winner === BattleWinner.HomeWin ? 1 : 0), 0);
  const awayWins = awayBattles.values.reduce((p, x) => p + (x.winner === BattleWinner.AwayWin ? 1 : 0), 0);
  const homeLosses = homeBattles.values.reduce((p, x) => p + (x.winner === BattleWinner.AwayWin ? 1 : 0), 0);
  const awayLosses = awayBattles.values.reduce((p, x) => p + (x.winner === BattleWinner.HomeWin ? 1 : 0), 0);
  return <div>
    <div>{homeTeamName} vs {awayTeamName}: {homeWins + awayWins} wins, {homeLosses + awayLosses} losses</div>
    <div><small>{history.map(x => x.result).join(' • ')}</small></div>
  </div>;
}

export function FormatRating({ value }: { value: number }) {
  return <span title={'' + value}>{Math.round(value)}</span>;
}

