function SignupSigninButton() {
  const [showSignInPopup, setShowSignInPopup] = React.useState<boolean>(false);
  return <>
    <Button onClick={() => {
      if (version.backend === 'production') {
        setShowSignInPopup(true);
      } else {
        const ret = `https://${window.location.host}/login/`;
        window.location.href = `https://app.derkgame.com/gettesttoken?redirect=${encodeURIComponent(ret)}`;
      }
    }}>Sign up / sign in</Button>
    {showSignInPopup && (
      <Popup onClose={() => setShowSignInPopup(false)}>
        <h1>Sign in</h1>
        <FirebaseLogin onSignedIn={user => setShowSignInPopup(false)} />
      </Popup>
    )}
  </>
}

