function LoginWithTokenPage({ token }: { token: string }) {
  const [status, setStatus] = React.useState<string>('Logging in...');
  React.useEffect(() => {
    (async () => {
      await firebase.auth().signInWithCustomToken(token);
      setStatus('Logged in, redirecting....');
      RoutingHistory.replace('/');
    })()
  }, [token]);
  return <div>
    {status}
  </div>
}

function LoginWithEmailPassword({ email, password }: { email: string, password: string }) {
  const [status, setStatus] = React.useState<string>('Logging in...');
  React.useEffect(() => {
    (async () => {
      await firebase.auth().signInWithEmailAndPassword(email, password);
      setStatus('Logged in, redirecting....');
      RoutingHistory.replace('/');
    })();
  }, [email, password]);
  return <div>
    {status}
  </div>
}

function GetTestServerTokenPage({ redirect }: { redirect: string }) {
  const loggedInUser = React.useContext(LoggedInUserContext);
  React.useEffect(() => {
    (async () => {
      if (loggedInUser) {
        const result = await firebase.functions().httpsCallable('getTestingLoginToken')();
        window.location.href = `${redirect}${result.data.token}`;
      }
    })();
  }, [redirect, loggedInUser]);
  if (!loggedInUser) {
    return <div>
      <FirebaseLogin />
    </div>
  }
  return <div>
    Getting test server token...
  </div>
}

class SteamOverlayRefresher extends React.Component {
  canvas1 = React.createRef<HTMLCanvasElement>();
  canvas2 = React.createRef<HTMLCanvasElement>();
  stop = false;
  componentDidMount() {
    requestAnimationFrame(this.tick);
  }
  componentWillUnmount() {
    this.stop = true;
  }
  tick = () => {
    if (this.canvas1.current) {
      this.canvas1.current.getContext('2d')!.clearRect(0, 0, 1, 1);
    }
    if (this.canvas2.current) {
      this.canvas2.current.getContext('2d')!.clearRect(0, 0, 1, 1);
    }
    if (!this.stop) {
      requestAnimationFrame(this.tick);
    }
  }
  render() {
    return <div className="SteamOverlayRefresher">
      <canvas ref={this.canvas1} width={1} height={1} style={{ position: 'fixed', left: 0, top: 0 }} />
      <canvas ref={this.canvas2} width={1} height={1} style={{ position: 'fixed', bottom: 0, right: 0 }} />
    </div>;
  }
}

export interface ChangelogEntry {
  version: string;
  date: Date;
  link: string;
  title: string;
  body: string;
}
async function getChangelog(path: string) {
  const resp = await fetch(path);
  let content = await resp.text();
  content = content.replace('/Images/EnergyModel05.png', EnergyModel05);
  content = content.replace('/Images/EnergyModel052.png', EnergyModel052);
  content = content.replace(/\r\n|\r|\n/g, '\n');
  const re = /\$\$[ ]+([^ ]+)[ ]+([^ ^\n]+)([ ]+[^ ^\n].+)?\n(.*)?\n([^$]*)?/g;
  let match;

  const entries: ChangelogEntry[] = [];
  // eslint-disable-next-line no-cond-assign
  while (match = re.exec(content)) {
    entries.push({
      version: match[1],
      date: new Date(Date.parse(match[2])),
      link: match[3],
      title: match[4],
      body: match[5],
    });
  }
  return entries;
}
export function WhatsNewPopup() {
  const { changelog } = useAppStateSelect('changelog');
  const loggedInUser = React.useContext(LoggedInUserContext);
  const userSettings = useFirebaseStreamDocument<DbUserSettings>(Db.userSettings, loggedInUser?.uid);
  const [show, setShow] = React.useState(false);
  React.useEffect(() => {
    (async () => {
      if (!version.isDevelopment && userSettings.status === 'loaded') {
        const last = changelog[0];
        const doShow = last && last.date > userSettings.value.whatsNewDate.toDate();
        console.log(`[WhatsNewPopup] changelog=${last?.date} userSettings=${userSettings.value.whatsNewDate.toDate()} show=${doShow}`);
        setShow(!!doShow);
      }
    })()
  }, [userSettings, changelog]);
  if (!show || !loggedInUser) {
    return null;
  }
  return <Popup fullscreen={false} onClose={() => {
    firebase.firestore().collection(Db.userSettings).doc(loggedInUser.uid)
      .update(partial<DbUserSettings>({ whatsNewDate: firebase.firestore.Timestamp.now() }));
    }}>
    <ScrollVertical className="WhatsNew">
      <h2>What's new</h2>
      <Changelog />
    </ScrollVertical>
  </Popup>;
}

export function Changelog() {
  const { changelog } = useAppStateSelect('changelog');
  return <div className="Changelog">{changelog.map((entry, i) => <div className="ChangelogEntry" key={i}>
    {entry.version !== '-' ? <span className="ChangelogVersion">{entry.version}</span> : ''}
    {entry.link ? <a href={entry.link} target="_blank" rel="noopener noreferrer"><i className="fab fa-youtube" /> {entry.title}</a> : entry.title} <small>{moment(entry.date).fromNow()}</small>
    <ReactMarkdown source={entry.body} className="ChangelogBody" />
  </div>)}</div>;
}


