function SteamLandingPage({ ticket, appid }: { ticket: string, appid: string }) {
  const [status, setStatus] = React.useState('Logging in (Waiting for ticket)...');
  React.useEffect(() => {
    (async () => {
      if (ticket) {
        setStatus('Logging in (Fetching token)...');
        let result;
        try {
          result = await firebase.functions().httpsCallable('steamLogin')({
            steamTicket: ticket,
            appid
          });
        } catch (err) {
          Sentry.captureException(err);
          setStatus(`Steam login call failed`);
          return;
        }
        if (result.data.status !== 'ok') {
          setStatus(result.data.error);
          return;
        }
        setStatus(`Logging in (Signing in)...`);
        await firebase.auth().signInWithCustomToken(result.data.firebaseToken);
        setStatus(`Logged in! Redirecting...`);
        RoutingHistory.replace('/');
      }
    })()
  }, [ticket, appid]);
  return <SteamLoading msg={status} />;
}


export function BattleNotifTime({ time }: { time: Date }) {
  const [, forceUpdate] = React.useState();
  useInterval(() => forceUpdate({} as any), 60*1000);
  if (Date.now() - time.getTime() < (60*1000)) {
    return <span>now</span>;
  } else {
    return <span>{moment(time).fromNow()}</span>;
  }
}

function BattleNotifContent({ battleId }: { battleId: string }) {
  const battle = useFirebaseStreamDocument<DbBattle>(Db.battles, battleId);
  if (battle.status !== 'loaded') {
    return <FirebaseLoading pending={[battle]} />;
  }
  return <Column className="BattleNotifContent">
    <small><BattleNotifTime time={battle.value.created.toDate()} /> • {capitalizeFirstLetter(battle.value.battleground)}</small>
    <Block><b><UserNameComponent userId={parseTeamId(battle.value.homeTeamId).userId} /></b></Block>
    <Block>{battle.value.homeScore} - {battle.value.awayScore}</Block>
    {battle.value.winner === BattleWinner.Undecided && <Block><i className="fas fa-hourglass-half" /> In progress...</Block>}
    {battle.value.winner === BattleWinner.AwayWin && <Block><b><i className="fas fa-trophy" /> You won!</b></Block>}
    {battle.value.winner === BattleWinner.HomeWin && <Block><i className="fas fa-frown" /> You lost</Block>}
    {battle.value.winner === BattleWinner.Tie && <Block><i className="fas fa-times" /> Tied match</Block>}
  </Column>
}

async function getLatestAppBuild() {
  const r = await fetch('/index.html');
  const content = await r.text();
  const buildRegex = /<meta name="pixling-world-build" content="([^"]*)">/g;
  const buildMatch = buildRegex.exec(content);
  if (buildMatch) {
    return buildMatch[1];
  } else {
    return undefined;
  }
}

function Notifications() {
  const loggedInUser = React.useContext(LoggedInUserContext);
  const [newVersion, setNewVersion] = React.useState<string | undefined>();
  const notifs = useFirebaseStreamQuery<DbNotification>({
    collection: Db.notifications,
    where: [['userId', '==', loggedInUser ? loggedInUser.uid : 'no-user']]
  });
  useInterval(() => {
    if (version.isDevelopment) {
      return;
    }
    (async () => {
      console.log('Checking for new build...');
      const build = await getLatestAppBuild();
      console.log('Latest build: ', build);
      if (build && build !== version.build) {
        // Sometimes the server respondes with a cached file
        const s = build.split('_');
        const newDate = new Date(s[1] + ' ' + s[2]);
        const olds = version.date.split('_');
        const oldDate = new Date(olds[0] + ' ' + olds[1]);
        if (newDate > oldDate) {
          setNewVersion(build);
        }
      }
    })();
  }, 1000 * 60 * 10);
  if (notifs.status !== 'loaded') {
    return null;
  }
  return <div className="Notifications">
    {notifs.values.map(notif => {
      if (notif.type === 'battle') {
        return <Panel key={notif.id} className="BattleNotif"
            title="Battle!" onDelete={() => db.collection(Db.notifications).doc(notif.id).delete()}>
          <BattleNotifContent battleId={notif.battleId} />
        </Panel>;
      } else {
        return <Panel key={notif.id} className="BattleNotif"
            onDelete={() => db.collection(Db.notifications).doc(notif.id).delete()}>
          {notif.value}
        </Panel>;
      }
    })}
    {newVersion && <Panel title="Update" className="NewBuildNotif" onDelete={() => setNewVersion(undefined)}>
      <h3>New version available!</h3>
      <div><small>{newVersion}</small></div>
      <div><small><i className="fas fa-arrow-alt-circle-up" /></small></div>
      <div><small>{version.build}</small></div>
      <Section>
        <Button primary={true} onClick={() => window.location.reload()}>Reload</Button>
      </Section>
    </Panel>}
    {notifs.values.length > 0 && <Button onPerform={async () => {
      for (const { l, count } of createBatches(notifs.values.length, 500)) {
        const batch = firebase.firestore().batch();
        for (let i=0; i < count; i++) {
          batch.delete(db.collection(Db.notifications).doc(notifs.values[i + l].id));
        }
        batch.commit();
      }
    }}>Close all notifications ({notifs.values.length})</Button>}
  </div>
}

function useFirebaseLoggedInUser() {
  const [user, setUser] = React.useState<firebase.User | null>(null);
  const [loadingUser, setLoadingUser] = React.useState(true);
  const appState = React.useContext(AppStateContext);
  React.useEffect(() => {
    const updateUser = (usr: firebase.User | null) => {
      console.log('Logged in user changed to:', usr?.uid);
      setLoadingUser(false);
      if (usr) {
        Sentry.setUser({
          email: usr.email || undefined,
          id: usr.uid
        });
        // Because of https://github.com/firebase/firebase-functions/issues/95
        const profileDoc = firebase.firestore().collection(Db.userProfiles).doc(usr.uid);
        profileDoc.get().then(p => {
          if (!p.exists) {
            return;
          }
          const profile = p.data() as DbUserProfile;
          if (!profile.displayName && usr.displayName) {
            profileDoc.update(partial<DbUserProfile>({
              displayName: usr.displayName
            }));
          }
        });
      }
      setUser(usr);
      appState.update({ loggedInUserId: usr ? usr.uid : undefined });
    };
    const unsub = firebase.auth().onAuthStateChanged(updateUser);
    return () => {
      unsub();
    }
  }, [appState]);
  return { user, loadingUser };
}

function NewsletterSignup() {
  const [email, setEmail] = React.useState('');
  return <div>
    <input type="text" value={email} onChange={e => setEmail(e.target.value)} placeholder="your@email.com"/>
    <Button
      onPerform={async () => {
        await firebase.functions().httpsCallable('subscribeToNewsletter')({ email });
        setEmail('');
      }}
      disabled={!email}
      primary={true}>
        Subscribe to newsletter
    </Button>
  </div>
}

export function IssuesPanel({ loggedInUser }: { loggedInUser: firebase.User }) {
  const [show, setShow] = React.useState(false); // version.commit !== 'development'
  return <Panel mode={PanelMode.Floating} className={`IssuesPanel ${show ? 'IssuesPanelShow' : 'IssuesPanelMinimized'}`} title="Issues" onDelete={!show ? undefined : () => setShow(false)}>
    {show ? <Issues loggedInUser={loggedInUser} tag="steam" /> : <Column>
      <FontAwesomeButton className="IssuePanelShowAll"
        icon="book-open"
        title="Show all issues"
        flat={true} onClick={() => setShow(true)}><span className="IssuesPanelShowAllTitle">View issues</span></FontAwesomeButton>
    </Column>}
    <Column className="IssuesNew">
      <small className="IssuesPanelNewTitle">New issue</small>
      <NewIssue loggedInUser={loggedInUser} tags={['steam']} onStartNewIssue={() => setShow(true)} />
    </Column>
  </Panel>;
}


