import { EntityComponentSystem, EntityInit } from "../../GpuUtil/ECS";
import { GpuWorldState } from "../State";
import { LocalToWorld, Position, Quaternion, Scale, InvLocalToWorld } from "./Transform";
import { quat, mat4 } from "gl-matrix";
import { EntitiesQuery } from "../../GpuUtil/ECS/Query";
import { AnimationControllerComponent } from "./Animation";
import { VisibleComponent } from "./Components";

export const Camera = {
  Fovy: EntityComponentSystem.registerComponent({
    name: 'CameraFovy',
    type: 'cpu',
    format: 'float',
  }),
  Near: EntityComponentSystem.registerComponent({
    name: 'CameraNear',
    type: 'cpu',
    format: 'float',
  }),
  Far: EntityComponentSystem.registerComponent({
    name: 'CameraFar',
    type: 'cpu',
    format: 'float',
  }),
  Projection: EntityComponentSystem.registerComponent({
    name: 'CameraProjection',
    type: 'cpu',
    format: 'mat4',
  }),
  ProjectionView: EntityComponentSystem.registerComponent({
    name: 'CameraProjectionView',
    type: 'cpu',
    format: 'mat4',
  }),
  InvProjectionView: EntityComponentSystem.registerComponent({
    name: 'CameraInvProjectionView',
    type: 'cpu',
    format: 'mat4',
  }),
}

export function createCamera(state: GpuWorldState) {
  return state.ecs.createEntities(new EntityInit()
    .setComponent(Position.cpuComponent)
    .setComponent(Quaternion.cpuComponent, new Float32Array(quat.identity(quat.create())))
    .setComponent(Scale.cpuComponent, new Float32Array([1, 1, 1]))
    .setComponent(LocalToWorld.cpuComponent)
    .setComponent(InvLocalToWorld.cpuComponent)
    .setComponent(Camera.Fovy, new Float32Array([(70 / 360) * Math.PI * 2]))
    .setComponent(Camera.Near, new Float32Array([0.1]))
    .setComponent(Camera.Far, new Float32Array([850]))
    .setComponent(Camera.Projection)
    .setComponent(Camera.ProjectionView)
    .setComponent(Camera.InvProjectionView)
    .setComponent(AnimationControllerComponent, { actions: [] })
    .setComponent(VisibleComponent, true)
  ).getIndex(0);
}

export class UpdateCameraMatrices {
  query = new EntitiesQuery([Camera.Projection]);
  run(state: GpuWorldState) {
    for (const entityCell of this.query.get(state)) {
      const projection = state.ecs.getCpuComponentDataView(Camera.Projection, entityCell);
      const fovy = state.ecs.getComponentData(Camera.Fovy, entityCell);
      const near = state.ecs.getComponentData(Camera.Near, entityCell);
      const far = state.ecs.getComponentData(Camera.Far, entityCell);
      const aspect = state.data.canvasRect.width / state.data.canvasRect.height;
      if (projection) {
        mat4.perspective(projection, fovy[0], aspect, near[0], far[0]);
        const view = state.ecs.getCpuComponentDataView(InvLocalToWorld.cpuComponent, entityCell);
        const projectionView = state.ecs.getCpuComponentDataView(Camera.ProjectionView, entityCell);
        if (projectionView && view) {
          mat4.mul(projectionView, projection, view);

          const invProjectionView = state.ecs.getCpuComponentDataView(Camera.InvProjectionView, entityCell);
          if (invProjectionView) {
            mat4.invert(invProjectionView, projectionView);
          }
        }
      }
    }
  }
}
