import * as React from 'react';
import './Editors.css';
import { EditorProps, EditorDispatch } from './EditorProps';
import { EditorStructProperties } from './EditorTypedefs';
import { arrayReplaceIndex, arrayRemoveIndex } from '../../Util';
import { AnyEditor } from '.';
import { MaterialIconButton } from '../Button';

interface TableEditorProps<T> extends EditorProps<T[]> {
  itemProperties: EditorStructProperties;
  itemPropertiesLabels?: { [key: string]: string };
  newItem: () => T;
}

export class TableEditor<T> extends React.Component<TableEditorProps<T> & EditorDispatch<T[]>> {
  render() {
    const { value = [], onChange, itemProperties, itemPropertiesLabels, newItem } = this.props;
    return <table className="TableEditor">
      <tbody>
        <tr>
          {Object.keys(itemProperties).map((key, i) => (
            <th key={i}>{itemPropertiesLabels && itemPropertiesLabels[key] ? itemPropertiesLabels[key] : key}</th>
          ))}
        </tr>
        {value.map((v, i) => (
          <tr key={i}>
            {Object.keys(itemProperties).map((key, j) => (
              <td key={j}>
                <AnyEditor 
                  typedef={itemProperties[key]}
                  value={v[key]}
                  onChange={newValue => onChange(arrayReplaceIndex(value, i, {...(v as any), [key]: newValue}))}
                  />
              </td>
            ))}
            <td><MaterialIconButton flat={true} icon="delete" purpose="Delete" onClick={() => onChange(arrayRemoveIndex(value, i))} /></td>
          </tr>
        ))}
        <tr>
          <td><MaterialIconButton flat={true} icon="add" purpose="Add" onClick={() => onChange([...value, newItem()])} /></td>
        </tr>
      </tbody>
    </table>
  }
}
