import { RandomSeedable, EventDispatcher, arrayReplaceIndex, keysOfEnum } from '../Util';
import { sizeClosestPow2 } from '../Math/Math';
import { Texture, destroyTextures, DoubleBufferedTexture } from '../GpuUtil/Texture';
import { GpuWorldStateData, defaultGpuWorldStateData, TeamSettings } from './StateData';
import { InitWorldStateConfig } from './StateInit';
import { GpuWorldStateDerived } from './StateDerived';
import { Sampler } from '../GpuUtil/Program';
import { GlState, TextureFiltering } from '../GpuUtil/GlState';
import HeightmapExr from '../Models/ArenaHeightmap/Image0000.exr';
import NormalmapExr from '../Models/ArenaNormals/Image0000.exr';
import { GameMap, MapInstance } from './Map';
import { EntityComponentSystem, EntityInit } from '../GpuUtil/ECS';
import { EntitiesQuery } from '../GpuUtil/ECS/Query';
import { Unit, Creature, BountyComponents } from './ECS/UnitComponents';
import { Arena, ArenaInstance, ArenaMemberIndex } from './ECS/Components';
import { Image } from '../GpuUtil/Image';
import { ArenaMembersSize, BrainOutputsKeys, StepsInBattle, SideSize } from './StateLayout';
import { Sense } from './Brain/SensesDef';
import { UnitReplayChannelsSlices } from './Tools/Replay';
import { DecisionsVizHistory } from './Renderer/DecisionsViz';
import { GameLoop } from './GameLoop';
import { createCamera } from './ECS/Camera';
import { AppState } from '../SteamApp/AppState';
import { Store } from '../Components/Store';
import { AnimationTrackInterpolator } from './ECS/AnimationTrack';

const Heightmap = Image.fromEXRUrl(HeightmapExr);
const Normalmap = Image.fromEXRUrl(NormalmapExr);

export interface GpuWorldStateEventDataChanged {
  type: 'DataChanged';
  before: GpuWorldStateData;
  after: GpuWorldStateData;
  change: Partial<GpuWorldStateData>;
}
export interface GpuWorldStateEventRoundEnd {
  type: 'RoundEnd';
  round: number;
}
export interface GpuWorldStateEventCamera {
  type: 'Camera';
  delta: number;
}

export class GpuWorldState {
  constructor(public gls: GlState, public initConfig: InitWorldStateConfig, public gameMap?: GameMap,
      public appState?: Store<AppState>, public ecs = new EntityComponentSystem(gls)) {
    for (const k of Object.keys(this.textures)) {
      this.textures[k].name = k;
    }

    if (this.gameMap) {
      this.gameMap.createEntities(this);
    }
    for (let i=0; i < this.layout.arenas; i++) {
      this.ecs.createEntities(new EntityInit()
        .setComponents(Arena.components, new Float32Array([i])))
    }
    this.setData({
      camera: createCamera(this)
    });
    if (appState) {
      appState.onChange.attach(this.handleAppStateChange);
    }

    console.log(`[GpuWorldState] Map entities created`);
  }
  static async createWithMap(gls: GlState, initConfig: InitWorldStateConfig, appState?: Store<AppState>, ecs?: EntityComponentSystem) {
    const state = new GpuWorldState(gls, initConfig, await MapInstance, appState, ecs);
    const heightmap = await Heightmap;
    const normalmap = await Normalmap;
    state.textures.heightmap.loadImage(heightmap);
    state.textures.normalmap.loadImage(normalmap);
    return state;
  }
  get gl() { return this.gls.gl; }
  destroyed = false;
  destroy(destroyEcs = true) {
    destroyTextures(this.textures);
    if (destroyEcs) {
      this.ecs.destroy();
    }
    this.unitsInArenaGpu.destroy();
    this.textures = undefined as any;
    for (const key of Object.keys(this.events)) {
      this.events[key].listeners = [];
    }
    if (this.appState) {
      this.appState.onChange.detach(this.handleAppStateChange);
    }
    this.destroyed = true;
  }
  handleAppStateChange = (appData: AppState, change: Partial<AppState>) => {
    if (change.creatures) {
      updateUnitBounties(this);
    }
  }

  random = new RandomSeedable();

  private currentData = defaultGpuWorldStateData(this.initConfig);
  public get data() { return this.currentData }
  public setData(newData: Partial<GpuWorldStateData>) {
    const oldData = this.currentData;
    this.currentData = {...this.currentData, ...newData};
    this.events.dataChanged.dispatch({ type: 'DataChanged', before: oldData, after: this.currentData, change: newData });
  }
  events = {
    dataChanged: new EventDispatcher<[GpuWorldStateEventDataChanged]>(),
    step: new EventDispatcher(),
    render: new EventDispatcher(),
    camera: new EventDispatcher<[GpuWorldStateEventCamera]>(),
    roundEnd: new EventDispatcher<[GpuWorldStateEventRoundEnd]>(),
  };
  gameLoop?: GameLoop;

  public nextStep() {
    return new Promise((resolve) => {
      const listener = () => {
        this.events.step.detach(listener);
        resolve();
      }
      this.events.step.attach(listener);
    })
  }
  public async nextStepIfRunning() {
    if (this.derived.simulationRunning) {
      await this.nextStep();
    }
  }

  updateTeam(team: 'home' | 'away', value: Partial<TeamSettings>) {
    this.setData({
      teams: {
        ...this.data.teams,
        [team]: {
          ...this.data.teams[team],
          ...value
        }
      }
    });
  }
  log(category: string, message: string | string[]) {
    const index = this.data.log.length;
    this.setData({ log: [...this.data.log, {
      time: new Date(),
      step: this.data.stepCounter,
      category,
      message: typeof message === 'string' ? [message] : message }
    ] });
    return index;
  }
  updateLogPart(index: number, part: number, message: string) {
    const newLog = [...this.data.log];
    newLog[index] = {
      time: new Date(),
      step: newLog[index].step,
      category: newLog[index].category,
      message: arrayReplaceIndex(newLog[index].message, part, message)
    }
    this.setData({ log: newLog });
  }

  public get worldSize() {
    return this.layout.worldSize;
  }
  worldPow2Size = sizeClosestPow2(this.layout.worldSize);

  get layout() {
    return this.data.layout;
  }

  derived = new GpuWorldStateDerived(this);

  ensureRendered() {
    if (!this.data.ensureRenderNextLoop) {
      this.setData({ ensureRenderNextLoop: true });
    }
  }

  textures = {
    heightmap: new Texture(this.gls, this.gl.RGBA32F).init(),
    normalmap: new Texture(this.gls, this.gl.RGBA32F).init(),
    // curiosity: DoubleBufferedTexture.create(this.gls, this.gl.RGBA32F, { width: this.layout.arenas, height: CuriosityHistory }, CuriosityDepth).init(),
    sensesHistogram: new Texture(this.gls, this.gl.RGBA32F, { width: ArenaMembersSize, height: keysOfEnum(Sense).length }, 8).init(),
    decisionsHistogram: new Texture(this.gls, this.gl.RGBA32F, { width: ArenaMembersSize, height: BrainOutputsKeys.length }, 8).init(),
    unitsReplay: DoubleBufferedTexture.create(this.gls, this.gl.RGBA32F, { width: StepsInBattle, height: ArenaMembersSize }, UnitReplayChannelsSlices).init(),
    decisionsViz: DoubleBufferedTexture.create(this.gls, this.gl.R32F, { width: DecisionsVizHistory, height: ArenaMembersSize * BrainOutputsKeys.length }).init(),
  };

  samplers = {
    heightmap: {
      linear: new Sampler(this.textures.heightmap, TextureFiltering.Linear)
    },
  };

  // Indices
  unitsInArena = new EntitiesQuery(
    [ArenaInstance.cpuComponent, ArenaMemberIndex.cpuComponent, Unit.component])
    .lookupTable(ArenaMembersSize, (state, entityCell) => {
      const arenaIndex = state.ecs.getComponentData(ArenaInstance.cpuComponent, entityCell)[0];
      const arenaMemberIndex = state.ecs.getComponentData(ArenaMemberIndex.cpuComponent, entityCell)[0];
      return { row: arenaIndex, column: arenaMemberIndex };
    });
  unitsInArenaGpu = this.unitsInArena.toGpu(this.gls);

  animationInterpolators: { [key: string]: AnimationTrackInterpolator } = {};
}

export function updateUnitBounties(worldState: GpuWorldState) {
  const creatures = worldState.appState?.state.creatures;
  let empty = true;
  for (let i=0; i < SideSize * 2; i++) {
    const unit = i < SideSize ? worldState.data.teams.home.units[i] : worldState.data.teams.away.units[i - SideSize];
    const units = new EntitiesQuery([Creature.component])
      .filterValue(ArenaMemberIndex.cpuComponent, v => v[0] === i, () => [])
      .get(worldState);
    if (unit && unit.type === 'creature' && creatures && creatures.status === 'loaded') {
      const c = creatures.values.find(x => x.id === unit.id);
      if (c && c.bounties) {
        for (const key of Object.keys(c.bounties)) {
          for (const unit of units) {
            worldState.ecs.setComponentData(BountyComponents[key].component, unit, [c.bounties?.[key] ?? 0]);
          }
          empty = false;
        }
      }
    }
  }
  worldState.setData({
    unitBountiesEmpty: empty
  })
}
