import * as THREE from "three";

export interface AnimationTrack {
  type: 'vec3' | 'quat';
  targetName?: string;
  componentName: string;
  times: number[];
  values: number[];
}

export function stride(track: AnimationTrack) {
  return track.type === 'vec3' ? 3 : 4;
}
export function keyframeValue(track: AnimationTrack, index: number): number[] {
  return track.values.slice(index * stride(track), (index + 1) * stride(track));
}
export function keyframeTime(track: AnimationTrack, index: number): number {
  return track.times[index];
}
export function insertKeyframe(track: AnimationTrack, index: number, time: number, value: number[] | Float32Array) {
  if (value.length !== stride(track)) {
    throw new Error(`Invalid value length: ${value.length}. Must be ${stride(track)}`);
  }
  return {
    ...track,
    times: [...track.times.slice(0, index), time, ...track.times.slice(index)],
    values: [...track.values.slice(0, index * stride(track)), ...value, ...track.values.slice(index * stride(track))]
  };
}
export function insertKeyframeAtTime(track: AnimationTrack, time: number, value: number[] | Float32Array) {
  let index = 0;
  while (index < track.times.length && track.times[index] < time) {
    index++;
  }
  return insertKeyframe(track, index, time, value);
}
export function moveKeyframe(track: AnimationTrack, index: number, newTime: number) {
  return insertKeyframeAtTime(deleteKeyframe(track, index), newTime, keyframeValue(track, index));
}
export function copyKeyframe(track: AnimationTrack, index: number, newTime: number) {
  return insertKeyframeAtTime(track, newTime, keyframeValue(track, index));
}
export function deleteKeyframe(track: AnimationTrack, index: number) {
  return {
    ...track,
    times: [...track.times.slice(0, index), ...track.times.slice(index + 1)],
    values: [...track.values.slice(0, index * stride(track)), ...track.values.slice((index + 1) * stride(track))]
  };
}

export class AnimationTrackInterpolator {
  constructor(public track: AnimationTrack) {
  }
  private results = new Float32Array(stride(this.track));
  private interpolant = this.track.type === 'vec3' ?
    new THREE.LinearInterpolant(new Float32Array(this.track.times), new Float32Array(this.track.values), stride(this.track), this.results)
    : new THREE.QuaternionLinearInterpolant(new Float32Array(this.track.times), new Float32Array(this.track.values), stride(this.track), this.results);
  value(time: number): number[] {
    return this.interpolant!.evaluate(time);
  }
}
