import * as React from 'react';
import './index.css';
import '@fortawesome/fontawesome-free/css/all.css';
import { WonkyPanel, Padding, evenPadding } from '../WonkyPanel';
import { wait } from '../../Util';
import { RoutingHistory } from '../Routing';

interface ButtonProps {
  purpose?: string;
  className?: string;
  style?: React.CSSProperties;
  onClick?: (event: React.MouseEvent<HTMLButtonElement>) => void;
  onPerform?: (event: React.MouseEvent<HTMLButtonElement>) => Promise<void>;
  children?: any;
  disabled?: boolean;
  autoFocus?: boolean;
  title?: string;
  fill?: string;
  toggled?: boolean;
  primary?: boolean;
  flat?: boolean;
  large?: boolean;
  borderPx?: Padding;
  paddingPx?: Padding;
  materialIcon?: string;
  fontAwesomeIcon?: string;
  type?: 'button' | 'submit' | 'reset';
}

export function Button(props: ButtonProps) {
  const { className, style, onClick, onPerform, children, disabled, toggled,
    fill, primary, large, purpose, flat, borderPx, paddingPx, materialIcon, fontAwesomeIcon, ...rest} = props;
  const [working, setWorking] = React.useState<boolean>();
  const handleClick = async (e: React.MouseEvent<HTMLButtonElement>) => {
    e.stopPropagation();
    if (onPerform) {
      setWorking(true);
      await wait(1);
      await onPerform(e);
      setWorking(false);
    } else if (onClick) {
      onClick(e);
    }
  };
  let inner = children;
  if (fontAwesomeIcon) {
    inner = <span><i className={`fas fa-${fontAwesomeIcon}`} />&nbsp;{inner}</span>;
  }
  if (materialIcon) {
    inner = <span><i className="material-icons">{materialIcon}</i>&nbsp;{inner}</span>;
  }
  return (
    <button
      {...rest}
      disabled={disabled || working}
      onClick={handleClick}
      className={`Button ${props.className || ''} ${primary ? 'ButtonPrimary' : ''} ${flat ? 'ButtonFlat' : ''} ${toggled ? 'ButtonToggled' : ''} ${large ? 'ButtonLarge' : ''} Purpose${purpose}`}
      style={style}
      >
      {flat ? inner : (
        <WonkyPanel
          fill={(disabled || working) ? '#eee' : primary ? '#8ECC95' : (toggled ? '#c6c041' : (fill ? fill : '#fff'))}
          borderPx={borderPx || evenPadding(2)}
          paddingPx={paddingPx || { left: 5, right: 5, top: 2, bottom: 2 }}
          shadowPx={4}>{inner}</WonkyPanel>
      )}
    </button>
  );
}

interface MaterialIconButtonProps extends ButtonProps {
  icon: string;
}

export function MaterialIconButton(props: MaterialIconButtonProps) {
  const { className, icon, children, ...rest} = props;
  return <Button className={`${className !== undefined ? className : ''} MaterialIconButton`} paddingPx={evenPadding(3)} borderPx={evenPadding(3)} {...rest}>
    <i className="material-icons">{icon}</i>{children !== undefined ? <span>&nbsp;{children}</span> : null}
  </Button>;
}

export type FontAwesomeStyle = 'regular' | 'solid' | 'brand';
interface FontAwesomeButtonProps extends ButtonProps {
  icon: string;
  iconStyle?: FontAwesomeStyle;
}

export function FontAwesomeButton(props: FontAwesomeButtonProps) {
  const { className, icon, iconStyle, children, ...rest} = props;
  return <Button className={`${className !== undefined ? className : ''} MaterialIconButton`} paddingPx={evenPadding(3)} borderPx={evenPadding(3)} {...rest}>
    <i className={`${iconStyle === 'brand' ? 'fab' : iconStyle === 'regular' ? 'far' : 'fas'} fa-${icon}`} />{children !== undefined ? <span>&nbsp;{children}</span> : null}
  </Button>;
}

export function LinkButton({ to, ...rest }: { to: string } & ButtonProps) {
  return <Button onClick={() => RoutingHistory.push(to)} {...rest} />;
}
