import * as React from 'react';
import './index.css';
import * as ReactDOM from 'react-dom';

export type PopdownAttachment = 'horizontal' | 'vertical';

export function PopdownsContainer() {
  return <div id="PopdownsContainer" />
}

interface PopdownProps {
  onClickFader?: () => void;
  popdown: () => React.ReactNode;
  showPopdown: boolean;
  className?: string;
  fader?: boolean;
  attachment?: PopdownAttachment;
  onMouseOver?: (event: React.MouseEvent<HTMLDivElement>) => void;
  onMouseOut?: (event: React.MouseEvent<HTMLDivElement>) => void;
  onClick?: (event: React.MouseEvent<HTMLDivElement>) => void;
  onContextMenu?: (event: React.MouseEvent<HTMLDivElement>) => void;
}

export class Popdown extends React.Component<PopdownProps> {
  root: HTMLDivElement | null = null;

  render() {
    const { fader = true } = this.props;
    let popupPopdown = (
      <div className="PopdownPopup" style={this.popupStyle} onClick={e => e.stopPropagation()}>
        {this.props.popdown()}
      </div>
    );
    if (fader) {
      popupPopdown = <div className="PopdownPopupBackground" onClick={this.props.onClickFader}>{popupPopdown}</div>;
    }
    const portal = this.props.showPopdown ?
      ReactDOM.createPortal(
        popupPopdown
      , document.getElementById('PopdownsContainer') || document.documentElement!)
      : undefined;
    return (
      <div
        className={'Popdown' + (this.props.className ? ' ' + this.props.className : '')}
        onMouseOver={this.props.onMouseOver}
        onMouseOut={this.props.onMouseOut}
        onClick={this.props.onClick}
        onContextMenu={this.props.onContextMenu}
        ref={e => this.root = e}
        >
        {this.props.children}
        {portal}
      </div>
    );
  }

  private get popupStyle(): React.CSSProperties {
    if (this.root) {
      const { attachment = 'vertical' } = this.props;
      const bounding = this.root.getBoundingClientRect();
      const style: React.CSSProperties = {};
      if (attachment === 'vertical') {
        if (bounding.left < window.innerWidth / 2) {
          style.left = bounding.left + 'px';
        } else {
          style.right = (window.innerWidth - bounding.right) + 'px';
        }
        if (bounding.bottom < window.innerHeight / 2) {
          return {
            ...style,
            top: bounding.bottom + 'px',
            maxHeight: (window.innerHeight - bounding.bottom - 3) + 'px'
          };
        } else {
          return {
            ...style,
            bottom: (window.innerHeight - bounding.top) + 'px',
            maxHeight: (bounding.top - 3) + 'px'
          };
        }
      } else {
        if (bounding.left < window.innerWidth / 2) {
          style.left = bounding.right + 'px';
          style.maxWidth = (window.innerWidth - bounding.left) + 'px';
        } else {
          style.right = (window.innerWidth - bounding.left) + 'px';
          style.maxWidth = (bounding.left - 3) + 'px';
        }
        if (bounding.bottom < window.innerHeight / 2) {
          return {
            ...style,
            top: bounding.top + 'px'
          };
        } else {
          return {
            ...style,
            bottom: (window.innerHeight - bounding.bottom) + 'px'
          };
        }
      }
    } else {
      return {};
    }
  }
}