import * as React from 'react';
import './Editors.css';
import { EditorStructProperties, EditorTypedefResolver } from './EditorTypedefs';
import { AnyEditor } from './AnyEditor';
import { LineListLayout, LineListLayoutItem } from './LineListLayout';
import { ListEditor } from './ListEditor';
import { EditorProps } from './EditorProps';

export interface StructEditorChange {
  property: string;
  value: any;
}

interface StructEditorType {
  [key: string]: any;
}

export type StructEditorLayout = 'linelist' | 'table';

interface StructEditorProps extends EditorProps<StructEditorType> {
  onChange: (change: StructEditorChange) => void;
  properties: EditorStructProperties;
  layout?: StructEditorLayout;
  value: StructEditorType;

  typedefResolver?: EditorTypedefResolver;
}

const verticalEditors = [
  ListEditor,
  StructEditor,
];

export function StructEditor({ className, onChange, properties, readOnly, typedefResolver, value, layout }: StructEditorProps): any {
  let props;
  if (!Array.isArray(properties)) {
    props = Object.keys(properties)
      .filter(name => properties[name])
      .map((name: string) => ({ name, type: properties[name] }));
  } else {
    props = properties;
  }
  props = props.map(p => ({
    ...p,
    editor: AnyEditor({
      typedef: p.type,
      readOnly,
      value: value && value[p.name as string],
      onChange: (newValue: any) => onChange({ property: p.name as string, value: newValue }),
      typedefResolver,
    })
  }));
  if (layout === 'table') {
    return <table className="StructEditor"><tbody>
      {props.map(({ name, label, title, editor, visible }, i) => visible !== false && <tr key={i}>
        <td title={title} className="StructEditorKey">{label !== undefined ? label : name}</td>
        <td className="StructEditorValue">{editor}</td>
      </tr>)}
    </tbody></table>;
  } else {
    return <LineListLayout className={className}>
      {props.map(({ name, label, title, editor, visible }, i) => {
          return visible !== false && <LineListLayoutItem key={i} 
            isFirst={i === 0}
            isLast={i === props.length - 1}
            item={
                <div key={name} className={'StructEditorProperty' + (verticalEditors.indexOf(editor.type) === -1 ? ' Inline' : '')}>
                  <div className="StructEditorKey" title={title}>
                    {label !== undefined ? label : name}:&nbsp;
                  </div>
                  <div className="StructEditorValue">
                    {editor}
                  </div>
                </div>
            }
            />;
        })
      }
    </LineListLayout>;
  }
}
