import { GpuWorldState } from "../State";
import { EntityComponentSystem } from "../../GpuUtil/ECS";
import { rgba255ToVec401 } from "../../Util";
import { GlState, GL } from "../../GpuUtil/GlState";
import { LightSM2, FogSM2 } from "./Util";
import { Program } from "../../GpuUtil/Program";
import { BoneMatrix, LocalToWorld } from "../ECS/Transform";
import { VisibleComponent } from "../ECS/Components";
import { EntitiesQuery } from "../../GpuUtil/ECS/Query";
import { GpuBufferModule, GpuComponentModule } from "../../GpuUtil/ECS/ComponentModules";
import { uniformDef, uniformArrayDef, CompiledShaderModule, shaderModule, funcDef, outDef, inDef, ShaderModule, FunctionDef } from "../../GpuUtil/ShaderModule";
import { Vertex } from "../../GpuUtil/Mesh";
import { EntityRef } from "../StateData";
import { createBatches } from "../../GpuUtil/Computation";
import { visibleCacheKeys } from "../ECS/Visibility";

export enum CullFace {
  None = GL.NONE,
  Front = GL.FRONT,
  Back = GL.BACK,
  FrontAndBack = GL.FRONT_AND_BACK
}

export const ModelRendererBuffer = new GpuBufferModule({
  name: 'ModelRendererBuffer',
  format: 'vec4'
});
export const MaterialColor = new GpuComponentModule('MaterialColor', ModelRendererBuffer.buffer, 'vec4');
export const MaterialUseLighting = new GpuComponentModule('MaterialUseLighting', ModelRendererBuffer.buffer, 'bool');

interface Renderable {
  mesh: string;
  cullFace: CullFace;
  skinning?: boolean;
  transparent?: boolean;
}
export const RenderableComponent = EntityComponentSystem.registerComponent<Renderable>({
  name: 'Renderable',
  type: 'obj',
});

export const MaxBones = 256;
const SkinmeshBuffer = new GpuBufferModule({ name: 'SkinmeshBuffer', format: 'vec4' });
export const SkinmeshBones = new GpuComponentModule('SkinmeshBones', SkinmeshBuffer, 'ivec2', MaxBones);
export const SkinmeshBindMatrix = new GpuComponentModule('SkinmeshBindMatrix', SkinmeshBuffer, 'mat4');

class SM2Program {
  constructor(protected gls: GlState, public fragmentShader: CompiledShaderModule, public vertexShader: CompiledShaderModule) {}
  program = new Program(this.gls, this.fragmentShader.source, this.vertexShader.source);
}

export class ModelRenderer {
  constructor(protected gls: GlState) {}

  private maxInstanceCount = 256;
  private createVertexShader = (worldTransform: ShaderModule<{ getWorldTransform: FunctionDef<'mat4', [['ivec2', 'entityCell']]> }>) =>
    new CompiledShaderModule(shaderModule([
    Vertex,
    worldTransform,
    MaterialColor.sm,
    MaterialUseLighting.sm,
  ], {
    entityCells: uniformArrayDef('ivec2[]', this.maxInstanceCount),
    viewPosition: outDef('vec3'),
    normal: outDef('vec3'),
    materialColor: outDef('vec4'),
    materialUseLighting: outDef('int', true),
    viewMatrix: uniformDef('mat4', d => d.derived.cameraView),
    projectionMatrix: uniformDef('mat4', d => d.derived.cameraProjection),
    main: funcDef('void', [], `
      ivec2 entityCell = entityCells[gl_InstanceID];
      mat4 worldTransform = ${worldTransform.getWorldTransform}(entityCell);

      vec4 viewPos = viewMatrix * worldTransform * vec4(${Vertex.position}, 1.0 );
      viewPosition = viewPos.xyz / viewPos.w;
      gl_Position = projectionMatrix * viewPos;

      mat4 invTranspWorldTransform = transpose(inverse(worldTransform));
      normal = normalize((invTranspWorldTransform * vec4(${Vertex.normal}, 1)).xyz);

      materialColor = ${MaterialColor.sm.value}(entityCell);
      materialUseLighting = int(${MaterialUseLighting.sm.value}(entityCell));
    `)
  }));

  staticVertexShader = this.createVertexShader(shaderModule([
    LocalToWorld.sm,
  ], {
    getWorldTransform: funcDef('mat4', [['ivec2', 'entityCell']], `
      return ${LocalToWorld.sm.value}(entityCell);
    `)
  }));

  skinmeshVertexShader = this.createVertexShader(shaderModule([
    BoneMatrix.sm,
    SkinmeshBones.sm,
    SkinmeshBindMatrix.sm,
  ], {
    getWorldTransform: funcDef('mat4', [['ivec2', 'entityCell']], `
      ivec2 boneEntity0 = ${SkinmeshBones.sm.value}(entityCell, int(${Vertex.skinIndex}[0]));
      ivec2 boneEntity1 = ${SkinmeshBones.sm.value}(entityCell, int(${Vertex.skinIndex}[1]));
      ivec2 boneEntity2 = ${SkinmeshBones.sm.value}(entityCell, int(${Vertex.skinIndex}[2]));
      ivec2 boneEntity3 = ${SkinmeshBones.sm.value}(entityCell, int(${Vertex.skinIndex}[3]));
      mat4 bone0 = ${BoneMatrix.sm.value}(boneEntity0);
      mat4 bone1 = ${BoneMatrix.sm.value}(boneEntity1);
      mat4 bone2 = ${BoneMatrix.sm.value}(boneEntity2);
      mat4 bone3 = ${BoneMatrix.sm.value}(boneEntity3);
      mat4 skinMatrix = mat4(0.0);
      skinMatrix += ${Vertex.skinWeight}.x * bone0;
      skinMatrix += ${Vertex.skinWeight}.y * bone1;
      skinMatrix += ${Vertex.skinWeight}.z * bone2;
      skinMatrix += ${Vertex.skinWeight}.w * bone3;
      mat4 bindMatrix = ${SkinmeshBindMatrix.sm.value}(entityCell);
      return skinMatrix * bindMatrix;
    `)
  }));

  private fragmentShader = new CompiledShaderModule(shaderModule([LightSM2, FogSM2], {
    outColor: outDef('vec4'),
    materialColor: inDef('vec4'),
    materialUseLighting: inDef('int', true),
    materialIsUnit: inDef('int', true),
    normal: inDef('vec3'),
    viewPosition: inDef('vec3'),
    fogColor: uniformDef('vec4', d => rgba255ToVec401(d.data.rendererConfig.fogColor)),
    fogDensity: uniformDef('float', d => d.data.rendererConfig.fogDensity),
    fogOffset: uniformDef('float', d => d.data.rendererConfig.fogOffset),
    main: funcDef('void', [], `
      outColor = materialColor;
      if (bool(materialUseLighting)) {
        float lightAmount = ${LightSM2.light}(normal);
        outColor.rgb *= mix(0.8, 1., lightAmount);
      }

      float depthFog = ${FogSM2.fogFactorExp2}(max(0.0, length(viewPosition) - fogOffset), fogDensity);
      outColor.rgb = mix(outColor.rgb, fogColor.rgb, fogColor.a * depthFog);
    `)
  }));

  private programs = {
    static: new SM2Program(this.gls, this.fragmentShader, this.staticVertexShader),
    skinmesh: new SM2Program(this.gls, this.fragmentShader, this.skinmeshVertexShader)
  }

  private query = new EntitiesQuery([RenderableComponent, LocalToWorld.gpuComponent, VisibleComponent])
    .filterValue(VisibleComponent, v => !!v, visibleCacheKeys)
    .transform((state, entityCells) => {
      const programNode = (): {
        [renderable: string]: {
          entities: EntityRef[];
          renderable: Renderable
        }
      } => ({});
      const renderTree = {
        opaque: {
          skinmesh: programNode(),
          static: programNode()
        },
        transparent: {
          skinmesh: programNode(),
          static: programNode()
        }
      };
      for (const entityCell of entityCells) {
        const renderable = state.ecs.getComponentData(RenderableComponent, entityCell)!;
        const blendNode = renderTree[renderable.transparent ? 'transparent' : 'opaque'];
        const programNode = blendNode[renderable.skinning ? 'skinmesh' : 'static'];
        const renderableKey = JSON.stringify(renderable);
        if (!programNode[renderableKey]) {
          programNode[renderableKey] = { entities: [], renderable };
        }
        programNode[renderableKey].entities.push(entityCell);
      }
      return renderTree;
    });

  run(state: GpuWorldState) {
    // this.gls.cullFace(gl.BACK);
    this.gls.enable(GL.DEPTH_TEST);
    this.gls.depthFunc(GL.LEQUAL);
    this.gls.disable(GL.POLYGON_OFFSET_FILL);
    this.gls.polygonOffset(0.0, 0.0);
    const renderTree = this.query.get(state);
    for (const blendKey of Object.keys(renderTree)) {
      if (blendKey === 'opaque') {
        this.gls.disable(GL.BLEND);
      } else {
        this.gls.enable(GL.BLEND);
        this.gls.blendFuncSeparate(GL.SRC_ALPHA, GL.ONE_MINUS_SRC_ALPHA, GL.ONE, GL.ONE_MINUS_SRC_ALPHA); // https://limnu.com/webgl-blending-youre-probably-wrong/
      }
      const blendNode = renderTree[blendKey];
      for (const programKey of Object.keys(blendNode)) {
        // This must be called before we start setting up for this draw call, because
        // getting the values may involve renderings and state changes
        const program = this.programs[programKey];
        program.vertexShader.updateUniformValues(state);
        program.fragmentShader.updateUniformValues(state);
        program.program.prepare();
        program.vertexShader.writeUniforms(program.program);
        program.fragmentShader.writeUniforms(program.program);
        const programNode = blendNode[programKey];
        for (const groupKey of Object.keys(programNode)) {
          const group = programNode[groupKey];
          if (group.renderable.cullFace === CullFace.None) {
            this.gls.disable(GL.CULL_FACE);
          } else {
            this.gls.enable(GL.CULL_FACE);
            this.gls.cullFace(group.renderable.cullFace);
          }
          const mesh = this.gls.getMesh(group.renderable.mesh);
          if (!mesh) {
            throw new Error(`No such mesh: ${group.renderable.mesh}`);
          }
          for (const { l, count } of createBatches(group.entities.length, this.maxInstanceCount)) {
            program.program.setUniform('entityCells', 'ivec2[]', group.entities.slice(l, l + count));
            this.gls.draw(program.program, mesh, count);
          }
        }
      }
    }
  }
}
