import { GlState } from "../../GpuUtil/GlState";
import { Hitpoints, MaxHitpoints, Team, UnitStats } from "./UnitComponents";
import { ArchetypesQuery } from "../../GpuUtil/ECS/Query";
import { shaderModule, funcDef } from "../../GpuUtil/ShaderModule";
import { glslFloat } from "../../GpuUtil";
import { Teams, SideSize } from "../StateLayout";
import { UnitsInArena } from "./ArenaStats";
import { Position } from "./Transform";
import { ArenaInstance } from "./Components";

export function UpdateUnitStats(gls: GlState) {
  return new ArchetypesQuery([UnitStats.AliveTime.component, UnitStats.CumulativeHitpoints.component, Hitpoints.component])
    .updateGpu(gls, [UnitStats.AliveTime.component, UnitStats.CumulativeHitpoints.component, UnitStats.MinDistEnemyStatue.component], shaderModule([
      Hitpoints.sm, UnitStats.AliveTime.sm, UnitStats.CumulativeHitpoints.sm, UnitStats.MinDistEnemyStatue.sm,
      UnitStats.AliveTime.writer, UnitStats.CumulativeHitpoints.writer, UnitStats.MinDistEnemyStatue.writer,
      Team.sm, UnitsInArena, Position.sm, ArenaInstance.sm
    ], {
      update: funcDef('void', [['ivec2', 'entityCell']], `
        float aliveTime = ${UnitStats.AliveTime.sm.value}(entityCell);
        float cumulativeHitpoints = ${UnitStats.CumulativeHitpoints.sm.value}(entityCell);
        float minDistEnemyStatue = ${UnitStats.MinDistEnemyStatue.sm.value}(entityCell);
        float hitpoints = ${Hitpoints.sm.value}(entityCell);
        if (hitpoints > 0.0) {
          aliveTime += 1.0;

          int arenaIndex = ${ArenaInstance.sm.value}(entityCell);
          int team = ${Team.sm.value}(entityCell);
          int enemyStatueIndex = team == ${Teams.HomeTeam} ? ${SideSize} : ${0};
          ivec2 enemyStatue = ${UnitsInArena.unit}(arenaIndex, enemyStatueIndex);
          vec3 position = ${Position.sm.value}(entityCell);
          vec3 statuePosition = ${Position.sm.value}(enemyStatue);
          minDistEnemyStatue = min(distance(position, statuePosition), minDistEnemyStatue);
        }
        cumulativeHitpoints += clamp(hitpoints, 0., ${glslFloat(MaxHitpoints)});

        ${UnitStats.AliveTime.writer.write}(aliveTime);
        ${UnitStats.CumulativeHitpoints.writer.write}(cumulativeHitpoints);
        ${UnitStats.MinDistEnemyStatue.writer.write}(minDistEnemyStatue);
      `)
    }));
}
