import * as React from 'react';
import './index.css';
import { renderToString } from 'react-dom/server';
import * as _ from 'lodash';
import { MeasureSize } from '../MeasureSize';

export interface Padding { left: number, top: number, bottom: number, right: number }
export function evenPadding(padding: number): Padding {
  return { left: padding, top: padding, bottom: padding, right: padding };
}
const DefaultWonkyPanelBorder: Padding = evenPadding(5);
const DefaultWonkyPanelPadding: Padding = evenPadding(5);
const DefaultWonkyPanelBackgroundMargin: Padding = evenPadding(0);

interface WonkyPanelProps {
  children?: any;
  className?: string;
  style?: React.CSSProperties;
  borderPx?: Padding;
  paddingPx?: Padding;
  backgroundMarginPx?: Padding;
  shadowPx?: number;
  fill?: string;
  stroke?: string;
  strokeWidth?: number;
  onClick?: (event: React.MouseEvent<HTMLDivElement>) => void;
}
interface WonkyPanelState {
  size: { width: number, height: number };
}

class WonkyPanelSvg {
  constructor(private borderPx: Padding, private marginPx: Padding) {
  }
  topLeft = { x: this.borderPx.left * Math.random(), y: this.borderPx.top * Math.random() };
  topRight = { x: this.borderPx.right * Math.random(), y: this.borderPx.top * Math.random() };
  bottomRight = { x: this.borderPx.right * Math.random(), y: this.borderPx.bottom * Math.random() };
  bottomLeft = { x: this.borderPx.left * Math.random(), y: this.borderPx.bottom * Math.random() };
  renderToDataUrl(size: { width: number, height: number }, shadowPx: number, fill: string, stroke: string | undefined, strokeWidth: number | undefined) {
    const { topLeft, topRight, bottomLeft, bottomRight } = this;

    const d = `
      M${topLeft.x + this.marginPx.left} ${topLeft.y + this.marginPx.top}
      L ${size.width - topRight.x - shadowPx - this.marginPx.right} ${topRight.y + this.marginPx.top}
      L ${size.width - bottomRight.x - shadowPx - this.marginPx.right} ${size.height - bottomRight.y - shadowPx - this.marginPx.bottom}
      L ${bottomLeft.x + this.marginPx.left} ${size.height - bottomLeft.y - shadowPx - this.marginPx.bottom}
      Z
    `;
    const str = renderToString(
      <svg
        className="WonkyPanelBackground"
        xmlns="http://www.w3.org/2000/svg"
        width={size.width}
        height={size.height}
        >
        {shadowPx !== 0 && (
          <path d={d} fill="rgba(0, 0, 0, 0.3)" transform={`translate(${shadowPx}, ${shadowPx})`} />
        )}
        <path d={d} fill={fill} stroke={stroke} strokeWidth={strokeWidth} />
      </svg>);
    return `data:image/svg+xml;base64,${window.btoa(str)}`;
  }
}

export class WonkyPanel extends React.Component<WonkyPanelProps, WonkyPanelState> {
  constructor(props: WonkyPanelProps) {
    super(props);
    this.state = {
      size: { width: 0, height: 0 },
    };
  }
  componentDidUpdate(prevProps: WonkyPanelProps) {
    if (!_.isEqual(prevProps.borderPx, this.props.borderPx) || !_.isEqual(prevProps.backgroundMarginPx, this.props.backgroundMarginPx)) {
      this.wonkyPanelSvg = new WonkyPanelSvg(
        this.props.borderPx !== undefined ? this.props.borderPx : DefaultWonkyPanelBorder,
        this.props.backgroundMarginPx !== undefined ? this.props.backgroundMarginPx : DefaultWonkyPanelBackgroundMargin);
    }
  }
  measure = new MeasureSize(size => this.setState({ size }));
  componentWillUnmount() {
    this.measure.handleUnmount();
  }
  wonkyPanelSvg = new WonkyPanelSvg(
    this.props.borderPx !== undefined ? this.props.borderPx : DefaultWonkyPanelBorder,
    this.props.backgroundMarginPx !== undefined ? this.props.backgroundMarginPx : DefaultWonkyPanelBackgroundMargin);
  render () {
    const { children, className, shadowPx = 10,
      borderPx = DefaultWonkyPanelBorder, paddingPx = DefaultWonkyPanelPadding,
      fill = "#171717", onClick, stroke, strokeWidth } = this.props;

    return (
      <div
        className={`WonkyPanel ${className !== undefined ? className : ''}`}
        ref={this.measure.handleRef}
        style={{
          paddingLeft: (borderPx.left + paddingPx.left) + 'px',
          paddingTop: (borderPx.top + paddingPx.top) + 'px',
          paddingRight: (borderPx.right + paddingPx.right + shadowPx) + 'px',
          paddingBottom: (borderPx.bottom + paddingPx.bottom + shadowPx) + 'px',
          background: `url('${this.wonkyPanelSvg.renderToDataUrl(this.state.size, shadowPx, fill, stroke, strokeWidth)}') no-repeat`,
          ...this.props.style
        }}
        onClick={onClick}
        >
        {children}
      </div>
    );
  }
}
