import { GpuWorldState } from "../State";
import { EntitiesQuery } from "../../GpuUtil/ECS/Query";
import { GlState } from "../../GpuUtil/GlState";
import { shaderModule, funcDef } from "../../GpuUtil/ShaderModule";
import { ArenaStats } from "../ECS/ArenaStats";
import { Arena } from "../ECS/Components";
import lodash from 'lodash';

export class UpdateBattleScore {
  constructor(private gls: GlState) {}

  arenaStats = new EntitiesQuery([Arena.gpuComponent])
    .filterValue(Arena.cpuComponent, (v, d) => v[0] === d.data.renderArena, d => [d.data.renderArena])
    .toGpu(this.gls)
    .mapToTemp(shaderModule([ArenaStats, Arena.sm], {
      map: funcDef('vec4', [['ivec2', 'entityCell']], `
        int arenaIndex = ${Arena.sm.value}(entityCell);
        ${ArenaStats.Stats} stats = ${ArenaStats.stats}(arenaIndex);
        return vec4(stats.homePoints, stats.awayPoints, stats.home.unitsAlive, stats.away.unitsAlive);
      `)
    }))
    .toCpuQueuing(state => state.data.round);

  run(state: GpuWorldState) {
    const readResults = this.arenaStats.queueRead(state);
    const arenaStats = lodash.last(readResults());
    if (arenaStats) {
      const homePoints = arenaStats[0].value[0].x;
      const awayPoints = arenaStats[0].value[0].y;
      const homeAlive = arenaStats[0].value[0].z;
      const awayAlive = arenaStats[0].value[0].w;
      if (state.data.teams.home.battlePoints !== homePoints) {
        state.updateTeam('home', { battlePoints: homePoints });
      }
      if (state.data.teams.away.battlePoints !== awayPoints) {
        state.updateTeam('away', { battlePoints: awayPoints });
      }
      if (state.data.teams.home.battleUnitsAlive !== homeAlive) {
        state.updateTeam('home', { battleUnitsAlive: homeAlive });
      }
      if (state.data.teams.away.battleUnitsAlive !== awayAlive) {
        state.updateTeam('away', { battleUnitsAlive: awayAlive });
      }
    }
  }
}
