import * as React from 'react';
import './Editors.css';
import { EditorTypedef, EditorTypedefRef, EditorTypedefResolver } from './EditorTypedefs';
import { StringEditor } from './StringEditor';
import { AnyEditor } from './AnyEditor';
import { EditorProps } from './EditorProps';

export function KeyValueLayout({ children, stack, className }: { className?: string, stack?: boolean, children?: any }) {
  return (
    <div className={`KeyValueLayout ${stack ? 'KeyValueLayoutStack' : ''} ${className ? className : ''}`}>
      {children}
    </div>
  );
}

export function KeyValueLayoutRow({ children, className, keyName, keyElement }: { className?: string, keyName: string, keyElement: any, children?: any }) {
  return (
    <div className={`KeyValueLayoutRow ${className ? className : ''}`}>
      <div className="KeyValueLayoutKey" title={keyName}>
        {keyElement}
      </div>
      <div className="KeyValueLayoutValue">
        {children}
      </div>
    </div>
  );
}

export interface KeyValueEditorChange {
  oldKey: string;
  key: string;
  entryValue: any;
}

interface KeyValueType {
  [key: string]: any
}

interface KeyValueEditorProps extends EditorProps<KeyValueType> {
  onChange: (value: KeyValueType, change: KeyValueEditorChange) => void;
  value: KeyValueType;
  valueType: EditorTypedef | EditorTypedefRef;

  stack?: boolean;
  typedefResolver?: EditorTypedefResolver;
  valueDefault?: any;
}

export function KeyValueEditor({ className, onChange, readOnly, stack, typedefResolver, value, valueType, valueDefault }: KeyValueEditorProps): any {
  value = value || {};
  const keys = Object.keys(value);
  if (keys.indexOf('') === -1) {
    keys.push('');
  }
  return (
    <KeyValueLayout className={'KeyValueEditor ' + (className || '')} stack={stack}>
      {keys.map((key, i) => (
          <KeyValueLayoutRow
            keyName={key || ''}
            key={i}
            keyElement={(
              <StringEditor
                readOnly={readOnly}
                value={key}
                onChange={newKey => {
                  newKey = newKey === undefined ? '' : newKey;
                  const entryValue = value[key] !== undefined ? value[key] : valueDefault;
                  const newValue = {...value, [newKey]: entryValue };
                  delete newValue[key];
                  onChange(newValue, { oldKey: key, key: newKey, entryValue });
                }}
                multiline={false}
                widthMode="fit-content"
                />
            )}
            >
            <AnyEditor
              typedef={valueType}
              value={value[key]}
              onChange={(entryValue: any) =>
                onChange({...value, [key]: entryValue }, { oldKey: key, key, entryValue })
              }
              typedefResolver={typedefResolver}
              />
          </KeyValueLayoutRow>
      ))}
    </KeyValueLayout>
  );
}
