import * as React from 'react';
import './Editors.css';
import { EditorTypedef, EditorTypedefRef, EditorTypedefResolver } from './EditorTypedefs';
import { EditorProps, EditorDispatch } from './EditorProps';
import { AnyEditor } from './AnyEditor';

interface TupleEditorProps extends EditorProps<any[]> {
  itemTypes: Array<EditorTypedef | EditorTypedefRef>;
  typedefResolver?: EditorTypedefResolver;
}

export const TupleEditor = ({ className, onChange, itemTypes, readOnly, value, typedefResolver }: TupleEditorProps & EditorDispatch<any[]>) => {
  const tuple = value || [];
  return (
    <div className={'TupleEditor ' + (className || '')}>
      {itemTypes.map((type, i) => (
        <div key={i} className="TupleEditorItem">
          <AnyEditor
            readOnly={readOnly}
            value={tuple[i]}
            onChange={(newValue: any) => {
              const newTuple = [...tuple];
              newTuple[i] = newValue;
              onChange(newTuple);
            }}
            typedef={itemTypes[i]}
            typedefResolver={typedefResolver}
            />
        </div>
      ))}
    </div>
  );
}
