import * as React from 'react';
import { EditorProps, NonNullEditorDispatch } from './EditorProps';
import { getSizeOfInput } from './StringEditor';
import { Result, isErr, err, ok } from '../../Result';

export interface SliderEditorOptions {
  min?: number;
  max?: number;
  step?: number;
  suffix?: string;
}

export enum NumberEditorType {
  INT,
  FLOAT
}
export interface NumberEditorOptions extends SliderEditorOptions {
  clamp?: boolean;
  slider?: boolean;
  factor?: number;
  title?: string;
}
interface NumberEditorProps extends EditorProps<number>, NumberEditorOptions {
  type?: NumberEditorType;
  onErrorChange?: (error: boolean) => void;
  formatter?: (value: number) => string;
  onBlur?: () => void;
}
interface NumberEditorState {
  error?: boolean;
  editValue?: string;
  focused: boolean;
  committed: boolean;
}

export class NumberEditor extends React.Component<NumberEditorProps & NonNullEditorDispatch<number>, NumberEditorState> {
  constructor(props: NumberEditorProps & NonNullEditorDispatch<number>) {
    super(props);
    this.state = { error: false, editValue: this.stringify(), focused: false, committed: true };
  }
  get factor() {
    return this.props.factor !== undefined ? this.props.factor : 1;
  }
  parse(text: string) {
    if (this.props.type === NumberEditorType.INT) {
      return parseInt(text, 10);
    } else {
      return parseFloat(text);
    }
  }
  stringify() {
    if (this.props.value === undefined) {
      return '';
    } else {
      const value = this.props.value * this.factor;
      if (this.props.formatter) {
        return this.props.formatter(value);
      } else {
        return '' + value;
      }
    }
  }
  handleRef = (el: HTMLInputElement | null) => {
    this.inputRef = el;
    if (el && document.activeElement === el) {
      this.setState({ editValue: this.stringify(), focused: true });
    }
  }
  inputRef: HTMLInputElement | null = null;
  handleFocus = () => {
    this.setState({ editValue: this.stringify(), focused: true });
  }
  handleBlur = () => {
    this.tryCommit();
    this.setState({ editValue: undefined, focused: false, error: false, committed: true });
    if (this.props.onBlur) {
      this.props.onBlur();
    }
  }
  tryCommit() {
    const v = this.getValue();
    if (!isErr(v)) {
      this.props.onChange(v.value);
      this.setState({ committed: true });
    }
  }
  getValue = (): Result<number, boolean> => {
    if (!this.inputRef || this.inputRef.validity.badInput) {
      return err(true);
    } else {
      let val = this.parse(this.inputRef.value) / this.factor;
      if (this.props.min !== undefined && this.props.clamp !== false) {
        val = Math.max(this.props.min, val);
      }
      if (this.props.max !== undefined && this.props.clamp !== false) {
        val = Math.min(this.props.max, val);
      }
      const error = isNaN(val);
      if (error) {
        return err(true);
      } else {
        return ok(val);
      }
    }
  }
  handleChange = (event: React.FormEvent<HTMLInputElement>) => {
    this.setState({ editValue: (event.target as HTMLInputElement).value, committed: false });
    const v = this.getValue();
    if (isErr(v) !== this.state.error && this.props.onErrorChange) {
      this.props.onErrorChange(isErr(v));
    }
    this.setState({ error: isErr(v) });
  }
  handleKeyDown = (event: React.KeyboardEvent<HTMLInputElement>) => {
    if (event.key === 'Enter') {
      this.tryCommit();
    } else if (event.key === 'Escape') {
      this.setState({ editValue: this.stringify(), committed: true, error: false });
    }
  }
  handleSlider = (event: React.FormEvent<HTMLInputElement>) => {
    this.props.onChange((event.target as HTMLInputElement).valueAsNumber);
    this.setState({ editValue: this.stringify(), committed: true, error: false });
  }
  render() {
    const { className, slider } = this.props;
    const value = this.state.focused ? this.state.editValue : this.stringify();
    const width = this.props.slider ? 80 : // Make fixed with because otherwise the slider moves around when you drag it
      this.state.error ? undefined :
      (getSizeOfInput(value || '', undefined).width + 30);
    const inp = (
      <input
        className={`${className && !slider ? className : ''} ${this.state.error ? ' InvalidInput' : ''} ${!this.state.committed ? 'NumberEditorUncommitted' : ''}`}
        type="number"
        min={this.props.clamp !== false ? this.props.min : undefined}
        max={this.props.clamp !== false ? this.props.max : undefined}
        step={this.props.step}
        value={value}
        onChange={this.handleChange}
        onKeyDown={this.handleKeyDown}
        style={{ width }}
        readOnly={this.props.readOnly}
        onFocus={this.handleFocus}
        onBlur={this.handleBlur}
        ref={this.handleRef}
      />
    );
    if (slider) {
      return (
        <div className={'NumberRangeEditor ' + (className || '')}>
          {inp}
          {this.props.suffix}
          <input
            type="range"
            min={this.props.min}
            max={this.props.max}
            step={this.props.step}
            value={value}
            onChange={this.handleSlider}
            readOnly={this.props.readOnly}
          />
        </div>
      );
    } else {
      return <span>{inp}{this.props.suffix}</span>;
    }
  }
}

export function PercentageEditor(props: NumberEditorProps & NonNullEditorDispatch<number>) {
  return (
    <NumberEditor
        factor={100}
        suffix="%"
        {...props}
      />
  );
}


export function SliderEditor({ value, onChange, min, max, step, suffix }: EditorProps<number> & NonNullEditorDispatch<number> & SliderEditorOptions) {
  return <span><input
    type="range"
    min={min}
    max={max}
    step={step}
    value={value}
    onChange={e => onChange(parseFloat(e.target.value))}
    />{suffix}</span>;
}