


import { GpuWorldState } from "../State";
import { GlState } from "../../GpuUtil/GlState";
import { UpdateIronBubbable, UpdateHeliumBubbable } from "./Bubbles";
import { UpdateDazeBuff } from "./Daze";
import { UpdateCrippleBuff } from "./Cripple";

export class UpdateBuffs {
  constructor(private gls: GlState) {}

  buffs = [
    new UpdateDazeBuff(this.gls),
    new UpdateIronBubbable(this.gls),
    new UpdateHeliumBubbable(this.gls),
    new UpdateCrippleBuff(this.gls),
  ]

  run(state: GpuWorldState) {
    for (const b of this.buffs) {
      b.run(state);
    }
  }
}
