import { shaderModule, uniformDef, funcDef } from "../../GpuUtil/ShaderModule";
import { vec2FromSize } from "../../Math/Math";

export const GroundHeightSM = shaderModule('GroundHeight', [], {
  heightmap: uniformDef('sampler2D', d => d.textures.heightmap),
  heightmapSize: uniformDef('vec2', d => vec2FromSize(d.textures.heightmap.size)),
  worldSize: uniformDef('ivec2', d => d.derived.worldSize),
  atCoord: funcDef('float', [['vec2', 'worldCoord']], `
    return 59.1 + texelFetch(Self.heightmap, ivec2(worldCoord * Self.heightmapSize), 0).x;
  `),
  atPosition: funcDef('float', [['vec2', 'position']], `
    return Self.atCoord(position / vec2(Self.worldSize));
  `),
})

export const GroundNormalSM = shaderModule('GroundNormal', [], {
  normalmap: uniformDef('sampler2D', d => d.textures.normalmap),
  normalmapSize: uniformDef('vec2', d => vec2FromSize(d.textures.normalmap.size)),
  worldSize: uniformDef('ivec2', d => d.derived.worldSize),
  atCoord: funcDef('vec3', [['vec2', 'worldCoord']], `
    return texelFetch(Self.normalmap, ivec2(worldCoord * Self.normalmapSize), 0).xyz;
  `),
  atPosition: funcDef('vec3', [['vec2', 'position']], `
    return Self.atCoord(position / vec2(Self.worldSize));
  `),
})
