import * as glMatrix from "gl-matrix";

declare module 'gl-matrix' {
  module vec2 {
    function fromPolarCoordinates(angle: number, radius: number): vec2;
  }
  module vec3 {
    function fromPolarCoordinates(angle: number, radius: number): vec3;
    function fromSphericalCoordinates(theta: number, phi: number, radius: number): vec3;
    function mix(out: vec3, a: vec3, b: vec3, p: number): vec3;
  }
}

glMatrix.vec2.fromPolarCoordinates = (angle: number, radius: number): glMatrix.vec2 => {
  return glMatrix.vec2.fromValues(Math.cos(angle) * radius, Math.sin(angle) * radius);
}
glMatrix.vec3.fromPolarCoordinates = (angle: number, radius: number): glMatrix.vec3 => {
  return glMatrix.vec3.fromValues(Math.cos(angle) * radius, Math.sin(angle) * radius, 0);
}
glMatrix.vec3.fromSphericalCoordinates = (theta: number, phi: number, radius: number): glMatrix.vec3 => {
  return glMatrix.vec3.fromValues(
    Math.sin(theta) * Math.cos(phi) * radius,
    Math.sin(theta) * Math.sin(phi) * radius,
    Math.cos(theta) * radius
  );
}

glMatrix.vec3.mix = (out: glMatrix.vec3, a: glMatrix.vec3, b: glMatrix.vec3, p: number): glMatrix.vec3 => {
  return glMatrix.vec3.add(out,
    glMatrix.vec3.scale(glMatrix.vec3.create(), a, 1 - p),
    glMatrix.vec3.scale(glMatrix.vec3.create(), b, p));
}
