import { GpuWorldState } from "../../GpuWorld/State";
import { OverheadBarsRenderer } from "./OverheadBarsRenderer";
import { GlState } from "../../GpuUtil/GlState";
import { ModelRenderer } from "./ModelRenderer";
import { GazeRenderer } from "./Visualization";
import { UpdateAnimations } from "../ECS/Animation";
import { UpdateTransform } from "../ECS/Transform";
import { UpdateVisible } from "../ECS/Visibility";
import { BubblesRenderer } from "../Buffs/Bubbles";
import { DazeableRenderer } from "../Buffs/Daze";
import { HealingGlandRenderer } from "../Abilities/HealingGland";
import { BubblegumsRenderer } from "../Abilities/Bubblegums";
import { VampireGlandRenderer } from "../Abilities/VampireGland";
import { UpdateBrainHistograms, BrainHistogramsRenderer } from "../Tools/BrainHistograms";
import { DecisionsVizRenderer } from "./DecisionsViz";
import { UpdateCameraMatrices } from "../ECS/Camera";
import { AICrowdLogoRenderer } from "./AICrowdLogo";
// import { SkyRenderer } from "./Sky";

class AbilityEffectsRenderer {
  constructor(private gls: GlState) {}

  effects = [
    new HealingGlandRenderer(this.gls),
    new VampireGlandRenderer(this.gls),
    new BubblegumsRenderer(this.gls),
  ]

  run(state: GpuWorldState) {
    for (const eff of this.effects) {
      eff.run(state);
    }
  }
}

export class GpuWorldRenderer {
  constructor(private gls: GlState) {
    console.log('Renderer initialized');
  }
  destroyed = false;
  destroy() {
    console.log('Destroying renderer');
    this.destroyed = true;
  }

  systems = [
    new UpdateVisible(),
    new UpdateAnimations(),
    new UpdateTransform(this.gls),
    new UpdateCameraMatrices(),
    new UpdateBrainHistograms(this.gls),
  ]

  renderers = [
    new ModelRenderer(this.gls),
    new AICrowdLogoRenderer(this.gls),
    new DecisionsVizRenderer(this.gls),
    new DazeableRenderer(this.gls),
    new BubblesRenderer(this.gls),
    new AbilityEffectsRenderer(this.gls),
    new GazeRenderer(this.gls),
    new OverheadBarsRenderer(this.gls),
    new BrainHistogramsRenderer(this.gls),
    // new SkyRenderer(this.gls),
  ]

  render(state: GpuWorldState) {

    for (const system of this.systems) {
      system.run(state);
    }

    const canvasSize = state.data.canvasRect;
    const opts = state.data.rendererConfig;

    const gl = this.gls.gl;

    this.gls.framebuffer = null;

    this.gls.viewport = { x: 0, y: 0, width: canvasSize.width, height: canvasSize.height };

    this.gls.depthMask(true);
    gl.clearColor(opts.clearColor.r/255, opts.clearColor.g/255, opts.clearColor.b/255, opts.clearColor.a);
    // tslint:disable-next-line:no-bitwise
    gl.clear(gl.COLOR_BUFFER_BIT | gl.DEPTH_BUFFER_BIT);

    for (const renderer of this.renderers) {
      renderer.run(state);
    }

  }

}

export function setCanvasSizeToElementSize(state: GpuWorldState) {
  const canvasRect = state.gls.canvas.getBoundingClientRect();
  state.gls.canvas.width = canvasRect.width;
  state.gls.canvas.height = canvasRect.height;
  if (state.data.canvasRect.width !== canvasRect.width ||
    state.data.canvasRect.height !== canvasRect.height ||
    state.data.canvasRect.left !== canvasRect.left ||
    state.data.canvasRect.right !== canvasRect.right ||
    state.data.canvasRect.bottom !== canvasRect.bottom ||
    state.data.canvasRect.top !== canvasRect.top) {
    state.setData({ canvasRect });
  }
}