import * as React from 'react';
import './index.css';
import { GlState } from '../GpuUtil/GlState';
import { GpuWorldState } from '../GpuWorld/State';
import { GpuWorldProgram } from '../GpuWorld';
import { WorldProgramContext } from '../Components/WorldCanvas';
import { Store, useStore, useStoreSelect } from '../Components/Store';
import { ItemsRow, Column, ItemsColumn } from '../Components/Layout';
import { FontAwesomeButton } from '../Components/Button';
import { ECSInspector } from '../Components/ECSInspector';
import { wait } from '../Util';
import { useWorldDataSelect, WorldStateContext } from '../GameComponents/WorldDataConsumer';
import { defaultRTSCameraConf, RTSCameraController } from '../SteamApp/WorldPage/RTSCamera';
import { ShaderDebugger } from '../Components/WorldCanvas/ShaderDebugger';
import { ArenaMinimaps } from '../SteamApp/WorldPage/ArenaMinimaps';
import { BattleScore, BattleScoreChangeFlash } from '../GameComponents/BattleScore';
import { RoundClock } from '../GameComponents/RoundClock';
import { DerkGymAPI } from './GymApi';


export class GymState {
  worldState?: GpuWorldState;
  status?: string = 'Initializing...';
  newVersion?: string;
}
export const GymStateContext = React.createContext(new Store(new GymState()));
export function useGymState<T>(map: (state: GymState) => T): T {
  return useStore(GymStateContext, map);
}
export function useGymStateSelect(...keys: Array<keyof GymState>): GymState {
  return useStoreSelect(GymStateContext, ...keys);
}

function StatusBar() {
  const { status } = useStoreSelect(GymStateContext, 'status');
  return <div>{status}</div>
}

function GymUI() {
  const [showECS, setShowECS] = React.useState(false);
  const { newVersion, worldState } = useStoreSelect(GymStateContext, 'newVersion', 'worldState');
  const { simulationPaused } = useWorldDataSelect('simulationPaused');
  return <div className="GymUI">
    <Column>
      <ItemsRow className="GymActionBar">
        <FontAwesomeButton
          icon="play"
          title="Play"
          flat={true}
          disabled={!simulationPaused}
          onClick={() => worldState?.setData({ simulationPaused: false })}
          />
        <FontAwesomeButton
          icon="pause"
          title="Pause"
          flat={true}
          disabled={simulationPaused}
          onClick={() => worldState?.setData({ simulationPaused: true })}
          />
        <FontAwesomeButton
          icon="eye"
          title="ECS inspector"
          flat={true}
          onClick={() => setShowECS(!showECS)}
          />
      </ItemsRow>
      <StatusBar />
      {newVersion && <div>New version available: {newVersion}</div>}
    </Column>
    <ArenaMinimaps />
    <ItemsColumn className="GymUIBottom">
      <BattleScore />
      <RoundClock />
    </ItemsColumn>
    {showECS && <ECSInspector />}
    <BattleScoreChangeFlash />
  </div>
}

export function GymAppInner() {
  const canvas = React.useRef<HTMLCanvasElement>(null);
  const [worldProgram, setWorldProgram] = React.useState<GpuWorldProgram>();
  const appState = React.useContext(GymStateContext);
  const { worldState } = useGymStateSelect('worldState');
  const [loaded, setLoaded] = React.useState(false);
  React.useEffect(() => {
    (async () => {
      await wait(100);
      if (canvas.current) {
        const b = canvas.current.getBoundingClientRect();
        canvas.current.width = b.width;
        canvas.current.height = b.height;
        const gl = canvas.current.getContext('webgl2', { desynchronized: true }) as any as WebGL2RenderingContext;
        if (!gl) {
          console.log('Failed to create WebGL2 context');
          return;
        }
        const gls = new GlState(canvas.current, gl);
        const program = new GpuWorldProgram(gls);

        setWorldProgram(program);
        (window as any).derk = new DerkGymAPI(program, appState);
        setLoaded(true);
      }
    })();
  }, [appState]);
  return <WorldProgramContext.Provider value={worldProgram}>
    <WorldStateContext.Provider value={worldState}>
      <RTSCameraController conf={{...defaultRTSCameraConf(), fovy: (35 / 360) * Math.PI * 2 }} />
      <div className="Gym">
        <canvas ref={canvas} />
        <GymUI />
        {loaded && <div className="GymLoaded" />}
      </div>
      {(worldProgram?.gls.shaderCache.errors.length ?? 0) > 0 &&
        <ShaderDebugger shaderErrors={worldProgram?.gls.shaderCache.errors ?? []} />}
    </WorldStateContext.Provider>
  </WorldProgramContext.Provider>;
}

export default function GymApp() {
  const [gymStateStore] = React.useState(new Store(new GymState()));
  return <GymStateContext.Provider value={gymStateStore}>
    <GymAppInner />
  </GymStateContext.Provider>
}
