import * as React from 'react';
import './index.css';
import { Button } from '../../Components/Button';
import firebase from 'firebase/app';
import { DbCreature, full, DbItemInstance, DbId, Db } from 'db';
import { LoggedInUserContext } from '../App';
import { ItemsColumn } from '../../Components/Layout';
import { base62uuid, randomRGB, rgbToHex } from '../../Util';
import { CurrentSeason } from 'db';
import { CreaturePortrait } from '../CreaturePortrait';
import { AudioPlayerInstance } from '../../GpuWorld/Audio';
import { SoundEffect } from '../../GpuWorld/StateLayout';

export function newRandomCreature(): DbCreature {
  return {
    season: CurrentSeason,
    created: firebase.firestore.Timestamp.now(),
    deleted: false,
    ownerId: '',
    generation: 0,
    tags: [],
    bounties: {},

    primaryColor: randomRGB(),
    secondaryColor: randomRGB(),
    ears: 1 + Math.floor(Math.random() * 4),
    eyes: 1 + Math.floor(Math.random() * 5),
    backSpikes: 1 + Math.floor(Math.random() * 7),
  }
}
export function createDefStyleFromBase(base: DbCreature) {
  return {
    primaryColor: rgbToHex(base.primaryColor),
    secondaryColor: rgbToHex(base.secondaryColor),
    ears: base.ears,
    eyes: base.eyes,
    backSpikes: base.backSpikes,
  }
}

export function NewCreatureDialog({ onCreate, eggItem }: { onCreate: () => void, eggItem: DbItemInstance & DbId }) {
  const speciesId = React.useRef(base62uuid());
  const [name, setName] = React.useState<string>('');
  const [creature] = React.useState(newRandomCreature());
  const loggedInUser = React.useContext(LoggedInUserContext);
  if (!loggedInUser) {
    return null;
  }
  return <ItemsColumn className="CreatePixlingDialog">
    <CreaturePortrait
      unit={{ type: 'creature', slots: [], ...createDefStyleFromBase(creature) }}
      />
    <input type="text" value={name} onChange={e => setName(e.target.value)} placeholder="Name" />

    <Button className="CreateCreature" primary={true} disabled={!name} onPerform={async () => {
      await firebase.firestore().runTransaction(async t => {
        t.set(firebase.firestore().collection(Db.creatures).doc(speciesId.current), full<DbCreature>({
          ...creature,
          ownerId: loggedInUser!.uid,
          created: firebase.firestore.Timestamp.now(),
          name,
        }));
        t.update(firebase.firestore().collection(Db.items).doc(eggItem.id), {
          deleted: true,
          hatchTime: firebase.firestore.Timestamp.now()
        });
        AudioPlayerInstance?.playSoundEffect(SoundEffect.Bite);
      });
      onCreate();
    }}>
      Hatch egg!
    </Button>
  </ItemsColumn>
}
