import * as Vector2 from '../Math/Vector2';
import * as Vector3 from '../Math/Vector3';
import * as SphericalCoordinate from './SphericalCoordinate';
import * as Math2 from '../Math/Math';

interface AnimateEaseTo<T> {
  target: T;
  speed?: number;
  stopAnimateDistance?: number;
}
export function isAnimateEaseTo<T>(obj: any): obj is AnimateEaseTo<T> {
  return typeof obj === 'object' && obj.target !== undefined;
}
function animateEaseToWithDefaults<T>({ target, speed = 0.1, stopAnimateDistance = 0.001 }: AnimateEaseTo<T>): Required<AnimateEaseTo<T>> {
  return { target, speed, stopAnimateDistance };
}

export interface Animatable<T> {
  current: T;
  animate?: AnimateEaseTo<T>;
}
interface AnimatableValueImpl<T> {
  mix: (a: T, b: T, p: number) => T;
  distance: (a: T, b: T) => number;
  isEqual: (a: T, b: T) => boolean;
}
export function createAnimator<T>({ mix, distance, isEqual }: AnimatableValueImpl<T>) {
  return {
    create(current: T): Animatable<T> {
      return { current };
    },
    target(value: Animatable<T>) {
      if (value.animate !== undefined) {
        return value.animate.target;
      } else {
        return value.current;
      }
    },
    nextValue(value: Animatable<T>, dt: number): Animatable<T> {
      if (value.animate !== undefined) {
        const { target, stopAnimateDistance, speed } = animateEaseToWithDefaults(value.animate);
        const d = distance(value.current, target);
        if (d > stopAnimateDistance) {
          return {
            // See https://www.reddit.com/r/gamedev/comments/cayb4f/basic_smooth_spring_movement/
            current: mix(value.animate.target, value.current, Math.pow(1 - speed, dt * 60 / 1000)),
            animate: value.animate
          };
        } else {
          return {
            current: value.animate.target,
            animate: undefined
          };
        }
      } else {
        return value;
      }
    },
    easeTo(animatable: Animatable<T>, value: AnimateEaseTo<T> | T): Animatable<T> {
      if (!isAnimateEaseTo(value)) {
        value = { target: value };
      }
      if (isEqual(animatable.current, value.target)) {
        if (animatable.animate) {
          return { current: value.target };
        } else {
          return animatable;
        }
      } else {
        const { target, stopAnimateDistance } = animateEaseToWithDefaults(value);
        if (distance(animatable.current, target) < stopAnimateDistance) {
          return { current: value.target };
        } else {
          return { current: animatable.current, animate: value };
        }
      }
    }
  }
}

export const Vec2Anim = createAnimator(Vector2);
export const Vec3Anim = createAnimator(Vector3);
export const NumberAnim = createAnimator({ mix: Math2.mix, distance: (a, b) => Math.abs(a - b), isEqual: (a, b) => a === b });
export const SphericalCoordinateAnim = createAnimator(SphericalCoordinate);

export function animatableShouldUpdate<T>(animatable: Animatable<T>) {
  return animatable.animate !== undefined;
}

export function animatablesShouldUpdate(...animatables: Array<Animatable<any>>) {
  for (const a of animatables) {
    if (animatableShouldUpdate(a)) {
      return true;
    }
  }
  return false;
}
