export function mirrorPosition(p: Vector3.Vector3, worldSize: Size) {
  return {
    x: worldSize.width - p.x,
    y: worldSize.height - p.y,
    z: p.z
  }
}

export const ReplayCameraClipKey = 'ReplayCamera';

export class GpuWorldStateLayout {

  constructor(private config: InitWorldStateConfig) {}

  worldSize = { width: 128, height: 128 };

  brain2 = {
    hiddenLayerOutputSlices: 8,
  }

  arenas = this.config.arenaCount;
  arenasSize = arenaSizeFromCount(this.config.arenaCount);
  arenasRect = {
    x: 0, y: 0,
    ...this.arenasSize
  }
  arenasSizeVec2 = vec2FromSize(this.arenasSize);

  arenasWorldSize = {
    width: this.worldSize.width * this.arenasSize.width,
    height: this.worldSize.height * this.arenasSize.height,
  };
  arenasWorldSizeVec2 = vec2FromSize(this.arenasWorldSize);

  heatmapSize = {
    width: Math.max(1, this.worldSize.width / 32),
    height: Math.max(1, this.worldSize.height / 32),
  }
  heatmapSizeVec2 = vec2FromSize(this.heatmapSize);
}
function arenaSizeFromCount(count: number) {
  const maxWidth = 32;
  return {
    width: count <= maxWidth ? count : maxWidth,
    height: Math.ceil(count / maxWidth)
  }
}

