import * as React from 'react';
import { GlState } from "../../GpuUtil/GlState";

interface GlUnitTest {
  name: string;
  test: (gls: GlState) => void | Promise<void>;
}

export const GlUnitTests: GlUnitTest[] = [];

export function RunGlUnitTests() {
  const [results, setResults] = React.useState<{ [name: string]: string }>({});
  const canvas = React.useRef<HTMLCanvasElement | null>(null);
  React.useEffect(() => {
    if (canvas.current) {
      (async () => {
        const gl = canvas.current!.getContext('webgl2')!;
        const gls = new GlState(canvas.current!, gl);
        let res = {};
        for (const test of GlUnitTests) {
          res = {...res, [test.name]: 'running...' };
          setResults(res);
          try {
            let r = test.test(gls);
            if (r instanceof Promise) {
              r = await r;
            }
            res = {...res, [test.name]: 'ok' };
            setResults(res);
          } catch (err) {
            res = {...res, [test.name]: err.toString() };
            setResults(res);
          }
        }
      })();
    }
  }, []);
  return <div>
    <canvas width={1} height={1} ref={canvas} />
    {Object.keys(results).map((k) => <div key={k}>{k}: {results[k]}</div>)}
  </div>
}