import * as React from 'react';
import { EditorProps, NonNullEditorDispatch } from './EditorProps';
import { Popdown } from '../Popdown';

export function getSizeOfInput(text: string, className: string | undefined) {
  const tmp = document.createElement("span");
  tmp.className = className || '';
  tmp.innerHTML = text.replace(/&/g, '&amp;').replace(/</g, '&lt;').replace(/>/g, '&gt;');
  document.body.appendChild(tmp);
  const size = tmp.getBoundingClientRect();
  document.body.removeChild(tmp);
  return size;
}

export type WidthMode = 'fit-content' | 'fill' | 'fixed';

export interface StringEditorProps extends EditorProps<string> {
  multiline?: boolean;
  rows?: number;
  className?: string;
  placeholder?: string;
  widthMode?: WidthMode;
  spellCheck?: boolean;
  onClick?: (event: React.MouseEvent<HTMLElement>) => void;
  validate?: (value: string) => string | undefined;
}

export function useStringValidation(value: string | undefined, validate: ((value: string) => string | undefined) | undefined, onChange: (value: string) => void) {
  const [editValue, setEditValue] = React.useState<string>('');
  const [validationError, setValidationError] = React.useState<string | undefined>();
  const [committed, setCommitted] = React.useState<boolean>(true);
  React.useEffect(() => {
    if (committed) {
      setEditValue(value || '');
      setCommitted(true);
      setValidationError(validate ? validate(value || '') : undefined);
    }
  }, [value, validate, committed]);

  const handleChange = (event: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {
    setEditValue(event.target.value);
    setCommitted(false);
    setValidationError(validate ? validate(event.target.value) : undefined);
  }
  const tryCommit = () => {
    if (validationError === undefined && !committed) {
      onChange(editValue);
      setCommitted(true);
    }
  }
  const handleKeyDown = (event: React.KeyboardEvent<HTMLInputElement | HTMLTextAreaElement>) => {
    if (event.key === 'Enter') {
      tryCommit();
    } else if (event.key === 'Escape') {
      setEditValue(value || '');
      setCommitted(true);
      setValidationError(validate ? validate(value || '') : undefined);
    }
  }
  const handleBlur = () => {
    tryCommit();
  }
  return {
    props: {
      value: editValue || '',
      onChange: handleChange,
      onKeyDown: handleKeyDown,
      onBlur: handleBlur,
      className: `${!validationError ? '' : 'InvalidInput'} ${committed ? '' : 'StringEditorUncommited'}`
    },
    validationError,
    editValue,
    setEditValue: value => {
      setEditValue(value);
      setCommitted(false);
      setValidationError(validate ? validate(value || '') : undefined);
    }
  }
}

export function ValidationErrorPopdown({ validationError, children }: { validationError: string | undefined, children?: any }) {
  return <Popdown
    showPopdown={!!validationError}
    popdown={() => <div className="StringEditorValidationError">{validationError}</div>}
    fader={false}
    >{children}</Popdown>;
}

export function StringEditor({ className = '', multiline, readOnly, rows = 1,
    value, widthMode, onChange, validate, placeholder, spellCheck }: StringEditorProps & NonNullEditorDispatch<string>) {
  const { props, validationError, editValue } = useStringValidation(value, validate, onChange);

  const cn = `StringEditor ${props.className} ${className}`;
  let input;
  if (multiline) {
    input = (
      <textarea
        {...props}
        className={cn}
        rows={rows}
        spellCheck={spellCheck}
      />
    );
  } else {
    input = (
      <input
        {...props}
        className={cn}
        type="text"
        style={{
          width: widthMode === 'fill' ? '100%' :
          widthMode === 'fit-content' ? (getSizeOfInput(editValue || placeholder || '', className).width + 20) :
          '120px' }}
        readOnly={readOnly}
        placeholder={placeholder}
        spellCheck={spellCheck}
      />
    );
  }
  return <ValidationErrorPopdown validationError={validationError}>{input}</ValidationErrorPopdown>;
}
