import { GlState } from "../../GpuUtil/GlState";
import { ArchetypesQuery } from "../../GpuUtil/ECS/Query";
import { shaderModule, uniformDef, funcDef } from "../../GpuUtil/ShaderModule";
import { Position } from "./Transform";
import { Team, Gold, PreviousGold, BountyComponents } from "./UnitComponents";
import { Teams, SideSize, StatuePosition, StepsInBattle } from "../StateLayout";
import { GpuWorldState } from "../State";
import { UnitsInArena, ArenaStats, ArenaWinner } from "./ArenaStats";
import { ArenaInstance } from "./Components";
import { vec2FromSize } from "../../Math/Math";
import { StepsPerSecond } from "../../Time";
import { UnitFieldPositions } from "db";

const UnitFieldPosition = shaderModule('UnitFieldPosition', [
  Team.sm, Position.sm,
], {
  worldSize: uniformDef('vec2', d => vec2FromSize(d.data.worldSize)),
  statuePosition: uniformDef('vec3', d => StatuePosition),
  get: funcDef('int', [['ivec2', 'entityCell']], `
    int team = ${Team.sm.value}(entityCell);
    vec3 position = ${Position.sm.value}(entityCell);
    vec2 mapUp = team == ${Teams.HomeTeam} ? normalize(vec2(1, 1)) : -normalize(vec2(1, 1));
    vec2 mapCenter = Self.worldSize / 2.;
    vec2 positionD = position.xy - mapCenter;
    float positionUpDown = dot(positionD, mapUp) / length(mapCenter);
    bool inEnemyTerritory = positionUpDown > 0.;
    vec3 friendStatuePosition = vec3(Self.worldSize, 0) - Self.statuePosition;
    vec3 enemyStatuePosition = Self.statuePosition;
    if (team == ${Teams.HomeTeam}) {
      enemyStatuePosition = friendStatuePosition;
      friendStatuePosition = Self.statuePosition;
    }
    if (inEnemyTerritory) {
      bool inEnemyBase = distance(position, enemyStatuePosition) < 5.;
      if (inEnemyBase) {
        return ${UnitFieldPositions.AwayBase};
      } else {
        return ${UnitFieldPositions.AwayTerritory};
      }
    } else {
      bool inFriendBase = distance(position, friendStatuePosition) < 5.;
      if (inFriendBase) {
        return ${UnitFieldPositions.HomeBase};
      } else {
        return ${UnitFieldPositions.HomeTerritory};
      }
    }
  `)
})

export class UpdateTerritoryBounty {
  constructor(private gls: GlState) { }
  query = new ArchetypesQuery([Gold.component])
    .updateGpu(this.gls, [Gold.component], shaderModule([
      Gold.sm, Gold.writer, UnitFieldPosition,
      BountyComponents.timeSpentHomeBase.sm, BountyComponents.timeSpentHomeTerritory.sm,
      BountyComponents.timeSpentAwayBase.sm, BountyComponents.timeSpentAwayTerritory.sm,
    ], {
      update: funcDef('void', [['ivec2', 'entityCell']], `
        float gold = ${Gold.sm.value}(entityCell);
        int fieldPosition = ${UnitFieldPosition.get}(entityCell);
        float bounty = fieldPosition == ${UnitFieldPositions.HomeBase} ? ${BountyComponents.timeSpentHomeBase.sm.value}(entityCell) :
          fieldPosition == ${UnitFieldPositions.HomeTerritory} ? ${BountyComponents.timeSpentHomeTerritory.sm.value}(entityCell) :
          fieldPosition == ${UnitFieldPositions.AwayTerritory} ? ${BountyComponents.timeSpentAwayTerritory.sm.value}(entityCell) :
          fieldPosition == ${UnitFieldPositions.AwayBase} ? ${BountyComponents.timeSpentAwayBase.sm.value}(entityCell) :
          0.;
        gold += bounty;
        ${Gold.writer.write}(gold);
      `)
    }));
  run(state: GpuWorldState) {
    if ((state.data.stepCounter - state.data.battleStartStep) % (StepsPerSecond * 5) === 0) {
      this.query.run(state);
    }
  }
}

export class UpdateTeamSpirit {
  constructor(private gls: GlState) { }
  query = new ArchetypesQuery([Gold.component, PreviousGold.component])
    .updateGpu(this.gls, [Gold.component], shaderModule([
      Gold.sm, Gold.writer, PreviousGold.sm,
      UnitsInArena, ArenaInstance.sm, Team.sm,
      BountyComponents.teamSpirit.sm
    ], {
      update: funcDef('void', [['ivec2', 'entityCell']], `
        float gold = ${Gold.sm.value}(entityCell);
        float previousGold = ${PreviousGold.sm.value}(entityCell);
        int arenaIndex = ${ArenaInstance.sm.value}(entityCell);
        int team = ${Team.sm.value}(entityCell);
        int teamOffset = team == ${Teams.HomeTeam} ? 0 : ${SideSize};
        float teamGoldGain = 0.;
        int nTeamMates = 0;
        for (int i=1; i < ${SideSize}; i++) {
          ivec2 unit = ${UnitsInArena.unit}(arenaIndex, teamOffset + i);
          if (unit.x >= 0) {
            float unitGold = ${Gold.sm.value}(unit);
            float unitPreviousGold = ${PreviousGold.sm.value}(unit);
            float gain = unitGold - unitPreviousGold;
            teamGoldGain += gain;
            nTeamMates++;
          }
        }
        if (nTeamMates > 0) {
          float gain = gold - previousGold;
          float avgTeamGain = teamGoldGain / float(nTeamMates);
          float teamSpirit = ${BountyComponents.teamSpirit.sm.value}(entityCell);
          gold = previousGold + teamSpirit * avgTeamGain + (1. - teamSpirit) * gain;
        }
        ${Gold.writer.write}(gold);
      `)
    }));
  run(state: GpuWorldState) {
    this.query.run(state);
  }
}

