export class UpdateGoldTimeScaling {
  constructor(private gls: GlState) { }
  query = new ArchetypesQuery([Gold.component])
    .updateGpu(this.gls, [Gold.component], shaderModule([
      Gold.sm, PreviousGold.sm, Gold.writer,
      BountyComponents.timeScaling.sm
    ], {
      time: uniformDef('float', d => (d.data.stepCounter - d.data.battleStartStep) / StepsInBattle),
      update: funcDef('void', [['ivec2', 'entityCell']], `
        float gold = ${Gold.sm.value}(entityCell);
        float previousGold = ${PreviousGold.sm.value}(entityCell);
        float goldGain = gold - previousGold;
        goldGain = mix(goldGain, goldGain * ${BountyComponents.timeScaling.sm.value}(entityCell), time);
        gold = previousGold + goldGain;
        ${Gold.writer.write}(gold);
      `)
    }));
  run(state: GpuWorldState) {
    this.query.run(state);
  }
}

export class UpdatePreviousGold {
  constructor(private gls: GlState) { }
  query = new ArchetypesQuery([PreviousGold.component])
    .updateGpu(this.gls, [PreviousGold.component], shaderModule([
      Gold.sm, PreviousGold.writer
    ], {
      update: funcDef('void', [['ivec2', 'entityCell']], `
        float gold = ${Gold.sm.value}(entityCell);
        ${PreviousGold.writer.write}(gold);
      `)
    }));
  run(state: GpuWorldState) {
    this.query.run(state);
  }
}

export class UpdateVictoryLossBounty {
  constructor(private gls: GlState) { }
  query = new ArchetypesQuery([Gold.component])
    .updateGpu(this.gls, [Gold.component], shaderModule([
      Gold.sm, Gold.writer, ArenaInstance.sm, Team.sm,
      ArenaStats, BountyComponents.victory.sm, BountyComponents.tie.sm, BountyComponents.loss.sm,
    ], {
      update: funcDef('void', [['ivec2', 'entityCell']], `
        float gold = ${Gold.sm.value}(entityCell);
        int arena = ${ArenaInstance.sm.value}(entityCell);
        int winner = ${ArenaStats.stats}(arena).winner;
        int team = ${Team.sm.value}(entityCell);
        if ((team == ${Teams.HomeTeam} && winner == ${ArenaWinner.Home}) || (team == ${Teams.AwayTeam} && winner == ${ArenaWinner.Away})) {
          gold += ${BountyComponents.victory.sm.value}(entityCell);
        } else if ((team == ${Teams.HomeTeam} && winner == ${ArenaWinner.Away}) || (team == ${Teams.AwayTeam} && winner == ${ArenaWinner.Home})) {
          gold += ${BountyComponents.loss.sm.value}(entityCell);
        } else {
          gold += ${BountyComponents.tie.sm.value}(entityCell);
        }
        ${Gold.writer.write}(gold);
      `)
    }));
  run(state: GpuWorldState) {
    this.query.run(state);
  }
}