import { mat4 } from 'gl-matrix';
import { clamp, mod } from '../Math/Math';
import * as Vector3 from '../Math/Vector3';
import * as Vector2 from '../Math/Vector2';
import { SphericalCoordinate } from '../Math/SphericalCoordinate';

export interface OrthographicProjection {
  type: 'Ortho',
  left: number;
  right: number;
  bottom: number;
  top: number;
  near: number;
  far: number;
}
export interface PerspectiveProjection {
  type: 'Perspective',
  fovy: number;
  near: number;
  far: number;
  aspectRatio: number;
}

export type CameraProjection = OrthographicProjection | PerspectiveProjection;

export interface LookatView {
  type: 'LookatView';
  eye: Vector3.Vector3;
  lookAt: Vector3.Vector3;
  up: Vector3.Vector3;
  screenOffset: Vector2.Vector2;
}

export function maxDistanceLookatCamera(a: LookatView, b: LookatView) {
  return Math.max(Vector3.distance(a.eye, b.eye), Vector3.distance(a.lookAt, b.lookAt), Vector3.distance(a.up, b.up));
}

export interface SphericalView {
  type: 'SphericalView';
  rotation: SphericalCoordinate;
  lookAt: Vector3.Vector3;
  up: Vector3.Vector3;
  screenOffset: Vector2.Vector2;
}

export type CameraView = SphericalView | LookatView;

export interface Camera<View = CameraView, Projection = CameraProjection> {
  view: View;
  projection: Projection;
};
export type LookatCamera = Camera<LookatView, CameraProjection>;

export function lookatViewZoomTowards(view: LookatView, point: Vector3.Vector3, percent: number): CameraView {
  const lookatDelta = Vector3.mul(Vector3.sub(point, view.lookAt), percent);
  const positionDelta = Vector3.mul(Vector3.sub(point, view.eye), percent);
  return {
    ...view,
    lookAt: Vector3.add(view.lookAt, lookatDelta),
    eye: Vector3.add(view.eye, positionDelta),
  };
}
export function sphericalViewZoomTowards(view: SphericalView, point: Vector3.Vector3, percent: number, minRadius: number, maxRadius: number): SphericalView {
  const newRadius = clamp(view.rotation.radius - view.rotation.radius * percent, minRadius, maxRadius);
  percent = (1.0 - newRadius / view.rotation.radius);
  const lookatDelta = Vector3.mul(Vector3.sub(point, view.lookAt), percent);
  return {
    ...view,
    lookAt: Vector3.add(view.lookAt, lookatDelta),
    rotation: {
      ...view.rotation,
      radius: newRadius
    }
  };
}
export function sphericalViewZoom(view: SphericalView, percent: number, minRadius: number, maxRadius: number): SphericalView {
  const newRadius = clamp(view.rotation.radius - view.rotation.radius * percent, minRadius, maxRadius);
  return {
    ...view,
    rotation: {
      ...view.rotation,
      radius: newRadius
    }
  };
}
export function cameraDistance(camera: Camera): number {
  if (camera.view.type === 'LookatView') {
    return Vector3.length(Vector3.sub(camera.view.eye, camera.view.lookAt));
  } else {
    return camera.view.rotation.radius;
  }
}

export function eyeOfCamera(camera: Camera) {
  if (camera.view.type === 'LookatView') {
    return camera.view.eye;
  } else {
    return Vector3.add(camera.view.lookAt, Vector3.fromSphericalCoordinates(camera.view.rotation));
  }
}

export interface CameraMatrices {
  view: mat4;
  projection: mat4;
  viewProjection: mat4;
  invViewProjection: mat4 | null;
}
export function cameraMatricesFromCamera(camera: Camera): CameraMatrices {
  let proj;
  if (camera.projection.type === 'Ortho') {
    proj = mat4.ortho(mat4.create(), camera.projection.left, camera.projection.right, camera.projection.bottom, camera.projection.top, camera.projection.near, camera.projection.far);
  } else {
    proj = mat4.perspective(mat4.create(), camera.projection.fovy, camera.projection.aspectRatio, camera.projection.near, camera.projection.far);
  }
  const position = eyeOfCamera(camera);
  const view = mat4.lookAt(mat4.create(), Vector3.toGlVec3(position), Vector3.toGlVec3(camera.view.lookAt), Vector3.toGlVec3(camera.view.up));
  // mat4.mul(proj, mat4.translate(mat4.create(), mat4.identity(mat4.create()), [camera.view.screenOffset.x, camera.view.screenOffset.y, 0]), proj);

  const viewProjection = mat4.multiply(mat4.create(), proj, view);
  return {
    view,
    projection: proj,
    viewProjection,
    invViewProjection: mat4.invert(mat4.create(), viewProjection)
  }
}

export function rotateSphericalCoordinates(rotation: SphericalCoordinate, phi: number, theta: number, thetaLimit = 0.1) {
  return {
    theta: clamp(rotation.theta + theta, thetaLimit, Math.PI - thetaLimit),
    phi: mod(rotation.phi + phi, Math.PI * 2),
    radius: rotation.radius
  };
}

export function rotateSphericalCamera(camera: SphericalView, phi: number, theta: number, thetaLimit?: number) {
  return {
    ...camera,
    rotation: rotateSphericalCoordinates(camera.rotation, phi, theta, thetaLimit)
  };
}

export function rotateLookatCamera(camera: LookatView, phi: number, theta: number, thetaLimit?: number): LookatView {
  const s = Vector3.toSphericalCoordinates(Vector3.sub(camera.lookAt, camera.eye));
  const newS = rotateSphericalCoordinates(s, phi, theta, thetaLimit);
  return {
    ...camera,
    eye: Vector3.add(camera.lookAt, Vector3.fromSphericalCoordinates(newS))
  };
}

export function ortographicProjectionFitPoints(camera: LookatView, points: Vector3.Vector3[]): OrthographicProjection {
  const view = mat4.lookAt(mat4.create(), Vector3.toGlVec3(camera.eye), Vector3.toGlVec3(camera.lookAt), Vector3.toGlVec3(camera.up));
  let min;
  let max;
  for (const point of points) {
    const p = Vector3.transform(point, view);
    if (!min && !max) {
      min = {...p };
      max = {...p };
    } else {
      min.x = Math.min(min.x, p.x);
      min.y = Math.min(min.y, p.y);
      min.z = Math.min(min.z, p.z);

      max.x = Math.max(max.x, p.x);
      max.y = Math.max(max.y, p.y);
      max.z = Math.max(max.z, p.z);
    }
  }
  return {
    type: 'Ortho',
    left: min.x,
    right: max.x,
    bottom: min.y,
    top: max.y,
    near: -max.z,
    far: -min.z,
  }
}