import { shaderModule, uniformDef, structDef, funcDef, constDef } from "../../GpuUtil/ShaderModule";
import { GpuLookupTableSM } from "../../GpuUtil/ECS/Query";
import { Hitpoints, Team, WorthPoints, UnitStats, Gold } from "./UnitComponents";
import { Position } from "./Transform";
import { Teams, ArenaMembersSize, SideSize } from "../StateLayout";
import { vec2FromSize } from "../../Math/Math";

export enum ArenaWinner {
  Undecided,
  Home,
  Away,
  Tie
}

export const UnitsInArena = shaderModule('UnitsInArena', [GpuLookupTableSM], {
  size: constDef('int', '' + ArenaMembersSize),
  sampler: uniformDef('isampler2D', d => d.unitsInArenaGpu.get(d)),
  unit: funcDef('ivec2', [['int', 'arenaIndex'], ['int', 'arenaMemberIndex']], `
    return ${GpuLookupTableSM.entityCell}(Self.sampler, arenaIndex, arenaMemberIndex);
  `)
});

export const DistanceToClosestUnit = shaderModule('DistanceToClosestUnit', [
  UnitsInArena, Team.sm, Hitpoints.sm, Position.sm
], {
  value: funcDef('float', [['int', 'arenaIndex'], ['vec3', 'position'], ['int', 'team']], `
    float minDist = 999999.;
    for (int l=0; l < ${UnitsInArena.size}; l++) {
      ivec2 other = ${UnitsInArena.unit}(arenaIndex, l);
      if (other.x >= 0 && ${Team.sm.value}(other) == team && ${Hitpoints.sm.value}(other) > 0.) {
        vec3 otherPosition = ${Position.sm.value}(other);
        float d = distance(position, otherPosition);
        if (d < minDist) {
          minDist = d;
        }
      }
    }
    return minDist;
  `)
})

export const TeamStats = shaderModule('TeamStats', [
  Hitpoints.sm, Position.sm, UnitStats.AliveTime.sm, UnitStats.CumulativeHitpoints.sm,
  WorthPoints.sm, UnitsInArena, UnitStats.MinDistEnemyStatue.sm, Gold.sm
], {
  Stats: structDef({
    alive: 'int',
    unitsAlive: 'int',
    pointsLoss: 'int',
    hitpoints: 'float',
    aliveTime: 'float',
    cumulativeHitpoints: 'float',
    minDistEnemyStatue: 'float',
    gold: 'float',
  }),
  worldSize: uniformDef('vec2', d => vec2FromSize(d.data.worldSize)),
  stats: funcDef('Self.Stats', [['int', 'arenaIndex'], ['int', 'team']], `
    Self.Stats stats;
    stats.minDistEnemyStatue = Self.worldSize.x;
    ivec2 enemyStatue = ${UnitsInArena.unit}(arenaIndex, team == ${Teams.HomeTeam} ? ${SideSize} : 0);
    for (int l=0; l < ${SideSize}; l++) {
      int i = l + (team == ${Teams.AwayTeam} ? ${SideSize} : 0);
      ivec2 unit = ${UnitsInArena.unit}(arenaIndex, i);
      if (unit.x < 0) {
        continue;
      }
      float hitpoints = ${Hitpoints.sm.value}(unit);

      if (hitpoints > 0.0) {
        stats.alive++;
        if (l != 0) {
          stats.unitsAlive++;
        }
      } else {
        stats.pointsLoss += ${WorthPoints.sm.value}(unit);
      }
      stats.hitpoints += hitpoints;
      stats.aliveTime += ${UnitStats.AliveTime.sm.value}(unit);
      stats.cumulativeHitpoints += ${UnitStats.CumulativeHitpoints.sm.value}(unit);
      vec3 position = ${Position.sm.value}(unit);
      stats.minDistEnemyStatue = min(stats.minDistEnemyStatue, ${UnitStats.MinDistEnemyStatue.sm.value}(unit));
      stats.gold += ${Gold.sm.value}(unit);
    }

    return stats;
  `)
});

export const ArenaStats = shaderModule('ArenaStats', [
  TeamStats,
], {
  Stats: structDef({
    home: 'TeamStats_Stats',
    away: 'TeamStats_Stats',
    homePoints: 'int',
    awayPoints: 'int',
    winner: 'int',
  }),
  homeCount: uniformDef('int', d => d.data.teams.home.unitCount),
  awayCount: uniformDef('int', d => d.data.teams.away.unitCount),
  stats: funcDef('Self.Stats', [['int', 'arenaIndex']], `
    Self.Stats stats;
    stats.home = ${TeamStats.stats}(arenaIndex, ${Teams.HomeTeam});
    stats.away = ${TeamStats.stats}(arenaIndex, ${Teams.AwayTeam});
    stats.homePoints = stats.away.pointsLoss;
    stats.awayPoints = stats.home.pointsLoss;

    stats.winner = ${ArenaWinner.Tie};
    if (stats.homePoints > stats.awayPoints) {
      stats.winner = ${ArenaWinner.Home};
    } else if (stats.homePoints < stats.awayPoints) {
      stats.winner = ${ArenaWinner.Away};
    }
    return stats;
  `)
});
