import { shaderModule, funcDef, uniformDef } from "../../GpuUtil/ShaderModule";
import { Vertex } from "../../GpuUtil/Mesh";
import { Vector2SM } from "../../GpuUtil/SM2Utils";


export const LightSM2 = shaderModule([], {
  lightDirection: uniformDef('vec3', d => d.derived.renderLightDirection),
  light: funcDef('float', [['vec3', 'normal']], `
    return dot(normal, Self.lightDirection) > 0.0 ? 1.0 : 0.0;
  `)
});

export const FogGLSL = `
float fogFactorExp2(
  const float dist,
  const float density
) {
  const float LOG2 = -1.442695;
  float d = density * dist;
  return 1.0 - clamp(exp2(d * d * LOG2), 0.0, 1.0);
}
float fogAmount(float fogDensity) {
  return fogFactorExp2(gl_FragCoord.z / gl_FragCoord.w, fogDensity);
}
`
export const FogSM2 = shaderModule([], {
  fogFactorExp2: funcDef('float', [['float', 'dist'], ['float', 'density']], `
    const float LOG2 = -1.442695;
    float d = density * dist;
    return 1.0 - clamp(exp2(d * d * LOG2), 0.0, 1.0);
  `)
})

export const GrayscaleGLSL = `
vec3 grayscale(vec3 color) {
  return vec3(dot(color.rgb, vec3(0.299, 0.587, 0.114)));
}
`

export const QuadLineSM = shaderModule('QuadLine', [Vertex, Vector2SM], {
  vertexPosition01: funcDef('vec2', [], `
    return (${Vertex.position}.xy + 1.0) / 2.0;
  `),
  fromPoints: funcDef('vec3', [['vec3', 'backLeft'], ['vec3', 'backRight'], ['vec3', 'frontLeft'], ['vec3', 'frontRight']], `
    return mix(
      mix(backLeft, backRight, Self.vertexPosition01().y),
      mix(frontLeft, frontRight, Self.vertexPosition01().y),
      Self.vertexPosition01().x
    );
  `),
  fromLineWithDeltas: funcDef('vec3', [['vec3', 'start'], ['vec3', 'startDelta'], ['vec3', 'end'], ['vec3', 'endDelta']], `
    return Self.fromPoints(
      start - startDelta,
      start + startDelta,
      end - endDelta,
      end + endDelta
    );
  `),
  fromLineWithDelta: funcDef('vec3', [['vec3', 'start'], ['vec3', 'end'], ['vec3', 'delta']], `
    return Self.fromLineWithDeltas(
      start,
      delta,
      end,
      delta
    );
  `),
  fromLineWithWidths: funcDef('vec3', [['vec3', 'start'], ['float', 'startWidth'], ['vec3', 'end'], ['float', 'endWidth']], `
    vec2 v = ${Vector2SM.perpendicular}(normalize((end - start).xy));
    return Self.fromLineWithDeltas(
      start,
      vec3(v * startWidth / 2.0, 0),
      end,
      vec3(v * endWidth / 2.0, 0)
    );
  `),
  fromLineWithWidth: funcDef('vec3', [['vec3', 'start'], ['vec3', 'end'], ['float', 'width']], `
    return Self.fromLineWithWidths(
      start,
      width,
      end,
      width
    );
  `),
});

