import * as React from 'react';
import { useFirebaseStreamDocument, FirebaseLoading, FirebaseStreamDocumentResult } from '../../Components/Firebase';
import { DbUserSettings, Db, getTeamId, CurrentSeason, partial, DbCreature, DbItemInstance, DbUserProfile, DbBattleground } from 'db';
import { MaterialIconButton, Button } from '../../Components/Button';
import { Popup } from '../../Components/Popup';
import { ScrollVertical, VerticalLeft, Column, Row } from '../../Components/Layout';
import { AnyEditor } from '../../Components/Editors';
import firebase from 'firebase/app';
import { editorStructTypedef } from '../../Components/Editors/EditorTypedefs';
import { AudioPlayerInstance } from '../../GpuWorld/Audio';
import { useDialog, Dialog } from '../../Components/Dialog';

export function UserSettingsButton({ loggedInUser }: { loggedInUser: firebase.User }) {
  const [showPopup, setShowPopup] = React.useState(false);
  return <span className="UserSettings">
    <MaterialIconButton flat={true} onClick={() => setShowPopup(!showPopup)}
      icon="settings" purpose="OpenUserSettings" title="User settings" />
    {showPopup && <Popup fullscreen={true} onClose={() => setShowPopup(false)}>
      <UserSettings loggedInUser={loggedInUser} />
    </Popup>}
  </span>
}

const UserSettingsTypedef = editorStructTypedef<DbUserSettings>({
  properties: {
    sound: { metatype: 'boolean' },
    volume: { metatype: 'float', suffix: '%' },
    musicVolume: { metatype: 'float', suffix: '%' },
    environmentVolume: { metatype: 'float', suffix: '%' },
    soundEffectsVolume: { metatype: 'float', suffix: '%' },
    mouseWheelSensitivity: { metatype: 'float' },
    advancedTrainingSettings: { metatype: 'boolean' }
  }
});

function UserSettings({ loggedInUser }: { loggedInUser: firebase.User }) {
  const userSettings = useFirebaseStreamDocument<DbUserSettings>(Db.userSettings, loggedInUser.uid);
  const userProfile = useFirebaseStreamDocument<DbUserProfile>(Db.userProfiles, loggedInUser.uid);
  const dialog = useDialog();
  if (userSettings.status !== 'loaded' || userProfile.status !== 'loaded') {
    return <FirebaseLoading pending={[userSettings]} />;
  }
  return <Column>
    <Dialog state={dialog.current} />
    <h3>Settings</h3>
    <Row>
      <Button onPerform={async () => {
          if (!loggedInUser.email) {
            await dialog.alert('No email associated with this account, cannot reset password.');
          } else if (await dialog.confirm('Send password reset email?')) {
            await firebase.auth().sendPasswordResetEmail(loggedInUser.email);
          }
        }}>
          Reset password
      </Button>
      <Button onPerform={async () => {
        const newName = await dialog.prompt('New name', userProfile.value.displayName);
          if (newName) {
            await firebase.firestore().collection(Db.userProfiles).doc(loggedInUser.uid).update(partial<DbUserProfile>({
              displayName: newName
            }));
          }
        }}>
        Change name ({userProfile.value.displayName})
      </Button>
      <Button onPerform={async () => {
        if (await dialog.confirm('Are you sure? This will reset the game for you completely; it will remove all creatures, items and team points.')) {
          const items = await firebase.firestore().collection(Db.items)
            .where('ownerId', '==', loggedInUser.uid)
            .where('season', '==', CurrentSeason)
            .get();
          const batch = firebase.firestore().batch();
          for (const item of items.docs) {
            batch.update(item.ref, partial<DbItemInstance>({
              deleted: true
            }));
          }
          const creatures = await firebase.firestore().collection(Db.creatures)
            .where('ownerId', '==', loggedInUser.uid)
            .where('season', '==', CurrentSeason)
            .get();
          for (const creature of creatures.docs) {
            batch.update(creature.ref, partial<DbCreature>({
              deleted: true
            }));
          }
          const soloTeam = firebase.firestore().collection(Db.teams).doc(getTeamId(loggedInUser.uid, CurrentSeason, DbBattleground.Solo));
          const duoTeam = firebase.firestore().collection(Db.teams).doc(getTeamId(loggedInUser.uid, CurrentSeason, DbBattleground.Duo));
          const squadTeam = firebase.firestore().collection(Db.teams).doc(getTeamId(loggedInUser.uid, CurrentSeason, DbBattleground.Team));
          batch.delete(soloTeam);
          batch.delete(duoTeam);
          batch.delete(squadTeam);
          await batch.commit();
          window.location.reload();
        }
      }}>
        Factory reset game
      </Button>
    </Row>
    <ScrollVertical>
      <VerticalLeft>
        <AnyEditor
          typedef={UserSettingsTypedef}
          value={userSettings.value}
          onChange={settings => {
            firebase.firestore().collection(Db.userSettings).doc(loggedInUser.uid)
              .update(settings);
          }}
          />
      </VerticalLeft>
    </ScrollVertical>
  </Column>
}

export function useUserSettingsAudioSync(userSettings: FirebaseStreamDocumentResult<DbUserSettings>) {
  React.useEffect(() => {
    if (userSettings.status === 'loaded' && AudioPlayerInstance) {
      if (userSettings.value.sound) {
        AudioPlayerInstance.audioContext.resume();
      } else {
        AudioPlayerInstance.audioContext.suspend();
      }
      AudioPlayerInstance.masterGain.gain.setValueAtTime(userSettings.value.volume / 100, AudioPlayerInstance.audioContext.currentTime);
      AudioPlayerInstance.musicGain.gain.setValueAtTime(userSettings.value.musicVolume / 100, AudioPlayerInstance.audioContext.currentTime);
      AudioPlayerInstance.environmentGain.gain.setValueAtTime(userSettings.value.environmentVolume / 100, AudioPlayerInstance.audioContext.currentTime);
      AudioPlayerInstance.effectsGain.gain.setValueAtTime(userSettings.value.soundEffectsVolume / 100, AudioPlayerInstance.audioContext.currentTime);
    }
  }, [userSettings]);
}
