import * as _ from 'lodash';
import { Vector2 } from "../Math/Vector2";
import { PIGlsl } from './Glsl';
import { Vector4 } from '../Math/Vector4';
import { Vector3 } from '../Math/Vector3';

export const shaderHeader = `#version 300 es
precision highp float;
precision highp int;
precision highp sampler2D;
precision highp sampler2DArray;
precision highp isampler2D;
precision highp isampler2DArray;
precision highp usampler2D;
precision highp usampler2DArray;
`;

const CellPosDefines = [
  `#define cellPos ivec2(gl_FragCoord.xy)`,
  `#define cellPos3(l) ivec3(gl_FragCoord.xy, l)`,
];
export const CellPosDefinesGLSL = CellPosDefines.join('\n');
export const computationShaderHeader = shaderHeader + `
${PIGlsl}
${CellPosDefinesGLSL}
`;

export function glslFloat(value: number): string {
  if (_.isNaN(value)) {
    return '0.0';
  } else if (value === Math.floor(value)) {
    return value + '.0';
  } else {
    return '' + value;
  }
}
export function glslInt(value: number): string {
  if (_.isNaN(value)) {
    return '0';
  } else {
    return '' + Math.floor(value);
  }
}
export function glslUInt(value: number): string {
  if (_.isNaN(value)) {
    return '0';
  } else {
    return '' + Math.abs(Math.floor(value)) + 'u';
  }
}
export function glslBool(value: number | boolean): string {
  if (_.isNaN(value)) {
    return 'false';
  } else {
    return value ? 'true' : 'false';
  }
}
export function glslVec2(value: Vector2): string {
  return `vec2(${glslFloat(value.x)}, ${glslFloat(value.y)})`;
}
export function glslVec3(value: Vector3): string {
  return `vec3(${glslFloat(value.x)}, ${glslFloat(value.y)}, ${glslFloat(value.z)})`;
}
export function glslVec4(value: Vector4): string {
  return `vec4(${glslFloat(value.x)}, ${glslFloat(value.y)}, ${glslFloat(value.z)}, ${glslFloat(value.w)})`;
}
export function glslUVec2(value: Vector2): string {
  return `uvec2(${glslInt(value.x)}, ${glslInt(value.y)})`;
}
export function glslIVec2(value: Vector2): string {
  return `ivec2(${glslInt(value.x)}, ${glslInt(value.y)})`;
}

export function temporaryWrapGlForErrors(gl: WebGL2RenderingContext, ms: number = 2000) {
  wrapGlForErrors(gl);
  setTimeout(() => unwrapGlForErrors(gl), ms);
}
export function wrapGlForErrors(gl: WebGL2RenderingContext) {
  gl = gl as any;
  (gl as any).errorWraps = (gl as any).errorWraps !== undefined ? ((gl as any).errorWraps + 1) : 1;
  if ((gl as any).errorWraps > 1) {
    return;
  }
  const glGetError = gl.getError;
  // tslint:disable-next-line:forin
  for (const k in gl) {
    const f = gl[k];
    if (_.isFunction(f)) {
      gl[k] = (...args) => {
        handleGlError(gl, `before ${k}`, glGetError);
        const res = f.apply(gl, args);
        handleGlError(gl, `after ${k}`, glGetError);
        return res;
      }
      gl[k].originalFunction = f;
    }
  }
}
export function unwrapGlForErrors(gl: WebGL2RenderingContext) {
  gl = gl as any;
  (gl as any).errorWraps = (gl as any).errorWraps !== undefined ? ((gl as any).errorWraps - 1) : 0;
  if ((gl as any).errorWraps > 0) {
    return;
  }
  // tslint:disable-next-line:forin
  for (const k in gl) {
    const f = gl[k];
    if (f.originalFunction) {
      gl[k] = f.originalFunction;
    }
  }
}
export function getGlConstantName(gl: WebGL2RenderingContext, constant: number) {
  gl = gl as any;
  // tslint:disable-next-line:forin
  for (const k in gl) {
    if (gl[k] === constant) {
      return k;
    }
  }
  return `Unknown constant: ${constant}`;
}
export function handleGlError(gl: WebGL2RenderingContext, context: string = '', glGetError = gl.getError) {
  const err = glGetError.apply(gl);
  if (err !== gl.NO_ERROR) {
    // tslint:disable-next-line:no-console
    console.error(`Gl error ${context}: ${err} ${getGlConstantName(gl, err)}`);
  }
}


export function cleanVarName(name: string): string {
  return name.replace(/\W+/g, '');
}

export function initGlExtensions(gl: WebGL2RenderingContext): string[] {
  const missing: string[] = [];
  if (!gl.getExtension("EXT_color_buffer_float")) {
    missing.push('EXT_color_buffer_float');
  }
  if (!gl.getExtension('OES_texture_float_linear')) {
    missing.push('OES_texture_float_linear');
  }
  return missing;
}

export function loadImage(url: string) {
  return new Promise<HTMLImageElement>((resolve) => {
    const image = new Image();
    image.src = url;
    image.addEventListener('load', () => resolve(image))
  })
}

export function loadArrayBuffer(url: string): Promise<ArrayBuffer> {
  return new Promise((resolve) => {
    const xhr = new XMLHttpRequest();
    xhr.open('GET', url, true);
    xhr.responseType = 'arraybuffer';
    xhr.onload = () => {
      resolve(xhr.response);
    };
    xhr.send();
  });
}
