import { GpuWorldState } from "../State";
import { Hitpoints, AnimatableUnitComponent, WalkingSpeed, DiedStep, PreviousHitpoints, UnitSoundMaterialComponent, UnitSoundsComponent } from "./UnitComponents";
import { GlState } from "../../GpuUtil/GlState";
import { AnimationControllerComponent, AnimationAction } from "./Animation";
import { PixlingTimespan, SecondsPerStep } from "../../Time";
import { EntitiesQuery } from "../../GpuUtil/ECS/Query";
import { Casting } from "../Abilities/Abilities";
import { abilitiesData } from "../Abilities/Abilities";
import { shaderModule, funcDef, uniformDef } from "../../GpuUtil/ShaderModule";
import { CastingState, UnitAnimations, SoundEffect } from "../StateLayout";
import { Dazeable } from "../Buffs/Daze";
import { AudioPlayerInstance } from "../Audio";
import { Position } from "./Transform";
import { DamageCalcHelpers } from "./DamageCalc";
import { mod, interpolateClamped } from "../../Math/Math";
import { HeliumBubbable } from "../Buffs/Bubbles";
import { VisibleComponent } from "./Components";
import { visibleCacheKeys } from "./Visibility";

export class UpdateUnitAnimationAndSound {
  constructor(private gls: GlState) {}

  query = new EntitiesQuery([AnimatableUnitComponent, VisibleComponent])
    .filterValue(VisibleComponent, v => !!v, visibleCacheKeys)
    .toGpu(this.gls)
    .mapToTemp(shaderModule([
      Hitpoints.sm,
      Casting.Ability.sm,
      Casting.StateStartStep.sm,
      Casting.State.sm,
      Casting.Target.sm,
      WalkingSpeed.sm,
      Dazeable.sm,
      Position.sm,
      DiedStep.sm,
      PreviousHitpoints.sm,
      HeliumBubbable.sm,
      DamageCalcHelpers(),
    ], {
      time: uniformDef('int', d => d.data.stepCounter),
      map: funcDef('vec4[4]', [['ivec2', 'entityCell']], `
        ivec2 castingTarget = ${Casting.Target.sm.value}(entityCell);
        return vec4[](
          vec4(
            ${Position.sm.value}(entityCell),
            ${WalkingSpeed.sm.value}(entityCell)
          ),
          vec4(
            time - ${Casting.StateStartStep.sm.value}(entityCell),
            ${Casting.Ability.sm.value}(entityCell),
            ${Casting.State.sm.value}(entityCell),
            ${HeliumBubbable.sm.value}(entityCell) > 0
          ),
          vec4(
            ${Hitpoints.sm.value}(entityCell),
            time - ${DiedStep.sm.value}(entityCell),
            ${PreviousHitpoints.sm.value}(entityCell),
            ${Dazeable.sm.value}(entityCell) > 0
          ),
          vec4(
            ${DamageCalcHelpers().armor}(entityCell),
            castingTarget.x,
            castingTarget.y,
            0
          )
        );
      `)
    }))
    .toCpuQueuing(state => state.data.round);

  run(state: GpuWorldState) {
    AudioPlayerInstance?.updateCamera(state.derived.cameraView);
    const time = new PixlingTimespan(state.data.stepCounter).timeInSecondsFractional;
    const readResults = this.query.queueRead(state);
    for (const res of readResults()) {
      for (const { entityCell, value } of res) {
        const position = value[0];
        const walkSpeed = value[0].w; // m/s
        const castStateSteps = value[1].x;
        const castAbility = value[1].y;
        const castState = value[1].z;
        const heliumBubble = value[1].w;
        const hitpoints = value[2].x;
        const deadSteps = value[2].y;
        const previousHitpoints = value[2].z;
        const dazed = value[2].w;
        const armor = value[3].x;
        const castingTarget = { x: value[3].y, y: value[3].z };

        const incomingDamage = Math.max(0, previousHitpoints - hitpoints);

        const material = state.ecs.getComponentData(UnitSoundMaterialComponent, entityCell)!;
        const au = state.ecs.getComponentData(AnimatableUnitComponent, entityCell)!;
        const ac = state.ecs.getComponentData(AnimationControllerComponent, entityCell)!;
        const us = state.ecs.getComponentData(UnitSoundsComponent, entityCell)!;

        if (incomingDamage > 5) {
          if (armor > 1) {
            AudioPlayerInstance?.playSoundEffect(SoundEffect.StoneHit, 0, position);
          } else if (material.value === 'wood') {
            AudioPlayerInstance?.playSoundEffect(SoundEffect.WoodHit, 0, position);
          } else if (material.value === 'statue') {
            AudioPlayerInstance?.playSoundEffect(SoundEffect.StoneHit, 0, position);
          } else {
            AudioPlayerInstance?.playSoundEffect(SoundEffect.FleshHit, 0, position);
          }
        }

        let actions: AnimationAction[] = [];

        const dead = hitpoints <= 0;

        if (us.playing.Dazed && (dead || !dazed)) {
          us.playing.Dazed.fadeOut();
          delete us.playing.Dazed;
        }
        if (us.playing.HeliumBubble && (dead || !heliumBubble)) {
          us.playing.HeliumBubble.fadeOut();
          delete us.playing.HeliumBubble;
        }
        if (us.playing.Ability && us.playing.Ability.looping && castState === CastingState.WindDown) {
          us.playing.Ability.fadeOut();
          delete us.playing.Ability;
        }

        if (dead) {
          const clip = UnitAnimations[UnitAnimations.Death1];
          const active = ac.actions.find(x => x.clip === clip);
          actions.push({
            positionType: 'startTime',
            positionValue: active ? active.positionValue : (time - deadSteps),
            clip,
            looping: false
          });

          if (deadSteps === 0) {
            if (material.value === 'wood') {
              AudioPlayerInstance?.playSoundEffect(SoundEffect.PlanksFalling, 0, position);
            } else if (material.value === 'statue') {
              AudioPlayerInstance?.playSoundEffect(SoundEffect.Explosion, 0, position);
            } else {
              AudioPlayerInstance?.playSoundEffect(SoundEffect.Wimper, 0, position);
            }
          }
        } else {

          if (dazed) {
            const clip = UnitAnimations[UnitAnimations.Dazed1];
            const active = ac.actions.find(x => x.clip === clip);
            actions.push({
              positionType: 'startTime',
              positionValue: active ? active.positionValue : time,
              clip,
              looping: true
            });
            if (!us.playing.Dazed && AudioPlayerInstance) {
              us.playing.Dazed = AudioPlayerInstance.playSoundEffect(SoundEffect.Snore, 0, position);
            }
          }
          if (heliumBubble) {
            if (!us.playing.HeliumBubble && AudioPlayerInstance) {
              us.playing.HeliumBubble = AudioPlayerInstance.playSoundEffect(SoundEffect.BubbleWobble, 0, position);
            }
          }

          const walkIdleCuttoff = 0.1;
          {
            const clip = UnitAnimations[UnitAnimations.Run1];
            const active = ac.actions.find(x => x.clip === clip);
            const prevPerc = (active?.positionValue ?? 0);
            // p + (s/f) * (m/s) / m
            const walkDist = walkSpeed * SecondsPerStep;
            const perc = prevPerc + walkDist / au.stepLength;
            actions.push({
              clip,
              looping: true,
              positionType: 'percentage',
              positionValue: perc,
              weight: interpolateClamped(Math.abs(walkSpeed), 0, walkIdleCuttoff, 0, 1)
            });
            const footstepFrame1 = 43;
            const footstepFrame2 = 18;
            const totalFrames = 48;
            const prevAnimFrame = mod(Math.floor(prevPerc * totalFrames), totalFrames);
            const nextAnimFrame = mod(Math.floor(perc * totalFrames), totalFrames);
            if ((prevAnimFrame < footstepFrame1 && nextAnimFrame >= footstepFrame1) ||
              (prevAnimFrame < footstepFrame2 && nextAnimFrame >= footstepFrame2)) {
              AudioPlayerInstance?.playSoundEffect(SoundEffect.Footstep, 0, position);
            }
          }
          {
            const clip = UnitAnimations[UnitAnimations.Idle1];
            const active = ac.actions.find(x => x.clip === clip);
            actions.push({
              clip,
              looping: true,
              positionType: 'time',
              positionValue: (active?.positionValue ?? 0) + SecondsPerStep,
              weight: interpolateClamped(Math.abs(walkSpeed), 0, walkIdleCuttoff, 1, 0) *
                ((castState === CastingState.Casting || castState === CastingState.Performing) ? 0 : 1)
            });
          }

          if (castState !== CastingState.Inactive) {
            abilitiesData.castAnimation[castAbility]?.(ac.actions, actions, castState, castStateSteps, castAbility, time);
          }
          const sound = abilitiesData.castSound[castAbility]?.(state, castState, castStateSteps, castAbility, castingTarget, time, position);
          if (sound) {
            us.playing.Ability = sound;
          }
        }

        state.ecs.setComponentData(AnimationControllerComponent, entityCell, { actions });
      }
    }
  }
}