import { EntityRef } from "../../GpuWorld/StateData";
import { Component, Archetype, EntitiesBlock, GpuComponentBlock, GpuComponent, CpuComponent, ObjComponent, ComponentFormats } from ".";
import { GpuWorldState } from "../../GpuWorld/State";
import { Texture, TextureSlicing, DoubleBufferedTextureSlicing, DoubleBufferedTexture } from "../Texture";
import { GlState, GL } from "../GlState";
import { Image } from '../Image';
import { RenderTarget } from "../RenderTarget";
import { Program } from "../Program";
import { TextureReader } from "../TextureReader";
import { Vector4 } from "../../Math/Vector4";
import { Size, sizeClosestPow2 } from "../../Math/Math";
import { FunctionDef, shaderModule, funcDef, uniformDef, outDef, ShaderModule, CompiledShaderModule, uniformArrayDef } from "../ShaderModule";
import { GpuReduce } from "../Reduce";
import { glslTypeArraySize } from "../GLSLTypes";
import { MapGpuComponent, UpdateGpuComponents, UpdateGpuType } from "./Update";

export interface Query<T> {
  get(state: GpuWorldState): T;
  destroy(): void;
}

export class CacheByDeps<T> {
  private value?: T;
  private disposeValue?: (value: T) => void;
  private deps: any[] = [];
  get(getValue: () => T, deps: any[], dispose?: (value: T) => void): T {
    if (this.value === undefined) {
      return this.value = getValue();
    }
    for (let i=0; i < deps.length; i++) {
      if (this.deps[i] !== deps[i]) {
        this.deps = deps;
        if (this.disposeValue) {
          this.disposeValue(this.value);
        }
        this.disposeValue = dispose;
        return this.value = getValue();
      }
    }
    return this.value;
  }
  destroy() {
    if (this.disposeValue && this.value !== undefined) {
      this.disposeValue(this.value);
    }
    this.value = undefined;
  }
}

export class ArchetypesQuery {
  constructor(private requiredComponents: Component[]) {
  }
  destroy() {}
  private cache = new CacheByDeps<Archetype[]>();
  get(state: GpuWorldState) {
    return this.cache.get(() => {
      return state.ecs.getMatchingArchetypes(this.requiredComponents);
    }, [state.ecs.archetypes]);
  }
  entityBlocks() {
    return new ArchetypesToEntityBlocks(this);
  }
  gpuComponentBlocks(component: GpuComponent) {
    return new ArchetypesToGpuComponentBlocks(this, component);
  }
  updateGpu(gls: GlState, components: Array<GpuComponent>, update: ShaderModule<{ update: FunctionDef<'void' | 'ivec2', [['ivec2', 'entityCell']]> }>, updateType: UpdateGpuType = 'set') {
    return new UpdateGpuComponents(gls, this, components, update, updateType);
  }
  mapGpu<T extends string>(gls: GlState, component: GpuComponent, map: ShaderModule<{ map: FunctionDef<T, [['ivec2', 'entityCell'], [T, 'value']] | [['ivec2', 'entityCell']]> }>) {
    return new MapGpuComponent<T>(gls, this, component, map);
  }
}

export class ArchetypeIndexQuery {
  constructor(private archetypeIndex: number) {
  }
  destroy() {}
  get(state: GpuWorldState) {
    const arch = state.ecs.archetypes[this.archetypeIndex];
    return arch ? [arch] : [];
  }
  entities() {
    return new ArchetypesToEntities(this);
  }
  entityBlocks() {
    return new ArchetypesToEntityBlocks(this);
  }
  gpuComponentBlocks(component: GpuComponent) {
    return new ArchetypesToGpuComponentBlocks(this, component);
  }
}

class ArchetypesToEntityBlocks {
  constructor(private archetypes: Query<Archetype[]>) {}
  destroy() {
    this.archetypes.destroy();
  }
  private cache = new CacheByDeps<EntitiesBlock[]>();
  get(state: GpuWorldState) {
    const archetypes = this.archetypes.get(state);
    return this.cache.get(() => {
      const res: EntitiesBlock[] = [];
      for (const arch of archetypes) {
        res.push(new EntitiesBlock(arch.index, arch.chunks.reduce((p, x) => p + x.entities, 0)));
      }
      return res;
    }, [archetypes, state.ecs.formVersion]);
  }
}

class ArchetypesToGpuComponentBlocks {
  constructor(private archetypes: Query<Archetype[]>, public component: GpuComponent) {}
  destroy() {
    this.archetypes.destroy();
  }
  private cache = new CacheByDeps<GpuComponentBlock[]>();
  get(state: GpuWorldState) {
    const archetypes = this.archetypes.get(state);
    return this.cache.get(() => {
      const buffer = state.ecs.gpuBuffers[this.component.buffer.name];
      const res: GpuComponentBlock[] = [];
      for (const arch of archetypes) {
        const buffArch = buffer.archetypes[arch.index]!;
        res.push(new GpuComponentBlock(
          buffer.getArchetypeComponentDataSlice(this.component, arch),
          arch,
          buffArch.dataRowOffset
        ));
      }
      return res;
    }, [archetypes, state.ecs.formVersion]);
  }
  uploadFrom(fromComponent: CpuComponent) {
    return new GpuComponentUploadFrom(this, fromComponent);
  }
}


class GpuComponentUploadFrom {
  constructor(private blocks: Query<GpuComponentBlock[]>, private fromComponent: CpuComponent, private alwaysUpload = false) {}
  destroy() {
    this.blocks.destroy();
  }
  private contentVersions: { [key: string]: number } = {};
  private prevBlocks: GpuComponentBlock[] = [];
  run(state: GpuWorldState) {
    const blocks = this.blocks.get(state);
    if (this.prevBlocks !== blocks) {
      this.prevBlocks = blocks;
      this.contentVersions = {};
    }
    for (const block of blocks) {
      for (let c=0; c < block.archetype.chunks.length; c++) {
        const chunk = block.archetype.chunks[c];
        const cpuComp = chunk.cpuComponent[this.fromComponent.name];
        const chunkKey = block.archetype.index + '_' + c;
        if (cpuComp.contentVersion === this.contentVersions[chunkKey] && !this.alwaysUpload) {
          continue;
        }
        this.contentVersions[chunkKey] = cpuComp.contentVersion;
        block.slices.selectRelative({
          x: 0,
          y: c,
          width: block.slices.rect.width,
          height: 1
        }).upload(cpuComp.data);
      }
    }
  }
}

export class EntitiesQuery {
  constructor(public requiredComponents: Component[] = []) {}
  destroy() {
  }
  private filters: Array<{
    component: ObjComponent<any> | CpuComponent,
    filter: (value: any, state: GpuWorldState) => boolean,
    cacheKeys: (state: GpuWorldState) => any[],
    entities: CacheByDeps<EntityRef[]>
  }> = [];
  filterValue<T>(component: ObjComponent<T>, filter: (value: T | undefined, state: GpuWorldState) => boolean, cacheKeys: (state: GpuWorldState) => any[]): EntitiesQuery;
  filterValue<T extends ComponentFormats>(component: CpuComponent<T>, filter: (value: Float32Array, state: GpuWorldState) => boolean, cacheKeys: (state: GpuWorldState) => any[]): EntitiesQuery;
  filterValue(component: ObjComponent<any> | CpuComponent, filter: (value: any, state: GpuWorldState) => boolean, cacheKeys: (state: GpuWorldState) => any[]) {
    this.filters.push({
      component,
      filter,
      cacheKeys,
      entities: new CacheByDeps<EntityRef[]>()
    });
    return this;
  }
  private cachedArchetypes = new CacheByDeps<Archetype[]>();
  private cachedEntities = new CacheByDeps<EntityRef[]>();
  get(state: GpuWorldState) {
    const archetypes = this.cachedArchetypes.get(
      () => state.ecs.getMatchingArchetypes([...this.requiredComponents, ...this.filters.map(f => f.component)]),
      [state.ecs.archetypes, ...this.requiredComponents]
    );
    let entities = this.cachedEntities.get(
      () => state.ecs.getEntityRefsForArchetypes(archetypes),
      [archetypes, state.ecs.formVersion]
    );
    for (const filter of this.filters) {
      const cacheKeys: any[] = [
        entities,
        ...filter.cacheKeys(state)
      ];
      for (const arch of archetypes) {
        for (let c=0; c < arch.chunks.length; c++) {
          const chunk = arch.chunks[c];
          const comp = filter.component.type === 'cpu' ?
            chunk.cpuComponent[filter.component.name]
            : chunk.objComponent[filter.component.name];
          cacheKeys.push(comp.contentVersion);
        }
      }
      const nextEntities = filter.entities.get(
        // eslint-disable-next-line
        () => entities.filter(entity => {
          const obj = state.ecs.getComponentData(filter.component, entity);
          return filter.filter(obj, state);
        }),
        cacheKeys
      );
      entities = nextEntities;
    }
    return entities;
  }
  updateCpu(update: (state: GpuWorldState, entities: EntityRef[]) => void, dependencyKeys: (state: GpuWorldState) => any) {
    return new UpdateCpu(this, update, dependencyKeys);
  }
  transform<T>(transform: TransformFunc<T>) {
    return new Transform(this, transform);
  }
  toGpu(gls: GlState) {
    return new EntitiesToGpu(this, gls);
  }
  lookupTable(width: number, index: LookupFunc) {
    return new LookupTableQuery(this, width, index);
  }
  render(gls: GlState, props: RenderEntitiesProps) {
    return new RenderEntities(this, gls, props);
  }
}

class UpdateCpu {
  constructor(
    private entities: Query<EntityRef[]>,
    private update: (state: GpuWorldState, entities: EntityRef[]) => void,
    private dependencyKeys: (state: GpuWorldState) => any[]
  ) {
  }
  destroy() {
    this.entities.destroy();
  }
  private deps: any[] = [];
  run(state: GpuWorldState) {
    const entities = this.entities.get(state);
    const deps = [entities, ...this.dependencyKeys(state)];
    for (let i=0; i < deps.length; i++) {
      if (this.deps[i] !== deps[i]) {
        this.deps = deps;
        this.update(state, entities);
        return;
      }
    }
  }
}

interface RenderEntitiesProps {
  mesh: string;
  vertexShader: ShaderModule<{ shader: FunctionDef<'void', [['ivec2', 'entityCell']]> }>;
  fragmentShader: ShaderModule<{ main: FunctionDef<'void', []> }>;
}

class RenderEntities {
  constructor(
    protected entities: Query<EntityRef[]>,
    protected gls: GlState,
    protected props: RenderEntitiesProps) {}
  destroy() {
    this.entities.destroy();
    this.program.destroy();
  }

  private maxEntities = 128;
  private vertexShader = new CompiledShaderModule(shaderModule([this.props.vertexShader], {
    entities: uniformArrayDef('ivec2[]', this.maxEntities),
    main: funcDef('void', [], `
      ${this.props.vertexShader.shader}(Self.entities[gl_InstanceID]);
    `)
  }));
  private fragmentShader = new CompiledShaderModule(this.props.fragmentShader);
  private program = new Program(this.gls, this.fragmentShader.source, this.vertexShader.source);

  run(state: GpuWorldState) {
    const entities = this.entities.get(state);
    if (entities.length === 0) {
      return;
    }
    if (entities.length >= this.maxEntities) {
      throw new Error(`Max entities reached`);
    }
    // This must be called before we start setting up for this draw call, because
    // getting the values may involve renderings and state changes
    this.vertexShader.updateUniformValues(state);
    this.fragmentShader.updateUniformValues(state);
    this.program.prepare();
    this.vertexShader.writeUniforms(this.program);
    this.fragmentShader.writeUniforms(this.program);
    this.program.setUniform('entities', 'ivec2[]', entities);
    const mesh = this.gls.getMesh(this.props.mesh);
    if (!mesh) {
      throw new Error(`No such mesh: ${this.props.mesh}`);
    }
    this.gls.draw(this.program, mesh, entities.length);
  }
}

class ArchetypesToEntities extends EntitiesQuery {
  constructor(private archetypes: Query<Archetype[]>) {
    super();
  }
  destroy() {
    this.archetypes.destroy();
  }
  private cache = new CacheByDeps<EntityRef[]>();
  get(state: GpuWorldState) {
    const archetypes = this.archetypes.get(state);
    return this.cache.get(
        () => state.ecs.getEntityRefsForArchetypes(archetypes),
        [archetypes, state.ecs.formVersion]
    );
  }
}

type TransformFunc<T> = (state: GpuWorldState, entityCells: EntityRef[]) => T;
class Transform<T> {
  constructor(private entities: Query<EntityRef[]>,
    private transform: TransformFunc<T>) {}
  destroy() {
    this.entities.destroy();
  }
  private cache = new CacheByDeps<T>();
  get(state: GpuWorldState) {
    const entities = this.entities.get(state);
    return this.cache.get(() => this.transform(state, entities), [entities]);
  }
}

class EntitiesToGpu {
  constructor(private entities: Query<EntityRef[]>, private gls: GlState) {}
  destroy() {
    this.entities.destroy();
    this.cache.destroy();
  }
  private cache = new CacheByDeps<{ entityListTexture: Texture, entityList: EntityRef[] }>();
  private chunkWidth = 128;
  get(state: GpuWorldState) {
    const entityList = this.entities.get(state);
    return this.cache.get(() => {
      let entityListTexture: Texture;
      if (entityList.length !== 0) {
        const data = Image.zeroesI32({
          width: Math.min(entityList.length, this.chunkWidth),
          height: Math.ceil(entityList.length / this.chunkWidth),
          depth: 1
        }, 2);
        for (let i=0; i < entityList.length; i++) {
          data.write(entityList[i].x, i % this.chunkWidth, Math.floor(i / this.chunkWidth), 0, 0);
          data.write(entityList[i].y, i % this.chunkWidth, Math.floor(i / this.chunkWidth), 0, 1);
        }
        entityListTexture = new Texture(this.gls, this.gls.gl.RG32I, data.size, undefined).init(data.data);
      } else {
        entityListTexture = new Texture(this.gls, this.gls.gl.RG32I, { width: 1, height: 1}).init();
      }
      return { entityList, entityListTexture };
    }, [entityList], t => t.entityListTexture.destroy());
  }
  map(map: ShaderModule<{ map: FunctionDef<string, [['ivec2', 'entityCell']]> }>, output: (state: GpuWorldState, size: Size) => TextureSlicing | DoubleBufferedTextureSlicing) {
    return new MapEntitiesGpu(this, this.gls, map, output);
  }
  mapToTemp(map: ShaderModule<{ map: FunctionDef<string, [['ivec2', 'entityCell']]> }>) {
    return new MapEntitiesToTempGpu(this, this.gls, map);
  }
}


class MapEntitiesGpu {
  constructor(
    protected entities: Query<{ entityList: EntityRef[], entityListTexture: Texture }>,
    protected gls: GlState,
    private map: ShaderModule<{ map: FunctionDef<string, [['ivec2', 'entityCell']]> }>,
    private output: (state: GpuWorldState, size: Size) => TextureSlicing | DoubleBufferedTextureSlicing) {}
  destroy() {
    this.entities.destroy();
    this.renderTarget.destroy();
    this.program.destroy();
  }

  private renderTarget = new RenderTarget(this.gls);
  private shader = new CompiledShaderModule(shaderModule([this.map], {
    entityList: uniformDef('isampler2D'),
    outValue: outDef(this.map.map.returnType),
    main: funcDef('void', [], `
      ivec2 outEntityCell = texelFetch(entityList, ivec2(gl_FragCoord), 0).xy;
      outValue = ${this.map.map}(outEntityCell);
    `)
  }))
  private program = new Program(this.gls, this.shader.source);

  get(state: GpuWorldState) {
    const { entityList, entityListTexture } = this.entities.get(state);
    const output = this.output(state, entityListTexture.size);
    // This must be called before we start setting up for this draw call, because
    // getting the values may involve renderings and state changes
    this.shader.updateUniformValues(state);
    this.program.prepare();
    this.shader.writeUniforms(this.program);
    this.renderTarget.set(output instanceof TextureSlicing ? output : output.back);
    this.gls.prepareForComputation(output.rect);
    this.program.setUniform('entityList', 'isampler2D', entityListTexture);
    this.gls.drawQuad(this.program);
    if (output instanceof DoubleBufferedTextureSlicing) {
      this.gls.getCopyTexture().copyBackToFront(output);
    }
    return { entityList, values: output instanceof DoubleBufferedTextureSlicing ? output.front : output };
  }
  toCpuQueuing(queueCashKey: (state: GpuWorldState) => any) {
    return new QueuingEntityValueTextureReader(this, queueCashKey);
  }
  toCpuSync() {
    return new SyncEntityValueTextureReader(this);
  }
  reduceSum() {
    return new ReduceSum(this, this.gls);
  }
}

class MapEntitiesToTempGpu extends MapEntitiesGpu {
  constructor(entities: Query<{ entityList: EntityRef[], entityListTexture: Texture }>,
    gls: GlState,
    map: ShaderModule<{ map: FunctionDef<string, [['ivec2', 'entityCell']]> }>) {
    super(entities, gls, map, (state, size) => {
      const depth = glslTypeArraySize(map.map.returnType) ?? 1;
      if (this.tmp.size.width < size.width || this.tmp.size.height < size.height || this.tmp.depth! < depth) {
        this.tmp.destroy();
        this.tmp = Texture.create(this.gls, this.gls.gl.RGBA32F, size, depth).init();
      }
      this.tmp.sliceAll().clear();
      return this.tmp.sliceRange(0, depth);
    })
  }
  destroy() {
    this.entities.destroy();
    this.tmp.destroy();
  }
  private tmp = Texture.create(this.gls, this.gls.gl.RGBA32F, { width: 1, height: 1 }, 1).init();
}

class SyncEntityValueTextureReader {
  constructor(private query: Query<{ entityList: EntityRef[], values: TextureSlicing }>) {}
  destroy() {
    this.query.destroy();
  }

  get(state: GpuWorldState) {
    const value = this.query.get(state);
    const img = value.values.readSync();
    return value.entityList.map((entityCell, i) => {
      return {
        entityCell,
        value: new Array(img.size.depth).fill(0).map((_, z) =>
          img.readPixel({ x: i % value.values.rect.width, y: Math.floor(i / value.values.rect.width), z })
        )
      }
    });
  }
}

type QueuingEntityResult = Array<{ entityCell: EntityRef, value: Vector4[] }>;
class QueuingEntityValueTextureReader {
  constructor(
    private query: Query<{ entityList: EntityRef[], values: TextureSlicing }>,
    private queueCashKey: (state: GpuWorldState) => any
  ) {}
  destroy() {
    this.query.destroy();
    this.resultReader.destroy();
  }

  private resultReader = new CacheByDeps<TextureReader>();
  private resultQueue = new CacheByDeps<Array<() => QueuingEntityResult | undefined>>();
  queueRead(state: GpuWorldState) {
    const value = this.query.get(state);
    const resultReader = this.resultReader.get(() => new TextureReader(value.values), [value.values], t => t.destroy());
    const tryRead = resultReader.read('background', value.values.rect);
    const resultQueue = this.resultQueue.get(() => [], [state, value.entityList, this.queueCashKey(state)]);
    resultQueue.push(() => {
      const img = tryRead();
      if (!img) {
        return undefined;
      }
      return value.entityList.map((entityCell, i) => {
        return {
          entityCell,
          value: new Array(img.size.depth).fill(0).map((_, z) =>
            img.readPixel({ x: i % value.values.rect.width, y: Math.floor(i / value.values.rect.width), z })
          )
        }
      });
    });
    return function readResults(): QueuingEntityResult[] {
      const res: QueuingEntityResult[] = [];
      while (resultQueue.length > 0) {
        const result = resultQueue[0]();
        if (!result) {
          return res;
        } else {
          resultQueue.shift();
          res.push(result);
        }
      }
      return res;
    }
  }
}

class ReduceSum {
  constructor(private query: Query<{ values: TextureSlicing }>, protected gls: GlState) {}
  destroy() {
    this.query.destroy();
    this.reduce.destroy();
    this.tmp.destroy();
  }

  reduce = GpuReduce.reduceSum(this.gls);
  tmp = DoubleBufferedTexture.create(this.gls, GL.RGBA32F, { width: 1, height: 1 }, 1).init();

  get(state: GpuWorldState) {
    const value = this.query.get(state);
    const size = sizeClosestPow2(value.values.rect);
    if (this.tmp.size.width < size.width || this.tmp.size.height < size.height || this.tmp.depth! < value.values.layers.length) {
      this.tmp.destroy();
      this.tmp = DoubleBufferedTexture.create(this.gls, GL.RGBA32F, size, value.values.layers.length).init();
    }
    this.tmp.clear();
    const tmpSlice = this.tmp.sliceAll().selectAbsolute({ x: 0, y: 0, ...size });
    this.gls.getCopyTexture().copy(tmpSlice.selectAbsolute(value.values.rect).front, value.values);
    return this.reduce.reduceSync(tmpSlice);
  }
}

type LookupFunc = (state: GpuWorldState, entityCell: EntityRef) => { row: number, column: number };
interface LookupTable {
  width: number;
  rows: Array<{
    entities: EntityRef[];
  }>
}

class LookupTableQuery {
  constructor(private entities: Query<EntityRef[]>, private indexWidth: number, private index: LookupFunc) {}
  destroy() {
    this.entities.destroy();
  }
  private cache = new CacheByDeps<LookupTable>();
  public get(state: GpuWorldState) {
    const entities = this.entities.get(state);
    return this.cache.get(() => {
      const index: LookupTable = {
        width: this.indexWidth,
        rows: []
      };
      for (const entityCell of entities) {
        const { row, column } = this.index(state, entityCell);
        if (!index.rows[row]) {
          index.rows[row] = {
            entities: new Array(this.indexWidth).fill(0).map(() => ({ x: -1, y: -1 }))
          };
        }
        index.rows[row].entities[column] = entityCell;
      }
      return index;
    }, [entities])
  }
  toGpu(gls: GlState) {
    return new LookupTableToGpu(this, gls);
  }
}

export const GpuLookupTableSM = shaderModule('GpuIndex', [], {
  entityCell: funcDef('ivec2', [['isampler2D', 'index'], ['int', 'row'], ['int', 'i']], `
    return texelFetch(index, ivec2(i, row), 0).xy;
  `)
})

class LookupTableToGpu {
  constructor(private indexQuery: Query<LookupTable>, private gls: GlState) {}
  destroy() {
    this.indexQuery.destroy();
    this.buffer.destroy();
    this.cache.destroy();
  }

  private buffer = new Texture(this.gls, GL.RG32I, { width: 1, height: 1 }).init();
  private cache = new CacheByDeps<Texture>();
  public get(state: GpuWorldState) {
    const index = this.indexQuery.get(state);
    return this.cache.get(() => {
      const indexData = Image.zeroesI32({ width: index.width, height: Math.max(1, index.rows.length), depth: 1 }, 2);
      indexData.fill(-1);
      for (let i=0; i < index.rows.length; i++) {
        if (index.rows[i]) {
          for (let l=0; l < index.rows[i].entities.length; l++) {
            const entity = index.rows[i].entities[l];
            indexData.write(entity.x, l, i, 0, 0);
            indexData.write(entity.y, l, i, 0, 1);
          }
        }
      }
      if (this.buffer.size.width < indexData.size.width || this.buffer.size.height < indexData.size.height) {
        this.buffer.destroy();
        this.buffer = new Texture(this.gls, GL.RG32I, indexData.size).init();
      }
      this.buffer.init(indexData.data);
      return this.buffer;
    }, [index]);
  }
}
