export interface TrainingConfig {
  weightsRenormalizationRate: number;
  elitismCount: number;
}

export class TrainingStats {
  winPercs: number[] = [];
  tiePercs: number[] = [];
  lossPercs: number[] = [];
  homeDistanceClosestEnemy: number[] = [];
  win5Percs: number[] = [];
  tie5Percs: number[] = [];
  losses5Percs: number[] = [];
  homeGold: number[] = [];
  awayGold: number[] = [];
}
export class TeamTrainingStats {
  points: number[] = [];
  gold: number[] = [];
}
