export class UpdateBreeding {
  constructor(private gls: GlState) {}

  // curiosity = new UpdateCuriosity(this.gls);

  creatures = new EntitiesQuery([Creature.component])
    .toGpu(this.gls)
    .mapToTemp(shaderModule([Gold.sm], {
      map: funcDef('vec4', [['ivec2', 'entityCell']], `
        return vec4(${Gold.sm.value}(entityCell));
      `)
    }))
    .toCpuSync();

  private arenaStats = new EntitiesQuery([Arena.gpuComponent])
    .toGpu(this.gls)
    .mapToTemp(shaderModule([ArenaStats, InterpolateSM, Arena.sm], {
      map: funcDef('vec4[2]', [['ivec2', 'entityCell']], `
        int arenaIndex = ${Arena.sm.value}(entityCell);
        ${ArenaStats.Stats} stats = ${ArenaStats.stats}(arenaIndex);
        return vec4[](
          vec4(arenaIndex, stats.homePoints, stats.awayPoints, 0),
          vec4(stats.home.gold, stats.away.gold, 0, 0)
        );
      `)
    }))
    .toCpuSync();

  async runSelection(state: GpuWorldState) {
    state.ecs.gpuComponentInfo(CopyMutateParent.component)?.slice.clear([-1, -1, -1, -1]);
    state.ecs.gpuComponentInfo(MutationRate.component)?.slice.clear();

    const creatures = this.creatures.get(state);
    const creaturesGold: Array<{ units: Array<{ entityCell: EntityRef, gold: number, fitness: number }> }> =
      new Array(ArenaMembersSize).fill(0).map(() => ({ units: [] }));
    for (let i=0; i < creatures.length; i++) {
      const entityCell = creatures[i].entityCell;
      const arenaIndex = state.ecs.getComponentData(ArenaInstance.cpuComponent, entityCell)[0];
      const arenaMemberIndex = state.ecs.getComponentData(ArenaMemberIndex.cpuComponent, entityCell)[0];
      creaturesGold[arenaMemberIndex].units[arenaIndex] = {
        entityCell,
        gold: creatures[i].value[0].x,
        fitness: 0,
      };
    }
    for (let arenaMemberIndex=0; arenaMemberIndex < ArenaMembersSize; arenaMemberIndex++) {
      const units = creaturesGold[arenaMemberIndex].units;
      const breed = (arenaMemberIndex < SideSize && state.data.teams.home.train) ||
        (arenaMemberIndex >= SideSize && state.data.teams.away.train);
      if (units.length === 0 || !breed) {
        continue;
      }
      const unitsSorted = units.slice().sort((a, b) => b.gold - a.gold);
      for (let arenaIndex=0; arenaIndex < state.layout.arenas; arenaIndex++) {
        unitsSorted[arenaIndex].fitness = 32 - 31 * Math.pow(arenaIndex / state.layout.arenas, 1/4);
      }

      const totalFitness = unitsSorted.reduce((p, x) => p + x.fitness, 0);
      const selectParent = (arenaIndex: number) => {
        if (arenaIndex < state.data.trainingConfig.elitismCount) {
          return unitsSorted[arenaIndex].entityCell;
        }
        // CDF sampling
        const r = Math.random();
        let culm = 0;
        for (let i=0; i < unitsSorted.length; i++) {
          culm += unitsSorted[i].fitness / totalFitness;
          if (r <= culm) {
            return unitsSorted[i].entityCell;
          }
        }
        throw new Error('Unreachable');
      }
      for (let arenaIndex=0; arenaIndex < state.layout.arenas; arenaIndex++) {
        const unit = units[arenaIndex].entityCell;
        const parent = selectParent(arenaIndex);
        state.ecs.setComponentData(CopyMutateParent.component, unit, [parent.x, parent.y]);
        if (arenaIndex >= state.data.trainingConfig.elitismCount) {
          state.ecs.setComponentData(MutationRate.component, unit, [Math.random() > 0.8 ? 0.1 : 0.01]);
        }
      }
    }
    this.copyAndMutate.run(state);

    await this.updateArenaStats(state);
  }


  async updateArenaStats(state: GpuWorldState) {

    const arenaStats = this.arenaStats.get(state).map(x => {
      return ({
        arenaIndex: x.value[0].x,
        homePoints: x.value[0].y,
        awayPoints: x.value[0].z,
        homeGold: x.value[1].x,
        awayGold: x.value[1].y,
      })
    });
    arenaStats.sort((a, b) => a.arenaIndex - b.arenaIndex);
    const validation = arenaStats.slice(0, state.data.trainingConfig.elitismCount);
    const trainingStats = {...state.data.trainingStats};
    const homeTrainingStats = {...state.data.teams.home.trainingStats};
    const awayTrainingStats = {...state.data.teams.away.trainingStats};
    trainingStats.winPercs = [...trainingStats.winPercs, validation.reduce((p, x) => p + (x.homePoints > x.awayPoints ? 1 : 0), 0) / validation.length];
    trainingStats.lossPercs = [...trainingStats.lossPercs, validation.reduce((p, x) => p + (x.homePoints < x.awayPoints ? 1 : 0), 0) / validation.length];
    trainingStats.tiePercs = [...trainingStats.tiePercs, validation.reduce((p, x) => p + (x.homePoints === x.awayPoints ? 1 : 0), 0) / validation.length];
    const win5s = trainingStats.winPercs.slice(-5).reduce((p, x) => p + x, 0);
    const tie5s = trainingStats.tiePercs.slice(-5).reduce((p, x) => p + x, 0);
    const losses5s = trainingStats.lossPercs.slice(-5).reduce((p, x) => p + x, 0);
    const total5s = win5s + tie5s + losses5s;
    trainingStats.win5Percs = [...trainingStats.win5Percs, win5s / total5s];
    trainingStats.tie5Percs = [...trainingStats.tie5Percs, tie5s / total5s];
    trainingStats.losses5Percs = [...trainingStats.losses5Percs, losses5s / total5s];
    homeTrainingStats.points = [...homeTrainingStats.points, validation.reduce((p, x) => p + x.homePoints, 0) / validation.length];
    awayTrainingStats.points = [...awayTrainingStats.points, validation.reduce((p, x) => p + x.awayPoints, 0) / validation.length];
    homeTrainingStats.gold = [...homeTrainingStats.gold, validation.reduce((p, x) => p + x.homeGold, 0) / validation.length];
    awayTrainingStats.gold = [...awayTrainingStats.gold, validation.reduce((p, x) => p + x.awayGold, 0) / validation.length];

    // console.log('[Breeding]', JSON.stringify(getLastTrainingStats(trainingStats)));

    // const arenaInfos = [...state.data.arenaInfos];
    // // Update winners
    // for (let i=0; i < state.layout.arenas; i++) {
    //   arenaInfos[i] = {
    //     ...arenaInfos[i],
    //     winner: arenaStats[i].homePoints > arenaStats[i].awayPoints ? ArenaWinner.Home
    //       : arenaStats[i].homePoints < arenaStats[i].awayPoints ? ArenaWinner.Away
    //       : ArenaWinner.Tie
    //   }
    // }

    // console.table(arenaSelectionSorted);

    state.setData({
      trainingStats,
    });
    state.updateTeam('home', { trainingStats: homeTrainingStats });
    state.updateTeam('away', { trainingStats: awayTrainingStats });

    // Save top creatures

    const getToSaveBrains = async (team: Teams) => {
      const elitism = arenaStats.slice(0, state.data.trainingConfig.elitismCount).map(x => ({
        ...x,
        points: team === Teams.HomeTeam ? (x.homeGold - x.awayGold) : (x.awayGold - x.homeGold)
      }));
      elitism.sort((a, b) => b.points - a.points);
      let toSaveArenaIndex = elitism[0].arenaIndex;
      const toSaveUnits = new EntitiesQuery([Creature.component])
        .filterValue(ArenaInstance.cpuComponent, o => o[0] === toSaveArenaIndex, d => [])
        .filterValue(CpuTeamComponent, o => o === team, d => [])
        .get(state);
      return await Promise.all(toSaveUnits.map(unit => serializeCreatureBrain(state, unit)));
    }
    state.setData({
      toSaveCreatureBrains: [
        ...(state.data.teams.home.train ? await getToSaveBrains(Teams.HomeTeam) : []),
        ...(state.data.teams.away.train ? await getToSaveBrains(Teams.AwayTeam) : []),
      ]
    });

    // state.setData({ arenaInfos });
  }

  copyAndMutate = new CopyAndMutateBrains(this.gls);
}

function numberArrayToFirebaseBlob(values: number[]) {
  const arr = new Float32Array(values);
  return firebase.firestore.Blob.fromUint8Array(new Uint8Array(arr.buffer));
}
async function serializeCreatureBrain(state: GpuWorldState, entityCell: EntityRef) {
  const hiddenLayerWeightsP = state.ecs.getComponentData(HiddenLayer.weights.component, entityCell, true);
  const hiddenLayerBiasesP = state.ecs.getComponentData(HiddenLayer.biases.component, entityCell, true);
  const memoryLayerWeightsP = state.ecs.getComponentData(MemoryLayer.weights.component, entityCell, true);
  const memoryLayerBiasesP = state.ecs.getComponentData(MemoryLayer.biases.component, entityCell, true);
  const outputLayerWeightsP = state.ecs.getComponentData(OutputLayer.weights.component, entityCell, true);
  const outputLayerBiasesP = state.ecs.getComponentData(OutputLayer.biases.component, entityCell, true);
  const playerUnit = state.ecs.getComponentData(CpuPlayerUnitComponent, entityCell)!;
  if (playerUnit.unit.type !== 'creature') {
    throw new Error('Not a creature');
  }
  const brain: DbCreatureBrain = {
    creatureId: playerUnit.unit.id ?? '',
    name: '',
    ownerId: state.appState?.state.loggedInUserId ?? '',
    generation: (playerUnit.unit.generation ?? 0) + state.data.round + 1,
    hiddenLayerWeights: numberArrayToFirebaseBlob(lodash.flatMap(await hiddenLayerWeightsP, v => [v.x, v.y, v.z, v.w])),
    hiddenLayerBiases: numberArrayToFirebaseBlob(await hiddenLayerBiasesP),
    memoryLayerWeights: numberArrayToFirebaseBlob(lodash.flatMap(await memoryLayerWeightsP, v => [v.x, v.y, v.z, v.w])),
    memoryLayerBiases: numberArrayToFirebaseBlob(await memoryLayerBiasesP),
    outputLayerWeights: numberArrayToFirebaseBlob(lodash.flatMap(await outputLayerWeightsP, v => [v.x, v.y, v.z, v.w])),
    outputLayerBiases: numberArrayToFirebaseBlob(await outputLayerBiasesP),
  };
  return { brain, id: playerUnit.unit.id ?? '' };
}

export async function uploadCreatureBrains(state: GpuWorldState) {
  for (const brain of state.data.toSaveCreatureBrains) {
    console.log(`Uploading creature ${brain.id} brain (${brain.brain.generation} generation)`);
    await firebase.firestore().runTransaction(async t => {
      const doc = firebase.firestore().collection(Db.creatureBrains).doc(brain.id);
      t.set(doc, brain.brain);
      t.update(firebase.firestore().collection(Db.creatures).doc(brain.id), partial<DbCreature>({ generation: brain.brain.generation }));
    });
  }
}

export async function getCreatureBrain(creatureId: string) {
  const savedSnap = await firebase.firestore().collection(Db.creatureBrains).doc(creatureId).get();
  return savedSnap.data() as (DbCreatureBrain | undefined);
}
