import * as React from 'react';
import './Editors.css';

export interface LineListLayoutItemProps {
  action?: React.ReactNode,
  item?: React.ReactNode,
  isLast?: boolean;
  isFirst?: boolean;
};

export function LineListLayoutItem({ isLast, isFirst, action, item }: LineListLayoutItemProps) {
  return <div 
      className={`LineListLayoutItem`}
      >
      <div className={`LineListLayoutItemVerticalLine ${isLast ? 'LineListLast' : (isFirst ? 'LineListFirst' : '')}`} />
      <div className="LineListLayoutItemLine" />
      {action}
      {action && item && (
        <div className="LineListLayoutItemLine" />
      )}
      {item && (
        <div className="LineListLayoutItemValue">
          {item}
        </div>
      )}
    </div>;
}

interface LineListLayoutProps {
  className?: string;
  children?: any;
}

export function LineListLayout({ className, children }: LineListLayoutProps) {
  return (
    <div className={'LineListLayout ' + (className || '')}>
      {children}
    </div>
  );
}
