import * as React from 'react';

export function hotkeyIgnoreElement(target: EventTarget | null) {
  if (!target) {
    return false;
  }
  const el = target as Element;
  if (['input', 'select', 'textarea'].indexOf(el.tagName.toLowerCase()) !== -1) {
    return true;
  }
  if ((el as HTMLElement).contentEditable === 'true') {
    return true;
  }
  return false;
}

export function useHotkeyRaw(onKeydown: (event: KeyboardEvent) => void, onKeyup?: (event: KeyboardEvent) => void) {
  React.useEffect(() => {
    const handleKeydown = (event: KeyboardEvent) => {
      if (hotkeyIgnoreElement(event.target)) {
        return;
      }
      onKeydown(event);
    };
    const handleKeyup = (event: KeyboardEvent) => {
      if (hotkeyIgnoreElement(event.target)) {
        return;
      }
      if (onKeyup) {
        onKeyup(event);
      }
    };
    document.addEventListener('keydown', handleKeydown);
    document.addEventListener('keyup', handleKeyup);
    return () => {
      document.removeEventListener('keydown', handleKeydown);
      document.removeEventListener('keyup', handleKeyup);
    }
  }, [onKeydown, onKeyup]);
}

export function useHotkey(keyCode: string | undefined, onKeydown: () => void, onKeyup?: () => void) {
  useHotkeyRaw(
    React.useCallback(event => event.code === keyCode && onKeydown(), [keyCode, onKeydown]),
    React.useCallback(event => event.code === keyCode && onKeyup && onKeyup(), [keyCode, onKeyup])
    );
}
