export class UpdateBloodClaws {
  constructor(private gls: GlState) {}

  singleTargetDamage = new SingleTargetDamage(this.gls,
    [abilitiesData.component[Abilities.BloodClaws].gpuComponent],
    shaderModule([Casting.Target.sm, CastingAbilityHelpers, UnitRef.sm], {
      originator: funcDef('ivec2', [['ivec2', 'entityCell']], `
        return ${UnitRef.sm.value}(entityCell);
      `),
      getTarget: funcDef('ivec2', [['ivec2', 'entityCell']], `
        ivec2 unit = ${UnitRef.sm.value}(entityCell);
        if (!${CastingAbilityHelpers.isPerforming}(unit, ${Abilities.BloodClaws})) {
          return ivec2(-1);
        } else {
          return ${Casting.Target.sm.value}(unit);
        }
      `),
      damageSetting: uniformDef('float', () => abilitiesData.get(Abilities.BloodClaws, 'Settings').damage.value),
      baseDamage: funcDef('float', [['ivec2', 'entityCell']], `
        return Self.damageSetting;
      `)
    }));

  heal = new ArchetypesQuery([abilitiesData.component[Abilities.BloodClaws].gpuComponent])
    .mapGpu(this.gls, Hitpoints.component, shaderModule([
      CastingAbilityHelpers,
      Casting.Target.sm,
      Hitpoints.sm,
      Hitpoints.writer,
      UnitRef.sm,
    ], {
      map: funcDef('float', [['ivec2', 'entityCell'], ['float', 'value']], `
        ivec2 unit = ${UnitRef.sm.value}(entityCell);
        if (!${CastingAbilityHelpers.isPerforming}(unit, ${Abilities.BloodClaws})) {
          return value;
        }
        float targetHp = ${Hitpoints.sm.value}(${Casting.Target.sm.value}(unit));
        return value + min(targetHp, 20.);
      `)
    }));

  run(state: GpuWorldState) {
    this.singleTargetDamage.run(state);
    this.heal.run(state);
  }
}

export class UpdateCripplers {
  constructor(private gls: GlState) {}

  singleTargetDamage = new SingleTargetDamage(this.gls,
    [abilitiesData.component[Abilities.Cripplers].gpuComponent],
    shaderModule([Casting.Target.sm, CastingAbilityHelpers, UnitRef.sm], {
      originator: funcDef('ivec2', [['ivec2', 'entityCell']], `
        return ${UnitRef.sm.value}(entityCell);
      `),
      getTarget: funcDef('ivec2', [['ivec2', 'entityCell']], `
        ivec2 unit = ${UnitRef.sm.value}(entityCell);
        if (!${CastingAbilityHelpers.isPerforming}(unit, ${Abilities.Cripplers})) {
          return ivec2(-1);
        } else {
          return ${Casting.Target.sm.value}(unit);
        }
      `),
      damageSetting: uniformDef('float', () => abilitiesData.get(Abilities.BloodClaws, 'Settings').damage.value),
      baseDamage: funcDef('float', [['ivec2', 'entityCell']], `
        return Self.damageSetting;
      `)
    }));

  buff = new ArchetypesQuery([abilitiesData.component[Abilities.Cripplers].gpuComponent])
    .updateGpu(this.gls, [Crippleable.component], shaderModule([
      CastingAbilityHelpers, Casting.Target.sm, Crippleable.writer, UnitRef.sm
    ], {
      update: funcDef('ivec2', [['ivec2', 'entityCell']], `
        ivec2 unit = ${UnitRef.sm.value}(entityCell);
        if (!${CastingAbilityHelpers.isPerforming}(unit, ${Abilities.Cripplers})) {
          return ivec2(-1);
        }
        ${Crippleable.writer.write}(60 * 3); // duration
        return ${Casting.Target.sm.value}(unit);
      `),
    }));

  run(state: GpuWorldState) {
    this.singleTargetDamage.run(state);
    this.buff.run(state);
  }
}
