import { GpuBuffer, EntityComponentSystem, GpuBufferFormats, GpuComponent, TokenComponent, CpuComponent, ComponentFormats } from ".";
import { gpuComponentReadSM, gpuComponentBufferWriterSM, hasComponentSM } from "./GpuComponentSM";

export class GpuBufferModule<F extends GpuBufferFormats> {
  constructor(public buffer: GpuBuffer<F>) {
    EntityComponentSystem.registerGpuBuffer(this.buffer);
  }
}

export class TokenComponentModule {
  constructor(private name: string) {
  }
  component: TokenComponent = { type: 'token', name: this.name };
  hasComponentSM = hasComponentSM(this.component);
}

export class GpuComponentModule<F extends GpuBufferFormats> {
  constructor(private name: string, private buffer: GpuBuffer<F> | GpuBufferModule<F>, private format: GpuComponent['format'], private depth?: number) {
  }
  component: GpuComponent<F> = {
    type: 'gpu',
    name: this.name,
    buffer: this.buffer instanceof GpuBufferModule ? this.buffer.buffer : this.buffer,
    format: this.format,
    depth: this.depth
  };
  sm = gpuComponentReadSM(this.component);
  writer = gpuComponentBufferWriterSM(this.component);
  hasComponentSM = hasComponentSM(this.component);
}

export class DualComponentModule<F extends GpuBufferFormats, C extends ComponentFormats> {
  constructor(private name: string, private buffer: GpuBuffer<F> | GpuBufferModule<F>, private format: C) {
  }
  gpuComponent: GpuComponent<F> = {
    type: 'gpu',
    name: this.name + 'Gpu',
    buffer: this.buffer instanceof GpuBufferModule ? this.buffer.buffer : this.buffer,
    format: this.format
  };
  cpuComponent: CpuComponent<C> = {
    type: 'cpu',
    name: this.name + 'Cpu',
    format: this.format,
    stride: this.gpuComponent.buffer.format === 'vec4' ? 4 : undefined
  };
  components = [this.gpuComponent, this.cpuComponent];
  sm = gpuComponentReadSM(this.gpuComponent);
  writer = gpuComponentBufferWriterSM(this.gpuComponent);
  hasComponentSM = hasComponentSM(this.gpuComponent);
}
