import { Subtract } from "utility-types";
import { NumberEditorOptions, StructEditorLayout, SliderEditorOptions } from ".";

export type EditorTypedefRef = string;
export type EditorTypedefUserDefined = EditorStructTypedef | EditorEnumStructTypedef | EditorObnumTypedef | EditorEnumTypedef;
export type EditorTypedef = EditorTypedefUserDefined | EditorBooleanTypedef | EditorFloatTypedef | EditorPercentageTypedef |
  EditorIntTypedef | EditorSliderTypedef | EditorStringTypedef |
  EditorListTypedef | EditorTableTypedef | EditorTupleTypedef| EditorKeyValueTypedef | EditorSelectTypedef | EditorMapTypedef |
  EditorColorTypedef | EditorFileTypedef | EditorPixlingTimespanDefTypedef;

export type EditorTypedefConvertible = EditorTypedefRef | EditorTypedef | (() => EditorTypedef);

export type EditorStructProperties1<T=any> = {
  [P in keyof Partial<T>]: EditorTypedefConvertible;
}
export type EditorStructProperties2<T=any> = Array<{
  name: keyof T;
  type: EditorTypedefConvertible;
  label?: string;
  title?: string;
  visible?: boolean;
}>
export type EditorStructProperties<T=any> = EditorStructProperties1<T> | EditorStructProperties2<T>;
export function editorStructProperties<T>(props: EditorStructProperties<T>): EditorStructProperties<T> {
  return props;
}
export interface EditorStructTypedef<T=any> {
  metatype: 'struct';
  properties: EditorStructProperties<T>;
  layout?: StructEditorLayout;
}
export function editorStructTypedef<T>(opts: Subtract<EditorStructTypedef<T>, { metatype: 'struct' }>): EditorStructTypedef<T> {
  return {
    metatype: 'struct',
    ...opts
  };
}

export interface EditorEnumStructVariants {
  [typeName: string]: {
    displayName?: string;
    labels?: { [key: string]: string };
    properties: EditorStructProperties;
    default: (type: string) => any;
    description?: string
  };
}
export interface EditorEnumStructTypedef {
  metatype: 'enumstruct';
  variants: EditorEnumStructVariants;
}

export type EditorEnumVariants = Array<{ value: any; displayName?: JSX.Element, description?: string, className?: string } | string> | { [displayName: string]: any };
interface EditorEnumTypedef {
  metatype: 'enum';
  variants: EditorEnumVariants | (() => EditorEnumVariants);
  default?: any;
}

export interface EditorObnumVariants {
  [name: string]: { itemType: EditorTypedef, default?: any };
}
export interface EditorObnumTypedef {
  metatype: 'obnum';
  variants: EditorObnumVariants;
}

export interface EditorBooleanTypedef {
  metatype: 'boolean';
  asDropDown?: { true: string, false: string };
  default?: boolean;
}

export interface EditorFloatTypedef extends NumberEditorOptions {
  metatype: 'float';
}

export interface EditorIntTypedef extends NumberEditorOptions {
  metatype: 'int';
}

export interface EditorSliderTypedef extends SliderEditorOptions {
  metatype: 'slider';
}

export interface EditorPercentageTypedef {
  metatype: 'percentage';
  min?: number;
  max?: number;
  step?: number;
  slider?: boolean;
}


export interface EditorStringTypedef {
  metatype: 'string';
  multiline?: boolean;
  readOnly?: boolean;
}

export interface EditorListTypedef {
  metatype: 'list';
  itemType: EditorTypedefConvertible;
  newItem: () => any;
  keyFromItem?: (item: any) => any;
  acceptItemDrop?: (item: any) => boolean;
  readOnly?: boolean;
}

export interface EditorTableTypedef {
  metatype: 'table';
  itemProperties: EditorStructProperties;
  itemPropertiesLabels?: { [key: string]: string };
  newItem: () => any;
}

export interface EditorTupleTypedef {
  metatype: 'tuple';
  itemTypes: Array<EditorTypedef | EditorTypedefRef>;
}

export interface EditorKeyValueTypedef {
  metatype: 'keyvalue';
  valueType: EditorTypedef | EditorTypedefRef;
  valueDefault?: any;
}

export interface EditorSelectTypedef {
  metatype: 'select';
  propertyKey: string;
  propertyType: EditorTypedef;
}

export interface EditorMapTypedef {
  metatype: 'map';
  map: (value: any) => any;
  mappedType: EditorTypedef;
}

export interface EditorColorTypedef {
  metatype: 'color';
}

export interface EditorFileTypedef {
  metatype: 'file';
}


export interface EditorPixlingTimespanDefTypedef {
  metatype: 'pixlingTimespanDef';
}

export type EditorTypedefResolver = (typeName: string) => EditorTypedef | undefined;
