import { GpuComponentModule, GpuBufferModule } from "../../GpuUtil/ECS/ComponentModules";
import { GlState } from "../../GpuUtil/GlState";
import { ArchetypesQuery, EntitiesQuery } from "../../GpuUtil/ECS/Query";
import { shaderModule, funcDef, structDef } from "../../GpuUtil/ShaderModule";
import { Position, RotationZ } from "../ECS/Transform";
import { GpuWorldState } from "../State";
import { EntityInit, EntityComponentSystem } from "../../GpuUtil/ECS";
import { SingleTargetDamage } from "../ECS/DamageCalc";
import { ArenaInstance } from "../ECS/Components";
import { Dazeable } from "../Buffs/Daze";
import { Force, LastPushedBy } from "../ECS/UnitComponents";
import BulletGlb from '../../Models/Bullet.glb';
import DartGlb from '../../Models/Dart.glb';
import { ThreeModel } from "../../GpuUtil/Model";
import { VisibleComponent } from "../ECS/Components";
import { AudioPlayerInstance } from "../Audio";
import { SoundEffect } from "../StateLayout";
import { visibleCacheKeys } from "../ECS/Visibility";

export const BulletModel = ThreeModel.fromPath(BulletGlb);
export const DartModel = ThreeModel.fromPath(DartGlb);

const ProjectileBuffer = new GpuBufferModule({ name: 'ProjectileBuffer', format: 'vec4' });

export enum ProjectileType {
  Bullet,
  KnockbackBullet,
  SleepingDart,
}

const CpuProjectileTypeComponent = EntityComponentSystem.registerComponent<ProjectileType>({
  name: 'CpuProjectileType',
  type: 'obj',
});
export const ProjectileOriginator = new GpuComponentModule('ProjectileOriginator', ProjectileBuffer, 'ivec2');
export const SeekingProjectileTarget = new GpuComponentModule('SeekingProjectileTarget', ProjectileBuffer, 'ivec2');
export const ProjectileSpeed = new GpuComponentModule('ProjectileSpeed', ProjectileBuffer, 'float');
export const DamageProjectile = new GpuComponentModule('DamageProjectile', ProjectileBuffer, 'float');
export const DazeProjectile = new GpuComponentModule('DazeProjectile', ProjectileBuffer, 'float');
export const KnockbackProjectile = new GpuComponentModule('KnockbackProjectile', ProjectileBuffer, 'float');
export const ProjectilePreviousPosition = new GpuComponentModule('ProjectilePreviousPosition', ProjectileBuffer, 'vec3');

export async function createBulletProjectile(state: GpuWorldState, arenaInstance: number, type: ProjectileType) {
  let entityInit = new EntityInit()
    .setComponent(Position.gpuComponent)
    .setComponent(RotationZ.component)
    .setComponent(ProjectileOriginator.component)
    .setComponent(SeekingProjectileTarget.component)
    .setComponent(DamageProjectile.component)
    .setComponent(ProjectileSpeed.component, [new Float32Array([1, 0, 0, 0])])
    .setComponent(ProjectilePreviousPosition.component)
    .setComponents(ArenaInstance.components, new Float32Array([arenaInstance]))
    .setComponent(CpuProjectileTypeComponent, type)
  if (type === ProjectileType.KnockbackBullet) {
    entityInit = entityInit.setComponent(KnockbackProjectile.component);
  } else if (type === ProjectileType.SleepingDart) {
    entityInit = entityInit.setComponent(DazeProjectile.component);
  }
  const res = (type === ProjectileType.SleepingDart ? await DartModel : await BulletModel).createInstances(state, {
    rootEntityInit: entityInit,
    visible: false,
  })
  return res.roots.getIndex(0);
}

const ProjectileHelpers = shaderModule([
  Position.sm,
  SeekingProjectileTarget.sm,
  ProjectileSpeed.sm,
  ProjectilePreviousPosition.sm,
], {
  ProjectileState: structDef({
    target: 'ivec2',
    position: 'vec3',
    targetPosition: 'vec3',
    speed: 'float',
    delta: 'vec3',
    velocity: 'vec3',
    hitting: 'bool'
  }),
  getProjectileState: funcDef('ProjectileState', [['ivec2', 'entityCell']], `
    ProjectileState state;
    state.target = ${SeekingProjectileTarget.sm.value}(entityCell);
    state.position = ${Position.sm.value}(entityCell);
    state.targetPosition = ${Position.sm.value}(state.target) + vec3(0, 0, 1);
    state.speed = ${ProjectileSpeed.sm.value}(entityCell);
    state.delta = state.targetPosition - state.position;
    state.hitting = length(state.delta) < state.speed;
    vec3 previousPosition = ${ProjectilePreviousPosition.sm.value}(entityCell);
    state.velocity = state.position - previousPosition;
    return state;
  `),
})

export class UpdateProjectiles {
  constructor(private gls: GlState) {}

  applyDamage = new SingleTargetDamage(this.gls,
    [DamageProjectile.component],
    shaderModule([
      ProjectileHelpers, DamageProjectile.sm, ProjectileOriginator.sm
    ], {
      originator: funcDef('ivec2', [['ivec2', 'entityCell']], `
        return ${ProjectileOriginator.sm.value}(entityCell);
      `),
      getTarget: funcDef('ivec2', [['ivec2', 'entityCell']], `
        ProjectileState state = ${ProjectileHelpers.getProjectileState}(entityCell);
        if (!state.hitting) {
          return ivec2(-1);
        } else {
          return state.target;
        }
      `),
      baseDamage: funcDef('float', [['ivec2', 'entityCell']], `
        return ${DamageProjectile.sm.value}(entityCell);
      `)
    }));

  applyDaze = new ArchetypesQuery([DazeProjectile.component])
    .updateGpu(this.gls, [Dazeable.component], shaderModule([
      ProjectileHelpers,
      SeekingProjectileTarget.sm,
      DazeProjectile.sm,
      Dazeable.writer
    ], {
      update: funcDef('ivec2', [['ivec2', 'entityCell']], `
        ProjectileState state = ${ProjectileHelpers.getProjectileState}(entityCell);
        if (!state.hitting) {
          return ivec2(-1);
        }
        float duration = ${DazeProjectile.sm.value}(entityCell);
        ${Dazeable.writer.write}(int(duration));
        return state.target;
      `)
    }), 'sum');

  applyKnockback = new ArchetypesQuery([KnockbackProjectile.component])
    .updateGpu(this.gls, [Force.component], shaderModule([
      ProjectileHelpers,
      SeekingProjectileTarget.sm,
      KnockbackProjectile.sm,
      Force.writer
    ], {
      update: funcDef('ivec2', [['ivec2', 'entityCell']], `
        ProjectileState state = ${ProjectileHelpers.getProjectileState}(entityCell);
        if (!state.hitting) {
          return ivec2(-1);
        }
        float force = ${KnockbackProjectile.sm.value}(entityCell);
        ${Force.writer.write}(normalize(state.velocity) * force);
        return state.target;
      `)
    }), 'sum');

  applyLastPushedBy = new ArchetypesQuery([KnockbackProjectile.component])
    .updateGpu(this.gls, [LastPushedBy.component], shaderModule([
      ProjectileHelpers,
      SeekingProjectileTarget.sm,
      KnockbackProjectile.sm,
      LastPushedBy.writer,
      ProjectileOriginator.sm,
    ], {
      update: funcDef('ivec2', [['ivec2', 'entityCell']], `
        ProjectileState state = ${ProjectileHelpers.getProjectileState}(entityCell);
        if (!state.hitting) {
          return ivec2(-1);
        }
        ${LastPushedBy.writer.write}(${ProjectileOriginator.sm.value}(entityCell));
        return state.target;
      `)
    }));

  updateProjectiles = new ArchetypesQuery([SeekingProjectileTarget.component, ProjectileSpeed.component])
    .updateGpu(this.gls, [Position.gpuComponent, RotationZ.component, SeekingProjectileTarget.component, ProjectilePreviousPosition.component], shaderModule([
      Position.writer,
      RotationZ.writer,
      Position.sm,
      SeekingProjectileTarget.sm,
      ProjectileSpeed.sm,
      SeekingProjectileTarget.writer,
      ProjectileHelpers,
      ProjectilePreviousPosition.writer,
    ], {
      update: funcDef('void', [['ivec2', 'entityCell']], `
        ProjectileState state = ${ProjectileHelpers.getProjectileState}(entityCell);
        if (!state.hitting) {
          vec3 delta = normalize(state.delta);
          ${Position.writer.write}(state.position + delta * state.speed);
          ${RotationZ.writer.write}(atan(delta.y, delta.x));
          ${SeekingProjectileTarget.writer.write}(state.target);
          ${ProjectilePreviousPosition.writer.write}(state.position);
        } else {
          ${Position.writer.write}(vec3(-10000));
          ${RotationZ.writer.write}(0.0);
          ${SeekingProjectileTarget.writer.write}(ivec2(-1));
          ${ProjectilePreviousPosition.writer.write}(vec3(0));
        }
      `)
    }));

  sound = new EntitiesQuery([ProjectileSpeed.component])
    .filterValue(VisibleComponent, v => !!v, visibleCacheKeys)
    .toGpu(this.gls)
    .mapToTemp(shaderModule([
      ProjectileHelpers,
    ], {
      map: funcDef('vec4[1]', [['ivec2', 'entityCell']], `
        ProjectileState state = ${ProjectileHelpers.getProjectileState}(entityCell);
        return vec4[](
          vec4(state.position, state.hitting)
        );
      `)
    }))
    .toCpuQueuing(state => state.data.round);

  run(state: GpuWorldState) {
    this.applyDamage.run(state);
    this.applyDaze.run(state);
    this.applyKnockback.run(state);
    this.applyLastPushedBy.run(state);
    this.updateProjectiles.run(state);
    const readResults = this.sound.queueRead(state);
    for (const res of readResults()) {
      for (const { entityCell, value } of res) {
        const position = value[0];
        const hitting = !!value[0].w;
        if (hitting && AudioPlayerInstance) {
          const type = state.ecs.getComponentData(CpuProjectileTypeComponent, entityCell);
          if (type === ProjectileType.SleepingDart) {
            AudioPlayerInstance.playSoundEffect(SoundEffect.DartThud, 0, position);
          }
        }
      }
    }
  }
}
