import lodash from 'lodash'

export interface ResultOk<T> {
  result: 'ok';
  value: T;
}
export function ok<T>(value: T): ResultOk<T> {
  return {
    result: 'ok',
    value
  }
}
export function isOk<T, E>(result: Result<T, E>): result is ResultOk<T> {
  return result.result === 'ok';
}
export interface ResultErr<E> {
  result: 'err';
  error: E;
}
export function err<E>(error: E): ResultErr<E> {
  return {
    result: 'err',
    error
  }
}
export function isErr<T, E>(result: Result<T, E>): result is ResultErr<E> {
  return result.result === 'err';
}
export type Result<T, E = string> = ResultOk<T> | ResultErr<E>;

export function isResult<T, E>(value: Result<T, E> | T): value is Result<T, E> {
  return lodash.isObject(value) && (isOk(value as Result<T, E>) || isErr(value as Result<T, E>));
}

class UnwrapError<E> extends Error {
  constructor(public error: E) {
    super(`UnwrapError: ${JSON.stringify(error)}`);
  }
}

export function unwrap<T, E>(result: Result<T, E>): T {
  if (isOk(result)) {
    return result.value;
  } else {
    throw new UnwrapError(result.error);
  }
}

export function unwrapOr<T, E>(result: Result<T, E>, or: T): T {
  if (isOk(result)) {
    return result.value;
  } else {
    return or;
  }
}

export function unwrapError<T, E>(result: Result<T, E>): E {
  if (isErr(result)) {
    return result.error;
  } else {
    throw new Error('Result was ok');
  }
}

export function ifThen<T1, T2, E1, E2=E1>(a: Result<T1, E1>, b: (value: T1) => (Result<T2, E2> | T2)): Result<T2, E1 | E2> {
  if (isErr(a)) {
    return a;
  } else {
    const r = b(a.value);
    if (isResult(r)) {
      return r;
    } else {
      return ok(r);
    }
  }
}

type ResolvableObject<T, E> = {
  [P in keyof T]: Result<T[P], E>;
};

export function resolveObject<T extends { [key: string]: any }, E>(
    args: ResolvableObject<T, E>,
    errType?: E
  ): Result<T, E> {
  const resolvedArgs = {} as any;
  for (const key of Object.keys(args)) {
    const res = args[key];
    if (isErr(res)) {
      return res;
    } else {
      resolvedArgs[key] = res.value;
    }
  }
  return ok(resolvedArgs as T);
}

export function resolveArray<T, E>(
    array: Array<Result<T, E>>
  ): Result<T[], E> {
  const resolvedArray: T[] = [];
  for (const res of array) {
    if (isErr(res)) {
      return res;
    } else {
      resolvedArray.push(res.value);
    }
  }
  return ok(resolvedArray);
}

export function mapError<T, E1, E2>(result: Result<T, E1>, map: (error: E1) => E2): Result<T, E2> {
  if (isErr(result)) {
    return err(map(result.error));
  } else {
    return result;
  }
}

export function mapOk<T1, T2, E>(result: Result<T1, E>, map: (ok: T1) => T2): Result<T2, E> {
  if (isErr(result)) {
    return result;
  } else {
    return ok(map(result.value));
  }
}
