import { InitWorldStateConfig, newInitWorldStateConfiguration } from "./StateInit";
import { GpuWorldStateLayout, SideSize } from "./StateLayout";
import { emptyGameLoopStats } from "./GameLoop";
import { defaultRenderOptions } from "./Renderer/Configuration";
import * as Vector2 from '../Math/Vector2';
import * as Vector3 from '../Math/Vector3';
import { RGBA, rgbFromHex, rgb255ToVec3 } from "../Util";
import { BattleWinner, BattleResult, DbCreatureBrain, DbBattleground } from "db";
import { AbilitiesData } from "./Abilities/Abilities";
import { HomeHealthColor, ManaColor, AwayHealthColor } from "./StateLayout";
import { RoundStats } from "./Tools/RoundStats";
import { DbTeam } from 'db';
import { ArenaInfo } from "./Tools/ArenaInfos";
import { TrainingStats, TeamTrainingStats, TrainingConfig } from "./Tools/Breeding";
import { UnitDef, CreatureDef } from "./UnitDef";
import { GpuComponent } from "../GpuUtil/ECS";
import { AnimationClip } from "./ECS/Animation";

export type GameInstance = {
  type: 'none'
} | {
  type: 'battle';
  battleground: DbBattleground;
  homeTeam: Array<UnitDef | undefined>;
} | {
  type: 'train';
  battleground: DbBattleground;
  setupIndex: number;
  homeTeam: Array<UnitDef | undefined>;
  awayTeam: Array<UnitDef | undefined>;
  trainHome: boolean;
  trainAway: boolean;
  arenas: number;
  rounds: number;
  turbo?: boolean;
} | {
  type: 'replay';
  replayId: string;
} | {
  type: 'gym'
};


export interface DebugAudioEffectPlaying { id: string, position: Vector3.Vector3, color: RGBA }

export class TeamSettings {
  id?: string;
  units: Array<UnitDef | undefined> = new Array(SideSize).fill(undefined);
  unitCount = 0;
  dbTeam?: DbTeam;
  battlePoints = 0;
  battleUnitsAlive = SideSize;
  maxPossiblePoints = 0;
  trainingStats = new TeamTrainingStats();
  train = true;
}
export class TeamMetrics {
  alive = 0;
  generation = 0;
}

export type EntityRef = Vector2.Vector2;
export const NullEntityRef: EntityRef = { x: -1, y: -1 };

interface LogEntry {
  time: Date;
  step: number;
  category: string;
  message: string[];
}

export interface RoundResult {
  victories: number;
  defeats: number;
  ties: number;
}

export class GpuWorldStateData {
  constructor(public initConfig: InitWorldStateConfig = newInitWorldStateConfiguration()) {}

  layout = new GpuWorldStateLayout(this.initConfig);

  gameInstance: GameInstance = { type: 'none' };
  battleground = DbBattleground.Solo;

  battleId = '';
  battleResult: BattleWinner = BattleWinner.Undecided;
  completeBattleRes?: BattleResult;
  battlePostUpdateDone = false;

  trainingRounds = 0;

  worldSize = this.layout.worldSize;
  worldNCells = this.worldSize.width * this.worldSize.height;
  groundResolution = 4;
  groundSize = {
    width: Math.ceil(this.layout.worldSize.width / this.groundResolution),
    height: Math.ceil(this.layout.worldSize.height / this.groundResolution),
  };


  simulationStartTime = new Date();

  stepCounter = 0;
  battleStartStep = 0;
  roundResults: RoundResult[] = [];
  round = 0;
  randomSeed = Math.random();
  log: Array<LogEntry> = [];

  simulationPaused = true;
  simulationEnded = false;
  substep = 0;
  simulationSpeed = 1;
  turboMode = false;
  teams = {
    home: new TeamSettings(),
    away: new TeamSettings()
  };
  creaturesById: { [id: string]: CreatureDef } = {};

  ensureRenderNextLoop = true;  // Don't modify directly, use state.ensureRenderer(), so breakpoints to figure out why things are rendering work
  renderWorld = true;

  physicsTimestep = 1/60;

  previousMousePosition?: Vector2.Vector2;
  mousePosition?: Vector2.Vector2;
  mouseoverGround?: Vector3.Vector3;
  canvasRect: ClientRect = { left: 0, top: 0, width: 1, height: 1, right: 0, bottom: 0 };
  fullscreen = false;
  camera?: EntityRef;
  cameraMode: 'rts' | 'free' | 'follow' = 'rts';
  decisionHistogramView: ClientRect = { left: 0, top: 0, width: 1, height: 1, right: 0, bottom: 0 };
  sensesHistogramView: ClientRect = { left: 0, top: 0, width: 1, height: 1, right: 0, bottom: 0 };
  showDecisionsHistogram = false;
  showSensesHistogram = false;
  showDecisionsViz = false;
  renderGazes = false;

  gameLoopStats = emptyGameLoopStats();

  animationClips: {
    [key: string]: AnimationClip
  } = {};

  movement = {
    crippledSlowdown: 0.8,
    uphillPunish: 3,
  }

  userControlEntity?: EntityRef;
  userControlSet: { [key: string]: { component: GpuComponent, value: any } } = {};

  abilities = new AbilitiesData();
  showAbilitiesList = false;

  rendererConfig = defaultRenderOptions();

  renderArena = 0;
  renderAllArenas = false;
  roundStats = new RoundStats();

  trainingConfig: TrainingConfig = {
    weightsRenormalizationRate: 0,
    elitismCount: 10,
  };
  unitBountiesEmpty = true;
  arenaInfos: ArenaInfo[] = new Array(this.layout.arenas).fill(0).map(() => new ArenaInfo());
  toSaveCreatureBrains: Array<{ id: string, brain: DbCreatureBrain }> = [];
  // parentArenaIndices: number[] = [];
  autoStartNextTrainingRound = true;
  trainingStats = new TrainingStats();

  showArenaList = false;
  showBrainMemberIndex = -1;

  homeHealthColor = rgb255ToVec3(rgbFromHex(HomeHealthColor));
  awayHealthColor = rgb255ToVec3(rgbFromHex(AwayHealthColor));
  manaColor = rgb255ToVec3(rgbFromHex(ManaColor));
  aiCrowdLogo = false;

  debugAudioEffectsPlaying: DebugAudioEffectPlaying[] = [];
}
export function defaultGpuWorldStateData(initConfig?: InitWorldStateConfig): GpuWorldStateData {
  return new GpuWorldStateData(initConfig);
}
