import { Texture } from "./Texture";
import { Mesh, createQuadMesh, createBoxMesh } from "./Mesh";
import { ShaderCache } from "./ShaderCache";
import { OutputRect } from "./Computation";
import { Program } from "./Program";
import { CopyTexture } from "./Computations/Copy";
import { initGlExtensions } from ".";
import * as THREE from "three";
import { Size } from "../Math/Math";

export interface Destructible {
  destroy(): void;
}

class GpuStateCurrent {
  constructor(private gl: WebGL2RenderingContext) {}

  maxCombinedTextureImageUnits = this.gl.getParameter(this.gl.MAX_COMBINED_TEXTURE_IMAGE_UNITS);

  framebuffer: WebGLFramebuffer | null | undefined = undefined;
  program: WebGLProgram | null | undefined = undefined;
  // mesh: Mesh | null | undefined = undefined;
  activeTextures: Array<Texture | null | undefined> = new Array(this.maxCombinedTextureImageUnits).fill(undefined);
  samplers: Array<TextureFiltering | null | undefined> = new Array(this.maxCombinedTextureImageUnits).fill(undefined);
  viewport: OutputRect = { width: 0, height: 0 };
  caps = {};
  enabledVertexAttribArray: number[] = [];
}

// Because it's expensive to update state in GL all states need to be cached and only updated when actually changed
export class GlState {
  constructor(public canvas: HTMLCanvasElement, public gl: WebGL2RenderingContext) {
    for (const e of initGlExtensions(gl)) {
      console.warn(`Missing GL extension:`, e);
    }
    gl.disable(gl.BLEND);
    canvas.addEventListener("webglcontextlost", () => this.contextLost = true);
  }
  destroy() {
    for (const k of Object.keys(this.meshes)) {
      this.meshes[k].destroy();
    }
  }

  private current = new GpuStateCurrent(this.gl);
  // eslint-disable-next-line no-use-before-define
  private commonSamplers = new CommonSamplers(this.gl);

  public contextLost = false;
  public shaderCache = new ShaderCache(this.gl);

  public reset() {
    this.current = new GpuStateCurrent(this.gl);
  }

  get framebuffer() { return this.current.framebuffer || null; }
  set framebuffer(value: WebGLFramebuffer | null) {
    if (this.current.framebuffer !== value) {
      this.gl.bindFramebuffer(this.gl.FRAMEBUFFER, value);
      this.current.framebuffer = value;
    }
  }

  get program() { return this.current.program || null; }
  set program(value: WebGLProgram | null) {
    if (this.current.program !== value) {
      this.gl.useProgram(value);
      this.current.program = value;
    }
  }

  // private get mesh() { return this.current.mesh || null; }
  // private set mesh(value: Mesh | null) {
  //   if (this.current.mesh !== value) {
  //     if (value) {
  //       value.bind();
  //     }
  //     this.current.mesh = value;
  //   }
  // }

  getActiveTexture(index: number) {
    return this.current.activeTextures[index];
  }
  setActiveTexture(index: number, value: Texture | null) {
    if (this.current.activeTextures[index] !== value) {
      const gl = this.gl;
      gl.activeTexture(gl.TEXTURE0 + index);
      const target = value && value.is2dArray ? gl.TEXTURE_2D_ARRAY : gl.TEXTURE_2D;
      const nonTarget = value && value.is2dArray ? gl.TEXTURE_2D : gl.TEXTURE_2D_ARRAY;
      gl.bindTexture(target, value ? value.texture : null);
      gl.bindTexture(nonTarget, null);

      this.current.activeTextures[index] = value;
    }
  }
  get enabledVertexAttribArray() { return this.current.enabledVertexAttribArray; }
  set enabledVertexAttribArray(value: number[]) {
    for (const i of this.current.enabledVertexAttribArray) {
      if (value.indexOf(i) === -1) {
        this.gl.disableVertexAttribArray(i);
      }
    }
    for (const i of value) {
      if (this.current.enabledVertexAttribArray.indexOf(i) === -1) {
        this.gl.enableVertexAttribArray(i);
      }
    }
    this.current.enabledVertexAttribArray = value;
  }

  getSampler(index: number) {
    return this.current.samplers[index];
  }
  setSampler(index: number, value: TextureFiltering) {
    if (this.current.samplers[index] !== value) {
      this.gl.bindSampler(index, this.commonSamplers.getSampler(value));
      this.current.samplers[index] = value;
    }
  }

  get viewport() { return this.current.viewport; }
  set viewport(value: OutputRect) {
    this.gl.viewport(value.x !== undefined ? value.x : 0, value.y !== undefined ? value.y : 0,
      value.width, value.height);
    this.current.viewport = value;
  }

  getCap(cap: number) { return !!this.current.caps[cap]; }
  setCap(cap: number, value: boolean) {
    if (this.current.caps[cap] !== value) {
      if (value) {
        this.gl.enable(cap);
      } else {
        this.gl.disable(cap);
      }
      this.current.caps[cap] = value;
    }
  }
  enable(cap: number) { this.setCap(cap, true); }
  disable(cap: number) { this.setCap(cap, false); }

  blendFunc(sfactor: number, dfactor: number) {
    this.gl.blendFunc(sfactor, dfactor);
  }
  blendFuncSeparate(srcRGB: number, dstRGB: number, srcAlpha: number, dstAlpha: number) {
    this.gl.blendFuncSeparate(srcRGB, dstRGB, srcAlpha, dstAlpha);
  }
  blendEquation(mode: number) {
    this.gl.blendEquation(mode);
  }
  depthFunc(func: number) {
    this.gl.depthFunc(func);
  }
  cullFace(mode: number) {
    this.gl.cullFace(mode);
  }
  polygonOffset(factor: number, units: number) {
    this.gl.polygonOffset(factor, units);
  }
  depthMask(flag: boolean) {
    this.gl.depthMask(flag);
  }

  draw(program: Program, mesh: Mesh, instanceCount?: number) {
    this.program = program.program;
    // this.mesh = mesh;
    mesh.bind(program);

    const gl = this.gl;
    if (mesh.index) {
      if (instanceCount !== undefined) {
        gl.drawElementsInstanced(gl.TRIANGLES, mesh.index.size, gl.UNSIGNED_SHORT, 0, instanceCount);
      } else {
        gl.drawElements(gl.TRIANGLES, mesh.index.size, gl.UNSIGNED_SHORT, 0);
      }
    } else {
      const nVertices = mesh.attributes.position.buffer.size / mesh.attributes.position.itemSize;
      if (instanceCount !== undefined) {
        gl.drawArraysInstanced(gl.TRIANGLE_STRIP, 0, nVertices, instanceCount);
      } else {
        gl.drawArrays(gl.TRIANGLE_STRIP, 0, nVertices);
      }
    }
    this.stats.drawCalls++;

    // const status = this.gl.getError();
    // if (status !== this.gl.NO_ERROR) {
    //   throw new Error(`Gl error: ${status} ${getGlConstantName(this.gl, status)}`);
    // }
  }
  drawQuad(program: Program, instanceCount?: number) {
    this.draw(program, this.meshes.quad, instanceCount);
  }

  // Some generally usefull stuff
  private meshes: { [key: string]: Mesh } = {
    box: createBoxMesh(this),
    quad: createQuadMesh(this),
    sphere: Mesh.fromThree(this, new THREE.SphereBufferGeometry(1, 0))
  };
  public textures: { [key: string]: Texture } = {
  }
  public samplers: { [key: string]: Texture } = {
  }
  getMesh(id: string) {
    if (this.meshes[id]) {
      return this.meshes[id];
    } else {
      const m = MeshLoaders[id];
      if (m) {
        return this.meshes[id] = m(this);
      } else {
        return undefined;
      }
    }
  }
  private tmpTextures: { [key: string]: Texture } = {};
  getTmpTexture(internalFormat: number, minSize: Size, minDepth: number) {
    let t = this.tmpTextures[internalFormat];
    if (!t || t.size.width < minSize.width || t.size.height < minSize.height || t.depth! < minDepth) {
      if (t) {
        t.destroy();
      }
      const newSize = t ? { width: t.size.width, height: t.size.height } : { width: 1, height: 1 };
      let newDepth = t ? t.depth! : 1;
      while (newSize.width < minSize.width) {
        newSize.width *= 2;
      }
      while (newSize.height < minSize.height) {
        newSize.height *= 2;
      }
      while (newDepth < minDepth) {
        newDepth *= 2;
      }
      this.tmpTextures[internalFormat] = t = new Texture(this, internalFormat, newSize, newDepth).init();
      // console.log(`[getTmpTexture] Resizing ${internalFormat} ${JSON.stringify(t.size)} ${t.depth}`);
    }
    return t;
  }
  getTmpTextureSlice(internalFormat: number, size: Size, depth: number) {
    return this.getTmpTexture(internalFormat, size, depth)
      .sliceRange(0, depth)
      .selectAbsolute({ x: 0, y: 0, width: size.width, height: size.height });
  }
  stats = {
    drawCalls: 0
  }
  maxColorAttachements = this.gl.getParameter(this.gl.MAX_COLOR_ATTACHMENTS);
  maxCombinedTextureImageUnits = this.gl.getParameter(this.gl.MAX_COMBINED_TEXTURE_IMAGE_UNITS);
  maxTextureSize = this.gl.getParameter(this.gl.MAX_TEXTURE_SIZE);
  private copyTexture = new CopyTexture(this);
  getCopyLayers() {
    return this.copyTexture;
  }
  getCopyTexture() {
    return this.copyTexture;
  }
  prepareForComputation(outputRect: OutputRect, blendMode?: BlendMode) {
    this.viewport = outputRect;
    if (blendMode) {
      this.enable(this.gl.BLEND);
      this.blendEquation(blendMode.equation);
      this.blendFunc(blendMode.sfactor, blendMode.dfactor);
    } else {
      this.disable(this.gl.BLEND);
    }
    this.disable(this.gl.CULL_FACE);
    this.disable(this.gl.DEPTH_TEST);
  }
}

function createDummyGL() {
  const canvas = document.createElement('canvas');
  return canvas.getContext('webgl2') || {} as any as WebGL2RenderingContext;
}
export const GL = createDummyGL();

export class BlendMode {
  constructor(public sfactor: number, public dfactor: number, public equation: number) {}
  static Sum = new BlendMode(GL.ONE, GL.ONE, GL.FUNC_ADD);
  static Alpha = new BlendMode(GL.SRC_ALPHA, GL.ONE_MINUS_SRC_ALPHA, GL.FUNC_ADD);
}
export const MeshLoaders: {
  [id: string]: (gls: GlState) => Mesh
} = {
};


export enum TextureFiltering {
  Nearest,
  Linear,
  MipMap,
  LinearMipMap,
}

export class CommonSamplers {
  constructor(private gl: WebGL2RenderingContext) {
    if (!this.nearestSampler || !this.linearSampler || !this.mipmapSampler || !this.linearMipmapSampler) {
      throw new Error('Failed to create sampler');
    }
    gl.samplerParameteri(this.nearestSampler, gl.TEXTURE_MIN_FILTER, gl.NEAREST);
    gl.samplerParameteri(this.nearestSampler, gl.TEXTURE_MAG_FILTER, gl.NEAREST);
    gl.samplerParameteri(this.nearestSampler, gl.TEXTURE_WRAP_S, gl.REPEAT);
    gl.samplerParameteri(this.nearestSampler, gl.TEXTURE_WRAP_T, gl.REPEAT);

    gl.samplerParameteri(this.linearSampler, gl.TEXTURE_MIN_FILTER, gl.LINEAR);
    gl.samplerParameteri(this.linearSampler, gl.TEXTURE_MAG_FILTER, gl.LINEAR);
    gl.samplerParameteri(this.linearSampler, gl.TEXTURE_WRAP_S, gl.REPEAT);
    gl.samplerParameteri(this.linearSampler, gl.TEXTURE_WRAP_T, gl.REPEAT);

    gl.samplerParameteri(this.mipmapSampler, gl.TEXTURE_MIN_FILTER, gl.NEAREST_MIPMAP_LINEAR);
    gl.samplerParameteri(this.mipmapSampler, gl.TEXTURE_MAG_FILTER, gl.LINEAR);
    gl.samplerParameteri(this.mipmapSampler, gl.TEXTURE_WRAP_S, gl.REPEAT);
    gl.samplerParameteri(this.mipmapSampler, gl.TEXTURE_WRAP_T, gl.REPEAT);

    gl.samplerParameteri(this.linearMipmapSampler, gl.TEXTURE_MIN_FILTER, gl.LINEAR_MIPMAP_LINEAR);
    gl.samplerParameteri(this.linearMipmapSampler, gl.TEXTURE_MAG_FILTER, gl.LINEAR);
    gl.samplerParameteri(this.linearMipmapSampler, gl.TEXTURE_WRAP_S, gl.REPEAT);
    gl.samplerParameteri(this.linearMipmapSampler, gl.TEXTURE_WRAP_T, gl.REPEAT);
  }
  destroy() {
    this.gl.deleteSampler(this.nearestSampler);
    this.gl.deleteSampler(this.linearSampler);
    this.gl.deleteSampler(this.mipmapSampler);
    this.gl.deleteSampler(this.linearMipmapSampler);
  }
  nearestSampler = this.gl.createSampler();
  linearSampler = this.gl.createSampler();
  mipmapSampler = this.gl.createSampler();
  linearMipmapSampler = this.gl.createSampler();

  getSampler(sampler: TextureFiltering) {
    return sampler === TextureFiltering.Nearest ? this.nearestSampler :
      sampler === TextureFiltering.MipMap ? this.mipmapSampler :
      sampler === TextureFiltering.LinearMipMap ? this.linearMipmapSampler :
      this.linearSampler;
  }

  setupTextureFiltering(unit: number, target: number, filtering: TextureFiltering) {
    const gl = this.gl;

    // For some reason on windows the sampler object isn't respected, so need to make sure the texture is set up right here
    gl.texParameteri(target, gl.TEXTURE_MIN_FILTER,
      filtering === TextureFiltering.Linear ? gl.LINEAR :
      filtering === TextureFiltering.MipMap ? gl.NEAREST_MIPMAP_LINEAR :
      filtering === TextureFiltering.LinearMipMap ? gl.LINEAR_MIPMAP_LINEAR :
      gl.NEAREST);
    gl.texParameteri(target, gl.TEXTURE_MAG_FILTER,
      filtering === TextureFiltering.Linear ? gl.LINEAR :
      filtering === TextureFiltering.MipMap ? gl.LINEAR :
      gl.NEAREST);
    gl.texParameteri(target, gl.TEXTURE_WRAP_S, gl.REPEAT);
    gl.texParameteri(target, gl.TEXTURE_WRAP_T, gl.REPEAT);
    gl.bindSampler(unit, this.getSampler(filtering));
  }

}
