import * as React from 'react';
import './index.css';
import { EditorProps, NonNullEditorDispatch } from '../Editors/EditorProps';

interface CheckboxProps extends EditorProps<boolean>, NonNullEditorDispatch<boolean> {
  style?: React.CSSProperties;
  children?: any;
}

export function Checkbox({ className, style, onChange, readOnly, value, children }: CheckboxProps) {
  return <label
    className={`Checkbox ${className || ''} ${value ? 'CheckboxChecked' : ''}`}
    style={style}
    onClick={e => { e.stopPropagation(); }}
    >
    {!readOnly && <input className={className} type="checkbox" checked={value} onChange={() => onChange(!value)} />}
    <i className={`fas ${value ? 'fa-check-square' : 'fa-square'}`} />
    {children && <span>{children}</span>}
  </label>;
}
