import * as React from 'react';
import { EditorProps, NonNullEditorDispatch } from './EditorProps';
import { PixlingTimespanDef, PixlingTimespanDefUnits } from '../../Time';
import { NumberEditor } from './NumberEditor';
import { EnumEditor, enumEnumVariants } from './PrimitiveEditors';

export function PixlingTimespanDefEditor({ value, onChange }: EditorProps<PixlingTimespanDef> & NonNullEditorDispatch<PixlingTimespanDef>) {
  value = value || { value: 1, unit: PixlingTimespanDefUnits.Days };
  return <span>
    <NumberEditor value={value.value} onChange={v => onChange({ value: v, unit: value!.unit })} />
    <EnumEditor 
      value={value.unit} 
      onChange={v => onChange({ value: value!.value, unit: v })} 
      variants={enumEnumVariants(PixlingTimespanDefUnits)}
      />
  </span>
}