import React from "react";
import './index.css';
import { PortraitRenderer } from "../../GpuWorld/Renderer/PortraitRenderer";
import { DefaultPortraitRenderer } from "../../GpuWorld/Renderer/DefaultPortraitRenderer";
import LoadingPortraitPNG from '../../Images/LoadingPortrait.png';
import { UnitDef } from "../../GpuWorld/UnitDef";

interface CreaturePortraitProps {
  unit: UnitDef;
  fallbackPortrait?: string;
  renderer?: PortraitRenderer;
}
export class CreaturePortrait extends React.Component<CreaturePortraitProps> {
  state: { img?: string } = {};
  imgRef = React.createRef<HTMLImageElement>();
  componentDidMount() {
    this.renderPortrait();
  }
  componentDidUpdate(prevProps: CreaturePortraitProps) {
    if (this.props.unit !== prevProps.unit) {
      this.renderPortrait();
    }
  }
  renderPortrait() {
    const { renderer = DefaultPortraitRenderer, unit } = this.props;
    if (renderer) {
      renderer.renderToDataURL(unit).then(img => this.setState({ img }));
    }
  }
  render() {
    return <img className="PixlingPortrait" alt="Portrait"
      src={this.state.img || this.props.fallbackPortrait || LoadingPortraitPNG} ref={this.imgRef} />;
  }
}

const PortraitRendererPool: { free: PortraitRenderer[], taken: PortraitRenderer[] } = { free: [], taken: [] };
export function LiveCreaturePortrait({ unit, playing = true }: { unit: UnitDef, playing?: boolean }) {
  const div = React.useRef<HTMLDivElement>(null);
  const [renderer, setRenderer] = React.useState<PortraitRenderer>();
  React.useEffect(() => {
    const currentDiv = div.current;
    if (!currentDiv) {
      return;
    }
    if (PortraitRendererPool.free.length === 0 && PortraitRendererPool.taken.length >= 10) {
      return;
    }
    let pooledRenderer = PortraitRendererPool.free.pop();
    if (!pooledRenderer) {
      const canvas = document.createElement('canvas');
      pooledRenderer = PortraitRenderer.create(canvas, { preserveDrawingBuffer: true });
      if (pooledRenderer) {
        pooledRenderer.state.setData({ stepCounter: Math.floor(Math.random() * 10000) });
        PortraitRendererPool.taken.push(pooledRenderer);
      }
    }
    if (pooledRenderer) {
      currentDiv.appendChild(pooledRenderer.canvas);
      setRenderer(pooledRenderer);
      return () => {
        currentDiv.removeChild(pooledRenderer!.canvas);
        PortraitRendererPool.taken.splice(PortraitRendererPool.taken.indexOf(pooledRenderer!), 1);
        PortraitRendererPool.free.push(pooledRenderer!);
      }
    } else {
      return;
    }
  }, [div]);
  React.useEffect(() => {
    renderer?.setUnit(unit);
  }, [renderer, unit]);
  React.useEffect(() => {
    if (renderer && playing) {
      renderer.startRenderLoop();
      return () => renderer.stopRenderLoop();
    } else {
      return () => {};
    }
  }, [renderer, playing]);
  return <div className="LivePixlingPortrait" ref={div} />;
}
