import * as React from 'react';
import './index.css';
import { Button } from '../Button';
import { capitalizeFirstLetter } from '../../Util';

export interface TabTitleProps {
  title: any;
  isActive: boolean;
  onClick: () => void;
}
export interface TabBodyProps {
  children: any;
  isActive: boolean;
}

interface CommonTabsProps {
  titleComponent?: ((props: TabTitleProps) => any);
  bodyComponent?: ((props: TabBodyProps) => any);
  tab?: number;
  onTabChange?: (tab: number) => void | boolean;
  showTabBar?: boolean;
}

interface TabsProps extends CommonTabsProps {
  orientation?: 'left' | 'right' | 'top' | 'bottom';
  className?: string;
  style?: React.CSSProperties;
}
interface TabsContainerState {
  selectedTab: number;
}

export class TabsContainer extends React.Component<TabsProps, TabsContainerState> {
  constructor(props: TabsProps) {
    super(props);
    this.state = {
      selectedTab: props.tab !== undefined ? props.tab : 0
    };
  }
  handleTabChange = (tab: number) => {
    if (this.props.onTabChange) {
      if (this.props.onTabChange(tab) === false) {
        return;
      }
    }
    this.setState({ selectedTab: tab });
  }
  render() {
    const { tab, onTabChange, ...rest } = this.props;
    return <Tabs tab={this.state.selectedTab} onTabChange={this.handleTabChange} {...rest}>{this.props.children}</Tabs>;
  }
}

export class Tabs extends React.Component<TabsProps> {
  public static defaultProps: TabsProps = {
    titleComponent: TabTitle,
    bodyComponent: TabBody,
    orientation: 'top'
  };
  render() {
    const TitleComponent = this.props.titleComponent!;
    const BodyComponent = this.props.bodyComponent!;
    const selectedTab = this.props.tab !== undefined ? this.props.tab : 0;
    return (
      <div className={`Tabs Tabs${capitalizeFirstLetter(this.props.orientation || 'top')} ${this.props.className || ''}`} style={this.props.style}>
        {this.props.showTabBar !== false && <div className="TabsBar">
          {React.Children.map(this.props.children, (tab, i) => (
            tab ?
              <TitleComponent
                title={(tab as any as Tab).props.title}
                isActive={i === this.props.tab}
                onClick={() => { if (this.props.onTabChange) { this.props.onTabChange(i); } }}
                />
              : null
          ))}
        </div>}
        <BodyComponent
          key={selectedTab}
          isActive={true}
          children={(this.props.children as any[])[selectedTab]}
        />
      </div>
    );
  }
}
export function TabTitle({ isActive, onClick, title }: TabTitleProps) {
  return (
    <Button className={`TabTitle`} flat={true} toggled={isActive} onClick={onClick} purpose="SwitchTab">
      {title}
    </Button>
  );
}
export function TabBody({ isActive, children }: TabBodyProps) {
  return (
    <div className="TabBody">
      {children}
    </div>
  );
}
interface AccordionState {
  expandedIndex: number;
}
export class Accordion extends React.Component<CommonTabsProps, AccordionState> {
  public static defaultProps: CommonTabsProps = {
    titleComponent: AccordionTabTitle,
    bodyComponent: TabBody
  };
  constructor(props: CommonTabsProps) {
    super(props);
    this.state = {
      expandedIndex: props.tab !== undefined ? props.tab : -1
    };
  }
  handleClickTitle(titleIndex: number) {
    if (this.state.expandedIndex === titleIndex) {
      this.setState({ expandedIndex: -1 });
    } else {
      this.setState({ expandedIndex: titleIndex });
    }
  }
  render() {
    const children = React.Children.toArray(this.props.children) as any as Tab[];
    let counter = 0;
    const grouped = children.reduce((p, tab) => {
      const group = tab.props.group || '';
      p[group] = (p[group] || []);
      p[group].push({ tab, i: counter++ });
      return p;
    }, {} as { [group: string]: Array<{ tab: Tab, i: number }> });
    const TitleComponent = this.props.titleComponent!;
    const BodyComponent = this.props.bodyComponent!;
    return (
      <div className="Accordion">
        {Object.keys(grouped).map(groupName => (
          <div key={groupName} className="AccordionGroup">
            {groupName && <div className="AccordionGroupName">{groupName}</div>}
            {grouped[groupName].map(({ tab, i }) => (
              <div className="AccordionTab" key={i}>
                <TitleComponent
                  title={(tab as any as Tab).props.title}
                  isActive={i === this.state.expandedIndex}
                  onClick={() => this.handleClickTitle(i)}
                  />
                {i === this.state.expandedIndex && (
                  <BodyComponent isActive={true} children={(tab as any as Tab).props.children} />
                )}
              </div>
            ))}
          </div>
        ))}
      </div>
    );
  }
}
export function AccordionTabTitle({ isActive, onClick, title }: TabTitleProps) {
  return (
    <div className="AccordionTabTitle" onClick={onClick}>
      {title} {
        isActive ?
          <i className="Chevron fa fa-chevron-up" aria-hidden="true" /> :
          <i className="Chevron fa fa-chevron-down" aria-hidden="true" />
      }
    </div>
  );
}
export interface TabProps {
  title: any;
  group?: string;
  className?: string;
}
export class Tab extends React.Component<TabProps> {
  render() {
    return (
      <div className={`Tab ${this.props.className !== undefined ? this.props.className : ''}`}>
        {this.props.children}
      </div>
    );
  }
}