import * as React from 'react';
import './index.css';
import * as _ from 'lodash';

// Due to a bug in Chrome/FF/(more?) modifier keys are not available in the dragend event: https://stackoverflow.com/questions/24016457/html5-image-drag-event-ctrlkey-remains-false-in-firefox-29
const globalIsCopy = { copy: false };

type DragAndDropItemRenderer<T> = (item: T, onItemChange: (item: T) => void, index: number) => React.ReactNode;

interface DragAndDropListProps<T> {
  value: T[];
  onChange: (value: T[]) => void;
  itemRenderer: DragAndDropItemRenderer<T>;
  keyFromItem?: (item: T) => string;
  onItemRemoved?: (index: number) => void;
  onItemInserted?: (index: number) => void;
  acceptItem?: (item: any) => boolean;
  filter?: (item: T) => boolean;
}

const DragAndDropItemContext = React.createContext<{
  item?: any,
  onDragRemove?: () => void,
  onDragChange?: (dragging: boolean) => void,
}>({});

interface DragAndDropListState {
  over: false;
  queuedUpdates: Array<{ key: any, item: any }>;
}

export class DragAndDropList<T> extends React.Component<DragAndDropListProps<T>, DragAndDropListState> {
  state: DragAndDropListState = {
    over: false,
    queuedUpdates: []
  }
  queuedInsert?: { key?: any, mode: 'top' | 'bottom', item: any };
  queuedRemove?: { key: any };
  performChange = _.debounce(() => {
    const { keyFromItem } = this.props;
    const nextValue = [...this.props.value];
    for (const queuedUpdate of this.state.queuedUpdates) {
      const index = keyFromItem ? this.props.value.findIndex(x => keyFromItem(x) === queuedUpdate.key) : queuedUpdate.key;
      nextValue.splice(index, 1, queuedUpdate.item);
    }
    const queuedRemove = this.queuedRemove;
    const queuedInsert = this.queuedInsert;
    if (queuedRemove && queuedInsert && queuedRemove.key === queuedInsert.key) {
      // Do nothing, we're moving to ourselves
    } else {
      if (queuedRemove) {
        const index = keyFromItem ? nextValue.findIndex(x => keyFromItem(x) === queuedRemove.key) : queuedRemove.key;
        nextValue.splice(index, 1);
        if (this.props.onItemRemoved) {
          this.props.onItemRemoved(index);
        }
      }
      if (queuedInsert) {
        let index = 0;
        if (queuedInsert.key) {
          index = keyFromItem ? nextValue.findIndex(x => keyFromItem(x) === queuedInsert.key) : queuedInsert.key;
          if (queuedInsert.mode === 'bottom') {
            index++;
          }
          if (!keyFromItem && this.queuedRemove && this.queuedRemove.key < index) {
            index--;
          }
        } else {
          if (queuedInsert.mode === 'bottom') {
            index = nextValue.length;
          }
        }
        nextValue.splice(index, 0, queuedInsert.item);
        if (this.props.onItemInserted) {
          this.props.onItemInserted(index);
        }
      }
    }
    this.queuedInsert = undefined;
    this.queuedRemove = undefined;
    this.props.onChange(nextValue);
    this.setState({ queuedUpdates: [] });
  }, 50);
  render() {
    const { value, itemRenderer, keyFromItem, filter } = this.props;
    return <DragAndDropArea
      className="DragAndDropList"
      onDrop={(drop, item) => {
        this.queuedInsert = { mode: drop, item };
        this.performChange();
      }}
      acceptItem={this.props.acceptItem}
      >
      {value.map((item, i) => {
        if (filter && !filter(item)) {
          return null;
        }
        const key = keyFromItem ? keyFromItem(item) : i;
        let pendingValue = item;
        for (const update of this.state.queuedUpdates) {
          if (update.key === key) {
            pendingValue = update.item;
          }
        }
        return <DragAndDropItem
          key={i}
          item={pendingValue}
          onDrop={(drop, dropItem) => {
            this.queuedInsert = { key, mode: drop, item: dropItem };
            this.performChange();
          }}
          onDragRemove={() => {
            this.queuedRemove = { key };
            this.performChange();
          }}
          acceptItem={this.props.acceptItem}
          >
          {itemRenderer(
            pendingValue, 
            (newItem) => {
              this.setState(s => ({ queuedUpdates: [...s.queuedUpdates, { key, item: newItem }] }));
              this.performChange();
            },
            i
          )}
        </DragAndDropItem>
      })}
    </DragAndDropArea>;
  }
}

function isCopy(event: React.DragEvent<HTMLDivElement>) {
  return (event.altKey || event.ctrlKey || event.metaKey);
}

interface DragAndDropAreaProps {
  className?: string;
  onDrop: (drop: 'top' | 'bottom', value: any) => void;
  acceptItem?: (item: any) => boolean;
}
interface DragAndDropAreaState {
  over: 'none' | 'top' | 'bottom';
}

class DragAndDropArea extends React.Component<DragAndDropAreaProps, DragAndDropAreaState> {
  state: DragAndDropAreaState = {
    over: 'none'
  }
  handleDragEnter = (event: React.DragEvent<HTMLDivElement>) => {
    event.stopPropagation();
    event.dataTransfer.dropEffect = isCopy(event) ? 'copy' : 'move';
    this.setState({ over: 'top' });
  }
  handleDragOver = (event: React.DragEvent<HTMLDivElement>) => {
    event.preventDefault();
    event.stopPropagation();
    event.dataTransfer.dropEffect = isCopy(event) ? 'copy' : 'move';
    if (!this.div) {
      return;
    }
    const bound = this.div.getBoundingClientRect();
    if (event.clientY <= bound.top + bound.height / 2) {
      this.setState({ over: 'top' });
    } else {
      this.setState({ over: 'bottom' });
    }
  }
  handleDrop = (event: React.DragEvent<HTMLDivElement>) => {
    event.preventDefault();
    event.stopPropagation();
    globalIsCopy.copy = isCopy(event);
    if (this.state.over === 'none') {
      return;
    }
    let data;
    try {
      data = JSON.parse(event.dataTransfer.getData('text/plain'));
    } catch {
      alert('Invalid JSON data!');
      return;
    }
    if (!this.props.acceptItem || this.props.acceptItem(data)) {
      this.props.onDrop(this.state.over, data);
    }
    this.setState({ over: 'none' });
  }
  div: HTMLDivElement | null = null;
  render() {
    const className = [
      'DragAndDropArea',
      this.state.over === 'top' ? 'DragAndDropAreaOverTop' : '',
      this.state.over === 'bottom' ? 'DragAndDropAreaOverBottom' : '',
      this.props.className || ''
    ].join(' ');
    return <div
      ref={e => this.div = e}
      className={className}
      onDragEnter={this.handleDragEnter}
      onDragLeave={() => this.setState({ over: 'none' })}
      onDragOver={this.handleDragOver}
      onDrop={this.handleDrop}
      >
      {this.props.children}
    </div>
  }
}

interface DragAndDropItemProps {
  item: any;
  onDrop: (drop: 'top' | 'bottom', value: any) => void;
  onDragRemove: () => void;
  acceptItem?: (item: any) => boolean;
}
interface DragAndDropItemState {
  dragging: boolean;
}

class DragAndDropItem extends React.Component<DragAndDropItemProps, DragAndDropItemState> {
  state: DragAndDropItemState = {
    dragging: false
  }
  render() {
    const className = [
      'DragAndDropItem',
      this.state.dragging ? 'DragAndDropItemDragging' : ''
    ].join(' ');
    return <DragAndDropArea
      className={className}
      onDrop={this.props.onDrop}
      acceptItem={this.props.acceptItem}
      >
      <DragAndDropItemContext.Provider value={{ 
        item: this.props.item, 
        onDragRemove: this.props.onDragRemove, 
        onDragChange: dragging => this.setState({ dragging })
        }}>
        {this.props.children}
      </DragAndDropItemContext.Provider>
    </DragAndDropArea>;
  }
}

export class DragAndDropHandle extends React.Component {
  render() {
    return <DragAndDropItemContext.Consumer>
      {({ item, onDragRemove, onDragChange }) => 
        <span
          className={`DragAndDropHandle`}
          onDragStart={event => {
            event.stopPropagation();
            event.dataTransfer.effectAllowed = 'copyMove';
            event.dataTransfer.setData('text/plain', JSON.stringify(item, null, 2));
            if (onDragChange) {
              onDragChange(true);
            }
          }}
          onDragEnd={event => {
            if (onDragChange) {
              onDragChange(false);
            }
            if (!globalIsCopy.copy && onDragRemove) {
              onDragRemove();
            }
          }}
          draggable={true}
          >
          {this.props.children}
        </span>
      }
    </DragAndDropItemContext.Consumer>
  }
}