
import PlayerUnitGlb from '../Models/PlayerUnit.glb';
import ScarecrowGlb from '../Models/Scarecrow.glb';
import DuckGlb from '../Models/Duck.glb';
import CrabGlb from '../Models/Crab.glb';
import TurtleGlb from '../Models/Turtle.glb';
import StatueGlb from '../Models/Statue.glb';
import { ThreeModel } from '../GpuUtil/Model';
import { EntityRef } from './StateData';
import { GpuWorldState } from './State';
import { CpuPlayerUnitComponent, TailAttachment, BackAttachment, HeadAttachment, HandLeftAttachment, HandRightAttachment, ProjectileAttachment, ModelCreatedComponent } from './ECS/UnitComponents';
import { GpuComponent, EntitiesRange } from '../GpuUtil/ECS';
import { rgb255ToVec401, Lazy, rgbFromHex } from '../Util';
import { UnitDef } from './UnitDef';
import { createImpactEffects } from './ECS/DamageCalc';

const PlayerUnitModel = new Lazy(() => ThreeModel.fromPath(PlayerUnitGlb));
const ScarecrowModel = new Lazy(() => ThreeModel.fromPath(ScarecrowGlb));
const DuckModel = new Lazy(() => ThreeModel.fromPath(DuckGlb));
const CrabModel = new Lazy(() => ThreeModel.fromPath(CrabGlb));
const TurtleModel = new Lazy(() => ThreeModel.fromPath(TurtleGlb));
const StatueModel = new Lazy(() => ThreeModel.fromPath(StatueGlb));

export async function createUnitModelsForArena(state: GpuWorldState, arenaIndex: number) {
  const unitsInArena = state.unitsInArena.get(state);
  for (const unit of unitsInArena.rows[arenaIndex].entities) {
    if (unit.x >= 0) {
      await createUnitModel(state, unit);
    }
  }
}

async function getUnitModel(unit: UnitDef) {
  switch (unit.type) {
    case 'creature': return await PlayerUnitModel.value;
    case 'statue': return await StatueModel.value;
    case 'scarecrow': return await ScarecrowModel.value;
    case 'crab': return await CrabModel.value;
    case 'duck': return await DuckModel.value;
    case 'turtle': return await TurtleModel.value;
  }
}

export async function createUnitModel(state: GpuWorldState, entityCell: EntityRef) {

  const unit = state.ecs.getComponentData(CpuPlayerUnitComponent, entityCell)!.unit;
  const model = await getUnitModel(unit);
  const modelCreated = state.ecs.getComponentData(ModelCreatedComponent, entityCell);
  if (modelCreated) {
    return model;
  }
  state.ecs.setComponentData(ModelCreatedComponent, entityCell, true);
  await createImpactEffects(state, EntitiesRange.fromEntity(entityCell));
  if (unit.type === 'creature') {
    const attachmentPoints: [GpuComponent<'float'>, number[]][] = [];
    model.createInstances(state, {
      rootEntities: EntitiesRange.fromEntity(entityCell),
      beforeEntityCreated: (o, entityInit) => {
        if (o.name.indexOf('Item') === 0) {
          if (o.name.indexOf(`Item${unit.slots![0]}`) === 0 ||
            o.name.indexOf(`Item${unit.slots![1]}`) === 0 ||
            o.name.indexOf(`Item${unit.slots![2]}`) === 0) {
            return entityInit;
          } else {
            return undefined;
          }
        } else if (o.name.indexOf('StyleEars') === 0) {
          if (o.name.indexOf(`StyleEars00${unit.ears}`) === 0) {
            return entityInit;
          } else {
            return undefined;
          }
        } else if (o.name.indexOf('StyleEyes') === 0) {
          if (o.name.indexOf(`StyleEyes00${unit.eyes}`) === 0) {
            return entityInit;
          } else {
            return undefined;
          }
        } else if (o.name.indexOf('StyleBackSpikes') === 0) {
          if (o.name.indexOf(`StyleBackSpikes00${unit.backSpikes}`) === 0) {
            return entityInit;
          } else {
            return undefined;
          }
        } else {
          return entityInit;
        }
      },
      afterEntityCreated: (o, entities) => {
        const entityCell = entities.getIndex(0);
        if (o.name === 'TailEnd') {
          attachmentPoints.push([TailAttachment.component, [entityCell.x, entityCell.y]]);
        } else if (o.name === 'Back') {
          attachmentPoints.push([BackAttachment.component, [entityCell.x, entityCell.y]]);
        } else if (o.name === 'Head') {
          attachmentPoints.push([HeadAttachment.component, [entityCell.x, entityCell.y]]);
        } else if (o.name === 'HandL') {
          attachmentPoints.push([HandLeftAttachment.component, [entityCell.x, entityCell.y]]);
        } else if (o.name === 'HandR') {
          attachmentPoints.push([HandRightAttachment.component, [entityCell.x, entityCell.y]]);
        } else if (o.name === 'ProjectileAttachment') {
          attachmentPoints.push([ProjectileAttachment.component, [entityCell.x, entityCell.y]]);
        }
      },
      onMaterial: mat => {
        if (mat.name === 'Primary') {
          return rgb255ToVec401(rgbFromHex(unit.primaryColor!));
        } else if (mat.name === 'Secondary') {
          return rgb255ToVec401(rgbFromHex(unit.secondaryColor!));
        } else {
          return undefined;
        }
      },
      visible: false,
    });
    for (const [comp, val] of attachmentPoints) {
      state.ecs.setComponentData(comp, entityCell, val);
    }
    return model;
  } else if (unit.type === 'statue') {
    model.createInstances(state, { rootEntities: EntitiesRange.fromEntity(entityCell), visible: false });
    return model;
  } else {
    const attachmentPoints: [GpuComponent<'float'>, number[]][] = [];
    model.createInstances(state, {
      rootEntities: EntitiesRange.fromEntity(entityCell),
      afterEntityCreated: (o, entities) => {
        const entityCell = entities.getIndex(0);
        if (o.name === 'ProjectileAttachment') {
          attachmentPoints.push([ProjectileAttachment.component, [entityCell.x, entityCell.y]]);
        }
      },
      visible: false,
    });
    for (const [comp, val] of attachmentPoints) {
      state.ecs.setComponentData(comp, entityCell, val);
    }
    return model;
  }
}