import { GlState, GL, TextureFiltering } from "../../GpuUtil/GlState";
import { CompiledShaderModule, shaderModule, funcDef, outDef, uniformDef, inDef } from "../../GpuUtil/ShaderModule";
import { Program, Sampler } from "../../GpuUtil/Program";
import { GpuWorldState } from "../State";
import { Vertex } from "../../GpuUtil/Mesh";
import { loadImage } from "../../GpuUtil";
import { Texture } from "../../GpuUtil/Texture";
import LogoPng from '../../Images/AICrowdLogo.png';
import { MathConstsSM, MatrixSM } from "../../GpuUtil/SM2Utils";

const Logo = loadImage(LogoPng);

export class AICrowdLogoRenderer {
  constructor(private gls: GlState) {
    Logo.then(img => {
      this.logoTexture = Texture.fromImage(gls, img);
      this.logoTexture.generateMipmap();
      this.logoSampler = new Sampler(this.logoTexture, TextureFiltering.LinearMipMap);
    });
  }
  destroy() {
    this.logoTexture?.destroy();
  }

  logoTexture?: Texture;
  logoSampler?: Sampler;

  vertexShader = new CompiledShaderModule(shaderModule([
    Vertex, MatrixSM, MathConstsSM
  ], {
    texcoord: outDef('vec2'),
    worldSize: uniformDef('vec2', d => d.derived.worldSize),
    projectionViewMatrix: uniformDef('mat4', d => d.derived.cameraProjectionView),
    main: funcDef('void', [], `
      texcoord = ${Vertex.texcoord};
      texcoord.y = 1. - texcoord.y;
      gl_Position = projectionViewMatrix * ${MatrixSM.translate}(vec3(worldSize / 2. - 0.7, 3.)) * ${MatrixSM.rotateZ}(${MathConstsSM.PI} / 4.) * vec4(${Vertex.position}.x * 2.3, ${Vertex.position}.y * ${2.3 * 24/126}, 0., 1.0);
    `)
  }));
  fragmentShader = new CompiledShaderModule(shaderModule([
  ], {
    outColor: outDef('vec4'),
    texcoord: inDef('vec2'),
    logo: uniformDef('sampler2D', d => this.logoSampler!),
    main: funcDef('void', [], `
      outColor = texture(logo, texcoord);
      if (outColor.a < 2.0/255.0) {
        discard;
      }
    `)
  }));
  program = new Program(this.gls, this.fragmentShader.source, this.vertexShader.source);

  run(state: GpuWorldState) {
    if (!state.data.aiCrowdLogo) {
      return;
    }
    this.vertexShader.updateUniformValues(state);
    this.fragmentShader.updateUniformValues(state);
    this.program.prepare();
    this.vertexShader.writeUniforms(this.program);
    this.fragmentShader.writeUniforms(this.program);
    this.gls.depthMask(false);
    this.gls.enable(GL.DEPTH_TEST);
    this.gls.disable(GL.BLEND);
    this.gls.blendFuncSeparate(GL.SRC_ALPHA, GL.ONE_MINUS_SRC_ALPHA, GL.ONE, GL.ONE_MINUS_SRC_ALPHA); // https://limnu.com/webgl-blending-youre-probably-wrong/
    this.gls.disable(GL.CULL_FACE);
    this.gls.drawQuad(this.program);
  }

}