import * as React from 'react';
import './index.css';
import { WorldStateContext } from '../../../GameComponents/WorldDataConsumer';
import { EntitiesQuery } from '../../../GpuUtil/ECS/Query';
import { Unit } from '../../../GpuWorld/ECS/UnitComponents';
import { ArenaInstance, ArenaMemberIndex } from '../../../GpuWorld/ECS/Components';
import { shaderModule, uniformDef, funcDef } from '../../../GpuUtil/ShaderModule';
import { Position } from '../../../GpuWorld/ECS/Transform';
import * as Vector2 from '../../../Math/Vector2';
import * as Vector3 from '../../../Math/Vector3';
import * as Vector4 from '../../../Math/Vector4';
import lodash from 'lodash';
import { Popup } from '../../../Components/Popup';
import { InGameUnitBountyEditor } from '../../BountyEditor';
import { Gold } from '../../../GpuWorld/ECS/UnitComponents';
import { SideSize, SoundEffect } from '../../../GpuWorld/StateLayout';
import { AudioPlayerInstance } from '../../../GpuWorld/Audio';
import { Lazy } from '../../../Util';


interface UnitLayoverData {
  position: Vector3.Vector3;
  screenPosition: Vector2.Vector2;
  gold: number;
  arenaMemberIndex: number;
}

export function UnitsLayover() {
  const worldState = React.useContext(WorldStateContext);
  const [units, setUnits] = React.useState<UnitLayoverData[]>([]);
  React.useEffect(() => {

    if (!worldState) {
      return;
    }

    const unitsQuery = new EntitiesQuery([Unit.component])
      .filterValue(ArenaInstance.cpuComponent, (v, state) => v[0] === state.data.renderArena, d => [d.data.renderArena])
      .filterValue(ArenaMemberIndex.cpuComponent, (v, d) => (v[0] > 0 && v[0] < SideSize && d.derived.trainingSetup.trainHome) || (v[0] > SideSize && d.derived.trainingSetup.trainAway), d => [d.derived.trainingSetup])
      .toGpu(worldState.gls)
      .mapToTemp(shaderModule([Position.sm, Gold.sm, ArenaMemberIndex.sm], {
        viewProjection: uniformDef('mat4', d => d.derived.cameraProjectionView),
        map: funcDef('vec4[2]', [['ivec2', 'entityCell']], `
          vec3 position = ${Position.sm.value}(entityCell);
          vec4 screenPos = Self.viewProjection * vec4(position + vec3(0, 0, 1), 1);
          screenPos /= screenPos.w;
          return vec4[](
            vec4(position, 0),
            vec4(screenPos.xy, ${Gold.sm.value}(entityCell), ${ArenaMemberIndex.sm.value}(entityCell))
          );
        `)
      }))
      .toCpuQueuing(state => state.data.round);

    let prevUnits: UnitLayoverData[];

    const update = () => {
      const readResults = unitsQuery.queueRead(worldState);
      const unitData = lodash.last(readResults());

      const newUnits: UnitLayoverData[] = [];
      let unitsChanged = false;
      if (unitData && unitData.length > 0) {
        for (let i=0; i < unitData.length; i++) {
          const unit: UnitLayoverData = {
            position: unitData[i].value[0],
            screenPosition: Vector2.interpolate(
              Vector4.xy(unitData[i].value[1]),
              Vector2.create(-1, 1),
              Vector2.create(1, -1),
              Vector2.create(0),
              Vector2.create(worldState.data.canvasRect.width, worldState.data.canvasRect.height)),
            gold: unitData[i].value[1].z,
            arenaMemberIndex: unitData[i].value[1].w,
          }
          newUnits.push(unit);
          unitsChanged = unitsChanged || !prevUnits || unit.gold !== prevUnits[i]?.gold;
        }
        if (unitsChanged) {
          setUnits(newUnits);
        }
      }
    }
    worldState.events.render.attach(update);
    return () => {
      worldState.events.render.detach(update);
      unitsQuery.destroy();
    }

  }, [worldState]);

  return <div className="UnitsLayover">
    {units?.map((unit, i) => <UnitLayover key={i} unit={unit} />)}
  </div>
}

const throttledPlaySoundEffect = new Lazy(() => lodash.throttle(AudioPlayerInstance!.playSoundEffect.bind(AudioPlayerInstance), 300, { leading: true }));

function UnitLayover({ unit }: { unit: UnitLayoverData }) {
  const worldState = React.useContext(WorldStateContext);
  const pausedState = React.useRef<boolean>(false);
  const [goldInfo, setGoldInfo] = React.useState({ prevGold: 0, round: -1, renderArena: -2, worldState });
  const [goldTicker, setGoldTicker] = React.useState(0);
  const [showBountyConfig, setShowBountyConfig] = React.useState(false);
  const showGold = (unit.arenaMemberIndex < SideSize && worldState?.data.teams.home.train) ||
    (unit.arenaMemberIndex >= SideSize && worldState?.data.teams.away.train);
  React.useEffect(() => {
    setGoldInfo({
      prevGold: unit.gold,
      round: worldState?.data.round!,
      renderArena: worldState?.data.renderArena!,
      worldState
    });
    if (!showGold || worldState?.data.round !== goldInfo.round || worldState?.data.renderArena !== goldInfo.renderArena ||
        worldState !== goldInfo.worldState) {
      setGoldTicker(0);
    } else {
      const inc = unit.gold - goldInfo.prevGold;
      if (inc > 0) {
        throttledPlaySoundEffect.value(SoundEffect.Coins, 0.3, unit.position);
      }
      setGoldTicker(goldTicker + inc);
    }
    const t = setTimeout(() => setGoldTicker(0), 1000);
    return () => {
      clearTimeout(t);
    }
  }, [unit.gold, worldState, showGold, goldInfo.prevGold, goldInfo.renderArena, goldInfo.round, goldInfo.worldState, goldTicker, unit.position]);

  return <div className="UnitLayover" style={{ left: unit.screenPosition.x, top: unit.screenPosition.y }}>
    <div className="UnitLayoverClickarea" onClick={() => {
      pausedState.current = !!worldState?.data.simulationPaused;
      worldState?.setData({ simulationPaused: true });
      setShowBountyConfig(true);
    }} >
      {showGold && <div className={`UnitLayoverGold ${unit.gold < 0 ? 'UnitLayoverGoldNegative' : ''}`}>{Math.round(unit.gold)}</div>}
    </div>
    {goldTicker !== 0 && <div key={goldTicker} className={`UnitLayoverGoldTicker UnitLayoverGold ${goldTicker > 0 ? '' : 'UnitLayoverGoldNegative'}`}>{goldTicker > 0 ? '+' : ''}{Math.round(goldTicker)}</div>}
    {showBountyConfig && <Popup onClose={() => {
      worldState?.setData({ simulationPaused: pausedState.current });
      setShowBountyConfig(false);
    }}>
      <InGameUnitBountyEditor arenaMemberIndex={unit.arenaMemberIndex} />
    </Popup>}
  </div>
}
