import { shaderModule, constDef, funcDef } from "../../GpuUtil/ShaderModule";
import { UnitsInArena } from "../ECS/ArenaStats";
import { Teams, SideSize, ArenaMembersSize } from "../StateLayout";

export const UnitFocusables = shaderModule('BrainFocusables', [UnitsInArena], {
  size: constDef('int', `${ArenaMembersSize - 1}`),
  get: funcDef(`ivec3[${ArenaMembersSize - 1}]`, [['ivec2', 'entityCell'], ['int', 'selfTeam'], ['int', 'arenaIndex']], `
    ivec3 res[${ArenaMembersSize - 1}];
    int r=0;
    for (int i=0; i < ${UnitsInArena.size}; i++) {
      int l = selfTeam == ${Teams.HomeTeam} ? i : ((i + ${SideSize}) % ${ArenaMembersSize});
      ivec2 unit = ${UnitsInArena.unit}(arenaIndex, l);
      if (unit == entityCell) {
        continue;
      }
      if (unit.x < 0) {
        unit = ${UnitsInArena.unit}(arenaIndex, (l / ${SideSize}) * ${SideSize});
      }
      res[r++] = ivec3(unit, l);
    }
    return res;
  `)
})
