import { GlState } from "../../GpuUtil/GlState";
import { ArchetypesQuery } from "../../GpuUtil/ECS/Query";
import { shaderModule, funcDef, uniformDef } from "../../GpuUtil/ShaderModule";
import { Decisions } from "../Brain/Decisions";
import { RandomUtilSM, SpatialSM } from "../../GpuUtil/SM2Utils";
import { Seeds } from "../../Util";
import { Team, CrabAI, Focuser } from "./UnitComponents";
import { Position, RotationZ } from "./Transform";
import { GroundHeightSM } from "./Ground";
import { SideSize, Teams } from "../StateLayout";

export function UpdateCrabAI(gls: GlState) {
  return new ArchetypesQuery([CrabAI.component])
    .updateGpu(gls, [
      Decisions.ChaseFocus.component, Decisions.CastingSlot.component, Decisions.MoveX.component,
      Decisions.Rotate.component, Decisions.ChangeFocus.component
    ], shaderModule([
      Decisions.ChaseFocus.writer, Decisions.CastingSlot.writer, Decisions.MoveX.writer,
      Decisions.Rotate.writer, Decisions.ChangeFocus.writer,
      Decisions.MoveX.sm, Decisions.Rotate.sm,
      Focuser.sm, Team.sm, SpatialSM,
      RandomUtilSM, GroundHeightSM, Position.sm, RotationZ.sm,
    ], {
      homeCount: uniformDef('int', d => d.data.teams.home.unitCount),
      awayCount: uniformDef('int', d => d.data.teams.away.unitCount),
      update: funcDef('void', [['ivec2', 'entityCell']], `
        vec3 position = ${Position.sm.value}(entityCell);
        float rotation = ${RotationZ.sm.value}(entityCell);
        vec2 forward = ${SpatialSM.forward}(rotation);
        int team = ${Team.sm.value}(entityCell);
        float forwardHeight = ${GroundHeightSM.atPosition}(position.xy + forward * 2.);
        float forwardElevation = position.z - forwardHeight;
        bool precipiceInFront = forwardElevation > 1.;

        ivec2 focus = ${Focuser.sm.value}(entityCell);
        bool hasFocus = focus.x >= 0;
        float moveX = ${Decisions.MoveX.sm.value}(entityCell);
        float rotate = ${Decisions.Rotate.sm.value}(entityCell);
        bool changeFocus = true;
        if (hasFocus) {
          int focusTeam = ${Team.sm.value}(focus);
          if (team != focusTeam) {
            vec3 focusPosition = ${Position.sm.value}(focus);
            float focusHeight = ${GroundHeightSM.atPosition}(focusPosition.xy);
            float focusElevation = focusPosition.z - focusHeight;
            if (focusElevation > 1. || precipiceInFront) {
              ${Decisions.ChaseFocus.writer.write}(0.);
              ${Decisions.CastingSlot.writer.write}(0);
              changeFocus = true;
            } else {
              ${Decisions.ChaseFocus.writer.write}(1.);
              ${Decisions.CastingSlot.writer.write}(1);
              changeFocus = false;
            }
          }
          moveX = 0.;
          rotate = 0.;
        } else {
          // Roaming
          float mc = ${RandomUtilSM.entityRandom}(entityCell, ${Seeds.get('crabMoveChange1')}, ${Seeds.get('crabMoveChange2')});
          if (mc > 0.9) {
            moveX = moveX > 0. ? 0. : 0.1;
          }
          float rc = ${RandomUtilSM.entityRandom}(entityCell, ${Seeds.get('crabRotateChange1')}, ${Seeds.get('crabRotateChange2')});
          if (rc > 0.9) {
            float rr = ${RandomUtilSM.entityRandom}(entityCell, ${Seeds.get('crabRotate1')}, ${Seeds.get('crabRotate2')});
            rotate = (rr * 2. - 1.);
          }
          if (precipiceInFront) {
            moveX = 0.;
          }
          ${Decisions.ChaseFocus.writer.write}(0.);
          ${Decisions.CastingSlot.writer.write}(0);
        }
        ${Decisions.MoveX.writer.write}(moveX);
        ${Decisions.Rotate.writer.write}(rotate);
        if (changeFocus) {
          int enemyCount = team == ${Teams.HomeTeam} ? awayCount : homeCount;
          int newFocus = ${SideSize} - 1;
          if (enemyCount > 1) { // If there are any units
            float r = ${RandomUtilSM.entityRandom}(entityCell, ${Seeds.get('crabAiFocus1')}, ${Seeds.get('crabAiFocus2')});
            newFocus += 1 + int(r * float(enemyCount - 1));
          }
          ${Decisions.ChangeFocus.writer.write}(newFocus + 1);
        } else {
          ${Decisions.ChangeFocus.writer.write}(0);
        }
      `)
    }))
}
