import { GlState } from "../../GpuUtil/GlState";
import { ArchetypesQuery } from "../../GpuUtil/ECS/Query";
import { Hitpoints, HitpointsTrailing, HitpointsRegenComponent, MaxHitpoints, Mana, ManaRegenComponent, MaxMana } from "./UnitComponents";
import { shaderModule, funcDef } from "../../GpuUtil/ShaderModule";
import { glslFloat } from "../../GpuUtil";
import { StepsInBattle } from "../StateLayout";
import { GpuWorldState } from "../State";

export function UpdateHitpointsTrailing(gls: GlState) {
  return new ArchetypesQuery([Hitpoints.component, HitpointsTrailing.component])
    .mapGpu(gls, HitpointsTrailing.component, shaderModule([Hitpoints.sm], {
      map: funcDef('float', [['ivec2', 'entityCell'], ['float', 'value']], `
        float hp = ${Hitpoints.sm.value}(entityCell);
        float d = hp - value;
        d = sign(d) * min(1., abs(d));
        return value + d;
      `)
    }));
}

export class UpdateHitpointsRegen {
  constructor(private gls: GlState) {}
  private update = new ArchetypesQuery([Hitpoints.component, HitpointsRegenComponent])
    .mapGpu(this.gls, Hitpoints.component, shaderModule([], {
      map: funcDef('float', [['ivec2', 'entityCell'], ['float', 'value']], `
        if (value > 0.) {
          float hitpointsRegen = 0.25 * ${glslFloat(MaxHitpoints)} / ${glslFloat(StepsInBattle)};
          return clamp(value + hitpointsRegen, 0., ${glslFloat(MaxHitpoints)});
        } else {
          return value;
        }
      `)
    }));
  run(state: GpuWorldState) {
    this.update.run(state);
  }
}

export class UpdateManaRegen {
  constructor(private gls: GlState) {}
  private update = new ArchetypesQuery([Mana.component, ManaRegenComponent])
    .mapGpu(this.gls, Mana.component, shaderModule([], {
      map: funcDef('float', [['ivec2', 'entityCell'], ['float', 'value']], `
        if (value > 0.) {
          float regen = 0.5 * ${glslFloat(MaxMana)} / ${glslFloat(StepsInBattle)};
          return clamp(value + regen, 0., ${glslFloat(MaxMana)});
        } else {
          return value;
        }
      `)
    }));
  run(state: GpuWorldState) {
    this.update.run(state);
  }
}

