import { Program } from "./Program";
import { computationShaderHeader } from ".";
import { DoubleBufferedTextureSlicing } from "./Texture";
import { TextureReadPollOptions } from "./TextureReader";
import { GlState } from "./GlState";
import { RenderTarget } from "./RenderTarget";

export class GpuReduce {
  public constructor(private gls: GlState,
    private reduceGlsl: (a: string, b: string) =>  string,
    private reduceJs: (a: number, b: number) => number) {
  }
  destroy() {
    this.renderTarget.destroy();
    this.computation.destroy();
  }

  static reduceSum(gls: GlState) {
    return new GpuReduce(gls, (a, b) => `${a} + ${b}`, (p, x) => p + x);
  }
  static reduceMax(gls: GlState) {
    return new GpuReduce(gls, (a, b) => `max(${a}, ${b})`, (p, x) => Math.max(p, x));
  }
  static reduceMin(gls: GlState) {
    return new GpuReduce(gls, (a, b) => `min(${a}, ${b})`, (p, x) => Math.min(p, x));
  }

  private renderTarget = new RenderTarget(this.gls);
  private computation = new Program(this.gls, computationShaderHeader + `
    in vec2 texcoord;
    out vec4 out_values[${this.gls.maxColorAttachements}];
    uniform sampler2DArray source;

    uniform int inputLayers[${this.gls.maxColorAttachements}];

    void main() {
      ${new Array(this.gls.maxColorAttachements).fill(0).map((x, i) => `{
        int inputLayer = inputLayers[${i}];
        vec4 a = texelFetch(source, ivec3(cellPos * 2 + ivec2(0, 0), inputLayer), 0);
        vec4 b = texelFetch(source, ivec3(cellPos * 2 + ivec2(1, 0), inputLayer), 0);
        vec4 c = texelFetch(source, ivec3(cellPos * 2 + ivec2(0, 1), inputLayer), 0);
        vec4 d = texelFetch(source, ivec3(cellPos * 2 + ivec2(1, 1), inputLayer), 0);
        out_values[${i}] = ${this.reduceGlsl(this.reduceGlsl('a', 'b'), this.reduceGlsl('c', 'd'))};
      }`).join('\n')}
    }
  `);

  reduceGl(slice: DoubleBufferedTextureSlicing, glReduceUntil = 8) {
    let s = Math.max(slice.rect.width, slice.rect.height);

    this.gls.disable(this.gls.gl.BLEND);
    this.gls.disable(this.gls.gl.CULL_FACE);
    this.gls.disable(this.gls.gl.DEPTH_TEST);
    // const layers = new Array(layersCount).fill(0).map((x, i) => startLayer + i);
    while (s > glReduceUntil) {
      s = s / 2;
      if (slice.rect.width < s || slice.rect.height < s) {
        throw new Error(`Texture too small. Expected at least (${s}, ${s}), but is (${slice.rect.width}, ${slice.rect.height})`);
      }
      this.renderTarget.set(slice.back);
      this.gls.viewport = { x: 0, y: 0, width: s, height: s };
      this.computation.setUniforms({
        inputLayers: {
          type: 'int[]',
          value: slice.layers
        },
        source: {
          type: 'sampler2DArray',
          value: slice.front.texture
        }
      });
      this.gls.drawQuad(this.computation);
      slice.texture.swap();
    }
  }

  reduceSync(slice: DoubleBufferedTextureSlicing, glReduceUntil = 8) {
    this.reduceGl(slice, glReduceUntil);
    return slice.front
      .selectAbsolute({ x: 0, y: 0, width: glReduceUntil, height: glReduceUntil })
      .readSync()
      .reduce(this.reduceJs);
  }

  async reduceAsync(slice: DoubleBufferedTextureSlicing, glReduceUntil = 8, asyncOpts?: TextureReadPollOptions) {
    this.reduceGl(slice, glReduceUntil);
    return (await slice.front
      .selectAbsolute({ x: 0, y: 0, width: glReduceUntil, height: glReduceUntil })
      .readAsync(asyncOpts))
      .reduce(this.reduceJs);
  }
}
