export function AdminUserNameComponent({ userId }: { userId: string }) {
  const user = useFirebaseStreamDocument<DbUser>(Db.users, userId);
  const userProfile = useFirebaseStreamDocument<DbUserProfile>(Db.userProfiles, userId);
  if (user.status === 'loading' || userProfile.status === 'loading') {
    return <span>Loading...</span>;
  }
  if (user.status === 'no-such-document' || userProfile.status === 'no-such-document') {
    return <span>User doesn't exist</span>;
  }
  const ageDays = moment().diff(user.value.created.toDate(), 'days');
  return <span title={`User signed up ${moment(user.value.created.toDate()).fromNow()}`}>
    <Link to={`/admin/user/${userId}`}>{userProfile.value.displayName}</Link>{ageDays <= 1 ? '👶' : ageDays < 3 ? '👦' : ageDays < 14 ? '👨' : ''}{user.value.stripeSubscription && '🏵'}{user.value.vip && '😎'}{user.value.alphaTester && '🐞'}{user.value.steamIsOwner && '🎮'}
    <ActionMenu actions={[
      { title: 'User page', onClick: () => RoutingHistory.push(`/user/${userId}`) },
      { title: 'Copy user id', onClick: () => navigator.clipboard.writeText(userId) },
    ]} />
  </span>;
}

