function buildSessionsByUser(simulations: DbSession[]) {
  const simsByUser: { [key: string]: { sims: DbSession[] } } = {};
  for (const simulation of simulations) {
    if (!simsByUser[simulation.userId]) {
      simsByUser[simulation.userId] = { sims: [] }
    }
    simsByUser[simulation.userId].sims.push(simulation);
  }
  return simsByUser;
}
function buildFeedbackByUser(feedbacks: DbFeedback[]) {
  const feedbackByUser: { [key: string]: DbFeedback[] } = {};
  for (const feedback of feedbacks) {
    if (!feedbackByUser[feedback.userId]) {
      feedbackByUser[feedback.userId] = [];
    }
    feedbackByUser[feedback.userId].push(feedback);
  }
  return feedbackByUser;
}

const DerkStartDate = new Date('2019-11-12');

export function AdminUsers() {
  const sessions = useFirebaseStreamQuery<DbSession>({
    collection: Db.sessions,
    where: []
  });
  const users = useFirebaseStreamQuery<DbUser>({
    collection: Db.users,
    where: []
  });
  const [sort, setSort] = React.useState<boolean>(false);
  const [lastSeason, setLastSeason] = React.useState<boolean>(false);
  if (sessions.status !== 'loaded' || users.status !== 'loaded') {
    return <FirebaseLoading pending={[sessions, users]} />
  }
  const simsByUser = buildSessionsByUser(sessions.values
    .filter(x => x.started.toDate() > DerkStartDate &&
    (!lastSeason || x.season === CurrentSeason)));
  const userById = {};
  for (const user of users.values) {
    userById[user.id] = user;
  }
  const data = Object.keys(simsByUser)
    .filter(uid => userById[uid])
    .sort((uida, uidb) => userById[uida].created < userById[uidb].created ? 1 : -1)
    .map(uid => ({
      uid,
      interacting: simsByUser[uid].sims.reduce((p, x) => p + x.minutesInteracting, 0) / 60,
      running: simsByUser[uid].sims.reduce((p, x) => p + (x.lastActive.toMillis() - x.started.toMillis()) / (1000*60), 0) / 60,
    }));
  const maxInteracting = data.reduce((p, x) => Math.max(p, x.interacting), 0);
  const maxRunning = data.reduce((p, x) => Math.max(p, x.running), 0);
  if (sort) {
    data.sort((a, b) => b.interacting - a.interacting);
  }
  return <div className="Page">
    <Checkbox value={sort} onChange={setSort}>Sort</Checkbox>
    <Checkbox value={lastSeason} onChange={setLastSeason}>Last season</Checkbox>
    <table>
    <tbody>
      <tr>
        <th>Name</th>
        <th>h interacting</th>
        <th>h running</th>
      </tr>
    {data
      .map(({ uid, interacting, running }) => <tr>
        <td><AdminUserNameComponent userId={uid} /></td>
        <td><ProgressBar progress={interacting / maxInteracting}><FormatNumberPrecision value={interacting} /></ProgressBar></td>
        <td><ProgressBar progress={running / maxRunning}><FormatNumberPrecision value={running} /></ProgressBar></td>
      </tr>)}
    </tbody>
  </table></div>
}


