enum PeriodType {
  _7Day,
  _28Day,
  Daily,
  Weekly,
  Monthly
}
const PeriodTypeVariants = keysOfEnum(PeriodType).map((k) => ({ value: PeriodType[k], displayName: k }));

function groupPeriod(date: Date, type: PeriodType) {
  if (type === PeriodType._7Day || type === PeriodType._28Day) {
    const daysSinceNow = moment().diff(date, 'days');
    const periodNum = Math.floor(daysSinceNow / (type === PeriodType._7Day ? 7 : 28));
    return {
      period: '' + periodNum,
      startDate: moment().subtract((periodNum + 1) * (type === PeriodType._7Day ? 7 : 28), 'days')
    }
  } else {
    const format = type === PeriodType.Daily ? 'YYYY-MM-DD'
      : type === PeriodType.Weekly ? 'YYYY-WW'
      : 'YYYY-MM';
    const period = moment(date).format(format);
    return {
      period,
      startDate: moment(period, format),
    }
  }
}

export function AdminFunnel() {
  const [periodSel, setPeriodSel] = React.useState<PeriodType>(PeriodType._7Day);
  const [fromFirstSim, setFromFirstSim] = React.useState<boolean>(false);
  const sessions = useFirebaseStreamQuery<DbSession>({
    collection: Db.sessions,
    where: []
  });
  const users = useFirebaseStreamQuery<DbUser>({
    collection: Db.users,
    where: []
  });
  const feedbacks = useFirebaseStreamQuery<DbFeedback>({
    collection: Db.feedback,
    where: []
  });
  if (sessions.status !== 'loaded' || users.status !== 'loaded' || feedbacks.status !== 'loaded') {
    return <FirebaseLoading pending={[sessions, users, feedbacks]} />
  }
  const simsByUser = buildSessionsByUser(sessions.values
    .sort((a, b) => a.started < b.started ? -1 : 1)
    .filter(x => x.started.toDate() > DerkStartDate));
  const feedbackByUser = buildFeedbackByUser(feedbacks.values
    .filter(x => x.time.toDate() > DerkStartDate));
  const periods = {};
  for (const user of users.values) {
    let userStartDate = user.created.toDate();
    if (fromFirstSim && simsByUser[user.id] && simsByUser[user.id].sims.length > 0) {
      userStartDate = simsByUser[user.id].sims[0].started.toDate();
    }
    if (userStartDate > DerkStartDate) {
      const { period, startDate } = groupPeriod(userStartDate, periodSel);
      if (!periods[period]) {
        periods[period] = {
          startDate: startDate.format('YYYY-MM-DD'),
          newUsers: 0,
          newUsersWithSim: 0,
          playtimeWeekOne: 0,
          playtimeDayOne: 0,
          confused: 0,
          loveThis: 0
        };
      }
      periods[period].newUsers++;
      if (simsByUser[user.id]) {
        periods[period].newUsersWithSim++;
        const weekOne = moment(userStartDate).add(7, 'days').toDate();
        periods[period].playtimeWeekOne += simsByUser[user.id].sims.reduce((p, x) => p +
          (x.started.toDate() < weekOne ? x.minutesInteracting : 0), 0) / 60;

        const dayOne = moment(userStartDate).add(1, 'days').toDate();
        periods[period].playtimeDayOne += simsByUser[user.id].sims.reduce((p, x) => p +
          (x.started.toDate() < dayOne ? x.minutesInteracting : 0), 0) / 60;
      }
      if (feedbackByUser[user.id]) {
        periods[period].confused += feedbackByUser[user.id].reduce((p, x) => p ||
          x.category === 'confused', false) ? 1 : 0;
        periods[period].loveThis += feedbackByUser[user.id].reduce((p, x) => p ||
          x.category === 'love-this', false) ? 1 : 0;
      }
    }
  }
  const data = Object.keys(periods)
    .sort((a, b) => periods[a].startDate < periods[b].startDate ? 1 : -1)
    .map(k => periods[k])
    .map(({ startDate, newUsers, newUsersWithSim, playtimeDayOne, playtimeWeekOne, confused, loveThis }) => ({
      startDate,
      newUsers,
      startConv: newUsersWithSim / newUsers,
      newUsersWithSim,
      avgPlaytimeDayOne: playtimeDayOne / newUsersWithSim,
      playtimeDayOne,
      avgPlaytimeWeekOne: playtimeWeekOne / newUsersWithSim,
      playtimeWeekOne,
      confused: confused / newUsersWithSim,
      loveThis: loveThis / newUsersWithSim
    }));
  const maxNewUsers = data.reduce((p, x) => Math.max(p, x.newUsers), 0);
  const maxStartConv = data.reduce((p, x) => Math.max(p, x.startConv), 0);
  const maxNewUsersWithSim = data.reduce((p, x) => Math.max(p, x.newUsersWithSim), 0);
  const maxAvgPlaytimeDayOne = data.reduce((p, x) => Math.max(p, x.avgPlaytimeDayOne), 0);
  const maxPlaytimeDayOne = data.reduce((p, x) => Math.max(p, x.playtimeDayOne), 0);
  const maxAvgPlaytimeWeekOne = data.reduce((p, x) => Math.max(p, x.avgPlaytimeWeekOne), 0);
  const maxPlaytimeWeekOne = data.reduce((p, x) => Math.max(p, x.playtimeWeekOne), 0);
  const maxConfused = data.reduce((p, x) => Math.max(p, x.confused), 0);
  const maxLoveThis = data.reduce((p, x) => Math.max(p, x.loveThis), 0);
  return <div className="Page">
    <Checkbox value={fromFirstSim} onChange={setFromFirstSim}>From first sim</Checkbox>&nbsp;•&nbsp;
    <EnumEditor value={periodSel} onChange={setPeriodSel} variants={PeriodTypeVariants} />
    <table>
    <tbody>
      <tr>
        <th>{periodSel}</th>
        <th>New users</th>
        <th>Start sim rate</th>
        <th>New users with sim</th>
        <th>Avg D1 playtime [h]</th>
        <th>D1 Playtime [h]</th>
        <th>Avg W1 playtime [h]</th>
        <th>W1 Playtime [h]</th>
        <th>Feedback confused</th>
        <th>Feedback love-this</th>
      </tr>
    {data.map(period => <tr>
        <td>{period.startDate}</td>
        <td><ProgressBar progress={period.newUsers / maxNewUsers}><FormatNumberPrecision value={period.newUsers} /></ProgressBar></td>
        <td><ProgressBar progress={period.startConv / maxStartConv}><FormatNumberPrecision value={100 * period.startConv} />%</ProgressBar></td>
        <td><ProgressBar progress={period.newUsersWithSim / maxNewUsersWithSim}><FormatNumberPrecision value={period.newUsersWithSim} /></ProgressBar></td>
        <td><ProgressBar progress={period.avgPlaytimeDayOne / maxAvgPlaytimeDayOne}><FormatNumberPrecision value={period.avgPlaytimeDayOne} /></ProgressBar></td>
        <td><ProgressBar progress={period.playtimeDayOne / maxPlaytimeDayOne}><FormatNumberPrecision value={period.playtimeDayOne} /></ProgressBar></td>
        <td><ProgressBar progress={period.avgPlaytimeWeekOne / maxAvgPlaytimeWeekOne}><FormatNumberPrecision value={period.avgPlaytimeWeekOne} /></ProgressBar></td>
        <td><ProgressBar progress={period.playtimeWeekOne / maxPlaytimeWeekOne}><FormatNumberPrecision value={period.playtimeWeekOne} /></ProgressBar></td>
        <td><ProgressBar progress={period.confused / maxConfused}><FormatNumberPrecision value={100*period.confused} />%</ProgressBar></td>
        <td><ProgressBar progress={period.loveThis / maxLoveThis}><FormatNumberPrecision value={100*period.loveThis} />%</ProgressBar></td>
      </tr>)}
    </tbody>
  </table></div>
}

