function activeUsersFromSims(sessions: DbSession[], all: boolean, period: PeriodType): Array<{ date: string, value: number }> {
  if (!all) {
    sessions = sessions.filter(x => x.started.toDate() >= DerkStartDate);
  }
  sessions = sessions.filter(x => x.minutesInteracting > 1);
  const data: { [key: string]: { date: moment.Moment, users: { [id: string]: boolean } } } = {};
  for (const sim of sessions) {
    const g = groupPeriod(sim.started.toDate(), period);
    if (!data[g.period]) {
      data[g.period] = {
        date: g.startDate,
        users: {}
      }
    }
    data[g.period].users[sim.userId] = true;
  }
  return Object.keys(data)
    .map(k => ({ date: data[k].date, value: Object.keys(data[k].users).length }))
    .sort((a, b) => a.date.toDate() < b.date.toDate() ? -1 : 1)
    .map(x => ({ date: x.date.format('YYYY-MM-DD'), value: x.value }));
}

function minutesInteractingFromSims(sessions: DbSession[], all: boolean, period: PeriodType): Array<{ date: string, value: number }> {
  if (!all) {
    sessions = sessions.filter(x => x.started.toDate() >= DerkStartDate);
  }
  const data: { [key: string]: { date: moment.Moment, value } } = {};
  for (const sim of sessions) {
    const g = groupPeriod(sim.started.toDate(), period);
    if (!data[g.period]) {
      data[g.period] = {
        date: g.startDate,
        value: 0
      }
    }
    data[g.period].value += sim.minutesInteracting;
  }
  return Object.keys(data)
    .map(k => ({ date: data[k].date, value: data[k].value }))
    .sort((a, b) => a.date.toDate() < b.date.toDate() ? -1 : 1)
    .map(x => ({ date: x.date.format('YYYY-MM-DD'), value: x.value }));
}

function newUsersFromUsers(users: DbUser[], all: boolean, period: PeriodType): Array<{ date: string, value: number }> {
  if (!all) {
    users = users.filter(x => x.created.toDate() >= DerkStartDate);
  }
  const data: { [key: string]: { date: moment.Moment, value: number } } = {};
  for (const user of users) {
    const g = groupPeriod(user.created.toDate(), period);
    if (!data[g.period]) {
      data[g.period] = {
        date: g.startDate,
        value: 0
      }
    }
    data[g.period].value++;
  }
  return Object.keys(data)
    .map(k => ({ date: data[k].date, value: data[k].value }))
    .sort((a, b) => a.date.toDate() < b.date.toDate() ? -1 : 1)
    .map(x => ({ date: x.date.format('YYYY-MM-DD'), value: x.value }));
}

function DateGraph({ data }: { data: Array<{ date: string, value: number }> }) {
  return <BarChart width={800} height={300} data={data}>
    <XAxis dataKey="date" />
    <YAxis yAxisId={0} dataKey="value" orientation="right" />
    <Bar yAxisId={0} type="monotone" dataKey="value" fill="#8884d8" />
    <Tooltip content={({ payload }) => payload && payload.length > 0 ?
      <span>
        {moment(payload[0].payload.date).format('D MMM YYYY')}<br />
        Value: {payload[0].payload.value}<br/>
      </span> : null} />
  </BarChart>
}

