class RerenderInterval extends React.Component {
  componentDidMount() {
    this.timer = window.setInterval(() => this.forceUpdate(), 1000);
  }
  timer?: number;
  componentWillUnmount() {
    if (this.timer) {
      clearInterval(this.timer);
      this.timer = undefined;
    }
  }
  render() {
    return this.props.children;
  }
}

function normalizeTrainStatsTeam(team: DbTrainingStatsTeam) {
  const nCreatures = team.creatures.length;
  return {
    ...team,
    nCreatures,
    nUnits: team.crabs + team.ducks + team.scarecrows + team.turtles + nCreatures,
    minGeneration: (team.creatures.length > 0 ? team.creatures.map(x => x.generation).reduce((p, x) => Math.min(p, x)) : 0),
    maxGeneration: (team.creatures.length > 0 ? team.creatures.map(x => x.generation).reduce((p, x) => Math.max(p, x)) : 0),
  }
}

function trainingStatsTeamSummary(team: DbTrainingStatsTeam, specificGeneration = true, includeItems = true) {
  const t = normalizeTrainStatsTeam(team);
  return [
    ...t.creatures.map(c => `${includeItems ? c.items.join('/') : ''}:${specificGeneration ? c.generation : (c.generation === 0 ? '0' : '>')}`),
    t.scarecrows > 0 ? `${t.scarecrows} scarecrows` : undefined,
    t.crabs > 0 ? `${t.crabs} crabs` : undefined,
    t.turtles > 0 ? `${t.turtles} turtles` : undefined,
    t.ducks > 0 ? `${t.ducks} ducks` : undefined,
  ].filter(x => x).join(', ');
}

function derivedTrainingStats(stat: DbTrainingStats) {
  const lastRounds = stat.winPercs.slice(-5);
  return {
    ...stat,
    rounds: stat.winPercs.length,
    startWins: stat.winPercs[0],
    endWins: lastRounds.length > 0 ? (lastRounds.reduce((p, x) => p + x) / lastRounds.length) : 0
  }
}

function seriesAvgSmoothed(data: number[], count = 5) {
  return data.map((_, i) => {
    const slice = data.slice(Math.max(0, i + 1 - count), i + 1);
    return slice.reduce((p, x) => p + x) / slice.length;
  })
}

function seriesTrailingSmoothed(data: number[], strength = 0.9) {
  const res = [data[0]];
  for (let i=1; i < data.length; i++) {
    res.push(res[i - 1] * strength + data[i] * (1 - strength));
  }
  return res;
}

function TrainingStatsSummary({ stat }: { stat: DbTrainingStats & DbId }) {
  const { startWins, endWins, winPercs, tiePercs } = derivedTrainingStats(stat);
  return <Row>
    <Column>
      <Block>From {Math.round(100 * startWins)}% win to {Math.round(100 * endWins)}%</Block>
      <Block>Rounds: {winPercs.length}</Block>
      <Block>Home: {trainingStatsTeamSummary(stat.home)}</Block>
      <Block>Away: {trainingStatsTeamSummary(stat.away)}</Block>
      <Block>{stat.appBuild}</Block>
      <Block><AdminUserNameComponent userId={stat.userId} /></Block>
      <Plot lines={[
        { data: winPercs, label: 'Wins', color: 'green', yMin: 0, yMax: 1 },
        { data: seriesAvgSmoothed(winPercs), label: 'Wins 5', color: 'yellow', yMin: 0, yMax: 1 },
        { data: seriesAvgSmoothed(winPercs, 20), label: 'Wins 20', color: 'blue', yMin: 0, yMax: 1 },
        { data: seriesTrailingSmoothed(winPercs), label: 'Wins trailing 0.9', color: 'pink', yMin: 0, yMax: 1 },
        { data: seriesTrailingSmoothed(winPercs, 0.95), label: 'Wins trailing 0.95', color: 'cyan', yMin: 0, yMax: 1 },
        { data: tiePercs, label: 'Ties', color: 'grey', yMin: 0, yMax: 1 },
      ]} />
    </Column>
    <Block><pre>{stat.experiment ?? ''}</pre></Block>
  </Row>
}

enum TrainStatsScatterPlotKind {
  StartWinEndWin = 'StartWinEndWin',
  RoundsEndWin = 'RoundsEndWin'
}

function TrainStatsScatterPlot({ stats, kind = TrainStatsScatterPlotKind.StartWinEndWin }: { stats: Array<DbTrainingStats & DbId>, kind?: TrainStatsScatterPlotKind }) {
  const [hover, setHover] = React.useState<DbTrainingStats & DbId>();
  const [selected, setSelected] = React.useState<DbTrainingStats & DbId>();
  const summary = (hover ?? selected);
  return <Row>
    <svg xmlns="http://www.w3.org/2000/svg" viewBox={`-0.1 -0.1 1.2 1.2 `} width={300} height={300}>
      <line x1={0} x2={1} y1={1} y2={1} stroke="rgba(255, 255, 255, 0.1)" strokeWidth={0.01} />
      <line x1={0} x2={0} y1={0} y2={1} stroke="rgba(255, 255, 255, 0.1)" strokeWidth={0.01} />
      {kind === TrainStatsScatterPlotKind.RoundsEndWin ?
        <line x1={0} x2={1} y1={0.5} y2={0.5} stroke="rgba(255, 255, 255, 0.1)" strokeWidth={0.01} /> :
        <line x1={0} x2={1} y1={1} y2={0} stroke="rgba(255, 255, 255, 0.1)" strokeWidth={0.01} />}
      {stats.slice().sort((a, b) => a.created.toMillis() - b.created.toMillis()).map(stat => {
        const { startWins, endWins, rounds } = derivedTrainingStats(stat);
        const x = kind === TrainStatsScatterPlotKind.StartWinEndWin ? startWins : clamp(rounds / 200, 0, 1);
        const [, currentMinor, ] = version.version.split('.');
        const color = stat.appVersion.minor === parseInt(currentMinor) ? (
          stat.experiment ? stringToRGBHex(`${stat.appVersion.minor} ${stat.experiment} lejwwjkehrksdkjfwejkhwekjrhdsf`) : 'yellow')
          : 'red';
        // const color = (stat.experiment === 'softmax-cast-and-focus') ? 'yellow' : 'red';
        return <g key={stat.id}>
          <circle
            cx={x} cy={1 - endWins} r={0.01}
            fill={color}
            stroke={selected && selected.id === stat.id ? 'yellow' : undefined} strokeWidth={0.01}
            onClick={() => setSelected(stat)}
            onMouseOver={() => setHover(stat)} onMouseOut={() => setHover(undefined)} />
          {kind === TrainStatsScatterPlotKind.RoundsEndWin && <line x1={x} y1={1 - endWins} x2={x} y2={1 - startWins}
            stroke={`rgba(${endWins > startWins ? '100, 255, 100' : '255, 100, 100'}, 0.1)`} strokeWidth={0.005} />}
        </g>;
      })}
    </svg>
    <Column>
      <Row>
        {kind}
        <Button onClick={() => setSelected(stats[Math.floor(Math.random() * stats.length)])}>
          Random
        </Button>
      </Row>
      {summary && <TrainingStatsSummary stat={summary} />}
    </Column>
  </Row>
}


function trainingStatsDescription(stats: DbTrainingStats, includeItems: boolean) {
  return `${trainingStatsTeamSummary(stats.home, false, includeItems)} vs ${trainingStatsTeamSummary(stats.away, false, includeItems) || 'statue'}`;
}

function groupAndSortTrainingStats(stats: Array<DbTrainingStats & DbId>, includeItems: boolean) {
  const groups = {};
  for (const stat of stats) {
    const desc = trainingStatsDescription(stat, includeItems);
    if (!groups[desc]) {
      groups[desc] = {
        stats: [stat]
      }
    } else {
      groups[desc].stats.push(stat);
    }
  }
  const res = Object.keys(groups).map(key => ({
    desc: key,
    stats: groups[key].stats
  }));
  res.sort((a, b) => b.stats.length - a.stats.length);
  return res;
}

enum TrainStatsBattleground {
  Any = 'Any',
  Solo = 'Solo',
  Duo = 'Duo',
  Trio = 'Trio'
}

export function AdminTrainingStats() {
  const [reqItems, setReqItems] = React.useState(true);
  const [plotKind, setPlotKind] = React.useState(TrainStatsScatterPlotKind.RoundsEndWin);
  const [battleground, setBatttleground] = React.useState(TrainStatsBattleground.Any);
  const [userId, setUserId] = React.useState<string>();
  const stats = useFirebaseStreamQuery<DbTrainingStats>({
    collection: Db.trainingStats,
    where: []
  });
  if (stats.status !== 'loaded') {
    return <FirebaseLoading pending={[stats]} />;
  }
  const filteredStats = stats.values.filter(x => {
    const teamSize = Math.max(normalizeTrainStatsTeam(x.home).nUnits, normalizeTrainStatsTeam(x.away).nUnits);
    if (userId && x.userId !== userId) {
      return false;
    }
    switch (battleground) {
      case TrainStatsBattleground.Any: return true;
      case TrainStatsBattleground.Solo: return teamSize === 1;
      case TrainStatsBattleground.Duo: return teamSize === 2;
      case TrainStatsBattleground.Trio: return teamSize === 3;
    }
    return false;
  })
  const groups = groupAndSortTrainingStats(filteredStats, reqItems);
  return <Block className="AdminTrainingStats">
    <ItemsRow>
      <Checkbox value={reqItems} onChange={setReqItems}>Items</Checkbox>
      <EnumEditor value={plotKind} onChange={setPlotKind} variants={keysOfEnum(TrainStatsScatterPlotKind)} />
      <Block>
        Battleground: <EnumEditor value={battleground} onChange={setBatttleground} variants={keysOfEnum(TrainStatsBattleground)} />
      </Block>
      <StringEditor value={userId} onChange={setUserId} placeholder="User id" />
    </ItemsRow>
    <table>
      <tbody>
        <tr>
          <td className="AdminTrainingStatsGroup">all</td>
          <td className="AdminTrainingStatsGroupSize">{filteredStats.length}</td>
          <td><TrainStatsScatterPlot stats={filteredStats} kind={plotKind} /></td>
        </tr>
        {groups.slice(0, 20).map((group, i) => <tr key={i}>
          <td className="AdminTrainingStatsGroup">{group.desc}</td>
          <td className="AdminTrainingStatsGroupSize">{group.stats.length}</td>
          <td><TrainStatsScatterPlot stats={group.stats} kind={plotKind} /></td>
        </tr>)}
      </tbody>
    </table>
    Total {groups.length} groups (20 shown)
  </Block>
}
