import { GlState, GL } from "../../GpuUtil/GlState";
import { shaderModule, funcDef, outDef, CompiledShaderModule, uniformDef, inDef } from "../../GpuUtil/ShaderModule";
import { UnitsInArena } from "../ECS/ArenaStats";
import { OutputLayer, Senses } from "../Brain/Components";
import { InterpolateSM } from "../../GpuUtil/SM2Utils";
import { Program } from "../../GpuUtil/Program";
import { GpuWorldState } from "../State";
import { RenderTarget } from "../../GpuUtil/RenderTarget";
import { Vertex } from "../../GpuUtil/Mesh";
import { vec2FromSize, interpolateClamped } from "../../Math/Math";
import { Hitpoints } from "../ECS/UnitComponents";
import { GpuComponentModule } from "../../GpuUtil/ECS/ComponentModules";
import { TextureSlicing, Texture } from "../../GpuUtil/Texture";

class UpdateBrainHistogram {
  constructor(private gls: GlState, private component: GpuComponentModule<'vec4'>) {}

  shader = new CompiledShaderModule(shaderModule([
    UnitsInArena, this.component.sm, InterpolateSM, this.component.hasComponentSM, Hitpoints.sm
  ], {
    outValues: outDef('vec4[8]'),
    nArenas: uniformDef('int', d => d.layout.arenas),
    main: funcDef('void', [], `
      vec4 values[8];
      int arenaMemberIndex = int(gl_FragCoord.x);
      int decision = int(gl_FragCoord.y);
      float nAlive = 0.;
      for (int i=0; i < Self.nArenas; i++) {
        ivec2 unit = ${UnitsInArena.unit}(i, arenaMemberIndex);
        if (unit.x >= 0 && ${this.component.hasComponentSM.value}(unit) && ${Hitpoints.sm.value}(unit) > 0.) {
          vec4 outputSlice = ${this.component.sm.value}(unit, decision / 4);
          float decisionValue = outputSlice[decision % 4];
          float bucketFloat = ${InterpolateSM.interpolateClamped}(decisionValue, -1., 1., 0., 32.);
          int bucket = clamp(int(bucketFloat), 0, 31);
          nAlive++;
          values[bucket / 4][bucket % 4]++;
        }
      }
      for (int i=0; i < 32; i++) {
        values[i / 4][i % 4] /= nAlive;
      }
      outValues = values;
    `)
  }));
  program = new Program(this.gls, this.shader.source);
  renderTarget = new RenderTarget(this.gls);

  run(state: GpuWorldState, slice: TextureSlicing) {
    this.shader.updateUniformValues(state);
    this.renderTarget.set(slice);
    this.program.prepare();
    this.shader.writeUniforms(this.program);
    this.gls.prepareForComputation(slice.rect);
    this.gls.drawQuad(this.program);
  }
}

export class UpdateBrainHistograms {
  constructor(private gls: GlState) {}

  senses = new UpdateBrainHistogram(this.gls, Senses);
  decisions = new UpdateBrainHistogram(this.gls, OutputLayer.res);

  run(state: GpuWorldState) {
    if (state.data.showSensesHistogram) {
      this.senses.run(state, state.textures.sensesHistogram.sliceAll());
    }
    if (state.data.showDecisionsHistogram) {
      this.decisions.run(state, state.textures.decisionsHistogram.sliceAll());
    }
  }
}

class BrainHistogramRenderer {
  constructor(private gls: GlState) {}

  vertexShader = new CompiledShaderModule(shaderModule([
    Vertex, InterpolateSM
  ], {
    texcoord: outDef('vec2'),
    corners: uniformDef('vec4'),
    main: funcDef('void', [], `
      gl_Position = vec4(
        ${InterpolateSM.interpolate}(${Vertex.position}.xy, vec2(-1.), vec2(1.), corners.xy, corners.zw),
        0.,
        1.
      );
      texcoord = (${Vertex.position}.xy + 1.0) / 2.0;
    `)
  }));

  fragmentShader = new CompiledShaderModule(shaderModule([
    Vertex
  ], {
    texcoord: inDef('vec2'),
    outColor: outDef('vec4'),
    histogram: uniformDef('sampler2DArray'),
    histogramSize: uniformDef('vec2'),
    main: funcDef('void', [], `
      vec2 histogramCell = texcoord * Self.histogramSize;
      int arenaMemberIndex = int(histogramCell.x);
      int decision = int(histogramCell.y);
      int bucket = int(32. * mod(histogramCell.x, 1.));
      float y = 1. - mod(histogramCell.y, 1.);
      vec4 bucketSlice = texelFetch(Self.histogram, ivec3(arenaMemberIndex, decision, bucket / 4), 0);
      float bucketPerc = bucketSlice[bucket % 4];
      vec3 background;
      if (arenaMemberIndex % 2 == 0) {
        background += 0.1;
      }
      if (decision % 2 == 0) {
        background += 0.1;
      }
      outColor = y < bucketPerc ? vec4(1) : vec4(background, 1);
    `)
  }));

  program = new Program(this.gls, this.fragmentShader.source, this.vertexShader.source);

  run(state: GpuWorldState, histogram: Texture, rect: ClientRect) {
    const corners = {
      x: interpolateClamped(rect.left, 0, state.data.canvasRect.width, -1, 1),
      y: interpolateClamped(rect.top, 0, state.data.canvasRect.height, 1, -1),
      z: interpolateClamped(rect.left + rect.width, 0, state.data.canvasRect.width, -1, 1),
      w: interpolateClamped(rect.top + rect.height, 0, state.data.canvasRect.height, 1, -1),
    }

    this.vertexShader.updateUniformValues(state);
    this.fragmentShader.updateUniformValues(state);
    this.program.prepare();
    this.vertexShader.writeUniforms(this.program);
    this.fragmentShader.writeUniforms(this.program);
    this.program.setUniform('histogram', 'sampler2DArray', histogram);
    this.program.setUniform('histogramSize', 'vec2', vec2FromSize(histogram.size));
    this.program.setUniform('corners', 'vec4', corners);
    this.gls.disable(GL.DEPTH_TEST);
    this.gls.disable(GL.BLEND);
    this.gls.drawQuad(this.program);
  }
}

export class BrainHistogramsRenderer {
  constructor(private gls: GlState) {}
  renderer = new BrainHistogramRenderer(this.gls);
  run(state: GpuWorldState) {
    if (state.data.showDecisionsHistogram) {
      this.renderer.run(state, state.textures.decisionsHistogram, state.data.decisionHistogramView);
    }
    if (state.data.showSensesHistogram) {
      this.renderer.run(state, state.textures.sensesHistogram, state.data.sensesHistogramView);
    }
  }
}