import { BrainOutputs, ArenaMembersSize } from "../StateLayout";
import { OutputLayer } from "./Components";
import { shaderModule, funcDef } from "../../GpuUtil/ShaderModule";
import { WithoutNaNInfSM, ArrayMaxSM } from "../../GpuUtil/SM2Utils";
import { GpuComponentModule } from "../../GpuUtil/ECS/ComponentModules";
import { UnitBuffer } from "../ECS/UnitComponents";
import { GlState } from "../../GpuUtil/GlState";
import { ArchetypesQuery } from "../../GpuUtil/ECS/Query";
import { GpuWorldState } from "../State";

export const Decisions = {
  MoveX: new GpuComponentModule('DecisionsMoveX', UnitBuffer, 'float'),
  Rotate: new GpuComponentModule('DecisionsRotate', UnitBuffer, 'float'),
  ChaseFocus: new GpuComponentModule('DecisionsChaseFocus', UnitBuffer, 'float'),
  CastingSlot: new GpuComponentModule('DecisionsCastingSlot', UnitBuffer, 'int'),
  ChangeFocus: new GpuComponentModule('DecisionsChangeFocus', UnitBuffer, 'int'),
}
export const DecisionsCMs = Object.keys(Decisions).map(key => Decisions[key] as GpuComponentModule<'float'>);

const SampleCastSlotSM = ArrayMaxSM(3);
const SampleFocusSM = ArrayMaxSM(ArenaMembersSize - 1);

export function UpdateDecisionsFromBrain(gls: GlState) {
  return new ArchetypesQuery([OutputLayer.res.component])
    .updateGpu(gls, DecisionsCMs.map(d => d.component), shaderModule([
      ...DecisionsCMs.map(c => c.writer),
      SampleCastSlotSM, SampleFocusSM, OutputLayer.res.sm, WithoutNaNInfSM
    ], {
      brainOutput: funcDef('float', [['ivec2', 'entityCell'], ['int', 'brainOutput']], `
        int slice = brainOutput / 4;
        int channel = brainOutput % 4;
        return ${WithoutNaNInfSM.withoutNaNInf}(${OutputLayer.res.sm.value}(entityCell, slice)[channel]);
      `),
      update: funcDef('void', [['ivec2', 'entityCell']], `
        ${Decisions.MoveX.writer.write}(tanh(Self.brainOutput(entityCell, ${BrainOutputs.MoveX})));
        ${Decisions.Rotate.writer.write}(tanh(Self.brainOutput(entityCell, ${BrainOutputs.Rotate})));
        ${Decisions.ChaseFocus.writer.write}(clamp(Self.brainOutput(entityCell, ${BrainOutputs.ChaseFocus}), 0.0, 1.0));
        float[] castSlots = float[](
          clamp(Self.brainOutput(entityCell, ${BrainOutputs.CastSlot0}), 0., 1.),
          clamp(Self.brainOutput(entityCell, ${BrainOutputs.CastSlot1}), 0., 1.),
          clamp(Self.brainOutput(entityCell, ${BrainOutputs.CastSlot2}), 0., 1.)
        );
        int maxCastSlotI = ${SampleCastSlotSM.max}(castSlots);
        float maxCastSlot = castSlots[maxCastSlotI];
        ${Decisions.CastingSlot.writer.write}(maxCastSlot > 0. ? (maxCastSlotI + 1) : 0);
        float[] focuses = float[](
          clamp(Self.brainOutput(entityCell, ${BrainOutputs.FocusFriendStatue}), 0., 1.),
          clamp(Self.brainOutput(entityCell, ${BrainOutputs.FocusFriend1}), 0., 1.),
          clamp(Self.brainOutput(entityCell, ${BrainOutputs.FocusFriend2}), 0., 1.),
          clamp(Self.brainOutput(entityCell, ${BrainOutputs.FocusEnemyStatue}), 0., 1.),
          clamp(Self.brainOutput(entityCell, ${BrainOutputs.FocusEmemy1}), 0., 1.),
          clamp(Self.brainOutput(entityCell, ${BrainOutputs.FocusEnemy2}), 0., 1.),
          clamp(Self.brainOutput(entityCell, ${BrainOutputs.FocusEnemy3}), 0., 1.)
        );
        int maxFocusI = ${SampleFocusSM.max}(focuses);
        float maxFocus = focuses[maxFocusI];
        if (maxFocus > 0.) {
          ${Decisions.ChangeFocus.writer.write}(maxFocusI + 1);
        } else {
          ${Decisions.ChangeFocus.writer.write}(0);
        }
      `)
    }))
}

export class UpdateUserControlledDecisions {
  run(state: GpuWorldState) {
    if (state.data.userControlEntity && state.data.userControlSet) {
      for (const key of Object.keys(state.data.userControlSet)) {
        const v = state.data.userControlSet[key];
        state.ecs.setComponentData(v.component, state.data.userControlEntity, v.value);
      }
    }
  }
}