import * as React from 'react';
import { SketchPicker } from 'react-color';
import { Button } from '../Button';
import { Popdown } from '../Popdown';
import { EditorProps, NonNullEditorDispatch } from './EditorProps';
import { RGBA } from '../../Util';
import color from 'color';

interface ColorEditorProps extends EditorProps<RGBA> {
  title?: string;
}
interface ColorEditorState {
  showColorEditor: boolean;
}

export class ColorEditor extends React.Component<ColorEditorProps & NonNullEditorDispatch<RGBA>, ColorEditorState> {
  state: ColorEditorState = { showColorEditor: false };
  render() {
    const { title } = this.props;
    const fill = this.props.value ? color({ r: this.props.value.r, g: this.props.value.g, b: this.props.value.b }).toString() : undefined;
    return (
      <Popdown
        className="ColorEditor"
        showPopdown={this.state.showColorEditor}
        onClickFader={() => setTimeout(() => this.setState({ showColorEditor: false }), 10)}
        popdown={() => (
          <SketchPicker
            color={this.props.value}
            onChangeComplete={col => {
              this.props.onChange(col.rgb);
            }} />
        )}>
        <Button
          className={this.props.className}
          fill={fill}
          onClick={() => !this.props.readOnly && this.setState({ showColorEditor: true })}
          purpose="OpenColorEditor">
          {title !== undefined ? title : 'color'}
        </Button>
      </Popdown>
    );
  }
}