import { EntityComponentSystem } from "../../GpuUtil/ECS";
import { GpuWorldState } from "../State";
import * as THREE from 'three';
import { EntityRef } from "../StateData";
import { PixlingTimespan } from "../../Time";
import { VisibleComponent } from "./Components";
import { EntitiesQuery } from "../../GpuUtil/ECS/Query";
import { mod } from "../../Math/Math";
import { Position, Quaternion, Scale } from "./Transform";
import { AnimationTrack, AnimationTrackInterpolator } from "./AnimationTrack";
import { visibleCacheKeys } from "./Visibility";

export interface AnimationAction {
  clip: string;
  positionType: 'time' | 'startTime' | 'percentage';
  positionValue: number;
  looping?: boolean;
  weight?: number;
}
interface AnimationControllerProps {
  actions: AnimationAction[];
}
export const AnimationControllerComponent = EntityComponentSystem.registerComponent<AnimationControllerProps>({
  name: 'AnimationController',
  type: 'obj'
});

interface AnimationBinderProps {
  entityLookup: {
    [name: string]: EntityRef
  },
  clipNames: {
    [name: string]: string;
  }
}
export const AnimationBinderComponent = EntityComponentSystem.registerComponent<AnimationBinderProps>({
  name: 'AnimationBinder',
  type: 'obj'
});

function mixTracks(type: AnimationTrack['type'], a: number[], b: number[], p: number) {
  if (type === 'vec3') {
    a[0] = a[0] * (1 - p) + b[0] * p;
    a[1] = a[1] * (1 - p) + b[1] * p;
    a[2] = a[2] * (1 - p) + b[2] * p;
  } else {
    THREE.Quaternion.slerpFlat(a, 0, a, 0, b, 0, p);
  }
}

export interface AnimationClip {
  duration?: number;
  tracks: AnimationTrack[];
}

export function animationClipFromThree(clip: THREE.AnimationClip): AnimationClip {
  return {
    duration: clip.duration,
    tracks: clip.tracks.map(animationTrackFromThree)
  }
}
export function animationTrackFromThree(track: THREE.KeyframeTrack): AnimationTrack {
  const [name, property] = track.name.split('.');
  if (track instanceof THREE.VectorKeyframeTrack) {
    let component;
    if (property === 'position') {
      component = Position.cpuComponent;
    } else if (property === 'scale') {
      component = Scale.cpuComponent;
    } else {
      throw new Error(`Unsupported property: ${property}`);
    }
    return {
      type: 'vec3',
      targetName: name,
      componentName: component.name,
      times: Array.from(track.times),
      values: Array.from(track.values),
    }
  } else {
    let component;
    if (property === 'quaternion') {
      component = Quaternion.cpuComponent;
    } else {
      throw new Error(`Unsupported property: ${property}`);
    }
    return {
      type: 'quat',
      targetName: name,
      componentName: component.name,
      times: Array.from(track.times),
      values: Array.from(track.values),
    }
  }
}

function getAnimationInterpolator(state: GpuWorldState, clipName: string, trackIndex: number, track: AnimationTrack) {
  const key = clipName + '_' + trackIndex;
  const interpolator = state.animationInterpolators[key];
  if (interpolator && interpolator.track === track) {
    return interpolator;
  } else {
    return state.animationInterpolators[key] = new AnimationTrackInterpolator(track);
  }
}

export class UpdateAnimations {
  query = new EntitiesQuery([AnimationControllerComponent, VisibleComponent])
    .filterValue(VisibleComponent, v => !!v, visibleCacheKeys);
  run(state: GpuWorldState) {
    const currentTime = new PixlingTimespan(state.data.stepCounter + state.data.substep).timeInSecondsFractional;
    for (const entityCell of this.query.get(state)) {
      const controller = state.ecs.getComponentData(AnimationControllerComponent, entityCell)!;
      const binder = state.ecs.getComponentData(AnimationBinderComponent, entityCell);
      // Calc
      const outputs: { [key: string]: { track: AnimationTrack, value: number[], weight: number } } = {};
      for (const action of controller.actions) {
        const clipName = binder ? binder.clipNames[action.clip] : action.clip;
        const clip = state.data.animationClips[clipName];
        if (!clip) {
          // console.warn(`No such animation: ${action.clip} (${clipName})`);
          continue;
        }
        let animTime: number;
        if (action.positionType === 'startTime') {
          animTime = currentTime - action.positionValue;
        } else if (action.positionType === 'percentage') {
          if (clip.duration === undefined) {
            throw new Error(`'percentage' positionType requires clip.duration to be defined. Clip name=${clipName}`);
          }
          animTime = action.positionValue * clip.duration;
        } else {
          animTime = action.positionValue;
        }
        if (action.looping) {
          if (clip.duration === undefined) {
            throw new Error(`Looping clip requires clip.duration to be defined. Clip name=${clipName}`);
          }
          animTime = mod(animTime, clip.duration);
        }
        for (let i=0; i < clip.tracks.length; i++) {
          const track = clip.tracks[i];
          const interpolator = getAnimationInterpolator(state, clipName, i, track);
          const value = interpolator.value(animTime);
          const key = track.targetName + track.componentName;
          const o = outputs[key];
          const weight = action.weight ?? 1;
          if (weight === 0) {
            continue;
          }
          if (!o) {
            outputs[key] = {
              track,
              value,
              weight
            }
          } else {
            o.weight += weight;
            const mix = weight / o.weight;
            mixTracks(track.type, o.value, value, mix);
          }
        }
      }
      // Apply
      for (const key of Object.keys(outputs)) {
        const output = outputs[key];
        const ec = (binder && output.track.targetName) ? binder.entityLookup[output.track.targetName] : entityCell;
        const comp = state.ecs.getComponentByName(output.track.componentName);
        if (!comp) {
          throw new Error(`[UpdateAnimation] No such component: ${output.track.componentName}`);
        } else if (comp.type !== 'cpu') {
          throw new Error(`[UpdateAnimation] Not a cpu component: ${output.track.componentName}`);
        }
        const compData = state.ecs.getCpuComponentDataView(comp, ec);
        if (!compData) {
          throw new Error(`[UpdateAnimation] No data for component: ${output.track.componentName}`);
        }
        for (let i=0; i < output.value.length; i++) {
          compData[i] = output.value[i];
        }
      }
    }
  }
}