import * as Math2 from './Math/Math';

enum Month {
  Jan,
  Feb,
  Mar,
  Apr,
  May,
  Jun,
  Jul,
  Aug,
  Sep,
  Oct,
  Nov,
  Dec,
}

export const DaysInMonth = 10;
export const StepsPerSecond = 60;
export const SecondsPerStep = 1/StepsPerSecond;
export const MonthsInYear = 12;
export const DaysInYear = MonthsInYear * DaysInMonth; // 120
export const SecondsInMinute = 60;
export const SecondsInHour = 60 * SecondsInMinute;
export const SecondsInDay = SecondsInHour * 24;
export const StepsInMinute = SecondsInMinute * StepsPerSecond;
export const StepsInHour = SecondsInHour * StepsPerSecond;
export const StepsInDay = SecondsInDay * StepsPerSecond;
export const StepsInMonth = DaysInMonth * StepsInDay;
export const StepsInYear = DaysInYear * StepsInDay;

export class PixlingDate {
  constructor(public steps: number) {
  }
  private secondsOffset = 60 * 60 * 8; // Start time at 8am instead of midnight, so we get sunlight
  timeInSeconds = this.steps * SecondsPerStep + this.secondsOffset;
  timeInMinutesFractional = this.timeInSeconds / 60;
  timeInHoursFractional = this.timeInSeconds / (60 * 60);
  timeInDaysFractional = this.timeInSeconds / (60 * 60 * 24);
  timeInMonthsFractional = this.timeInSeconds / (60 * 60 * 24 * DaysInMonth);
  timeInYearsFractional = this.timeInSeconds / (60 * 60 * 24 * DaysInYear);
  timeInMinutes = Math.floor(this.timeInMinutesFractional);
  timeInHours = Math.floor(this.timeInHoursFractional);
  timeInDays = Math.floor(this.timeInDaysFractional);
  timeInMonths = Math.floor(this.timeInMonthsFractional);
  timeInYears = Math.floor(this.timeInYearsFractional);
  secondOfMinute = this.timeInSeconds % 60;
  minuteOfHour = this.timeInMinutes % 60;
  hourOfDay = this.timeInHours % 24;
  dayOfYear = this.timeInDays % DaysInYear;
  monthOfYear = this.timeInMonths % MonthsInYear;
  year = this.timeInYears;
  dayOfMonth = this.timeInDays - this.timeInMonths * DaysInMonth + 1; // 1 index
  timeOfDayPercentage = Math2.fract(this.timeInDaysFractional);
  timeOfYearPercentage = Math2.fract(this.timeInYearsFractional);
  toString() {
    return `${this.year} ${Month[this.monthOfYear]} ${this.dayOfMonth} - ${this.hourOfDay.toString().padStart(2, '0')}:${this.minuteOfHour.toString().padStart(2, '0')}`
  }
}

interface PixlingTimespanOpts {
  short?: boolean;
  padded?: boolean;
}

export class PixlingTimespan {
  constructor(public steps: number) { }
  static fromDef(def: PixlingTimespanDef) { return new PixlingTimespan(pixlingTimespanDefToSteps(def)); }
  timeInSecondsFractional = this.steps * SecondsPerStep;
  timeInMinutesFractional = this.timeInSecondsFractional / 60;
  timeInHoursFractional = this.timeInSecondsFractional / (60 * 60);
  timeInDaysFractional = this.timeInSecondsFractional / (60 * 60 * 24);
  timeInMonthsFractional = this.timeInSecondsFractional / (60 * 60 * 24 * DaysInMonth);
  timeInYearsFractional = this.timeInSecondsFractional / (60 * 60 * 24 * DaysInYear);
  timeInSeconds = Math.floor(this.timeInSecondsFractional);
  timeInMinutes = Math.floor(this.timeInMinutesFractional);
  timeInHours = Math.floor(this.timeInHoursFractional);
  timeInDays = Math.floor(this.timeInDaysFractional);
  timeInMonths = Math.floor(this.timeInMonthsFractional);
  timeInYears = Math.floor(this.timeInYearsFractional);
  secondOfMinute = this.timeInSeconds % 60;
  minuteOfHour = this.timeInMinutes % 60;
  hourOfDay = this.timeInHours % 24;
  dayOfYear = this.timeInDays % DaysInYear;
  monthOfYear = this.timeInMonths % MonthsInYear;
  years = this.timeInYears;
  dayOfMonth = this.timeInDays - this.timeInMonths * DaysInMonth;
  private stringifyComp(value: number, name: string, { short = true, padded = true }: PixlingTimespanOpts = {}) {
    const valuePadded = padded ? (value + '').padStart(2, ' ') : value;
    if (short) {
      return valuePadded + name.slice(0, 1);
    } else {
      if (value === 1) {
        return valuePadded + ' ' + name + (padded ? ' ' : '');
      } else {
        return valuePadded + ' ' + name + 's';
      }
    }
  }
  toString(opts?: PixlingTimespanOpts) {
    const c = [this.stringifyComp(this.secondOfMinute, 'second', opts)];
    if (this.timeInMinutes > 0) {
      c.unshift(this.stringifyComp(this.minuteOfHour, 'minute', opts));
    }
    if (this.timeInHours > 0) {
      c.unshift(this.stringifyComp(this.hourOfDay, 'hour', opts));
    }
    if (this.timeInDays > 0) {
      c.unshift(this.stringifyComp(this.dayOfMonth, 'day', opts));
    }
    if (this.timeInMonths > 0) {
      c.unshift(this.stringifyComp(this.monthOfYear, 'month', opts));
    }
    if (this.timeInYears > 0) {
      c.unshift(this.stringifyComp(this.years, 'year', opts));
    }
    return c.join(' ');
  }
  toStringMinimal(opts?: PixlingTimespanOpts) {
    if (this.years > 0) {
      return this.stringifyComp(this.years, 'year', opts);
    } else if (this.dayOfYear > 0) {
      return this.stringifyComp(this.dayOfYear, 'day', opts);
    } else if (this.hourOfDay > 0) {
      return this.stringifyComp(this.hourOfDay, 'hour', opts);
    } else if (this.minuteOfHour > 0) {
      return this.stringifyComp(this.minuteOfHour, 'minute', opts);
    } else {
      return this.stringifyComp(this.secondOfMinute, 'second', opts);
    }
  }
}

export enum PixlingTimespanDefUnits {
  Steps,
  Minutes,
  Hours,
  Days,
  Years
}

export interface PixlingTimespanDef {
  value: number;
  unit: PixlingTimespanDefUnits;
}

export function pixlingTimespanDefToSteps(def: PixlingTimespanDef) {
  return Math.floor(pixlingTimespanDefToStepsFloat(def));
}

export function pixlingTimespanDefToStepsFloat(def: PixlingTimespanDef) {
  switch (def.unit) {
    case PixlingTimespanDefUnits.Steps: return def.value;
    case PixlingTimespanDefUnits.Minutes: return (def.value * 60) / SecondsPerStep;
    case PixlingTimespanDefUnits.Hours: return (def.value * 60 * 60) / SecondsPerStep;
    case PixlingTimespanDefUnits.Days: return (def.value * 60 * 60 * 24) / SecondsPerStep;
    case PixlingTimespanDefUnits.Years: return (def.value * 60 * 60 * 24 * DaysInYear) / SecondsPerStep;
  }
}