export function assertNever(x: never): never {
  throw new Error('Unexpected: ' + x);
}

export function base62uuid() {
  return base62.encode(uuid(null, new Buffer(16)));
}

// From: https://gist.github.com/hyamamoto/fd435505d29ebfa3d9716fd2be8d42f0
export function hashCode(s: string): number {
  let h = 0;
  for(let i = 0; i < s.length; i++) {
    // tslint:disable-next-line:no-bitwise
    h = Math.imul(31, h) + s.charCodeAt(i) | 0;
  }

  return h;
}

export function intToRGBHex(i: number) {
  // tslint:disable-next-line: no-bitwise
  const c = (i & 0x00FFFFFF)
      .toString(16)
      .toUpperCase();

  return "00000".substring(0, 6 - c.length) + c;
}
export function stringToRGBHex(s: string) {
  return '#' + intToRGBHex(hashCode(s));
}
export function stringToHSL(string: string, saturation: number, light: number) {
  return `hsl(${hashCode(string) % 360}, ${100*saturation}%, ${100*light}%)`;
}
export function rgbFromStringHash(s: string) {
  return rgbFromHex(stringToRGBHex(s));
}

export function arrayReplaceIndex<T>(array: T[], index: number, value: T): T[] {
  const a2 = [...array];
  a2[index] = value;
  return a2;
}
export function arrayInsertIndex<T>(array: T[], index: number, value: T): T[] {
  return [...array.slice(0, index), value, ...array.slice(index)];
}
export function arrayRemoveIndex<T>(array: T[], index: number): T[] {
  return [...array.slice(0, index), ...array.slice(index + 1)];
}
export function arrayMove<T>(array: T[], fromIndex: number, toIndex: number): T[] {
  const arr = array.slice();
  const [el] = arr.splice(fromIndex, 1);
  arr.splice(Math.max(toIndex, 0), 0, el);
  return arr;
}
export function objectDeleteKey<T extends object>(obj: T, key: keyof T): Partial<T> {
  const o = {...obj as any};
  delete o[key];
  return o;
}

// Conservative object update, will return the same object if the updated values are the same
export function objectSetKeys<T extends object>(obj: T, update: Partial<T>): T {
  let shouldUpdate = false;
  for (const k of Object.keys(update)) {
    if (obj[k] !== update[k]) {
      shouldUpdate = true;
    }
  }
  if (shouldUpdate) {
    return {...obj, ...update};
  } else {
    return obj;
  }
}

export function objdiff(a: any, b: any) {
  const d = {};
  for (const k of [...Object.keys(a), ...Object.keys(b)]) {
    if (a[k] !== b[k]) {
      d[k] = { a: a[k], b: b[k] };
    }
  }
  return d;
}

export function formatNumberPrecision(value: number, precision = 2, sign = false): string {
  if (value === 0 || isNaN(value) || !isFinite(value)) {
    return '' + value;
  }
  const s = Math.sign(value);
  value = Math.abs(value);
  const l = Math.ceil(Math.log10(value));
  const p = Math.pow(10, l - precision);
  const v = s * Math.floor(value / p) * p;
  return (v !== value ? '~' : '') + ((sign && v > 0) ? '+' : '') + v.toLocaleString('en-EN');
}

export enum MassUnits {
  kg = 'kg',
  g = 'g',
  mg = 'mg'
}
export function unitFromMass(value: number): MassUnits {
  return MassUnits.kg;
  // if (value >= 1) {
  //   return MassUnits.kg;
  // } else if (value >= Math.pow(10, -3)) {
  //   return MassUnits.g;
  // } else {
  //   return MassUnits.mg;
  // }
}
export function massToUnit(value: number, unit: MassUnits): number {
  switch (unit) {
    case MassUnits.kg:
      return value;
    case MassUnits.g:
      return value * Math.pow(10, 3);
    case MassUnits.mg:
      return value * Math.pow(10, 6);
  }
}
