export function endSlash(str: string): string {
  if (!str.endsWith('/')) {
    return str + '/';
  } else {
    return str;
  }
}

export function percentage(value: number, min: number, max: number, fractionDigits: number = 1): string {
  return ((value - min) / (max - min) * 100).toFixed(fractionDigits) + '%';
}

export const IndexToColor: RGB[] = [
  { r: 255, g: 0, b: 0 },
  { r: 0, g: 255, b: 0 },
  { r: 0, g: 0, b: 255 },
  { r: 255, g: 0, b: 255 },
  { r: 255, g: 255, b: 0 },
  { r: 0, g: 255, b: 255 },
  { r: 0, g: 0, b: 0 },
];
export function indexToColor(index: number): RGB {
  if (index < IndexToColor.length) {
    return IndexToColor[index];
  } else {
    return IndexToColor[IndexToColor.length - 1];
  }
}

export interface RGB {
  r: number;
  g: number;
  b: number;
}
export interface RGBA {
  r: number;
  g: number;
  b: number;
  a: number;
}
export function rgba01ToVec401({ r, g, b, a }: RGBA): Vector4 {
  return { x: r, y: g, z: b, w: a };
}
export function rgba255ToVec401({ r, g, b, a }: RGBA): Vector4 {
  return { x: r / 255, y: g / 255, z: b / 255, w: a };
}
export function rgb255ToVec401({ r, g, b }: RGB): Vector4 {
  return { x: r / 255, y: g / 255, z: b / 255, w: 1 };
}
export function rgb01ToVec3({ r, g, b }: RGB): Vector3 {
  return { x: r, y: g, z: b };
}
export function rgb255ToVec3({ r, g, b }: RGB): Vector3 {
  return { x: r / 255, y: g / 255, z: b / 255 };
}
export function vec301ToRgba255({ x, y, z }: Vector3): RGBA {
  return { r: x * 255, g: y * 255, b: z * 255, a: 1 };
}
export function rgbToHex({ r, g, b }: RGB): string {
  return color({ r, g, b }).toString();
}
export function rgbFromHex(hex: string): RGB {
  return color(hex).rgb().object() as any as RGB;
}
export function randomRGB(): RGB {
  return { r: Math.random() * 255, g: Math.random() * 255, b: Math.random() * 255 };
}
export function mixRGB(a: RGB, b: RGB, p: number) {
  return { r: mix(a.r, b.r, p), g: mix(a.g, b.g, p), b: mix(a.b, b.b, p) };
}
export function mixRGBA(a: RGBA, b: RGBA, p: number) {
  return { r: mix(a.r, b.r, p), g: mix(a.g, b.g, p), b: mix(a.b, b.b, p), a: mix(a.a, b.a, p) };
}

export type EventListener<T extends Array<any>> = (...event: T) => void;
export class EventDispatcher<T extends Array<any>> {
  listeners: Array<EventListener<T>> = [];
  dispatch(...event: T) {
    for (const listener of this.listeners) {
      listener(...event);
    }
  }
  attach(listener: EventListener<T>) {
    this.listeners.push(listener);
  }
  detach(listener: EventListener<T>) {
    this.listeners.splice(this.listeners.indexOf(listener), 1);
  }
  transform<T2 extends Array<any>>(map: (...event: T) => T2) {
    const dispatcher = new EventDispatcher<T2>();
    this.attach((...event) => dispatcher.dispatch(...map(...event)));
    return dispatcher;
  }
}

export function visitJSON<T>(userdata: T, obj: any, visitor: (userdata: T, obj: any) => T): T {
  userdata = visitor(userdata, obj);
  if (Array.isArray(obj)) {
    for (const v of obj) {
      userdata = visitJSON(userdata, v, visitor);
    }
  } else if (_.isObject(obj)) {
    for (const k of Object.keys(obj)) {
      userdata = visitJSON(userdata, obj[k], visitor);
    }
  }
  return userdata;
}

export function mapJSON(obj: any, mapObj: (obj: any) => any): any {
  if (Array.isArray(obj)) {
    return obj.map(e => mapJSON(e, mapObj));
  } else if (_.isObject(obj)) {
    obj = mapObj(obj);
    return Object.keys(obj).reduce((p, k) => ({...p, [k]: mapJSON(obj[k], mapObj)}), {});
  } else {
    return obj;
  }
}

export function keysOfEnum(EnumType: any): string[] {
  return Object.keys(EnumType).filter(x => isNaN(parseInt(x, 10)))
}

export function umap<T, R>(value: T | undefined, ok: (value: T) => R, undef?: () => R): R | undefined {
  if (value) {
    return ok(value);
  } else {
    return undef?.();
  }
}

export function removeProps<T extends {}>(obj: T, props: Array<keyof T>): T {
  const res = {...(obj as any)};
  for (const prop of props) {
    delete res[prop];
  }
  return res;
}

export class RandomSeedable {
  constructor(seed?: string) {
    this.generator = arbit(seed);
  }
  generator: any;
  random(): number {
    return this.generator();
  }
}
export class SeedGenerator {
  cached: { [id: string]: number } = {};
  get(id: string) {
    if (this.cached[id] !== undefined) {
      return this.cached[id];
    } else {
      return this.cached[id] = new RandomSeedable(id).random();
    }
  }
}

export const Seeds = new SeedGenerator(); // Seeds from ids are always the same


export function wait(ms: number): Promise<void> {
  return new Promise(resolve => setTimeout(resolve, ms));
}

export type Gradient<T> = Array<{ time: number, value: T }>;
export function getGradientValue<T>(gradient: Gradient<T>, time: number, mixer: (a: T, b: T, p: number) => T) {
  for (let i=0; i < gradient.length - 1; i++) {
    const left = gradient[i];
    const right = gradient[i + 1];
    if (left.time <= time && right.time >= time) {
      const d = right.time - left.time;
      const p = (time - left.time) / d;
      return mixer(left.value, right.value, p);
    }
  }
  return mixer(gradient[0].value, gradient[0].value, 0);
  // throw new Error(`Time ${time} was outside of gradient: ${JSON.stringify(gradient)}`);
}

