export function capitalizeFirstLetter(str: string): string {
  return str.charAt(0).toUpperCase() + str.slice(1);
}
export function lowercaseFirstLetter(str: string): string {
  return str.charAt(0).toLowerCase() + str.slice(1);
}

export class Lazy<T> {
  constructor(private get: () => T) {
  }
  private cached?: { value: T };
  get value() {
    if (this.cached === undefined) {
      this.cached = { value: this.get() };
    }
    return this.cached.value;
  }
}

export function blobToUint8Array(blob: Blob) {
  return new Promise<Uint8Array>((resolve) => {
    const fileReader = new FileReader();
    fileReader.onload = (event) => {
      resolve(new Uint8Array(fileReader.result as ArrayBuffer));
    };
    fileReader.readAsArrayBuffer(blob);
  });
}

// Slow but effective... Turns anything into JSON
export function declassify(obj: any) {
  return JSON.parse(JSON.stringify(obj));
}

export function nanToZero(value: number) {
  if (Number.isNaN(value)) {
    return 0;
  } else {
    return value;
  }
}


// From: https://developer.mozilla.org/en-US/docs/Web/API/Window/navigator
export function getBrowser() {
  if (navigator.userAgent.indexOf("Firefox") > -1) {
    return "Firefox";
    // "Mozilla/5.0 (X11; Ubuntu; Linux x86_64; rv:61.0) Gecko/20100101 Firefox/61.0"
  } else if (navigator.userAgent.indexOf("SamsungBrowser") > -1) {
    return "SamsungInternet";
    // "Mozilla/5.0 (Linux; Android 9; SAMSUNG SM-G955F Build/PPR1.180610.011) AppleWebKit/537.36 (KHTML, like Gecko) SamsungBrowser/9.4 Chrome/67.0.3396.87 Mobile Safari/537.36
  } else if (navigator.userAgent.indexOf("Opera") > -1 || navigator.userAgent.indexOf("OPR") > -1) {
    return "Opera";
    // "Mozilla/5.0 (Macintosh; Intel Mac OS X 10_14_0) AppleWebKit/537.36 (KHTML, like Gecko) Chrome/70.0.3538.102 Safari/537.36 OPR/57.0.3098.106"
  } else if (navigator.userAgent.indexOf("Trident") > -1) {
    return "InternetExplorer";
    // "Mozilla/5.0 (Windows NT 10.0; WOW64; Trident/7.0; .NET4.0C; .NET4.0E; Zoom 3.6.0; wbx 1.0.0; rv:11.0) like Gecko"
  } else if (navigator.userAgent.indexOf("Edge") > -1) {
    return "Edge";
    // "Mozilla/5.0 (Windows NT 10.0; Win64; x64) AppleWebKit/537.36 (KHTML, like Gecko) Chrome/58.0.3029.110 Safari/537.36 Edge/16.16299"
  } else if (navigator.userAgent.indexOf("Chrome") > -1) {
    return "Chrome";
    // "Mozilla/5.0 (X11; Linux x86_64) AppleWebKit/537.36 (KHTML, like Gecko) Ubuntu Chromium/66.0.3359.181 Chrome/66.0.3359.181 Safari/537.36"
  } else if (navigator.userAgent.indexOf("Safari") > -1) {
    return "Safari";
    // "Mozilla/5.0 (iPhone; CPU iPhone OS 11_4 like Mac OS X) AppleWebKit/605.1.15 (KHTML, like Gecko) Version/11.0 Mobile/15E148 Safari/604.1 980x1306"
  } else {
    return "Unknown";
  }
}

