export function isNullOrUndefined<T>(v: T | undefined | null): v is undefined | null {
  return v === undefined || v === null;
}

// https://github.com/microsoft/TypeScript/issues/20707
export function notNullOrUndefined<T>(x: T | undefined | null): x is T {
  return x !== undefined && x !== null;
}
export function notFalsy<T>(x: T | undefined | null | '' | false): x is T {
  return !!x;
}

// // https://stackoverflow.com/questions/21797299/convert-base64-string-to-arraybuffer/21797381
export function base64ToArrayBuffer(base64: string) {
  var binary_string = window.atob(base64);
  var len = binary_string.length;
  var bytes = new Uint8Array(len);
  for (var i = 0; i < len; i++) {
      bytes[i] = binary_string.charCodeAt(i);
  }
  return bytes.buffer;
}
// https://stackoverflow.com/questions/9267899/arraybuffer-to-base64-encoded-string
export function arrayBufferToBase64(buffer: ArrayBuffer) {
  var binary = '';
  var bytes = new Uint8Array( buffer );
  var len = bytes.byteLength;
  for (var i = 0; i < len; i++) {
      binary += String.fromCharCode( bytes[ i ] );
  }
  return window.btoa( binary );
}

export class SimpleProfiler {
  private stack: Array<{ title: string, start: number, childrenTime: number }> = [];
  enter(title: string) {
    this.stack.push({ start: window.performance.now(), title, childrenTime: 0 });
    console.log('⏱ ' + this.stack.map(s => s.title).join('/') + '...');
  }
  exit() {
    const now = window.performance.now();
    const title = this.stack.map(s => s.title).join('/');
    const s = this.stack.pop()!;
    const total = now - s.start;
    if (this.stack.length > 0) {
      this.stack[this.stack.length - 1].childrenTime += total;
    }
    let childrenPerc = '';
    if (s.childrenTime !== 0) {
      childrenPerc = `${Math.round(10000 * s.childrenTime / total) / 100}% in children`;
    }
    console.log(`⏱ ${title} ${this.msToString(total)}ms ${childrenPerc}`);
  }
  private msToString(ms: number) {
    return (Math.round(ms * 100) / 100).toLocaleString('en');
  }
}