import * as React from 'react';
import './index.css';
import { useWorldDataSelect, WorldStateContext } from "../../../GameComponents/WorldDataConsumer";
import { ScrollableColumn, ItemsColumn } from "../../../Components/Layout";
import { CreaturePortrait } from '../../CreaturePortrait';
import { WonkyPanel, evenPadding } from '../../../Components/WonkyPanel';
import { ArenaInfo, ArenaInfosLimit } from '../../../GpuWorld/Tools/ArenaInfos';
import { MaxHitpoints } from '../../../GpuWorld/ECS/UnitComponents';
import { HomeHealthColor, AwayHealthColor } from '../../../GpuWorld/StateLayout';
import { interpolate } from '../../../Math/Math';
import { Button } from '../../../Components/Button';
import { createUnitModelsForArena } from '../../../GpuWorld/CreateUnitModel';

export function ArenaMinimaps() {
  const { layout, arenaInfos } = useWorldDataSelect('layout', 'arenaInfos');
  const [all, setAll] = React.useState(false);
  if (!layout || !arenaInfos) {
    return null;
  }
  const renderMax = all ? ArenaInfosLimit : 10;
  return <ScrollableColumn className="ArenaMinimaps">
    <ItemsColumn>
      {/* <AllArenasMinimap /> */}
      {new Array(Math.min(layout.arenas, renderMax)).fill(0).map((_, i) =>
        <ArenaMinimap key={i} arenaIndex={i} arenaInfo={arenaInfos[i]} />)}
      {layout.arenas > renderMax && <span><i>({layout.arenas - renderMax} arenas hidden)</i></span>}
      <Button flat={true} onClick={() => setAll(!all)}>Show {all ? 'less' : 'more'}</Button>
    </ItemsColumn>
  </ScrollableColumn>
}

