export function ArenaMinimap({ arenaIndex, arenaInfo }: { arenaIndex: number, arenaInfo: ArenaInfo }) {
  const worldState = React.useContext(WorldStateContext);
  const { renderArena } = useWorldDataSelect('renderArena');
  if (!worldState) {
    return null;
  }
  let title = `Arena #${arenaIndex}\nHome gold: ${arenaInfo.homeGold}\nAway gold: ${arenaInfo.awayGold}`;
  return <WonkyPanel shadowPx={0} fill={'rgba(0, 0, 0, 0.5)'} borderPx={evenPadding(5)} paddingPx={evenPadding(0)}
    strokeWidth={3} stroke={renderArena === arenaIndex ? '#fff' : 'transparent'}>
    <div
      className="ArenaMinimap"
      title={title}
      onClick={() => {
        worldState.setData({ renderArena: arenaIndex });
        createUnitModelsForArena(worldState, arenaIndex);
        worldState.ensureRendered();
      }}>
      {[...arenaInfo.homeUnits, ...arenaInfo.awayUnits].filter(x => x).map((unit, i) => <div
        key={i}
        className="ArenaMinimapUnit"
        style={{
          left: interpolate(unit.position.x, 50, 78, 0, 100) + '%',
          top: interpolate(unit.position.y, 50, 78, 100, 0) + '%',
        }}
        >
        <CreaturePortrait unit={unit.unit} />
      </div>)}
      <div className="ArenaMinimapHpsContainer">
        <div className="ArenaMinimapHps ArenaMinimapHpsHome">
          {arenaInfo.homeUnits.filter(x => x).map((unit, i) => <div key={i} className="ArenaMinimapHpBar">
            <div
              className="ArenaMinimapHp"
              style={{ width: (100 * unit.hitpoints / MaxHitpoints) + '%', backgroundColor: HomeHealthColor }} />
          </div>)}
        </div>
        <div className="ArenaMinimapHps ArenaMinimapHpsAway">
          {arenaInfo.awayUnits.filter(x => x).map((unit, i) => <div key={i} className="ArenaMinimapHpBar">
            <div
              className="ArenaMinimapHp"
              style={{ width: (100 * unit.hitpoints / MaxHitpoints) + '%', backgroundColor: AwayHealthColor }} />
          </div>)}
        </div>
      </div>
      <div className="ArenaMinimapHomeGold">{Math.round(arenaInfo.homeGold)}</div>
      {worldState.data.teams.away.train && <div className="ArenaMinimapAwayGold">{Math.round(arenaInfo.awayGold)}</div>}
    </div>
  </WonkyPanel>
}
