const SeasonEndTime = Seasons[CurrentSeason].end;
const SeasonEnded = SeasonEndTime && new Date() > SeasonEndTime;

function useSeasonEnded() {
  const [seasonEnded, setSeasonEnded] = React.useState(SeasonEnded);
  React.useEffect(() => {
    if (!SeasonEnded) {
      if (SeasonEndTime) {
        const dtime = SeasonEndTime.getTime() - Date.now();
        window.setTimeout(() => setSeasonEnded(true), dtime);
      }
    }
  }, []);
  return seasonEnded;
}

export function MainMenu({ loggedInUser, loggedInUserData }: { loggedInUser: firebase.User, loggedInUserData: DbUser }) {
  const userSettings = useFirebaseStreamDocument<DbUserSettings>(Db.userSettings, loggedInUser.uid);
  const userSettingsDoc = firebase.firestore().collection(Db.userSettings).doc(loggedInUser.uid);
  useUserSettingsAudioSync(userSettings);
  AudioPlayerInstance?.ensureWindStarted();
  const seasonEnded = useSeasonEnded();
  const appState = React.useContext(AppStateContext);

  useFirebaseStreamQueryTo({
    collection: Db.creatures,
    where: [['ownerId', '==', loggedInUser.uid], ['season', '==', CurrentSeason], ['deleted', '==', false]],
    orderBy: [{ field: 'created' }]
  }, snapshot => appState.update({ creatures: { status: 'loaded', values: snapshot.docs.map(d => ({ id: d.id, ...d.data() as DbCreature})) } }));

  useFirebaseStreamQueryTo({
    collection: Db.items,
    where: [['ownerId', '==', loggedInUser.uid], ['season', '==', CurrentSeason], ['deleted', '==', false]],
    orderBy: [{ field: 'created' }]
  }, snapshot => appState.update({ items: { status: 'loaded', values: snapshot.docs.map(d => ({ id: d.id, ...d.data() as DbItemInstance})) } }));

  const { creatures, items, gameInstance } = useAppStateSelect('creatures', 'items', 'gameInstance');

  const battleground = (userSettings.status === 'loaded' ? userSettings.value.battleground : null) ?? DbBattleground.Solo;
  const soloTeam = useFirebaseStreamDocument<DbTeam>(Db.teams, getTeamId(loggedInUser.uid, CurrentSeason, DbBattleground.Solo));
  const duoTeam = useFirebaseStreamDocument<DbTeam>(Db.teams, getTeamId(loggedInUser.uid, CurrentSeason, DbBattleground.Duo));
  const squadTeam = useFirebaseStreamDocument<DbTeam>(Db.teams, getTeamId(loggedInUser.uid, CurrentSeason, DbBattleground.Team));
  const [showReplays, setShowReplays] = React.useState(false);

  React.useEffect(() => { AudioPlayerInstance?.startMusic(); }, []);
  const dialog = useDialog();
  const isFullscreen = useIsFullscreen();
  const { hideUI } = useAppStateSelect('hideUI');
  const [hasEntered, setHasEntered] = React.useState(false);
  if (creatures.status !== 'loaded' || soloTeam.status === 'loading' || duoTeam.status === 'loading' || squadTeam.status === 'loading' || items.status === 'loading') {
    return <FirebaseLoading pending={[creatures, soloTeam, duoTeam, squadTeam, items]} />;
  }
  if (seasonEnded) {
    return <Column className="MainMenu">
      Season {CurrentSeason} has ended. Please stand by while the next season is starting.
    </Column>
  }
  const enter = async () => {
    if (userSettings.status === 'loaded' && userSettings.value.sound) {
      AudioPlayerInstance?.audioContext.resume();
    }
    AudioPlayerInstance?.playSoundEffect(SoundEffect.Gunshot);
    await wait(10);
    if (soloTeam.status === 'no-such-document') {
      await firebase.functions().httpsCallable('createTeam')({ battleground: DbBattleground.Solo });
    }
    if (duoTeam.status === 'no-such-document') {
      await firebase.functions().httpsCallable('createTeam')({ battleground: DbBattleground.Duo });
    }
    if (squadTeam.status === 'no-such-document') {
      await firebase.functions().httpsCallable('createTeam')({ battleground: DbBattleground.Team });
    }
    setHasEntered(true);
  }
  const toggleFullscreen = () => {
    if (isFullscreen) {
      exitFullscreen();
    } else {
      enterFullscreen();
    }
  }

  const duoEnabled = (soloTeam.status === 'loaded' && soloTeam.value.matches >= BattlesToUnlockDuo) || version.isDevelopment;
  const squadEnabled = (duoTeam.status === 'loaded' && duoTeam.value.matches >= BattlesToUnlockTeam) || version.isDevelopment;
  return <div className="MainMenuContainer">
    <Dialog state={dialog.current} />
    <WorldCanvas>
      <GameComponent
        loggedInUser={loggedInUser}
        loggedInUserData={loggedInUserData}
        />
    </WorldCanvas>
    <div className={`MainMenu ${gameInstance.type !== 'none' ? 'MainMenuPlaying' : ''}`}>
      {!hasEntered ? <div className="MainMenuEnter">
        <img src={LogoPng} alt="Logo" />
        <Button onPerform={enter} disabled={hasEntered} className="MainMenuEnterBattlegrounds" primary={true}>Enter</Button>
      </div> : <WonkyPanel
        className="MainMenuPanel"
        shadowPx={5}
        borderPx={evenPadding(10)}
        paddingPx={evenPadding(5)}
        fill={FloatingPanelColor}
        >
        <Row>
          <Column className="MainMenuLeft">
            <CreaturesBrowser />
            <Inventory />
            <MainMenuTraining />
          </Column>
          <Column className="MainMenuRight">
            <Row className="MainMenuLogo">
              <img src={LogoPng} alt="Logo" />
            </Row>
            <ItemsColumn>
              {/* <FontAwesomeButton icon="video" flat={true} onClick={() => setShowReplays(true)}>
                View replays
              </FontAwesomeButton> */}
              <ItemsRow>
                <Button flat={true}
                  toggled={battleground === DbBattleground.Solo}
                  title="1v1"
                  className={`MainMenuBattle${DbBattleground.Solo}`}
                  onClick={() => userSettingsDoc.update(partial<DbUserSettings>({ battleground: DbBattleground.Solo }))}>
                  Solo
                </Button>
                <Button flat={true}
                  toggled={battleground === DbBattleground.Duo}
                  disabled={!duoEnabled}
                  title={duoEnabled ? '2v2' : `You must play at least ${BattlesToUnlockDuo} solo battles to enter the duo battleground`}
                  className={`MainMenuBattle${DbBattleground.Duo}`}
                  onClick={() => userSettingsDoc.update(partial<DbUserSettings>({ battleground: DbBattleground.Duo }))}>
                  Duo
                </Button>
                <Button flat={true}
                  toggled={battleground === DbBattleground.Team}
                  disabled={!squadEnabled}
                  title={squadEnabled ? '4v4' : `You must play at least ${BattlesToUnlockTeam} duo battles to enter the team battleground`}
                  className={`MainMenuBattle${DbBattleground.Team}`}
                  onClick={() => userSettingsDoc.update(partial<DbUserSettings>({ battleground: DbBattleground.Team }))}>
                  Team
                </Button>
              </ItemsRow>
            </ItemsColumn>
            <MainMenuBattle
              team={battleground === DbBattleground.Solo ? soloTeam
                : battleground === DbBattleground.Duo ? duoTeam
                : squadTeam}
              battleground={battleground} />
          </Column>
        </Row>
      </WonkyPanel>}
    </div>

    <ItemsRow className="MainMenuTopRight">
      {version.backend !== 'production' && <span>DERK TEST SERVER</span>}
      <MaterialIconButton flat={true} onClick={toggleFullscreen} icon={isFullscreen ? 'fullscreen_exit' : 'fullscreen'} title="Fullscreen" purpose="Fullscreen" />
      <MaterialIconButton icon="web" flat={true} toggled={hideUI} title="Show/hide UI" onClick={() => appState.update({ hideUI: !hideUI })} />
      {userSettings.status === 'loaded' && <FontAwesomeButton icon={userSettings.value.sound  ? 'volume-up' : 'volume-off'} flat={true} title="Toggle audio" onClick={() => userSettingsDoc.update(partial<DbUserSettings>({ sound: !userSettings.value.sound }))} />}
      <UserSettingsButton loggedInUser={loggedInUser} />
      <a href="https://discord.gg/ZBRwc5b" target="_blank" rel="noopener noreferrer">
        <FontAwesomeButton icon="discord" iconStyle="brand" flat={true} title="Open discord" />
      </a>
      <a href="https://gym.derkgame.com" target="_blank" rel="noopener noreferrer">
        <FontAwesomeButton icon="python" iconStyle="brand" flat={true} title="Go to Derk's Gym: Python RL Environment" />
      </a>
      {version.deployment === 'production' ?
        <FontAwesomeButton icon="vial" flat={true} onPerform={async () => {
          const result = await firebase.functions().httpsCallable('getTestingLoginToken')();
          window.location.href = `https://test.derkgame.com/login/${result.data.token}`;
        }} title="Switch to test server" /> :
        <FontAwesomeButton icon="vial" toggled={true} flat={true}
          onClick={() => window.location.href = 'https://app.derkgame.com'}
          title="Switch to live server" />
      }
      {IsInElectron ?
        <FontAwesomeButton icon="times" flat={true}
            onPerform={async () => {
              if (await dialog.confirm('Exit game?')) {
                RoutingHistory.push('/exit');
              }
            }} title="Exit">Exit</FontAwesomeButton>
        : <FontAwesomeButton  icon="sign-out-alt" flat={true} className="Signout" title="Sign out"
          onPerform={async () => {
            if (await dialog.confirm('Sign out?')) {
              firebase.auth().signOut();
            }
          }} />
      }
    </ItemsRow>
    {showReplays && <Popup onClose={() => setShowReplays(false)}>
      <ReplaysBrowser onPlayReplay={replayId => {
        appState.update({ gameInstance: { type: 'replay', replayId } });
        setShowReplays(false);
      }} />
    </Popup>}
  </div>;
}

function MainMenuBattle({ team, battleground }: { team: FirebaseStreamDocumentResult<DbTeam>, battleground: DbBattleground }) {
  const { creatures = { status: 'loading' }, items = { status: 'loading' } } = useAppStateSelect('creatures', 'items');
  const maxTeamSize = teamSize(battleground);
  const battleTeamUrls: Array<string | null> = new Array(maxTeamSize).fill(null).map((_, i) => {
    if (team.status === 'loaded') {
      const id = team.value.creatureIds[i];
      if (id) {
        return stringifyDerkUrl({ collection: 'creatures', id });
      }
    }
    return null;
  });
  const battleTeam = unitsFromUrls(creatures, items, battleTeamUrls);
  const battleDisabled = battleTeam.filter(x => x).length === 0;
  const dialog = useDialog();
  const appState = React.useContext(AppStateContext);
  if (team.status !== 'loaded') {
    return <FirebaseLoading pending={[team]} />;
  }
  return <Column className="MainMenuBattle">
    <Dialog state={dialog.current} />
    <ScrollableColumn className="MainMenuBattleBattleGroup">
      <Leaderboard season={CurrentSeason} battleground={battleground} />
    </ScrollableColumn>
    {team.status === 'loaded' && <TeamStats team={team.value} />}
    <ItemsColumn className="MainMenuStartBattleContainer">
      <Button
        className={`MainMenuStartBattle ${battleDisabled ? '' : 'MainMenuStartBattleEnabled'}`}
        primary={true}
        disabled={battleDisabled}
        onPerform={async () => {
          if (AudioPlayerInstance) {
            AudioPlayerInstance.playSoundEffect(SoundEffect.HeavyDoorClose);
            AudioPlayerInstance.highpassMusic();
          }
          await wait(1500);
          appState.update({ gameInstance: { type: 'battle', battleground, homeTeam: battleTeam } });
          SessionRecorder.Current?.onClickBattle();
        }}>
          Battle
      </Button>
    </ItemsColumn>
    <Column className="MainMenuBattleTeam">
      <Row>
        {new Array(MaxTeamSize).fill(0).map((_, i) => <UnitSlot
          key={i}
          slotUrl={battleTeamUrls[i]}
          unit={battleTeam[i]}
          enabled={i < maxTeamSize}
          onSlotUrlChange={async newUrlString => {
            const newBattleTeamUrls = arrayReplaceIndex(battleTeamUrls.map(x => x === newUrlString ? null : x), i, newUrlString);
            const creatureIds = newBattleTeamUrls.map(urlString => {
              if (urlString) {
                const url = parseDerkUrl(urlString);
                if (url.collection === 'creatures') {
                  return url.id;
                }
              }
              return null;
            });
            if (creatureIds.filter(x => x).length > teamSize(battleground)) {
              await dialog.alert(`This team is full`);
              return;
            }
            console.log('Drop battle team', { newBattleTeamUrls, creatureIds });
            await firebase.firestore().collection(Db.teams).doc(team.value.id).update(partial<DbTeam>({
              activated: creatureIds.filter(x => x).length > 0,
              creatureIds
            }));
          }}
          />)}
      </Row>
      <Column className="MainMenuBattleTeamTitle">
        <small>Battle team</small>
      </Column>
    </Column>
  </Column>
}

function TeamStats({ team }: { team: DbTeam }) {
  return <Column>
    {team.matches < MinGamesRanked && <BlockSection>
      <i><small>Play {MinGamesRanked - team.matches} more games to show up in rankings</small></i>
    </BlockSection>}
    <TeamName team={team} />
  </Column>
}

export function runNextScheduledTraining(appState: Store<AppState>) {
  const { creatures, items, userSettings, scheduledTrainingIndex, scheduledEnd } = appState.state;
  if (userSettings.status !== 'loaded') {
    throw new Error(`User settings not loaded`);
  }
  const index = (scheduledTrainingIndex + 1) % userSettings.value.trainingSchedule.length;
  const trainingEpoch = Math.floor((scheduledTrainingIndex + 1) / userSettings.value.trainingSchedule.length);
  if (scheduledEnd >= 0) {
    if (trainingEpoch >= scheduledEnd) {
      return;
    }
  }
  const schedule = userSettings.value.trainingSchedule[index];
  const trainingSetup = userSettings.value.trainingSetups[schedule.setup];
  const homeTrainTeam = unitsFromUrls(creatures, items, trainingSetup.homeTeamUrls);
  const awayTrainTeam = unitsFromUrls(creatures, items, trainingSetup.awayTeamUrls);
  const maxTeamSize = teamSize(trainingSetup.battleground);
  appState.update({
    gameInstance: {
      type: 'train',
      battleground: trainingSetup.battleground,
      setupIndex: schedule.setup,
      homeTeam: homeTrainTeam.slice(0, maxTeamSize),
      awayTeam: awayTrainTeam.slice(0, maxTeamSize),
      trainHome: true,
      trainAway: false,
      arenas: 128,
      rounds: schedule.rounds,
      turbo: true
    },
    scheduledTrainingIndex: scheduledTrainingIndex + 1
  });
}

function TrainingSchedule({ onRun }: { onRun: () => void }) {
  const { userSettings, loggedInUserId, scheduledEnd } = useAppStateSelect('loggedInUserId', 'userSettings', 'scheduledEnd');
  const userSettingsDoc = firebase.firestore().collection(Db.userSettings).doc(loggedInUserId);
  const appState = React.useContext(AppStateContext);
  if (userSettings.status !== 'loaded') {
    return <FirebaseLoading pending={[userSettings]} />;
  }
  const scheduleOk = userSettings.value.trainingSchedule.length > 0 &&
    userSettings.value.trainingSchedule.reduce((p, x) => p && !!userSettings.value.trainingSetups[x.setup], true);
  return <Column>
    <h3>Scheduled training</h3>
    <table>
      <tbody>
        <tr>
          <td />
          <td>Rounds</td>
          <td />
        </tr>
        {userSettings.value.trainingSchedule.map((item, i) => <tr key={i}>
          <td>
            <EnumEditor
              value={item.setup}
              onChange={s => userSettingsDoc.update(partial<DbUserSettings>({
                trainingSchedule: arrayReplaceIndex(userSettings.value.trainingSchedule, i, {
                  ...item,
                  setup: s
                })
              }))}
              variants={userSettings.value.trainingSetups.map((s, l) => ({ displayName: s.name, value: l }))}
              />
          </td>
          <td>
            <NumberEditor
              value={item.rounds}
              onChange={s => userSettingsDoc.update(partial<DbUserSettings>({
                trainingSchedule: arrayReplaceIndex(userSettings.value.trainingSchedule, i, {
                  ...item,
                  rounds: s
                })
              }))}
              />
          </td>
          <td>
            <FontAwesomeButton icon="trash" flat={true} onClick={() => {
              userSettingsDoc.update(partial<DbUserSettings>({
                trainingSchedule: arrayRemoveIndex(userSettings.value.trainingSchedule, i)
              }))
            }} />
          </td>
        </tr>)}
        <tr>
          <td colSpan={2}>
            <FontAwesomeButton icon="plus" flat={true} onClick={() => {
              userSettingsDoc.update(partial<DbUserSettings>({
                trainingSchedule: [...userSettings.value.trainingSchedule, {
                  setup: 0,
                  rounds: 500
                }]
              }))
            }}>Add</FontAwesomeButton>
          </td>
        </tr>
      </tbody>
    </table>
    <Button primary={true} disabled={!scheduleOk} onClick={() => {
      appState.update({ scheduledTrainingIndex: -1 });
      runNextScheduledTraining(appState);
      onRun();
    }}>
      Run schedule
    </Button>
    {scheduledEnd < 0 ? <small><Button flat={true} onClick={() => appState.update({ scheduledEnd: 1 })}>
      Set fixed number of runs
    </Button></small> : <Column>
      <small>Max number of runs:</small>
      <NumberEditor value={scheduledEnd} onChange={v => appState.update({ scheduledEnd: v })} />
      <small><Button flat={true} onClick={() => appState.update({ scheduledEnd: -1 })}>
        Remove
      </Button></small>
    </Column>}
  </Column>
}

function MainMenuTraining() {
  const { userSettings, loggedInUserId } = useAppStateSelect('loggedInUserId', 'userSettings');
  const userSettingsDoc = firebase.firestore().collection(Db.userSettings).doc(loggedInUserId);
  const [showSchedule, setShowSchedule] = React.useState(false);
  const dialog = useDialog();
  if (userSettings.status !== 'loaded') {
    return <FirebaseLoading pending={[userSettings]} />;
  }

  const trainingSetup = userSettings.value.trainingSetups[userSettings.value.activeTrainingSetup];
  return <Column className="MainMenuTraining">
    <Dialog state={dialog.current} />
    <ItemsRow className="MainMenuTrainingSets">
      {userSettings.value.trainingSetups.map((ts, i) => <div key={i}>
        <small>
          <Button
            flat={true}
            toggled={userSettings.value.activeTrainingSetup === i}
            onClick={() => userSettingsDoc.update(partial<DbUserSettings>({ activeTrainingSetup: i }))}
            >{ts.name}</Button>
          <ActionMenu actions={[
            {
              title: 'Rename',
              onClick: async () => {
                const newName = await dialog.prompt('New name', ts.name);
                if (newName) {
                  userSettingsDoc.update(partial<DbUserSettings>({
                    trainingSetups: arrayReplaceIndex(userSettings.value.trainingSetups, i, {
                      ...ts,
                      name: newName
                    })
                  }))
                }
              }
            },
            {
              title: 'Duplicate',
              onClick: async () => {
                userSettingsDoc.update(partial<DbUserSettings>({
                  trainingSetups: [...userSettings.value.trainingSetups, {...trainingSetup, name: trainingSetup.name + ' copy'}],
                  activeTrainingSetup: userSettings.value.trainingSetups.length
                }));
              }
            },
            {
              title: 'Delete',
              onClick: async () => {
                if (await dialog.confirm('Delete?')) {
                  userSettingsDoc.update(partial<DbUserSettings>({
                    trainingSetups: arrayRemoveIndex(userSettings.value.trainingSetups, i),
                    activeTrainingSetup: userSettings.value.activeTrainingSetup === i ? 0 : userSettings.value.activeTrainingSetup
                  }));
                }
              }
            }
          ]} />
          </small>
      </div>)}
      <small><FontAwesomeButton
          flat={true}
          icon="plus-circle"
          onClick={() => userSettingsDoc.update(partial<DbUserSettings>({
            activeTrainingSetup: userSettings.value.trainingSetups.length,
            trainingSetups: [...userSettings.value.trainingSetups, {
              ...emptyTrainingSetup(),
              name: 'Training ' + userSettings.value.trainingSetups.length,
            }]
          }))}
          /></small>
        <FontAwesomeButton flat={true} icon="calendar-alt" onClick={() => setShowSchedule(true)} />
    </ItemsRow>
    {trainingSetup && <TrainingSetupEditor
      value={trainingSetup}
      onChange={async ts => {
        await userSettingsDoc.update(partial<DbUserSettings>({
          trainingSetups: arrayReplaceIndex(userSettings.value.trainingSetups, userSettings.value.activeTrainingSetup, ts)
        }));
      }}
      trainingSetupIndex={userSettings.value.activeTrainingSetup}
      />}
    {showSchedule && <Popup onClose={() => setShowSchedule(false)}>
      <TrainingSchedule onRun={() => setShowSchedule(false)} />
    </Popup>}
  </Column>
}

function TrainingSetupEditor({ value, onChange, trainingSetupIndex }: { value: TrainingSetup, onChange: (value: TrainingSetup) => void, trainingSetupIndex: number }) {
  const { creatures, items } = useAppStateSelect('creatures', 'items');
  const homeTrainTeam = unitsFromUrls(creatures, items, value.homeTeamUrls);
  const awayTrainTeam = unitsFromUrls(creatures, items, value.awayTeamUrls);
  const [arenaCount] = React.useState(128);
  const [rounds, ] = React.useState(window.localStorage.puppeteerRounds ? parseInt(window.localStorage.puppeteerRounds) : Number.POSITIVE_INFINITY);
  const dialog = useDialog();
  const appState = React.useContext(AppStateContext);
  const trainEnabled = (homeTrainTeam.filter(x => x && x.type === 'creature').length > 0 && value.trainHome) ||
    (awayTrainTeam.filter(x => x && x.type === 'creature').length > 0 && value.trainAway);
  const maxTeamSize = teamSize(value.battleground);

  return <ItemsRow className="MainMenuTrainingTeams">
    <Dialog state={dialog.current} />
    <Column className="MainMenuTrainingTeam MainMenuTrainingHome">
      <Row>
        {new Array(MaxTeamSize).fill(0).map((_, i) => <Column key={i} className="TrainingSetupEditorHomeUnit">
          <UnitSlot
            slotUrl={value.homeTeamUrls[i]}
            unit={homeTrainTeam[i]}
            enabled={i < maxTeamSize}
            onSlotUrlChange={newId => {
              onChange({
                ...value,
                homeTeamUrls: arrayReplaceIndex(value.homeTeamUrls.map(x => x === newId ? null : x), i, newId),
                awayTeamUrls: value.awayTeamUrls.map(x => x === newId ? null : x)
              });
            }}
            />
          </Column>)}
      </Row>
      <ItemsRow>
        <small>Trainees</small>
        <FontAwesomeButton
          icon="dumbbell"
          title={`Training is ${value.trainHome ? 'enabled' : 'disabled'} for this side`}
          flat={true}
          toggled={value.trainHome}
          onClick={() => onChange({...value, trainHome: !value.trainHome})}
          />
      </ItemsRow>
    </Column>
    <ItemsColumn>
      <EnumEditor
        value={value.battleground}
        onChange={bg => onChange({
          ...value,
          battleground: bg
        })}
        variants={[
          { displayName: 'Solo', value: DbBattleground.Solo, className: 'Battleground' + DbBattleground.Solo },
          { displayName: 'Duo', value: DbBattleground.Duo, className: 'Battleground' + DbBattleground.Duo },
          { displayName: 'Team', value: DbBattleground.Team, className: 'Battleground' + DbBattleground.Team },
        ]}
        />
      <Button
        className={`MainMenuTrain ${trainEnabled ? 'MainMenuTrainEnabled' : ''}`}
        primary={true}
        onPerform={async () => {
          if (trainEnabled || await dialog.confirm(`Nothing will be trained. Continue anyway?`)) {
            if (AudioPlayerInstance) {
              AudioPlayerInstance.playSoundEffect(SoundEffect.HeavyDoorClose);
              AudioPlayerInstance.highpassMusic();
            }
            await wait(1500);
            appState.update({
              gameInstance: {
                type: 'train',
                battleground: value.battleground,
                setupIndex: trainingSetupIndex,
                homeTeam: homeTrainTeam.slice(0, maxTeamSize),
                awayTeam: awayTrainTeam.slice(0, maxTeamSize),
                trainHome: value.trainHome,
                trainAway: value.trainAway,
                arenas: arenaCount,
                rounds,
              }
            });
            SessionRecorder.Current?.onClickTrain();
          }
        }}
        >
        Train
      </Button>
      <ItemsRow>
        <FontAwesomeButton
          icon="exchange-alt"
          flat={true}
          title="Swap training teams"
          onClick={() => {
            onChange({
              ...value,
              homeTeamUrls: value.awayTeamUrls.slice(0, maxTeamSize),
              awayTeamUrls: value.homeTeamUrls.slice(0, maxTeamSize)
            });
          }}
          />
      </ItemsRow>
    </ItemsColumn>
    <Column className="MainMenuTrainingTeam MainMenuTrainingAway">
      <Row>
        {new Array(MaxTeamSize).fill(0).map((_, i) => <UnitSlot
          key={i}
          slotUrl={value.awayTeamUrls[i]}
          unit={awayTrainTeam[i]}
          enabled={i < maxTeamSize}
          onSlotUrlChange={newId => {
            onChange({
              ...value,
              homeTeamUrls: value.homeTeamUrls.map(x => x === newId ? null : x),
              awayTeamUrls: arrayReplaceIndex(value.awayTeamUrls.map(x => x === newId ? null : x), i, newId)
            });
          }}
          />)}
      </Row>
      <ItemsRow>
        <small>Opponents</small>
        <FontAwesomeButton
          icon="dumbbell"
          title={`Training is ${value.trainAway ? 'enabled' : 'disabled'} for this side`}
          flat={true}
          toggled={value.trainAway}
          onClick={() => onChange({...value, trainAway: !value.trainAway})}
          />
      </ItemsRow>
    </Column>
  </ItemsRow>
}

function UnitSlot({ slotUrl, unit, enabled, onSlotUrlChange }: { slotUrl: string | null, unit: UnitDef | undefined, enabled: boolean, onSlotUrlChange: (url: string | null) => void }) {
  if (!enabled) {
    return <WonkyPanel shadowPx={2} className="TrainTeamSlot TrainTeamSlotDisabled">
      <div className="TrainTeamSlotEmpty" />
    </WonkyPanel>
  }
  return <DerkUrlDropTarget
    className="TeamSlot"
    accepts={url => {
      if (url.collection === 'creatures') {
        return true;
      } else if (url.collection === 'items') {
        return url.itemType === 'dummy-unit';
      }
      return false;
    }}
    onDrop={(url, urlString) => {
      AudioPlayerInstance?.playSoundEffect(SoundEffect.StoneTick);
      onSlotUrlChange(urlString);
    }}
  >
    <WonkyPanel shadowPx={2} className="TrainTeamSlot">
      {slotUrl && unit ? <DraggableDerkUrl
          url={slotUrl}
          onDragStart={() => {
            setTimeout(() => onSlotUrlChange(null), 10);
          }}
          >
        <CreaturePortrait unit={unit} />
      </DraggableDerkUrl> : <div className="TrainTeamSlotEmpty" />}
    </WonkyPanel>
  </DerkUrlDropTarget>
}

