import { EntityComponentSystem } from "../../GpuUtil/ECS";
import { GpuBufferModule, GpuComponentModule, DualComponentModule } from "../../GpuUtil/ECS/ComponentModules";
import { EntityRef } from "../StateData";

export const CommonBuffer = new GpuBufferModule({
  name: 'CommonBuffer',
  format: 'float'
});

export const NameComponent = EntityComponentSystem.registerComponent<{ value: string }>({
  name: 'Name',
  type: 'obj',
});

export const Exists = new GpuComponentModule('Exists', CommonBuffer.buffer, 'bool');

export const ArenaInstance = new DualComponentModule('ArenaInstance', CommonBuffer, 'int');
export const ArenaMemberIndex = new DualComponentModule('ArenaMemberIndex', CommonBuffer, 'int');

export const ArenaBuffer = new GpuBufferModule({
  name: 'ArenaBuffer',
  format: 'float'
});
export const Arena = new DualComponentModule('Arena', ArenaBuffer, 'int');

export const VisibleComponent = EntityComponentSystem.registerComponent<boolean>({
  name: 'Visible',
  type: 'obj',
});

export const VisibilityFromComponent = EntityComponentSystem.registerComponent<{ value: EntityRef }>({
  name: 'VisibilityFrom',
  type: 'obj',
});
