import { Texture, DoubleBufferedTexture } from "./Texture";
import { Sampler } from "./Program";
import { Image } from "./Image";
import * as Vector2 from '../Math/Vector2';
import * as Vector3 from '../Math/Vector3';
import * as Vector4 from '../Math/Vector4';
import * as Mat4 from '../Math/Mat4';

export type TextureRef = Texture | DoubleBufferedTexture | Sampler | Image;
export type GLSLBaseType = 'float' | 'int' | 'uint' | 'bool' |
  'vec2' | 'ivec2' | 'vec3' | 'ivec3' | 'vec4' | 'ivec4' | 'mat4' |
  'sampler2D' | 'isampler2D' | 'usampler2D' | 'sampler2DArray' | 'isampler2DArray' | 'usampler2DArray';
export type GLSLArrayType = 'float[]' | 'int[]' | 'uint[]' | 'bool[]' |
'vec2[]' | 'ivec2[]' | 'vec3[]' | 'ivec3[]' | 'vec4[]' | 'ivec4[]' | 'mat4[]' |
'sampler2D[]' | 'isampler2D[]' | 'usampler2D[]' | 'sampler2DArray[]' | 'isampler2DArray[]' | 'usampler2DArray[]';
export type GLSLType = GLSLBaseType | GLSLArrayType;
export function toGLSLArrayType(baseType: GLSLBaseType): GLSLArrayType {
  return (baseType + '[]') as GLSLArrayType;
}
export type GLSLTsType = number | boolean | number[] | boolean[] |
  Float32Array | Int32Array |
  Vector2.Vector2 | Vector3.Vector3 | Vector4.Vector4 | Mat4.Mat4 |
  Vector2.Vector2[] | Vector3.Vector3[] | Vector4.Vector4[] | Mat4.Mat4[] |
  TextureRef | TextureRef[];
export interface GLSLTypeMappings {
  'float': number,
  'int': number,
  'uint': number,
  'bool': boolean,

  'float[]': number[] | Float32Array,
  'int[]': number[] | Int32Array,
  'uint[]': number[],
  'bool[]': boolean[],

  'vec2': Vector2.Vector2,
  'ivec2': Vector2.Vector2,
  'vec3': Vector3.Vector3,
  'ivec3': Vector3.Vector3,
  'vec4': Vector4.Vector4,
  'ivec4': Vector4.Vector4,
  'mat4': Mat4.Mat4,

  'vec2[]': Vector2.Vector2[],
  'ivec2[]': Vector2.Vector2[],
  'vec3[]': Vector3.Vector3[],
  'ivec3[]': Vector3.Vector3[],
  'vec4[]': Vector4.Vector4[],
  'ivec4[]': Vector4.Vector4[],
  'mat4[]': Mat4.Mat4[],

  'sampler2D': TextureRef,
  'isampler2D': TextureRef,
  'usampler2D': TextureRef,
  'sampler2DArray': TextureRef,
  'isampler2DArray': TextureRef,
  'usampler2DArray': TextureRef,

  'sampler2D[]': TextureRef[],
  'isampler2D[]': TextureRef[],
  'usampler2D[]': TextureRef[],
  'sampler2DArray[]': TextureRef[],
  'isampler2DArray[]': TextureRef[],
  'usampler2DArray[]': TextureRef[],
}

export function glslTypeArraySize(type: string) {
  const ss = type.indexOf('[');
  if (ss === -1) {
    return undefined;
  }
  return Number.parseInt(type.slice(ss + 1, type.length - 1));
}