import { GpuComponentModule } from "../../GpuUtil/ECS/ComponentModules";
import { UnitBuffer } from "../ECS/UnitComponents";
import { GlState } from "../../GpuUtil/GlState";
import { ArchetypesQuery } from "../../GpuUtil/ECS/Query";
import { shaderModule, funcDef } from "../../GpuUtil/ShaderModule";
import { GpuWorldState } from "../State";

export const Crippleable = new GpuComponentModule('Crippleable', UnitBuffer, 'int');

export class UpdateCrippleBuff {
  constructor(private gls: GlState) {}

  update = new ArchetypesQuery([Crippleable.component])
    .mapGpu(this.gls, Crippleable.component, shaderModule([], {
      map: funcDef('int', [['ivec2', 'entityCell'], ['int', 'value']], `
        return max(0, value - 1);
      `)
    }));

  run(state: GpuWorldState) {
    this.update.run(state);
  }
}
