class UpdateAbilities {
  constructor(private gls: GlState) {}
  private abilities = [
    new UpdateTrombone(this.gls),
    new UpdateTalons(this.gls),
    new UpdateCleavers(this.gls),
    new UpdateBloodClaws(this.gls),
    new UpdateCripplers(this.gls),
    new UpdateSleepDart(this.gls),
    new UpdateHealingGland(this.gls),
    new UpdateVampireGland(this.gls),
    new UpdateLeap(this.gls),
    new UpdateIronBubblegum(this.gls),
    new UpdateHeliumBubblegum(this.gls),
    new UpdatePistol(this.gls),
    new UpdateBlaster(this.gls),
    new UpdateMagnum(this.gls)
  ]
  run(state: GpuWorldState) {
    for (const ability of this.abilities) {
      ability.run(state);
    }
  }
}
