import { GlState } from "../../GpuUtil/GlState";
import { Focuser } from "../ECS/UnitComponents";
import { TransformHelpers } from "../ECS/Transform";
import { QuadLineSM } from "./Util";
import { GpuWorldState } from "../State";
import { EntitiesQuery } from "../../GpuUtil/ECS/Query";
import { VisibleComponent } from "../ECS/Components";
import { shaderModule, uniformDef, funcDef, outDef } from "../../GpuUtil/ShaderModule";
import { BackAttachment, HeadAttachment } from "../ECS/UnitComponents";
import { visibleCacheKeys } from "../ECS/Visibility";

export class GazeRenderer {
  constructor(private gls: GlState) {}

  query = new EntitiesQuery([Focuser.component])
    .filterValue(VisibleComponent, v => !!v, visibleCacheKeys)
    .render(this.gls, {
      mesh: 'quad',
      vertexShader: shaderModule([QuadLineSM, Focuser.sm, TransformHelpers,
        HeadAttachment.sm, BackAttachment.sm, HeadAttachment.hasComponentSM, BackAttachment.hasComponentSM], {
        viewProjection: uniformDef('mat4', d => d.derived.cameraProjectionView),
        shader: funcDef('void', [['ivec2', 'entityCell']], `
          ivec2 focus = ${Focuser.sm.value}(entityCell);
          if (focus.x < 0) {
            gl_Position = vec4(-1);
            return;
          }
          ivec2 head = ${HeadAttachment.hasComponentSM.value}(entityCell) ? ${HeadAttachment.sm.value}(entityCell) : entityCell;
          ivec2 back = ${BackAttachment.hasComponentSM.value}(focus) ? ${BackAttachment.sm.value}(focus) : focus;
          vec3 position = ${QuadLineSM.fromLineWithWidths}(
            ${TransformHelpers.worldPosition}(head),
            0.2,
            ${TransformHelpers.worldPosition}(back),
            0.0
          );
          gl_Position = Self.viewProjection * vec4(position, 1);
        `)
      }),
      fragmentShader: shaderModule([], {
        outColor: outDef('vec4'),
        main: funcDef('void', [], `
          outColor = vec4(1);
        `)
      })
    });

  run(state: GpuWorldState) {
    if (state.data.renderGazes) {
      this.query.run(state);
    }
  }
}
