import { Focuser, MaxFocusDistance, Stuck } from "../ECS/UnitComponents";
import { Position, RotationZ } from "../ECS/Transform";
import { Hitpoints, Team, MaxHitpoints, WorthPoints } from "../ECS/UnitComponents";
import { GlState } from "../../GpuUtil/GlState";
import { ArchetypesQuery } from "../../GpuUtil/ECS/Query";
import { shaderModule, funcDef, uniformDef } from "../../GpuUtil/ShaderModule";
import { GpuWorldState } from "../State";
import { glslFloat } from "../../GpuUtil";
import { SpatialSM, MathConstsSM, WithoutNaNInfSM } from "../../GpuUtil/SM2Utils";
import { Senses, SensesSlices, GymExtraSenses, GymExtraSensesSlices } from "./Components";
import { Dazeable } from "../Buffs/Daze";
import { GroundHeightSM } from "../ECS/Ground";
import { Crippleable } from "../Buffs/Cripple";
import { Teams } from "../StateLayout";
import { UnitsInArena } from "../ECS/ArenaStats";
import { ArenaInstance } from "../ECS/Components";
import { Casting, UnitAbilities, Abilities } from "../Abilities/Abilities";
import { UnitFocusables } from "./UnitFocusables";
import { Sense } from "./SensesDef";
import { keysOfEnum } from "../../Util";

export class UpdateSenses {
  constructor(private gls: GlState) {}

  private get(sense: Sense) {
    return `values[${Math.floor(sense / 4)}][${sense % 4}]`;
  }
  query = new ArchetypesQuery([Senses.component])
    .updateGpu(this.gls, [Senses.component], shaderModule([
      Focuser.sm,
      Hitpoints.sm,
      Casting.Cooldowns.sm,
      Senses.writer,
      Position.sm,
      SpatialSM,
      Team.sm,
      RotationZ.sm,
      Dazeable.sm,
      Crippleable.sm,
      GroundHeightSM,
      MathConstsSM,
      WorthPoints.sm,
      UnitsInArena,
      ArenaInstance.sm,
      Stuck.sm,
      UnitFocusables,
      WithoutNaNInfSM
    ], {

      mapCenter: uniformDef('vec2', d => ({ x: d.data.worldSize.width / 2, y: d.data.worldSize.width / 2 })),
      update: funcDef('void', [['ivec2', 'entityCell']], `
        ivec2 focus = ${Focuser.sm.value}(entityCell);
        vec3 position = ${Position.sm.value}(entityCell);
        float rotation = ${RotationZ.sm.value}(entityCell);
        vec2 forward = ${SpatialSM.forward}(rotation);
        int selfTeam = ${Team.sm.value}(entityCell);

        vec4 values[${SensesSlices}];
        ${this.get(Sense.Hitpoints)} = clamp(${Hitpoints.sm.value}(entityCell) / ${glslFloat(MaxHitpoints)}, 0.0, 1.0);
        ${this.get(Sense.Ability0Ready)} = ${Casting.Cooldowns.sm.value}(entityCell, 0) == 0 ? 1. : 0.;
        ${this.get(Sense.Ability1Ready)} = ${Casting.Cooldowns.sm.value}(entityCell, 1) == 0 ? 1. : 0.;
        ${this.get(Sense.Ability2Ready)} = ${Casting.Cooldowns.sm.value}(entityCell, 2) == 0 ? 1. : 0.;
        bool hasFocus = focus.x >= 0;
        ${this.get(Sense.HasFocus)} = float(hasFocus);

        int arenaIndex = ${ArenaInstance.sm.value}(entityCell);
        ivec3[] brainFocusables = ${UnitFocusables.get}(entityCell, selfTeam, arenaIndex);
        int distanceI = ${Sense.FriendStatueDistance};
        int angleI = ${Sense.FriendStatueAngle};
        for (int i=0; i < ${UnitFocusables.size}; i++) {
          ivec2 unit = brainFocusables[i].xy;
          vec3 unitPosition = ${Position.sm.value}(unit);
          bool isAlive = ${Hitpoints.sm.value}(unit) > 0.;
          if (isAlive) {
            values[distanceI / 4][distanceI % 4] = clamp(distance(position, unitPosition) / ${glslFloat(MaxFocusDistance)}, 0.0, 1.0);
            values[angleI / 4][angleI % 4] = ${WithoutNaNInfSM.withoutNaNInf}(
              ${SpatialSM.angleToPosition}(position.xy, forward, unitPosition.xy) / ${MathConstsSM.PI}
            );
          } else {
            values[distanceI / 4][distanceI % 4] = 2.;
            values[angleI / 4][angleI % 4] = 0.;
          }
          distanceI += 2;
          angleI += 2;
        }


        if (hasFocus) {
          vec3 focusPosition = ${Position.sm.value}(focus);
          float focusRotation = ${RotationZ.sm.value}(focus);
          vec2 focusForward = ${SpatialSM.forward}(focusRotation);

          ${this.get(Sense.FocusRelativeRotation)} = ${WithoutNaNInfSM.withoutNaNInf}(
            ${SpatialSM.relativeRotationTo}(forward, focusForward) / ${MathConstsSM.PI}
          );
          ${this.get(Sense.FocusFacingUs)} = ${WithoutNaNInfSM.withoutNaNInf}(
            ${SpatialSM.facingUs}(position.xy, focusPosition.xy, focusForward)
          );

          ivec2 focusOfFocus = ${Focuser.sm.value}(focus);
          ${this.get(Sense.FocusFocusingBack)} = (focusOfFocus == entityCell ? 1.0 : 0.0);

          int focusTeam = ${Team.sm.value}(focus);
          ${this.get(Sense.FocusHitpoints)} = clamp(${Hitpoints.sm.value}(focus) / ${glslFloat(MaxHitpoints)}, 0.0, 1.0);
          ${this.get(Sense.FocusDazed)} = ${Dazeable.sm.value}(focus) > 0 ? 1.0 : 0.0;
          ${this.get(Sense.FocusCrippled)} = ${Crippleable.sm.value}(focus) > 0 ? 1.0 : 0.0;
        }
        float heightFront2 = ${GroundHeightSM.atPosition}(position.xy + forward * 1.);
        ${this.get(Sense.HeightFront1)} = ${WithoutNaNInfSM.withoutNaNInf}(
          tanh(heightFront2 - position.z)
        );
        float heightFront7 = ${GroundHeightSM.atPosition}(position.xy + forward * 5.);
        ${this.get(Sense.HeightFront5)} = ${WithoutNaNInfSM.withoutNaNInf}(
          tanh(heightFront7 - position.z)
        );
        float heightBack2 = ${GroundHeightSM.atPosition}(position.xy - forward * 2.);
        ${this.get(Sense.HeightBack2)} = ${WithoutNaNInfSM.withoutNaNInf}(
          tanh(heightBack2 - position.z)
        );

        vec2 mapUp = selfTeam == ${Teams.HomeTeam} ? normalize(vec2(1, 1)) : -normalize(vec2(1, 1));
        vec2 mapRight = selfTeam == ${Teams.HomeTeam} ? normalize(vec2(1, -1)) : normalize(vec2(-1, 1));
        vec2 positionD = position.xy - mapCenter;
        ${this.get(Sense.PositionUpDown)} = dot(positionD, mapUp) / length(mapCenter);
        ${this.get(Sense.PositionLeftRight)} = dot(positionD, mapRight) / length(mapCenter);

        ${this.get(Sense.Stuck)} = ${Stuck.sm.value}(entityCell) ? 1. : 0.;

        ${Senses.writer.write}(values);
      `)
    }));

  run(state: GpuWorldState) {
    this.query.run(state);
  }
}

export class UpdateGymExtraSenses {
  constructor(private gls: GlState) {}

  query = new ArchetypesQuery([GymExtraSenses.component])
    .updateGpu(this.gls, [GymExtraSenses.component], shaderModule([
      Focuser.sm,
      GymExtraSenses.writer,
      UnitAbilities.sm
    ], {

      update: funcDef('void', [['ivec2', 'entityCell']], `
        ivec2 focus = ${Focuser.sm.value}(entityCell);

        int ability0 = ${UnitAbilities.sm.value}(entityCell, 0);
        int ability1 = ${UnitAbilities.sm.value}(entityCell, 1);
        int ability2 = ${UnitAbilities.sm.value}(entityCell, 2);

        vec4 values[${GymExtraSensesSlices}];
        int i=0;
        ${keysOfEnum(Abilities).slice(1).map(ability => `
        values[i / 4][i % 4] = float(ability0 == ${Abilities[ability]} ||
          ability1 == ${Abilities[ability]} ||
          ability2 == ${Abilities[ability]});
        i++;
        `).join('\n')}

        if (focus.x >= 0) {
          int focusAbility0 = ${UnitAbilities.sm.value}(focus, 0);
          int focusAbility1 = ${UnitAbilities.sm.value}(focus, 1);
          int focusAbility2 = ${UnitAbilities.sm.value}(focus, 2);

          ${keysOfEnum(Abilities).slice(1).map(ability => `
          values[i / 4][i % 4] = float(focusAbility0 == ${Abilities[ability]} ||
            focusAbility1 == ${Abilities[ability]} ||
            focusAbility2 == ${Abilities[ability]});
          i++;
          `).join('\n')}
        }

        ${GymExtraSenses.writer.write}(values);
      `)
    }));

  run(state: GpuWorldState) {
    if (state.data.gameInstance.type === 'gym') {
      this.query.run(state);
    }
  }
}
