import * as React from "react";

// tslint:disable-next-line:no-var-requires
let ResizeObserver = require('resize-observer-polyfill');
if (ResizeObserver.default) {
  ResizeObserver = ResizeObserver.default;
}

export class MeasureSize {
  static resizeObserver: any = new ResizeObserver((entries: any, observer: any) => {
    // Wrap in requestAnimationFrame to avoid "ResizeObserver loop limit exceeded". See: https://stackoverflow.com/questions/49384120/resizeobserver-loop-limit-exceeded
    requestAnimationFrame(() => {
      for (const entry of entries) {
        entry.target.__measureSizeOnSize(entry.target.getBoundingClientRect());
      }
    });
  });

  constructor(public onSize: (rect: ClientRect) => void) {
  }

  ref: Element | null = null;
  public handleRef = (ref: Element | null) => {
    if (this.ref) {
      MeasureSize.resizeObserver.unobserve(this.ref);
    }
    if (ref) {
      (ref as any).__measureSizeOnSize = this.handleOnSize;
      MeasureSize.resizeObserver.observe(ref);
    }
    this.ref = ref;
  }
  public handleUnmount() {
    if (this.ref) {
      MeasureSize.resizeObserver.unobserve(this.ref);
    }
  }
  private handleOnSize = (rect: ClientRect) => {
    this.onSize(rect);
  }
}

export class MeasuredDiv extends React.Component<React.HTMLProps<HTMLDivElement> & { onSize: (rect: ClientRect) => void }> {
  measure = new MeasureSize(this.props.onSize);
  componentDidUpdate() {
    this.measure.onSize = this.props.onSize;
  }
  componentWillUnmount() {
    this.measure.handleUnmount();
  }
  render() {
    const { onSize, children, ...rest } = this.props;
    return <div {...rest} ref={this.measure.handleRef}>
      {children}
    </div>
  }
}