


import { GpuWorldState } from "../State";
import { GlState, GL } from "../../GpuUtil/GlState";
import { ArchetypesQuery, EntitiesQuery } from "../../GpuUtil/ECS/Query";
import { GpuComponentModule } from "../../GpuUtil/ECS/ComponentModules";
import { shaderModule, funcDef, inDef, outDef, uniformDef } from "../../GpuUtil/ShaderModule";
import { LocalToWorld } from "../ECS/Transform";
import { CullFace } from "../Renderer/ModelRenderer";
import { MatrixSM } from "../../GpuUtil/SM2Utils";
import { Vertex } from "../../GpuUtil/Mesh";
import { UnitBuffer, Velocity } from "../ECS/UnitComponents";
import { VisibleComponent } from "../ECS/Components";
import { visibleCacheKeys } from "../ECS/Visibility";

export const IronBubbable = new GpuComponentModule<'float'>('IronBubbable', UnitBuffer.buffer, 'int');
export const HeliumBubbable = new GpuComponentModule<'float'>('HeliumBubbable', UnitBuffer.buffer, 'int');

export class UpdateIronBubbable {
  constructor(private gls: GlState) {}

  private update = new ArchetypesQuery([IronBubbable.component])
    .mapGpu(this.gls, IronBubbable.component, shaderModule([], {
      map: funcDef('int', [['ivec2', 'entityCell'], ['int', 'value']], `
        return max(0, value - 1);
      `)
    }));

  run(state: GpuWorldState) {
    this.update.run(state);
  }
}

export class UpdateHeliumBubbable {
  constructor(private gls: GlState) {}

  private update = new ArchetypesQuery([HeliumBubbable.component])
    .mapGpu(this.gls, HeliumBubbable.component, shaderModule([], {
      map: funcDef('int', [['ivec2', 'entityCell'], ['int', 'value']], `
        return max(0, value - 1);
      `)
    }));
  private velocity = new ArchetypesQuery([HeliumBubbable.component, Velocity.component])
    .mapGpu(this.gls, Velocity.component, shaderModule([HeliumBubbable.sm], {
      map: funcDef('vec3', [['ivec2', 'entityCell'], ['vec3', 'value']], `
        if (${HeliumBubbable.sm.value}(entityCell) > 0) {
          return vec3(0, 0, 1.0);
        } else {
          return value;
        }
      `)
    }))

  run(state: GpuWorldState) {
    this.update.run(state);
    this.velocity.run(state);
  }
}

export const BubbleShader = {
  vertex: shaderModule([Vertex], {
    viewProjection: uniformDef('mat4', d => d.derived.cameraProjectionView),
    normal: outDef('vec3'),
    update: funcDef('void', [['mat4', 'worldTransform']], `
      mat4 invTranspWorldTransform = transpose(inverse(worldTransform));
      normal = normalize((invTranspWorldTransform * vec4(${Vertex.normal}, 1)).xyz);
      normal = (viewProjection * vec4(normal, 0)).xyz;

      gl_Position = viewProjection * worldTransform * vec4(${Vertex.position}, 1);
    `)
  }),
}

class BubbleRenderer {
  constructor(private gls: GlState, private buff: GpuComponentModule<'float'>, private color: string) {}

  private query = new EntitiesQuery([this.buff.component])
    .filterValue(VisibleComponent, v => !!v, visibleCacheKeys)
    .render(this.gls, {
      mesh: 'sphere',
      vertexShader: shaderModule([LocalToWorld.sm, MatrixSM, this.buff.sm, BubbleShader.vertex], {
        shader: funcDef('void', [['ivec2', 'entityCell']], `
          if (${this.buff.sm.value}(entityCell) <= 0) {
            gl_Position = vec4(-1);
            return;
          }

          mat4 worldTransform = ${MatrixSM.translate}(vec3(0, 0, 1)) *
            ${LocalToWorld.sm.value}(entityCell) *
            ${MatrixSM.scale}(vec3(1.5));

          ${BubbleShader.vertex.update}(worldTransform);
        `)
      }),
      fragmentShader: shaderModule([], {
        outColor: outDef('vec4'),
        normal: inDef('vec3'),
        main: funcDef('void', [], `
          if (normal.z < -0.3) {
            discard;
          }
          outColor = vec4(${this.color}, 1);
        `)
      })
    });

  run(state: GpuWorldState) {
    this.gls.enable(GL.CULL_FACE);
    this.gls.cullFace(CullFace.Back);
    this.query.run(state);
  }
}

export const IronBubbleColor = `vec3(0.5)`;
export const HeliumBubbleColor = `vec3(1.0, 0.75, 1.0)`;

export class BubblesRenderer {
  constructor(private gls: GlState) {}

  bubbles = [
    new BubbleRenderer(this.gls, IronBubbable, IronBubbleColor),
    new BubbleRenderer(this.gls, HeliumBubbable, HeliumBubbleColor),
  ]

  run(state: GpuWorldState) {
    for (const b of this.bubbles) {
      b.run(state);
    }
  }
}
