import * as React from 'react';
import './index.css';

interface ProgressBarProps {
  children?: any;
  progress: number;
  color?: string;
  className?: string;
  title?: string;
}

export function ProgressBar({ progress, color = '#757415', className, title, children }: ProgressBarProps) {
  return <div className={`ProgressBar ${className || ''}`} title={title}>
    <div className="ProgressBarProgress" style={{ width: Math.max(0, Math.round(100 * progress)) + '%', background: color }} />
    <div className="ProgressBarContent">
      {children}
    </div>
  </div>
}
