


import { GpuWorldState } from "../State";
import { GlState } from "../../GpuUtil/GlState";
import { UnitBuffer } from "../ECS/UnitComponents";
import { GpuComponentModule } from "../../GpuUtil/ECS/ComponentModules";
import { ArchetypesQuery, EntitiesQuery } from "../../GpuUtil/ECS/Query";
import { shaderModule, funcDef, uniformDef, outDef, inDef } from "../../GpuUtil/ShaderModule";
import { loadImage } from "../../GpuUtil";
import { Texture } from "../../GpuUtil/Texture";
import { VisibleComponent } from "../ECS/Components";
import { Vertex } from "../../GpuUtil/Mesh";
import { TransformHelpers } from "../ECS/Transform";
import { HeadAttachment } from "../ECS/UnitComponents";
import { MatrixSM } from "../../GpuUtil/SM2Utils";
import BuffsPng from '../../Images/Buffs.png';
import { visibleCacheKeys } from "../ECS/Visibility";

export const Dazeable = new GpuComponentModule('Dazeable', UnitBuffer, 'int');

export class UpdateDazeBuff {
  constructor(private gls: GlState) {}

  dazed = new ArchetypesQuery([Dazeable.component])
    .mapGpu(this.gls, Dazeable.component, shaderModule([], {
      map: funcDef('int', [['ivec2', 'entityCell'], ['int', 'value']], `
        return max(0, value - 1);
      `)
    }));

  run(state: GpuWorldState) {
    this.dazed.run(state);
  }
}

const BuffsImage = loadImage(BuffsPng);

export class DazeableRenderer {
  constructor(private gls: GlState) {
    BuffsImage.then(img => gls.textures['buffs'] = Texture.fromImage(gls, img));
  }

  maxInstanceCount = 128;

  query = new EntitiesQuery([Dazeable.component])
    .filterValue(VisibleComponent, v => !!v, visibleCacheKeys)
    .render(this.gls, {
      mesh: 'quad',
      vertexShader: shaderModule([Vertex, TransformHelpers, HeadAttachment.sm, MatrixSM, Dazeable.sm], {
        projection: uniformDef('mat4', d => d.derived.cameraProjection),
        view: uniformDef('mat4', d => d.derived.cameraView),
        texcoord: outDef('vec2'),
        shader: funcDef('void', [['ivec2', 'entityCell']], `
          if (${Dazeable.sm.value}(entityCell) <= 0) {
            gl_Position = vec4(-1);
            return;
          }
          vec3 translation = ${TransformHelpers.worldPosition}(${HeadAttachment.sm.value}(entityCell)) + vec3(0, 0, 0.5);
          mat4 transform = ${MatrixSM.toBillboard}(view * ${MatrixSM.translate}(translation)) * ${MatrixSM.scale}(vec3(0.2));
          gl_Position = projection * transform * vec4(${Vertex.position}, 1);
          texcoord = (${Vertex.position}.xy + 1.0) / 2.0;
        `)
      }),
      fragmentShader: shaderModule([], {
        outColor: outDef('vec4'),
        tex: uniformDef('sampler2D', d => d.gls.textures.buffs),
        texcoord: inDef('vec2'),
        main: funcDef('void', [], `
          outColor = texture(tex, texcoord);
          if (outColor.a < 2.0/255.0) {
            discard;
          }
        `)
      })
    });

  run(state: GpuWorldState) {
    this.query.run(state);
  }
}
