import { ShaderModule, FunctionDef, CompiledShaderModule, shaderModule, outDef, funcDef, inDef, constDef, uniformDef, uniformArrayDef } from "../ShaderModule";
import { GpuComponent, Archetype, ECSConsts } from ".";
import { GlState, BlendMode, GL } from "../GlState";
import { RenderTarget } from "../RenderTarget";
import { gpuComponentBufferWriterSM, gpuComponentReadSM } from "./GpuComponentSM";
import { Program } from "../Program";
import { GpuWorldState } from "../../GpuWorld/State";
import { Query } from "./Query";
import { componentBufferDepth, GpuBufferData } from "./GpuBuffer";
import { Vertex } from "../Mesh";
import { TextureSlicing } from "../Texture";
import { ArrowTransformSM } from "../SM2Utils";
import { GLSLType } from "../GLSLTypes";


export class MapGpuComponent<T extends string> {
  constructor(
    protected gls: GlState,
    private archetypes: Query<Archetype[]>,
    private component: GpuComponent,
    private map: ShaderModule<{ map: FunctionDef<T, [['ivec2', 'entityCell'], [T, 'value']] | [['ivec2', 'entityCell']]> }>) {
  }
  private reader = gpuComponentReadSM(this.component);
  private writer = gpuComponentBufferWriterSM(this.component);
  private isReading = this.map.map.args.length === 2;
  private updateMulti = new UpdateGpuComponents(this.gls, this.archetypes, [this.component], shaderModule([
    this.map, this.writer, ...(this.isReading ? [this.reader] : [])
  ], {
    update: funcDef('void', [['ivec2', 'entityCell']], `
      ${this.writer.write}(${this.map.map}(entityCell${this.isReading ? `, ${this.reader.value}(entityCell)` : ''}));
    `)
  }), 'set');
  run(state: GpuWorldState) {
    this.updateMulti.run(state);
  }
}

export type UpdateGpuType = 'set' | 'sum';

export class UpdateGpuComponents {
  constructor(
    protected gls: GlState,
    private archetypes: Query<Archetype[]>,
    private components: Array<GpuComponent>,
    private update: ShaderModule<{ update: FunctionDef<'void' | 'ivec2', [['ivec2', 'entityCell']]> }>,
    private updateType: UpdateGpuType) {}

  private arrows = this.update.update.returnType === 'ivec2';
  private renderTarget = new RenderTarget(this.gls);
  // private output = new ComponentsBufferOutput(this.components);

  public componentDepths = this.components.map((c) => componentBufferDepth(c));
  public componentOffsets = this.components.reduce((p, c) => [...p, p[p.length - 1] + componentBufferDepth(c)], [0]);
  private componentsTotalDepth = this.componentOffsets[this.componentOffsets.length - 1];
  public outDepth = this.componentsTotalDepth + (this.arrows ? 1 : 0);
  public writeShaderPrep = shaderModule([], {
    outValues: outDef(`vec4[${this.outDepth}]`),
    ...this.components.reduce((p, c, i) => ({...p, [`${c.name}_outOffset`]: constDef('int', '' + this.componentOffsets[i]) }), {}),
  });

  private shader = new CompiledShaderModule(shaderModule([this.writeShaderPrep, this.update], {
    outArchetype: uniformDef('int'),
    outYOffset: uniformDef('int'),
    main: funcDef('void', [], `
      ivec2 outDataCell = ivec2(gl_FragCoord) - ivec2(0, outYOffset);
      ivec2 outEntityCell = ivec2(
        outDataCell.y * ${ECSConsts.ChunkSize} + outDataCell.x,
        outArchetype
      );
      ${!this.arrows ? `
        ${this.update.update}(outEntityCell);
      ` : `
        ivec2 targetEntityCell = ${this.update.update}(outEntityCell);
        outValues[${this.outDepth - 1}] = vec4(targetEntityCell, 0, 0);
      `}
    `)
  }))
  private program = new Program(this.gls, this.shader.source);
  private copyArrows = this.arrows ? new CopyArrows(this.gls, this.componentsTotalDepth, this.updateType === 'sum' ? BlendMode.Sum : undefined) : undefined;

  run(state: GpuWorldState, uniforms: Array<{ name: string, type: GLSLType, value: any }> = []) {
    // This must be called before we start setting up for this draw call, because
    // getting the values may involve renderings and state changes
    this.shader.updateUniformValues(state);

    const archetypes = this.archetypes.get(state);
    const archetypesChunkHeights = archetypes.reduce((p, x) => p + x.chunks.length, 0);
    const tmp = this.gls.getTmpTexture(GL.RGBA32F, { width: ECSConsts.ChunkSize, height: archetypesChunkHeights }, this.outDepth);
    let heightOffset = 0;
    // It's important that we first compute all values and _then_ copy them back, so that the operation is atomic
    this.program.prepare();
    this.shader.writeUniforms(this.program);
    for (const arch of archetypes) {
      const width = arch.chunks.reduce((p, x) => Math.max(p, x.entities), 0);

      const slice = tmp.sliceRange(0, this.outDepth).selectAbsolute({
        x: 0, y: heightOffset, width, height: arch.chunks.length });
      this.program.setUniform('outArchetype', 'int', arch.index);
      this.program.setUniform('outYOffset', 'int', heightOffset);
      for (const uniform of uniforms) {
        this.program.setUniform(uniform.name, uniform.type, uniform.value);
      }
      this.renderTarget.set(slice);
      this.gls.prepareForComputation(slice.rect);
      this.gls.drawQuad(this.program);

      heightOffset += arch.chunks.length;
    }

    for (let i=0; i < this.components.length; i++) {
      const info = state.ecs.gpuComponentInfo(this.components[i]);
      if (info) {

        heightOffset = 0;
        for (let a=0; a < archetypes.length; a++) {
          const arch = archetypes[a];
          const width = arch.chunks.reduce((p, x) => Math.max(p, x.entities), 0);
          const tmpSlice = tmp.sliceRange(0, this.outDepth).selectAbsolute({
            x: 0, y: heightOffset, width, height: arch.chunks.length });
          heightOffset += arch.chunks.length;

          const tmpComponentSlice = tmpSlice.sliceRange(this.componentOffsets[i], this.componentDepths[i]);
          if (this.copyArrows) {
            this.copyArrows.run(tmpComponentSlice, info.slice, info.buffer);
          } else {
            const target = info.slice.selectAbsolute({
              x: 0,
              y: info.buffer.archetypes[arch.index]!.dataRowOffset,
              width,
              height: arch.chunks.length
            });

            // This while loop is an optimization; it looks ahead and tries to group copies together
            while ((a + 1) < archetypes.length) {
              const nextArch = archetypes[a + 1];
              if (target.rect.y + target.rect.height === info.buffer.archetypes[nextArch.index]!.dataRowOffset) {
                target.rect.height += nextArch.chunks.length;
                tmpSlice.rect.height += nextArch.chunks.length;
                const nextWidth = nextArch.chunks.reduce((p, x) => Math.max(p, x.entities), 0);
                target.rect.width = Math.max(target.rect.width, nextWidth);
                a++;
                heightOffset += nextArch.chunks.length;
              } else {
                break;
              }
            }

            this.gls.getCopyTexture().copy(target, tmpComponentSlice, this.updateType);
          }

        }

      }
    }
  }
}

class CopyArrows {
  constructor(private gls: GlState, private valuesDepth: number, private blendMode: BlendMode | undefined) {}

  private vertexShader = new CompiledShaderModule(shaderModule([Vertex, ArrowTransformSM], {
    targetArchetypeDataRowOffset: uniformArrayDef('int[]', ECSConsts.MaxArchetypes),
    targetDataCell: funcDef('ivec2', [['ivec2', 'entityCell']], `
      const int chunkSize = ${ECSConsts.ChunkSize};
      return ivec2(
        entityCell.x % chunkSize,
        entityCell.x / chunkSize + Self.targetArchetypeDataRowOffset[entityCell.y]
      );
    `),
    targetSize: uniformDef('ivec2'),

    sourceDataCell: outDef('ivec2', true),
    source: uniformDef('sampler2DArray'),
    sourceOffset: uniformDef('ivec2'),
    main: funcDef('void', [], `
      sourceDataCell = ivec2(
        gl_InstanceID % ${ECSConsts.ChunkSize},
        gl_InstanceID / ${ECSConsts.ChunkSize}
      ) + sourceOffset;
      ivec2 targetEntityCell = ivec2(texelFetch(source, ivec3(sourceDataCell, ${this.valuesDepth}), 0).xy);
      if (targetEntityCell.x < 0) {
        gl_Position = vec4(-1);
        return;
      }
      mat4 arrow = ${ArrowTransformSM.cellArrowTransform}(Self.targetSize, Self.targetDataCell(targetEntityCell));
      gl_Position = arrow * vec4(${Vertex.position}, 1);
    `)
  }));
  private fragmentShader = new CompiledShaderModule(shaderModule([], {
    source: uniformDef('sampler2DArray'),
    sourceDataCell: inDef('ivec2', true),
    outValues: outDef('vec4[8]'),
    sourceLayerOffset: uniformDef('int'),
    sourceCount: uniformDef('int'),
    main: funcDef('void', [], `
      vec4 values[8];
      for (int i=0; i < sourceCount; i++) {
        values[i] = texelFetch(source, ivec3(sourceDataCell, sourceLayerOffset + i), 0);
      }
      outValues = values;
    `)
  }));
  private program = new Program(this.gls, this.fragmentShader.source, this.vertexShader.source);
  private renderTarget = new RenderTarget(this.gls);

  run(source: TextureSlicing, target: TextureSlicing, targetGpuBuffer: GpuBufferData) {
    this.program.prepare();
    this.program.setUniform('targetArchetypeDataRowOffset', 'int[]', targetGpuBuffer.archetypeToDataRowOffset);
    this.program.setUniform('targetSize', 'ivec2', targetGpuBuffer.sizeVec2);
    this.program.setUniform('source', 'sampler2DArray', source.texture);
    this.program.setUniform('sourceOffset', 'ivec2', source.rect);
    this.program.setUniform('sourceLayerOffset', 'int', source.layers[0]);
    this.program.setUniform('sourceCount', 'int', source.layers.length);
    this.renderTarget.set(target);
    this.gls.prepareForComputation(target.rect, this.blendMode);
    this.gls.drawQuad(this.program, source.rect.width * source.rect.height);
  }
}