import * as React from 'react';
import * as ReactDOM from 'react-dom';
import './index.css';
import { MaterialIconButton } from '../Button';
import { WonkyPanel } from '../WonkyPanel';
import { FloatingPanelColor } from '../Panel';

export function PopupsContainer() {
  return <div id="PopupsContainer" />
}

interface PopupProps {
  onClose?: () => void;
  fullscreen?: boolean;
  background?: boolean;
  showCloseButton?: boolean;
  className?: string;
}

export class Popup extends React.Component<PopupProps> {
  render() {
    const background = this.props.background !== undefined ? this.props.background : true;
    const showCloseButton = this.props.showCloseButton !== undefined ? this.props.showCloseButton : true;
    const popupContainer = document.getElementById("PopupsContainer");
    if (popupContainer) {
      return ReactDOM.createPortal(
          <div className={`Fader ${this.props.fullscreen ? 'FaderPopupFullscreen' : ''}`} onClick={this.props.onClose}>
            {background ? (
              <WonkyPanel className={`Popup ${this.props.className || ''}`} fill={FloatingPanelColor} onClick={e => e.stopPropagation()}>
                {showCloseButton && <MaterialIconButton icon="clear" className="ClosePopup" onClick={this.props.onClose} purpose="ClosePopup" />}
                {this.props.children}
              </WonkyPanel>
            ) : (
              <div className={`Popup`} onClick={e => e.stopPropagation()}>
                {showCloseButton && <MaterialIconButton icon="clear" className="ClosePopup" onClick={this.props.onClose} purpose="ClosePopup" />}
                {this.props.children}
              </div>
            )}
          </div>,
          popupContainer
      );
    } else {
      setTimeout(() => this.forceUpdate(), 0);
      return null;
    }
  }
}
