import { GpuWorldState } from "../State";
import { GpuWorldRenderer, setCanvasSizeToElementSize } from ".";
import { initGlExtensions } from "../../GpuUtil";
import { JobQueue } from "../../JobQueue";
import { defaultRenderOptions } from "./Configuration";
import { Size } from "../../Math/Math";
import { GlState } from "../../GpuUtil/GlState";
import { createPlayerUnits, CreatePlayerUnitOptions } from "../CreateUnit";
import { UnitDef } from "../UnitDef";
import { createUnitModel } from "../CreateUnitModel";

export class PortraitRenderer {
  private constructor(public canvas: HTMLCanvasElement, private gl: WebGL2RenderingContext) {
    this.state.setData({
      rendererConfig: {
        ...defaultRenderOptions(),
        skybox: false,
        overheadBars: false,
        clearColor: { r: 0, g: 0, b: 0, a: 0 },
      },
      renderArena: 0,
    });
  }
  static create(canvas = document.createElement('canvas'), options: WebGLContextAttributes = { desynchronized: true }) {
    const gl = canvas.getContext('webgl2', options) as any as WebGL2RenderingContext;
    if (gl) {
      if (initGlExtensions(gl).length === 0) {
        return new PortraitRenderer(canvas, gl);
      }
    }
    return undefined;
  }
  private loopRunning = false;
  startRenderLoop() {
    this.loopRunning = true;
    this.renderLoop();
  }
  stopRenderLoop() {
    this.loopRunning = false;
    this.renderLoop();
  }
  private renderLoop = () => {
    if (!this.loopRunning) {
      return;
    }
    window.requestAnimationFrame(this.renderLoop);
    this.state.setData({ stepCounter: this.state.data.stepCounter + 1 });
    setCanvasSizeToElementSize(this.state);
    this.renderer.render(this.state);
  }

  private gls = new GlState(this.canvas, this.gl);
  state = new GpuWorldState(this.gls, { arenaCount: 1, });

  private renderer = new GpuWorldRenderer(this.gls);

  private async renderOnce(canvasSize: Size = { width: 480, height: 480 }) {
    this.canvas.width = canvasSize.width;
    this.canvas.height = canvasSize.height;
    this.state.setData({
      canvasRect: {
        left: 0,
        top: 0,
        bottom: 0,
        right: 0,
        width: this.canvas.width,
        height: this.canvas.height
      }
    });
    this.renderer.render(this.state);
  }

  async setUnit(unit: UnitDef) {
    this.state.ecs.removeAllEntities();
    const res = await createPlayerUnits(this.state, [{ unit }], {...new CreatePlayerUnitOptions(), createAbilities: false});
    const model = await createUnitModel(this.state, res.getIndex(0));
    this.state.setData({ camera: model.createCamera(this.state) });
    return res.getIndex(0);
  }

  private jobQueue = new JobQueue();
  private dataUrlCache: { [key: string]: Promise<string> } = {};

  renderToDataURL(unit: UnitDef, canvasSize?: Size) {
    const key = JSON.stringify({ unit, canvasSize });
    if (this.dataUrlCache[key]) {
      return this.dataUrlCache[key];
    }
    return this.dataUrlCache[key] = this.jobQueue.runJob(async () => {
      await this.setUnit(unit);
      await this.renderOnce(canvasSize);
      this.gl.finish();
      return this.canvas.toDataURL();
    });
  }
  renderToBlob(unit: UnitDef, canvasSize?: Size) {
    return this.jobQueue.runJob(async () => {
      await this.setUnit(unit);
      await this.renderOnce(canvasSize);
      this.gl.finish();
      return await new Promise<Blob>(resolve2 => this.canvas.toBlob(blob => resolve2(blob!)));
    });
  }
  async renderToCanvas(canvas: HTMLCanvasElement, unit: UnitDef, canvasSize?: Size) {
    await this.setUnit(unit);
    await this.renderOnce(canvasSize);
    this.gl.finish();
    const ctx = canvas.getContext('2d')!;
    ctx.clearRect(0, 0, canvas.width, canvas.height);
    ctx.drawImage(this.canvas, 0, 0);
  }
}
