import { GlState } from "../../GpuUtil/GlState";
import { GpuWorldState } from "../State";
import { CastingAbilityHelpers, UnitRef } from "./Abilities";
import { Abilities, abilitiesData } from "./Abilities";
import { Force } from "../ECS/UnitComponents";
import { RotationZ } from "../ECS/Transform";
import { ArchetypesQuery } from "../../GpuUtil/ECS/Query";
import { shaderModule, funcDef } from "../../GpuUtil/ShaderModule";
import { SpatialSM } from "../../GpuUtil/SM2Utils";

export class UpdateLeap {
  constructor(private gls: GlState) {}

  private forces = new ArchetypesQuery([abilitiesData.component[Abilities.Leap].gpuComponent, Force.component])
    .updateGpu(this.gls, [Force.component], shaderModule([
      CastingAbilityHelpers, RotationZ.sm, SpatialSM, Force.writer, UnitRef.sm, Force.sm
    ], {
      update: funcDef('ivec2', [['ivec2', 'entityCell']], `
        ivec2 unit = ${UnitRef.sm.value}(entityCell);
        if (${CastingAbilityHelpers.isPerforming}(unit, ${Abilities.Leap})) {
          float rotation = ${RotationZ.sm.value}(unit);
          vec2 forward = ${SpatialSM.forward}(rotation);
          ${Force.writer.write}(vec3(forward, 1.4) * 1000.);
        } else {
          ${Force.writer.write}(${Force.sm.value}(unit));
        }
        return unit;
      `)
    }));

  run(state: GpuWorldState) {
    this.forces.run(state);
  }
}
