import * as React from 'react';
import './index.css';
import { GpuWorldProgram } from '../../GpuWorld';
import { initGlExtensions } from '../../GpuUtil';
import { Button } from '../Button';
import { GlState } from '../../GpuUtil/GlState';
import { ShaderError } from '../../GpuUtil/ShaderCache';
import { version } from '../../version';
import { ShaderDebugger } from './ShaderDebugger';

export const WorldCanvasContext = React.createContext<HTMLCanvasElement | null>(null);
export const WorldProgramContext = React.createContext<GpuWorldProgram | undefined>(undefined);

export function WorldCanvas({ children }: { children?: any }) {
  const [worldProgram, setWorldProgram] = React.useState<GpuWorldProgram | undefined>();

  const [webgl2FailedToCreate, setWebgl2FailedToCreate] = React.useState<boolean>(false);
  const [webgl2ExtensionErrors, setWebgl2ExtensionErrors] = React.useState<string[]>([]);
  const [contextLost, setContextLost] = React.useState<boolean>(false);
  const [shaderErrors, setShaderErrors] = React.useState<ShaderError[]>([]);
  const [worldCanvas, setWorldCanvas] = React.useState<HTMLCanvasElement | null>(null);
  const [loading, setLoading] = React.useState(true);

  const handleCanvasRef = React.useCallback((canvas: HTMLCanvasElement | null) => {
    if (canvas) {
      canvas.addEventListener("webglcontextlost", () => {
        // tslint:disable-next-line:no-console
        console.log('WebGL context lost');
        setContextLost(true);
      });
      const gl = canvas.getContext('webgl2', { alpha: false, desynchronized: false, preserveDrawingBuffer: true }) as any as WebGL2RenderingContext; // Have to do this cast to get storybook to work
      if (!gl) {
        setWebgl2FailedToCreate(true);
        return;
      }
      const extensionsErrors = initGlExtensions(gl);
      if (extensionsErrors.length > 0) {
        setWebgl2ExtensionErrors(extensionsErrors);
        return;
      }
      setWorldProgram(undefined);

      const gls = new GlState(canvas, gl);
      const program = new GpuWorldProgram(gls);
      if (program.gls.shaderCache.getErrors().length > 0) {
        setShaderErrors(program.gls.shaderCache.getErrors());
      }
      setWorldProgram(program);
      setLoading(false);
    }
    setWorldCanvas(canvas);
  }, []);

  return <WorldProgramContext.Provider value={worldProgram}>
    <WorldCanvasContext.Provider value={worldCanvas}>
      <canvas
        className="WorldCanvas"
        ref={handleCanvasRef}
        />

      {(contextLost || webgl2FailedToCreate || webgl2ExtensionErrors.length > 0) && <div className="WorldCanvasErrors">
        <div className="WorldCanvasErrorsInner">
          {contextLost && <div>
            <h2>WebGL context lost</h2>
            <p>The WebGL context has been lost. This can happen for a number of reasons. The most common
              reason is running out of GPU memory, or the graphics card drivers crashing.</p>
          </div>}
          {webgl2FailedToCreate && <div>
              <h2>WebGL2 not available</h2>
              <ul>
                <li>If the WebGL context was lost, you may have to restart your browser.</li>
                <li>WebGL2 is currently only supported by latest Chrome and Firefox on desktop computers.</li>
                <li>WebGL2 may not be available if your graphics drivers have crashed. Restarting your browser/computer may help.</li>
                <li>Make sure your graphics drivers are up to date, some browsers disallow old drivers to be used.</li>
                <li>Make sure WebGL2 is enabled. On Chrome you need to make sure "Hardware Acceleration" is enabled under advanced settings.</li>
                <li>Visit <a href="http://webglreport.com/?v=2">WebGL report</a> to see if your browser / device is supported.</li>
              </ul>
            </div>}
          {webgl2ExtensionErrors.length > 0 && <div>
              <h2>WebGL2 required extensions not available</h2>
              The following required extensions are not available: {webgl2ExtensionErrors.join(', ')}
              <ul>
                <li>Try upgrading your graphics card drives.</li>
              </ul>
            </div>}
          <Button onClick={() => window.location.reload()}>Reload page</Button>
        </div>
      </div>}

      <div className="WorldCanvasContent">
        {loading ? <div className="WorldCanvasLoading">Loading....</div> : children}
      </div>

      {shaderErrors.length > 0 && <div className="ShaderErrors" title="Dr. Derk's Mutant Battlegrounds may not work properly on this device"><i className="fas fa-exclamation-triangle" /> {shaderErrors.length} shader compilation errors</div>}
      {shaderErrors.length > 0 && version.isDevelopment && <ShaderDebugger shaderErrors={shaderErrors} />}

    </WorldCanvasContext.Provider>
  </WorldProgramContext.Provider>
}
