export enum UnitCategories {
  Friend,
  Enemy,
  FriendStatue,
  EnemyStatue
}
export const NUnitCategories = keysOfEnum(UnitCategories).length;
export const UnitCategory = shaderModule([ArenaMemberIndex.sm], {
  category: funcDef('int', [['ivec2', 'selfEntityCell'], ['ivec2', 'otherEntityCell']], `
    int selfArenaMemberIndex = ${ArenaMemberIndex.sm.value}(selfEntityCell);
    int otherArenaMemberIndex = ${ArenaMemberIndex.sm.value}(otherEntityCell);
    bool isStatue = otherArenaMemberIndex == 0 || otherArenaMemberIndex == ${SideSize};
    bool selfIsHome = selfArenaMemberIndex < ${SideSize};
    bool otherIsHome = otherArenaMemberIndex < ${SideSize};
    bool isFriendly = selfIsHome == otherIsHome;
    if (isStatue) {
      return isFriendly ? ${UnitCategories.FriendStatue} : ${UnitCategories.EnemyStatue};
    } else {
      return isFriendly ? ${UnitCategories.Friend} : ${UnitCategories.Enemy};
    }
  `)
});
export function unitCategoryFromArenaMemberIndex(arenaMemberIndex: number, selfIsHome = true) {
  const isStatue = arenaMemberIndex === 0 || arenaMemberIndex === SideSize;
  const otherIsHome = arenaMemberIndex < SideSize;
  const isFriendly = selfIsHome === otherIsHome;
  if (isStatue) {
    return isFriendly ? UnitCategories.FriendStatue : UnitCategories.EnemyStatue;
  } else {
    return isFriendly ? UnitCategories.Friend : UnitCategories.Enemy;
  }
}

