import { GlState, GL } from "../../GpuUtil/GlState";
import { CompiledShaderModule, shaderModule, outDef, uniformDef, funcDef, inDef } from "../../GpuUtil/ShaderModule";
import { Vertex, Mesh } from "../../GpuUtil/Mesh";
import { MatrixSM, InterpolateSM } from "../../GpuUtil/SM2Utils";
import { Program } from "../../GpuUtil/Program";
import { GpuWorldState } from "../State";
import { UnitsInArena } from "../ECS/ArenaStats";
import { BrainOutputsKeys, ArenaMembersSize } from "../StateLayout";
import { OutputLayer } from "../Brain/Components";
import { RenderTarget } from "../../GpuUtil/RenderTarget";
import * as THREE from 'three';
import { glslVec2 } from "../../GpuUtil";
import { Position } from "../ECS/Transform";

export const DecisionsVizHistory = 24;

export class UpdateDecisionsViz {
  constructor(private gls: GlState) {}

  shader = new CompiledShaderModule(shaderModule([
    UnitsInArena, OutputLayer.res.sm, OutputLayer.res.hasComponentSM
  ], {
    outValue: outDef('float'),
    renderArena: uniformDef('int', d => d.data.renderArena),
    decisionsViz: uniformDef('sampler2D', d => d.textures.decisionsViz),
    main: funcDef('void', [], `
      int history = int(gl_FragCoord.x);
      int arenaMemberIndex = int(gl_FragCoord.y) / ${BrainOutputsKeys.length};
      int brainOutput = int(gl_FragCoord.y) % ${BrainOutputsKeys.length};
      if (history == 0) {
        ivec2 unit = ${UnitsInArena.unit}(renderArena, arenaMemberIndex);
        if (${OutputLayer.res.hasComponentSM.value}(unit)) {
          vec4 outputSlice = ${OutputLayer.res.sm.value}(unit, brainOutput / 4);
          outValue = outputSlice[brainOutput % 4];
        } else {
          outValue = 0.;
        }
      } else {
        outValue = texelFetch(Self.decisionsViz, ivec2(gl_FragCoord) - ivec2(1, 0), 0).x;
      }
    `)
  }));
  program = new Program(this.gls, this.shader.source);
  renderTarget = new RenderTarget(this.gls);

  run(state: GpuWorldState) {
    if (!state.data.showDecisionsViz) {
      return;
    }
    const slice = state.textures.decisionsViz.sliceAll();
    this.shader.updateUniformValues(state);
    this.renderTarget.set(slice.back);
    this.program.prepare();
    this.shader.writeUniforms(this.program);
    this.gls.prepareForComputation(slice.rect);
    this.gls.drawQuad(this.program);
    this.gls.getCopyLayers().copyBackToFront(slice);
  }
}

export class DecisionsVizRenderer {
  constructor(private gls: GlState) {}

  gridSize = { x: DecisionsVizHistory - 1, y: BrainOutputsKeys.length - 1 };
  grid = Mesh.fromThree(this.gls, new THREE.PlaneBufferGeometry(1, 1,
    this.gridSize.x, this.gridSize.y));

  vertexShader = new CompiledShaderModule(shaderModule([
    Vertex, MatrixSM, InterpolateSM, UnitsInArena, Position.sm, OutputLayer.res.hasComponentSM
  ], {
    decisionsViz: uniformDef('sampler2D', d => d.textures.decisionsViz),
    viewProjection: uniformDef('mat4', d => d.derived.cameraProjectionView),
    renderArena: uniformDef('int', d => d.data.renderArena),
    color: outDef('vec4'),
    main: funcDef('void', [], `
      int arenaMemberIndex = gl_InstanceID;
      ivec2 unit = ${UnitsInArena.unit}(renderArena, arenaMemberIndex);
      if (!${OutputLayer.res.hasComponentSM.value}(unit)) {
        gl_Position = vec4(-1);
        return;
      }
      ivec2 read = ivec2((vec2(${Vertex.position}.x, -${Vertex.position}.y) + 0.5) * ${glslVec2({ x: DecisionsVizHistory, y: BrainOutputsKeys.length })}) +
        ivec2(0, arenaMemberIndex * ${BrainOutputsKeys.length});
      float value = texelFetch(Self.decisionsViz, read, 0).x;
      vec3 position = ${Position.sm.value}(unit) + vec3(0, -2, 0.2);
      gl_Position = viewProjection * ${MatrixSM.translateScale}(position, vec3(2, 2, 0.3)) * vec4(${Vertex.position}.xy, value, 1);
      color = vec4(${InterpolateSM.interpolateClamped}(value, -1., 1., vec3(1., 0.1, 0.2), vec3(0.3, 1., 0.4)), 1);
    `)
  }));

  fragmentShader = new CompiledShaderModule(shaderModule([
  ], {
    outColor: outDef('vec4'),
    color: inDef('vec4'),
    main: funcDef('void', [], `
      outColor = color;
    `)
  }));

  program = new Program(this.gls, this.fragmentShader.source, this.vertexShader.source);

  run(state: GpuWorldState) {
    if (!state.data.showDecisionsViz) {
      return;
    }

    this.vertexShader.updateUniformValues(state);
    this.fragmentShader.updateUniformValues(state);
    this.program.prepare();
    this.vertexShader.writeUniforms(this.program);
    this.fragmentShader.writeUniforms(this.program);
    this.gls.enable(GL.DEPTH_TEST);
    this.gls.disable(GL.BLEND);
    this.gls.draw(this.program, this.grid, ArenaMembersSize);
  }
}
