import * as Vector3 from './Vector3';
import * as Vector2 from './Vector2';
import { mat4 } from 'gl-matrix';
import { Vector4 } from './Vector4';

// See http://stackoverflow.com/questions/4467539/javascript-modulo-not-behaving
export function mod(m: number, n: number) {
  return ((m%n)+n)%n;
}

export interface Size {
  width: number;
  height: number;
}
export interface Size3 {
  width: number;
  height: number;
  depth: number;
}
export interface Rect {
  x: number;
  y: number;
  width: number;
  height: number;
}

export function vec2FromSize(size: Size): Vector2.Vector2 {
  return { x: size.width, y: size.height };
}
export function sizeFromVec2(vec: Vector2.Vector2): Size {
  return { width: vec.x, height: vec.y };
}
export function vec4FromRect(rect: Rect): Vector4 {
  return { x: rect.x, y: rect.y, z: rect.width, w: rect.height };
}
export function closestPow2(value: number) {
  return Math.pow(2, Math.ceil(Math.log2(value)));
}
export function sizeClosestPow2(size: Size) {
  const s = closestPow2(Math.max(size.width, size.height));
  return { width: s, height: s };
}

export function indexToCell(size: Size, index: number) {
  return index < 0 ? { x: -1, y: -1 } : { x: index % size.width, y: Math.floor(index / size.width) };
}
export function cellToIndex(size: Size, cell: Vector2.Vector2);
export function cellToIndex(width: number, cell: Vector2.Vector2);
export function cellToIndex(sizeOrWidth: Size | number, cell: Vector2.Vector2) {
  if (typeof sizeOrWidth === 'number') {
    return cell.y * sizeOrWidth + cell.x;
  } else {
    return cell.y * sizeOrWidth.width + cell.x;
  }
}

export function clamp(value: number, min: number, max: number): number {
  return Math.min(Math.max(value, min), max);
}
export function mix(a: number, b: number, p: number) {
  return a * (1 - p) + b * p;
}
export function interpolate(x: number, x0: number, x1: number, y0: number, y1: number): number {
  const p = (x - x0) / (x1 - x0);
  return y0 + p * (y1 - y0);
}
export function interpolateClamped(x: number, x0: number, x1: number, y0: number, y1: number): number {
  const p = clamp((x - x0) / (x1 - x0), 0, 1);
  return y0 + p * (y1 - y0);
}

export function gaussianBlurKernel3x3(sigma: number): number[] {
  const kernel: number[] = [];
  const euler = 1 / (2 * Math.PI * sigma * sigma);
  for (let y=-1; y <= 1; y++) {
    for (let x=-1; x <= 1; x++) {
      kernel.push(euler * Math.exp(- (x * x + y * y) / (2 * sigma * sigma)));
    }
  }
  const sum = kernel.reduce((p, x) => p + x, 0);
  return kernel.map(x => x / sum);
}


// From: https://gist.github.com/kchapelier/b1fd7e71f5378b871e3d6daa5ae193dc
// based on https://warpycode.wordpress.com/2009/04/13/computing-a-1d-gaussian-kernel/
// see http://www.stat.wisc.edu/~mchung/teaching/MIA/reading/diffusion.gaussian.kernel.pdf.pdf for more info
const sqr2pi = Math.sqrt(2 * Math.PI);
export function gaussianKernel1d (size: number, sigma: number) {
  // ensure size is even and prepare variables
  // tslint:disable-next-line:no-bitwise
  const width = (size / 2) | 0;
  const kernel = new Array(width * 2 + 1);
  const norm = 1.0 / (sqr2pi * sigma);
  const coefficient = 2 * sigma * sigma;
  let total = 0;
  let x;

  // set values and increment total
  for (x = -width; x <= width; x++) {
      total += kernel[width + x] = norm * Math.exp(-x * x / coefficient);
  }

  // divide by total to make sure the sum of all the values is equal to 1
  for (x = 0; x < kernel.length; x++) {
      kernel[x] /= total;
  }

  return kernel;
}

// This gives you a sigma to plug into gaussianKernel1d (only when size=2).
// For example, if I want 50% decay for each cell after 10 steps I do findGaussianKernel1dSize1Sigma(10, 0.5)
// NOTE: assumes the gaussian blur is run in two passes!
export function findGaussianKernel1dSize1Sigma(steps: number, percentile: number) {
  // Derivation:
  // sqr2pi = sqrt(2 * PI);
  // norm = 1.0 / (sqr2pi * sigma);
  // coefficient = 2 * sigma * sigma;
  // left = norm * e^(-1 / coefficient)
  // centerPrel = norm;
  // total = norm + left * 2;
  // center = centerPrel / total;
  // decayRate = center;
  // percentile = 0.5;
  // stepsUntil50perc = ln(percentile) / ln(decayRate);
  // Plugged into a solver to get bellow:
  steps *= 2; // Assume we run the gaussian blur in two passes
  const a = 1 - Math.exp(Math.log(percentile) / steps);
  const b = 2 * Math.exp(Math.log(percentile) / steps);
  return Math.sqrt(-1/(2 * Math.log(a/b)));
}



export function screenToWorld(invViewProjection: mat4, viewport: Rect, point: Vector3.Vector3) {
  return clipSpaceToWorld(invViewProjection, {
    x: 2.0 * (point.x - viewport.x) / viewport.width - 1.0,
    y: -(2.0 * (point.y - viewport.y) / viewport.height - 1.0),
    z: point.z
  });
}
export function clipSpaceToWorld(invViewProjection: mat4, point: Vector3.Vector3) {
  return Vector3.transform(point, invViewProjection);
}
export function worldToScreen(view: mat4, point: Vector3.Vector3) {
  return Vector3.transform(point, view);
}

interface Ray {
  origin: Vector3.Vector3;
  direction: Vector3.Vector3;
}
export function screenRay(invViewProjection: mat4, viewport: Rect, screenPosition: Vector2.Vector2): Ray {
  const p1 = screenToWorld(invViewProjection, viewport, {...screenPosition, z: 1 });
  const p2 = screenToWorld(invViewProjection, viewport, {...screenPosition, z: -1 });
  return {
    origin: p2,
    direction: Vector3.normalize(Vector3.sub(p1, p2))
  }
}

export interface Plane {
  normal: Vector3.Vector3;
  distance: number;
}
// From https://github.com/mattdesl/ray-plane-intersection/blob/master/index.js
export function rayPlaneIntersection(ray: Ray, plane: Plane): null | Vector3.Vector3 {
  const denom = Vector3.dot(ray.direction, plane.normal);
  if (denom  !== 0) {
    const t = -(Vector3.dot(ray.origin, plane.normal) + plane.distance) / denom;
    if (t < 0) {
      return null;
    }
    return Vector3.add(ray.origin, Vector3.mul(ray.direction, t));
  } else if (Vector3.dot(plane.normal, ray.origin) + plane.distance === 0) {
    return ray.origin;
  } else {
    return null;
  }
}

export function pickingMatrix(screenSize: Size, screenPosition: Vector2.Vector2) {
  const select = mat4.identity(mat4.create());
  mat4.translate(select, select, [screenSize.width - screenPosition.x * 2, -(screenSize.height - screenPosition.y * 2), 0]);
  mat4.scale(select, select, [screenSize.width, screenSize.height, 1]);
  return select;
}

export function volumeOfBox(box: Vector3.Vector3) {
  return Vector3.boxVolume(box);
}

export function surfaceAreaOfBox(box: Vector3.Vector3) {
  return Vector3.boxSurfaceArea(box);
}

export function mindimOfBox(box: Vector3.Vector3) {
  return Math.min(Math.min(box.x, box.y), box.z);
}
export function maxdimOfBox(box: Vector3.Vector3) {
  return Math.max(Math.max(box.x, box.y), box.z);
}

export function triangleWave(i: number, m = 1) {
  return m - Math.abs(mod(i, 2 * m) - m);
}

export function withoutNaNInf(v: number) {
  if (Number.isNaN(v) || !Number.isFinite(v)) {
    return 0;
  } else {
    return v;
  }
}

export function shortestAngleDist(a0: number, a1: number) {
  const max = Math.PI * 2.0;
  const da = mod((a1 - a0), max);
  return mod(2.0 * da, max) - da;
}

export function angleLerp(a0: number, a1: number, t: number) {
  return a0 + shortestAngleDist(a0, a1) * t;
}

export function fract(value: number) {
  return value - Math.floor(value);
}

export function snoise2d(p: Vector2.Vector2): number {
  throw new Error('Not implemented yet');
}

// This is not exactly the same as "normal" exponential decay (https://en.wikipedia.org/wiki/Exponential_decay)
// because the time steps are discrete.
export function discreteExponentialDecayHalftime(decayRate: number, percentile: number) {
  return Math.log(percentile) / Math.log(1 - decayRate);
}

export function floatBitsToUint(f: number) {
  var buf = new ArrayBuffer(4);
  (new Float32Array(buf))[0] = f;
  return (new Uint32Array(buf))[0];
}

export function uintBitsToFloat(f: number) {
  var buf = new ArrayBuffer(4);
  (new Uint32Array(buf))[0] = f;
  return (new Uint32Array(buf))[0];
}


export function bitCheckedUInt32(value: number, bitIndex: number) {
  return !!((value & ((1 << bitIndex) >>> 0)) >>> 0);
}

export function flipBitUInt32(value: number, bitIndex: number) {
  return (value ^ ((1 << bitIndex) >>> 0)) >>> 0;
}

export function softmax(values: Float32Array | number[]) {
  const res: number[] = [];
  let sum = 0;
  for (let i=0; i < values.length; i++) {
    const v = Math.exp(values[i]);
    sum += v;
    res.push(v);
  }
  return res.map(v => v / sum);
}

export function sampleSoftmax(p: number, ...values: number[]) {
  const s = softmax(values);
  let cummulative = 0;
  for (let i=0; i < values.length; i++) {
    cummulative += s[i];
    if (cummulative < p) {
      return i;
    }
  }
  return 0;
}

// From: https://stackoverflow.com/questions/25582882/javascript-math-random-normal-distribution-gaussian-bell-curve
// Random number from a gausssian distribution, mean=0, variance=1
// Standard Normal variate using Box-Muller transform.
export function randomBoxMuller() {
  let u = 0, v = 0;
  while(u === 0) u = Math.random(); //Converting [0,1) to (0,1)
  while(v === 0) v = Math.random();
  return Math.sqrt( -2.0 * Math.log( u ) ) * Math.cos( 2.0 * Math.PI * v );
}