import { GpuWorldState } from "../GpuWorld/State";
import { EntityInit, EntitiesRange } from "./ECS";
import { BoneMatrix, TransformParent, LocalToParent, LocalToParentOrderComponents, BoneOffset, LocalToWorld, Position, Quaternion, Scale } from "../GpuWorld/ECS/Transform";
import { RenderableComponent, CullFace, MaterialColor, SkinmeshBones, MaxBones, SkinmeshBindMatrix, MaterialUseLighting } from "../GpuWorld/Renderer/ModelRenderer";
import { GLTFLoader, GLTF } from 'three/examples/jsm/loaders/GLTFLoader';
import * as THREE from 'three';
import * as Mat4 from '../Math/Mat4';
import * as Vector3 from '../Math/Vector3';
import * as Vector4 from '../Math/Vector4';
import { MeshLoaders } from "./GlState";
import { Mesh } from "./Mesh";
import { animationClipFromThree, AnimationControllerComponent, AnimationBinderComponent, AnimationClip } from "../GpuWorld/ECS/Animation";
import { EntityRef } from "../GpuWorld/StateData";
import { NameComponent } from "../GpuWorld/ECS/Components";
import { VisibleComponent, VisibilityFromComponent } from "../GpuWorld/ECS/Components";
import { full } from "db";
import { LookatCamera } from "./Camera";
import { Camera, createCamera } from "../GpuWorld/ECS/Camera";

THREE.Object3D.DefaultUp = new THREE.Vector3(0, 0, 1);

interface CreateModelInstanceOptions {
  count?: number;
  rootEntities?: EntitiesRange;
  rootEntityInit?: EntityInit;
  beforeEntityCreated?: (object: THREE.Object3D, entityInit: EntityInit) => EntityInit | undefined;
  afterEntityCreated?: (object: THREE.Object3D, entityCell: EntitiesRange) => void;
  onMaterial?: (material: THREE.MeshStandardMaterial, entityIndex: number) => Vector4.Vector4 | undefined;
  localToParentStartOrder?: number;
  visible?: boolean;
}

export class ThreeModel {
  constructor(public name: string, private model: GLTF) {
    model.scene.updateMatrixWorld();
    console.log('Model loaded', name, model);
    for (const clip of model.animations) {
      const globalName = name + '/' + clip.name;
      this.clipNames[clip.name] = globalName;
      this.animationClips[globalName] = animationClipFromThree(clip);
    }
    model.scene.traverse(obj => {
      if (obj.type === 'Mesh' || obj.type === 'SkinnedMesh') {
        const o = obj as THREE.Mesh;
        const geom = o.geometry;
        if (geom instanceof THREE.BufferGeometry) {
          MeshLoaders[o.geometry.id] = gls => Mesh.fromThree(gls, geom);
        }
      }
    })
  }
  animationClips: {
    [key: string]: AnimationClip
  } = {};
  static fromPath(path: string) {
    return new Promise<ThreeModel>(resolve => {
      const loader = new GLTFLoader();
      loader.load(path, gltf => {
        resolve(new ThreeModel(path, gltf));
      });
    });
  }
  private clipNames: { [name: string]: string } = {};

  createInstances(state: GpuWorldState, opts: CreateModelInstanceOptions = {}) {
    const roots = opts.rootEntities ?? state.ecs.createEntities((opts.rootEntityInit ?? new EntityInit())
      .setComponent(LocalToWorld.gpuComponent)
      .setComponent(AnimationBinderComponent, { entityLookup: {}, clipNames: {} })
      .setComponent(AnimationControllerComponent, {
        actions: []
      })
      .setComponent(VisibleComponent, false),
      opts.count ?? 1
    )
    const entityLookup: { [key: string]: EntitiesRange } = {};
    const skinMeshes: Array<[THREE.SkinnedMesh, EntitiesRange]> = [];
    for (const c of this.model.scene.children) {
      this.createRecursive(state, c, roots, roots, entityLookup, skinMeshes, (opts.localToParentStartOrder ?? 0) + 1, opts);
    }
    for (const [o, entities] of skinMeshes) {
      for (let i=0; i < (opts.count ?? 1); i++) {
        const boneEntityCells = o.skeleton.bones.map(bone => entityLookup[bone.name].getIndex(i));
        const boneCount = Math.min(MaxBones, boneEntityCells.length);
        const bones = new Float32Array(boneCount * 4);
        for (let i=0; i < boneCount; i++) {
          bones[i * 4 + 0] = boneEntityCells[i].x;
          bones[i * 4 + 1] = boneEntityCells[i].y;
        }
        state.ecs.setComponentData(SkinmeshBones.component, entities.getIndex(i), bones);
      }
    }
    for (let i=0; i < (opts.count ?? 1); i++) {
      const lookup = {};
      for (const key of Object.keys(entityLookup)) {
        lookup[key] = entityLookup[key].getIndex(i);
      }
      state.ecs.setComponentData(AnimationBinderComponent, roots.getIndex(i), {
        entityLookup: lookup,
        clipNames: this.clipNames
      });
    }
    state.ecs.setComponentData(VisibleComponent, roots, opts.visible ?? true);
    for (const clip of Object.keys(this.animationClips)) {
      state.data.animationClips[clip] = this.animationClips[clip];
    }
    return { roots, entities: Object.keys(entityLookup).map(k => entityLookup[k]) };
  }
  private createRecursive(state: GpuWorldState, object: THREE.Object3D,
    rootEntities: EntitiesRange,
    parentEntities: EntitiesRange, entityLookup: { [name: string]: EntitiesRange },
    skinMeshes: Array<[THREE.SkinnedMesh, EntitiesRange]>,
    order: number, opts: CreateModelInstanceOptions) {
    let entityInit: EntityInit | undefined = new EntityInit()
      .setComponent(LocalToParent.component, Mat4.toRows(Mat4.fromArray(object.matrix.elements)))
      .setComponent(TransformParent.component)
      .setComponent(LocalToParentOrderComponents[order])
      .setComponent(LocalToWorld.gpuComponent, Mat4.toRows(Mat4.fromArray(object.matrixWorld.elements)))
      .setComponents(Position.components, new Float32Array([object.position.x, object.position.y, object.position.z, 0]))
      .setComponents(Quaternion.components, new Float32Array([object.quaternion.x, object.quaternion.y, object.quaternion.z, object.quaternion.w]))
      .setComponents(Scale.components, new Float32Array([object.scale.x, object.scale.y, object.scale.z, 0]))
      .setComponent(NameComponent, { value: object.name });
    if (object.type === 'Object3D' || object.type === 'Group') {
      // Do nothing
    } else if (object.type === 'Bone') {
      const offsetMatrix = new THREE.Matrix4();
      offsetMatrix.getInverse(object.matrixWorld);
      entityInit = entityInit
        .setComponent(BoneOffset.component, Mat4.toRows(Mat4.fromArray(offsetMatrix.elements)))
        .setComponent(BoneMatrix.component);
    } else if (object.type === 'SkinnedMesh' || object.type === 'Mesh') {
      const o = object as THREE.Mesh;
      const geom = o.geometry;
      if (geom instanceof THREE.BufferGeometry) {
        const mat = (o.material as THREE.MeshStandardMaterial);
        const mesh = '' + o.geometry.id;
        const cullFace = mat.side === THREE.FrontSide ? CullFace.Back
          : mat.side === THREE.BackSide ? CullFace.Front
          : CullFace.None;
        entityInit = entityInit
          .setComponent(VisibleComponent, true)
          .setComponent(VisibilityFromComponent, { value: { x: -1, y: -1 } })
          .setComponent(MaterialColor.component)
          .setComponent(MaterialUseLighting.component, [new Float32Array([mat.name.indexOf('NoLighting') === -1 ? 1 : 0])]);
        if (object.type === 'SkinnedMesh') {
          entityInit = entityInit
            .setComponent(SkinmeshBones.component)
            .setComponent(SkinmeshBindMatrix.component, Mat4.fromArray(o.matrixWorld.elements) as Float32Array)
        }
        entityInit = entityInit
          .setComponent(RenderableComponent, {
            mesh,
            cullFace,
            skinning: object.type === 'SkinnedMesh',
            transparent: false
          });
      } else {
        throw new Error(`Not implemented yet`);
      }
    } else if (object.type === 'PerspectiveCamera' || object.type === 'OrthographicCamera') {
      return; // Ignore
    } else {
      throw new Error(`Not implemented yet`);
    }

    if (opts.beforeEntityCreated) {
      entityInit = opts.beforeEntityCreated(object, entityInit);
    }

    let entities: EntitiesRange | undefined;
    if (entityInit) {
      entities = state.ecs.createEntities(entityInit, opts.count);
      if (entityInit.hasComponent(TransformParent.component)) {
        for (let i=0; i < (opts.count ?? 1); i++) {
          state.ecs.setComponentData(TransformParent.component, entities.getIndex(i), [{ ...parentEntities.getIndex(i), z: 0, w: 0 }])
        }
      }
      if (object.type === 'SkinnedMesh' || object.type === 'Mesh') {
        const o = object as THREE.Mesh;
        const mat = (o.material as THREE.MeshStandardMaterial);
        for (let i=0; i < (opts.count ?? 1); i++) {
          state.ecs.setComponentData(VisibilityFromComponent, entities.getIndex(i), { value: rootEntities.getIndex(i) });
          const color = opts.onMaterial?.(mat, i) ??
            (mat.emissiveIntensity === 1 ? threeColorToVec4(mat.emissive) : threeColorToVec4(mat.color));
          state.ecs.setComponentData(MaterialColor.component, entities.getIndex(i), [new Float32Array([color.x, color.y, color.z, color.w])])
          if (color.w < 1) {
            const r = state.ecs.getComponentData(RenderableComponent, entities.getIndex(i))!;
            state.ecs.setComponentData(RenderableComponent, entities.getIndex(i), {
              ...r,
              transparent: true
            });
          }
        }
      }
      if (object.type === 'SkinnedMesh') {
        skinMeshes.push([object as THREE.SkinnedMesh, entities]);
      }
      if (opts.afterEntityCreated) {
        opts.afterEntityCreated(object, entities);
      }

      entityLookup[object.name] = entities;

      for (const c of object.children) {
        this.createRecursive(state, c, rootEntities, entities, entityLookup, skinMeshes, order + 1, opts);
      }
    }
    return entities;
  }

  createCamera(state: GpuWorldState, cameraName = 'Camera') {
    const writeCamera = createCamera(state);
    this.writeCamera(state, writeCamera, cameraName);
    return writeCamera;
  }
  writeCamera(state: GpuWorldState, writeCamera: EntityRef, cameraName = 'Camera') {
    const camera = this.model.scene.getObjectByName(cameraName) as THREE.PerspectiveCamera;
    state.ecs.setComponentData(Position.cpuComponent, writeCamera, new Float32Array([camera.position.x, camera.position.y, camera.position.z]));
    state.ecs.setComponentData(Quaternion.cpuComponent, writeCamera, new Float32Array([camera.quaternion.x, camera.quaternion.y, camera.quaternion.z, camera.quaternion.w]));
    state.ecs.setComponentData(Scale.cpuComponent, writeCamera, new Float32Array([-camera.scale.x, -camera.scale.y, camera.scale.z]));
    state.ecs.setComponentData(Camera.Fovy, writeCamera, new Float32Array([camera.fov]));
    state.ecs.setComponentData(Camera.Near, writeCamera, new Float32Array([camera.near]));
    state.ecs.setComponentData(Camera.Far, writeCamera, new Float32Array([camera.far]));
  }

  getCamera(name = 'Camera') {
    const camera = this.model.scene.getObjectByName(name) as THREE.PerspectiveCamera;
    var lookAtVector = new THREE.Vector3(0, 0, -1);
    lookAtVector.applyQuaternion(camera.quaternion);
    return full<LookatCamera>({
      view: {
        type: 'LookatView',
        eye: camera.position,
        lookAt: Vector3.add(camera.position, lookAtVector),
        up: { x: 0, y: 0, z: 1 },
        screenOffset: { x: 0, y: 0 }
      },
      projection: {
        type: 'Perspective',
        fovy: camera.fov,
        aspectRatio: camera.aspect,
        near: camera.near,
        far: camera.far
      }
    })
  }

}
export function threeColorToVec3(color: THREE.Color): Vector3.Vector3 {
  return { x: color.r, y: color.g, z: color.b };
}
export function threeColorToVec4(color: THREE.Color): Vector4.Vector4 {
  return { x: color.r, y: color.g, z: color.b, w: 1 };
}

