import { GlState } from "../../GpuUtil/GlState";
import { GpuWorldState } from "../State";
import { UpdateSenses, UpdateGymExtraSenses } from "./Senses";
import { Hitpoints } from "../ECS/UnitComponents";
import { ArchetypesQuery } from "../../GpuUtil/ECS/Query";
import { shaderModule, funcDef } from "../../GpuUtil/ShaderModule";
import { WithoutNaNInfSM } from "../../GpuUtil/SM2Utils";
import { HiddenLayerLayout, HiddenLayer, OutputLayerLayout, OutputLayer, Senses, MemoryLayer, HiddenAndMemoryInputSlices, SensesSlices, MemorySlices, MemoryLayerLayout } from "./Components";
import { GpuComponentReader } from "../../GpuUtil/ECS/GpuComponentSM";
import { UpdateDecisionsViz } from "../Renderer/DecisionsViz";

function DenseLayerSM2(weights: GpuComponentReader, biases: GpuComponentReader, nInputSlices: number, nOutputSlices: number, activation: boolean) {
  return shaderModule('DenseLayer', [
    WithoutNaNInfSM,
    weights,
    biases
  ], {
    calcNeuron: funcDef('float', [['int', 'outputIndex'], ['vec4', `inputs[${nInputSlices}]`], ['ivec2', 'entityCell']], `
      float v = 0.0;
      for (int i=0; i < ${nInputSlices}; i++) {
        vec4 weight = ${weights.value}(entityCell, outputIndex * ${nInputSlices} + i);
        v += dot(inputs[i], weight);
      }
      float bias = ${biases.value}(entityCell, outputIndex);
      return v + bias;
    `),
    runLayer: funcDef(`void`, [[`vec4[${nInputSlices}]`, 'inputs'], ['ivec2', 'entityCell']], `
      ${new Array(nOutputSlices).fill(0).map((_, outputSlice) => `{
        for (int l=0; l < 4; l++) {
          int outputIndex = ${outputSlice * 4} + l;
          float value = Self.calcNeuron(outputIndex, inputs, entityCell);
          value = ${activation ? 'tanh(value)' : 'value'};
          value = clamp(value, -10.0, 10.0);
          value = ${WithoutNaNInfSM.withoutNaNInf}(value);
          outValues[${outputSlice}][l] = value;
        }
      }`).join('\n')}
    `)
  })
}

const BrainRunningSM2 = shaderModule([Hitpoints.sm], {
  running: funcDef('bool', [['ivec2', 'entityCell']], `
    return ${Hitpoints.sm.value}(entityCell) > 0.0;
  `)
})


class UpdateHiddenLayer {
  constructor(private gls: GlState) {
  }

  dense = DenseLayerSM2(
    HiddenLayer.weights.sm, HiddenLayer.biases.sm,
    HiddenLayerLayout.inputSlices, HiddenLayerLayout.outputSlices, true);
  query = new ArchetypesQuery([HiddenLayer.res.component, HiddenLayer.weights.component, HiddenLayer.biases.component])
    .updateGpu(this.gls, [HiddenLayer.res.component], shaderModule([
      BrainRunningSM2,
      this.dense,
      Senses.sm,
      MemoryLayer.res.sm,
    ], {
      update: funcDef('void', [['ivec2', 'entityCell']], `
        if (!${BrainRunningSM2.running}(entityCell)) {
          return;
        }
        vec4 inputs[${HiddenAndMemoryInputSlices}];
        for (int i=0; i < ${SensesSlices}; i++) {
          inputs[i] = ${Senses.sm.value}(entityCell, i);
        }
        for (int i=0; i < ${MemorySlices}; i++) {
          inputs[${SensesSlices} + i] = ${MemoryLayer.res.sm.value}(entityCell, i);
        }
        ${this.dense.runLayer}(inputs, entityCell);
      `)
    }));

  run(state: GpuWorldState) {
    this.query.run(state);
  }
}

