class UpdateOutputLayer {
  constructor(private gls: GlState) {
  }


  dense = DenseLayerSM2(
    OutputLayer.weights.sm, OutputLayer.biases.sm,
    OutputLayerLayout.inputSlices, OutputLayerLayout.outputSlices, false);
  query = new ArchetypesQuery([OutputLayer.res.component, OutputLayer.weights.component, OutputLayer.biases.component])
    .updateGpu(this.gls, [OutputLayer.res.component], shaderModule([
      BrainRunningSM2,
      this.dense,
      OutputLayer.res.writer,
      HiddenLayer.res.sm
    ], {
      update: funcDef('void', [['ivec2', 'entityCell']], `
        if (!${BrainRunningSM2.running}(entityCell)) {
          return;
        }
        vec4 inputs[${OutputLayerLayout.inputSlices}];
        for (int i = 0; i < ${OutputLayerLayout.inputSlices}; i++) {
          inputs[i] = ${HiddenLayer.res.sm.value}(entityCell, i);
        }
        ${this.dense.runLayer}(inputs, entityCell);
      `)
    }));


  run(state: GpuWorldState) {
    this.query.run(state);
  }
}

export class UpdateNeuralNetwork {
  constructor(private gls: GlState) {
  }
  senses = new UpdateSenses(this.gls);
  gymExtraSenses = new UpdateGymExtraSenses(this.gls);
  hiddenLayer = new UpdateHiddenLayer(this.gls);
  memoryLayer = new UpdateMemoryLayer(this.gls);
  outputLayer = new UpdateOutputLayer(this.gls);
  decisionsViz = new UpdateDecisionsViz(this.gls);
  run(state: GpuWorldState) {
    if (state.data.gameInstance.type !== 'battle' && state.data.gameInstance.type !== 'train') {
      return;
    }
    const stepsSinceRoundStart = state.data.stepCounter - state.data.battleStartStep;
    if (stepsSinceRoundStart % 8 === 1) {
      this.senses.run(state);
    }
    if (stepsSinceRoundStart % 8 === 2) {
      this.hiddenLayer.run(state);
    }
    if (stepsSinceRoundStart % 8 === 3) {
      this.memoryLayer.run(state);
    }
    if (stepsSinceRoundStart % 8 === 4) {
      this.outputLayer.run(state);
      this.decisionsViz.run(state);
    }
  }
}
