import { GlState, GL } from "../GlState";
import { GpuBuffer, ECSConsts, GpuComponent, Archetype, chunkAddressFromEntityCell, componentFormatChannels, EntitiesRange } from ".";
import { Texture, TextureSlicing } from "../Texture";
import { Vector2 } from "../../Math/Vector2";
import { Vector4 } from "../../Math/Vector4";
import { EntityRef } from "../../GpuWorld/StateData";
import { Image } from "../Image";
import { flatMap } from "lodash";

export function componentBufferDepth(component: GpuComponent) {
  if (component.buffer.format === 'float') {
    return componentFormatChannels(component.format) * (component.depth ?? 1);
  } else {
    if (component.format === 'mat4') {
      return 4 * (component.depth ?? 1);
    } else {
      return component.depth ?? 1;
    }
  }
}

interface GpuBufferArchetype {
  archetypeIndex: number;
  dataRowOffset: number;
  chunks: number;
}
export class GpuBufferData {
  constructor(private gls: GlState,
    public def: GpuBuffer) {
  }
  destroy() {
    this.texture.destroy();
  }

  dataRowToArchetype: Int32Array = new Int32Array(0);
  archetypeToDataRowOffset: Int32Array = new Int32Array(0);

  size = { width: ECSConsts.ChunkSize, height: 1, depth: 1 };
  texture = Texture.create(this.gls,
    getGpuBufferInternalFormat(this.def.format),
    this.size, this.size.depth).init();

  public components: {
    [key: string]: {
      layerOffset: number;
    };
  } = {};

  public archetypes: Array<GpuBufferArchetype | undefined> = [];

  public get sizeVec2() { return { x: this.size.width, y: this.size.height }; }

  private copy = this.gls.getCopyTexture();

  removeAllEntities() {
    this.dataRowToArchetype = new Int32Array(0);
    this.archetypeToDataRowOffset = new Int32Array(0);
    this.size = { width: ECSConsts.ChunkSize, height: 1, depth: 1 };
  }

  enuserComponentAllocated(component: GpuComponent) {
    const c = this.components[component.name];
    if (!c) {
      const oldDepth = Object.keys(this.components).length === 0 ? 0 : this.size.depth;
      this.components[component.name] = {
        layerOffset: oldDepth
      };
      const newDepth = oldDepth + componentBufferDepth(component);
      this.size.depth = newDepth;
      if (this.texture.depth! < newDepth) {
        const texOldDepth = this.texture.depth!;
        const tmp = this.gls.getTmpTextureSlice(
          getGpuBufferInternalFormat(this.def.format),
          this.texture.size, newDepth
        );
        tmp.clear([0, 0, 0, 0]);
        this.copy.copy(tmp.sliceRange(0, texOldDepth), this.texture.sliceAll());
        this.texture.destroy();
        this.texture = Texture.create(this.gls,
          getGpuBufferInternalFormat(this.def.format),
          this.texture.size, newDepth).init();
        this.copy.copy(this.texture.sliceAll(), tmp);
      }
    }
  }

  setArchetypeChunks(archetype: Archetype, chunks: number) {
    const arch: GpuBufferArchetype = this.archetypes[archetype.index] || {
      archetypeIndex: archetype.index,
      dataRowOffset: -1000,
      chunks: 0
    };
    if (arch.chunks === chunks) {
      return;
    }
    this.archetypes[archetype.index] = arch;

    const heightChange = chunks - arch.chunks;
    const oldHeight = this.dataRowToArchetype.length;
    const newHeight = oldHeight + heightChange;
    const tmp = this.gls.getTmpTextureSlice(
      getGpuBufferInternalFormat(this.def.format),
      { width: this.size.width, height: newHeight }, this.size.depth);
    tmp.clear([0, 0, 0, 0]);

    if (arch.dataRowOffset < 0) {
      arch.dataRowOffset = oldHeight;
    }

    const sourceTop = this.texture.sliceAll().selectAbsolute({
      x: 0,
      y: 0,
      width: this.size.width,
      height: arch.dataRowOffset + arch.chunks
    });
    const destTop = tmp.selectAbsolute({
      x: 0,
      y: 0,
      width: this.size.width,
      height: arch.dataRowOffset + arch.chunks
    });

    const sourceBottom = this.texture.sliceAll().selectAbsolute({
      x: 0,
      y: arch.dataRowOffset + arch.chunks,
      width: this.size.width,
      height: oldHeight - (arch.dataRowOffset + arch.chunks)
    });
    const destBottom = tmp.selectAbsolute({
      x: 0,
      y: arch.dataRowOffset + chunks,
      width: this.size.width,
      height: sourceBottom.rect.height
    });
    this.copy.copy(destTop, sourceTop);
    if (sourceBottom.rect.height > 0) {
      this.copy.copy(destBottom, sourceBottom);
    }
    if (this.texture.size.height < tmp.rect.height) {
      this.texture.destroy();
      this.texture = Texture.create(this.gls,
        getGpuBufferInternalFormat(this.def.format),
        { width: tmp.rect.width, height: tmp.rect.height }, this.texture.depth).init();
    }
    this.copy.copy(this.texture.sliceAll(), tmp);
    this.size.height = tmp.rect.height;

    arch.chunks = chunks;
    for (let i=arch.archetypeIndex + 1; i < this.archetypes.length; i++) {
      const a = this.archetypes[i];
      if (a && a.dataRowOffset >= 0) {
        a.dataRowOffset += heightChange;
      }
    }

    this.dataRowToArchetype = new Int32Array(this.size.height).fill(-1);
    for (const arch2 of this.archetypes) {
      if (arch2 && arch2.dataRowOffset >= 0) {
        for (let r=0; r < arch2.chunks; r++) {
          this.dataRowToArchetype[arch2.dataRowOffset + r] = arch2.archetypeIndex;
        }
      }
    }

    this.archetypeToDataRowOffset = new Int32Array(this.archetypes.map(a => a ? a.dataRowOffset : -1000));
  }

  dataCell(entityCell: Vector2) {
    const { chunkIndex, entityIndex, archIndex } = chunkAddressFromEntityCell(entityCell);
    const arch = this.archetypes[archIndex];
    if (!arch) {
      return undefined;
    }
    return {
      x: entityIndex,
      y: chunkIndex + arch.dataRowOffset
    }
  }

  getArchetypeComponentDataSlice(component: GpuComponent, arch: Archetype) {
    const buffArch = this.archetypes[arch.index]!;
    return this.texture
      .sliceRange(this.components[component.name].layerOffset, componentBufferDepth(component))
      .selectAbsolute({
        x: 0,
        y: buffArch.dataRowOffset,
        width: arch.chunks.reduce((p, x) => Math.max(p, x.entities), 0),
        height: arch.chunks.length
      })
  }
  getArchetypeComponentData(component: GpuComponent, arch: Archetype) {
    const img = this.getArchetypeComponentDataSlice(component, arch).readSync();
    return flatMap(arch.chunks, (chunk, i) => {
      const data = img.select({
        x: 0,
        y: i,
        width: chunk.entities,
        height: 1
      });
      return imageToComponentData(component, data.toImage());
    })
  }
  *getRangeComponentDataSlices(component: GpuComponent, range: EntitiesRange): Generator<TextureSlicing> {
    if (range.count === 0) {
      return;
    }
    const buffArch = this.archetypes[range.archetypeIndex]!;
    const slice = this.texture
      .sliceRange(this.components[component.name].layerOffset, componentBufferDepth(component))
      .selectAbsolute({
        x: 0,
        y: buffArch.dataRowOffset,
        width: ECSConsts.ChunkSize,
        height: 1
      });
    let start = chunkAddressFromEntityCell({ x: range.startIndex, y: range.archetypeIndex });
    if (start.entityIndex !== 0) {
      const end = Math.min(ECSConsts.ChunkSize, start.entityIndex + range.count);
      const count = end - start.entityIndex;
      yield slice.selectRelative({
        x: start.entityIndex,
        y: 0,
        width: count,
        height: 1
      });
      for (const res of this.getRangeComponentDataSlices(component, new EntitiesRange(range.archetypeIndex,
          range.startIndex + count,
          range.count - count
        ))) {
        yield res;
      }
    } else {
      const end = chunkAddressFromEntityCell({ x: range.startIndex + range.count - 1, y: range.archetypeIndex });
      if (end.entityIndex !== 0) {
        for (const res of this.getRangeComponentDataSlices(component, new EntitiesRange(range.archetypeIndex,
            range.startIndex,
            range.count - (end.entityIndex + 1)
          ))) {
          yield res;
        }
        yield slice.selectRelative({
          x: 0,
          y: end.chunkIndex,
          width: end.entityIndex + 1,
          height: 1
        });
      } else {
        yield slice.selectRelative({
          x: 0,
          y: start.chunkIndex,
          width: ECSConsts.ChunkSize,
          height: end.chunkIndex - start.chunkIndex
        });
      }
    }
  }

  getEntityComponentDataSlice(component: GpuComponent, entityCell: Vector2) {
    const dataCell = this.dataCell(entityCell);
    if (!dataCell) {
      return undefined;
    }
    const dataRect = {...dataCell, width: 1, height: 1};
    const comp = this.components[component.name];
    return this.texture.sliceRange(comp.layerOffset, componentBufferDepth(component)).selectAbsolute(dataRect);
  }
  setComponentData(component: GpuComponent, entity: EntityRef | EntitiesRange, data: Float32Array[]) {
    if (entity instanceof EntitiesRange) {
      if (entity.count === 1) {
        this.setComponentData(component, { x: entity.startIndex, y: entity.archetypeIndex }, data);
      } else {
        this.setRangeComponentData(component, entity, data);
      }
    } else {
      const dataSlice = this.getEntityComponentDataSlice(component, entity);
      if (!dataSlice) {
        throw new Error(`No data cell for entity ${JSON.stringify(entity)}`);
      }
      if (data.length > dataSlice.layers.length) {
        console.warn(`GpuBuffer.setComponentData: More data provided (${data.length}) than fits the target component "${component.name}" (${dataSlice.layers.length}). The data uploaded will be truncated.`)
      }
      for (let l=0; l < data.length && l < dataSlice.layers.length; l++) {
        dataSlice.slice([l]).upload(data[l]);
      }
    }
  }
  setRangeComponentData(component: GpuComponent, entities: EntitiesRange, data: Float32Array[]) {
    for (const dataSlice of this.getRangeComponentDataSlices(component, entities)) {
      if (!dataSlice) {
        throw new Error(`No data for range ${JSON.stringify(entities)}`);
      }
      if (data.length > dataSlice.layers.length) {
        console.warn(`GpuBuffer.setChunksComponentData: More data provided (${data.length}) than fits the target component "${component.name}" (${dataSlice.layers.length}). The data uploaded will be truncated.`)
      }
      for (let l=0; l < dataSlice.layers.length; l++) {
        let d: Float32Array | [number, number, number, number] = data[l];
        if (d.length === 1) {
          d = [d[0], d[0], d[0], d[0]];
        }
        dataSlice.slice([l]).clear(d);
      }
    }
  }
  getComponentData(component: GpuComponent, entityCell: Vector2, asyncr: boolean, raw: boolean) {
    const slice = this.getEntityComponentDataSlice(component, entityCell);
    if (!slice) {
      if (asyncr) {
        return Promise.resolve(undefined);
      } else {
        return undefined;
      }
    }
    const finalize = img => imageToComponentData(component, img);
    if (asyncr) {
      const p = slice.readAsync();
      return raw ? p : p.then(finalize);
    } else {
      const v = slice.readSync();
      return raw ? v : finalize(v);
    }
  }
}
function imageToComponentData(component: GpuComponent, img: Image) {
  if (component.buffer.format === 'float') {
    return Array.from(img.data);
  } else {
    const v: Vector4[] = [];
    for (let i=0; i < componentBufferDepth(component); i++) {
      v.push(img.readPixel({ x: 0, y: 0, z: i }));
    }
    return v;
  }
}

function getGpuBufferInternalFormat(format: GpuBuffer['format']) {
  switch (format) {
    case 'float': return GL.R32F;
    case 'vec4': return GL.RGBA32F;
  }
}
