import { GroundHeightSM, GroundNormalSM } from "./Ground";
import { GlState } from "../../GpuUtil/GlState";
import { GpuWorldState } from "../State";
import { Position, RotationZ } from "./Transform";
import { ArchetypesQuery, GpuLookupTableSM } from "../../GpuUtil/ECS/Query";
import { shaderModule, funcDef, uniformDef } from "../../GpuUtil/ShaderModule";
import { Unit, Hitpoints, Velocity, Force, IsOnGround, Gold, WalkingSpeed, Stuck, LastPushedBy, IsTakingFallDamage, BountyComponents } from "./UnitComponents";
import { ArenaInstance, ArenaMemberIndex } from "./Components";
import { SpatialSM } from "../../GpuUtil/SM2Utils";
import { SideSize } from "../StateLayout";

export class UpdateUnitPhysics {
  constructor(private gls: GlState) {}

  update = new ArchetypesQuery([Velocity.component, Force.component, Position.gpuComponent, IsOnGround.component, Unit.component])
    .updateGpu(this.gls, [
      Velocity.component, Force.component, Position.gpuComponent, IsOnGround.component,
      Hitpoints.component, Gold.component, Stuck.component, IsTakingFallDamage.component
    ],
      shaderModule([
        Velocity.writer, Velocity.sm, IsOnGround.sm, IsOnGround.writer, Force.sm,
        Force.writer, Position.sm, Position.writer, GroundHeightSM, GroundNormalSM,
        Hitpoints.sm, Hitpoints.writer, GpuLookupTableSM, ArenaInstance.sm,
        Hitpoints.sm, RotationZ.sm, SpatialSM, WalkingSpeed.sm, Gold.sm, Gold.writer,
        Stuck.writer, ArenaMemberIndex.sm, IsTakingFallDamage.writer,
        BountyComponents.fallDamageTaken.sm
      ], {
        timestep: uniformDef('float', d => d.data.physicsTimestep),
        unitsInArena: uniformDef('isampler2D', d => d.unitsInArenaGpu.get(d)),
        clampPositions: uniformDef('bool', d => d.data.gameInstance.type === 'gym'),
        mapCenter: uniformDef('vec2', d => ({ x: d.data.worldSize.width / 2, y: d.data.worldSize.width / 2 })),
        update: funcDef('void', [['ivec2', 'entityCell']], `
          // https://gamedev.stackexchange.com/questions/15708/how-can-i-implement-gravity/41917#41917
          vec3 position = ${Position.sm.value}(entityCell);
          vec3 velocity = ${Velocity.sm.value}(entityCell);
          vec3 force = ${Force.sm.value}(entityCell);
          bool isOnGround = ${IsOnGround.sm.value}(entityCell);
          float hitpoints = ${Hitpoints.sm.value}(entityCell);
          float walking = ${WalkingSpeed.sm.value}(entityCell);
          bool selfAlive = hitpoints > 0.;
          float gold = ${Gold.sm.value}(entityCell);
          vec3 prevPosition = position;
          if (length(force) > 0.01 || velocity.z > 0.1) {
            isOnGround = false;
          }
          if (isOnGround) {
            float rotation = ${RotationZ.sm.value}(entityCell);
            vec2 forward = ${SpatialSM.forward}(rotation);
            velocity = vec3(forward, 0) * walking;
            vec3 delta = Self.timestep * velocity;
            position += delta;
            float height = ${GroundHeightSM.atPosition}(position.xy);
            float elevation = position.z - height;
            vec3 normal = ${GroundNormalSM.atPosition}(position.xy);
            if (elevation > 0.1) {
              isOnGround = false;
            } else if (normal.z < 0.7 && height > prevPosition.z) { // sin(pi/4) = 0.7 -> 45deg incline
              // Prevent hillclimbing
              position = prevPosition;
              position.z = ${GroundHeightSM.atPosition}(position.xy);
            } else {
              position.z = height;
            }
          } else {
            const float G = 9.82 * 6.0;
            const float mass = 1.0;
            vec3 acceleration = force / mass;
            acceleration += vec3(0, 0, -G);
            velocity += Self.timestep * acceleration;
            position += Self.timestep * velocity;
            float height = ${GroundHeightSM.atPosition}(position.xy);
            if (position.z <= height) {
              if (velocity.z < -40.) {
                // Fall damage
                if (selfAlive) {
                  hitpoints -= 10000.;
                  int arenaMemberIndex = ${ArenaMemberIndex.sm.value}(entityCell);
                  gold += ${BountyComponents.fallDamageTaken.sm.value}(entityCell);
                  ${IsTakingFallDamage.writer.write}(true);
                }
              }
              if (prevPosition.z < height && velocity.z < -10.) {
                // We hit a "wall", so start falling straight down instead
                // to avoid characters "leaping up from the abyss"
                position = prevPosition;
                velocity.xy = vec2(0);
              } else {
                position.z = height;
                // We're on the ground, but might be sliding
                velocity -= normalize(velocity) * length(velocity) * 0.1; // Friction
                if (length(velocity.xy) < 1.) {
                  velocity = vec3(0);
                  isOnGround = true;
                } else {
                  velocity.z = -0.1;
                }
              }
            }
          }
          // Unit-unit collisions
          if (selfAlive) {
            int arenaIndex = ${ArenaInstance.sm.value}(entityCell);
            int unitsCount = ${SideSize * 2};
            vec3 unitCenter = vec3(0, 0, 1);
            vec3 selfCenter = position + unitCenter;
            const float unitRadius = 0.5;
            for (int i=0; i < unitsCount; i++) {
              ivec2 other = ${GpuLookupTableSM.entityCell}(Self.unitsInArena, arenaIndex, i);
              if (other == entityCell || other.x < 0) {
                continue;
              }
              bool otherAlive = ${Hitpoints.sm.value}(other) > 0.;
              if (!otherAlive) {
                continue;
              }
              vec3 otherCenter = ${Position.sm.value}(other) + unitCenter;
              vec3 delta = selfCenter - otherCenter;
              if (length(delta) < unitRadius * 2.) {
                selfCenter = otherCenter + normalize(delta) * unitRadius * 2.;
              }
            }
            position = selfCenter - unitCenter;
          }
          if (clampPositions) {
            vec2 size = vec2(20);
            position.xy = clamp(position.xy, mapCenter - size, mapCenter + size);
          }
          ${Velocity.writer.write}(velocity);
          ${Position.writer.write}(position);
          ${IsOnGround.writer.write}(isOnGround);
          ${Force.writer.write}(vec3(0));
          ${Hitpoints.writer.write}(hitpoints);
          ${Gold.writer.write}(gold);
          ${Stuck.writer.write}(distance(prevPosition, position) < walking * Self.timestep * 0.5 && isOnGround && walking > 0.01);
        `)
      }));

  fallDamageKillBounty = new ArchetypesQuery([LastPushedBy.component])
      .updateGpu(this.gls, [Gold.component], shaderModule([
        LastPushedBy.sm, Gold.writer, IsTakingFallDamage.sm,
        BountyComponents.killEnemyUnit.sm
      ], {
        update: funcDef('ivec2', [['ivec2', 'entityCell']], `
          if (${IsTakingFallDamage.sm.value}(entityCell)) {
            ivec2 lastPushedBy = ${LastPushedBy.sm.value}(entityCell);
            if (lastPushedBy.x >= 0) {
              float bounty = ${BountyComponents.killEnemyUnit.sm.value}(lastPushedBy);
              ${Gold.writer.write}(bounty);
              return lastPushedBy;
            }
          }
          return ivec2(-1);
        `)
      }), 'sum');

  resetLastPushedBy = new ArchetypesQuery([LastPushedBy.component])
      .mapGpu(this.gls, LastPushedBy.component, shaderModule([
        IsOnGround.sm
      ], {
        map: funcDef('ivec2', [['ivec2', 'entityCell'], ['ivec2', 'value']], `
          if (${IsOnGround.sm.value}(entityCell)) {
            return ivec2(-1);
          } else {
            return value;
          }
        `)
      }));

  run(state: GpuWorldState) {
    state.ecs.gpuComponentInfo(IsTakingFallDamage.component)?.slice.clear();
    this.update.run(state);
    this.fallDamageKillBounty.run(state);
    this.resetLastPushedBy.run(state);
  }
}
