import { GLSLBaseType, GLSLType, GLSLTypeMappings, GLSLArrayType } from "./GLSLTypes";
import { GpuWorldState } from "../GpuWorld/State";
import { Program } from "./Program";
import { shaderHeader } from ".";

export interface UniformDef<T extends GLSLBaseType = GLSLBaseType> {
  def: 'uniform';
  type: T;
  value?: (state: GpuWorldState) => GLSLTypeMappings[T];
  required: boolean;
}
export function uniformDef<T extends GLSLBaseType>(type: T, value?: (state: GpuWorldState) => GLSLTypeMappings[T], required = false): UniformDef<T> {
  return { def: 'uniform', type, value, required };
}
export interface UniformArrayDef<T extends GLSLArrayType = GLSLArrayType> {
  def: 'uniformArray';
  type: T;
  size: number;
  value?: (state: GpuWorldState) => GLSLTypeMappings[T];
  required: boolean;
}
export function uniformArrayDef<T extends GLSLArrayType>(type: T, size: number, value?: (state: GpuWorldState) => GLSLTypeMappings[T], required = false): UniformArrayDef<T> {
  return { def: 'uniformArray', type, size, value, required };
}
export interface InDef<T extends string = string> {
  def: 'in';
  type: T;
  flat: boolean;
}
export function inDef<T extends string>(type: T, flat = false): InDef<T> {
  return { def: 'in', type, flat };
}
export interface OutDef<T extends string = string> {
  def: 'out';
  type: T;
  flat: boolean;
}
export function outDef<T extends string>(type: T, flat = false): OutDef<T> {
  return { def: 'out', type, flat };
}
export interface ConstDef<T extends string = string> {
  def: 'const';
  type: T;
  value: string;
}
export function constDef<T extends string>(type: T, value: string): ConstDef<T> {
  return { def: 'const', type, value };
}
export interface StructDef<M extends { [key: string]: string } = { [key: string]: string }> {
  def: 'struct';
  members: M;
}
export function structDef<M extends { [key: string]: string }>(members: M): StructDef<M> {
  return { def: 'struct', members };
}
type FuncArg = [string, string] | ['out' | 'in' | 'inout', string, string];
export interface FunctionDef<R extends string, As extends Array<FuncArg>> {
  def: 'func';
  returnType: R;
  args: As;
  body: string;
}
export function funcDef<R extends string, As extends Array<FuncArg>>(
  returnType: R, args: As, body: string): FunctionDef<R, As> {
  return {
    def: 'func',
    returnType,
    args,
    body
  }
}

export interface OverloadedFunctionDef {
  def: 'overloadedfunc';
  funcs: FunctionDef<string, Array<FuncArg>>[];
}

export function overloadedFuncDef(funcs: FunctionDef<string, Array<FuncArg>>[]): OverloadedFunctionDef {
  return {
    def: 'overloadedfunc',
    funcs
  }
}


type RawDef = UniformDef | UniformArrayDef | InDef | OutDef | ConstDef |
  StructDef | FunctionDef<string, Array<FuncArg>> | OverloadedFunctionDef;
type Def<T extends RawDef> = T & { name: string, toString: () => string };

type ShaderModuleDef = { [key: string]: RawDef };
export type ShaderModule<M extends ShaderModuleDef = ShaderModuleDef> = { [K in keyof M]: Def<M[K]> };
export function shaderModule<M extends ShaderModuleDef>(dependencies: ShaderModule[], sm: M): ShaderModule<M>;
export function shaderModule<M extends ShaderModuleDef>(name: string, dependencies: ShaderModule[], sm: M): ShaderModule<M>;
export function shaderModule(...args: any[]) {
  let sm: { [key: string]: Def<RawDef> };
  let dependencies: ShaderModule[];
  let name: string = '';
  if (args.length === 2) {
    dependencies = args[0];
    sm = args[1];
  } else {
    name = args[0];
    dependencies = args[1];
    sm = args[2];
  }
  const res = { dependencies };
  for (const k of Object.keys(sm)) {
    const def = sm[k];
    def.name = name ? `${name}_${k}` : k;
    def.toString = () => def.name;
    if (def.def === 'func') {
      // Replace all Self_something with the actual module name
      const modlPrefix = name ? (name + '_') : '';
      def.body = def.body.split('Self.').join(modlPrefix);
      def.returnType = def.returnType.split('Self.').join(modlPrefix);
    }
    res[k] = def;
  }
  return res;
}

function flattenModules(sm: ShaderModule): { [key: string]: Def<RawDef> } {
  const self = {};
  for (const k of Object.keys(sm)) {
    if (k !== 'dependencies') {
      self[sm[k].name] = sm[k];
    }
  }
  return Object.assign({}, ...[...(sm as any).dependencies.map(d => flattenModules(d)), self])
}

export class CompiledShaderModule {
  constructor(public shaderModule: ShaderModule) {
    this.source = shaderHeader + '\n';
    const mod = flattenModules(shaderModule);
    for (const k of Object.keys(mod)) {
      const def = mod[k];
      this.source += defToGlsl(def.name, def) + '\n\n';
      if (def.def === 'uniform' || def.def === 'uniformArray') {
        if (def.value) {
          this.uniforms.push({
            name: def.name,
            type: def.type,
            getValue: def.value,
            value: null,
            required: def.required
          });
        }
      }
    }
  }
  uniforms: Array<{
    name: string,
    type: GLSLType,
    getValue: (state: GpuWorldState) => any,
    value: any,
    required: boolean
  }> = [];
  source: string;
  updateUniformValues(state: GpuWorldState) {
    for (const uniform of this.uniforms) {
      uniform.value = uniform.getValue(state);
    }
  }
  writeUniforms(program: Program) {
    for (const uniform of this.uniforms) {
      program.setUniform(uniform.name, uniform.type, uniform.value, uniform.required ?? false);
    }
  }
}

function defToGlsl(name: string, def: RawDef) {
  switch (def.def) {
    case 'in':
    case 'out':  return `${def.flat ? 'flat' : ''} ${def.def} ${def.type} ${name};`;
    case 'uniform': return `${def.def} ${def.type} ${name};`;
    case 'uniformArray': return `uniform ${def.type.slice(0, def.type.length - 2)} ${name}[${def.size}];`;
    case 'const': return `const ${def.type} ${name} = ${def.value};`;
    case 'struct': return `struct ${name} {\n${Object.keys(def.members).map(k => `  ${def.members[k]} ${k};`).join('\n')}\n};`;
    case 'func': return `${def.returnType} ${name}(${(def.args as any).map(arg => arg.join(' ')).join(', ')}) {${def.body}}`;
    case 'overloadedfunc': return def.funcs.map(d => defToGlsl(name, d)).join('\n')
  }
}
