import { Size, Rect } from "../Math/Math";
import * as _ from "lodash";
import { Image, ImageDataType } from "./Image";
import { ReadRect, TextureReadPollOptions } from "./TextureReader";
import { GlState, Destructible } from "./GlState";
import { getGlConstantName } from ".";
import { RenderTarget } from "./RenderTarget";

function defaultFormat(gl: WebGL2RenderingContext, internalFormat: number) {
  switch (internalFormat) {
    // From https://www.khronos.org/registry/webgl/specs/latest/2.0/#TEXTURE_TYPES_FORMATS_FROM_DOM_ELEMENTS_TABLE
    case gl.RGB: return gl.RGB;
    case gl.RGBA: return gl.RGBA;

    case gl.R32F: return gl.RED;
    case gl.RG32F: return gl.RG;
    case gl.RGB32F: return gl.RGB;
    case gl.RGBA32F: return gl.RGBA;

    case gl.R8UI: return gl.RED_INTEGER;
    case gl.RG8UI: return gl.RG_INTEGER;
    case gl.RGB8UI: return gl.RGB_INTEGER;
    case gl.RGBA8UI: return gl.RGBA_INTEGER;

    // These I'm just guessing because I didn't see them in the spec above
    case gl.R8I: return gl.RED_INTEGER;
    case gl.RG8I: return gl.RG_INTEGER;
    case gl.RGB8I: return gl.RGB_INTEGER;
    case gl.RGBA8I: return gl.RGBA_INTEGER;

    case gl.R32I: return gl.RED_INTEGER;
    case gl.RG32I: return gl.RG_INTEGER;
    case gl.RGB32I: return gl.RGB_INTEGER;
    case gl.RGBA32I: return gl.RGBA_INTEGER;

    default: throw new Error(`Not implemeted yet`);
  }
}
function defaultType(gl: WebGL2RenderingContext, internalFormat: number) {
  switch (internalFormat) {
    // From https://www.khronos.org/registry/webgl/specs/latest/2.0/#TEXTURE_TYPES_FORMATS_FROM_DOM_ELEMENTS_TABLE
    case gl.RGB: return gl.UNSIGNED_BYTE;
    case gl.RGBA: return gl.UNSIGNED_BYTE;

    case gl.R32F: return gl.FLOAT;
    case gl.RG32F: return gl.FLOAT;
    case gl.RGB32F: return gl.FLOAT;
    case gl.RGBA32F: return gl.FLOAT;

    case gl.R8UI: return gl.UNSIGNED_BYTE;
    case gl.RG8UI: return gl.UNSIGNED_BYTE;
    case gl.RGB8UI: return gl.UNSIGNED_BYTE;
    case gl.RGBA8UI: return gl.UNSIGNED_BYTE;

    // These I'm just guessing because I didn't see them in the spec above
    case gl.R8I: return gl.BYTE;
    case gl.RG8I: return gl.BYTE;
    case gl.RGB8I: return gl.BYTE;
    case gl.RGBA8I: return gl.BYTE;

    case gl.R32I: return gl.INT;
    case gl.RG32I: return gl.INT;
    case gl.RGB32I: return gl.INT;
    case gl.RGBA32I: return gl.INT;

    default: throw new Error(`Not implemeted yet`);
  }
}

function defaultUnpackAlignment(gl: WebGL2RenderingContext, internalFormat: number) {
  switch (internalFormat) {
    case gl.R8UI: return 1;
    case gl.R8I: return 1;
    default: return 4;
  }
}
function defaultPixelBytes(gl: WebGL2RenderingContext, internalFormat: number) {
  switch (internalFormat) {
    case gl.RGB: return 3;
    case gl.RGBA: return 4;

    case gl.R32F: return 4;
    case gl.RG32F: return 8;
    case gl.RGB32F: return 16;
    case gl.RGBA32F: return 32;

    case gl.R8UI: return 1;
    case gl.RG8UI: return 2;
    case gl.RGB8UI: return 3;
    case gl.RGBA8UI: return 4;

    case gl.R8I: return 1;
    case gl.RG8I: return 2;
    case gl.RGB8I: return 3;
    case gl.RGBA8I: return 4;

    case gl.R32I: return 4;
    case gl.RG32I: return 8;
    case gl.RGB32I: return 12;
    case gl.RGBA32I: return 16;

    default: throw new Error(`Not implemeted yet`);
  }
}
function defaultChannels(gl: WebGL2RenderingContext, internalFormat: number) {
  switch (internalFormat) {
    case gl.RGB: return 3;
    case gl.RGBA: return 4;

    case gl.R32F: return 1;
    case gl.RG32F: return 2;
    case gl.RGB32F: return 3;
    case gl.RGBA32F: return 4;

    case gl.R8UI: return 1;
    case gl.RG8UI: return 2;
    case gl.RGB8UI: return 3;
    case gl.RGBA8UI: return 4;

    case gl.R8I: return 1;
    case gl.RG8I: return 2;
    case gl.RGB8I: return 3;
    case gl.RGBA8I: return 4;

    case gl.R32I: return 1;
    case gl.RG32I: return 2;
    case gl.RGB32I: return 3;
    case gl.RGBA32I: return 4;

    default: throw new Error(`Not implemeted yet`);
  }
}
export type TextureFormatType = 'float' | 'int' | 'uint';
function textureFormatType(gl: WebGL2RenderingContext, internalFormat: number): TextureFormatType {
  switch (internalFormat) {
    case gl.RGB:
    case gl.RGBA: return 'float';

    case gl.R32F:
    case gl.RG32F:
    case gl.RGB32F:
    case gl.RGBA32F: return 'float';

    case gl.R8UI:
    case gl.RG8UI:
    case gl.RGB8UI:
    case gl.RGBA8UI: return 'uint';

    case gl.R8I:
    case gl.RG8I:
    case gl.RGB8I:
    case gl.RGBA8I: return 'int';

    case gl.R32I:
    case gl.RG32I:
    case gl.RGB32I:
    case gl.RGBA32I: return 'int';

    default: throw new Error(`Not implemeted yet`);
  }
}

