export class Texture implements Destructible {
  constructor(public gls: GlState,
      public internalFormat: number = gls.gl.RGBA,
      public size: Size = { width: 1, height: 1 },
      public depth?: number,
      public format = defaultFormat(gls.gl, internalFormat),
      public type = defaultType(gls.gl, internalFormat)) {
    this.validateSize();
    const tex = gls.gl.createTexture();
    if (!tex) {
      throw new Error('Failed to create texture');
    }
    this.texture = tex;
    Texture.nAliveTextures++;
    // Texture.aliveTextures.push(this.stackTrace);
    // console.log('Texture.nAliveTextures', Texture.nAliveTextures);
  }
  static create(gls: GlState, internalFormat: number, size: Size, depth?: number, format?: number, type?: number) {
    return new Texture(gls, internalFormat, size, depth, format, type);
  }
  destroyed = false;
  inited = false;
  destroy() {
    if (this.destroyed) {
      throw new Error("Already destroyed");
    }
    this.gl.deleteTexture(this.texture);
    this.destroyed = true;
    Texture.nAliveTextures--;
    // const index = Texture.aliveTextures.indexOf(this.stackTrace);
    // if (index < 0) {
    //   throw new Error("Not among textures");
    // }
    // Texture.aliveTextures.splice(index, 1);
  }
  public texture: WebGLTexture;
  public name = '';
  public isDoubleBuffered: false = false;
  get gl() {
    return this.gls.gl;
  }
  public static nAliveTextures = 0;
  // stackTrace = getStack();
  // static aliveTextures: string[] = [];

  get internalFormatName() {
    return getGlConstantName(this.gl, this.internalFormat);
  }
  get formatName() {
    return getGlConstantName(this.gl, this.format);
  }
  get typeName() {
    return getGlConstantName(this.gl, this.type);
  }
  get is2dArray() {
    return this.depth !== undefined;
  }
  unpackAlignment = defaultUnpackAlignment(this.gl, this.internalFormat);
  formatType = textureFormatType(this.gl, this.internalFormat);
  pixelBytes = defaultPixelBytes(this.gl, this.internalFormat);
  get pixelSizeInBytes() {
    return (this.depth !== undefined ? this.depth : 1) * this.pixelBytes;
  }
  get sizeInBytes() {
    return this.size.width * this.size.height * this.pixelSizeInBytes;
  }
  get size3() { return {...this.size, depth: this.depth || 1 } }
  get pixelSizeOnGPU() { return this.pixelSizeInBytes; }
  get sizeOnGPU() { return this.sizeInBytes; }
  get channels() { return defaultChannels(this.gl, this.internalFormat); }

  init(data: ArrayBufferView | null = null) {
    const gl = this.gl;
    if (data !== null) {
      const validData = (this.formatType === 'float' && data instanceof Float32Array) ||
        (this.formatType === 'int' && data instanceof Int32Array) ||
        (this.formatType === 'uint' && data instanceof Uint32Array) ||
        (this.formatType === 'uint' && data instanceof Uint8Array);
      if (!validData) {
        throw new Error(`Texture format ${this.formatType} but data is ${data.constructor.name}`);
      }
    }
    gl.activeTexture(gl.TEXTURE31);
    if (this.depth !== undefined) {
      gl.bindTexture(gl.TEXTURE_2D_ARRAY, this.texture);
      gl.pixelStorei(gl.UNPACK_ALIGNMENT, this.unpackAlignment);
      gl.texImage3D(gl.TEXTURE_2D_ARRAY, 0, this.internalFormat, this.size.width, this.size.height, this.depth, 0, this.format, this.type, data);
    } else {
      gl.bindTexture(gl.TEXTURE_2D, this.texture);
      gl.pixelStorei(gl.UNPACK_ALIGNMENT, this.unpackAlignment);
      gl.texImage2D(gl.TEXTURE_2D, 0, this.internalFormat, this.size.width, this.size.height, 0, this.format, this.type, data);
    }
    this.inited = true;
    // const err = gl.getError();
    // if (err !== gl.NO_ERROR) {
    //   throw new Error(`[Texture.init] Failed to init texture. Error=${getGlConstantName(gl, err)}. Size=${JSON.stringify(this.size)} Depth=${this.depth} SizeInBytes=${this.sizeInBytes}`);
    // }
    return this;
  }
  loadImage(image: ImageData | HTMLImageElement | HTMLCanvasElement | HTMLVideoElement | Image) {
    if (image instanceof Image) {
      this.size = {...image.size};
      this.init(image.data);
    } else {
      const gl = this.gls.gl;
      gl.activeTexture(gl.TEXTURE31);
      gl.bindTexture(gl.TEXTURE_2D, this.texture);

      gl.pixelStorei(gl.UNPACK_ALIGNMENT, this.unpackAlignment);
      gl.texImage2D(gl.TEXTURE_2D, 0, this.internalFormat, this.format, this.type, image);
      this.size = {
        width: image.width,
        height: image.height
      };
      this.inited = true;
    }
  }
  generateMipmap() {
    const gl = this.gls.gl;
    gl.activeTexture(gl.TEXTURE31);
    gl.bindTexture(gl.TEXTURE_2D, this.texture);
    gl.generateMipmap(gl.TEXTURE_2D);
  }
  static fromImage(gls: GlState, image: ImageData | HTMLImageElement | HTMLCanvasElement | HTMLVideoElement, mipmap = true) {
    const t = new Texture(gls);
    t.loadImage(image);
    if (mipmap) {
      t.generateMipmap();
    }
    return t;
  }
  private validateSize() {
    if (this.size.width < 0 || this.size.height < 0 ||
      !Number.isInteger(this.size.width) || !Number.isInteger(this.size.height)) {
      throw new Error(`Invalid texture size (${this.size.width}, ${this.size.height})`);
    }
    if (this.depth !== undefined) {
      if (this.depth < 1 || !Number.isInteger(this.depth)) {
        throw new Error(`Invalid texture depth (${this.depth})`);
      }
    }
    if (this.size.width > this.gls.maxTextureSize || this.size.height > this.gls.maxTextureSize) {
      throw new Error(`Texture (${this.size.width}x${this.size.height}) larger than max texture size (${this.gls.maxTextureSize})`);
    }
    if (this.size.width === 0 || this.size.height === 0) {
      console.warn(`Texture with 0 size created: ${this.size.width} x ${this.size.height}`);
    }
  }

  slice(layers: number[]) {
    return new TextureSlicing(this, { x: 0, y: 0, width: this.size.width, height: this.size.height }, layers);
  }
  sliceRange(startLayer: number, count: number) {
    return this.slice(new Array(count).fill(0).map((_, i) => startLayer + i));
  }
  sliceAll() {
    return this.sliceRange(0, this.depth || 1);
  }

  clear(color: [number, number, number, number] | Float32Array = [0, 0, 0, 0], layers?: number[], rect?: Rect) {

    if (this.depth === undefined && layers) {
      throw new Error(`This is not a multilayer texture`);
    }

    const rt = new RenderTarget(this.gls);

    const doLayers = this.depth === undefined ? [0]
      : layers !== undefined ? layers
      : new Array(this.depth).fill(0).map((_, i) => i);

    for (const layer of doLayers) {
      rt.set(this.slice([layer]));
      rt.clear(color, 0, rect);
    }

    rt.destroy();
  }

  readSync<T extends ImageDataType = Float32Array>(readRect?: ReadRect) {
    return Image.readTextureSync<T>(this.sliceAll(), readRect);
  }
  readAsync<T extends ImageDataType = Float32Array>(readRect?: ReadRect, asyncOpts?: TextureReadPollOptions) {
    return Image.readTextureAsync<T>(this.sliceAll(), readRect, asyncOpts);
  }
}

export class DoubleBufferedTexture implements Destructible {
  constructor(public texture0: Texture, public texture1: Texture) {}
  static create(gls: GlState, internalFormat: number, size: Size, depth?: number, format?: number, type?: number) {
    return new DoubleBufferedTexture(
      new Texture(gls, internalFormat, size, depth, format, type),
      new Texture(gls, internalFormat, size, depth, format, type)
    );
  }
  destroy() {
    this.front.destroy();
    this.back.destroy();
  }
  get destroyed() { return this.front.destroyed; }
  get inited() { return this.front.inited; }
  public swapped = false;
  public isDoubleBuffered: true = true;

  // Back is the one we write to
  get back() { return this.swapped ? this.texture1 : this.texture0 }
  get write() { return this.back; }

  // Front is the one we read from. It's the "currently active" buffer.
  get front() { return this.swapped ? this.texture0 : this.texture1 }
  get read() { return this.front; }

  swap() {
    this.swapped = !this.swapped;
  }
  resetSwap() {
    this.swapped = false;
  }
  get texture() {
    return this.front.texture;
  }
  get gls() {
    return this.texture0.gls;
  }
  get gl() {
    return this.texture0.gl;
  }
  private selfName;
  get name() {
    return this.selfName;
  }
  set name(value: string) {
    this.selfName = value;
    this.texture0.name = value + '_0';
    this.texture1.name = value + '_1';
  }

  get internalFormat() { return this.front.internalFormat; }
  get size() { return this.front.size; }
  get depth() { return this.front.depth; }
  get channels() { return this.front.channels; }
  get is2dArray() { return this.front.is2dArray; }
  get formatType() { return this.front.formatType; }
  get pixelSizeInBytes() { return this.front.pixelSizeInBytes; }
  get sizeInBytes() { return this.front.sizeInBytes; }
  get pixelSizeOnGPU() { return this.front.pixelSizeInBytes * 2; }
  get sizeOnGPU() { return this.sizeInBytes * 2; }

  init(data: ArrayBufferView | null = null) {
    this.front.init(data);
    this.back.init(data);
    return this;
  }

  slice(layers: number[]) {
    return new DoubleBufferedTextureSlicing(this, { x: 0, y: 0, width: this.size.width, height: this.size.height }, layers);
  }
  sliceRange(startLayer: number, count: number) {
    return this.slice(new Array(count).fill(0).map((_, i) => startLayer + i));
  }
  sliceAll() {
    return this.sliceRange(0, this.depth || 1);
  }
  clear(color: [number, number, number, number] | Float32Array = [0, 0, 0, 0], layers?: number[]) {
    this.front.clear(color, layers);
    this.back.clear(color, layers);
  }
  readSync<T extends ImageDataType = Float32Array>(readRect?: ReadRect) {
    return this.front.readSync<T>(readRect);
  }
  readAsync<T extends ImageDataType = Float32Array>(readRect?: ReadRect, asyncOpts?: TextureReadPollOptions) {
    return this.front.readAsync<T>(readRect, asyncOpts);
  }
}

export type ITexture = Texture | DoubleBufferedTexture;

export class TextureSlicing {
  constructor(public texture: Texture, public rect: Rect, public layers: number[]) {
    if (texture.is2dArray && layers === undefined) {
      throw new Error(`2d array must have layers`);
    } else if (!texture.is2dArray && layers.length !== 1) {
      throw new Error(`2d textures must have one layer`);
    }
  }
  get count() {
    return this.layers.length;
  }
  readSync<T extends ImageDataType = Float32Array>() {
    return Image.readTextureSync<T>(this, this.rect);
  }
  readAsync<T extends ImageDataType = Float32Array>(asyncOpts?: TextureReadPollOptions) {
    return Image.readTextureAsync<T>(this, this.rect, asyncOpts);
  }
  clear(color: [number, number, number, number] | Float32Array = [0, 0, 0, 0]) {
    this.texture.clear(color, this.layers, this.rect);
  }
  upload(data: ArrayBufferView) {
    const gl = this.texture.gl;
    gl.activeTexture(gl.TEXTURE31);

    const buff = gl.createBuffer();
    gl.bindBuffer(gl.PIXEL_UNPACK_BUFFER, buff);
    gl.bufferData(gl.PIXEL_UNPACK_BUFFER, data, gl.STREAM_DRAW);

    if (this.texture.is2dArray) {
      if (this.layers[0] + this.layers.length - 1 !== this.layers[this.layers.length - 1]) {
        throw new Error(`Can only upload to a continuous range of layers (3, 4, 5, 6 for instance). Found: ${JSON.stringify(this.layers)}`);
      }
      gl.bindTexture(gl.TEXTURE_2D_ARRAY, this.texture.texture);
      gl.texSubImage3D(gl.TEXTURE_2D_ARRAY, 0, this.rect.x, this.rect.y, this.layers[0],
        this.rect.width, this.rect.height, this.layers.length, this.texture.format, this.texture.type, 0);
    } else {
      gl.bindTexture(gl.TEXTURE_2D, this.texture.texture);

      gl.texSubImage2D(gl.TEXTURE_2D, 0, this.rect.x, this.rect.y, this.rect.width, this.rect.height, this.texture.format, this.texture.type, 0);
    }
    gl.bindBuffer(gl.PIXEL_UNPACK_BUFFER, null);
    gl.deleteBuffer(buff);
    // const status = gl.getError();
    // if (status !== gl.NO_ERROR) {
    //   throw new Error(`Gl error: ${status} ${getGlConstantName(gl, status)}`);
    // }
  }
  selectAbsolute(rect: Rect) {
    return new TextureSlicing(this.texture, rect, this.layers);
  }
  selectRelative(rect: Rect) {
    return new TextureSlicing(this.texture, {...rect, x: this.rect.x + rect.x, y: this.rect.y + rect.y }, this.layers);
  }
  selectAll() {
    return new TextureSlicing(this.texture, { x: 0, y: 0, ...this.texture.size }, this.layers);
  }
  slice(layers: number[]) {
    return new TextureSlicing(this.texture, this.rect, layers.map(i => this.layers[i]));
  }
  sliceRange(startLayer: number, count: number) {
    return this.slice(new Array(count).fill(0).map((_, i) => startLayer + i));
  }
}

export class DoubleBufferedTextureSlicing {
  constructor(public texture: DoubleBufferedTexture, public rect: Rect, public layers: number[]) {
    if (texture.is2dArray && layers === undefined) {
      throw new Error(`2d array must have layers`);
    } else if (!texture.is2dArray && layers.length !== 1) {
      throw new Error(`2d texture must have one slice`);
    }
  }
  get count() {
    return this.layers.length;
  }
  readSync<T extends ImageDataType = Float32Array>() {
    return this.front.readSync<T>();
  }
  readAsync<T extends ImageDataType = Float32Array>(asyncOpts?: TextureReadPollOptions) {
    return this.front.readAsync<T>(asyncOpts);
  }
  get back() {
    return new TextureSlicing(this.texture.back, this.rect, this.layers);
  }
  get front() {
    return new TextureSlicing(this.texture.front, this.rect, this.layers);
  }
  get texture0() {
    return new TextureSlicing(this.texture.texture0, this.rect, this.layers);
  }
  get texture1() {
    return new TextureSlicing(this.texture.texture1, this.rect, this.layers);
  }
  clear(color: [number, number, number, number] | Float32Array = [0, 0, 0, 0]) {
    this.front.clear(color);
    this.back.clear(color);
  }
  upload(data: ArrayBufferView) {
    this.front.upload(data);
    this.back.upload(data);
  }
  selectAbsolute(rect: Rect) {
    return new DoubleBufferedTextureSlicing(this.texture, rect, this.layers);
  }
  selectRelative(rect: Rect) {
    return new DoubleBufferedTextureSlicing(this.texture, {...rect, x: this.rect.x + rect.x, y: this.rect.y + rect.y }, this.layers);
  }
  selectAll() {
    return new DoubleBufferedTextureSlicing(this.texture, { x: 0, y: 0, ...this.texture.size }, this.layers);
  }
  slice(layers: number[]) {
    return new DoubleBufferedTextureSlicing(this.texture, this.rect, layers.map(i => this.layers[i]));
  }
  sliceRange(startLayer: number, count: number) {
    return this.slice(new Array(count).fill(0).map((_, i) => startLayer + i));
  }
}


export function destroyTextures(textures: { [key: string]: Texture | Texture[] | DoubleBufferedTexture | undefined }) {
  for (const tex of _.values(textures)) {
    if (Array.isArray(tex)) {
      for (const t of tex) {
        t.destroy();
      }
    } else {
      if (tex) {
        tex.destroy();
      }
    }
  }
}
