import { EditorTypedefConvertible, EditorTypedefResolver, EditorTypedef } from "./EditorTypedefs";

export function editorTypedefConvertibleToTypedef(typedef: EditorTypedefConvertible, typedefResolver?: EditorTypedefResolver): EditorTypedef {
  if (typeof typedef === 'string') {
    if (!typedefResolver) {
      throw new Error('Cannot resolve type reference without typedefResolver');
    }
    const res = typedefResolver(typedef);
    if (!res) {
      throw new Error('Unrecognized type: ' + typedef);
    }
    return res;
  } else if (typeof typedef === 'function') {
    return typedef();
  } else {
    return typedef;
  }
}
