import { GlState, GL } from "../../GpuUtil/GlState";
import { GpuWorldState } from "../State";
import { ArchetypesQuery, EntitiesQuery } from "../../GpuUtil/ECS/Query";
import { shaderModule, funcDef, uniformDef, outDef, inDef } from "../../GpuUtil/ShaderModule";
import { CastingAbilityHelpers, Casting, UnitRef } from "./Abilities";
import { Abilities, abilitiesData } from "./Abilities";
import { IronBubbable, BubbleShader, HeliumBubbable, IronBubbleColor, HeliumBubbleColor } from "../Buffs/Bubbles";
import { VisibleComponent } from "../ECS/Components";
import { LocalToWorld } from "../ECS/Transform";
import { MatrixSM, InterpolateSM } from "../../GpuUtil/SM2Utils";
import { HeadAttachment } from "../ECS/UnitComponents";
import { CullFace } from "../Renderer/ModelRenderer";
import { CastingState } from "../StateLayout";
import { GpuComponent } from "../../GpuUtil/ECS";
import { visibleCacheKeys } from "../ECS/Visibility";
import { StepsPerSecond } from "../../Time";

export class UpdateIronBubblegum {
  constructor(private gls: GlState) {}

  arrows = new ArchetypesQuery([abilitiesData.component[Abilities.IronBubblegum].gpuComponent])
    .updateGpu(this.gls, [IronBubbable.component], shaderModule([
      CastingAbilityHelpers, Casting.Target.sm, IronBubbable.writer, UnitRef.sm
    ], {
      duration: uniformDef('int', () => abilitiesData.get(Abilities.IronBubblegum, 'Settings').duration.value * StepsPerSecond),
      update: funcDef('ivec2', [['ivec2', 'entityCell']], `
        ivec2 unit = ${UnitRef.sm.value}(entityCell);
        if (!${CastingAbilityHelpers.isPerforming}(unit, ${Abilities.IronBubblegum})) {
          return ivec2(-1);
        }
        ${IronBubbable.writer.write}(Self.duration);
        return ${Casting.Target.sm.value}(unit);
      `),
    }));

  run(state: GpuWorldState) {
    this.arrows.run(state);
  }
}

