class BubblegumRenderer {
  constructor(private gls: GlState, private abilityComponent: GpuComponent, private ability: Abilities, private color: string) {}

  private query = new EntitiesQuery([this.abilityComponent])
    .filterValue(VisibleComponent, v => !!v, visibleCacheKeys)
    .render(this.gls, {
      mesh: 'sphere',
      vertexShader: shaderModule([
        LocalToWorld.sm, MatrixSM, BubbleShader.vertex, UnitRef.sm,
        HeadAttachment.sm, InterpolateSM, Casting.Ability.sm, Casting.State.sm, Casting.StateStartStep.sm
      ], {
        time: uniformDef('float', d => d.data.stepCounter),
        shader: funcDef('void', [['ivec2', 'entityCell']], `
          ivec2 unit = ${UnitRef.sm.value}(entityCell);

          int ability = ${Casting.Ability.sm.value}(unit);
          int state = ${Casting.State.sm.value}(unit);

          float scale = 0.5;
          if (ability == ${this.ability} && state == ${CastingState.Casting}) {
            float t = time - float(${Casting.StateStartStep.sm.value}(unit));
            scale = 0.5 + t * 0.05;
          } else if (ability == ${this.ability} && (state == ${CastingState.Performing} || state == ${CastingState.WindDown})) {
            scale = 0.0;
          } else {
            float scaleMin = 0.5;
            float scaleMax = 0.55;
            scale = ${InterpolateSM.interpolate}(sin(time * 0.1), -1.0, 1.0, scaleMin, scaleMax);
          }

          ivec2 head = ${HeadAttachment.sm.value}(unit);
          mat4 worldTransform = ${LocalToWorld.sm.value}(head) *
            ${MatrixSM.translate}(vec3(0, 0.7, 0.8)) *
            ${MatrixSM.scale}(vec3(scale));

          ${BubbleShader.vertex.update}(worldTransform);
        `)
      }),
      fragmentShader: shaderModule([], {
        outColor: outDef('vec4'),
        normal: inDef('vec3'),
        main: funcDef('void', [], `
          if (normal.z < -0.6) {
            discard;
          }
          outColor = vec4(${this.color}, 1);
        `)
      })
    });

  run(state: GpuWorldState) {
    this.gls.enable(GL.CULL_FACE);
    this.gls.cullFace(CullFace.Back);
    this.gls.disable(GL.BLEND);
    this.query.run(state);
  }
}

export class BubblegumsRenderer {
  constructor(private gls: GlState) {}

  bubblegums = [
    new BubblegumRenderer(this.gls, abilitiesData.component[Abilities.IronBubblegum].gpuComponent, Abilities.IronBubblegum, IronBubbleColor),
    new BubblegumRenderer(this.gls, abilitiesData.component[Abilities.HeliumBubblegum].gpuComponent, Abilities.HeliumBubblegum, HeliumBubbleColor),
  ]

  run(state: GpuWorldState) {
    for (const r of this.bubblegums) {
      r.run(state);
    }
  }
}
