import * as React from 'react';
import firebase from 'firebase/app';
import { Db, DbId } from 'db';

// https://github.com/microsoft/TypeScript/issues/20707
export function notNullOrUndefined<T>(x: T | undefined | null): x is T {
  return x !== undefined && x !== null;
}

const db = firebase.firestore();

export interface FbQueryDesc<T extends object> {
  collection: Db;
  where: Array<[keyof T, firebase.firestore.WhereFilterOp, any] | null>;
  orderBy?: Array<{ field: keyof T, desc?: boolean }>;
  limit?: number;
  disabled?: boolean;
}

function fbQueryFromDesc<T extends object>(desc: FbQueryDesc<T>) {
  let q: firebase.firestore.CollectionReference | firebase.firestore.Query = db.collection(desc.collection);
  for (const w of desc.where.filter(notNullOrUndefined)) {
    q = q.where(w[0] as string, w[1], w[2]);
  }
  if (desc.orderBy) {
    for (const orderBy of desc.orderBy) {
      q = q.orderBy(orderBy.field as string, orderBy.desc ? 'desc' : 'asc');
    }
  }
  if (desc.limit !== undefined) {
    q = q.limit(desc.limit);
  }
  return q;
}

export function useFirebaseStreamQueryTo<T extends object>(query: FbQueryDesc<T>, onSnapshot: (docs: firebase.firestore.QuerySnapshot) => void ) {
  const queryString = JSON.stringify(query);
  React.useEffect(() => {
    const desc = JSON.parse(queryString) as FbQueryDesc<T>;
    if (desc.disabled) {
      return;
    }
    const q = fbQueryFromDesc(desc);
    const unsub = q.onSnapshot(snapshot => {
      onSnapshot(snapshot);
    });
    return unsub;
  }, [queryString, onSnapshot]);
}

export type FirebaseStreamQueryResult<T> = { status: 'loading' } | { status: 'loaded', values: Array<T & DbId> };

export function useFirebaseStreamQuery<T extends object>(
  query: FbQueryDesc<T>,
  docToValue?: (doc: firebase.firestore.DocumentData) => T): FirebaseStreamQueryResult<T & { id: string }>;
export function useFirebaseStreamQuery<T extends object>(
  query: FbQueryDesc<T>,
  docToValue?: (doc: firebase.firestore.DocumentData) => T):
  FirebaseStreamQueryResult<T & { id: string }> {
  const [docs, setDocs] = React.useState<null | firebase.firestore.QueryDocumentSnapshot[]>(null);
  useFirebaseStreamQueryTo(query, React.useCallback(snapshot => setDocs(snapshot.docs), []));
  if (!docs) {
    return { status: 'loading' };
  } else {
    if (docToValue) {
      return { status: 'loaded', values: docs.map(d => ({ id: d.id, ...docToValue(d) })) };
    } else {
      return { status: 'loaded', values: docs.map(d => ({ id: d.id, ...d.data() as T })) };
    }
  }
}


export type FirebaseStreamDocumentResult<T> =
  { status: 'loading' } | { status: 'no-such-document' } | { status: 'loaded', value: T & { id: string } };

export function useFirebaseStreamDocumentTo(
  collection: Db, id: string | undefined, onSnapshot: (doc: firebase.firestore.DocumentSnapshot) => void) {
  React.useEffect(() => {
    if (!id) {
      return;
    }
    return db.collection(collection).doc(id).onSnapshot(onSnapshot);
  }, [collection, id, onSnapshot]);
}

export function useFirebaseStreamDocument<T extends object>(
  collection: Db, id: string | undefined, { docToValue, fallbackDoc }: {
    docToValue?: (doc: firebase.firestore.DocumentSnapshot) => T,
    fallbackDoc?: T
  } = {}):
  FirebaseStreamDocumentResult<T> {
  const [doc, setDoc] = React.useState<null | firebase.firestore.DocumentSnapshot>(null);
  useFirebaseStreamDocumentTo(collection, id, setDoc);
  if (!id) {
    if (fallbackDoc) {
      return { status: 'loaded', value: {...fallbackDoc, id: '' } };
    } else {
      return { status: 'no-such-document' };
    }
  } else if (!doc) {
    return { status: 'loading' };
  } else {
    if (!doc.exists) {
      if (fallbackDoc) {
        return { status: 'loaded', value: {...fallbackDoc, id } };
      } else {
        return { status: 'no-such-document' };
      }
    }
    return {
      status: 'loaded',
      value: {...(docToValue ? docToValue(doc) : doc.data() as T), id }
    };
  }
}

export type FirebaseStreamDocumentsResult<T> =
  { status: 'loading' } | { status: 'loaded', values: Array<T & { id: string } | undefined> };

export function useFirebaseStreamDocuments<T extends object>(
  collection: string, ids: string[], { docToValue }: {
    docToValue?: (doc: firebase.firestore.DocumentSnapshot) => T
  } = {}):
  FirebaseStreamDocumentsResult<T> {
  const [docs, setDocs] = React.useState<null | firebase.firestore.DocumentSnapshot[]>(null);
  const idsString = ids.join(',');
  React.useEffect(() => {
    const idsList = idsString.length > 0 ? idsString.split(',') : [];
    if (idsList.length === 0) {
      setDocs([]);
      return;
    }
    const unsubs: Array<() => void> = [];
    const docsArray: firebase.firestore.DocumentSnapshot[] = new Array(idsList.length).fill(undefined);
    for (let i=0; i < idsList.length; i++) {
      unsubs.push(db.collection(collection).doc(idsList[i]).onSnapshot(snapshot => {
        docsArray[i] = snapshot;
        if (docsArray.filter(x => x === undefined).length === 0) {
          setDocs([...docsArray]);
        }
      }));
    }
    return () => {
      unsubs.forEach(unsub => unsub());
    };
  }, [collection, idsString]);
  if (docs === null) {
    return { status: 'loading' };
  } else {
    return {
      status: 'loaded',
      values: docs.map(d => d.exists ? {...(docToValue ? docToValue(d) : (d.data() as T)), id: d.id } : undefined)
    };
  }
}

export function FirebaseLoading({ pending }: { pending: Array<FirebaseStreamDocumentResult<any> | FirebaseStreamQueryResult<any>> }) {
  const hasError = pending.find(x => x.status !== 'loading' && x.status !== 'loaded');
  if (hasError) {
    return <span>Error</span>;
  } else {
    return <span>Loading...</span>;
  }
}
