import * as React from 'react';
import './index.css';
import { WorldStateContext } from '../../GameComponents/WorldDataConsumer';
import { Column, Row, BlockSection, ScrollableColumn } from '../Layout';
import { Button, FontAwesomeButton } from '../Button';
import { EntityRef } from '../../GpuWorld/StateData';
import { ArchetypeIndexQuery } from '../../GpuUtil/ECS/Query';
import { Component } from '../../GpuUtil/ECS';

export function ECSInspector() {
  const worldState = React.useContext(WorldStateContext);
  const [archetypes, setArchetypes] = React.useState<Array<{ components: string[] }>>([]);
  const [selectedArchetype, setSelectedArchetype] = React.useState(0);
  const [entities, setEntities] = React.useState<Array<EntityRef>>([]);
  const [selectedEntity, setSelectedEntity] = React.useState(0);
  const [entityData, setEntityData] = React.useState<Array<{ component: Component, data: any }>>([]);
  const [componentFilter, setComponentFilter] = React.useState('');
  React.useEffect(() => {
    if (worldState) {
      setEntities(new ArchetypeIndexQuery(selectedArchetype).entities().get(worldState));
    }
  }, [worldState, selectedArchetype]);
  const updateEntityData = () => {
    if (worldState) {
      const entityCell = entities[selectedEntity];
      if (entityCell) {
        setEntityData(worldState.ecs.getAllEntityData(entityCell, false));
      } else {
        setEntityData([]);
      }
    }
  };
  React.useEffect(updateEntityData, [worldState, selectedEntity, entities]);
  const updateArchetypes = () => {
    if (worldState) {
      setArchetypes(worldState.ecs.archetypes.map(arch => ({
        components: arch.components.map(comp => comp.name)
      })));
    }
  }
  React.useEffect(updateArchetypes, []);
  if (!worldState) {
    return null;
  }
  const compFilter = componentFilter.split(',').map(x => x.trim().toLowerCase());
  return <Row className="ECSInspector" style={{ overflow: 'hidden' }}>
    <Column style={{ maxWidth: 300 }}>
      <Button onClick={updateArchetypes}>Update archetypes</Button>
      <ScrollableColumn>
        <Column>
          {archetypes.map((arch, i) => <Row key={i}>
            <BlockSection>
              <Button toggled={i === selectedArchetype} onClick={() => setSelectedArchetype(i)}>{i}:</Button>
              {arch.components.join(', ')}
            </BlockSection>
          </Row>)}
        </Column>
      </ScrollableColumn>
    </Column>
    <ScrollableColumn className="ECSInspectorEntitiesColumn">
      <Column>
        {entities.map((entityCell, i) => <Row key={i}>
          <Button toggled={i === selectedEntity} onClick={() => setSelectedEntity(i)}>
            {JSON.stringify(entityCell)}
          </Button>
        </Row>)}
      </Column>
    </ScrollableColumn>
    <Column style={{ overflow: 'hidden' }}>
      <Row style={{ flexShrink: 0 }}>
        <input type="text" className="ECSInspectorComponentFilter" value={componentFilter} onChange={e => setComponentFilter(e.target.value)} />
        <FontAwesomeButton icon="sync" onClick={() => updateEntityData()} />
        <FontAwesomeButton icon="copy" onClick={() => navigator.clipboard.writeText(JSON.stringify(entityData, null, 2))} />
      </Row>
      <ScrollableColumn>
        <Column>
          {entityData
            .filter(x => componentInFilter(x.component.name, compFilter))
            .map(({ component, data }, i) => <Row key={i}>
              <Column className="ECSInspectorComponentName">
                {component.name}
              </Column>
              <Column className="ECSInspectorComponentValue">
                <pre>{JSON.stringify(data, null, 2)}</pre>
              </Column>
            </Row>)}
        </Column>
      </ScrollableColumn>
    </Column>
  </Row>
}
function componentInFilter(componentName: string, filters: string[]) {
  if (filters.length === 0) {
    return true;
  }
  for (const filter of filters) {
    if (componentName.toLowerCase().indexOf(filter) !== -1) {
      return true;
    }
  }
  return false;
}
