export type GpuBufferFormats = 'float' | 'vec4';
export interface GpuBuffer<F extends GpuBufferFormats = GpuBufferFormats> {
  name: string;
  format: F;
}

export type ComponentFormats = 'float' | 'int' | 'bool' | 'vec2' | 'ivec2' | 'vec3' | 'ivec3' | 'vec4' | 'ivec4' | 'mat4';
export function componentFormatChannels(format: ComponentFormats) {
  switch (format) {
    case 'float': return 1;
    case 'int': return 1;
    case 'bool': return 1;
    case 'vec2': return 2;
    case 'ivec2': return 2;
    case 'vec3': return 3;
    case 'ivec3': return 3;
    case 'vec4': return 4;
    case 'ivec4': return 4;
    case 'mat4': return 16;
  }
}
function cpuComponentItemSize(component: CpuComponent) {
  const itemSize = componentFormatChannels(component.format);
  if (component.stride !== undefined) {
    return Math.ceil(itemSize / component.stride) * component.stride;
  } else {
    return itemSize;
  }
}

export interface GpuComponent<F extends GpuBufferFormats = GpuBufferFormats, B extends GpuBuffer<F> = GpuBuffer<F>> {
  type: 'gpu';
  name: string;
  buffer: B;
  format: ComponentFormats;
  depth?: number;
}
export interface CpuComponent<F extends ComponentFormats = ComponentFormats> {
  type: 'cpu';
  name: string;
  format: F;
  stride?: number;
}
export interface ObjComponent<T> {
  type: 'obj';
  name: string;
}
export interface TokenComponent {
  type: 'token';
  name: string;
}

export type Component = GpuComponent | CpuComponent | ObjComponent<any> | TokenComponent;

export class EntityInit {
  constructor(public readonly data: { [name: string]: { component: Component, data: any } } = {}) {}
  setComponent(component: TokenComponent): EntityInit;
  setComponent<T>(component: ObjComponent<T>, data: T): EntityInit;
  setComponent<F extends GpuBufferFormats>(component: GpuComponent<F>, data?: Array<GLSLTypeMappings[F] | Float32Array>): EntityInit;
  setComponent(component: GpuComponent, data?: Float32Array): EntityInit;
  setComponent(component: CpuComponent, data?: Float32Array): EntityInit;
  setComponent(component: Component, data: any = null): EntityInit {
    return new EntityInit({...this.data, [component.name]: { component, data } });
  }
  setComponents(components: Array<GpuComponent | CpuComponent>, data?: Float32Array): EntityInit {
    let entityInit: EntityInit = this;
    for (const component of components) {
      if (component.type === 'cpu') {
        entityInit = entityInit.setComponent(component, data);
      } else {
        entityInit = entityInit.setComponent(component, data);
      }
    }
    return entityInit;
  }
  removeComponent(component: Component) {
    const d = {...this.data};
    delete d[component.name];
    return new EntityInit(d);
  }
  hasComponent(component: Component) {
    return !!this.data[component.name];
  }
}

export enum ECSConsts {
  MaxArchetypes = 64,
  ChunkSize = 128
}


function normalizeGpuComponentData(format: GpuBufferFormats, data: Array<number | Vector4 | Float32Array> | Float32Array): Float32Array[] {
  const channels = (format === 'vec4') ? 4 : 1;
  if (data instanceof Float32Array) {
    const res: Float32Array[] = [];
    for (let i=0; i < data.length / channels; i++) {
      res.push(data.subarray(i * channels, (i + 1) * channels));
    }
    return res;
  } else {
    return data.map((d): Float32Array => {
      if (d instanceof Float32Array) {
        if (d.length < channels) {
          const d2 = new Float32Array(channels).fill(0);
          d2.set(d);
          return d2;
        } else {
          return d;
        }
      } else if (typeof d === 'number') {
        return new Float32Array([d]);
      } else {
        return new Float32Array([d.x, d.y, d.z, d.w]);
      }
    });
  }
}

export function chunkAddressFromEntityCell(entityCell: Vector2) {
  const entityIndex = entityCell.x % ECSConsts.ChunkSize;
  const chunkIndex = Math.floor(entityCell.x / ECSConsts.ChunkSize);
  return { chunkIndex, entityIndex, archIndex: entityCell.y };
}
export function entityCellFromChunkAddress(archIndex: number, chunkIndex: number, entityIndex: number) {
  return {
    x: chunkIndex * ECSConsts.ChunkSize + entityIndex,
    y: archIndex
  }
}


interface ArchetypeChunk {
  arch: Archetype;
  index: number;
  entities: number;
  objComponent: {
    [componentName: string]: {
      data: any[];
      contentVersion: number
    }
  };
  cpuComponent: {
    [componentName: string]: {
      data: Float32Array;
      contentVersion: number;
    }
  };
}
export class Archetype {
  constructor(
    public index: number,
    public components: Component[],
  ) {}
  chunks: ArchetypeChunk[] = [];

  get entitiesCount() {
    return this.chunks.reduce((p, x) => p + x.entities, 0);
  }
}


export class GpuBufferArchetypeBlock {
  constructor(
    public archetype: Archetype,
    public archetypeDataRowOffset: number
  ) {}
  get uniforms(): UniformValues {
    return {
      outArchetype: {
        type: 'int',
        value: this.archetype.index
      },
      outArchetypeDataRowOffset: {
        type: 'int',
        value: this.archetypeDataRowOffset
      }
    }
  }
  get entitiesCount() {
    return this.archetype.entitiesCount;
  }
  writeUniforms(program: Program) {
    program.setUniform('outArchetype', 'int', this.archetype.index);
    program.setUniform('outArchetypeDataRowOffset', 'int', this.archetypeDataRowOffset);
  }
  static SM = shaderModule([], {
    outArchetype: uniformDef('int'),
    outArchetypeDataRowOffset: uniformDef('int'),
    getOutEntityCell: funcDef('ivec2', [], `
      ivec2 outDataCell = ivec2(gl_FragCoord);
      return ivec2(
        (outDataCell.y - outArchetypeDataRowOffset) * ${ECSConsts.ChunkSize} + outDataCell.x,
        outArchetype
      );
    `)
  });
}

export class GpuComponentBlock extends GpuBufferArchetypeBlock {
  constructor(
    public slices: TextureSlicing,
    archetype: Archetype,
    archetypeDataRowOffset: number
  ) {
    super(archetype, archetypeDataRowOffset)
  }
}

export class EntitiesRange {
  constructor(public archetypeIndex: number, public startIndex: number, public count: number) {}
  static fromEntity(entityCell: EntityRef) {
    return new EntitiesRange(entityCell.y, entityCell.x, 1);
  }
  getIndex(index: number) {
    return { x: this.startIndex + index, y: this.archetypeIndex };
  }
  *entities() {
    for (let i=0; i < this.count; i++) {
      yield this.getIndex(i);
    }
  }
}


export class EntitiesBlock {
  constructor(public archetype: number, public count: number) {

  }
  get uniforms(): UniformValues {
    return {
      outArchetype: {
        type: 'int',
        value: this.archetype
      },
    };
  }
  writeUniforms(program: Program) {
    program.setUniform('outArchetype', 'int', this.archetype);
  }
  get firstEntityCell() {
    return {
      x: 0,
      y: this.archetype,
    }
  }
  static SM = shaderModule([], {
    outArchetype: uniformDef('int'),
    getOutEntityCell: funcDef('ivec2', [], `
      return ivec2(
        gl_InstanceID,
        outArchetype
      );
    `)
  });
}


export class EntityComponentSystem {
  constructor(private gls: GlState) {
    for (const k of Object.keys(EntityComponentSystem.registeredGpuBuffers)) {
      const gpuBuffer = EntityComponentSystem.registeredGpuBuffers[k];
      this.gpuBuffers[gpuBuffer.name] = new GpuBufferData(this.gls, gpuBuffer);
    }
  }
  destroy() {
    for (const k of Object.keys(this.gpuBuffers)) {
      this.gpuBuffers[k].destroy();
    }
    this.archetypeHasComponentTexture.destroy();
  }
  reset() {
    throw new Error(`This is not working yet`);
    // for (const k of Object.keys(this.gpuBuffers)) {
    //   this.gpuBuffers[k].removeAllEntities();
    // }
    // this.gpuComponentInfos = {};
    // this.formVersion++;
    // this.archetypeHasComponentTexture.clear();
    // this.archetypes = [];
    // this.archetypesByKey = {};
  }

  createEntities(entity: EntityInit, count: number = 1) {
    const entityData = Object.keys(entity.data).map(k => entity.data[k]);
    const components = entityData.map(d => d.component);
    const arch = this.getOrCreateArchetype(components);
    const range = this.allocateEntities(arch, count);
    for (const { component, data } of entityData) {
      if (component.type === 'obj') {
        this.setComponentData(component, range, data);
      } else if (component.type === 'gpu' && data !== undefined && data !== null) {
        this.setComponentData(component, range, data);
      } else if (component.type === 'cpu' && data !== undefined && data !== null) {
        this.setComponentData(component, range, data);
      }
    }
    this.formVersion++;
    return range;
  }

  private allocateEntities(arch: Archetype, totalCount: number) {
    let lastChunk = arch.chunks[arch.chunks.length - 1];
    let leftCount = totalCount;
    let firstEntity;
    if (lastChunk && lastChunk.entities < this.chunkSize) {
      firstEntity = entityCellFromChunkAddress(arch.index, lastChunk.index, lastChunk.entities);
      let roomLeft = this.chunkSize - lastChunk.entities;
      let take = Math.min(totalCount, roomLeft);
      lastChunk.entities += take;
      leftCount -= take;
    }
    if (leftCount > 0) {
      const newChunks = Math.ceil(leftCount / this.chunkSize);
      const chunks = this.createArchetypeChunks(arch, newChunks);
      if (!firstEntity) {
        firstEntity = entityCellFromChunkAddress(arch.index, chunks[0].index, 0);
      }
      for (let i=0; i < chunks.length - 1; i++) {
        chunks[i].entities = this.chunkSize;
        leftCount -= this.chunkSize;
      }
      chunks[chunks.length - 1].entities = leftCount;
    }
    return new EntitiesRange(arch.index, firstEntity.x, totalCount);
  }

  private getOrCreateArchetype(components: Component[]) {
    const archKey = components.map(x => x.name).join('_');
    const arch = this.archetypesByKey[archKey];
    if (arch) {
      return arch;
    }
    return this.createArchetype(archKey, components);
  }
  private createArchetype(key: string, components: Component[]) {
    if (this.archetypes.length >= ECSConsts.MaxArchetypes) {
      console.warn(`[ECS] Exceeded max number of archetypes!`);
    }
    const index = this.archetypes.length;
    const arch = new Archetype(index, components);
    this.archetypes = [...this.archetypes, arch];
    this.archetypesByKey[key] = arch;

    for (const component of arch.components) {
      if (!(component.name in this.components)) {
        this.components[component.name] = {
          archetypeHasComponent: [],
          component,
          index: Object.keys(this.components).length
        }
      }
      this.components[component.name].archetypeHasComponent[arch.index] = true;
      if (component.type === 'gpu') {
        this.gpuComponentInfos[component.name] = new GpuComponentInfo(this, component);
        this.gpuBuffers[component.buffer.name].enuserComponentAllocated(component);
      }
    }
    this.createArchetypeHasComponentTexture();

    return arch;
  }

  private createArchetypeChunks(arch: Archetype, count: number) {
    const chunks: ArchetypeChunk[] = [];
    for (let i=0; i < count; i++) {
      const chunkId = arch.chunks.length;
      const chunk: ArchetypeChunk = {
        arch,
        index: chunkId,
        entities: 0,
        objComponent: {},
        cpuComponent: {}
      };
      arch.chunks.push(chunk);
      chunks.push(chunk);
      for (const component of arch.components) {
        if (component.type === 'obj') {
          chunk.objComponent[component.name] = { data: [], contentVersion: 0 };
        }
        if (component.type === 'cpu') {
          const itemSize = cpuComponentItemSize(component);
          chunk.cpuComponent[component.name] = {
            data: new Float32Array(itemSize * this.chunkSize),
            contentVersion: 0
          };
        }
      }
    }
    for (const component of arch.components) {
      if (component.type === 'gpu') {
        this.gpuBuffers[component.buffer.name].setArchetypeChunks(arch, arch.chunks.length);
      }
    }
    return chunks;
  }

  getCpuComponentDataView(component: CpuComponent, entityCell: Vector2, incContentVersion = true) {
    const { chunkIndex, entityIndex, archIndex } = chunkAddressFromEntityCell(entityCell);
    const chunk = this.archetypes[archIndex].chunks[chunkIndex];
    const itemSize = cpuComponentItemSize(component);
    const comp = chunk.cpuComponent[component.name];
    const arr = comp.data;
    if (!arr) {
      return null;
    }
    if (incContentVersion) {
      comp.contentVersion++;
    }
    const offset = entityIndex * itemSize;
    return arr.subarray(offset, offset + itemSize);
  }

  setComponentData<F extends GpuBufferFormats>(component: GpuComponent<F>, entity: EntityRef | EntitiesRange, data: Array<GLSLTypeMappings[F] | Float32Array> | Float32Array): void;
  setComponentData<F extends ComponentFormats>(component: CpuComponent<F>, entity: EntityRef | EntitiesRange, data: Float32Array): void;
  setComponentData<T>(component: ObjComponent<T>, entity: EntityRef | EntitiesRange, data: T): void;
  setComponentData(component: GpuComponent | CpuComponent | ObjComponent<any>, entity: EntityRef | EntitiesRange, data: any) {
    if (component.type === 'gpu') {
      this.gpuBuffers[component.buffer.name].setComponentData(component, entity,
        normalizeGpuComponentData(component.buffer.format, data));
    }
    if (component.type === 'cpu') {
      if (entity instanceof EntitiesRange) {
        for (let i=0; i < entity.count; i++) {
          this.setComponentData(component, { x: entity.startIndex + i, y: entity.archetypeIndex }, data);
        }
      } else {
        const view = this.getCpuComponentDataView(component, entity);
        if (!view) {
          throw new Error(`[ECS.setComponentData] Entity (${JSON.stringify(entity)}) doesn't have component ${component.name}.`);
        }
        view.set(data);
      }
    }
    if (component.type === 'obj') {
      if (entity instanceof EntitiesRange) {
        for (let i=0; i < entity.count; i++) {
          this.setComponentData(component, { x: entity.startIndex + i, y: entity.archetypeIndex }, data);
        }
      } else {
        const { chunkIndex, entityIndex, archIndex } = chunkAddressFromEntityCell(entity);
        const comp = this.archetypes[archIndex].chunks[chunkIndex].objComponent[component.name];
        comp.data[entityIndex] = data;
        comp.contentVersion++;
      }
    }
  }
  setComponentsData(components: Array<CpuComponent | GpuComponent>, entityCell: Vector2, data: Float32Array): void {
    for (const component of components) {
      if (component.type === 'cpu') {
        this.setComponentData(component, entityCell, data);
      } else {
        this.setComponentData(component, entityCell, data);
      }
    }
  }
  getComponentData<T>(component: ObjComponent<T>, entityCell: Vector2): T | undefined;
  getComponentData<F extends GpuBufferFormats>(component: GpuComponent<F>, entityCell: EntityRef, asyncr?: true): Promise<Array<GLSLTypeMappings[F]>>;
  getComponentData<F extends GpuBufferFormats>(component: GpuComponent<F>, entityCell: EntityRef, asyncr: false): Array<GLSLTypeMappings[F]>;
  getComponentData<F extends ComponentFormats>(component: CpuComponent<F>, entityCell: EntityRef): Float32Array;
  getComponentData(component: GpuComponent | CpuComponent | ObjComponent<any> | string, entityCell: Vector2, asyncr?: boolean): any;
  getComponentData(component: GpuComponent | CpuComponent | ObjComponent<any> | string, entityCell: Vector2, asyncr = true): any {
    if (typeof component === 'string') {
      const c = this.components[component].component;
      if (!c) {
        throw new Error(`Component doesn't exist in this ecs`);
      }
      if (c.type === 'token') {
        throw new Error(`Cannot get component data for token component`);
      }
      component = c;
    }
    if (component.type === 'obj') {
      const { archIndex, chunkIndex, entityIndex } = chunkAddressFromEntityCell(entityCell);
      const arch = this.archetypes[archIndex];
      return arch.chunks[chunkIndex].objComponent[component.name]?.data[entityIndex];
    } else if (component.type === 'cpu') {
      return this.getCpuComponentDataView(component, entityCell, false);
    } else {
      return this.gpuBuffers[component.buffer.name].getComponentData(component, entityCell, asyncr, false);
    }
  }
  getComponentDataRaw<F extends GpuBufferFormats>(component: GpuComponent<F>, entityCell: EntityRef, asyncr?: true): Promise<Image>;
  getComponentDataRaw<F extends GpuBufferFormats>(component: GpuComponent<F>, entityCell: EntityRef, asyncr: false): Image;
  getComponentDataRaw(component: GpuComponent, entityCell: Vector2, asyncr = true): any {
    return this.gpuBuffers[component.buffer.name].getComponentData(component, entityCell, asyncr, true);
  }
  getEntityGpuComponentBlock(component: GpuComponent, entityCell: EntityRef): GpuComponentBlock {
    const arch = this.archetypes[entityCell.y];
    const buff = this.gpuBuffers[component.buffer.name];
    const buffArch = buff.archetypes[entityCell.y]!;
    return new GpuComponentBlock(
      buff.getEntityComponentDataSlice(component, entityCell)!,
      arch,
      buffArch.dataRowOffset
    );
  }
  getComponentByName(name: string) {
    return this.components[name].component;
  }


  hasComponent<T>(component: ObjComponent<T>, entityCell: Vector2): boolean;
  hasComponent<F extends GpuBufferFormats>(component: GpuComponent<F>, entityCell: EntityRef): boolean;
  hasComponent<F extends ComponentFormats>(component: CpuComponent<F>, entityCell: EntityRef): boolean;
  hasComponent(component: GpuComponent | CpuComponent | ObjComponent<any> | TokenComponent | string, entityCell: Vector2): boolean;
  hasComponent(component: GpuComponent | CpuComponent | ObjComponent<any> | TokenComponent | string, entityCell: Vector2, asyncr = true): boolean {
    if (typeof component === 'string') {
      const c = this.components[component].component;
      if (!c) {
        throw new Error(`Component doesn't exist in this ecs`);
      }
      component = c;
    }
    const { archIndex } = chunkAddressFromEntityCell(entityCell);
    return this.components[component.name].archetypeHasComponent[archIndex];
  }


  getAllEntityData(entityCell: EntityRef, asyncr: false): Array<{ component: Component, data: any }>;
  getAllEntityData(entityCell: EntityRef, asyncr?: true): Promise<Array<{ component: Component, data: any }>>;
  getAllEntityData(entityCell: EntityRef, asyncr = true) {
    const components = this.archetypes[entityCell.y].components;
    if (asyncr) {
      return Promise.all(components.map(async component => {
        let data: any = null;
        if (component.type === 'gpu') {
          data = await this.getComponentData(component, entityCell);
        } else if (component.type === 'obj') {
          data = this.getComponentData(component, entityCell);
        } else if (component.type === 'cpu') {
          data = this.getComponentData(component, entityCell);
        }
        return {
          component,
          data
        }
      }));
    } else {
      return components.map(component => ({
        component,
        data: component.type === 'obj' ? this.getComponentData(component, entityCell)
          : component.type === 'cpu' ? this.getComponentData(component, entityCell)
          : component.type === 'gpu' ? this.getComponentData(component, entityCell, false)
          : null
      }))
    }
  }

  public getMatchingArchetypes(requiredComponents: Component[]) {
    const archetypes: Archetype[] = [];
    for (const arch of this.archetypes) {
      if (this.archetypeMatches(arch, requiredComponents)) {
        archetypes.push(arch);
      }
    }
    return archetypes;
  }
  private archetypeMatches(arch: Archetype, requiredComponents: Component[]) {
    for (let i=0; i < requiredComponents.length; i++) {
      const c = arch.components.find(x => x.name === requiredComponents[i].name);
      if (!c) {
        return false;
      }
    }
    return true;
  }

  public getEntityRefsForArchetypes(archetypes: Archetype[]) {
    return lodash.flatMap(archetypes, arch => this.getEntityRefsForArchetype(arch));
  }
  public getEntityRefsForArchetype(arch: Archetype) {
    const res: EntityRef[] = [];
    for (let c=0; c < arch.chunks.length; c++) {
      for (let i=0; i < arch.chunks[c].entities; i++) {
        res.push(entityCellFromChunkAddress(arch.index, c, i));
      }
    }
    return res;
  }

  public *getEntitiesForGpuComponentBlock(block: GpuComponentBlock) {
    const arch = block.archetype;
    for (let c=0; c < arch.chunks.length; c++) {
      for (let i=0; i < arch.chunks[c].entities; i++) {
        const entityCell = entityCellFromChunkAddress(arch.index, c, i);
        yield {
          entityCell,
          blockCell: { x: i, y: c }
        }
      }
    }
  }

  private createArchetypeHasComponentTexture() {
    const compKeys = Object.keys(this.components);
    const data = Image.zeroesU8({
      width: compKeys.length,
      height: this.archetypes.length,
      depth: 1,
    }, 1);
    for (let y=0; y < this.archetypes.length; y++) {
      const arch = this.archetypes[y];
      for (const c of arch.components) {
        const x = compKeys.indexOf(c.name);
        data.write(1, x, y);
      }
    }
    this.archetypeHasComponentTexture.size = data.size;
    this.archetypeHasComponentTexture.init(data.data);
  }

  public removeAllEntities() {
    for (const arch of this.archetypes) {
      for (const chunk of arch.chunks) {
        chunk.entities = 0;
      }
    }
    for (const k of Object.keys(this.gpuBuffers)) {
      this.gpuBuffers[k].removeAllEntities();
    }
    this.formVersion++;
  }

  public dump() {
    // console.log(`ECS Archetypes:`);
    // for (const arch of this.archetypes) {
    //   console.log(`${arch.index}: ${arch.components.map(x => x.name).join(', ')}`);
    // }
    console.log(`ECS GpuBuffers:`);
    const buffs = Object.keys(this.gpuBuffers).map(key => ({
      key,
      size: this.gpuBuffers[key].texture.sizeInBytes / (1024*1024)
    }));
    buffs.sort((a, b) => a.size - b.size);
    for (const buff of buffs) {
      console.log(`${buff.key}: ${buff.size}mb`);
    }
  }
  public dumpJSON(worldState: GpuWorldState) {
    return this.archetypes.map(a => {
      let entities = new ArchetypeIndexQuery(a.index).entities().get(worldState);
      return {
        archetypeIndex: a.index,
        components: a.components.map(c => {
          return {
            name: c.name,
            type: c.type,
            data: c.type === 'gpu' ? this.gpuBuffers[c.buffer.name].getArchetypeComponentData(c, a) :
              c.type === 'token' ? undefined :
              entities.map(e => this.getComponentData(c, e))
          }
        }),
      }
    });
  }

  public static registerComponent(component: TokenComponent): TokenComponent;
  public static registerComponent<C extends GpuComponent>(component: C): C;
  public static registerComponent<C extends CpuComponent>(component: C): C;
  public static registerComponent<T>(component: ObjComponent<T>): ObjComponent<T>;
  public static registerComponent(component: Component) {
    return component;
  }


  public static registerGpuBuffer<F extends GpuBufferFormats>(buffer: GpuBuffer<F>): GpuBuffer<F> {
    this.registeredGpuBuffers[buffer.name] = buffer;
    return buffer;
  }
  private static registeredGpuBuffers: { [key: string]: GpuBuffer } = {};

  public gpuBuffers: { [key: string]: GpuBufferData } = {};
  public gpuBuffer(buffer: GpuBuffer) {
    return this.gpuBuffers[buffer.name];
  }

  private gpuComponentInfos: { [key: string]: GpuComponentInfo } = {};
  public gpuComponentInfo(component: GpuComponent): GpuComponentInfo | undefined {
    return this.gpuComponentInfos[component.name];
  }
  public components: { [key: string]: ComponentInfo } = new Proxy({}, {
    get: (obj, prop): ComponentInfo => {
      if (prop in obj) {
        return obj[prop];
      } else {
        return { archetypeHasComponent: [], index: -1 };
      }
    }
  });

  /// This increases every time an entity is added or removed, but not when their data is changed
  public formVersion = 0;
  public chunkSize: number = ECSConsts.ChunkSize;
  public archetypeHasComponentTexture = Texture.create(this.gls, this.gls.gl.R8UI, { width: 1, height: 1 }).init();

  public archetypes: Archetype[] = [];
  private archetypesByKey: { [key: string]: Archetype } = {};
}

class GpuComponentInfo {
  constructor(private ecs: EntityComponentSystem, private component: GpuComponent) {}
  get buffer() { return this.ecs.gpuBuffers[this.component.buffer.name]; }
  get bufferComponent() { return this.buffer.components[this.component.name]; }
  get layerOffset() { return this.bufferComponent.layerOffset; }
  get slice() { return this.buffer.texture.sliceRange(this.bufferComponent.layerOffset, componentBufferDepth(this.component)) }
}

interface ComponentInfo {
  index: number;
  archetypeHasComponent: boolean[];
  component?: Component;
}
