import firebase from 'firebase/app';

const Cookies = document.cookie.split(';').reduce((p, x) => {
  const [key, value] = x.trim().split('=');
  return {...p, [key]: value };
}, {});
export const ElectronInfo: undefined | {
  platform: string;
  isDevelopment: boolean
} = Cookies['electronInfo'] ? JSON.parse(Cookies['electronInfo']) : undefined;
export const IsInElectron = !!ElectronInfo;
if (IsInElectron) {
  firebase.auth().setPersistence(firebase.auth.Auth.Persistence.SESSION);
}
