import * as React from 'react';
import { GpuWorldStateData } from "../GpuWorld/StateData";
import { GpuWorldState, GpuWorldStateEventDataChanged } from "../GpuWorld/State";

export const WorldStateContext = React.createContext<GpuWorldState | undefined>(undefined);

function didPropsChange<T extends object>(prevProps: T, newProps: T) {
  for (const k of Object.keys(newProps)) {
    if (prevProps[k] !== newProps[k]) {
      return true;
    }
  }
  for (const k of Object.keys(prevProps)) {
    if (prevProps[k] !== newProps[k]) {
      return true;
    }
  }
  return false;
}

export function useWorldData<Selection extends object>(map: (data: GpuWorldStateData) => Selection): Partial<Selection> {
  const worldState = React.useContext(WorldStateContext);
  const [selection, setSelection] = React.useState(worldState ? map(worldState.data) : {});

  React.useEffect(() => {
    function eventListener(event: GpuWorldStateEventDataChanged) {
      const newSelection = worldState ? map(worldState.data) : {};
      if (!didPropsChange(selection, newSelection)) {
        return;
      }
      setSelection(newSelection);
    }

    if (worldState) {
      worldState.events.dataChanged.attach(eventListener)
    }

    return () => {
      if (worldState) {
        worldState.events.dataChanged.detach(eventListener)
      }
    };
  });

  return selection;
}

export function useWorldDataSingle<T>(map: (data: GpuWorldStateData) => T): T | undefined {
  return useWorldData(d => ({ value: map(d) })).value;
}

export function useWorldDataSelect(...keys: Array<keyof GpuWorldStateData>): Partial<GpuWorldStateData> {
  return useWorldData(d => keys.reduce((p, k) => ({...p, [k]: d[k] }), {}));
}
