import * as React from 'react';
import './index.css';
import { WonkyPanel, evenPadding } from '../WonkyPanel';
import { MaterialIconButton } from '../Button';

export enum PanelMode {
  Normal,
  Primary,
  Floating,
  Thin
}

interface PanelProps {
  className?: string;
  style?: React.CSSProperties;
  mode?: PanelMode;
  title?: any;
  onDelete?: () => void;
  children?: any;
}

export const FloatingPanelColor = `rgba(31, 31, 33, 0.9)`;

export function Panel({ className, style, mode = PanelMode.Normal, title, onDelete, children }: PanelProps) {
  let header: null | JSX.Element = null;
  const padding = evenPadding(5);
  const backgroundMargin = evenPadding(0);
  const border = evenPadding(15);
  if (title || onDelete) {
    backgroundMargin.top = 5;
    border.top = 10;
    padding.top = -15;
    header = <div className="PanelHeader">
      <WonkyPanel shadowPx={0} paddingPx={{ left: 5, right: 5, top: 3, bottom: 3 }} borderPx={{ left: 2, right: 2, top: 1, bottom: 1 }} fill="#53585e" className="PanelTitle">{title}</WonkyPanel>
      {onDelete && <MaterialIconButton icon="clear" className="PanelDeleteButton" onClick={onDelete} purpose="DeletePanel" />}
    </div>;
  }
  return <WonkyPanel
    className={`Panel ${className !== undefined ? className : ''}`}
    style={style}
    fill={mode === PanelMode.Primary ? '#4f7c55'
      : mode === PanelMode.Floating ? FloatingPanelColor
      : mode === PanelMode.Thin ? 'none'
      : '#171717'}
    strokeWidth={mode === PanelMode.Thin ? 1 : undefined}
    stroke={mode === PanelMode.Thin ? 'rgba(255, 255, 255, 0.3)' : undefined}
    backgroundMarginPx={backgroundMargin}
    borderPx={border}
    paddingPx={padding}
    shadowPx={(mode === PanelMode.Floating || mode === PanelMode.Thin) ? 0 : 10}
    >{header}{children}</WonkyPanel>;
}
