interface GameUIProps {
  loggedInUserData: DbUser;
  onExit: () => Promise<void>;
}

export function GameUI({ loggedInUserData, onExit }: GameUIProps) {
  const worldState = React.useContext(WorldStateContext);
  const [showDebugOptions, setShowDebugOptions] = React.useState<boolean>(false);
  const [showViewOptions, setShowViewOptions] = React.useState<boolean>(false);
  const [showBrainConfig, setShowBrainConfig] = React.useState<boolean>(false);
  const { gameInstance, showDecisionsHistogram, showSensesHistogram, cameraMode } = useWorldDataSelect('gameInstance', 'showDecisionsHistogram', 'showSensesHistogram', 'cameraMode');

  const showUIStyle = { opacity: undefined };

  return <div className="GameUI">
    {gameInstance?.type === 'train' && <UnitsLayover />}
    <Column className="GameHUD">
      <Row className="GameHUDTop" style={showUIStyle}>
        <Column>
          <FPSCounter />
          {version.isDevelopment && <Row>
            <FontAwesomeButton flat={true} onClick={() => setShowViewOptions(!showViewOptions)} toggled={showViewOptions} icon="palette" title="Show view options" />
            <MaterialIconButton flat={true} onClick={() => setShowDebugOptions(!showDebugOptions)} toggled={showDebugOptions} icon="bug_report" title="Open debug" purpose="OpenDebug" />
            {gameInstance?.type === 'train' && <FontAwesomeButton flat={true} onClick={() => setShowBrainConfig(!showBrainConfig)} toggled={showBrainConfig} icon="brain" title="Show brain config" />}
            <FontAwesomeButton flat={true} icon="camera" toggled={cameraMode === 'free'}
              onClick={() => worldState?.setData({ cameraMode: cameraMode === 'rts' ? 'free' : 'rts' })}
              title="Toggle camera mode" />
          </Row>}
        </Column>
      </Row>

      <Row className="GameHUDMiddle" style={showUIStyle}>
        <div className="GameHUDLeft">
          {showDebugOptions && worldState && <WorldPageDebugPanel />}
          {showDecisionsHistogram && <BrainHistogramView keys={BrainOutputsKeys} writeRectTo="decisionHistogramView" />}
          {showSensesHistogram && <BrainHistogramView keys={keysOfEnum(Sense)} writeRectTo="sensesHistogramView" />}
        </div>
        {gameInstance?.type === 'train' && <Block className="ArenaMinimapsContainer"><ArenaMinimaps /></Block>}
      </Row>

      {gameInstance?.type === 'train' && <TrainingHUD onExit={onExit} />}
      {gameInstance?.type === 'replay' && <ReplayHUD onExit={onExit} />}
      {gameInstance?.type === 'battle' && <BattleHUD />}
      {gameInstance?.type === 'battle' && <BattleScoreChangeFlash />}

    </Column>

    <BattleResultComponent onExit={onExit} />

  </div>;
}

function BattleResultComponent({ onExit }: { onExit: () => Promise<void> }) {
  const { battleResult, completeBattleRes, teams, battlePostUpdateDone } = useWorldDataSelect('battleResult', 'completeBattleRes', 'creaturesById', 'teams', 'battlePostUpdateDone');
  const team = useFirebaseStreamDocument<DbTeam>(Db.teams, teams?.home.id);
  const worldState = React.useContext(WorldStateContext);
  const dialog = useDialog();
  const appState = React.useContext(AppStateContext);
  if (battleResult === undefined || battleResult === BattleWinner.Undecided || !worldState) {
    return null;
  }

  // const onSaveReplay = async () => {
  //   const title = await dialog.prompt('Replay title');
  //   if (!title) {
  //     return;
  //   }
  //   await saveReplay(worldState, title);
  // }

  return <Column className="BattleResultContainer">
    <Dialog state={dialog.current} />
    <Panel mode={PanelMode.Floating}>
      <ItemsColumn className={`BattleResult ${battleResult === BattleWinner.HomeWin ? 'BattleResultWinner' : 'BattleResultLoser'}`}>
        <div className={`BattleResultHeader animated zoomIn`}>{
          battleResult === BattleWinner.HomeWin ? 'VICTORY!' :
          battleResult === BattleWinner.AwayWin ? 'Defeat' :
          'Tie'}</div>
        {completeBattleRes && team.status === 'loaded' ? <ItemsColumn>
          {/* {completeBattleRes.status === 'ok' && completeBattleRes.newDivision !== completeBattleRes.oldDivision && <Row>
            <b>Congratulations, you've advanced to the next division!</b>
          </Row>} */}
          {battlePostUpdateDone ?
            <TeamName team={team.value} /> : <Row>Loading team stats...</Row>
          }
          {completeBattleRes.status === 'ok' && completeBattleRes.rewardItemIds.length > 0 && <ItemsColumn>
            <Row><small>New item:</small></Row>
            {completeBattleRes.rewardItemIds.map((itemId, i) => <RewardItem key={i} itemId={itemId} />)}
          </ItemsColumn>}
          {completeBattleRes.status === 'err' && <span><i>There was an error completing the battle</i></span>}
        </ItemsColumn> : <Row>Loading...</Row>}

        <ItemsRow>
          {/* <FontAwesomeButton icon="video" flat={true} className="BattleResultSaveReplay" onPerform={onSaveReplay}>Publish replay</FontAwesomeButton> */}
          <Button className="BattleResultBattleAgain" autoFocus={true} onPerform={async () => {
            const next = {...appState.state.gameInstance}
            appState.update({ gameInstance: { type: 'none' } });
            await wait(100);
            appState.update({ gameInstance: next });
          }} primary={true}>Battle again</Button>
          <Button className="BattleResultExit" onClick={onExit}>Home</Button>
        </ItemsRow>
      </ItemsColumn>
    </Panel>
  </Column>
}

function plotTrainingStat(ctx: CanvasRenderingContext2D, size: Size, data: number[], color: string, yMin: number, yMax: number) {
  if (data.length <= 1) {
    return;
  }
  ctx.beginPath();
  const xStep = size.width / (data.length - 1);
  const getPoint = (i: number): [number, number] => [i * xStep, size.height - 1 - (data[i] - yMin) * (size.height - 2) / (yMax - yMin)]
  ctx.moveTo(...getPoint(0));
  for (let i=1; i < data.length; i++) {
    ctx.lineTo(...getPoint(i));
  }
  ctx.strokeStyle = color;
  ctx.stroke();
}

const EmptyTeamSettings = new TeamSettings();
const HitpointsGraphColor = '#ebe575';
// const HitpointsTimeColor = '#8dbdd6';
function TeamTrainingStats({ team }: { team: 'home' | 'away' }) {
  const { teams, gameInstance } = useWorldDataSelect('teams', 'gameInstance');
  const canvas = React.useRef<HTMLCanvasElement>(null);
  const teamData = teams?.[team] ?? EmptyTeamSettings;
  const maxPoints = teamData.maxPossiblePoints;
  React.useEffect(() => {
    if (canvas.current) {
      const ctx = canvas.current.getContext('2d')!;
      ctx.clearRect(0, 0, canvas.current.width, canvas.current.height);
      const size: Size = canvas.current;
      ctx.lineWidth = 3;
      plotTrainingStat(ctx, size, teamData.trainingStats.points, team === 'home' ? HomeHealthColor : AwayHealthColor, 0, maxPoints);
      if (gameInstance && gameInstance.type === 'train') {
        if ((team === 'home' && gameInstance.trainHome) || (team === 'away' && gameInstance.trainAway)) {
          ctx.lineWidth = 1;
          const minGold = teamData.trainingStats.gold.reduce((p, x) => Math.min(p, x), 0);
          const maxGold = teamData.trainingStats.gold.reduce((p, x) => Math.max(p, x), 0);
          plotTrainingStat(ctx, size, teamData.trainingStats.gold, HitpointsGraphColor, minGold, maxGold);
        }
      }
      // plotTrainingStat(ctx, size, teamData.trainingStats.cumulativeHitpoints, HitpointsTimeColor, 1);
    }
  }, [teamData, gameInstance, maxPoints, team]);
  return <Tooltip className="TeamTrainingStats" tooltip={() => <Column>
    <Block><b>{team === 'home' ? 'Trainees' : 'Opponents'}</b></Block>
    <Block style={{ color: team === 'home' ? HomeHealthColor : AwayHealthColor }}>Points: {(lodash.last(teamData.trainingStats.points) ?? 0).toFixed(1)} / {maxPoints}</Block>
    {team === 'home' && <Block style={{ color: HitpointsGraphColor }}>Gold: {Math.round((lodash.last(teamData.trainingStats.gold) ?? 0))}</Block>}
    {/* <Block style={{ color: HitpointsGraphColor }}>Ending hitpoints: {Math.round(100 * (lodash.last(teamData.trainingStats.hitpoints) ?? 0))}%</Block> */}
    {/* <Block style={{ color: HitpointsTimeColor }}>Cumulative hitpoints: {Math.round(100 * (lodash.last(teamData.trainingStats.cumulativeHitpoints) ?? 0))}%</Block> */}
  </Column>}>
    <canvas width={160} height={70} ref={canvas} />
  </Tooltip>
}

function TrainingWinTieLoss() {
  const nRounds = 5;
  const { trainingStats } = useWorldDataSelect('trainingStats');
  if (!trainingStats || trainingStats.win5Percs.length === 0) {
    return <div className="TrainingWinTieLoss" />;
  }
  const wins = lodash.last(trainingStats.win5Percs)!;
  const ties = lodash.last(trainingStats.tie5Percs)!;
  const losses = lodash.last(trainingStats.losses5Percs)!;
  return <Tooltip tooltip={() => <Column>
    <Block><small><i>Average over last {nRounds} rounds</i></small></Block>
    <Block>Wins: {Math.round(100 * wins)}%</Block>
    <Block>Ties: {Math.round(100 * ties)}%</Block>
    <Block>Losses: {Math.round(100 * losses)}%</Block>
  </Column>}>
      <div className="TrainingWinTieLoss">
      <div className="TrainingWins" style={{ flex: wins, background: HomeHealthColor }} />
      <div className="TrainingTies" style={{ flex: ties }} />
      <div className="TrainingLosses" style={{ flex: losses, background: AwayHealthColor }} />
    </div>
  </Tooltip>
}

