function NoBountiesWarning() {
  const { unitBountiesEmpty } = useWorldDataSelect('unitBountiesEmpty');

  if (unitBountiesEmpty) {
    return <WonkyPanel fill="none" stroke="#fff" strokeWidth={2} shadowPx={0} borderPx={evenPadding(10)} className="NoBountiesWarning">
      <img src={BountyPosterSvg} alt="BountyPoster" />
      The Derklings love gold, and you can use this to train them! Click one of them to get started.
    </WonkyPanel>
  } else {
    return null;
  }
}

