          <FontAwesomeButton flat={true} onClick={() => { setShowFAQ(true); worldState?.setData({ simulationPaused: true }); }} icon="question-circle" title="Help" />
          {/* {userSettings.advancedTrainingSettings && <FontAwesomeButton flat={true} onClick={() => setShowSettings(true)} icon="cog" title="Advanced training settings" />} */}
          <FontAwesomeButton flat={true} toggled={renderAllArenas} onClick={() => worldState.setData({ renderAllArenas: !renderAllArenas })} icon="location-arrow" title="Show ghost images of all arenas" />
