import * as React from 'react';
import './index.css';

interface LayoutProps { children?: any, className?: string, style?: React.CSSProperties }

function createLayoutClass(layoutClass: string) {
  return ({ children, className = '', style }: LayoutProps) => {
    return <div className={`${layoutClass} ${className}`} style={style}>{children}</div>;
  }
}

export const Row = createLayoutClass('Row');
export const Column = createLayoutClass('Column');
export const ColumnCentered = createLayoutClass('Column Centered');
export const Block = createLayoutClass('Block');
export const ColumnSection = createLayoutClass('Column Section');
export const BlockSection = createLayoutClass('Block Section');
export const SectionHeading = createLayoutClass('SectionHeading');
export const ScrollableColumn = createLayoutClass('Column Scrollable');
export const ScrollableRow = createLayoutClass('Row Scrollable');
export const ItemsRow = createLayoutClass('Row Items');
export const ItemsColumn = createLayoutClass('Column Items');
export const FloatList = createLayoutClass('FloatList');

export function Spreadsheet({ className = '', style, children }: LayoutProps) {
  return <table className={`Spreadsheet ${className}`} style={style}>{children}</table>;
}

export const VerticalLeft = createLayoutClass('VerticalLeft');
export const VerticalCentered = createLayoutClass('VerticalCentered');
export const VerticalFill = createLayoutClass('VerticalFill');
export const HorizontalCentered = createLayoutClass('HorizontalCentered');
export const HorizontalLeft = createLayoutClass('HorizontalLeft');
export const HorizontalRight = createLayoutClass('HorizontalRight');
export const HorizontalFill = createLayoutClass('HorizontalFill');

export const ScrollVertical = createLayoutClass('ScrollVertical');
export const ScrollBoth = createLayoutClass('ScrollBoth');

export function Section({ children, heading, className = '', style }: LayoutProps & { heading?: string }) {
  return <div className={`Section ${className}`} style={style}>
    {heading && <SectionHeading>{heading}</SectionHeading>}
    {children}
  </div>;
}
