import { GlState } from "../../GpuUtil/GlState";
import { GpuWorldState } from "../State";
import { CastingAbilityHelpers, Casting, UnitRef } from "./Abilities";
import { Abilities, abilitiesData } from "./Abilities";
import { Hitpoints, MaxHitpoints, TailAttachment, UnitCenterSM } from "../ECS/UnitComponents";
import { ArchetypesQuery, EntitiesQuery } from "../../GpuUtil/ECS/Query";
import { shaderModule, funcDef, outDef, uniformDef } from "../../GpuUtil/ShaderModule";
import { glslFloat } from "../../GpuUtil";
import { VisibleComponent } from "../ECS/Components";
import { QuadLineSM } from "../Renderer/Util";
import { TransformHelpers } from "../ECS/Transform";
import { SingleTargetDamage } from "../ECS/DamageCalc";
import { visibleCacheKeys } from "../ECS/Visibility";
import { StepsPerSecond } from "../../Time";

export class UpdateVampireGland {
  constructor(private gls: GlState) {}

  singleTargetDamage = new SingleTargetDamage(this.gls,
    [abilitiesData.component[Abilities.VampireGland].gpuComponent],
    shaderModule([Casting.Target.sm, CastingAbilityHelpers, UnitRef.sm], {
      originator: funcDef('ivec2', [['ivec2', 'entityCell']], `
        return ${UnitRef.sm.value}(entityCell);
      `),
      getTarget: funcDef('ivec2', [['ivec2', 'entityCell']], `
        ivec2 unit = ${UnitRef.sm.value}(entityCell);
        if (!${CastingAbilityHelpers.isPerforming}(unit, ${Abilities.VampireGland})) {
          return ivec2(-1);
        } else {
          return ${Casting.Target.sm.value}(unit);
        }
      `),
      dps: uniformDef('float', () => abilitiesData.get(Abilities.VampireGland, 'Settings').damage.value / StepsPerSecond),
      baseDamage: funcDef('float', [['ivec2', 'entityCell']], `
        return Self.dps;
      `)
    }));

  heal = new ArchetypesQuery([abilitiesData.component[Abilities.VampireGland].gpuComponent])
    .updateGpu(this.gls, [Hitpoints.component], shaderModule([
      CastingAbilityHelpers,
      Hitpoints.writer,
      Hitpoints.sm,
      UnitRef.sm,
    ], {
      hps: uniformDef('float', () => abilitiesData.get(Abilities.VampireGland, 'Settings').healing.value / StepsPerSecond),
      update: funcDef('ivec2', [['ivec2', 'entityCell']], `
        ivec2 unit = ${UnitRef.sm.value}(entityCell);
        float hitpoints = ${Hitpoints.sm.value}(unit);
        if (${CastingAbilityHelpers.isPerforming}(unit, ${Abilities.VampireGland})) {
          hitpoints = clamp(hitpoints + Self.hps, 0.0, ${glslFloat(MaxHitpoints)});
        }
        ${Hitpoints.writer.write}(hitpoints);
        return unit;
      `),
    }));

  run(state: GpuWorldState) {
    this.singleTargetDamage.run(state);
    this.heal.run(state);
  }
}

export class VampireGlandRenderer {
  constructor(private gls: GlState) {}

  query = new EntitiesQuery([abilitiesData.component[Abilities.VampireGland].gpuComponent])
    .filterValue(VisibleComponent, v => !!v, visibleCacheKeys)
    .render(this.gls, {
      mesh: 'quad',
      vertexShader: shaderModule([
        QuadLineSM, CastingAbilityHelpers, Casting.Target.sm, TransformHelpers,
        TailAttachment.sm, UnitCenterSM, abilitiesData.component[Abilities.VampireGland].sm,
        UnitRef.sm,
      ], {
        viewProjection: uniformDef('mat4', d => d.derived.cameraProjectionView),
        shader: funcDef('void', [['ivec2', 'entityCell']], `
          ivec2 unit = ${UnitRef.sm.value}(entityCell);
          if (!${CastingAbilityHelpers.isPerforming}(unit, ${Abilities.VampireGland})) {
            gl_Position = vec4(-1);
            return;
          }
          float progress = ${CastingAbilityHelpers.performingProgress}(unit);
          float width = 0.4;
          vec3 position = ${QuadLineSM.fromLineWithWidths}(
            ${TransformHelpers.worldPosition}(${TailAttachment.sm.value}(unit)),
            width + 0.2 * sin(progress * 10.0),
            ${UnitCenterSM.center}(${Casting.Target.sm.value}(unit)),
            width + 0.2 * cos(progress * 10.0)
          );
          gl_Position = Self.viewProjection * vec4(position, 1);
        `)
      }),
      fragmentShader: shaderModule([], {
        outColor: outDef('vec4'),
        main: funcDef('void', [], `
          outColor = vec4(0, 0, 0, 1);
        `)
      })
    });

  run(state: GpuWorldState) {
    this.query.run(state);
  }
}
