import * as React from 'react';
import Route from 'route-parser';
import { EventDispatcher } from '../Util';

function getRoute() {
  return window.location.pathname + window.location.search;
}

class RoutingHistoryImpl {
  onChange = new EventDispatcher();
  push(url: string) {
    window.history.pushState(null, '', url);
    this.onChange.dispatch();
  }
  replace(url: string) {
    window.history.replaceState(null, '', url);
    this.onChange.dispatch();
  }
}
export const RoutingHistory = new RoutingHistoryImpl();

export function useRoute() {
  const [route, setRoute] = React.useState(getRoute());
  React.useEffect(() => {
    const handlePopstate = (e: PopStateEvent) => {
      e.preventDefault();
      setTimeout(() => {
        setRoute(getRoute());
      }, 0);
    }
    const handleChange = () => {
      setTimeout(() => {
        setRoute(getRoute());
      }, 0);
    }
    window.addEventListener('popstate', handlePopstate);
    RoutingHistory.onChange.attach(handleChange);
    return () => {
      window.removeEventListener('popstate', handlePopstate);
      RoutingHistory.onChange.detach(handleChange);
    }
  }, []);
  return route;
}
export function Router({ routes }: { routes: { [path: string]: (match: { [key: string]: string }) => React.ReactElement } }) {
  const route = useRoute();
  for (const key of Object.keys(routes)) {
    const m = new Route(key).match(route);
    if (m) {
      console.log(`Router rendering path ${key}`);
      return routes[key](m);
    }
  }
  return null;
}

export function Link({ to, children }: { to: string, children?: any }) {
  // eslint-disable-next-line
  return <a href="#" onClick={e => {
    e.preventDefault();
    RoutingHistory.push(to);
  }}>{children}</a>
}
