import { CastingState } from "../StateLayout";
import { GlState } from "../../GpuUtil/GlState";
import { GpuWorldState } from "../State";
import { Decisions } from "../Brain/Decisions";
import { Dazeable } from "../Buffs/Daze";
import { Abilities, UnitAbilities, AbilitySM, ValidTarget, AbilityRange, Casting } from "./Abilities";
import { Focuser } from "../ECS/UnitComponents";
import { Mana, Hitpoints } from "../ECS/UnitComponents";
import { ArchetypesQuery } from "../../GpuUtil/ECS/Query";
import { shaderModule, funcDef, uniformDef } from "../../GpuUtil/ShaderModule";
import { SpatialSM, MathConstsSM } from "../../GpuUtil/SM2Utils";
import { Position, RotationZ } from "../ECS/Transform";

export class UpdateCastingAbility {
  constructor(private gls: GlState) {}

  castingAbility = new ArchetypesQuery([Casting.Ability.component])
    .updateGpu(this.gls, [
      Casting.Ability.component,
      Casting.State.component,
      Casting.StateStartStep.component,
      Casting.Target.component,
      Casting.Cooldowns.component,
    ], shaderModule([
      Casting.Ability.writer,
      Casting.State.writer,
      Casting.StateStartStep.writer,
      Casting.Target.writer,
      Casting.Cooldowns.writer,
      Casting.Ability.sm,
      Casting.State.sm,
      Casting.StateStartStep.sm,
      Casting.Target.sm,
      Casting.Cooldowns.sm,
      Hitpoints.sm,
      AbilitySM,
      Position.sm,
      RotationZ.sm,
      SpatialSM,
      Dazeable.sm,
      Mana.sm,
      Decisions.CastingSlot.sm,
      UnitAbilities.sm,
      Focuser.sm,
      MathConstsSM,
    ], {
      time: uniformDef('int', d => d.data.stepCounter),

      isValidTarget: funcDef('bool', [['int', 'ability'], ['ivec2', 'targetCell']], `
        switch (${AbilitySM.validTargets}[ability]) {
          case ${ValidTarget.Anything}: return true;
          case ${ValidTarget.Creature}: return targetCell.x >= 0 && ${Hitpoints.sm.value}(targetCell) > 0.0;
        }
      `),

      targetInRange: funcDef('bool', [['ivec2', 'entityCell'], ['int', 'ability'], ['ivec2', 'targetCell']], `
        int abilityRange = ${AbilitySM.targetRange}[ability];
        if (abilityRange == ${AbilityRange.Any}) {
          return true;
        } else {
          float maxRange = abilityRange == ${AbilityRange.Close} ? 2.0 : 15.0;
          float minRange = abilityRange == ${AbilityRange.Close} ? 0.0 : 1.5;
          vec3 selfPosition = ${Position.sm.value}(entityCell);
          float selfRotation = ${RotationZ.sm.value}(entityCell);
          vec2 selfForward = ${SpatialSM.forward}(selfRotation);
          vec3 targetPosition = ${Position.sm.value}(targetCell);
          float angle = ${SpatialSM.angleToPosition}(selfPosition.xy, selfForward, targetPosition.xy);
          bool isInFront = abs(angle) < (${MathConstsSM.PI} / 4.);
          float dist = distance(selfPosition, targetPosition);
          return isInFront && dist >= minRange && dist < maxRange;
        }
      `),

      update: funcDef('void', [['ivec2', 'entityCell']], `
        int currentAbility = ${Casting.Ability.sm.value}(entityCell);
        int currentState = ${Casting.State.sm.value}(entityCell);
        int currentStateStartStep = ${Casting.StateStartStep.sm.value}(entityCell);
        ivec2 currentTarget = ${Casting.Target.sm.value}(entityCell);
        int cooldowns[] = int[](
          ${Casting.Cooldowns.sm.value}(entityCell, 0),
          ${Casting.Cooldowns.sm.value}(entityCell, 1),
          ${Casting.Cooldowns.sm.value}(entityCell, 2)
        );

        bool dazed = ${Dazeable.sm.value}(entityCell) > 0;

        int nextState = currentState;
        int nextAbility = currentAbility;
        int nextStateStartStep = currentStateStartStep;
        ivec2 nextTarget = currentTarget;
        int nextCooldowns[] = int[](
          max(0, cooldowns[0] - 1),
          max(0, cooldowns[1] - 1),
          max(0, cooldowns[2] - 1)
        );

        if (currentState != ${CastingState.Inactive}) {
          int steps = Self.time - nextStateStartStep;
          int castTime = ${AbilitySM.castTime}[currentAbility];
          int duration = ${AbilitySM.duration}[currentAbility];
          int windDown = ${AbilitySM.windDown}[currentAbility];

          bool shouldAbort = dazed ||
            !Self.isValidTarget(currentAbility, currentTarget) ||
            !Self.targetInRange(entityCell, currentAbility, currentTarget) ||
            ${Hitpoints.sm.value}(entityCell) <= 0.0;

          if (currentState != ${CastingState.WindDown} && shouldAbort) {
            nextState = ${CastingState.WindDown};
            nextStateStartStep = time;
          } else if (currentState == ${CastingState.Casting}) {
            if (steps >= castTime) {
              nextState = ${CastingState.Performing};
              nextStateStartStep = time;
            }
          } else if (currentState == ${CastingState.Performing}) {
            if (steps >= duration) {
              nextState = ${CastingState.WindDown};
              nextStateStartStep = time;
            }
          } else if (currentState == ${CastingState.WindDown}) {
            if (steps >= windDown) {
              nextState = ${CastingState.Inactive};
              nextAbility = ${Abilities.None};
              nextStateStartStep = -1;
              nextTarget = ivec2(-1);
            }
          }

        } else {

          bool canCastAbility = ${Hitpoints.sm.value}(entityCell) > 0.0 &&
            currentState == ${CastingState.Inactive} &&
            !dazed;

          int decisionCastingSlot = ${Decisions.CastingSlot.sm.value}(entityCell);
          int desiredCastingAbility = ${UnitAbilities.sm.value}(entityCell, decisionCastingSlot - 1);

          ivec2 focus = ${Focuser.sm.value}(entityCell);

          bool isStartingCast = canCastAbility &&
            decisionCastingSlot > 0 &&
            cooldowns[decisionCastingSlot - 1] == 0 &&
            Self.isValidTarget(desiredCastingAbility, focus) &&
            Self.targetInRange(entityCell, desiredCastingAbility, focus) &&
            desiredCastingAbility != ${Abilities.None};

          if (isStartingCast) {
            nextState = ${CastingState.Casting};
            nextAbility = desiredCastingAbility;
            nextStateStartStep = Self.time;
            nextTarget = focus;
            nextCooldowns[decisionCastingSlot - 1] = ${AbilitySM.cooldown}[nextAbility];
          }
        }

        ${Casting.Ability.writer.write}(nextAbility);
        ${Casting.State.writer.write}(nextState);
        ${Casting.StateStartStep.writer.write}(nextStateStartStep);
        ${Casting.Target.writer.write}(nextTarget);
        ${Casting.Cooldowns.writer.write}(nextCooldowns);
      `)
    }));

  run(state: GpuWorldState) {
    this.castingAbility.run(state);
  }
}