import * as React from 'react';
import { EditorProps, EditorDispatch } from './EditorProps';

interface FileEditorProps extends EditorProps<File> {
}

export class FileEditor extends React.Component<FileEditorProps & EditorDispatch<File>> {
  render() {
    return (
      <input type="file" onChange={e => this.props.onChange(e.target.files![0])} />
    );
  }
}