import { EntityComponentSystem } from "../../GpuUtil/ECS";
import { GlState } from "../../GpuUtil/GlState";
import * as Mat4 from '../../Math/Mat4';
import { GpuWorldState } from "../State";
import { CommonBuffer } from "./Components";
import { GpuBufferModule, GpuComponentModule, DualComponentModule } from "../../GpuUtil/ECS/ComponentModules";
import { ArchetypesQuery, EntitiesQuery } from "../../GpuUtil/ECS/Query";
import { shaderModule, funcDef } from "../../GpuUtil/ShaderModule";
import { MatrixSM } from "../../GpuUtil/SM2Utils";
import { mat4 } from "gl-matrix";

export const TransformBuffer = new GpuBufferModule({
  name: 'TransformBuffer',
  format: 'vec4'
});

export const Position = new DualComponentModule('Position', TransformBuffer, 'vec3');
export const Quaternion = new DualComponentModule('Quaternion', TransformBuffer.buffer, 'vec4');
export const Scale = new DualComponentModule('Scale', TransformBuffer.buffer, 'vec3');

export const RotationZ = new GpuComponentModule('RotationZ', CommonBuffer, 'float');

export const CpuBoneOffsetComponent = EntityComponentSystem.registerComponent<{
  value: Mat4.Mat4
}>({
  name: 'CpuBoneOffset',
  type: 'obj'
});

export const BoneOffset = new GpuComponentModule('BoneOffset', TransformBuffer.buffer, 'mat4');
export const BoneMatrix = new GpuComponentModule('BoneMatrix', TransformBuffer.buffer, 'mat4');

export const TransformParent = new GpuComponentModule('TransformParent', TransformBuffer.buffer, 'ivec2');

export const LocalToParentOrderComponents = new Array(20).fill(0).map((_, i) => EntityComponentSystem.registerComponent({
  name: 'LocalToParentOrder' + i,
  type: 'token',
}));

export const LocalToWorld = new DualComponentModule('LocalToWorld', TransformBuffer.buffer, 'mat4');
export const LocalToParent = new GpuComponentModule('LocalToParent', TransformBuffer.buffer, 'mat4');
export const OffsetLocalToWorld = new GpuComponentModule('OffsetLocalToWorld', TransformBuffer.buffer, 'mat4');
export const InvLocalToWorld = new DualComponentModule('LocalToWorld', TransformBuffer.buffer, 'mat4');


export const TransformHelpers = shaderModule('TransformHelpers', [MatrixSM, LocalToWorld.sm], {
  worldPosition: funcDef('vec3', [['ivec2', 'entityCell']], `
    return ${MatrixSM.transformPoint}(${LocalToWorld.sm.value}(entityCell), vec3(0));
  `)
});

export class UpdateTransform {
  constructor(private gls: GlState) {}

  positionRotation = new ArchetypesQuery([LocalToWorld.gpuComponent, Position.gpuComponent, RotationZ.component])
    .mapGpu(this.gls, LocalToWorld.gpuComponent, shaderModule([MatrixSM, Position.sm, RotationZ.sm], {
      map: funcDef('mat4', [['ivec2', 'entityCell']], `
        return ${MatrixSM.translate}(${Position.sm.value}(entityCell)) *
          ${MatrixSM.rotateZ}(${RotationZ.sm.value}(entityCell));
      `)
    }));
  runPosRot(state: GpuWorldState) {
    this.positionRotation.run(state);
  }

  localToWorld = LocalToParentOrderComponents.map(ltpo =>
    new ArchetypesQuery([LocalToWorld.gpuComponent, LocalToParent.component, TransformParent.component, ltpo])
      .mapGpu(this.gls, LocalToWorld.gpuComponent, shaderModule([LocalToParent.sm, TransformParent.sm, LocalToWorld.sm], {
        map: funcDef('mat4', [['ivec2', 'entityCell']], `
          mat4 localToParent = ${LocalToParent.sm.value}(entityCell);
          ivec2 parent = ${TransformParent.sm.value}(entityCell);
          mat4 parentWorld = ${LocalToWorld.sm.value}(parent);
          return parentWorld * localToParent;
        `)
      }))
    );
  runLocalToWorld(state: GpuWorldState) {
    for (const w of this.localToWorld) {
      w.run(state);
    }
  }

  boneMatrix = new ArchetypesQuery([BoneMatrix.component, BoneOffset.component, LocalToWorld.gpuComponent])
    .mapGpu(this.gls, BoneMatrix.component, shaderModule([LocalToWorld.sm, BoneOffset.sm], {
      map: funcDef('mat4', [['ivec2', 'entityCell']], `
        mat4 localToWorld = ${LocalToWorld.sm.value}(entityCell);
        mat4 boneOffset = ${BoneOffset.sm.value}(entityCell);
        return localToWorld * boneOffset;
      `)
    }));
  runBoneMatrix(state: GpuWorldState) {
    this.boneMatrix.run(state);
  }

  offsetLocalToWorld = new ArchetypesQuery([LocalToWorld.gpuComponent, OffsetLocalToWorld.component])
    .mapGpu(this.gls, LocalToWorld.gpuComponent, shaderModule([LocalToWorld.sm, OffsetLocalToWorld.sm], {
      map: funcDef('mat4', [['ivec2', 'entityCell']], `
        mat4 localToWorld = ${LocalToWorld.sm.value}(entityCell);
        mat4 offset = ${OffsetLocalToWorld.sm.value}(entityCell);
        return localToWorld * offset;
      `)
    }));
  runOffset(state: GpuWorldState) {
    this.offsetLocalToWorld.run(state);
  }

  cpuPositionToGpu = new ArchetypesQuery([Position.cpuComponent, Position.gpuComponent])
    .gpuComponentBlocks(Position.gpuComponent)
    .uploadFrom(Position.cpuComponent);
  cpuQuaternionToGpu = new ArchetypesQuery([Quaternion.cpuComponent, Quaternion.gpuComponent])
    .gpuComponentBlocks(Quaternion.gpuComponent)
    .uploadFrom(Quaternion.cpuComponent);
  cpuScaleToGpu = new ArchetypesQuery([Scale.cpuComponent, Scale.gpuComponent])
    .gpuComponentBlocks(Scale.gpuComponent)
    .uploadFrom(Scale.cpuComponent);
  localToParentFromPosQatScaleQuery = new ArchetypesQuery([LocalToParent.component,
    Position.gpuComponent, Quaternion.gpuComponent, Scale.gpuComponent])
    .mapGpu(this.gls, LocalToParent.component, shaderModule([Position.sm, Quaternion.sm, Scale.sm, MatrixSM], {
      map: funcDef('mat4', [['ivec2', 'entityCell']], `
        vec3 position = ${Position.sm.value}(entityCell);
        vec4 rotation = ${Quaternion.sm.value}(entityCell);
        vec3 scale = ${Scale.sm.value}(entityCell);
        return ${MatrixSM.rotateTranslateScale}(rotation, position, scale);
      `)
    }));
  runLocalToParentFromCpuPosQatScale(state: GpuWorldState) {
    this.cpuPositionToGpu.run(state);
    this.cpuQuaternionToGpu.run(state);
    this.cpuScaleToGpu.run(state);
    this.localToParentFromPosQatScaleQuery.run(state);
  }

  boneMatrixFromCpuQuery = new EntitiesQuery([BoneMatrix.component, CpuBoneOffsetComponent, LocalToWorld.cpuComponent]);
  runBoneMatrixFromCpu(state: GpuWorldState) {
    for (const entityCell of this.boneMatrixFromCpuQuery.get(state)) {
      const localToWorld = state.ecs.getComponentData(LocalToWorld.cpuComponent, entityCell);
      const bone = state.ecs.getComponentData(CpuBoneOffsetComponent, entityCell)!;
      const res = Mat4.mul(localToWorld, bone.value);
      state.ecs.setComponentData(BoneMatrix.component, entityCell, Mat4.toRows(res));
    }
  }

  localToWorldFromCpuQuery = new EntitiesQuery([LocalToWorld.gpuComponent, LocalToWorld.cpuComponent]);
  runLocalToWorldFromCpu(state: GpuWorldState) {
    for (const entityCell of this.localToWorldFromCpuQuery.get(state)) {
      const localToWorld = state.ecs.getComponentData(LocalToWorld.cpuComponent, entityCell);
      state.ecs.setComponentData(LocalToWorld.gpuComponent, entityCell, Mat4.toRows(localToWorld));
    }
  }

  localToWorldFromCpuPosQuatScaleQuery = new EntitiesQuery([LocalToWorld.cpuComponent, Position.cpuComponent, Quaternion.cpuComponent, Scale.cpuComponent]);
  runLocalToWorldFromCpuPosQuatScale(state: GpuWorldState) {
    for (const entityCell of this.localToWorldFromCpuPosQuatScaleQuery.get(state)) {
      const position = state.ecs.getComponentData(Position.cpuComponent, entityCell);
      const quaternion = state.ecs.getComponentData(Quaternion.cpuComponent, entityCell);
      const scale = state.ecs.getComponentData(Scale.cpuComponent, entityCell);
      const localToWorld = state.ecs.getCpuComponentDataView(LocalToWorld.cpuComponent, entityCell);
      if (localToWorld) {
        mat4.fromRotationTranslationScale(localToWorld, quaternion, position, scale);
      }
    }
  }

  invLocalToWorldFromCpuQuery = new EntitiesQuery([LocalToWorld.cpuComponent, InvLocalToWorld.cpuComponent]);
  runInvLocalToWorldFromCpu(state: GpuWorldState) {
    for (const entityCell of this.invLocalToWorldFromCpuQuery.get(state)) {
      const localToWorld = state.ecs.getCpuComponentDataView(LocalToWorld.cpuComponent, entityCell);
      const invLocalToWorld = state.ecs.getCpuComponentDataView(InvLocalToWorld.cpuComponent, entityCell);
      if (localToWorld && invLocalToWorld) {
        mat4.invert(invLocalToWorld, localToWorld);
      }
    }
  }

  run(state: GpuWorldState) {
    this.runPosRot(state);
    this.runLocalToParentFromCpuPosQatScale(state);
    this.runOffset(state);
    this.runLocalToWorld(state);
    this.runBoneMatrixFromCpu(state);
    this.runBoneMatrix(state);
    this.runLocalToWorldFromCpu(state);
    this.runLocalToWorldFromCpuPosQuatScale(state);
    this.runInvLocalToWorldFromCpu(state);
  }
}