import { GlState } from "../../GpuUtil/GlState";
import { GpuWorldState } from "../State";
import { Decisions } from "../Brain/Decisions";
import { Position, RotationZ } from "./Transform";
import { ArenaInstance } from "./Components";
import { ArchetypesQuery, GpuLookupTableSM } from "../../GpuUtil/ECS/Query";
import { shaderModule, funcDef, uniformDef } from "../../GpuUtil/ShaderModule";
import { SpatialSM } from "../../GpuUtil/SM2Utils";
import { glslFloat } from "../../GpuUtil";
import { Hitpoints, Team, Focuser, MaxFocusDistance } from "./UnitComponents";
import { UnitFocusables } from "../Brain/UnitFocusables";


export class UpdateBrainFocusers {
  constructor(private gls: GlState) {}

  private writer = new ArchetypesQuery([Focuser.component])
    .mapGpu(this.gls, Focuser.component, shaderModule([
      Position.sm, RotationZ.sm, Focuser.sm, Decisions.ChangeFocus.sm,
      GpuLookupTableSM, ArenaInstance.sm, SpatialSM, Hitpoints.sm,
      Team.sm, UnitFocusables
    ], {
      unitsInArena: uniformDef('isampler2D', d => d.unitsInArenaGpu.get(d)),
      map: funcDef('ivec2', [['ivec2', 'entityCell'], ['ivec2', 'value']], `
        if (${Hitpoints.sm.value}(entityCell) <= 0.0) {
          return ivec2(-1);
        }

        vec3 position = ${Position.sm.value}(entityCell);
        float rotation = ${RotationZ.sm.value}(entityCell);
        vec2 forward = ${SpatialSM.forward}(rotation);
        ivec2 focus = ${Focuser.sm.value}(entityCell);
        bool hasFocus = focus.x >= 0;
        vec3 focusPosition = ${Position.sm.value}(focus);

        bool focusIsInFront = ${SpatialSM.isInFront}(position.xy, forward, focusPosition.xy);

        int changeFocusI = ${Decisions.ChangeFocus.sm.value}(entityCell);
        bool changeFocus = changeFocusI > 0 ||
          !hasFocus ||
          distance(position, focusPosition) > ${glslFloat(MaxFocusDistance)} ||
          !focusIsInFront ||
          ${Hitpoints.sm.value}(focus) <= 0.0;

        if (!changeFocus) {
          return value;
        }

        int arenaIndex = ${ArenaInstance.sm.value}(entityCell);
        int selfTeam = ${Team.sm.value}(entityCell);
        ivec3[] brainFocusables = ${UnitFocusables.get}(entityCell, selfTeam, arenaIndex);
        ivec2 candidate = brainFocusables[changeFocusI - 1].xy;

        // Can't focus on self or nothing
        if (changeFocusI == 0 || candidate == entityCell || candidate.x < 0) {
          return ivec2(-1);
        }

        vec3 candidatePosition = ${Position.sm.value}(candidate);

        bool isInFront = ${SpatialSM.isInFront}(position.xy, forward, candidatePosition.xy);
        bool isInRange = distance(position, candidatePosition) < ${glslFloat(MaxFocusDistance)};
        bool focusable = isInFront && isInRange && ${Hitpoints.sm.value}(candidate) > 0.0;
        if (focusable) {
          return candidate;
        } else {
          return ivec2(-1);
        }
      `)
    }));

  run(state: GpuWorldState) {
    this.writer.run(state);
  }
}
