import * as Math2 from './Math';
import { RandomSeedable } from '../Util';
import { EditorTypedef } from '../Components/Editors/EditorTypedefs';
import { mat2, vec2 } from 'gl-matrix';

export interface Vector2 {
  x: number;
  y: number;
}

export const Vector2Typedef: EditorTypedef = {
  metatype: 'struct',
  properties: {
    x: { metatype: 'float' },
    y: { metatype: 'float' },
  }
}

export function create(v: Vector2 | number): Vector2;
export function create(x: number, y: number): Vector2;
export function create(...args: any[]): Vector2 {
  if (args.length === 1) {
    if (typeof args[0] === 'number') {
      return { x: args[0], y: args[0] };
    } else {
      return {...args[0]};
    }
  } else {
    return { x: args[0], y: args[1] };
  }
}

export function zero(): Vector2 {
  return { x: 0, y: 0 };
}

export function random(min: number = 0, max: number = 1): Vector2 {
  return random2(min, min, max, max);
}

export function random2(minX: number, minY: number, maxX: number, maxY: number): Vector2 {
  return { x: minX + Math.random() * (maxX - minX), y: minY + Math.random() * (maxY - minY) };
}

export function randomSeedable(rand: RandomSeedable, min: number = 0, max: number = 1): Vector2 {
  return randomSeedable2(rand, min, min, max, max);
}

export function randomSeedable2(rand: RandomSeedable, minX: number, minY: number, maxX: number, maxY: number): Vector2 {
  return { x: minX + rand.random() * (maxX - minX), y: minY + rand.random() * (maxY - minY) };
}

export function fromPolarCoordinates(angle: number, radius: number): Vector2 {
  return { x: Math.cos(angle) * radius, y: Math.sin(angle) * radius };
}

export function length2(vec: Vector2): number {
  return vec.x * vec.x + vec.y * vec.y;
}

export function length(vec: Vector2): number {
  return Math.sqrt(length2(vec));
}
export function distance(a: Vector2, b: Vector2): number {
  return length(sub(a, b));
}

export function cross(a: Vector2, b: Vector2): number {
  return (a.x * b.y ) - (a.y * b.x);
}
export function dot(a: Vector2, b: Vector2): number {
  return a.x * b.x + a.y * b.y;
}

export function normalize(vec: Vector2): Vector2 {
  const l = length(vec);
  return { x: vec.x / l, y: vec.y / l };
}

export function round(vec: Vector2): Vector2 {
  return { x: Math.round(vec.x), y: Math.round(vec.y) };
}

export function floor(vec: Vector2): Vector2 {
  return { x: Math.floor(vec.x), y: Math.floor(vec.y) };
}
export function ceil(vec: Vector2): Vector2 {
  return { x: Math.ceil(vec.x), y: Math.ceil(vec.y) };
}
export function fract(vec: Vector2): Vector2 {
  return { x: Math2.fract(vec.x), y: Math2.fract(vec.y) };
}

export function add(...args: Array<Vector2 | number>): Vector2 {
  const res = create(args[0]);
  for (let i=1; i < args.length; i++) {
    const b = args[i];
    if (typeof b === 'number') {
      res.x += b;
      res.y += b;
    } else {
      res.x += b.x;
      res.y += b.y;
    }
  }
  return res;
}

export function inv(v: Vector2): Vector2 {
  return { x: -v.x, y: -v.y };
}

export function sub(a: Vector2 | number, b: Vector2 | number): Vector2 {
  return add(a, typeof b === 'number' ? -b : inv(b));
}

export function mul(vec: Vector2, f: number | Vector2): Vector2 {
  if (typeof f === 'number') {
    return { x: vec.x * f, y: vec.y * f };
  } else {
    return { x: vec.x * f.x, y: vec.y * f.y };
  }
}

export function div(vec: Vector2, f: number | Vector2): Vector2 {
  if (typeof f === 'number') {
    return mul(vec, 1 / f);
  } else {
    return { x: vec.x / f.x, y: vec.y / f.y };
  }
}

export function atan2(vec: Vector2): number {
  return Math.atan2(vec.y, vec.x);
}

export function mod(vec: Vector2, modulo: Vector2): Vector2 {
  return { x: Math2.mod(vec.x, modulo.x), y: Math2.mod(vec.y, modulo.y) };
}

export function clamp(vec: Vector2, min: number, max: number): Vector2;
export function clamp(vec: Vector2, min: Vector2, max: Vector2): Vector2;
export function clamp(vec: Vector2, min: any, max: any): Vector2 {
  if (typeof min === 'number') {
    min = create(min);
  }
  if (typeof max === 'number') {
    max = create(max);
  }
  return {
    x: Math2.clamp(vec.x, min.x, max.x),
    y: Math2.clamp(vec.y, min.y, max.y),
  };
}


export function mix(a: Vector2, b: Vector2, p: number | Vector2): Vector2 {
  if (typeof p === 'number') {
    return {
      x: Math2.mix(a.x, b.x, p),
      y: Math2.mix(a.y, b.y, p),
    }
  } else {
    return {
      x: Math2.mix(a.x, b.x, p.x),
      y: Math2.mix(a.y, b.y, p.y),
    }
  }
}

export const lerp = mix;

export function perpendicular(a: Vector2): Vector2 {
  return { x: a.y, y: -a.x };
}

export function isEqual(a: Vector2, b: Vector2): boolean {
  return a.x === b.x && a.y === b.y;
}

export function interpolate(x: number, x0: number, x1: number, y0: Vector2, y1: Vector2): Vector2;
export function interpolate(x: Vector2, x0: Vector2, x1: Vector2, y0: Vector2, y1: Vector2): Vector2;
export function interpolate(x: any, x0: any, x1: any, y0: Vector2, y1: Vector2): Vector2 {
  if (typeof x === 'number') {
    const p = (x - x0) / (x1 - x0);
    return mix(y0, y1, p);
  } else {
    const p = div(sub(x, x0), sub(x1, x0));
    return mix(y0, y1, p);
  }
}

export function interpolateClamped(x: number, x0: number, x1: number, y0: Vector2, y1: Vector2): Vector2;
export function interpolateClamped(x: Vector2, x0: Vector2, x1: Vector2, y0: Vector2, y1: Vector2): Vector2;
export function interpolateClamped(x: any, x0: any, x1: any, y0: Vector2, y1: Vector2): Vector2 {
  if (typeof x === 'number') {
    const p = Math2.clamp((x - x0) / (x1 - x0), 0, 1);
    return mix(y0, y1, p);
  } else {
    const p = clamp(div(sub(x, x0), sub(x1, x0)), 0, 1);
    return mix(y0, y1, p);
  }
}

export function toGlVec2(v: Vector2): vec2 {
  return vec2.fromValues(v.x, v.y);
}
export function fromGlVec2(v: vec2): Vector2 {
  return { x: v[0], y: v[1] };
}

export function rotate2d(v: Vector2, a: number) {
	const s = Math.sin(a);
  const c = Math.cos(a);
  const m = mat2.fromValues(c, s, -s, c);
	return fromGlVec2(vec2.transformMat2(vec2.create(), toGlVec2(v), m));
}

// From: https://math.stackexchange.com/questions/383321/rotating-x-y-points-45-degrees
export function rotate45deg({ x, y }: Vector2): Vector2 {
  return { x: (x + y) / Math.sqrt(2), y: (y - x) / Math.sqrt(2) };
}