export interface OutputRect {
  x?: number;
  y?: number;
  width: number;
  height: number;
}

export function* createBatches(outputsLength: number, outputBatchSize: number) {
  for (let l = 0; l < outputsLength; l += outputBatchSize) {
    const count = l + outputBatchSize > outputsLength ? (outputsLength - l) : outputBatchSize;
    yield { l, count };
  }
}
