import * as React from 'react';
import ReactMarkdown from 'react-markdown';

interface BundledMarkdownProps {
  path: string;
}

// Bundled, as in the markdown file is in the src folder
export class BundledMarkdown extends React.Component<BundledMarkdownProps> {
  state = { text: '' };
  static sources = {};
  async componentDidMount() {
    this.setState({ text: await this.loadSource() });
  }
  loadSource() {
    if (BundledMarkdown.sources[this.props.path]) {
      return BundledMarkdown.sources[this.props.path];
    } else {
      const source = fetch(this.props.path).then(response => response.text());
      return BundledMarkdown.sources[this.props.path] = source;
    }
  }
  render() {
    return <ReactMarkdown source={this.state.text} linkTarget="_blank" />;
  }
}
