import { mod, Size, Size3, Rect } from '../Math/Math';
import * as Vector2 from '../Math/Vector2';
import * as Vector3 from '../Math/Vector3';
import { RandomSeedable } from '../Util';
import { Texture, DoubleBufferedTexture, TextureSlicing } from './Texture';
import * as Vector4 from '../Math/Vector4';
import { ReadRect, TextureReader, TextureReadPollOptions } from './TextureReader';
import { GlState } from './GlState';
import { EXRLoader } from 'three/examples/jsm/loaders/EXRLoader';
import * as THREE from 'three';
import { loadArrayBuffer } from '.';

export function mapLayerInplace(data: Float32Array, layer: number, size: Size, map: (value: number) => number) {
  for (let y=0; y < size.height; y++) {
    for (let x=0; x < size.width; x++) {
      const index = imageIndex3dr(size, x, y, layer);
      data[index] = map(data[index]);
    }
  }
}

export function copyLayer(source: Float32Array, sourceLayer: number, target: Float32Array, targetLayer: number, size: Size) {
  for (let y=0; y < size.height; y++) {
    for (let x=0; x < size.width; x++) {
      target[imageIndex3dr(size, x, y, targetLayer)] = source[imageIndex3dr(size, x, y, sourceLayer)];
    }
  }
}

export function insertLayer(data: Float32Array, originalSize: Size3, newLayerIndex: number, value: number = 0) {
  const newData = new Float32Array(originalSize.width * originalSize.height * (originalSize.depth + 1));
  newData.fill(value);
  for (let z = 0; z < originalSize.depth; z++) {
    copyLayer(data, z, newData, z + (z >= newLayerIndex ? 1 : 0), originalSize);
  }
  return newData;
}

function imageIndex3dr(size: Size, x: number, y: number, z: number): number {
  return imageIndex(size, 1, x, y, z, 0);
}

function imageIndex(size: Size, channels: number, x: number, y: number, z: number, channel: number): number {
  return ((z * size.height + y) * size.width + x) * channels + channel;
}
function imagePosition(size: Size, channels: number, index: number): Vector4.Vector4 {
  const channel = index % channels;
  index = Math.floor(index / channels);
  const x = index % size.width;
  index = Math.floor(index / size.width);
  const y = index % size.height;
  index = Math.floor(index / size.height);
  const z = index;
  return Vector4.create(x, y, z, channel);
}

function threePixelFormatChannels(pixelFormat: THREE.PixelFormat) {
  switch (pixelFormat) {
    case THREE.AlphaFormat: return 1;
    case THREE.RGBFormat: return 3;
    case THREE.RGBAFormat: return 4;
    case THREE.LuminanceFormat: return 1;
    case THREE.LuminanceAlphaFormat: return 2;
    case THREE.RGBEFormat: return 4;
    case THREE.DepthFormat: return 1;
    case THREE.DepthStencilFormat: return 2;
    case THREE.RedFormat: return 1;
    case THREE.RedIntegerFormat: return 1;
    case THREE.RGFormat: return 2;
    case THREE.RGIntegerFormat: return 2;
    case THREE.RGBIntegerFormat: return 3;
    case THREE.RGBAIntegerFormat: return 4;
    default: throw new Error(`Unrecognized THREE pixel format: ${pixelFormat}`);
  }
}

export type ImageDataType = Float32Array | Int32Array | Uint8Array;

export class Image<T extends ImageDataType = Float32Array> {
  constructor(public data: T, public size: Size3, public channels: number) {}
  get channelSize() {
    if (this.data instanceof Float32Array || this.data instanceof Int32Array) {
      return 4;
    } else {
      return 1;
    }
  }
  get sizeInBytes() {
    return this.data.length * this.channelSize;
  }
  static zeroes<T extends ImageDataType = Float32Array>(arrayConstructor: new (length: number) => T, size: Size3, channels: number) {
    const data = new arrayConstructor(size.width * size.height * size.depth * channels);
    return new Image(data, size, channels);
  }
  static zeroesF32(size: Size3, channels: number) {
    return Image.zeroes(Float32Array, size, channels);
  }
  static zeroesI32(size: Size3, channels: number) {
    return Image.zeroes(Int32Array, size, channels);
  }
  static zeroesU8(size: Size3, channels: number) {
    return Image.zeroes(Uint8Array, size, channels);
  }
  static exrLoader = new EXRLoader();
  static fromEXRBuffer(buffer: ArrayBuffer) {
    const exr = Image.exrLoader.parse(buffer);
    return new Image(exr.data, { width: exr.width, height: exr.height, depth: 1 }, threePixelFormatChannels(exr.format));
  }
  static async fromEXRUrl(url: string) {
    return Image.fromEXRBuffer(await loadArrayBuffer(url));
  }
  fill(value: number) {
    this.data.fill(value);
    return this;
  }
  randomize(min: number = 0, max: number = 1, random: RandomSeedable = new RandomSeedable()) {
    for (let i=0; i < this.data.length; i++) {
      this.data[i] = min + random.random() * (max - min);
    }
    return this;
  }
  // waitUntilRead: "two frames" is a recommended amount of time to wait until reading
  static async readTextureAsync<T extends ImageDataType = Float32Array>(texture: TextureSlicing, readRect: ReadRect = {}, asyncOpts = new TextureReadPollOptions()): Promise<Image<T>> {
    return new TextureReader(texture).readAndDestroy('poll', readRect, asyncOpts);
  }
  static readTextureSync<T extends ImageDataType = Float32Array>(texture: TextureSlicing, readRect: ReadRect = {}): Image<T> {
    return new TextureReader(texture).readAndDestroy('block', readRect);
  }
  toGl(gls: GlState, as2dArray?: boolean): Texture {
    let tex;
    if (as2dArray === true || (as2dArray === undefined && this.size.depth > 1)) {
      tex = new Texture(gls, this.defaultTextureFormat(gls), this.size, this.size.depth);
    } else {
      tex = new Texture(gls, this.defaultTextureFormat(gls), this.size);
    }
    if (!tex) {
      throw new Error('Failed to create texture');
    }
    tex.init(this.data);
    return tex;
  }
  toGlDoubleBuffered(gls: GlState, as2dArray?: boolean): DoubleBufferedTexture {
    return new DoubleBufferedTexture(this.toGl(gls, as2dArray), this.toGl(gls, as2dArray));
  }
  defaultTextureFormat(gls: GlState) {
    if (this.channels === 1) {
      if (this.data instanceof Float32Array) {
        return gls.gl.R32F;
      } else if (this.data instanceof Int32Array) {
        return gls.gl.R32I;
      } else if (this.data instanceof Uint8Array) {
        return gls.gl.R8UI;
      }
    } else if (this.channels === 2) {
      if (this.data instanceof Float32Array) {
        return gls.gl.RG32F;
      } else if (this.data instanceof Int32Array) {
        return gls.gl.RG32I;
      } else if (this.data instanceof Uint8Array) {
        return gls.gl.RG8UI;
      }
    } else if (this.channels === 3) {
      if (this.data instanceof Float32Array) {
        return gls.gl.RGB32F;
      } else if (this.data instanceof Int32Array) {
        return gls.gl.RGB32I;
      } else if (this.data instanceof Uint8Array) {
        return gls.gl.RGB8UI;
      }
    } else if (this.channels === 4) {
      if (this.data instanceof Float32Array) {
        return gls.gl.RGBA32F;
      } else if (this.data instanceof Int32Array) {
        return gls.gl.RGBA32I;
      } else if (this.data instanceof Uint8Array) {
        return gls.gl.RGBA8UI;
      }
    }
    throw new Error('Not implemented yet');
  }
  safeIndex(position: Vector3.Vector3, channel = 0): number {
    const x = mod(position.x, this.size.width);
    const y = mod(position.y, this.size.height);
    return this.index(x, y, position.z, channel);
  }
  index(x: number, y: number, z: number, channel = 0): number {
    return imageIndex(this.size, this.channels, x, y, z, channel);
  }
  safeRead(position: Vector3.Vector3, channel = 0): number {
    return this.data[this.safeIndex(position, channel)];
  }
  read(x: number, y: number, z = 0, channel = 0): number {
    return this.data[this.index(x, y, z, channel)];
  }
  write(value: number, x: number, y: number, z = 0, channel = 0) {
    this.data[this.index(x, y, z, channel)] = value;
  }
  readPixel({ x, y, z = 0}: { x: number, y: number, z?: number }): Vector4.Vector4 {
    const index = this.index(x, y, z, 0);
    if (this.channels === 1) {
      return { x: this.data[index], y: 0, z: 0, w: 0 };
    } else if (this.channels === 2) {
      return { x: this.data[index], y: this.data[index + 1], z: 0, w: 0 };
    } else if (this.channels === 3) {
      return { x: this.data[index], y: this.data[index + 1], z: this.data[index + 2], w: 0 };
    } else {
      return { x: this.data[index], y: this.data[index + 1], z: this.data[index + 2], w: this.data[index + 3] };
    }
  }
  readPixelAsArray(pos: { x: number, y: number, z?: number }): [number, number, number, number] {
    return Vector4.toArray(this.readPixel(pos));
  }
  fillLayer(layer: number, value: number) {
    for (let y=0; y < this.size.height; y++) {
      for (let x=0; x < this.size.width; x++) {
        this.write(value, x, y, layer);
      }
    }
  }
  readLayer(layer: number) {
    return this.data.slice(layer * this.size.width * this.size.height, (layer + 1) * this.size.width * this.size.height);
  }
  reduceLayer(layer: number, channel: number, reduce: (p: number, x: number) => number, initialValue?: number): number {
    let value = initialValue !== undefined ? initialValue : this.read(0, 0, layer);
    for (let y=0; y < this.size.height; y++) {
      for (let x=0; x < this.size.width; x++) {
        if (initialValue === undefined && x === 0 && y === 0) {
          continue;
        }
        value = reduce(value, this.read(x, y, layer, channel));
      }
    }
    return value;
  }
  get DataConstructor(): new (length: number) => T {
    return this.data.constructor as  (new (length: number) => T);
  }
  reduce(reduce: (p: number, x: number) => number, initialValue?: number) {
    const res = Image.zeroes(this.DataConstructor, { width: 1, height: 1, depth: this.size.depth }, this.channels);
    for (let z=0; z < this.size.depth; z++) {
      for (let channel=0; channel < this.channels; channel++) {
        let v = initialValue ?? 0;
        for (let y=0; y < this.size.height; y++) {
          for (let x=0; x < this.size.height; x++) {
            v = reduce(v, this.read(x, y, z, channel));
          }
        }
        res.write(v, 0, 0, z, channel);
      }
    }
    return res;
  }
  map(map: (value: number, position: Vector4.Vector4) => number): Image<T> {
    const d = this.data.map((value, i) => {
      return map(value, imagePosition(this.size, this.channels, i));
    });
    return new Image<T>(d as T, {...this.size}, this.channels);
  }
  copyXY(from: Vector2.Vector2, to: Vector2.Vector2) {
    for (let l=0; l < this.size.depth; l++) {
      this.data[this.safeIndex({ x: to.x, y: to.y, z: l })] =
        this.data[this.safeIndex({ x: from.x, y: from.y, z: l })];
    }
  }
  *positionsXY() {
    for (let y=0; y < this.size.height; y++) {
      for (let x=0; x < this.size.width; x++) {
        yield { x, y };
      }
    }
  }
  select(rect: Rect): ImageSlice<T> {
    return new ImageSlice(this, rect, new Array(this.size.depth).fill(0).map((_, i) => i));
  }
  slice(layers: number[]): ImageSlice<T> {
    return new ImageSlice(this, { x: 0, y: 0, width: this.size.width, height: this.size.height }, layers);
  }
  sliceRange(startLayer: number, count: number): ImageSlice<T> {
    return this.slice(new Array(count).fill(0).map((_, i) => startLayer + i));
  }
  sliceAll(): ImageSlice<T> {
    return this.sliceRange(0, this.size.depth);
  }
  dump() {
    for (let z = 0; z < this.size.depth; z++) {
      for (let w = 0; w < this.channels; w++) {
        // tslint:disable-next-line:no-console
        console.log(`Layer: ${z}, Channel: ${w}`);
        const data: number[][] = [];
        for (let y = 0; y < this.size.height; y++) {
          const row: number[] = [];
          for (let x = 0; x < this.size.width; x++) {
            row.push(this.read(x, y, z, w));
          }
          data.push(row);
        }
        // tslint:disable-next-line:no-console
        console.table(data);
      }
    }
  }
  dumpImage() {
    const canvas = document.createElement('canvas');
    canvas.width = this.size.width;
    canvas.height = this.size.height;
    const context = canvas.getContext('2d')!;
    const style = "font-size: 1px; padding: " + Math.floor(canvas.height/2) + "px " + Math.floor(canvas.width/2) + "px; line-height: 0px;";
    for (let z = 0; z < this.size.depth; z++) {
      for (let w = 0; w < this.channels; w++) {
        // tslint:disable-next-line:no-console
        console.log(`Layer: ${z}, Channel: ${w}`);
        context.clearRect(0, 0, canvas.width, canvas.height);
        for (let y = 0; y < this.size.height; y++) {
          for (let x = 0; x < this.size.width; x++) {
            context.fillStyle = `hsl(0, 0%, ${100 * this.read(x, y, z, w)}%)`;
            context.fillRect(x, y, 1, 1);
          }
        }
        const dataUrl = canvas.toDataURL();
        console.log('%c+', `${style} background:url(${dataUrl}) no-repeat; background-size: ${canvas.width}px ${canvas.height}px`);
      }
    }
  }
}

export class ImageSlice<T extends ImageDataType> {
  constructor(public image: Image<T>, public rect: Rect, public layers: number[]) {}
  select(rect: Rect) {
    return new ImageSlice(this.image, rect, this.layers);
  }
  slice(layers: number[]) {
    return new ImageSlice(this.image, this.rect, layers.map(i => this.layers[i]));
  }
  sliceRange(startLayer: number, count: number) {
    return this.slice(new Array(count).fill(0).map((_, i) => startLayer + i));
  }
  index(x: number, y: number, z = 0, channel = 0) {
    return this.image.index(this.rect.x + x, this.rect.y + y, this.layers[z], channel);
  }
  read(x: number, y: number, z = 0, channel = 0) {
    return this.image.read(this.rect.x + x, this.rect.y + y, this.layers[z], channel);
  }
  write(value: number, x: number, y: number, z = 0, channel = 0) {
    this.image.write(value, this.rect.x + x, this.rect.y + y, this.layers[z], channel);
  }
  toArray(target = new this.image.DataConstructor(this.rect.width * this.rect.height * this.layers.length * this.image.channels)) {
    let offset = 0;
    const chunkSize = this.rect.width * this.image.channels;
    for (let z = 0; z < this.layers.length; z++) {
      for (let y = 0; y < this.rect.height; y++) {
        const index = this.index(0, y, z, 0);
        target.set(this.image.data.subarray(index, index + chunkSize), offset);
        offset += chunkSize;
      }
    }
    return target;
  }
  toImage() {
    return new Image(this.toArray(), { width: this.rect.width, height: this.rect.height, depth: this.layers.length }, this.image.channels);
  }
}
