import { mat4, vec4 } from 'gl-matrix';
import * as Vector4 from './Vector4';
import * as Vector3 from './Vector3';
import { ImageSlice, Image } from '../GpuUtil/Image';

export type Mat4 = mat4;

export function create(v0: number): Mat4;
export function create(
  r0: Vector4.Vector4,
  r1: Vector4.Vector4,
  r2: Vector4.Vector4,
  r3: Vector4.Vector4): Mat4;
export function create(
  m00: number, m01: number, m02: number, m03: number,
  m10: number, m11: number, m12: number, m13: number,
  m20: number, m21: number, m22: number, m23: number,
  m30: number, m31: number, m32: number, m33: number): Mat4;
export function create(...args: any[]): Mat4 {
  if (args.length === 1) {
    const v0 = args[0];
    return mat4.fromValues(
      v0, 0, 0, 0,
      0, v0, 0, 0,
      0, 0, v0, 0,
      0, 0, 0, v0);
  } else if (args.length === 4) {
    const [r0, r1, r2, r3] = args;
    return mat4.fromValues(
      r0.x, r0.y, r0.z, r0.w,
      r1.x, r1.y, r1.z, r1.w,
      r2.x, r2.y, r2.z, r2.w,
      r3.x, r3.y, r3.z, r3.w);
  } else {
    return fromArray(args);
  }
}
export function fromArray(values: number[]) {
  const [m00, m01, m02, m03, m10, m11, m12, m13, m20, m21, m22, m23, m30, m31, m32, m33] = values;
  return mat4.fromValues(m00, m01, m02, m03, m10, m11, m12, m13, m20, m21, m22, m23, m30, m31, m32, m33);
}
export function identity() {
  return mat4.identity(mat4.create());
}

export function get(mat: Mat4, i: number, j: number): number {
  return mat[i * 4 + j];
}
export function row(mat: Mat4, i: number): Vector4.Vector4 {
  return { x: get(mat, i, 0), y: get(mat, i, 1), z: get(mat, i , 2), w: get(mat, i, 3) };
}
export function toRows(mat: Mat4) {
  const rows: Vector4.Vector4[] = [];
  for (let i=0; i < 4; i++) {
    rows.push(row(mat, i));
  }
  return rows;
}
export function writeToImageSlice(mat: Mat4, slice: ImageSlice<Float32Array>) {
  for (let i=0; i < 4; i++) {
    for (let j=0; j < 4; j++) {
      slice.write(get(mat, i, j), 0, 0, i, j);
    }
  }
}
export function writeToImage(mat: Mat4, image: Image<Float32Array>, x: number, y: number) {
  for (let i=0; i < 4; i++) {
    for (let j=0; j < 4; j++) {
      image.write(get(mat, i, j), x, y, i, j);
    }
  }
}

export function mul(...mats: Mat4[]): Mat4;
export function mul(a: Mat4, b: Vector4.Vector4): Vector4.Vector4;
export function mul(...args: any[]): Mat4 | Vector4.Vector4 {
  if (args.length === 2) {
    const [a, b] = args;
    if (typeof b === 'object' && (b as any).x !== undefined) {
      return Vector4.fromGlVec4(vec4.transformMat4(vec4.create(), Vector4.toGlVec4(b as Vector4.Vector4), a));
    }
  }
  const res = mat4.clone(args[0]);
  for (let i=1; i < args.length; i++) {
    mat4.mul(res, res, args[i]);
  }
  return res;
}

export function inverse(mat: Mat4): Mat4 | null {
  return mat4.invert(mat4.create(), mat);
}

export function transpose(mat: Mat4): Mat4 {
  return mat4.transpose(mat4.create(), mat);
}

export function translation(position: Vector3.Vector3): Mat4 {
  const m = mat4.create();
  return mat4.translate(m, m, [position.x, position.y, position.z]);
}
export function scaling(scale: Vector3.Vector3): Mat4 {
  const m = mat4.create();
  return mat4.scale(m, m, [scale.x, scale.y, scale.z]);
}

export function rotationX(radians: number): Mat4 {
  const m = mat4.create();
  return mat4.rotateX(m, m, radians);
}
export function rotationY(radians: number): Mat4 {
  const m = mat4.create();
  return mat4.rotateY(m, m, radians);
}
export function rotationZ(radians: number): Mat4 {
  const m = mat4.create();
  return mat4.rotateZ(m, m, radians);
}
export function fromRotationTranslationScale(rotation: Vector4.Vector4, position: Vector3.Vector3, scale: Vector3.Vector3) {
  return mat4.fromRotationTranslationScale(mat4.create(),
    Vector4.toGlQuat(rotation),
    Vector3.toGlVec3(position),
    Vector3.toGlVec3(scale),
  );
}