import * as _ from 'lodash';
import * as React from 'react';
import { Checkbox } from '../Checkbox';
import { EditorProps, EditorDispatch } from './EditorProps';
import { EditorEnumVariants } from './EditorTypedefs';
import { withDefaultValue } from './WithDefaultValue';
import './Editors.css';
import { keysOfEnum } from '../../Util';
import { Popdown } from '../Popdown';
import { Menu, MenuItem } from '../Menu';
import { ScrollVertical } from '../Layout';

export const CheckboxEditor = Checkbox;

interface EnumEditorProps extends EditorProps<any> {
  variants: EditorEnumVariants | (() => EditorEnumVariants);
  searchable?: boolean;
}

export function enumEnumVariants(e: any) {
  return keysOfEnum(e).map(k => ({ value: e[k], displayName: k }));
}

function normalizeEnumVariants(variants: EditorEnumVariants | (() => EditorEnumVariants)): Array<{ value: any, displayName: string, description?: string, className?: string }> {
  if (_.isFunction(variants)) {
    variants = variants();
  }
  if (!Array.isArray(variants)) {
    return Object.keys(variants).map(displayName => ({
      displayName, value: (variants as { [displayName: string]: any })[displayName]
    }));
  } else {
    return variants.map(v => {
      if (typeof v === 'string') {
        return { value: v, displayName: v };
      } else {
        return { value: v.value, displayName: v.displayName || v.value, description: v.description, className: v.className };
      }
    }) as Array<{ value: any, displayName: string, description?: string, className?: string }>;
  }
}

export const EnumEditor = withDefaultValue(({ className, onChange, readOnly = false, value, variants, searchable }: EnumEditorProps & EditorDispatch<any>) => {
  const [showPopdown, setShowPopdown] = React.useState<boolean>(false);
  const [filter, setFilter] = React.useState<string>('');
  let vars = normalizeEnumVariants(variants);
  if (filter) {
    const filterLower = filter.toLowerCase();
    vars = vars.filter(x => x.displayName.toLowerCase().indexOf(filterLower) !== -1);
  }
  let displayValue = '' + value;
  for (const v of vars) {
    if (_.isEqual(v.value, value)) {
      displayValue = v.displayName || v.value;
    }
  }
  return (
    <Popdown
      className={className}
      showPopdown={showPopdown}
      onClickFader={() => setShowPopdown(false)}
      popdown={() => <div className="EnumEditorPopdown">
          <Menu>
            {searchable && <input type="text" value={filter} onChange={e => setFilter(e.target.value)} placeholder="Filter" />}
            <ScrollVertical>
              {vars.map((v, i) => <MenuItem
                key={i}
                onClick={() => { setShowPopdown(false); onChange(v.value); }}
                title={v.displayName}
                className={v.className}
                />)}
            </ScrollVertical>
          </Menu>
      </div>}
      >
      <div className="EnumEditor" onClick={() => !readOnly && setShowPopdown(true)}>
        {displayValue} <i className="material-icons">arrow_drop_down</i>
      </div>
    </Popdown>
  );
});
