import * as React from 'react';
import './Editors.css';
import { Button } from '../Button';
import { EditorProps, EditorDispatch } from './EditorProps';

export interface WithDefaultProps<T> {
  defaultValue?: T;
}

export function withDefaultValue<T, PropsType extends EditorProps<T> & EditorDispatch<T>>(WrappedComponent: React.ComponentClass<PropsType> | React.StatelessComponent<PropsType>)
  : React.ComponentClass<PropsType & WithDefaultProps<T>> {
  return class extends React.Component<PropsType & WithDefaultProps<T>, {}> {
    render() {
      const defaultValue = this.props.defaultValue as T | undefined;
      if (defaultValue !== undefined) {
        return (
          <div className={`EditorWithDefault ${this.props.value === undefined ? 'IsDefault' : ''}`}>
            <div className="EditorWithDefaultValue">
              <WrappedComponent {...this.props} value={this.props.value !== undefined ? this.props.value : defaultValue} />
            </div>
            <Button
              className="ResetToDefault EditorActionButton"
              onClick={() => this.props.onChange(undefined)}
              disabled={this.props.value === undefined}
              purpose="ResetToDefault"
              >
              <i className="material-icons">settings_backup_restore</i>
            </Button>
          </div>
        );
      } else {
        return <WrappedComponent {...this.props} />;
      }
    }
  };
}
