import * as React from 'react';
import './ShaderDebugger.css';
import { shaderFromSource, ShaderError } from '../../GpuUtil/ShaderCache';
import { CodeMirror } from '../CodeMirror';
import { Popup } from '../Popup';
import { Row, Column } from '../Layout';
import { GL } from '../../GpuUtil/GlState';

export function ShaderDebugger({ shaderErrors }: { shaderErrors: ShaderError[] }) {
  const compileErrors = shaderErrors.slice().sort((a, b) => {
    if (a.type === 'FailedToCompileShader' && b.type !== 'FailedToCompileShader') {
      return -1;
    } else if (a.type !== 'FailedToCompileShader' && b.type === 'FailedToCompileShader') {
      return 1;
    } else {
      return 0;
    }
  })
  const compileError = compileErrors[0];
  const [error, setError] = React.useState('');
  const [source, setSource] = React.useState(compileError.type === 'FailedToCompileShader' ? compileError.shaderSource : '');
  const shaderType = compileError.type === 'FailedToCompileShader' ? compileError.shaderType : 'vertex';
  React.useEffect(() => {
    if (compileError.type === 'FailedToCompileShader') {
      const res = shaderFromSource(GL, shaderType, source);
      if (res.result === 'ok') {
        setError('OK!');
      } else {
        if (res.error.type === 'FailedToCompileShader') {
          setError(res.error.message);
        } else {
          setError(res.error.type);
        }
      }
    }
  }, [source, shaderType, compileError.type]);
  React.useEffect(() => {
    setSource(compileError.type === 'FailedToCompileShader' ? compileError.shaderSource : '');
  }, [compileError]);
  return <Popup fullscreen={true}>
    <Row className="ShaderDebugger">
      <CodeMirror
        className="ShaderDebuggerCode"
        value={source}
        onChange={setSource}
        options={{
          theme: 'one-dark',
          mode: 'clike',
          lineNumbers: true
        }}
        />
      <Column className="ShaderDebuggerError">
        <pre>{error}</pre>
      </Column>
    </Row>
  </Popup>
}
