import { glslFloat, glslVec2 } from "../../GpuUtil";
import { GlState, GL } from "../../GpuUtil/GlState";
import { Vector2 } from "../../Math/Vector2";
import { Position } from "../ECS/Transform";
import { Hitpoints, Mana, Team, MaxHitpoints, MaxMana, HitpointsTrailing } from "../ECS/UnitComponents";
import { GpuWorldState } from "../State";
import { EntitiesQuery } from "../../GpuUtil/ECS/Query";
import { VisibleComponent } from "../ECS/Components";
import { shaderModule, funcDef, outDef, uniformDef, inDef, ShaderModule, constDef, FunctionDef } from "../../GpuUtil/ShaderModule";
import { Vertex } from "../../GpuUtil/Mesh";
import { Teams } from "../StateLayout";
import { MatrixSM } from "../../GpuUtil/SM2Utils";
import { visibleCacheKeys } from "../ECS/Visibility";

class PixlingOverheadBarBaseRenderer {
  constructor(private gls: GlState, private conf: {
    offsetY: number,
    size: Vector2,
    padding: Vector2,
    barColor: ShaderModule<{ getBarColor: FunctionDef<'vec4', [['ivec2', 'entityCell'], ['float', 'p']]> }>
  }) {}

  maxInstanceCount = 128;
  query = new EntitiesQuery([Hitpoints.component, Mana.component])
    .filterValue(VisibleComponent, v => !!v, visibleCacheKeys)
    .render(this.gls, {
      mesh: 'quad',
      vertexShader: shaderModule([Vertex, Hitpoints.sm, MatrixSM, Position.sm], {
        size: constDef('vec2', glslVec2(this.conf.size)),
        offsetY: constDef('float', glslFloat(this.conf.offsetY)),

        view: uniformDef('mat4', d => d.derived.cameraView),
        projection: uniformDef('mat4', d => d.derived.cameraProjection),

        texcoord: outDef('vec2'),
        barEntityCell: outDef('ivec2', true),

        shader: funcDef('void', [['ivec2', 'entityCell']], `
          if (${Hitpoints.sm.value}(entityCell) <= 0.0) {
            gl_Position = vec4(-1);
            return;
          }
          barEntityCell = entityCell;
          vec3 translation = ${Position.sm.value}(entityCell) + vec3(0, 0, 3.5);
          mat4 modelView = ${MatrixSM.toBillboard}(view * ${MatrixSM.translate}(translation));
          gl_Position = projection * modelView * ${MatrixSM.translateScale}(vec3(0, offsetY, 0),
            vec3(size, 1)) * vec4(${Vertex.position}.xy * 0.1, 0, 1);
          texcoord = (${Vertex.position}.xy + 1.0) / 2.0;
        `)
      }),
      fragmentShader: shaderModule([this.conf.barColor], {
        outColor: outDef('vec4'),
        padding: constDef('vec2', glslVec2(this.conf.padding)),
        texcoord: inDef('vec2'),
        barEntityCell: inDef('ivec2', true),
        main: funcDef('void', [], `
          outColor = vec4(0.1, 0.1, 0.1, 0.8);
          vec2 paddedTc = (texcoord - padding) / (1. - padding * 2.);
          if (paddedTc.x > 0. && paddedTc.y > 0. && paddedTc.x <= 1. && paddedTc.y <= 1.) {
            vec4 bar = ${this.conf.barColor.getBarColor}(barEntityCell, paddedTc.x);
            outColor.rgb = mix(outColor.rgb, bar.rgb, bar.a);
            outColor.a = max(bar.a, outColor.a);
          }
        `)
      })
    });
  render(state: GpuWorldState) {
    this.query.run(state);
  }
}

export class OverheadBarsRenderer {
  constructor(private gls: GlState) {}

  health = new PixlingOverheadBarBaseRenderer(this.gls, {
    offsetY: 0.3,
    size: { x: 10, y: 3 },
    padding: { x: 0.02, y: 0.1 },
    barColor: shaderModule([Hitpoints.sm, Team.sm, HitpointsTrailing.sm], {
      homeHealthColor: uniformDef('vec3', d => d.data.homeHealthColor),
      awayHealthColor: uniformDef('vec3', d => d.data.awayHealthColor),
      getBarColor: funcDef('vec4', [['ivec2', 'entityCell'], ['float', 'p']], `
        float perc = ${Hitpoints.sm.value}(entityCell) / ${glslFloat(MaxHitpoints)};
        float trailingPerc = ${HitpointsTrailing.sm.value}(entityCell) / ${glslFloat(MaxHitpoints)};
        if (p < perc) {
          vec3 col = ${Team.sm.value}(entityCell) == ${Teams.HomeTeam} ? homeHealthColor : awayHealthColor;
          if (p > trailingPerc) {
            col = vec3(24, 237, 194)/255.;
          }
          return vec4(col, 1);
        } else {
          if (p < trailingPerc) {
            return vec4(252, 223, 3, 255)/255.;
          } else {
            return vec4(0);
          }
        }
      `)
    })
  });
  mana = new PixlingOverheadBarBaseRenderer(this.gls, {
    offsetY: 0,
    size: { x: 10, y: 1 },
    padding: { x: 0.02, y: 0.2 },
    barColor: shaderModule([Mana.sm], {
      manaColor: uniformDef('vec3', d => d.data.manaColor),
      getBarColor: funcDef('vec4', [['ivec2', 'entityCell'], ['float', 'p']], `
        float perc = ${Mana.sm.value}(entityCell) / ${glslFloat(MaxMana)};
        if (p < perc) {
          return vec4(manaColor, 1);
        } else {
          return vec4(0);
        }
      `)
    })
  });
  // casting = new PixlingOverheadBarBaseRenderer(this.common, CastingAbility.castingProgress);

  // Debug:
  // brainOutputs = BrainOutputsKeys.map(key =>
  //   new PixlingOverheadBarBaseRenderer(this.gls, BrainOutput[`out${key}`], PixlingOverheadBarBaseRenderer.Calcs.manaColor));

  run(state: GpuWorldState) {
    if (!state.data.rendererConfig.overheadBars || state.appState?.state.hideUI) {
      return;
    }
    this.gls.disable(GL.DEPTH_TEST);
    this.gls.disable(GL.CULL_FACE)
    // this.mana.render(state);
    this.health.render(state);
    // this.casting.render(rinputs, 0.2, rgbFromHex(CastingColor), rinputs.state.derived.renderPixlingsCount);
    // for (let i=0; i < this.brainOutputs.length; i++) {
    //   this.brainOutputs[i].render(state, 0.9 + i / 4, { x: 10, y: 1 }, { x: 0.02, y: 0.2 });
    // }
  }
}