import * as React from 'react';
import './index.css';
import { WorldStateContext } from '../WorldDataConsumer';
import { ProgressBar } from '../../Components/ProgressBar';
import { useInterval } from "../../Components/Interval";
import { PixlingTimespan } from '../../Time';
import { StepsInBattle } from '../../GpuWorld/StateLayout';


export function RoundClock() {
  const worldState = React.useContext(WorldStateContext);
  const [stepCounter, setStepCounter] = React.useState<number>(0);
  useInterval(() => {
    if (worldState) {
      setStepCounter(worldState.data.stepCounter - worldState.data.battleStartStep);
    }
  }, 1000);
  if (!worldState) {
    return null;
  }
  const p = stepCounter / StepsInBattle;
  let clockString = new PixlingTimespan(StepsInBattle - stepCounter).toString();
  return <ProgressBar progress={p} color="#e6e699" className={`RoundClock`}>
    {clockString}
  </ProgressBar>;
}
