import { Decisions } from "../Brain/Decisions";
import { GlState } from "../../GpuUtil/GlState";
import { GpuWorldState } from "../State";
import { Dazeable } from "../Buffs/Daze";
import { Casting } from "../Abilities/Abilities";
import { RotationZ, Position } from "./Transform";
import { Hitpoints, UnitMovement, WalkingSpeed, Focuser, IsOnGround, UnitMovementSpeed, UnitRotationSpeed } from "./UnitComponents";
import { ArchetypesQuery } from "../../GpuUtil/ECS/Query";
import { shaderModule, funcDef } from "../../GpuUtil/ShaderModule";
import { CastingState } from "../StateLayout";
import { SpatialSM, AngleLerpSM } from "../../GpuUtil/SM2Utils";
import { Abilities } from "../Abilities/Abilities";
import { Crippleable } from "../Buffs/Cripple";

export class UpdateUnitMovement {
  constructor(private gls: GlState) {}

  update = new ArchetypesQuery([UnitMovement.component, RotationZ.component])
    .updateGpu(this.gls, [WalkingSpeed.component, RotationZ.component], shaderModule([
      WalkingSpeed.writer,
      RotationZ.writer,
      Casting.Ability.sm,
      Casting.State.sm,
      Focuser.sm,
      Decisions.MoveX.sm,
      Decisions.Rotate.sm,
      Decisions.ChaseFocus.sm,
      Dazeable.sm,
      Crippleable.sm,
      Hitpoints.sm,
      IsOnGround.sm,
      AngleLerpSM,
      RotationZ.sm,
      Position.sm,
      SpatialSM,
      UnitMovementSpeed.sm,
      UnitRotationSpeed.sm,
    ], {
      update: funcDef('void', [['ivec2', 'entityCell']], `

        int castingState = ${Casting.State.sm.value}(entityCell);
        int castingAbility = ${Casting.Ability.sm.value}(entityCell);
        ivec2 focus = ${Focuser.sm.value}(entityCell);

        vec3 selfPosition = ${Position.sm.value}(entityCell);
        float selfRotation = ${RotationZ.sm.value}(entityCell);
        vec2 selfForward = ${SpatialSM.forward}(selfRotation);
        vec3 focusPosition = ${Position.sm.value}(focus);

        float chaseFocus = focus.x >= 0 ? ${Decisions.ChaseFocus.sm.value}(entityCell) : 0.0;

        bool dazed = ${Dazeable.sm.value}(entityCell) > 0;
        bool crippled = ${Crippleable.sm.value}(entityCell) > 0;
        bool canWalk = ${Hitpoints.sm.value}(entityCell) > 0.0 &&
          !dazed &&
          ${IsOnGround.sm.value}(entityCell);

        float walkingSpeed = 0.0;
        float newRotation = 0.0;
        if (canWalk) {
          float maximumMovementSpeed = ${UnitMovementSpeed.sm.value}(entityCell);
          float move = mix(
            ${Decisions.MoveX.sm.value}(entityCell),
            1.0,
            chaseFocus
            // Scale with max move speed so that 1 from the brain is max
          ) * maximumMovementSpeed;

          float movementSpeed = maximumMovementSpeed *
            (castingAbility == ${Abilities.Shell} ? 0.2 : 1.) *
            (crippled ? 0.1 : 1.) *
            ((castingState == ${CastingState.Casting} || castingState == ${CastingState.Performing}) ? 0. : 1.);

          // It's important that we clamp like this so that the decision to move is absolute, since
          // the pixlings doesn't know it's own speed limitations (i.e. "I want to move forward 5km/h"
          // rather than "I want to move forward at 50% speed, whatever that means since I don't know my max speed")
          walkingSpeed = clamp(
            move,
            -movementSpeed,
            movementSpeed
          );

          float maximumRotationSpeed = ${UnitRotationSpeed.sm.value}(entityCell);
          float speed = maximumRotationSpeed *
            (crippled ? 0.2 : 1.) *
            ((castingState == ${CastingState.Casting} || castingState == ${CastingState.Performing}) ? 0.0 : 1.0);
          float chaseAngle = ${SpatialSM.angleToPosition}(selfPosition.xy, selfForward, focusPosition.xy);

          // Scale by maximum speed, so that 1 brain output is max
          float manualRotate = ${Decisions.Rotate.sm.value}(entityCell) * maximumRotationSpeed;
          // Same here with the clamping as in movement speed
          manualRotate = selfRotation + clamp(manualRotate, -speed, speed);

          float chaseRotate = selfRotation + clamp(chaseAngle, -speed, speed);

          newRotation = ${AngleLerpSM.angleLerp}(manualRotate, chaseRotate, chaseFocus);
        }

        ${WalkingSpeed.writer.write}(walkingSpeed);
        ${RotationZ.writer.write}(newRotation);
      `)
    }));

  run(state: GpuWorldState) {
    this.update.run(state);
  }
}
