import * as React from 'react';
import { EventDispatcher } from '../Util';
import lodash from 'lodash';

export class Store<T> {
  constructor(public state: T) {}
  update(change: Partial<T>) {
    this.state = {
      ...this.state,
      ...change
    };
    this.onChange.dispatch(this.state, change);
  }
  onChange = new EventDispatcher<[T, Partial<T>]>();
}

export function useStore<State, Selection>(context: React.Context<Store<State>>, map: (data: State) => Selection): Selection {
  const store = React.useContext(context);
  const sel = React.useRef(map(store.state));
  const [selection, setSelection] = React.useState(sel.current);

  React.useEffect(() => {
    const eventListener = (newData: State) => {
      const newSelection = map(store.state);
      if (lodash.isEqual(sel.current, newSelection)) {
        return;
      }
      sel.current = newSelection;
      setSelection(newSelection);
    }

    store.onChange.attach(eventListener);

    return () => {
      store.onChange.detach(eventListener);
    };
  }, [store, map]);

  return selection;
}

export function useStoreSelect<State>(context: React.Context<Store<State>>, ...keys: Array<keyof State>): State {
  // eslint-disable-next-line
  const map = React.useCallback(d => keys.reduce((p, k) => ({...p, [k]: d[k] }), {}), keys);
  return useStore(context, map) as State;
}
