import { handleGlError } from ".";
import * as Sentry from '@sentry/browser';
import { Result, err, ok, isErr, isOk } from "../Result";

export function addLineNumbersToSource(source: string): string {
  return source.split('\n').map((x, i) => (i + 1) + ': ' + x).join('\n');
}

export interface ShaderErrorCompile {
  type: 'FailedToCompileShader';
  message: string;
  shaderType: 'vertex' | 'fragment';
  shaderSource: string;
}
export type ShaderError =
  { type: 'FailedToCreateShader' } |
  ShaderErrorCompile |
  { type: 'FailedToLinkProgram', message: string, shaderSources: string[] } |
  { type: 'FailedToCreateProgram' };

export function handleShaderErrorHandler(error: ShaderError) {
  // tslint:disable-next-line:no-console
  console.error(new Error().stack);
  switch (error.type) {
    case 'FailedToCompileShader':
      // tslint:disable-next-line:no-console
      console.error(addLineNumbersToSource(error.shaderSource));
      // tslint:disable-next-line:no-console
      console.error(error.message);
      Sentry.captureException(new Error('Could not compile shader. \n\n' + error.message));
      break;
    case 'FailedToLinkProgram':
      console.error(addLineNumbersToSource(error.shaderSources[0]));
      console.error(addLineNumbersToSource(error.shaderSources[1]));
      // tslint:disable-next-line:no-console
      console.error(error.message);
      Sentry.captureException(new Error('Could not compile WebGL program. \n\n' + error.message));
      break;
    default:
      Sentry.captureException(new Error(error.type));
  }
}

export function shaderFromSource(gl: WebGL2RenderingContext, type: 'vertex' | 'fragment', sourceCode: string): Result<WebGLShader, ShaderError> {
  // // tslint:disable-next-line:no-console
  // console.log(sourceCode);
  const shader = gl.createShader(type === 'vertex' ? gl.VERTEX_SHADER : gl.FRAGMENT_SHADER);
  if (!shader) {
    handleGlError(gl, 'Failed to create shader');
    return err({ type: 'FailedToCreateShader' });
  }
  gl.shaderSource(shader, sourceCode);
  gl.compileShader(shader);

  if (!gl.getShaderParameter(shader, gl.COMPILE_STATUS)) {
    return err({
      type: 'FailedToCompileShader',
      message: gl.getShaderInfoLog(shader) || 'Unknown error',
      shaderType: type,
      shaderSource: sourceCode
    });
  }
  return ok(shader);
}

export function createProgram(gl: WebGL2RenderingContext, shaders: WebGLShader[], shaderSources: string[]): Result<WebGLProgram, ShaderError> {
  const program = gl.createProgram();
  if (!program) {
    return err({ type: 'FailedToCreateProgram' });
  }
  for (const shader of shaders) {
    gl.attachShader(program, shader);
  }
  gl.linkProgram(program);

  if (!gl.getProgramParameter(program, gl.LINK_STATUS)) {
    return err({ type: 'FailedToLinkProgram', message: gl.getProgramInfoLog(program) || 'Unknown error', shaderSources });
  }
  return ok(program);
}

export class ShaderCache {
  constructor(private gl: WebGL2RenderingContext) {}
  destroy() {
    for (const k of Object.keys(this.shaderCache)) {
      const c = this.shaderCache[k].compiled;
      if (isOk(c)) {
        this.gl.deleteShader(c.value);
      }
    }
  }

  shaderCache: { [key: string]: { source: string, compiled: Result<WebGLShader, ShaderError> } } = {};

  getShader(type: 'vertex' | 'fragment', source: string): Result<WebGLShader, ShaderError> {
    let cached = this.shaderCache[source];
    if (cached) {
      return cached.compiled;
    } else {
      cached = this.shaderCache[source] = { source, compiled: shaderFromSource(this.gl, type, source) };
      if (isErr(cached.compiled)) {
        this.errors.push(cached.compiled.error);
      }
      return cached.compiled;
    }
  }
  createProgram(vertexSource: string, fragmentSource: string, opts: { logErrors: boolean }): Result<WebGLProgram, ShaderError> {
    const vs = this.getShader('vertex', vertexSource);
    const fs = this.getShader('fragment', fragmentSource);
    let compiled: Result<WebGLProgram, ShaderError>;
    if (isErr(vs)) {
      compiled = vs;
    } else if (isErr(fs)) {
      compiled = fs;
    } else {
      compiled = createProgram(this.gl, [vs.value, fs.value], [vertexSource, fragmentSource]);
    }
    if (isErr(compiled) && opts.logErrors) {
      this.errors.push(compiled.error);
      handleShaderErrorHandler(compiled.error);
    }
    return compiled;
  }
  errors: ShaderError[] = [];
  getErrors(): ShaderError[] {
    return this.errors;
  }
}
