const Sounds = (process.env.REACT_APP_APPLICATION === 'steam' || !process.env.REACT_APP_APPLICATION) ? import('./Sounds') : {};

let GlobalAudioContext: AudioContext | undefined;
try {
  GlobalAudioContext = new AudioContext();
} catch(err) {
  // tslint:disable-next-line:no-console
  console.error(err);
}

async function loadMp3AudioBuffer(path: string) {
  if (!GlobalAudioContext) { return null; }
  const sounds = await Sounds;
  const sound = sounds[path];
  if (!sound) {
    return null;
  }
  const arrayBuffer = await loadArrayBuffer(sound);
  return await GlobalAudioContext.decodeAudioData(arrayBuffer);
}

type AudioEffect = {
  type: 'single';
  path: string;
  buffer: Promise<AudioBuffer | null>;
  looping: boolean;
} | {
  type: 'random-slice';
  path: string;
  buffer: Promise<AudioBuffer | null>;
  slices: Array<[number, number]>; // [start, duration]
} | {
  type: 'mix';
  effects: AudioEffect[];
} | {
  type: 'random-mix-of';
  effects: AudioEffect[];
} | {
  type: 'random-select';
  effects: AudioEffect[];
} | {
  type: 'random-lowpass';
  effect: AudioEffect;
  minFreq: number;
  maxFreq: number;
} | {
  type: 'gain';
  effect: AudioEffect;
  minGain: number;
  maxGain: number;
}
function singleEffect(path: string, looping = false): AudioEffect {
  return { type: 'single', path, buffer: Promise.resolve(null), looping };
}
function slicesEffect(path: string, slices: Array<[number, number]>): AudioEffect {
  return { type: 'random-slice', path, buffer: Promise.resolve(null), slices };
}
function mixEffect(effects: AudioEffect[]): AudioEffect {
  return { type: 'mix', effects };
}
function randomMixOfEffect(effects: AudioEffect[]): AudioEffect {
  return { type: 'random-mix-of', effects };
}
function randomSelectEffect(effects: AudioEffect[]): AudioEffect {
  return { type: 'random-select', effects };
}
function randomLowpassEffect(minFreq: number, maxFreq: number, effect: AudioEffect): AudioEffect {
  return { type: 'random-lowpass', effect, minFreq, maxFreq };
}
function gainEffect(minGain: number, maxGain: number, effect: AudioEffect): AudioEffect {
  return { type: 'gain', effect, minGain, maxGain };
}
