function loadEffectBuffers(effect: AudioEffect) {
  if (effect.type === 'random-mix-of' || effect.type === 'random-select' || effect.type === 'mix') {
    for (const ef of effect.effects) {
      loadEffectBuffers(ef);
    }
  } else if (effect.type === 'random-lowpass' || effect.type === 'gain') {
    return loadEffectBuffers(effect.effect);
  } else {
    effect.buffer = loadMp3AudioBuffer(effect.path);
  }
}
for (const key of Object.keys(SoundEffectDefs)) {
  loadEffectBuffers(SoundEffectDefs[key]);
}

const MiscBuffers = {
  wind1: loadMp3AudioBuffer('Wind1'),
}

export const MusicBuffers = {
  music1: loadMp3AudioBuffer('Music1'),
  music2: loadMp3AudioBuffer('Music2'),
  music3: loadMp3AudioBuffer('Music3'),
  music4: loadMp3AudioBuffer('Music4'),
}


export class SoundEffectController {
  constructor(private audioContext: AudioContext, private sources: AudioBufferSourceNode[], private gain: GainNode) {}
  private sourcesEnded = this.sources.map(source => new Promise(resolve => source.addEventListener('ended', resolve)));
  ended = Promise.all(this.sourcesEnded);
  readonly looping = this.sources.reduce((p, x) => p || x.loop, false as boolean);
  stop(when?: number) {
    for (const s of this.sources) {
      s.stop(when);
    }
  }
  fadeOut(duration = 0.3) {
    this.gain.gain.setValueAtTime(this.gain.gain.value, this.audioContext.currentTime);
    this.gain.gain.linearRampToValueAtTime(0, this.audioContext.currentTime + duration);
    this.stop(this.audioContext.currentTime + duration);
  }
}

class AudioPlayer {
  constructor(public audioContext: AudioContext) {
    this.masterGain.connect(this.audioContext.destination);
    this.masterCompressor.connect(this.masterGain);
    this.effectsGain.connect(this.masterCompressor);
    this.environmentGain.connect(this.masterCompressor);

    this.musicGain.connect(this.masterCompressor);
    this.musicInnerGain.connect(this.musicGain);
    this.musicHighass.connect(this.musicInnerGain);
    this.musicHighass.type = 'highpass';
    this.musicHighass.frequency.setValueAtTime(0, this.audioContext.currentTime);
    this.musicInnerGain.gain.setValueAtTime(0.1, this.audioContext.currentTime);

    this.windGain.connect(this.environmentGain);
    this.windGain.gain.setValueAtTime(0.1, this.audioContext.currentTime);
  }
  musicStarted = false;

  musicGain = this.audioContext.createGain();
  effectsGain = this.audioContext.createGain();
  environmentGain = this.audioContext.createGain();

  masterGain = this.audioContext.createGain();
  masterCompressor = this.audioContext.createDynamicsCompressor();

  windSource?: AudioBufferSourceNode;
  windGain = this.audioContext.createGain();

  async ensureWindStarted() {
    if (!this.windSource) {
      this.windSource = this.audioContext.createBufferSource();
      this.windSource.buffer = await MiscBuffers.wind1;
      this.windSource.connect(this.windGain);
      this.windSource.loop = true;
      this.windSource.start();
    }
  }

  musicSource?: AudioBufferSourceNode;
  musicHighass = this.audioContext.createBiquadFilter();
  musicInnerGain = this.audioContext.createGain();
  async startMusic() {
    if (!this.musicStarted) {
      this.musicStarted = true;
      this.playNextMusic();
    }
  }
  async playNextMusic(prevTrack?: string) {
    const tracks = Object.keys(MusicBuffers);
    let track;
    do {
      track = tracks[Math.floor(Math.random() * tracks.length)];
    } while (track === prevTrack);
    this.musicSource = this.audioContext.createBufferSource();
    this.musicSource.buffer = await MusicBuffers[track];
    this.musicSource.connect(this.musicHighass);
    this.musicSource.loop = false;
    this.musicSource.start();
    this.musicSource.onended = () => {
      this.playNextMusic(track);
    }
  }
  highpassMusic(startTime = this.audioContext.currentTime, duration = 0.5) {
    console.log('[highpassMusic]');
    this.musicHighass.frequency.setValueAtTime(this.musicHighass.frequency.value, startTime);
    this.musicHighass.frequency.linearRampToValueAtTime(1000, startTime + duration);
    this.musicInnerGain.gain.setValueAtTime(this.musicInnerGain.gain.value, startTime);
    this.musicInnerGain.gain.linearRampToValueAtTime(0.03, startTime + duration);
  }
  restoreMusic(startTime = this.audioContext.currentTime, duration = 3) {
    console.log('[restoreMusic]');
    this.musicHighass.frequency.setValueAtTime(this.musicHighass.frequency.value, startTime);
    this.musicHighass.frequency.linearRampToValueAtTime(0, startTime + duration);
    this.musicInnerGain.gain.setValueAtTime(this.musicInnerGain.gain.value, startTime);
    this.musicInnerGain.gain.linearRampToValueAtTime(0.1, startTime + duration);
  }
  tempHighpassMusic(duration: number) {
    this.highpassMusic();
    this.restoreMusic(this.audioContext.currentTime + duration);
  }

  createEffectSource(effect: AudioEffect, startDelay: number): [AudioNode, AudioBufferSourceNode[]] {
    if (effect.type === 'random-lowpass') {
      const [eff, sources] = this.createEffectSource(effect.effect, startDelay);
      const lowpass = this.audioContext.createBiquadFilter();
      lowpass.type = 'lowpass';
      lowpass.frequency.setValueAtTime(mix(effect.minFreq, effect.maxFreq, Math.random()), this.audioContext.currentTime);
      eff.connect(lowpass);
      return [lowpass, sources];
    }
    if (effect.type === 'gain') {
      const [eff, sources] = this.createEffectSource(effect.effect, startDelay);
      const gain = this.audioContext.createGain();
      gain.gain.setValueAtTime(mix(effect.minGain, effect.maxGain, Math.random()), this.audioContext.currentTime);
      eff.connect(gain);
      return [gain, sources];
    }
    if (effect.type === 'mix') {
      const gain = this.audioContext.createGain();
      const sources: AudioBufferSourceNode[][] = [];
      for (let i = 0; i < effect.effects.length; i++) {
        const [sub, subSources] = this.createEffectSource(effect.effects[i], startDelay);
        sub.connect(gain);
        sources.push(subSources);
      }
      return [gain, lodash.flatten(sources)];
    }
    if (effect.type === 'random-mix-of') {
      const gain = this.audioContext.createGain();
      const weights = effect.effects.map(() => Math.random());
      const totalWeights = weights.reduce((p, x) => p + x, 0);
      const sources: AudioBufferSourceNode[][] = [];
      for (let i = 0; i < effect.effects.length; i++) {
        const [sub, subSources] = this.createEffectSource(effect.effects[i], startDelay);
        const subGain = this.audioContext.createGain();
        sub.connect(subGain);
        const volume = weights[i] / totalWeights;
        // console.log(i, volume);
        subGain.gain.setValueAtTime(volume, this.audioContext.currentTime);
        subGain.connect(gain);
        sources.push(subSources);
      }
      return [gain, lodash.flatten(sources)];
    }
    if (effect.type === 'random-select') {
      const subEff = effect.effects[Math.floor(Math.random() * effect.effects.length)];
      return this.createEffectSource(subEff, startDelay);
    }
    let source = this.audioContext.createBufferSource();
    effect.buffer.then(buffer => {
      source.buffer = buffer;
      if (effect.type === 'random-slice') {
        const slice = effect.slices[Math.floor(Math.random() * effect.slices.length)];
        source.start(this.audioContext.currentTime + startDelay, slice[0], slice[1]);
      } else {
        source.loop = effect.looping;
        source.start(this.audioContext.currentTime + startDelay);
      }
    });
    return [source, [source]];
  }

  playSoundEffect(effectName: SoundEffect, startDelay: number = 0, position?: Vector3.Vector3): SoundEffectController | undefined {
    if (this.audioContext.state !== 'running') {
      return undefined;
    }
    // console.log('playSoundEffect', SoundEffect[effectName], position);
    const effect = SoundEffectDefs[effectName];
    // eslint-disable-next-line
    let [node, sources] = this.createEffectSource(effect, startDelay);
    const effectGain = this.audioContext.createGain();
    node = node.connect(effectGain);
    if (position) {
      const panner = this.audioContext.createPanner();
      panner.distanceModel = 'inverse';
      panner.maxDistance = 200;
      panner.refDistance = 32;
      panner.rolloffFactor = 1;
      panner.setPosition(position.x, position.y, position.z);
      node = node.connect(panner);
    }
    node = node.connect(this.effectsGain);
    const controller = new SoundEffectController(this.audioContext, sources, effectGain);
    this.playingSoundEffects.push(controller);
    controller.ended.then(() => {
      const i = this.playingSoundEffects.indexOf(controller);
      this.playingSoundEffects.splice(i, 1);
    });
    return controller;
  }
  fadeOutAllLoopingSoundEffects() {
    const playing = this.playingSoundEffects.slice(0);
    for (const c of playing) {
      if (c.looping) {
        c.fadeOut();
      }
    }
  }
  private playingSoundEffects: SoundEffectController[] = [];

  prevCameraView?: mat4;
  updateCamera(cameraView: mat4) {
    if (this.prevCameraView && mat4.equals(this.prevCameraView, cameraView)) {
      return;
    }
    this.prevCameraView = cameraView;
    const invView = mat4.invert(mat4.create(), cameraView);
    if (!invView) {
      return;
    }
    const position = vec3.create();
    mat4.getTranslation(position, invView);
    const rotation = quat.create();
    mat4.getRotation(rotation, invView);
    const forward = vec3.fromValues(0, 0, -1);
    vec3.transformQuat(forward, forward, rotation);
    const up = vec3.fromValues(0, 0, 1);
    const listener = this.audioContext.listener;
    if (!vec3IsNanOrInf(position)) {
      listener.setPosition(position[0], position[1], position[2]);
    }
    if (!vec3IsNanOrInf(forward) && !vec3IsNanOrInf(up)) {
      listener.setOrientation(forward[0], forward[1], forward[2], up[0], up[1], up[2]);
    }
  }
}
export const AudioPlayerInstance = GlobalAudioContext ? new AudioPlayer(GlobalAudioContext) : undefined;

function vec3IsNanOrInf(v: vec3) {
  return isNaN(v[0]) || isNaN(v[1]) || isNaN(v[2]) || !isFinite(v[0]) || !isFinite(v[1]) || !isFinite(v[2]);
}